import * as React from 'react';
import { memoize } from '@devexperts/utils/dist/function/memoize';

export * from '@devexperts/react-kit/dist/components/Popover/Popover';
import { Popover as DXCPopover, TPopoverProps } from '@devexperts/react-kit/dist/components/Popover/Popover';
import { SFC } from 'react';

const getRoot = memoize(() => {
	const root = document.getElementById('root');
	return root || undefined;
});

export const Popover: SFC<TPopoverProps> = (props: TPopoverProps) => <DXCPopover container={getRoot()} {...props} />;
