import { Option } from 'fp-ts/lib/Option';
import { string, array, boolean, type } from 'io-ts';
import { createOptionFromNullable } from 'io-ts-types';

export type CreateWatchlistTO = {
	name: Option<string>;
	description: Option<string>;
	symbols: Option<Array<string>>;
	futures: Option<boolean>;
};
export const CreateWatchlistTOIO = type({
	name: createOptionFromNullable(string),
	description: createOptionFromNullable(string),
	symbols: createOptionFromNullable(array(string)),
	futures: createOptionFromNullable(boolean),
});
