import * as React from 'react';
import { PureComponent } from 'react';
import * as cn from 'classnames';
import { withTheme } from '@devexperts/react-kit/dist/utils/withTheme';
import * as css from './theme/chart-map.module.css';

type TChartMap = {
	theme: {
		container?: string;
		map?: string;
		window?: string;
		chartMap?: string;
		button?: string;
		button_toLeft?: string;
		button_toRight?: string;
		canvasWrapper?: string;
		canvas?: string;
		controls?: string;
		labels?: string;
		area?: string;
		area_hidden?: string;
		knob?: string;
		knob_toLeft?: string;
		knob_toRight?: string;
	};
};

class RawChartMap extends PureComponent<TChartMap> {
	render() {
		const { theme } = this.props;

		return (
			<div className={theme.map} data-chart-container={true}>
				<div key="chartMap" className={theme.chartMap}>
					<div className="chartMap--container">
						<button data-element="buttonLeft" className={cn(theme.button, theme.button_toLeft)} />
						<div data-element="navigationContainer" className={theme.canvasWrapper}>
							<canvas data-element="vmCanvas" className={theme.canvas} />
							<div data-element="navLabels" className={theme.labels} />
							<div className={theme.controls}>
								<div data-element="windowLeft" className={cn(theme.area, theme.area_hidden)}>
									<span data-element="knotLeft" className={cn(theme.knob, theme.knob_toLeft)} />
								</div>
								<div className={theme.area}>
									<span data-element="slider" className={theme.window} />
								</div>
								<div data-element="windowRight" className={cn(theme.area, theme.area_hidden)}>
									<span data-element="knotRight" className={cn(theme.knob, theme.knob_toRight)} />
								</div>
							</div>
						</div>
						<button data-element="buttonRight" className={cn(theme.button, theme.button_toRight)} />
					</div>
				</div>
			</div>
		);
	}
}

const wTheme = {
	...css,
};

export const ChartMap = withTheme(Symbol(), wTheme)(RawChartMap);
