import { ResponseValidationError, TAPIClient } from '../client/client';
import {
	InstrumentInfoSubscriptionTO,
	InstrumentInfoSubscriptionTOIO,
} from '../definitions/InstrumentInfoSubscriptionTO';
import { InstrumentInfoTO, InstrumentInfoTOIO } from '../definitions/InstrumentInfoTO';
import { fromEither } from '@devexperts/remote-data-ts';
import { LiveData } from '@devexperts/rx-utils/dist/rd/live-data.utils';
import { asks } from 'fp-ts/lib/Reader';
import { array, string, type, partial, void as tvoid } from 'io-ts';
import { map } from 'rxjs/operators';

export type InstrumentinfoController = {
	/**
	 * Get instrument info for specified symbols
	 * @param { object } parameters
	 */
	readonly getInstrumentInfo: (parameters: {
		query: { symbols: Array<string> };
	}) => LiveData<Error, Array<InstrumentInfoTO>>;

	/**
	 * Change instrument info subscription
	 * @param { object } [parameters]
	 */
	readonly changeInstrumentInfoSubscription: (parameters: {
		body?: InstrumentInfoSubscriptionTO;
	}) => LiveData<Error, void>;
};

export const instrumentinfoController = asks(
	(e: { apiClient: TAPIClient }): InstrumentinfoController => ({
		getInstrumentInfo: parameters => {
			const encoded = partial({ query: type({ symbols: array(string) }) }).encode(parameters);

			return e.apiClient
				.request({
					url: `/instrumentinfo`,
					method: 'GET',
					query: encoded.query,
				})
				.pipe(
					map(data =>
						data.chain(value =>
							fromEither(
								array(InstrumentInfoTOIO)
									.decode(value)
									.mapLeft(ResponseValidationError.create),
							),
						),
					),
				);
		},

		changeInstrumentInfoSubscription: parameters => {
			const encoded = partial({ body: InstrumentInfoSubscriptionTOIO }).encode(parameters);

			return e.apiClient
				.request({
					url: `/instrumentinfo`,
					method: 'PUT',

					body: encoded.body,
				})
				.pipe(
					map(data =>
						data.chain(value => fromEither(tvoid.decode(value).mapLeft(ResponseValidationError.create))),
					),
				);
		},
	}),
);
