import { Sink } from '@devexperts/rx-utils/dist/sink.utils';
import { filterMap } from '../../utils/rxjs/filtermap';
import { OrderStore } from '../../store/order/order.store';
import { Option } from 'fp-ts/lib/Option';
import * as O from 'fp-ts/lib/Option';
import { combineLatestRD } from '@devexperts/rx-utils/dist/rd/operators/combineLatestRD';
import { LiveDataOption, LiveData } from '@devexperts/rx-utils/dist/rd/live-data.utils';
import { fromOption, success, isSuccess, failure } from '@devexperts/remote-data-ts';
import { merge, Observable, combineLatest, race } from 'rxjs';
import {
	distinctUntilChanged,
	filter,
	map,
	share,
	switchMap,
	takeUntil,
	tap,
	withLatestFrom,
	first,
	startWith,
	mapTo,
	delay,
} from 'rxjs/operators';
import { endOfYesterday, isSameDay } from 'date-fns';
import { ask, combineContext } from '@devexperts/rx-utils/dist/context.utils';
import {
	OrderEntryTakeProfitStopLossData,
	TakeProfitStopLossOptions,
	OrderKind,
} from '../../model/takeprofit-stoploss/takeprofit-stoploss.model';
import {
	OrderEntrySide,
	OrderEntryExecutionStatusFlags,
	OrderEntryExecutionPrice,
	OrderEntryExecutionDate,
	AmountValidationError,
} from '../../model/order-entry/order-entry.model';
import {
	getDefaultTakeProfitStopLossData,
	mapTakeProfitStopLossPayload,
	roundPriceWithPrecision,
	getTakeProfitStopLossValue,
} from '../../model/takeprofit-stoploss/takeprofit-stoploss.utils';
import { countDistance } from '../../model/limits/limits.utils';
import {
	getExpireAt,
	validateExecutionPrice,
	validateAmount,
	convertDateInputValueToDate,
	setoidOEValues,
	setoidBooleanRD,
	validateOrderEditPrice,
	toExpireAt,
	toOrderTOSide,
	toOrderEntryData,
	getAmount,
	setoidOrderType,
	setoidOrderEntryExecutionPrice,
	getOrderType,
} from './order-entry.utils';
import { InstrumentStore } from '../../store/instrument/instrument.store';
import { MarketDataStore } from '../../store/market-data/market-data.store';
import { ConversionRateStore } from '../../store/conversion-rate/conversion-rate.store';
import { AccountViewModel } from '../account/account.view-model';
import { mapRD } from '@devexperts/rx-utils/dist/rd/operators/mapRD';
import { createHandler } from '@devexperts/rx-utils/dist/create-handler.utils';
import { switchMapRD } from '@devexperts/rx-utils/dist/rd/operators/switchMapRD';
import { of } from 'rxjs/internal/observable/of';
import { routes } from '../../ui-common/routes';
import { href, Router } from '../../utils/router.utils';
import { LimitsStore } from '../../store/limits/limits.store';
import { Either } from 'fp-ts/lib/Either';
import * as E from 'fp-ts/lib/Either';
import { interval } from 'rxjs/internal/observable/interval';
import { currencyStringToSymbol } from '../../utils/currency.utils';
import { eqNumber, strictEqual } from 'fp-ts/lib/Eq';
import { getSignBySide } from '../orders/orders.utils';
import { Big } from 'big.js';
import { MarginImpactStore } from '../../store/margin-impact/margin-impact.store';
import { OrderTO } from '../../api/generated/swagger/definitions/OrderTO';
import {
	distinctRDNumber,
	setoidOptionDate,
	setoidOptionNumber,
	distinctRDOptionString,
	distinctRDString,
	setoidOptionString,
	distinctRDOptionNumber,
} from '../../utils/setoid.utils';
import { identity, constant, constTrue, constFalse } from 'fp-ts/lib/function';
import { pipe } from 'fp-ts/lib/pipeable';
import { shareReplayWithDefaults } from '../../utils/rxjs/shareReplayWithDefaults';

const DEFAULT_DELTA_IN_PIPS = 2;
// please, refer to https://jira.in.devexperts.com/browse/DXTF-10193 for delay duration and explanation
const DELAY_BEFORE_NEXT_SUBMIT_ATTEMPT = 5000;

export type OrderEntryValues = {
	value: string;
	execFormValue: Option<number>;
	isExecEnabled: boolean;
	isExpireChecked: boolean;
	expireAtDate: Option<Date>;
	isTPEnabled: boolean;
	takeProfitValue: number;
	isSLEnabled: boolean;
	stopLossValue: number;
};

export type OrderValue = {
	amount: string;
	side: OrderEntrySide;
};

export type OrderEntryData = {
	bid: Option<number>;
	ask: Option<number>;
	pipsSize: number;
	precision: number;
	description: Option<string>;
	marginImpact: Either<Error, number>;
	step: number;
};

export type OrderEntryViewModel = Readonly<{
	symbol: string;
	description$: LiveDataOption<Error, string>;
	bid$: LiveDataOption<Error, number>;
	ask$: LiveDataOption<Error, number>;
	lot$: Observable<Option<number>>;
	quantityIncrement$: LiveData<Error, number>;
	quantity$: LiveDataOption<Error, number>;
	accountBalance$: Observable<Option<number>>;
	valueSum$: Observable<number>;
	data$: LiveData<Error, OrderEntryData>;
	value$: LiveData<Error, OrderValue>;
	amountPrecision$: LiveData<Error, number>;
	onValueChange: (value: OrderValue) => void;
	onSend: (isValid: boolean) => () => void;
	moreOptionsOpened$: Observable<boolean>;
	precision$: LiveData<Error, number>;
	onToggleMoreOptions: (value: boolean) => void;
	executionCheckboxes$: Observable<OrderEntryExecutionStatusFlags>;
	onToggleExecutionCheckboxes: (value: OrderEntryExecutionStatusFlags) => void;
	amountValidated$: LiveData<Error, Either<AmountValidationError, string>>;
	amountMinOrderSize$: LiveData<Error, number>;
	amountMaxOrderSize$: LiveData<Error, number>;
	executionPrice$: Observable<OrderEntryExecutionPrice>;
	onChangeExecutionPrice: (value: OrderEntryExecutionPrice) => void;
	executionDates$: Observable<OrderEntryExecutionDate>;
	initExecutionDates: OrderEntryExecutionDate;
	onChangeExecutionDates: (value: OrderEntryExecutionDate) => void;
	takeProfitStopLossValue$: LiveData<Error, OrderEntryTakeProfitStopLossData>;
	onTakeProfitStopLossValueChange: (value: OrderEntryTakeProfitStopLossData) => void;
	error$: Observable<Option<Error>>;
	isValid$: Observable<boolean>;
	isShownValidErrors$: Observable<boolean>;
	isFundsEnough$: LiveData<Error, boolean>;
	isEmitingOrdersAllowed$: LiveData<Error, boolean>;
	isOrderEntryValuesChanged$: LiveData<Error, boolean>;
	currencySymbol$: Observable<string>;
	priceIncrement$: LiveData<Error, number>;
	unitName$: LiveDataOption<Error, string>;
}>;

type OrderEntryViewModelContext = {
	instrumentStore: InstrumentStore;
	marketDataStore: MarketDataStore;
	marginImpactStore: MarginImpactStore;
	conversionRateStore: ConversionRateStore;
	orderStore: OrderStore;
	accountViewModel: AccountViewModel;
	limitsStore: LimitsStore;
	router: Router;
};

type OrderId = string;
type CategoryId = string;

type CreateOrderEntryViewModel = (
	symbol: string,
	side: OrderEntrySide,
	elementId: Either<OrderId, CategoryId>,
	plKind: string,
) => Sink<OrderEntryViewModel>;

export const createOrderEntryViewModel = combineContext(
	ask<OrderEntryViewModelContext>(),
	(e: OrderEntryViewModelContext): CreateOrderEntryViewModel => {
		return (symbol, side, elementId, plKind) => {
			const {
				accountViewModel: { current$, accountMetrics$ },
				instrumentStore,
				marketDataStore,
				marginImpactStore,
				orderStore,
				conversionRateStore: { getRateBySymbol },
				limitsStore: { getLimitsByInstrumentId },
			} = e;

			// account sources
			const accountId$ = current$.pipe(
				map(current => current.id),
				distinctUntilChanged(),
			);
			const baseCurrency$ = current$.pipe(
				map(current => current.baseCurrency),
				distinctUntilChanged(),
			);
			const accountBalance$ = accountMetrics$.pipe(
				map(metrics => metrics.balance),
				distinctUntilChanged(),
			);
			const availableFunds$ = accountMetrics$.pipe(
				map(({ availableFunds }) => availableFunds),
				distinctUntilChanged(O.getEq(eqNumber).equals),
			);

			// instrument sources
			const instrument$ = instrumentStore.getBySymbol(symbol);
			const instrumentId$ = instrument$.pipe(
				filter(isSuccess),
				map(instrument => instrument.value.id),
				distinctUntilChanged(),
			);
			const instrumentBaseCurrency$ = instrument$.pipe(
				filter(isSuccess),
				map(({ value }) => value.baseCurrency),
				distinctUntilChanged(setoidOptionString.equals),
			);
			const currency$ = instrument$.pipe(
				map(instrumentRD =>
					instrumentRD.chain(instrument =>
						fromOption(instrument.currency, constant(new Error('There is no currency in this instrument'))),
					),
				),
				distinctRDString,
			);
			const description$ = instrument$.pipe(
				mapRD(instrument => instrument.description),
				distinctRDOptionString,
			);
			const pipsSize$ = instrument$.pipe(
				mapRD(instrument => instrument.pipsSize),
				distinctRDNumber,
			);
			const lotSize$ = instrument$.pipe(
				mapRD(instrument => instrument.lotSize),
				distinctRDNumber,
			);
			const quantityIncrement$ = instrument$.pipe(
				mapRD(instrument => instrument.quantityIncrement),
				distinctRDNumber,
			);
			const priceIncrement$ = instrument$.pipe(
				mapRD(instrument => instrument.priceIncrement),
				distinctRDNumber,
			);
			const precision$ = instrument$.pipe(
				mapRD(instrument => instrument.precision),
				distinctRDNumber,
			);
			const unitName$ = instrument$.pipe(
				mapRD(instrument => instrument.unitName),
				first(),
			);

			// order sources
			const order$ = pipe(
				elementId,
				E.fold(
					orderId => O.some(orderStore.getById(Number(orderId)).pipe(shareReplayWithDefaults())),
					constant(O.none),
				),
				O.map(data => data.pipe(filter(isSuccess))),
			);
			const orderAmount$ = pipe(
				order$,
				O.fold(constant(of(failure<Error, number>(new Error('No order were found')))), order =>
					order.pipe(
						mapRD(order => Math.abs(order.quantity)),
						distinctRDNumber,
					),
				),
			);
			const expireOrder$ = pipe(
				order$,
				O.fold(constant(of(O.none)), order =>
					order.pipe(
						mapRD(order => order.expireAt),
						filter(isSuccess),
						map(data => data.value),
						distinctUntilChanged(setoidOptionDate.equals),
					),
				),
			);
			const stopPrice$ = pipe(
				order$,
				O.fold(constant(of(O.none)), order =>
					order.pipe(
						mapRD(order => order.stopPrice),
						filter(isSuccess),
						map(data => data.value),
						distinctUntilChanged(setoidOptionNumber.equals),
					),
				),
			);
			const requestId$ = pipe(
				order$,
				O.fold(constant(of(O.none)), order =>
					order.pipe(
						mapRD(order => order.requestId),
						filter(isSuccess),
						map(data => data.value),
						distinctUntilChanged(setoidOptionString.equals),
					),
				),
			);
			const orderChainId$ = pipe(
				order$,
				O.fold(constant(of(O.none)), order =>
					order.pipe(
						mapRD(order => order.orderChainId),
						filter(isSuccess),
						map(data => O.some(data.value)),
						distinctUntilChanged(setoidOptionString.equals),
					),
				),
			);
			const orderTypeDefault$ = pipe(
				order$,
				O.fold(constant(of<Option<OrderTO['type']>>(O.none)), order =>
					order.pipe(
						mapRD(order => order.type),
						filter(isSuccess),
						map(data => O.some(data.value)),
						distinctUntilChanged(setoidOrderType.equals),
					),
				),
			);
			const expireAt$ = pipe(
				order$,
				O.fold(constant(of(O.none)), order =>
					order.pipe(
						mapRD(order => order.expireAt),
						filter(isSuccess),
						map(data => data.value),
						distinctUntilChanged(setoidOptionDate.equals),
					),
				),
			);
			const limitPrice$ = pipe(
				order$,
				O.fold(constant(of(O.none)), order =>
					order.pipe(
						mapRD(order => order.limitPrice),
						filter(isSuccess),
						map(data => data.value),
						distinctUntilChanged(setoidOptionNumber.equals),
					),
				),
			);
			const takeProfitFixedPrice$ = pipe(
				order$,
				O.fold(constant(of(O.none)), order =>
					order.pipe(
						mapRD(order =>
							pipe(
								order.takeProfit,
								O.chain(takeProfit => takeProfit.fixedPrice),
							),
						),
						filter(isSuccess),
						map(data => data.value),
						distinctUntilChanged(setoidOptionNumber.equals),
					),
				),
			);
			const stopLossFixedPrice$ = pipe(
				order$,
				O.fold(constant(of(O.none)), order =>
					order.pipe(
						mapRD(order =>
							pipe(
								order.stopLoss,
								O.chain(stopLoss => stopLoss.fixedPrice),
							),
						),
						filter(isSuccess),
						map(data => data.value),
						distinctUntilChanged(setoidOptionNumber.equals),
					),
				),
			);

			// market data sources
			const marketData$ = marketDataStore.getDataBySymbol(symbol);
			const marginImpactData$ = marginImpactStore.getMarginImpactBySymbol(symbol);
			const formulaMarginImpact$ = marginImpactData$.pipe(
				mapRD(data => data.formula),
				distinctRDOptionString,
			);
			const argumentsMarginImpact$ = marginImpactData$.pipe(
				mapRD(data => data.arguments),
				distinctRDOptionString,
			);
			const quantity$ = marketData$.pipe(
				mapRD(data =>
					pipe(
						data.lastQuote,
						O.chain(quote => quote.quantity),
					),
				),
				distinctRDOptionNumber,
			);
			const bid$ = marketData$.pipe(
				mapRD(data =>
					pipe(
						data.lastQuote,
						O.chain(quote => quote.bid),
					),
				),
				distinctRDOptionNumber,
			);
			const ask$ = marketData$.pipe(
				mapRD(data =>
					pipe(
						data.lastQuote,
						O.chain(quote => quote.ask),
					),
				),
				distinctRDOptionNumber,
			);

			// conversion rate sources
			const currencies$ = combineLatest(currency$, baseCurrency$).pipe(
				map(([currency, baseCurrency]) => currency.map(currency => ({ currency, baseCurrency }))),
			);
			const conversionRate$ = currencies$.pipe(
				switchMapRD(({ currency, baseCurrency }) => getRateBySymbol(currency, baseCurrency)),
			);
			const rate$ = conversionRate$.pipe(
				map(rates =>
					pipe(
						rates.toOption(),
						O.chain(rate =>
							pipe(
								rate.conversionRate,
								O.chain(conversionRate => conversionRate.value),
							),
						),
					),
				),
				distinctUntilChanged(setoidOptionNumber.equals),
			);

			// limits sources
			const limit$ = instrument$.pipe(switchMapRD(instrument => getLimitsByInstrumentId(instrument.id)));
			const minOrderIncrement$ = limit$.pipe(
				mapRD(limit => limit.minOrderIncrement),
				distinctRDNumber,
			);
			const defaultOrderSize$ = limit$.pipe(
				mapRD(limit => limit.defaultOrderSize),
				distinctRDNumber,
			);
			const minOrderSize$ = limit$.pipe(
				mapRD(limit => limit.minOrderSize),
				distinctRDNumber,
			);
			const maxOrderSize$ = limit$.pipe(
				mapRD(limit => limit.maxOrderSize),
				distinctRDNumber,
			);
			const successfulLimit$ = limit$.pipe(
				filter(isSuccess),
				map(data => data.value),
			);
			const limitStopDistanceType$ = successfulLimit$.pipe(
				map(limit => limit.limitStopDistanceType),
				distinctUntilChanged(strictEqual),
			);
			const limitStopDistance$ = successfulLimit$.pipe(
				map(limit => limit.limitStopDistance),
				distinctUntilChanged(eqNumber.equals),
			);
			const limitStopDistanceInPercentOfSpread$ = successfulLimit$.pipe(
				map(limit => limit.limitStopDistanceInPercentOfSpread),
				distinctUntilChanged(eqNumber.equals),
			);
			const limits$ = combineLatest(
				limitStopDistanceType$,
				limitStopDistance$,
				limitStopDistanceInPercentOfSpread$,
			).pipe(
				map(([limitStopDistanceType, limitStopDistance, limitStopDistanceInPercentOfSpread]) => ({
					limitStopDistanceType,
					limitStopDistance,
					limitStopDistanceInPercentOfSpread,
				})),
			);
			const amountDefaultOrderSize$ = combineLatestRD(defaultOrderSize$, lotSize$).pipe(
				mapRD(([defaultOrderSize, lotSize]) => defaultOrderSize * lotSize),
			);
			const amountMinOrderSize$ = combineLatestRD(minOrderSize$, lotSize$).pipe(
				mapRD(([minOrderSize, lotSize]) => minOrderSize * lotSize),
			);
			const amountMaxOrderSize$ = combineLatestRD(maxOrderSize$, lotSize$).pipe(
				mapRD(([maxOrderSize, lotSize]) => maxOrderSize * lotSize),
			);

			const amountInput = createHandler<OrderValue>();
			const send = createHandler<void>();
			const moreOptionsHandle = createHandler<boolean>();
			const executionCheckboxes = createHandler<OrderEntryExecutionStatusFlags>();
			const executionPrice = createHandler<OrderEntryExecutionPrice>();
			const executionDates = createHandler<OrderEntryExecutionDate>();
			// take profit / stop loss
			const takeProfitStopLossValueHandler = createHandler<OrderEntryTakeProfitStopLossData>();
			const isValidHandler = createHandler<boolean>();
			const isShownValidErrorsHandler = createHandler<boolean>();

			const onChangeExecutionPrice = (values: OrderEntryExecutionPrice) => executionPrice.handle(values);
			const onChangeExecutionDates = (values: OrderEntryExecutionDate) => executionDates.handle(values);
			const onToggleExecutionCheckboxes = (values: OrderEntryExecutionStatusFlags) => {
				executionCheckboxes.handle(values);
			};

			const moreOptions$ = moreOptionsHandle.value$.pipe(
				startWith(
					pipe(
						elementId,
						E.fold(constTrue, constFalse),
					),
				),
			);
			const executionCheckboxes$ = expireOrder$.pipe(
				switchMap(expireAt =>
					executionCheckboxes.value$.pipe(
						startWith({
							executePriceOpened: pipe(
								elementId,
								E.fold(constTrue, constFalse),
							),
							expireAtActive: pipe(
								expireAt,
								O.fold(constFalse, constTrue),
							),
						}),
					),
				),
			);

			const lotSize = lotSize$.pipe(
				filter(isSuccess),
				map(data => data.value),
			);

			const successfulBid$ = bid$.pipe(
				filter(isSuccess),
				filterMap(data => data.value),
			);

			const successfulAsk$ = ask$.pipe(
				filter(isSuccess),
				filterMap(data => data.value),
			);

			const step$ = combineLatestRD(minOrderIncrement$, lotSize$).pipe(
				mapRD(([minOrderIncrement, lotSize]) => minOrderIncrement * lotSize),
			);
			const amountPrecision$ = step$.pipe(
				mapRD(step => {
					const stepString = step.toString();
					return step < 1 ? stepString.slice(stepString.indexOf('.') + 1).length : 0;
				}),
			);

			const valueInit$: LiveData<Error, OrderValue> = pipe(
				elementId,
				E.fold(
					() =>
						orderAmount$.pipe(
							mapRD(value => ({ amount: value.toString(), side })),
							first(),
						),
					() =>
						amountDefaultOrderSize$.pipe(
							mapRD(minOrderSize => ({
								amount: minOrderSize.toString(),
								side,
							})),
							takeUntil(amountInput.value$),
						),
				),
			);

			const valueManuallyEntered$ = amountInput.value$.pipe(map(data => success<Error, OrderValue>(data)));

			const value$ = merge(valueInit$, valueManuallyEntered$);

			const successfulValue$ = value$.pipe(
				filter(isSuccess),
				map(data => data.value),
			);

			const amount$ = successfulValue$.pipe(map(data => data.amount));

			const lot$ = combineLatest(amount$, lotSize).pipe(
				map(([amount, lotSize]) => (amount.length === 0 ? O.none : O.some(Number(Big(amount).div(lotSize))))),
			);

			const spot$ = combineLatest(successfulValue$, successfulBid$, successfulAsk$).pipe(
				map(([{ side }, bid, ask]) => getAmount(side, bid, ask)),
				distinctUntilChanged(),
			);

			const valueSum$ = combineLatest(amount$, spot$, rate$, baseCurrency$, instrumentBaseCurrency$).pipe(
				map(([amount, spot, rate, accountBaseCurrency, instrumentBaseCurrency]) =>
					pipe(
						instrumentBaseCurrency,
						O.filter(baseCurrency => accountBaseCurrency === baseCurrency),
						O.fold(
							() =>
								Number(amount) *
								spot *
								pipe(
									rate,
									O.getOrElse(constant(1)),
								),
							() => Number(amount),
						),
					),
				),
			);

			const marginImpactQuantity$ = value$.pipe(mapRD(value => Number(value.amount) * getSignBySide(value.side)));

			const marginImpact$ = combineLatestRD(
				marginImpactQuantity$,
				formulaMarginImpact$,
				argumentsMarginImpact$,
			).pipe(
				mapRD(([quantity, formula, args]) =>
					E.tryCatch2v(
						() =>
							pipe(
								args,
								O.chain(value =>
									pipe(
										formula,
										O.map(data => new Function(value, data)),
									),
								),
								O.fold(
									constant(new Error('Something went wrong during margin impact counting')),
									data => data(quantity),
								),
								Number,
							),
						constant(new Error('Something went wrong during margin impact counting')),
					),
				),
			);

			const valueSettings$ = combineLatestRD(pipsSize$, precision$).pipe(
				mapRD(([pipsSize, precision]) => ({ pipsSize, precision })),
			);

			const currencySymbol$ = baseCurrency$.pipe(map(baseCurrency => currencyStringToSymbol(baseCurrency)));

			const baseData$ = combineLatestRD(bid$, ask$, valueSettings$, description$, step$, marginImpact$).pipe(
				mapRD(([bid, ask, valueSettings, description, step, marginImpact]) =>
					toOrderEntryData(
						bid,
						ask,
						valueSettings.pipsSize,
						valueSettings.precision,
						description,
						step,
						marginImpact,
					),
				),
			);

			const isShownValidErrors$ = isShownValidErrorsHandler.value$.pipe(
				startWith(false),
				distinctUntilChanged(),
				shareReplayWithDefaults(),
			);

			const successfulPipsSize$ = pipsSize$.pipe(
				filter(isSuccess),
				map(data => data.value),
			);

			const distance$ = combineLatest(limits$, successfulPipsSize$, successfulBid$, successfulAsk$).pipe(
				map(([limit, pipsSize, bid, ask]) =>
					countDistance(
						limit.limitStopDistanceType,
						limit.limitStopDistance,
						limit.limitStopDistanceInPercentOfSpread,
						Math.abs(bid - ask),
						pipsSize,
					),
				),
				distinctUntilChanged(),
			);

			const defaultDistance$ = combineLatest(limits$, successfulPipsSize$, successfulBid$, successfulAsk$).pipe(
				map(([limit, pipsSize, bid, ask]) =>
					countDistance(
						limit.limitStopDistanceType,
						limit.limitStopDistance * DEFAULT_DELTA_IN_PIPS,
						limit.limitStopDistanceInPercentOfSpread,
						Math.abs(bid - ask),
						pipsSize,
					),
				),
				distinctUntilChanged(),
			);

			const bidAskPrecision$ = combineLatest(successfulBid$, successfulAsk$, precision$);

			const precisionRaw$ = precision$.pipe(
				filter(isSuccess),
				map(precision => precision.value),
				startWith(0),
			);

			const defaultExecutionPrice$ = combineLatestRD(bid$, ask$).pipe(
				switchMap(bidAsk =>
					defaultDistance$.pipe(
						map(distance =>
							pipe(
								bidAsk.map(([bid, ask]) => ({ bid, ask })).toOption(),
								O.chain(data =>
									side === OrderEntrySide.Buy
										? pipe(
												data.ask,
												O.map(ask => ask + distance),
										  )
										: pipe(
												data.bid,
												O.map(bid => bid - distance),
										  ),
								),
							),
						),
						withLatestFrom(precisionRaw$),
						map(([value, precision]) =>
							pipe(
								value,
								O.map(val => roundPriceWithPrecision(val, precision)),
							),
						),
					),
				),
			);

			const defaultExecutionPriceWithOrder$ = combineLatest(stopPrice$, limitPrice$, defaultExecutionPrice$).pipe(
				map(([stopPrice, limitPrice, defaultPrice]) =>
					pipe(
						stopPrice,
						O.alt(() => limitPrice),
						O.alt(() => defaultPrice),
					),
				),
				distinctUntilChanged(setoidOptionNumber.equals),
			);

			const priceIncrementRaw$ = priceIncrement$.pipe(
				filter(isSuccess),
				map(priceIncrement => priceIncrement.value),
			);

			const validatedDefaultExecutionPrice$ = combineLatest(
				defaultExecutionPriceWithOrder$,
				bidAskPrecision$,
				distance$,
				precisionRaw$,
				priceIncrementRaw$,
				orderTypeDefault$,
			).pipe(
				map(([defaultExecutionPrice, [bid, ask], distance, precision, priceIncrement, orderType]) => {
					// need for correct type inference in initExecutionPrice$
					const validatedDefaultExecutionPrice: OrderEntryExecutionPrice = {
						priceValue: defaultExecutionPrice,
						executionPriceValidated: pipe(
							elementId,
							E.fold(
								() =>
									validateOrderEditPrice(
										defaultExecutionPrice,
										orderType,
										side,
										getAmount(side, bid, ask),
										distance,
										precision,
									),
								() =>
									validateExecutionPrice(
										defaultExecutionPrice,
										getAmount(side, bid, ask),
										distance,
										precision,
										priceIncrement,
									),
							),
						),
					};

					return validatedDefaultExecutionPrice;
				}),
				distinctUntilChanged(setoidOrderEntryExecutionPrice.equals),
			);

			const initExecutionPrice$ = validatedDefaultExecutionPrice$.pipe(
				switchMap(validatedDefaultExecutionPrice =>
					executionPrice.value$.pipe(startWith(validatedDefaultExecutionPrice)),
				),
				takeUntil(executionPrice.value$),
			);

			const disabledDaysMin$ = interval(1000).pipe(
				map(() => endOfYesterday()),
				startWith(endOfYesterday()),
				distinctUntilChanged((a, b) => isSameDay(a, b)),
			);

			const initExecutionDates = {
				expireAt: getExpireAt(),
				disabledDaysMin: endOfYesterday(),
			};

			const executionDatesValues$ = combineLatest(expireAt$, disabledDaysMin$).pipe(
				switchMap(([date, disabledDaysMin]) =>
					executionDates.value$.pipe(
						startWith({
							expireAt: pipe(
								date,
								O.fold(
									() => getExpireAt(),
									expireAt => ({
										day: O.some(expireAt.getDate()),
										month: O.some(expireAt.getMonth()),
										year: O.some(expireAt.getFullYear()),
									}),
								),
							),
							disabledDaysMin,
						}),
					),
				),
				takeUntil(executionDates.value$),
			);

			const executionFormValues$ = merge(initExecutionPrice$, executionPrice.value$);
			const executionDates$ = merge(executionDatesValues$, executionDates.value$);
			const orderTypeAndCheckboxes$ = combineLatest(orderTypeDefault$, executionCheckboxes$);

			const executionPrice$ = combineLatest(
				orderTypeAndCheckboxes$,
				executionFormValues$,
				bidAskPrecision$,
				distance$,
				precisionRaw$,
				priceIncrementRaw$,
			).pipe(
				filter(([[_, { executePriceOpened }]]) => executePriceOpened),
				map(([[orderType], executionData, [bid, ask], distance, precision, priceIncrement]) => ({
					...executionData,
					executionPriceValidated: pipe(
						elementId,
						E.fold(
							() =>
								validateOrderEditPrice(
									executionData.priceValue,
									orderType,
									side,
									getAmount(side, bid, ask),
									distance,
									precision,
								),
							() =>
								validateExecutionPrice(
									executionData.priceValue,
									getAmount(side, bid, ask),
									distance,
									precision,
									priceIncrement,
								),
						),
					),
				})),
				distinctUntilChanged(setoidOrderEntryExecutionPrice.equals),
				//shareReplayWithDefaults() prevents from unsubscribing, TODO: fix it
			);

			const executionPriceValue$ = executionPrice$.pipe(
				map(({ priceValue }) => priceValue),
				startWith(O.none),
			);

			// take profit / stop loss

			const orderPrice$ = combineLatest(executionCheckboxes$, executionPrice$).pipe(
				map(([checkboxes, form]) => (checkboxes.executePriceOpened ? form.priceValue : O.none)),
				startWith(O.none),
				distinctUntilChanged(setoidOptionNumber.equals),
			);

			const orderType$ = combineLatest(
				executionCheckboxes$,
				merge(initExecutionPrice$, executionPrice$),
				successfulValue$,
				successfulBid$,
				successfulAsk$,
				distance$,
			).pipe(
				map(([checkboxes, form, { side }, bid, ask, distance]) => {
					const currentPrice = getAmount(side, bid, ask);
					return checkboxes.executePriceOpened
						? getOrderType(form.priceValue, O.some(currentPrice), distance, side)
						: 'MARKET';
				}),
			);

			const spotAmountOrderPriceCombined$ = combineLatest(spot$, amount$, orderPrice$);

			const initTakeProfitStopLossValue$ = combineLatest(takeProfitFixedPrice$, stopLossFixedPrice$).pipe(
				switchMap(([takeProfitFixedPrice, stopLossFixedPrice]) =>
					takeProfitStopLossValueHandler.value$.pipe(
						startWith(
							getDefaultTakeProfitStopLossData({
								hasTP: pipe(
									takeProfitFixedPrice,
									O.fold(() => (plKind === 'profit' ? O.some(0) : O.none), data => O.some(data)),
								),
								hasSL: pipe(
									stopLossFixedPrice,
									O.fold(() => (plKind === 'loss' ? O.some(0) : O.none), data => O.some(data)),
								),
							}),
						),
					),
				),
			);

			const currentAndPreviousTakeProfitStopLoss$ = combineLatest(
				initTakeProfitStopLossValue$,
				takeProfitFixedPrice$,
				stopLossFixedPrice$,
			);

			const takeProfitStopLossPreCombineData1$ = combineLatest(
				currentAndPreviousTakeProfitStopLoss$,
				distance$,
				rate$,
			);

			const takeProfitStopLossPreCombineData2$ = combineLatest(
				priceIncrement$,
				spotAmountOrderPriceCombined$,
				successfulValue$,
				bidAskPrecision$,
				orderType$,
			);

			const takeProfitStopLossCalculatedData$ = combineLatest(
				takeProfitStopLossPreCombineData1$,
				takeProfitStopLossPreCombineData2$,
			).pipe(
				filter(
					([[[{ takeProfitData, stopLossData }]]]) => takeProfitData.isSelected || stopLossData.isSelected,
				),
				// prepare options for getTakeProfitStopLossValue
				map(
					([
						[[{ takeProfitData, stopLossData }, prevTakeProfitValue, prevStopLossValue], distance, rate],
						[priceIncrement, [spotValue, amount, orderPrice], { side }, [bid, ask, precision], orderType],
					]): TakeProfitStopLossOptions => ({
						currentTakeProfitData: takeProfitData,
						currentStopLossData: stopLossData,
						prevTakeProfitFixedPrice: prevTakeProfitValue,
						prevStopLossFixedPrice: prevStopLossValue,
						parentOrderPrice: pipe(
							orderPrice,
							O.getOrElse(() => spotValue),
						),
						bid,
						ask,
						orderType,
						amount: Number(amount),
						side,
						priceIncrement: priceIncrement.getOrElse(0),
						precision: precision.getOrElse(5),
						rate: pipe(
							rate,
							O.getOrElse(constant(1)),
						),
						orderKind: OrderKind.Order,
						distance,
					}),
				),
				map(data => getTakeProfitStopLossValue(data)),
			);

			const takeProfitStopLossData$ = merge(initTakeProfitStopLossValue$, takeProfitStopLossCalculatedData$);
			const amountValidated$ = combineLatestRD(
				value$,
				minOrderSize$,
				maxOrderSize$,
				minOrderIncrement$,
				lotSize$,
			).pipe(
				mapRD(([value, minOrderSize, maxOrderSize, minOrderIncrement, lotSize]) =>
					validateAmount(
						value.amount,
						minOrderSize * lotSize,
						maxOrderSize * lotSize,
						minOrderIncrement * lotSize,
					),
				),
			);

			const successfulAmountValidated$ = amountValidated$.pipe(
				filter(isSuccess),
				map(data => data.value),
			);

			const validationStatus$ = combineLatest(
				takeProfitStopLossData$,
				executionCheckboxes$,
				merge(initExecutionPrice$, executionPrice$),
				successfulAmountValidated$,
			).pipe(
				tap(([takeProfitStopLossData, { executePriceOpened }, executionPrice, amountValidated]) => {
					const {
						takeProfitData: { isSelected: isSelectedTP, validatedPrice: validatedPriceTP },
						stopLossData: { isSelected: isSelectedSL, validatedPrice: validatedPriceSL },
					} = takeProfitStopLossData;

					const { executionPriceValidated } = executionPrice;
					const isTPPriceValid = isSelectedTP ? E.isRight(validatedPriceTP) : true;
					const isSLPriceValid = isSelectedSL ? E.isRight(validatedPriceSL) : true;
					const isExecPriceValid = executePriceOpened ? E.isRight(executionPriceValidated) : true;

					isValidHandler.handle(
						isTPPriceValid && isSLPriceValid && isExecPriceValid && E.isRight(amountValidated),
					);
				}),
			);

			const sendData$ = combineLatest(
				successfulValue$,
				executionPriceValue$,
				executionDates$,
				executionCheckboxes$,
			).pipe(
				map(([value, priceValue, executionDates, executionCheckboxes]) => ({
					...value,
					expireAt: toExpireAt(executionDates, executionCheckboxes),
					executionPrice: executionCheckboxes.executePriceOpened ? priceValue : O.none,
				})),
			);

			const takeProfitStopLossLiveData$ = takeProfitStopLossData$.pipe(
				map(value => success<Error, OrderEntryTakeProfitStopLossData>(value)),
				shareReplayWithDefaults(),
			);

			const onToggleMoreOptions = (value: boolean) => moreOptionsHandle.handle(value);

			const isValid$ = isValidHandler.value$.pipe(
				startWith(true),
				distinctUntilChanged(),
				shareReplayWithDefaults(),
			);

			const onSend = (isValid: boolean) => () => {
				isShownValidErrorsHandler.handle(!isValid);
				isValid && send.handle(undefined);
			};

			const sendEffect$ = send.value$.pipe(
				withLatestFrom(instrumentId$, sendData$, bidAskPrecision$, accountId$, takeProfitStopLossData$),
				withLatestFrom(requestId$, orderChainId$, distance$),
				map(
					([
						[
							_,
							instrumentId,
							{ amount, expireAt, executionPrice, side: orderEntrySide },
							[bid, ask, precision],
							accountId,
							{ takeProfitData, stopLossData },
						],
						requestId,
						orderChainId,
						distance,
					]) => {
						const takeProfitPayload = mapTakeProfitStopLossPayload(takeProfitData, precision.getOrElse(0));
						const stopLossPayload = mapTakeProfitStopLossPayload(stopLossData, precision.getOrElse(0));
						const side = toOrderTOSide(orderEntrySide);

						return {
							bid,
							ask,
							requestId,
							orderChainId,
							distance,
							takeProfitPayload,
							stopLossPayload,
							side,
							instrumentId,
							accountId,
							expireAt,
							executionPrice,
							amount,
						};
					},
				),
				share(),
			);

			const sendUpdateOrder$ = sendEffect$.pipe(
				filter(() => E.isLeft(elementId)),
				tap(data =>
					orderStore.updateOrder({
						orderId: elementId.value,
						data: {
							requestId: data.requestId,
							accountId: data.accountId,
							instrumentId: data.instrumentId,
							bid: data.bid,
							ask: data.ask,
							quantity: Number(data.amount),
							side: O.some(data.side),
							expireAt: data.expireAt,
							executionPrice: data.executionPrice,
							takeProfit: data.takeProfitPayload,
							stopLoss: data.stopLossPayload,
							orderChainId: data.orderChainId,
							distance: data.distance,
						},
					}),
				),
			);

			const sendCreateOrder$ = sendEffect$.pipe(
				filter(() => E.isRight(elementId)),
				switchMap(data =>
					orderStore.place(
						data.accountId,
						data.instrumentId,
						data.bid,
						data.ask,
						Number(data.amount),
						O.some(data.side),
						data.expireAt,
						data.executionPrice,
						data.takeProfitPayload,
						data.stopLossPayload,
						data.distance,
					),
				),
			);

			const isEmitingOrdersAllowedRaw$ = merge(
				send.value$.pipe(mapTo(false)),
				orderStore.error$.pipe(map(O.isSome)),
				send.value$.pipe(
					mapTo(true),
					delay(DELAY_BEFORE_NEXT_SUBMIT_ATTEMPT),
				),
			);

			const isEmitingOrdersAllowed$ = isEmitingOrdersAllowedRaw$.pipe(map(v => success<Error, boolean>(v)));

			const isFundsEnough$ = combineLatestRD(
				marginImpact$,
				availableFunds$.pipe(map(val => success<Error, Option<number>>(val))),
			).pipe(
				mapRD(
					([marginImpact, availableFunds]) =>
						E.isRight(marginImpact) &&
						pipe(
							availableFunds,
							O.fold(constFalse, availableFunds => availableFunds > marginImpact.value),
						),
				),
				takeUntil(isEmitingOrdersAllowedRaw$),
			);

			const errorEffect$ = race(
				orderStore.error$.pipe(map(O.isNone)),
				isEmitingOrdersAllowedRaw$.pipe(filter(identity)),
			).pipe(
				tap(isSuccess => {
					if (isSuccess) {
						pipe(
							elementId,
							E.fold(
								orderId => e.router.push(href(routes.orders.order.root, { orderId: Number(orderId) })),
								categoryId =>
									e.router.push(href(routes.trade.category.symbol.root, { categoryId, symbol })),
							),
						);
					}
				}),
			);

			const orderEntryValues$: LiveData<Error, OrderEntryValues> = combineLatestRD(
				value$,
				executionFormValues$.pipe(map(val => success<Error, OrderEntryExecutionPrice>(val))),
				executionCheckboxes$.pipe(map(val => success<Error, OrderEntryExecutionStatusFlags>(val))),
				executionDates$.pipe(map(val => success<Error, OrderEntryExecutionDate>(val))),
				takeProfitStopLossLiveData$,
			).pipe(
				mapRD(
					([
						value,
						execFormValue,
						executionCheckboxes,
						executionDates,
						{ takeProfitData, stopLossData },
					]) => ({
						value: value.amount,
						execFormValue: execFormValue.priceValue,
						isExecEnabled: executionCheckboxes.executePriceOpened,
						isExpireChecked: executionCheckboxes.expireAtActive,
						expireAtDate: convertDateInputValueToDate(executionDates.expireAt),
						isTPEnabled: takeProfitData.isSelected,
						takeProfitValue: takeProfitData.isSelected ? takeProfitData.value : 0,
						isSLEnabled: stopLossData.isSelected,
						stopLossValue: stopLossData.isSelected ? stopLossData.value : 0,
					}),
				),
				share(),
			);

			const takeProfitStopLossPrices$ = combineLatestRD(
				takeProfitFixedPrice$.pipe(map(value => success<Error, Option<number>>(value))),
				stopLossFixedPrice$.pipe(map(value => success<Error, Option<number>>(value))),
			);

			const initOrderEntryValues$ = combineLatestRD(orderEntryValues$, takeProfitStopLossPrices$).pipe(
				mapRD(([oeValues, [takeProfit, stopLoss]]) => ({
					...oeValues,
					isTPEnabled: O.isSome(takeProfit),
					takeProfitValue: pipe(
						takeProfit,
						O.getOrElse(constant(0)),
					),
					isSLEnabled: O.isSome(stopLoss),
					stopLossValue: pipe(
						stopLoss,
						O.getOrElse(constant(0)),
					),
				})),
				first(),
			);

			const isOrderEntryValuesChanged$ = combineLatestRD(initOrderEntryValues$, orderEntryValues$).pipe(
				mapRD(([initVal, currVal]) => !setoidOEValues.equals(initVal, currVal)),
				distinctUntilChanged((a, b) => setoidBooleanRD.equals(a, b)),
			);

			return new Sink<OrderEntryViewModel>(
				{
					symbol,
					bid$,
					ask$,
					lot$,
					priceIncrement$,
					description$,
					quantity$,
					accountBalance$,
					valueSum$,
					quantityIncrement$,
					value$,
					amountPrecision$,
					data$: baseData$,
					onValueChange: amountInput.handle,
					onSend,
					moreOptionsOpened$: moreOptions$,
					precision$,
					onToggleMoreOptions,
					executionCheckboxes$,
					onToggleExecutionCheckboxes,
					amountValidated$,
					amountMinOrderSize$,
					amountMaxOrderSize$,
					executionPrice$,
					initExecutionDates,
					executionDates$,
					onChangeExecutionPrice,
					onChangeExecutionDates,
					currencySymbol$,
					takeProfitStopLossValue$: takeProfitStopLossLiveData$,
					onTakeProfitStopLossValueChange: takeProfitStopLossValueHandler.handle,
					error$: orderStore.error$,
					isValid$,
					isShownValidErrors$,
					isFundsEnough$,
					isEmitingOrdersAllowed$,
					isOrderEntryValuesChanged$,
					unitName$,
				},
				merge(sendEffect$, sendUpdateOrder$, sendCreateOrder$, validationStatus$, errorEffect$),
			);
		};
	},
);
