import * as React from 'react';
import { Router, href, parse } from '../../../utils/router.utils';
import { PostMessageClient } from '../../../api/post-message.client';
import { asks } from '@devexperts/rx-utils/dist/context.utils';
import { WatchlistStore, WatchlistEntity } from '../../../store/watchlist/watchlist.store';
import { map, filter, tap, switchMap } from 'rxjs/operators';
import { getPath, routes } from '../../routes';
import {
	isRedirectToTradeWithSymbol,
	isRedirectToRealAccount,
	isClosePopup,
	LocalRedirectPostMessageTO,
} from '../../../api/post-message/post-message.to';
import { not } from 'fp-ts/lib/function';
import { combineLatest, merge } from 'rxjs';
import { Option } from 'fp-ts/lib/Option';
import * as O from 'fp-ts/lib/Option';
import { findFirst } from 'fp-ts/lib/Array';
import { empty } from 'fp-ts/lib/Record';
import { isSuccess, success } from '@devexperts/remote-data-ts';
import { isProductionBuild } from '../../../utils/node-env.utils';
import { EnvViewModel } from '../../../view-model/env/env.view-model';
import { pipe } from 'fp-ts/lib/pipeable';

type RedirectListenerContext = {
	router: Router;
	watchlistStore: WatchlistStore;
	postMessageClient: PostMessageClient;
	envViewModel: EnvViewModel;
};

export const RedirectListener = asks((context: RedirectListenerContext) => {
	const {
		router,
		watchlistStore: { categories$ },
		postMessageClient: { postMessage$ },
		envViewModel,
	} = context;

	const directLinkRedirect$ = categories$.pipe(
		map(categoriesRD =>
			categoriesRD.map(categories => {
				const symbolAndActionHref = pipe(
					parse(routes.trade.symbol.actionId.root, router.state.location.pathname),
					O.map(([{ symbol, action }]) => ({
						symbol,
						action,
					})),
					O.map(params => getValueBySymbol(params.symbol, categories, params.action)),
					O.filter(O.isSome),
					O.map(data => data.value),
					O.map(hrefValue => href(routes.trade.category.symbol.action.root, hrefValue)),
				);

				const symbolHref = pipe(
					parse(routes.trade.symbol.root, router.state.location.pathname),
					O.map(([{ symbol }]) => ({
						symbol,
					})),
					O.map(params => getValueBySymbol(params.symbol, categories)),
					O.filter(O.isSome),
					O.map(data => data.value),
					O.map(hrefValue => href(routes.trade.category.symbol.root, hrefValue)),
				);

				return pipe(
					symbolAndActionHref,
					O.alt(() => symbolHref),
				);
			}),
		),
		tap(href => {
			if (href.isSuccess() && O.isSome(href.value)) {
				router.replace(href.value.value);
			}
		}),
	);

	const messagesWithMatch$ = postMessage$.pipe(
		filter(not(isClosePopup)),
		filter(not(isRedirectToRealAccount)),
		map(message => ({
			...message,
			match: getPath(message as LocalRedirectPostMessageTO),
		})),
	);

	const toTradeWithSymbolPostMessage$ = messagesWithMatch$.pipe(filter(isRedirectToTradeWithSymbol));

	const toTradeWithSymbolHref$ = combineLatest(toTradeWithSymbolPostMessage$, categories$).pipe(
		map(([message, categories]) =>
			categories.map(categories =>
				pipe(
					getValueBySymbol(message.payload.symbol, categories, message.payload.action),
					O.map(hrefValue =>
						hrefValue.action
							? href(routes.trade.category.symbol.action.root, hrefValue)
							: href(routes.trade.category.symbol.root, hrefValue),
					),
				),
			),
		),
	);
	const redirectToRealAccount$ = postMessage$.pipe(
		filter(isRedirectToRealAccount),
		map(message => window.location.replace(message.payload.url)),
	);

	const otherPostMessages$ = messagesWithMatch$.pipe(filter(not(isRedirectToTradeWithSymbol)));

	const otherHref$ = otherPostMessages$.pipe(
		map(other => success(O.some(href(getPath(other as LocalRedirectPostMessageTO), empty)))),
	);

	const localRedirects$ = merge(toTradeWithSymbolHref$, otherHref$).pipe(
		tap(href => {
			if (href.isSuccess() && O.isSome(href.value)) {
				router.replace(href.value.value);
			}
		}),
	);

	const DYListener$ = envViewModel.loadedScripts$.pipe(
		filter(isSuccess),
		tap(DYPush),
		switchMap(() => {
			return router.state$.pipe(tap(DYPush));
		}),
	);

	const redirectEffect$ = merge(localRedirects$, directLinkRedirect$, redirectToRealAccount$, DYListener$);

	return class RedirectListener extends React.Component {
		subscription = redirectEffect$.subscribe();

		componentWillUnmount() {
			this.subscription.unsubscribe();
		}

		render() {
			return null;
		}
	};
});

const getValueBySymbol = (
	symbol: string,
	categories: WatchlistEntity[],
	action = '',
): Option<{ categoryId: string; symbol: string; action: string }> =>
	pipe(
		categories,
		findFirst(category => category.symbols.includes(symbol)),
		O.map(category => ({
			categoryId: category.id,
			symbol,
			action,
		})),
	);

const DYPush = () => {
	if (isProductionBuild) {
		DY.API('spa', {
			context: {
				type: 'OTHER',
			},
			url: window.location.href,
			countAsPageview: true,
		});
	}
};
