import { distinctUntilChanged, map, first } from 'rxjs/operators';
import { Option } from 'fp-ts/lib/Option';
import * as O from 'fp-ts/lib/Option';
import { Big } from 'big.js';
import { LiveData, LiveDataOption } from '@devexperts/rx-utils/dist/rd/live-data.utils';
import { ask, combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { switchMapRD } from '@devexperts/rx-utils/dist/rd/operators/switchMapRD';
import { mapRD } from '@devexperts/rx-utils/dist/rd/operators/mapRD';
import { combineLatestRD } from '@devexperts/rx-utils/dist/rd/operators/combineLatestRD';
import { fromOption, success } from '@devexperts/remote-data-ts';
import { PositionsStore } from '../../store/positions/positions.store';
import { InstrumentStore } from '../../store/instrument/instrument.store';
import { MetricsStore } from '../../store/metrics/metrics.store';
import { MarketDataStore } from '../../store/market-data/market-data.store';
import { ConversionRateStore } from '../../store/conversion-rate/conversion-rate.store';
import { AccountViewModel } from '../account/account.view-model';
import { PositionSide } from '../../model/positions/position-details.model';
import { getCurrentPrice } from '../../model/takeprofit-stoploss/takeprofit-stoploss.utils';
import { distinctRDOptionNumber, distinctRDNumber, distinctRDOptionNumberArray } from '../../utils/setoid.utils';
import { pipe } from 'fp-ts/lib/pipeable';

export type PositionDetailsViewModel = {
	positionCode$: LiveDataOption<Error, string>;
	symbol$: LiveData<Error, string>;
	side$: LiveData<Error, PositionSide>;
	precision$: LiveData<Error, number>;
	pipsSize$: LiveData<Error, number>;
	openPrice$: LiveData<Error, number>;
	currentPriceAndDirection$: LiveData<Error, Option<number>[]>;
	quantity$: LiveData<Error, number>;
	sizeInLots$: LiveData<Error, number>;
	currentValue$: LiveDataOption<Error, number>;
	openDateTime$: LiveData<Error, Date>;
	takeProfitValue$: LiveDataOption<Error, number>;
	stopLossValue$: LiveDataOption<Error, number>;
	swap$: LiveDataOption<Error, number>;
	commission$: LiveDataOption<Error, number>;
};

type PositionDetailsViewModelContext = {
	positionsStore: PositionsStore;
	instrumentStore: InstrumentStore;
	metricsStore: MetricsStore;
	marketDataStore: MarketDataStore;
	conversionRateStore: ConversionRateStore;
	accountViewModel: AccountViewModel;
};

type CreatePositionDetailsViewModel = (uid: string) => PositionDetailsViewModel;

export const createPositionDetailsViewModel = combineContext(
	ask<PositionDetailsViewModelContext>(),
	(positionDetailsContext): CreatePositionDetailsViewModel => uid => {
		const {
			positionsStore: { getPositionByUid },
			instrumentStore: { getById, getBySymbol },
			metricsStore: { getMetricsByUid },
			marketDataStore: { getDataBySymbol },
			conversionRateStore: { getRateBySymbol },
			accountViewModel,
		} = positionDetailsContext;

		const metricsData$ = getMetricsByUid(uid);
		const position$ = getPositionByUid(uid);

		// positions can be partially closed, so amount may vary
		const positionAmount$ = position$.pipe(
			mapRD(position => position.quantity),
			distinctRDNumber,
		);
		const positionAmountAbs$ = position$.pipe(
			mapRD(position => Math.abs(position.quantity)),
			distinctRDNumber,
		);

		const instrumentData$ = position$.pipe(
			switchMapRD(({ positionKey: { instrumentId } }) => getById(instrumentId)),
			mapRD(instrument => ({
				symbol: instrument.symbol,
				lotSize: instrument.lotSize,
				precision: instrument.precision,
				pipsSize: instrument.pipsSize,
			})),
			first(),
		);

		const positionSide$ = positionAmount$.pipe(
			mapRD(amount => (amount > 0 ? PositionSide.Buy : PositionSide.Sell)),
			first(),
		);

		const extractedTakeProfitStopLoss$ = position$.pipe(
			mapRD(position => ({
				maybeTakeProfitValue: pipe(
					position.takeProfit,
					O.chain(takeProfit => takeProfit.fixedPrice),
				),
				maybeStopLossValue: pipe(
					position.stopLoss,
					O.chain(stopLoss => stopLoss.fixedPrice),
				),
			})),
		);

		const quote$ = instrumentData$.pipe(switchMapRD(({ symbol }) => getDataBySymbol(symbol)));

		const ask$ = quote$.pipe(
			mapRD(({ lastQuote }) =>
				pipe(
					lastQuote,
					O.chain(({ ask }) => ask),
				),
			),
			distinctRDOptionNumber,
		);
		const bid$ = quote$.pipe(
			mapRD(({ lastQuote }) =>
				pipe(
					lastQuote,
					O.chain(({ bid }) => bid),
				),
			),
			distinctRDOptionNumber,
		);
		const askDirection$ = quote$.pipe(
			mapRD(({ lastQuote }) =>
				pipe(
					lastQuote,
					O.map(({ askDirection }) => askDirection),
				),
			),
			distinctRDOptionNumber,
		);
		const bidDirection$ = quote$.pipe(
			mapRD(({ lastQuote }) =>
				pipe(
					lastQuote,
					O.map(({ bidDirection }) => bidDirection),
				),
			),
			distinctRDOptionNumber,
		);

		const currentPriceDirectionTuple$ = combineLatestRD(
			positionSide$,
			ask$,
			bid$,
			askDirection$,
			bidDirection$,
		).pipe(
			mapRD(([side, ask, bid, askDirection, bidDirection]) => {
				switch (side) {
					case PositionSide.Sell:
						return [ask, askDirection];
					case PositionSide.Buy:
						return [bid, bidDirection];
				}
			}),
			distinctRDOptionNumberArray,
		);

		const symbol$ = instrumentData$.pipe(mapRD(data => data.symbol));

		const baseCurrency$ = accountViewModel.current$.pipe(
			distinctUntilChanged((a, b) => a.baseCurrency === b.baseCurrency),
			map(currentAccount => success<Error, string>(currentAccount.baseCurrency)),
		);

		const conversionRate$ = symbol$.pipe(
			switchMapRD(symbol => {
				const currency$ = getBySymbol(symbol).pipe(
					map(instrumentRD =>
						instrumentRD.chain(instrument =>
							fromOption(instrument.currency, () => new Error('There is no currency in this instrument')),
						),
					),
				);
				return combineLatestRD(currency$, baseCurrency$).pipe(
					switchMapRD(([currency, baseCurrency]) => getRateBySymbol(currency, baseCurrency)),
				);
			}),
		);

		const value$ = combineLatestRD(symbol$, positionAmount$).pipe(
			switchMapRD(([symbol, quantity]) =>
				combineLatestRD(getDataBySymbol(symbol), conversionRate$).pipe(
					mapRD(([marketData, conversionRateData]) => {
						const { conversionRate } = conversionRateData;
						const isQuantityPositive = quantity > 0;
						return pipe(
							conversionRate,
							O.chain(rate => {
								const currentPrice = getCurrentPrice(isQuantityPositive)(marketData);
								return pipe(
									currentPrice,
									O.chain(price =>
										pipe(
											rate.value,
											O.map(value => Math.abs(quantity) * price * value),
										),
									),
								);
							}),
						);
					}),
				),
			),
			distinctRDOptionNumber,
		);

		return {
			positionCode$: position$.pipe(
				mapRD(({ positionKey: { positionCode } }) => positionCode),
				first(),
			),
			symbol$: instrumentData$.pipe(mapRD(({ symbol }) => symbol)),
			side$: positionSide$,
			precision$: instrumentData$.pipe(mapRD(({ precision }) => precision)),
			pipsSize$: instrumentData$.pipe(mapRD(({ pipsSize }) => pipsSize)),
			openPrice$: position$.pipe(
				mapRD(({ costBasis }) => Math.abs(costBasis)),
				first(),
			),
			currentPriceAndDirection$: currentPriceDirectionTuple$,
			quantity$: positionAmountAbs$,
			sizeInLots$: combineLatestRD(positionAmountAbs$, instrumentData$).pipe(
				mapRD(([positionAmount, { lotSize }]) => Number(Big(positionAmount).div(lotSize))),
				distinctRDNumber,
			),
			currentValue$: value$,
			openDateTime$: position$.pipe(
				mapRD(({ time }) => time),
				first(),
			),
			takeProfitValue$: extractedTakeProfitStopLoss$.pipe(
				mapRD(({ maybeTakeProfitValue }) => maybeTakeProfitValue),
				distinctRDOptionNumber,
			),
			stopLossValue$: extractedTakeProfitStopLoss$.pipe(
				mapRD(({ maybeStopLossValue }) => maybeStopLossValue),
				distinctRDOptionNumber,
			),
			swap$: metricsData$.pipe(
				mapRD(({ swap }) => swap),
				distinctRDOptionNumber,
			),
			commission$: metricsData$.pipe(
				mapRD(({ commission }) => commission),
				distinctRDOptionNumber,
			),
		};
	},
);
