import { withRXWithDefaults } from '../../../../utils/withRXWithDefaults';
import { ReloginViewModel, createReloginViewModel } from '../../../../view-model/relogin/relogin.view-model';
import { ask, combineContext, deferContext } from '@devexperts/rx-utils/dist/context.utils';
import { ReloginPopup } from '../../components/relogin-popup/relogin-popup.component';
import { runOnMount } from '../../../../utils/react.utils';

type ReloginPopupContainerContext = {
	reloginViewModel: ReloginViewModel;
};

const Container = combineContext(ask<ReloginPopupContainerContext>(), e =>
	withRXWithDefaults(ReloginPopup)(() => {
		const { shouldOpenPopup$ } = e.reloginViewModel;

		return {
			props: {
				isPopupOpened: shouldOpenPopup$,
			},
			defaultProps: {
				isPopupOpened: false,
			},
		};
	}),
);

export const ReloginPopupContainer = combineContext(
	deferContext(Container, 'reloginViewModel'),
	createReloginViewModel,
	(Container, createReloginViewModel) =>
		runOnMount(Container, () => () => ({
			reloginViewModel: createReloginViewModel(),
		})),
);
