import * as React from 'react';
import { Component, Fragment, ReactNode, createRef } from 'react';
import { withTranslation, WithTranslation } from 'react-i18next';
import * as cn from 'classnames';
import { RemoteData } from '@devexperts/remote-data-ts';
import { combineContext } from '@devexperts/rx-utils/dist/context.utils';
import throttle from '@devexperts/utils/dist/function/throttle';
import { RemoteDataState } from '../../../../ui-common/remote-data-state/remote-data-state.component';
import { CategoryList } from './categories.model';
import { routes } from '../../../../ui-common/routes';
import { RouteLink } from '../../../../ui-common/route-link/containers/route-link.container';
import { Scrollable } from '../../../../ui-common/scrollable/scrollable.component';
import { Link } from '../../../../ui-common/link/link.component';
import { tspan } from '../../../../utils/i18n.utils';
import { pipe } from 'fp-ts/lib/pipeable';
import * as O from 'fp-ts/lib/Option';
import * as scrollableCss from './theme/scrollable.child.module.css';
import * as css from './theme/categories.module.css';

export type TCategoriesListProps = {
	data: RemoteData<Error, CategoryList[]>;
};

const THROTTLE_DELAY = 100;

const linkTheme = {
	container: css.link,
};

const activeLinkTheme = {
	container: cn(css.link, css.link_active),
};

type CategoriesListState = {
	isEndOfScroll: boolean;
};

export const CategoriesList = combineContext(RouteLink, RouteLink =>
	withTranslation()(
		class CategoriesList extends Component<TCategoriesListProps & WithTranslation, CategoriesListState> {
			private ref = createRef<HTMLDivElement>();
			readonly state = {
				isEndOfScroll: false,
			};

			constructor(props: TCategoriesListProps & WithTranslation) {
				super(props);
				this.handleScroll = throttle(this.handleScroll, THROTTLE_DELAY);
			}

			render() {
				return <RemoteDataState data={this.props.data} success={this.renderSuccess} />;
			}

			private renderSuccess = (data: CategoryList[]): JSX.Element => {
				return (
					<Scrollable theme={scrollableCss} onScroll={this.handleScroll}>
						<div
							ref={this.ref}
							className={cn(css.container, {
								[css.container_hasShadow]: !this.state.isEndOfScroll,
							})}>
							{data.map(this.renderGroup)}
						</div>
					</Scrollable>
				);
			};

			renderGroup = (list: CategoryList, index: number): ReactNode =>
				pipe(
					list.categories,
					O.map(categories => (
						<Fragment key={index}>
							{pipe(
								list.title,
								O.map(title => <div className={css.title}>{this.localizeCategoryTitle(title)}</div>),
								O.toNullable,
							)}
							<div className={css.categories}>
								{categories.toArray().map(category => this.renderCategory(category.id, category.name))}
							</div>
						</Fragment>
					)),
					O.toNullable,
				);

			private renderCategory = (categoryId: string, name: string): ReactNode => (
				<RouteLink
					key={categoryId}
					to={routes.trade.category.root}
					value={{ categoryId }}
					render={({ href, onNavigate, isActive }) => (
						<Link
							theme={isActive ? activeLinkTheme : linkTheme}
							href={href}
							onClick={this.handleClick(isActive, onNavigate)}>
							{this.localizeCategoryName(name)}
						</Link>
					)}
				/>
			);

			private localizeCategoryTitle = (title: string) => {
				switch (title) {
					case 'Trends':
						return tspan('categoriesTrends', 'Trends');
					case 'Categories':
						return tspan('categoriesCategories', 'Categories');
					default:
						return title;
				}
			};

			private localizeCategoryName = (name: string) => {
				switch (name) {
					case 'Favorites':
						return tspan('categoriesFavorites', 'Favorites');
					case 'Most rising':
						return tspan('categoriesMostRising', 'Most rising');
					case 'Most falling':
						return tspan('categoriesMostFalling', 'Most falling');
					case 'Most buying':
						return tspan('categoriesMostBuying', 'Most buying');
					case 'Most selling':
						return tspan('categoriesMostSelling', 'Most selling');
					default:
						return name;
				}
			};

			handleClick = (isActive: boolean, onNavigate: () => void) => (e: React.MouseEvent<HTMLAnchorElement>) => {
				e.preventDefault();
				!isActive && onNavigate();
			};

			private handleScroll = (scrollLeft: number, scrollTop: number): void => {
				const { current } = this.ref;
				const { isEndOfScroll: stateIsEndOfScroll } = this.state;

				if (current && current.parentNode) {
					const { scrollHeight, offsetHeight } = current.parentNode.parentNode as HTMLDivElement;
					const isEndOfScroll: boolean = offsetHeight + scrollTop >= scrollHeight;

					if (stateIsEndOfScroll !== isEndOfScroll) {
						this.setState({ isEndOfScroll });
					}
				}
			};
		},
	),
);
