import { Option } from 'fp-ts/lib/Option';
import { string, number, boolean, union, literal, type } from 'io-ts';
import { createOptionFromNullable } from 'io-ts-types';

export type AggregationPeriodTO = {
	name: Option<string>;
	seconds: Option<number>;
	intraday: Option<boolean>;
	timeUnit: Option<'SECOND' | 'MINUTE' | 'HOUR' | 'DAY' | 'WEEK' | 'MONTH' | 'YEAR'>;
};
export const AggregationPeriodTOIO = type({
	name: createOptionFromNullable(string),
	seconds: createOptionFromNullable(number),
	intraday: createOptionFromNullable(boolean),
	timeUnit: createOptionFromNullable(
		union([
			literal('SECOND'),
			literal('MINUTE'),
			literal('HOUR'),
			literal('DAY'),
			literal('WEEK'),
			literal('MONTH'),
			literal('YEAR'),
		]),
	),
});
