import * as React from 'react';
import { PureComponent } from 'react';
import { withTranslation, WithTranslation } from 'react-i18next';
import { AutosizedTable as Table } from '../../../../ui-common/autosized-table-ava/autosized-table.component';
import { ListChildComponentProps } from 'react-window';
import { RemoteData } from '@devexperts/remote-data-ts';
import { RemoteDataState } from '../../../../ui-common/remote-data-state/remote-data-state.component';
import { isEmpty } from 'fp-ts/lib/Array';
import { combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { OrderEntityContainer } from '../../containers/order-entity/order-entity.container';
import { getColumnsConfig } from './orders-table.utils';
import { OrdersTableColumnNames, TableSize, ConfigItem } from './orders-table.model';
import { tspan } from '../../../../utils/i18n.utils';

export type OrdersTableProps = {
	data: RemoteData<Error, number[]>;
	config: ConfigItem[];
	tableSize: TableSize;
};

export const OrdersTable = combineContext(OrderEntityContainer, OrderEntityContainer =>
	withTranslation()(
		class OrdersTable extends PureComponent<OrdersTableProps & WithTranslation> {
			private getTranslatedColumnNames = (): OrdersTableColumnNames => ({
				symbol: tspan('ordersTableColumnSymbol', 'Symbol'),
				side: tspan('ordersTableColumnSide', 'Side'),
				quantity: tspan('ordersTableColumnQuantity', 'Quantity'),
				triggerPrice: tspan('ordersTableColumnTriggerPrice', 'Trigger Price'),
				distance: tspan('ordersTableColumnDistance', 'Distance'),
				stopLoss: tspan('ordersTableColumnStopLoss', 'Stop Loss'),
				takeProfit: tspan('ordersTableColumnTakeProfit', 'Take Profit'),
				validUntil: tspan('ordersTableColumnValidUntil', 'Valid Until'),
			});

			public render() {
				const { data } = this.props;
				return <RemoteDataState data={data} success={this.renderSuccess} noData={data => isEmpty(data)} />;
			}

			private renderSuccess = (data: number[]) => {
				const columnNames = this.getTranslatedColumnNames();
				const { config } = this.props;

				return (
					<Table
						itemCount={data.length}
						itemSize={60}
						itemData={data}
						itemKey={this.getItemKey}
						columns={getColumnsConfig(config, columnNames)}>
						{this.renderRow}
					</Table>
				);
			};

			private getItemKey = (rowIndex: number, data: number) => data[rowIndex];

			private renderRow = (props: ListChildComponentProps) => (
				<OrderEntityContainer
					config={this.props.config}
					tableSize={this.props.tableSize}
					rowData={props.data[props.index]}
					{...props}
				/>
			);
		},
	),
);
