import * as React from 'react';
import { PureComponent } from 'react';
import { Option } from 'fp-ts/lib/Option';
import * as O from 'fp-ts/lib/Option';
import { RemoteData } from '@devexperts/remote-data-ts';
import { combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { routes } from '../../../../ui-common/routes';
import { Route } from '../../../../ui-common/route/containers/route.container';
import { MatchType } from '../../../../utils/router.utils';
import { RemoteDataState } from '../../../../ui-common/remote-data-state/remote-data-state.component';
import { WatchlistContainer } from '../../../watchlist/containers/watchlist/watchlist.container';
import { OrderEntryPanelContainer } from '../../../order-entry/containers/order-entry-panel/order-entry-panel.container';
import { SymbolInfoContainer } from '../../../info-panel/containers/symbol-info/symbol-info.container';
import { CategoriesContainer } from '../../../categories/containers/categories/categories.container';
import { ChartLoaderContainer } from '../../../chart/containers/chart-loader/chart-loader.container';
import { SymbolSearchContainer } from '../../containers/symbol-search/symbol-search.container';
import { OrderEntrySide } from '../../../order-entry/components/order-entry-panel/order-entry-panel.model';
import { Plugin } from '../../../../ui-common/chart/plugin/plugin.model';
import { CurrentPriceLinePlugin } from '../../../chart/plugin/current-price-line/current-price-line-plugin';
import { right } from 'fp-ts/lib/Either';
import { Redirect } from '../../../../ui-common/redirect/containers/redirect.container';
import { Workspace } from '../../../ui-kit/components/workspace/workspace.component';
import { WorkspaceChart } from '../../../ui-kit/components/workspace/workspace-chart.component';
import { pipe } from 'fp-ts/lib/pipeable';
import * as css from './theme/trade-dashboard.module.css';

export type TradeDashboardProps = {
	firstCategoryId: RemoteData<Error, string>;
};

export const TradeDashboard = combineContext(
	Route,
	WatchlistContainer,
	OrderEntryPanelContainer,
	SymbolInfoContainer,
	CategoriesContainer,
	ChartLoaderContainer,
	SymbolSearchContainer,
	Redirect,
	(
		Route,
		WatchlistContainer,
		OrderEntryPanelContainer,
		SymbolInfoContainer,
		CategoriesContainer,
		ChartLoaderContainer,
		SymbolSearchContainer,
		Redirect,
	) => {
		return class TradeDashboard extends PureComponent<TradeDashboardProps> {
			plugins: Plugin[] = [new CurrentPriceLinePlugin()];

			render() {
				const { firstCategoryId } = this.props;

				return (
					<div className={css.container}>
						<RemoteDataState data={firstCategoryId} success={this.renderSuccess} />
					</div>
				);
			}

			private renderSuccess = (firstCategoryId: string) => {
				return (
					<section className={css.container}>
						<div className={css.left}>
							<Workspace innerShadow>
								<Route
									exact
									match={routes.trade.root}
									render={() => (
										<Redirect
											to={routes.trade.category.root}
											value={{ categoryId: firstCategoryId }}
										/>
									)}
								/>
								<div className={css.symbols}>
									<Route match={routes.trade.root} render={() => <SymbolSearchContainer />} />
									<Route match={routes.trade.category.root} render={this.renderCategory} />
								</div>
								<div className={css.watchlist}>
									<Route match={routes.trade.category.root} render={this.renderWatchlist} />
								</div>
							</Workspace>
							<WorkspaceChart>
								<Route match={routes.trade.category.symbol.root} render={this.renderChart} />
							</WorkspaceChart>
						</div>
						<Route match={routes.trade.category.symbol.action.root} exact render={this.renderAside} />
						<Route match={routes.trade.category.symbol.info.root} exact render={this.renderInfo} />
					</section>
				);
			};

			private renderCategory = () => <CategoriesContainer />;

			private renderWatchlist = (path: MatchType<typeof routes.trade.category.root>) => (
				<WatchlistContainer categoryId={path.categoryId} />
			);

			private renderChart = (path: MatchType<typeof routes.trade.category.symbol.root>) => (
				<ChartLoaderContainer symbol={path.symbol} plugins={this.plugins} />
			);
			private renderAside = (path: MatchType<typeof routes.trade.category.symbol.action.root>) => {
				const action = fromStringToSide(path.action);
				return pipe(
					action,
					O.map(data => (
						<div className={css.aside}>
							<OrderEntryPanelContainer
								symbol={path.symbol}
								plKind={''}
								action={data}
								elementId={right(path.categoryId)}
							/>
						</div>
					)),
					O.toNullable,
				);
			};

			private renderInfo = (path: MatchType<typeof routes.trade.category.symbol.info.root>) => (
				<div className={css.aside}>
					<SymbolInfoContainer symbol={path.symbol} categoryId={path.categoryId} />
				</div>
			);
		};
	},
);

const fromStringToSide = (side: string): Option<OrderEntrySide> => {
	switch (side) {
		case 'buy': {
			return O.some(OrderEntrySide.Buy);
		}
		case 'sell': {
			return O.some(OrderEntrySide.Sell);
		}
		default: {
			return O.none;
		}
	}
};
