import { SocketClient } from '../../api/socket-client';
import { Observable } from 'rxjs';
import { Sink } from '@devexperts/rx-utils/dist/sink.utils';
import { asks } from '@devexperts/rx-utils/dist/context.utils';
import { filter, first, map } from 'rxjs/operators';
import { isAvaSystemSettingsTO } from '../../api/socket/socket-message.to';
import { AvaAccountTO } from '../../api/generated/swagger/definitions/AvaAccountTO';
import { AvaSystemSettingsTO } from '../../api/generated/swagger/definitions/AvaSystemSettingsTO';

export type SystemSettingsViewModelContext = {
	socketClient: SocketClient;
};

export type SystemSettingsViewModel = {
	preview$: Observable<boolean>;
	availableAccounts$: Observable<Array<AvaAccountTO>>;
	platformType$: Observable<AvaSystemSettingsTO['platformType']>;
};

export type CreateSystemSettingsViewModel = () => Sink<SystemSettingsViewModel>;

export const createSystemSettingsViewModel = asks(
	(e: SystemSettingsViewModelContext): CreateSystemSettingsViewModel => {
		const settings$ = e.socketClient.message$.pipe(
			filter(isAvaSystemSettingsTO),
			// TODO: Remove after RIA update specs. Remove when develop task [DXTF-11554]
			map(data => ({ ...data.body, incomplete: false })),
		);
		const preview$ = settings$.pipe(
			map(settings => settings.incomplete),
			first(),
		);

		const availableAccounts$ = settings$.pipe(
			map(settings => settings.availableAccounts),
			first(),
		);

		const platformType$ = settings$.pipe(
			map(settings => settings.platformType),
			first(),
		);

		return () => new Sink({ preview$, availableAccounts$, platformType$ }, settings$);
	},
);
