import { Option } from 'fp-ts/lib/Option';
import { string, number, boolean, type } from 'io-ts';
import { createOptionFromNullable } from 'io-ts-types';

export type ProtectionOrderTO = {
	orderType: string;
	fixedPrice: Option<number>;
	fixedOffset: Option<number>;
	priceFixed: boolean;
	refOrderChainId: Option<string>;
	requestId: Option<string>;
};
export const ProtectionOrderTOIO = type({
	orderType: string,
	fixedPrice: createOptionFromNullable(number),
	fixedOffset: createOptionFromNullable(number),
	priceFixed: boolean,
	refOrderChainId: createOptionFromNullable(string),
	requestId: createOptionFromNullable(string),
});
