import { TableItem } from '../watchlist/watchlist-table.view-model';
import { LiveData } from '@devexperts/rx-utils/dist/rd/live-data.utils';
import { MarketDataStore } from '../../store/market-data/market-data.store';
import { createHandler } from '@devexperts/rx-utils/dist/create-handler.utils';
import * as O from 'fp-ts/lib/Option';
import { Option } from 'fp-ts/lib/Option';
import { identity } from 'fp-ts/lib/function';
import { pipe } from 'fp-ts/lib/pipeable';
import { mapRD } from '@devexperts/rx-utils/dist/rd/operators/mapRD';
import { ask, combineContext, fromReader } from '@devexperts/rx-utils/dist/context.utils';
import { PositionsStore } from '../../store/positions/positions.store';
import { WatchlistStore } from '../../store/watchlist/watchlist.store';
import { InstrumentStore } from '../../store/instrument/instrument.store';
import { switchMapRD } from '@devexperts/rx-utils/dist/rd/operators/switchMapRD';
import { map, startWith, filter, withLatestFrom, switchMap, distinctUntilChanged } from 'rxjs/operators';
import { combineLatest, merge } from 'rxjs';
import { success, isSuccess } from '@devexperts/remote-data-ts';
import { Sink } from '@devexperts/rx-utils/dist/sink.utils';
import { filter as filterArray, snoc, head } from 'fp-ts/lib/Array';
import { watchlistsController } from '../../api/generated/swagger/controllers/WatchlistsController';
import { distinctRDOptionNumber, distinctRDNumber } from '../../utils/setoid.utils';

export type WatchlistEntityViewModel = {
	data$: LiveData<Error, TableItem>;
	toggleFavorite: (data: TableItem) => void;
};

export type WatchlistEntityViewModelContext = {
	marketDataStore: MarketDataStore;
	positionsStore: PositionsStore;
	instrumentStore: InstrumentStore;
	watchlistStore: WatchlistStore;
};

export type CreateWatchlistEntityViewModel = (symbol: string) => Sink<WatchlistEntityViewModel>;

export const createWatchlistEntityViewModel = combineContext(
	fromReader(watchlistsController),
	ask<WatchlistEntityViewModelContext>(),
	(watchlistsController, e): CreateWatchlistEntityViewModel => symbol => {
		const { marketDataStore, positionsStore, instrumentStore, watchlistStore } = e;

		const toggleFavorite = createHandler<TableItem>();
		const instrument$ = instrumentStore.getBySymbol(symbol);

		const positions$ = instrumentStore.getBySymbol(symbol).pipe(
			switchMapRD(instrument =>
				positionsStore.getAllByInstrumentId(instrument.id).pipe(mapRD(positions => positions.length)),
			),
			startWith(success<Error, number>(0)),
			distinctRDNumber,
		);

		const symbolData$ = marketDataStore.getDataBySymbol(symbol);

		const bid$ = symbolData$.pipe(
			mapRD(data =>
				pipe(
					data.lastQuote,
					O.chain(quote => quote.bid),
				),
			),
			distinctRDOptionNumber,
		);
		const ask$ = symbolData$.pipe(
			mapRD(data =>
				pipe(
					data.lastQuote,
					O.chain(quote => quote.ask),
				),
			),
			distinctRDOptionNumber,
		);
		const pipsSize$ = instrument$.pipe(
			mapRD(instrument => instrument.pipsSize),
			distinctRDNumber,
		);
		const precision$ = instrument$.pipe(
			mapRD(instrument => instrument.precision),
			distinctRDNumber,
		);

		const change$ = symbolData$.pipe(
			mapRD(data => data.dayChange),
			distinctRDOptionNumber,
		);

		// first private watchlist is favorite list
		const favoriteWatchlist$ = watchlistStore.private$.pipe(
			filter(isSuccess),
			map(watchListsRD => watchListsRD.value),
			map(head),
			filter(O.isSome),
			map(favWatchList => favWatchList.value),
		);

		const symbolsOfFavoriteWatchlist$ = favoriteWatchlist$.pipe(map(watchList => watchList.symbols));

		const favoriteListId$ = favoriteWatchlist$.pipe(map(watchList => watchList.id));

		const hasThisSymbolInFavorites$ = symbolsOfFavoriteWatchlist$.pipe(
			map(symbols => symbols.includes(symbol)),
			startWith(false),
			distinctUntilChanged(),
		);

		const bidAsk$ = combineLatest(bid$, ask$);

		const data$: LiveData<Error, TableItem> = combineLatest(
			bidAsk$,
			change$,
			positions$,
			pipsSize$,
			precision$,
			hasThisSymbolInFavorites$,
		).pipe(
			map(([bidAskTuple, change, positions, pipsSize, precision, hasThisSymbolInFavorites]) => {
				const [bid, ask] = bidAskTuple;

				return success(
					toTableItem(
						symbol,
						pipe(
							bid.toOption(),
							O.chain(identity),
						),
						pipe(
							ask.toOption(),
							O.chain(identity),
						),
						pipe(
							change.toOption(),
							O.chain(identity),
						),
						positions.toOption(),
						pipsSize.toOption(),
						precision.toOption(),
						hasThisSymbolInFavorites,
					),
				);
			}),
		);

		const toggleFavoriteEffect$ = toggleFavorite.value$.pipe(
			withLatestFrom(symbolsOfFavoriteWatchlist$),
			map(([tableItem, symbols]) =>
				tableItem.isFavorite
					? filterArray(symbols, watchListSymbol => watchListSymbol !== symbol)
					: snoc(symbols, symbol),
			),
			withLatestFrom(favoriteListId$),
			switchMap(([favoriteSymbols, favListId]) =>
				watchlistsController.updateWatchlistCondition(favListId, { body: favoriteSymbols }),
			),
		);

		return new Sink(
			{
				data$,
				toggleFavorite: toggleFavorite.handle,
			},
			merge(toggleFavoriteEffect$),
		);
	},
);

const toTableItem = (
	symbol: string,
	bid: Option<number>,
	ask: Option<number>,
	change: Option<number>,
	positions: Option<number>,
	pipsSize: Option<number>,
	precision: Option<number>,
	isFavorite: boolean,
): TableItem => ({
	symbol,
	change,
	positions: positions.filter(positions => positions !== 0),
	pipsSize,
	precision,
	sell: bid,
	buy: ask,
	isFavorite,
});
