import { Observable } from 'rxjs';
import { Action, History, Location } from 'history';
import { ask } from '@devexperts/rx-utils/dist/context.utils';
import { Sink } from '@devexperts/rx-utils/dist/sink.utils';
import { end, format, Match, Route } from 'fp-ts-routing';
import { Option } from 'fp-ts/lib/Option';
import { shareReplayWithDefaults } from './rxjs/shareReplayWithDefaults';

export type RouterState = {
	location: Location;
	action: Action;
};

export type Router = {
	state: RouterState;
	state$: Observable<RouterState>;
	push: History['push'];
	replace: History['replace'];
};

export type CreateRouter = () => Sink<Router>;

export type RouterContext = {
	history: History;
};

export const createRouter = ask<RouterContext>().map(
	(e): CreateRouter => {
		const {
			history: { listen, action, location, push, replace },
		} = e;
		const state$ = new Observable<RouterState>(subscriber =>
			listen((location, action) =>
				subscriber.next({
					location,
					action,
				}),
			),
		).pipe(shareReplayWithDefaults());
		return () =>
			new Sink(
				{
					state: {
						location,
						action,
					},
					state$,
					push,
					replace,
				},
				state$,
			);
	},
);

export const parse = <A extends object>(match: Match<A>, path: string): Option<[A, Route]> =>
	match.parser.run(Route.parse(path));
export const href = <A extends object>(match: Match<A>, a: A): string => format(match.formatter, a, true);
export type MatchType<M extends Match<any>> = M extends Match<infer A> ? A : never;
export const exact = <A extends object>(match: Match<A>): Match<A> => match.then(end);
