import * as React from 'react';
import { PureComponent, Fragment } from 'react';
import { withTranslation, WithTranslation } from 'react-i18next';
import { empty } from 'fp-ts/lib/Record';
import { SidePanel } from '../../../../ui-common/side-panel/side-panel.component';
import { Button } from '../../../../ui-common/button/button.component';
import { ButtonCta } from '../../../../ui-common/button/button-cta.component';
import { Optional } from '../../../../ui-common/optional/optional.component';
import { OrderEntryPanelSide } from '../order-entry-panel-side/order-entry-panel-side.component';
import { NumericStepper } from '../../../../ui-common/numeric-stepper-ava/numeric-stepper.component';
import { formatMoney, formatNumber } from 'accounting';
import {
	OrderEntryPanelData,
	OrderEntrySideValue,
	OrderEntryValue,
	OrderEntrySide,
	AmountValidationError,
	AmountValidationErrorMessages,
} from './order-entry-panel.model';
import { RemoteData } from '@devexperts/remote-data-ts';
import { Option } from 'fp-ts/lib/Option';
import * as O from 'fp-ts/lib/Option';
import { RemoteDataState } from '../../../../ui-common/remote-data-state/remote-data-state.component';
import { RouteLink } from '../../../../ui-common/route-link/containers/route-link.container';
import { routes } from '../../../../ui-common/routes';
import { CloseButton } from '../../../../ui-common/close-button/close-button.component';
import { RouteLinkRenderProps } from '../../../../ui-common/route-link/components/route-link.component';
import { combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { Either } from 'fp-ts/lib/Either';
import * as E from 'fp-ts/lib/Either';
import { OrderEntryExecutionContainer } from '../../containers/order-entry-execution/order-entry-execution.container';
import { OrderEntryTakeProfitStopLossContainer } from '../../containers/order-entry-takeprofit-stoploss/order-entry-takeprofit-stoploss.container';
import { constUndefined, constNull, constant, constTrue, constFalse } from 'fp-ts/lib/function';
import { InfoBox } from '../../../../ui-common/info-box/info-box.component';
import { SymbolComponent } from '../../../../ui-common/symbol/symbol';
import { tspan, TRANSLATION_PLACEHOLDER } from '../../../../utils/i18n.utils';
import { pipe } from 'fp-ts/lib/pipeable';
import { ErrorPanel } from '../../../../ui-common/error-panel/components/error-panel.component';
import * as cn from 'classnames';
import { RegistrationInfoBox } from '../../../ui-kit/components/preview-mode/components/registration-info-box.component';
import { DisableOverlay } from '../../../ui-kit/components/preview-mode/components/disable-overlay.component';

import * as css from './theme/order-entry-panel.module.css';
import * as buttonCss from './theme/button.child.module.css';
import * as buttonDepositCss from './theme/button-deposit.child.module.css';
import * as stepperCss from './theme/numeric-stepper.child.module.css';
import * as stepperButtonCss from './theme/numeric-stepper.button.module.css';
import * as infoBoxCss from './theme/info-box.child.module.css';
import * as sidePanelCss from './theme/side-panel.child.module.css';
import * as symbolCss from './theme/symbol.child.module.css';
import * as registrationInfoBox from './theme/registration-info-box.child.module.css';

const steppableInputTheme = {
	Input: stepperCss,
	SteppableInput: {
		ButtonIcon: stepperButtonCss,
	},
};

const sidePanelTheme = { SidePanel: sidePanelCss };

export type OrderEntryPanelProps = {
	action: OrderEntrySide;
	plKind: string;
	elementId: Either<string, string>; //left - order, right - category
	data: RemoteData<Error, OrderEntryPanelData>;
	error: Option<string>;
	lot: Option<number>;
	accountBalance: Option<number>;
	moreOptionsOpened: boolean;
	onFooterButtonClick: (isValid: boolean) => () => void;
	onMoreOptionsClick: (value: boolean) => void;
	result: number;
	symbol: string;
	isValid: boolean;
	isShownValidErrors: boolean;
	onValueChange: (value: OrderEntryValue) => void;
	currencySymbol: string;
	isSubmitAllowed: RemoteData<Error, boolean>;
	isEditAllowed: RemoteData<Error, boolean>;
	isEdit?: boolean;
	previewMode: Option<boolean>;
};

export const OrderEntryPanel = combineContext(
	RouteLink,
	OrderEntryExecutionContainer,
	OrderEntryTakeProfitStopLossContainer,
	(RouteLink, OrderEntryExecutionContainer, OrderEntryTakeProfitStopLossContainer) =>
		withTranslation()(
			class OrderEntryPanel extends PureComponent<OrderEntryPanelProps & WithTranslation> {
				render() {
					const { data } = this.props;

					return <RemoteDataState data={data} success={data => this.renderSuccess(data)} />;
				}

				private renderSuccess = (data: OrderEntryPanelData) => {
					const {
						lot,
						result,
						symbol,
						elementId,
						moreOptionsOpened,
						action,
						currencySymbol,
						isEdit,
						previewMode,
					} = this.props;
					const {
						description,
						sell,
						buy,
						marginImpact,
						precision,
						step,
						minOrderSize,
						maxOrderSize,
						amountValidated,
						value,
						amountPrecision,
						unitName,
					} = data;
					const cnMoreOptions = cn(css.moreOptions, {
						[css.moreOptions_opened]: moreOptionsOpened,
					});

					return (
						<SidePanel
							header={this.renderHeader(description)}
							footer={this.renderFooter(value.side)}
							theme={sidePanelTheme}
							scrollable>
							<div className={css.innerPaddedTopmostWrapper}>
								{isEdit && (
									<SymbolComponent
										theme={symbolCss}
										title={symbol}
										subtitle={pipe(
											description,
											O.getOrElse(constant('—')),
										)}
									/>
								)}
								<div className={css.operations}>
									{pipe(
										elementId,
										E.map(categoryId => (
											<Fragment>
												<RouteLink
													to={routes.trade.category.symbol.action.root}
													value={{
														categoryId,
														symbol,
														action: 'sell',
													}}
													render={this.renderSide(
														sell,
														OrderEntrySide.Sell,
														precision,
														value,
													)}
												/>
												<RouteLink
													to={routes.trade.category.symbol.action.root}
													value={{
														categoryId,
														symbol,
														action: 'buy',
													}}
													render={this.renderSide(buy, OrderEntrySide.Buy, precision, value)}
												/>
											</Fragment>
										)),
										E.getOrElse(() => (
											<Fragment>
												<OrderEntryPanelSide
													amount={sell.amount}
													pipSettings={sell.pipSettings}
													side={OrderEntrySide.Sell}
													precision={precision}
													isActive={OrderEntrySide.Sell === action}
													isDisabled={OrderEntrySide.Sell !== action}
													onClick={constUndefined}
												/>
												<OrderEntryPanelSide
													amount={buy.amount}
													pipSettings={buy.pipSettings}
													side={OrderEntrySide.Buy}
													precision={precision}
													isActive={OrderEntrySide.Buy === action}
													isDisabled={OrderEntrySide.Buy !== action}
													onClick={constUndefined}
												/>
											</Fragment>
										)),
									)}
								</div>
								<div className={css.info}>
									<div className={css.element}>
										<div className={css.label}>
											{tspan('orderEntryAmount', 'Amount', `${TRANSLATION_PLACEHOLDER} `)}
											<Optional value={unitName} />
										</div>
										{pipe(
											elementId,
											E.map(() => (
												<NumericStepper
													value={value.amount}
													step={step}
													formatter={(inputValue: number) =>
														formatNumber(inputValue, amountPrecision)
													}
													min={minOrderSize}
													max={maxOrderSize}
													onIncrement={this.handleValueChange(value)}
													onDecrement={this.handleValueChange(value)}
													onValueChange={this.handleValueChange(value)}
													theme={steppableInputTheme}
												/>
											)),
											E.getOrElse(() => <div className={css.text}>{value.amount}</div>),
										)}
										{E.isLeft(amountValidated) &&
											pipe(
												amountValidated,
												E.fold(
													error => (
														<div className={css.validationError}>
															{this.stringifyAmountErrors(error)}
														</div>
													),
													constNull,
												),
											)}
									</div>
									<div className={css.element}>
										<div className={css.label}>{tspan('orderEntryLot', 'Lot')}</div>
										<div className={css.text}>
											<Optional value={lot} />
										</div>
									</div>
									<div className={css.element}>
										<div className={css.label}>
											{tspan(
												'orderEntryMarginImpact',
												'Margin Impact',
												`${TRANSLATION_PLACEHOLDER}, ${currencySymbol}`,
											)}
										</div>
										<div className={css.text}>
											{pipe(
												marginImpact,
												E.fold(
													() => <Optional value={O.none} />,
													dataR =>
														Number.isNaN(dataR) ? (
															<Optional value={O.none} />
														) : (
															<Fragment>
																{formatMoney(dataR, {
																	format: '%v',
																})}
															</Fragment>
														),
												),
											)}
										</div>
									</div>
									<div className={css.element}>
										<div className={css.label}>
											{tspan(
												'orderEntryValue',
												'Value',
												`${TRANSLATION_PLACEHOLDER}, ${currencySymbol}`,
											)}
										</div>
										<div className={css.text}>{formatMoney(result, { format: '%v' })}</div>
									</div>
								</div>
								<div className={cnMoreOptions} onClick={this.handleToggleMoreOptions}>
									{tspan('orderEntryMoreOptions', 'More Options')}
								</div>
							</div>
							{this.renderMoreOptionsPanels()}
							{pipe(
								previewMode,
								O.map(
									isPreviewMode =>
										isPreviewMode && (
											<Fragment>
												<RegistrationInfoBox
													theme={registrationInfoBox}
													text={tspan(
														'orderEntryRegistrationDescription',
														'Continue registration in order to start trading',
													)}
												/>
												<DisableOverlay />
											</Fragment>
										),
								),
								O.toNullable,
							)}
						</SidePanel>
					);
				};

				private stringifyAmountErrors = ({
					message,
					maxOrderSize,
					minOrderSize,
					minOrderIncrement,
				}: AmountValidationError) => {
					switch (message) {
						case AmountValidationErrorMessages.Max:
							return tspan('orderEntryValidationErrorMax', {
								defaultValue: 'Shall be multiple of {{minOrderIncrement}}. Max {{maxOrderSize}}',
								maxOrderSize,
								minOrderIncrement,
							});
						case AmountValidationErrorMessages.Min:
							return tspan('orderEntryValidationErrorMin', {
								defaultValue: 'Minimum amount is {{minOrderSize}}',
								minOrderSize,
							});
						case AmountValidationErrorMessages.Multiple:
							return tspan('orderEntryValidationErrorMultiple', {
								defaultValue: 'Shall be multiple of {{minOrderIncrement}}',
								minOrderIncrement,
							});
					}
				};

				private renderDepositBox = () => {
					const text = tspan(
						'orderEntryNeedToDeposit',
						'You need to deposit on your account in order to make your first trade',
					);

					const button = (
						<RouteLink
							to={routes.crm.deposit.root}
							value={empty}
							render={({ onNavigate }) => {
								return (
									<ButtonCta theme={buttonDepositCss} onClick={onNavigate}>
										{tspan('depositButton', 'Deposit')}
									</ButtonCta>
								);
							}}
						/>
					);

					return <InfoBox text={text} button={button} theme={infoBoxCss} hides />;
				};

				private renderMoreOptionsPanels = () =>
					this.props.moreOptionsOpened && (
						<Fragment>
							<div className={css.innerPaddedWrapper}>
								<OrderEntryExecutionContainer
									isShownValidErrors={this.props.isShownValidErrors}
									isChangeable={pipe(
										this.props.elementId,
										E.fold(constTrue, constFalse),
									)}
								/>
							</div>
							<OrderEntryTakeProfitStopLossContainer isShownValidErrors={this.props.isShownValidErrors} />
						</Fragment>
					);

				renderSide = (
					data: OrderEntrySideValue,
					side: OrderEntrySide,
					precision: number,
					value: OrderEntryValue,
				) => (props: RouteLinkRenderProps) => (
					<OrderEntryPanelSide
						amount={data.amount}
						pipSettings={data.pipSettings}
						precision={precision}
						side={side}
						isActive={props.isActive}
						onClick={this.handleSideChange(props.onNavigate, value)}
					/>
				);

				handleToggleMoreOptions = () => {
					const { onMoreOptionsClick, moreOptionsOpened } = this.props;
					onMoreOptionsClick(!moreOptionsOpened);
				};

				handleValueChange = (value: OrderEntryValue) => (amount: number) => {
					const { onValueChange } = this.props;
					onValueChange({ ...value, amount: Math.abs(amount).toString() });
				};

				private handleSideChange = (onNavigate: () => void, value: OrderEntryValue) => (
					side: OrderEntrySide,
				) => {
					const { onValueChange } = this.props;
					onValueChange({ ...value, side });
					onNavigate();
				};

				private getText = (value: OrderEntrySide) => {
					switch (value) {
						case OrderEntrySide.Sell: {
							return tspan('orderEntrySell', 'Sell');
						}
						case OrderEntrySide.Buy: {
							return tspan('orderEntryBuy', 'Buy');
						}
					}
				};

				renderHeader = (description: Option<string>) => {
					const { elementId, symbol, isEdit } = this.props;

					return (
						<Fragment>
							{isEdit ? (
								<span className={css.title}>{tspan('orderEntryPanelTitle', 'Edit Orders')}</span>
							) : (
								<SymbolComponent
									title={symbol}
									subtitle={pipe(
										description,
										O.getOrElse(constant('—')),
									)}
								/>
							)}
							{pipe(
								elementId,
								E.fold(
									orderId => (
										<RouteLink
											to={routes.orders.order.root}
											value={{ orderId: Number(orderId) }}
											render={({ onNavigate }) => <CloseButton onClick={onNavigate} />}
										/>
									),
									categoryId => (
										<RouteLink
											to={routes.trade.category.symbol.root}
											value={{ categoryId, symbol }}
											render={({ onNavigate }) => <CloseButton onClick={onNavigate} />}
										/>
									),
								),
							)}
						</Fragment>
					);
				};

				renderFooter = (side: OrderEntrySide) => {
					const {
						onFooterButtonClick,
						elementId,
						isValid,
						isSubmitAllowed,
						isEditAllowed,
						accountBalance,
						error,
						previewMode,
					} = this.props;
					const isAccountBalanceZero =
						pipe(
							accountBalance,
							O.getOrElse(constant(0)),
						) === 0;

					return (
						<Fragment>
							{pipe(
								accountBalance,
								O.fold(constant(<Fragment />), data =>
									data === 0 ? this.renderDepositBox() : <Fragment />,
								),
							)}
							<ErrorPanel
								error={error}
								title={tspan('orderEntryErrorPlacingOrder', 'Error Placing Your Order')}
							/>
							<Button
								onClick={onFooterButtonClick(isValid)}
								isPrimary={true}
								theme={buttonCss}
								isDisabled={
									pipe(
										elementId,
										E.fold(
											() => !isEditAllowed.getOrElse(false),
											() => !isSubmitAllowed.getOrElse(false),
										),
									) || isAccountBalanceZero
								}>
								{pipe(
									elementId,
									E.fold(() => tspan('orderEntrySaveChanges', 'Update'), () => this.getText(side)),
								)}
							</Button>
							{pipe(
								previewMode,
								O.map(isPreviewMode => isPreviewMode && <DisableOverlay />),
								O.toNullable,
							)}
						</Fragment>
					);
				};
			},
		),
);
