import { Observable, merge, combineLatest } from 'rxjs';
import { map, mapTo, filter, first, distinctUntilChanged, pluck } from 'rxjs/operators';
import { or, constFalse } from 'fp-ts/lib/function';
import * as O from 'fp-ts/lib/Option';
import { asks } from '@devexperts/rx-utils/dist/context.utils';
import { createHandler } from '@devexperts/rx-utils/dist/create-handler.utils';
import { PostMessageClient } from '../../api/post-message.client';
import { isClosePopup, isRedirectToCRMDeposit } from '../../api/post-message/post-message.to';
import { SocketClient } from '../../api/socket-client';
import { isAccountSettingsTO } from '../../api/socket/socket-message.to';
import { AccountViewModel } from '../account/account.view-model';
import { number } from 'io-ts';
import { pipe } from 'fp-ts/lib/pipeable';

export type MarginViewModel = {
	shouldOpenPopup$: Observable<boolean>;
	onClose: () => void;
};

type MarginViewModelContext = {
	socketClient: SocketClient;
	accountViewModel: AccountViewModel;
	postMessageClient: PostMessageClient;
};

type CreateMarginViewModel = () => MarginViewModel;

export const createMarginViewModel = asks(
	(e: MarginViewModelContext): CreateMarginViewModel => {
		const { socketClient, accountViewModel, postMessageClient } = e;
		const { accountMetrics$ } = accountViewModel;

		const accountMetricsWithMargin$ = accountMetrics$.pipe(
			filter(accountMetrics => O.isSome(accountMetrics.margin)),
		);
		const accountMargin$ = accountMetricsWithMargin$.pipe(map(accountMetrics => accountMetrics.margin));
		const reverseRiskLevel$ = accountMetricsWithMargin$.pipe(
			map(accountMetrics => accountMetrics.allMetrics),
			pluck('reverseRiskLevel'),
			filter(number.is),
		);

		const toggleHandler = createHandler();

		const marginAlertLevel$ = socketClient.message$.pipe(
			filter(isAccountSettingsTO),
			map(data => data.body.marginAlertLevel1),
			filter(marginAlert => O.isSome(marginAlert)),
		);
		const initState$ = combineLatest(marginAlertLevel$, accountMargin$, reverseRiskLevel$).pipe(
			map(([marginAlert, margin, reverseRiskLevel]) =>
				pipe(
					margin,
					O.chain(margin =>
						pipe(
							marginAlert,
							O.map(alert => {
								const marginAlertPercent = alert * 100;
								const isMarginCallCase = reverseRiskLevel < marginAlertPercent;
								const haveOpenedPositions = margin > 0;
								return haveOpenedPositions ? isMarginCallCase : false;
							}),
						),
					),
					O.getOrElse(constFalse),
				),
			),
			first(),
		);

		const closePopupFromPostMessage$ = postMessageClient.postMessage$.pipe(
			filter(or(isClosePopup, isRedirectToCRMDeposit)),
		);

		const handledState$ = merge(toggleHandler.value$, closePopupFromPostMessage$).pipe(
			mapTo(false),
			distinctUntilChanged(),
		);

		return () => ({
			shouldOpenPopup$: merge(initState$, handledState$),
			onClose: () => toggleHandler.handle(undefined),
		});
	},
);
