import * as t from 'io-ts';
import { createOptionFromNullable, DateFromISOString, DateFromUnixTime } from 'io-ts-types';
import { Option } from 'fp-ts/lib/Option';
import * as O from 'fp-ts/lib/Option';
import * as E from 'fp-ts/lib/Either';
import { Any, Type } from 'io-ts';
import { fromArray, NonEmptyArray } from 'fp-ts/lib/NonEmptyArray';

export const stringOption = createOptionFromNullable(t.string);
export const booleanOption = createOptionFromNullable(t.boolean);
export const numberOption = createOptionFromNullable(t.number);
export const dateOptionFromString = createOptionFromNullable(DateFromISOString);
export const dateOptionFromUnixTime = createOptionFromNullable(DateFromUnixTime);

const NAN = 'NaN';

export const doubleOption = new (class DoubleOptionType extends t.Type<
	Option<number>,
	number | undefined | null | typeof NAN
> {
	readonly _tag: 'DoubleOptionType' = 'DoubleOptionType';
	private v = createOptionFromNullable(t.union([t.literal(NAN), t.number]));

	constructor() {
		super(
			'DoubleOption',
			(m): m is Option<number> => this.v.is(m),
			(m, c) => {
				const validated = this.v.validate(m, c);
				if (E.isLeft(validated) || O.isNone(validated.value)) {
					return validated as any;
				}
				if (validated.value.value === NAN) {
					return t.success(O.none);
				}
				return numberOption.validate(validated.value.value, c);
			},
			a => (O.isNone(a) ? NAN : a.value),
		);
	}
})();

class NEAType<RT extends Any, A = any, O = A, I = t.mixed> extends Type<A, O, I> {
	readonly _tag: 'NEAType' = 'NEAType';
	constructor(
		name: string,
		is: NEAType<RT, A, O, I>['is'],
		validate: NEAType<RT, A, O, I>['validate'],
		encode: NEAType<RT, A, O, I>['encode'],
		readonly type: RT,
	) {
		super(name, is, validate, encode);
	}
}
export const nea = <RT extends t.Mixed>(
	type: RT,
	name: string = `NEA<${type.name}>`,
): NEAType<RT, NonEmptyArray<t.TypeOf<RT>>, Array<t.OutputOf<RT>>, t.mixed> => {
	const v = t.array(type);
	return new NEAType(
		name,
		(m): m is NonEmptyArray<t.TypeOf<RT>> => v.is(m) && m.length > 0,
		(m, c) => {
			const validatedArray = v.validate(m, c);
			if (E.isLeft(validatedArray)) {
				return validatedArray as any;
			}
			const result = fromArray(validatedArray.value);
			return O.isNone(result) ? t.failure(m, c) : t.success(result.value);
		},
		nea => v.encode(nea.toArray()),
		type,
	);
};
