import * as React from 'react';
import { memo, useCallback, useState } from 'react';
import { MinimizeIcon } from '../../icons/minimize.icon';
import { CloseContinueRegistrationIcon } from '../../icons/continue-registration-close.icon';
import { constVoid } from 'fp-ts/lib/function';
import { tspan } from '../../../../../utils/i18n.utils';
import { WithTranslation, withTranslation } from 'react-i18next';

import * as css from './theme/registration-open-button.module.css';

const RegistrationContinueButtonRaw = memo<WithTranslation>(() => {
	const [isShow, setShow] = useState(true);
	const toggleHandle = useCallback(() => setShow(!isShow), [isShow]);

	return isShow ? (
		<div className={css.container}>
			<span className={css.title}>{tspan('ContinueRegistrationButtonShowPopup', 'Continue Registration')}</span>
			<button className={css.btn} onClick={constVoid}>
				<MinimizeIcon />
			</button>
			<button className={css.btn} onClick={toggleHandle}>
				<CloseContinueRegistrationIcon />
			</button>
		</div>
	) : null;
});

export const RegistrationContinueButton = withTranslation()(RegistrationContinueButtonRaw);
