import { combineContext, ask } from '@devexperts/rx-utils/dist/context.utils';
import { withRXWithDefaults } from '../../../utils/withRXWithDefaults';
import { AccountViewModel } from '../../../view-model/account/account.view-model';
import { map, first } from 'rxjs/operators';
import * as O from 'fp-ts/lib/Option';
import { EmptyScreen } from '../../../ui-common/empty-screen/components/empty-screen.component';
import { pipe } from 'fp-ts/lib/pipeable';

export type EmptyScreenContainerContext = {
	accountViewModel: AccountViewModel;
};

export const EmptyScreenContainer = combineContext(ask<EmptyScreenContainerContext>(), EmptyScreen, (e, EmptyScreen) =>
	withRXWithDefaults(EmptyScreen)(() => {
		const { equity$, current$ } = e.accountViewModel;

		return {
			props: {
				isZeroEquity: equity$.pipe(
					map(someEquity =>
						pipe(
							someEquity,
							O.map(equity => equity === 0),
						),
					),
				),
				isCurrentDemo: current$.pipe(
					first(),
					map(account => account.paperMoney),
				),
			},
			defaultProps: {
				isZeroEquity: O.none,
				isCurrentDemo: false,
				previewMode: O.some(false),
			},
		};
	}),
);
