import { Sink } from '@devexperts/rx-utils/dist/sink.utils';
import { ask, combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { SocketClient } from '../../api/socket-client';
import { LimitsDomainTO } from '../../api/generated/swagger/definitions/LimitsDomainTO';
import { EntityStore } from '@devexperts/rx-utils/dist/rd/entity-store.utils';
import { isLimitsTO } from '../../api/socket/socket-message.to';
import { filter, map } from 'rxjs/operators';
import { Observable } from 'rxjs/index';
import { LiveData } from '@devexperts/rx-utils/dist/rd/live-data.utils';
import { findFirst } from 'fp-ts/lib/Array';
import { failure, success } from '@devexperts/remote-data-ts';
import { pipe } from 'fp-ts/lib/pipeable';
import * as O from 'fp-ts/lib/Option';

export type LimitsStore = Readonly<{
	getLimitsByInstrumentId: (id: number) => LiveData<Error, LimitsDomainTO>;
}>;

type LimitsStoreContext = {
	socketClient: SocketClient;
};

type CreateLimitsStore = () => Sink<LimitsStore>;

export const createLimitsStore = combineContext(
	ask<LimitsStoreContext>(),
	(e): CreateLimitsStore => {
		const { socketClient } = e;
		const pk = (limits: LimitsDomainTO) => limits.instrumentId.toString();

		const store = new EntityStore<Error, LimitsDomainTO>();

		const data$: Observable<LimitsDomainTO[]> = socketClient.message$.pipe(
			filter(isLimitsTO),
			map(message => message.body),
		);

		const all$ = store.getAll(pk, () => data$.pipe(map(to => success(to))));

		return () => {
			const getLimitsByInstrumentId = (id: number): LiveData<Error, LimitsDomainTO> =>
				store.get(id.toString(), () =>
					data$.pipe(
						map(limits =>
							pipe(
								findFirst(limits, limit => limit.instrumentId === id),
								O.fold(
									() =>
										failure<Error, LimitsDomainTO>(
											new Error(`Limits for instrument with id ${id} not found in limits.store`),
										),
									to => success<Error, LimitsDomainTO>(to),
								),
							),
						),
					),
				);

			return new Sink(
				{
					getLimitsByInstrumentId,
				},
				all$,
			);
		};
	},
);
