import { TPipSettings } from './pip-rate.component';

export const definePipSettings = (value: number, pipSize: number): TPipSettings => {
	const valueString = value.toString();
	const pipString = pipSize.toString();
	const valueDotIndex = valueString.indexOf('.');
	const pipDotIndex = pipString.indexOf('.');
	const valueInteger = valueDotIndex > -1 ? valueString.slice(0, valueDotIndex) : valueString;

	if (pipSize < 1) {
		const pipDecimal = pipSize <= 1 ? pipString.slice(pipDotIndex + 1, pipString.length) : '';
		const pipStart = pipDecimal.length + valueInteger.length - 2;
		return { start: pipStart, length: 2 };
	} else if (pipString.length === valueInteger.length) {
		return { start: 0, length: 1 };
	} else {
		const pipStart = valueInteger.length - pipString.length - 1;
		return { start: pipStart, length: 2 };
	}
};
