import { SocketClient } from '../../api/socket-client';
import { EntityStore } from '@devexperts/rx-utils/dist/rd/entity-store.utils';
import { LiveData } from '@devexperts/rx-utils/dist/rd/live-data.utils';
import { asks } from '@devexperts/rx-utils/dist/context.utils';
import { InstrumentinfoController } from '../../api/generated/swagger/controllers/InstrumentinfoController';
import { InstrumentInfoTO } from '../../api/generated/swagger/definitions/InstrumentInfoTO';
import { merge } from 'rxjs';
import { filter, map, switchMap } from 'rxjs/operators';
import { tapOnLastUnsubscribe } from '../../utils/rxjs/tap-on-last-unsubscribe';
import { createHandler } from '@devexperts/rx-utils/dist/create-handler.utils';
import { Sink } from '@devexperts/rx-utils/dist/sink.utils';
import { mapRD } from '@devexperts/rx-utils/dist/rd/operators/mapRD';
import { isAvaInstrumentInfoTO } from '../../api/socket/socket-message.to';
import { success } from '@devexperts/remote-data-ts';
import { combineLatestRD } from '@devexperts/rx-utils/dist/rd/operators/combineLatestRD';
import { filterMap } from '@devexperts/rx-utils/dist/operators/filter-map';
import { findFirst } from 'fp-ts/lib/Array';
import { shareReplayWithDefaults } from '../../utils/rxjs/shareReplayWithDefaults';

export type InstrumentInfoStore = Readonly<{
	getBySymbol: (symbol: string) => LiveData<Error, InstrumentInfoTO>;
}>;

type CreateInstrumentInfoStore = () => Sink<InstrumentInfoStore>;

type InstrumentInfoStoreContext = {
	socketClient: SocketClient;
	instrumentInfoController: InstrumentinfoController;
};

const pk = (entity: InstrumentInfoTO) => entity.symbol;

export const createInstrumentInfoStore = asks(
	(e: InstrumentInfoStoreContext): CreateInstrumentInfoStore => {
		return () => {
			const store = new EntityStore<Error, InstrumentInfoTO>();

			const removeBySymbol = createHandler<string>();

			const removeEffect$ = removeBySymbol.value$.pipe(
				switchMap(symbol =>
					store.remove(symbol, pk, () =>
						e.instrumentInfoController
							.changeInstrumentInfoSubscription({
								body: {
									symbolsToAdd: [],
									symbolsToRemove: [symbol],
								},
							})
							.pipe(mapRD(() => [])),
					),
				),
				shareReplayWithDefaults(),
			);

			const effects$ = merge(removeEffect$);

			const getBySymbol = (symbol: string) => {
				const instrumentInfoMessages$: LiveData<Error, InstrumentInfoTO> = e.socketClient.message$.pipe(
					filter(isAvaInstrumentInfoTO),
					filterMap(data => findFirst(data.body, item => item.symbol === symbol)),
					map(data => success<Error, InstrumentInfoTO>(data)),
					shareReplayWithDefaults(),
				);

				const request$ = e.instrumentInfoController.changeInstrumentInfoSubscription({
					body: {
						symbolsToAdd: [symbol],
						symbolsToRemove: [],
					},
				});

				return store.get(symbol, () =>
					combineLatestRD(request$, instrumentInfoMessages$).pipe(
						mapRD(([_, instrumentInfo]) => instrumentInfo),
						tapOnLastUnsubscribe(() => removeBySymbol.handle(symbol)),
						shareReplayWithDefaults(),
					),
				);
			};
			return new Sink(
				{
					getBySymbol,
				},
				effects$,
			);
		};
	},
);
