import { createSymbolInfoViewModel, SymbolInfoViewModel } from '../../../../view-model/info/symbol-info.view-model';
import { SymbolInfo } from '../../components/symbol-info/symbol-info.component';
import { ask, combineContext, deferContext } from '@devexperts/rx-utils/dist/context.utils';
import { withRXWithDefaults } from '../../../../utils/withRXWithDefaults';
import { runOnMount } from '../../../../utils/react.utils';
import { pending } from '@devexperts/remote-data-ts';
import { createSelector } from 'reselect';

export type SymbolInfoContainerContext = {
	symbolInfoViewModel: SymbolInfoViewModel;
};

const Container = combineContext(SymbolInfo, ask<SymbolInfoContainerContext>(), (SymbolInfo, e) =>
	withRXWithDefaults(SymbolInfo)(() => {
		const { symbolInfoViewModel } = e;
		const { symbolInfoData$ } = symbolInfoViewModel;

		return {
			props: { data: symbolInfoData$ },
			defaultProps: { data: pending },
		};
	}),
);

export const SymbolInfoContainer = combineContext(
	deferContext(Container, 'symbolInfoViewModel'),
	createSymbolInfoViewModel,
	(Container, createSymbolInfoViewModel) =>
		runOnMount(Container, props => {
			const symbol = createSelector(
				props,
				props => props.symbol,
			);

			return createSelector(
				symbol,
				symbol => ({
					symbolInfoViewModel: createSymbolInfoViewModel(symbol),
				}),
			);
		}),
);
