import * as React from 'react';
import { WithTranslation, withTranslation } from 'react-i18next';
import * as O from 'fp-ts/lib/Option';
import { RemoteData } from '@devexperts/remote-data-ts';
import { combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { RemoteDataState } from '../../../../ui-common/remote-data-state/remote-data-state.component';
import { SidePanel } from '../../../../ui-common/side-panel/side-panel.component';
import { HistoryDetailsData } from '../../../../ui-common/history-details/history-details.model';
import { HistoryDetailsCommon } from '../../../../ui-common/history-details/history-details.component';
import { memo, Fragment, ReactNode } from 'react';
import { empty } from 'fp-ts/lib/Record';
import { SymbolComponent } from '../../../../ui-common/symbol/symbol';
import { tspan, TRANSLATION_PLACEHOLDER } from '../../../../utils/i18n.utils';
import { routes } from '../../../../ui-common/routes';
import { CloseButton } from '../../../../ui-common/close-button/close-button.component';
import { RouteLink } from '../../../../ui-common/route-link/containers/route-link.container';
import { pipe } from 'fp-ts/lib/pipeable';
import { constant } from 'fp-ts/lib/function';
import * as css from './theme/history-details.module.css';

export type HistoryDetailsProps = {
	orderId: number;
	data: RemoteData<Error, HistoryDetailsData>;
};

export const HistoryDetails = combineContext(HistoryDetailsCommon, RouteLink, (HistoryDetailsCommon, RouteLink) =>
	withTranslation()(
		memo((props: HistoryDetailsProps & WithTranslation) => {
			const { data, orderId } = props;

			const renderHeader = (symbol: string, orderChainId: string) => (
				<Fragment>
					<div className={css.headerText}>
						<SymbolComponent
							title={pipe(
								orderChainId,
								O.fromNullable,
								O.fold<ReactNode, ReactNode>(constant('—'), x =>
									tspan('historyDetailsId', 'ID', `${TRANSLATION_PLACEHOLDER} ${x}`),
								),
							)}
							subtitle={symbol}
						/>
					</div>
					<RouteLink
						to={routes.history.root}
						value={empty}
						render={({ onNavigate }) => <CloseButton onClick={onNavigate} />}
					/>
				</Fragment>
			);

			const renderSuccess = (localData: HistoryDetailsData) => {
				const { symbol, orderChainId } = localData;
				return (
					<SidePanel header={renderHeader(symbol, orderChainId)} scrollable>
						<HistoryDetailsCommon data={data} orderId={orderId} />
					</SidePanel>
				);
			};

			return <RemoteDataState data={data} success={renderSuccess} />;
		}),
	),
);
