import { combineContext, ask } from '@devexperts/rx-utils/dist/context.utils';
import { mapRD } from '@devexperts/rx-utils/dist/rd/operators/mapRD';
import { switchMapRD } from '@devexperts/rx-utils/dist/rd/operators/switchMapRD';
import { combineLatestRD } from '@devexperts/rx-utils/dist/rd/operators/combineLatestRD';
import { LiveData } from '@devexperts/rx-utils/dist/rd/live-data.utils';
import { OrderEntrySide } from '../../model/order-entry/order-entry.model';
import { OrderStore } from '../../store/order/order.store';
import { InstrumentStore } from '../../store/instrument/instrument.store';
import { getOrderSideByQuantity } from './orders.utils';

type OrderEditData = {
	symbol: string;
	side: OrderEntrySide;
};

export type OrderEditViewModel = {
	orderEditData$: LiveData<Error, OrderEditData>;
};

type CreateOrderEditViewModel = (orderId: number) => OrderEditViewModel;

type OrderEditViewModelContext = {
	orderStore: OrderStore;
	instrumentStore: InstrumentStore;
};

export const createOrderEditViewModel = combineContext(
	ask<OrderEditViewModelContext>(),
	(context): CreateOrderEditViewModel => orderId => {
		const {
			orderStore: { getById },
			instrumentStore,
		} = context;

		const order$ = getById(orderId);
		const instrument$ = order$.pipe(switchMapRD(order => instrumentStore.getById(order.instrumentId)));

		const orderEditData$ = combineLatestRD(order$, instrument$).pipe(
			mapRD(([order, instrument]) => {
				const side = getOrderSideByQuantity(order.quantity);

				const orderEditData: OrderEditData = {
					symbol: instrument.symbol,
					side,
				};

				return orderEditData;
			}),
		);

		return { orderEditData$ };
	},
);
