import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { asks } from '@devexperts/rx-utils/dist/context.utils';
import { AccountViewModel } from '../account/account.view-model';
import * as O from 'fp-ts/lib/Option';
import { pipe } from 'fp-ts/lib/pipeable';

export type ReloginViewModel = {
	shouldOpenPopup$: Observable<boolean>;
};

type ReloginViewModelContext = {
	accountViewModel: AccountViewModel;
};

type CreateReloginViewModel = () => ReloginViewModel;

export const createReloginViewModel = asks(
	(e: ReloginViewModelContext): CreateReloginViewModel => {
		const { logoutEvent$ } = e.accountViewModel;

		const handledState$ = logoutEvent$.pipe(
			map(logoutEvent =>
				pipe(
					logoutEvent.reasonTO,
					O.exists(reason =>
						pipe(
							reason.reasonType,
							O.exists(type => type === 'SESSION_EXPIRED'),
						),
					),
				),
			),
		);

		return () => ({
			shouldOpenPopup$: handledState$,
		});
	},
);
