import { Option } from 'fp-ts/lib/Option';
import { union, literal, string, type } from 'io-ts';
import { createOptionFromNullable } from 'io-ts-types';

export type LogoutReasonTO = {
	reasonType: Option<
		| 'SESSION_EXPIRED'
		| 'USER_DISABLED'
		| 'SESSION_CLOSED_BY_DEALER'
		| 'SESSION_CLOSED_BY_REQUEST'
		| 'EXTERNAL_USER_DISABLED'
		| 'SESSION_FORCE_OUT'
		| 'UNKNOWN'
	>;
	message: Option<string>;
};
export const LogoutReasonTOIO = type({
	reasonType: createOptionFromNullable(
		union([
			literal('SESSION_EXPIRED'),
			literal('USER_DISABLED'),
			literal('SESSION_CLOSED_BY_DEALER'),
			literal('SESSION_CLOSED_BY_REQUEST'),
			literal('EXTERNAL_USER_DISABLED'),
			literal('SESSION_FORCE_OUT'),
			literal('UNKNOWN'),
		]),
	),
	message: createOptionFromNullable(string),
});
