import { LiveData } from '@devexperts/rx-utils/dist/rd/live-data.utils';
import { switchMapRD } from '@devexperts/rx-utils/dist/rd/operators/switchMapRD';
import { mapRD } from '@devexperts/rx-utils/dist/rd/operators/mapRD';
import { combineContext, ask } from '@devexperts/rx-utils/dist/context.utils';
import { PositionsStore } from '../../store/positions/positions.store';
import { InstrumentStore } from '../../store/instrument/instrument.store';
import { eqString } from 'fp-ts/lib/Eq';
import { distinctUntilChangedStrict } from '../../utils/live-data.utils';
import { first } from 'rxjs/operators';

export type PositionDashboardChartViewModel = {
	symbol$: LiveData<Error, string>;
	costBasis$: LiveData<Error, number>;
};

type CreatePositionDashboardChart = (positionId: string) => PositionDashboardChartViewModel;

type PositionDashboardChartContext = {
	positionsStore: PositionsStore;
	instrumentStore: InstrumentStore;
};

export const createPositionDashboardChartViewModel = combineContext(
	ask<PositionDashboardChartContext>(),
	(context): CreatePositionDashboardChart => positionId => {
		const { positionsStore, instrumentStore } = context;
		const position$ = positionsStore.getPositionByUid(positionId);
		const costBasis$ = position$.pipe(
			mapRD(position => position.costBasis),
			first(),
		);
		const symbol$ = position$.pipe(
			switchMapRD(position =>
				instrumentStore.getById(position.positionKey.instrumentId).pipe(mapRD(instrument => instrument.symbol)),
			),
			distinctUntilChangedStrict(eqString),
		);

		return {
			symbol$,
			costBasis$,
		};
	},
);
