import { string, number, union, literal, type } from 'io-ts';

export type LimitsDomainTO = {
	symbol: string;
	instrumentId: number;
	limitStopDistanceType: 'PIPS' | 'PERCENT_OF_SPREAD';
	limitStopDistance: number;
	limitStopDistanceInPercentOfSpread: number;
	minOrderSize: number;
	maxOrderSize: number;
	minOrderIncrement: number;
	limitType: 'LOTS' | 'NOTIONAL_CURRENCY';
	defaultOrderSize: number;
};
export const LimitsDomainTOIO = type({
	symbol: string,
	instrumentId: number,
	limitStopDistanceType: union([literal('PIPS'), literal('PERCENT_OF_SPREAD')]),
	limitStopDistance: number,
	limitStopDistanceInPercentOfSpread: number,
	minOrderSize: number,
	maxOrderSize: number,
	minOrderIncrement: number,
	limitType: union([literal('LOTS'), literal('NOTIONAL_CURRENCY')]),
	defaultOrderSize: number,
});
