import { ask, combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { withRXWithDefaults } from '../../../../utils/withRXWithDefaults';
import { OrdersViewModel } from '../../../../view-model/orders/orders.view-model';
import { OrderEntity } from '../../components/order-entity/order-entity.component';
import { switchMap, map, first } from 'rxjs/operators';
import { pending } from '@devexperts/remote-data-ts';
import { mapRD } from '@devexperts/rx-utils/dist/rd/operators/mapRD';
import { distinctRDOptionNumber, distinctRDOptionDate } from '../../../../utils/setoid.utils';
import { combineLatestRD } from '@devexperts/rx-utils/dist/rd/operators/combineLatestRD';
import { toOrderItem } from '../../../../view-model/orders/orders.utils';

export type OrderEntityContainerContext = {
	ordersViewModel: OrdersViewModel;
};

export const OrderEntityContainer = combineContext(
	OrderEntity,
	ask<OrderEntityContainerContext>(),
	(OrderEntity, context) =>
		withRXWithDefaults(OrderEntity)(props$ => {
			const { ordersViewModel } = context;

			const orderId$ = props$.pipe(
				// rowData is orderId
				map(({ rowData }) => rowData),
				first(),
			);

			const orderItem$ = orderId$.pipe(
				switchMap(orderId => ordersViewModel.getById(orderId)),
				mapRD(toOrderItem),
			);

			// constant values
			const orderIdRD$ = orderItem$.pipe(mapRD(({ orderId }) => orderId));
			const symbol$ = orderItem$.pipe(mapRD(({ symbol }) => symbol));
			const precision$ = orderItem$.pipe(mapRD(({ precision }) => precision));
			const side$ = orderItem$.pipe(mapRD(({ side }) => side));
			const quantity$ = orderItem$.pipe(mapRD(({ quantity }) => quantity));

			// probably will change
			const triggerPrice$ = orderItem$.pipe(
				mapRD(({ triggerPrice }) => triggerPrice),
				distinctRDOptionNumber,
			);
			const distance$ = orderItem$.pipe(
				mapRD(({ distance }) => distance),
				distinctRDOptionNumber,
			);
			const stopLoss$ = orderItem$.pipe(
				mapRD(({ stopLoss }) => stopLoss),
				distinctRDOptionNumber,
			);
			const takeProfit$ = orderItem$.pipe(
				mapRD(({ takeProfit }) => takeProfit),
				distinctRDOptionNumber,
			);
			const validUntil$ = orderItem$.pipe(
				mapRD(({ validUntil }) => validUntil),
				distinctRDOptionDate,
			);

			const combinedConstantData$ = combineLatestRD(orderIdRD$, symbol$, precision$, side$, quantity$).pipe(
				first(),
			);

			const combinedVariableData$ = combineLatestRD(
				triggerPrice$,
				distance$,
				stopLoss$,
				takeProfit$,
				validUntil$,
			);

			const data$ = combineLatestRD(combinedConstantData$, combinedVariableData$).pipe(
				mapRD(
					([
						[orderId, symbol, precision, side, quantity],
						[triggerPrice, distance, stopLoss, takeProfit, validUntil],
					]) => ({
						orderId,
						symbol,
						precision,
						side,
						quantity,
						triggerPrice,
						distance,
						stopLoss,
						takeProfit,
						validUntil,
					}),
				),
			);

			return {
				props: {
					remoteData: data$,
				},
				defaultProps: {
					remoteData: pending,
					onClose: ordersViewModel.onClose,
				},
			};
		}),
);
