import * as React from 'react';
import { Component, ComponentClass } from 'react';
import * as cn from 'classnames';
import { withTheme } from '@devexperts/react-kit/dist/utils/withTheme';
import { isNotNullable } from '@devexperts/utils/dist/object';
import {
	TSelectboxAnchorProps,
	TFullSelectboxAnchorProps,
} from '@devexperts/react-kit/dist/components/Selectbox/SelectboxAnchor';
import { Button } from '../button/button.component';
import { ArrowDownFlatIcon } from '../icons/arrow-down-flat.icon';
import { getFlagIcon } from './locale-selector.utils';
import { Locales } from './locale-selector.model';
import * as css from './theme/locale-selector-anchor.module.css';

class RawLocaleSelectorAnchor extends Component<TFullSelectboxAnchorProps> {
	render() {
		const { theme, children, onClick, isOpened, value } = this.props;

		const buttonTheme = {
			container: cn(theme.container, {
				[theme.container_isOpened as string]: isOpened,
			}),
		};

		return (
			<Button onClick={onClick} theme={buttonTheme}>
				<div className={theme.content}>
					<div className={theme.text}>{isNotNullable(value) ? getFlagIcon(value as Locales) : null}</div>
					<div className={cn({ [css.current_opened]: isOpened })}>
						<i className={css.icon}>
							<ArrowDownFlatIcon />
						</i>
					</div>
				</div>
				{children}
			</Button>
		);
	}
}

export const LocaleSelectorAnchor: ComponentClass<TSelectboxAnchorProps> = withTheme(Symbol('LocaleSelectorAnchor'))(
	RawLocaleSelectorAnchor,
);
