import * as React from 'react';
import { PureComponent } from 'react';
import { ButtonIcon, TButtonIconProps } from '@devexperts/react-kit/dist/components/ButtonIcon/ButtonIcon';
import { withTheme } from '@devexperts/react-kit/dist/utils/withTheme';
import { CloseIcon } from '../icons/close.icon';
import * as css from './theme/close-button.module.css';

export type CloseButtonProps = {
	onClick: () => void;
	theme: TButtonIconProps['theme'];
};

export class RawCloseButton extends PureComponent<CloseButtonProps> {
	render() {
		const { onClick, theme } = this.props;
		return <ButtonIcon icon={<CloseIcon />} onClick={onClick} theme={theme} />;
	}
}

export const CloseButton = withTheme(Symbol('CloseButton'), css)(RawCloseButton);
