import * as React from 'react';
import { PureComponent } from 'react';
import { withTranslation, WithTranslation } from 'react-i18next';
import { formatNumber } from 'accounting';
import { Checkbox } from '../../../../ui-common/checkbox/checkbox.component';
import { NumericStepper } from '../../../../ui-common/numeric-stepper-ava/numeric-stepper.component';
import { DateInput } from '../../../ui-kit/components/date-input/date-input';
import { Calendar } from '../../../ui-kit/components/calendar/calendar.component';
import * as O from 'fp-ts/lib/Option';
import * as E from 'fp-ts/lib/Either';
import { constNull, constFalse } from 'fp-ts/lib/function';
import { empty } from 'fp-ts/lib/Record';
import { TDateInputValue } from '@devexperts/react-kit/dist/components/DateInput/DateInput.model';
import { RemoteDataState } from '../../../../ui-common/remote-data-state/remote-data-state.component';
import {
	OrderEntryExecutionCheckboxes,
	OrderEntryExecutionVisibleFormDates,
	OrderEntryExecutionVisibleFormPrice,
	StepperData,
	ExecutionPriceValidationError,
	ExecutionPriceValidationErrorMessages,
} from './order-entry-execution.model';
import { RemoteData } from '@devexperts/remote-data-ts';
import { tspan } from '../../../../utils/i18n.utils';
import { pipe } from 'fp-ts/lib/pipeable';
import * as css from '../order-entry-panel/theme/order-entry-panel.module.css';
import * as stepperCss from '../order-entry-panel/theme/numeric-stepper.child.module.css';
import * as steppableButtonCss from '../order-entry-panel/theme/numeric-stepper.button.module.css';
import * as stepperErrorCss from '../order-entry-panel/theme/numeric-stepper-error.child.module.css';

const dateInputTheme = {
	Input: stepperCss,
	SteppableInput: {
		ButtonIcon: steppableButtonCss,
	},
};

export type OrderEntryExecutionComponentProps = {
	isShownValidErrors: boolean;
	visibleValues: OrderEntryExecutionCheckboxes;
	onToggleCheckbox: (values: OrderEntryExecutionCheckboxes) => void;
	isChangeable: boolean;
	formValues: OrderEntryExecutionVisibleFormPrice;
	formDate: OrderEntryExecutionVisibleFormDates;
	onChangeExecutionPrice: (values: OrderEntryExecutionVisibleFormPrice) => void;
	onChangeExecutionDates: (values: OrderEntryExecutionVisibleFormDates) => void;
	stepperData: RemoteData<Error, StepperData>;
};

class OrderEntryExecutionComponentUnwrapped extends PureComponent<OrderEntryExecutionComponentProps & WithTranslation> {
	render() {
		const { visibleValues, formDate, isChangeable, isShownValidErrors, formValues, stepperData } = this.props;

		return (
			<div className={css.optionsItem}>
				<Checkbox
					onValueChange={this.handleToggleCheckbox('executePriceOpened')}
					isDisabled={isChangeable}
					value={visibleValues.executePriceOpened}>
					{tspan('oeExecutionExecTitle', 'Execute When Price Hits')}
				</Checkbox>
				{visibleValues.executePriceOpened && (
					<div className={css.info}>
						<div className={css.priceHits}>
							<div className={css.label}>{tspan('oeExecutionPrice', 'Price')}</div>
							{pipe(
								formValues.priceValue,
								O.map(price => (
									<RemoteDataState
										data={stepperData}
										success={stepperData => this.renderStepper(price, stepperData)}
									/>
								)),
								O.toNullable,
							)}
							{isShownValidErrors && this.renderError()}
						</div>
						<div className={css.cancelOnDateCheckbox}>
							<Checkbox
								onValueChange={this.handleToggleCheckbox('expireAtActive')}
								value={visibleValues.expireAtActive}
							/>
						</div>
						<div className={css.cancelOnDate}>
							<div className={css.label}>{tspan('oeExecutionCoD', 'Cancel on Date')}</div>
							<DateInput
								value={formDate.expireAt}
								onValueChange={this.handleDateInput}
								Calendar={Calendar}
								min={formDate.disabledDaysMin}
								isDisabled={!visibleValues.expireAtActive}
								theme={dateInputTheme}
							/>
						</div>
					</div>
				)}
			</div>
		);
	}

	renderStepper = (price: number, { priceIncrement, precision }: StepperData) => {
		const {
			formValues: { executionPriceValidated },
			isShownValidErrors,
		} = this.props;

		const theme = {
			Input: stepperCss,
			SteppableInput: {
				Input: {
					...(E.isLeft(executionPriceValidated) && isShownValidErrors ? stepperErrorCss : empty),
				},
				ButtonIcon: steppableButtonCss,
			},
		};

		return (
			<NumericStepper
				value={price}
				min={priceIncrement}
				max={1000000}
				step={priceIncrement}
				formatter={(inputValue: number) => formatNumber(inputValue, precision)}
				onIncrement={this.handlePriceInput}
				onDecrement={this.handlePriceInput}
				onValueChange={this.handlePriceInput}
				theme={theme}
			/>
		);
	};

	renderError = () =>
		pipe(
			this.props.formValues.executionPriceValidated,
			E.fold(error => <div className={css.validationError}>{this.stringifyPriceErrors(error)}</div>, constNull),
		);

	private stringifyPriceErrors = ({ message, hiRange, loRange, step }: ExecutionPriceValidationError) => {
		switch (message) {
			case ExecutionPriceValidationErrorMessages.General:
				return tspan('oeExecutionValidationErrorInvalidValue', { defaultValue: 'Invalid value' });
			case ExecutionPriceValidationErrorMessages.OverRange:
				return tspan('oeExecutionValidationErrorOverRange', {
					defaultValue: 'You can issue order starting from {{hiRange}}',
					hiRange,
				});
			case ExecutionPriceValidationErrorMessages.BelowRange:
				return tspan('oeExecutionValidationErrorBelowRange', {
					defaultValue: 'You can issue order lower than {{loRange}}',
					loRange,
				});
			case ExecutionPriceValidationErrorMessages.NotInRange:
				return tspan('oeExecutionValidationErrorNotInRange', {
					defaultValue: 'You can issue order starting from {{hiRange}} or lower than {{loRange}}',
					loRange,
					hiRange,
				});
			case ExecutionPriceValidationErrorMessages.WrongStepping:
				return tspan('oeExecutionValidationErrorWrongStepping', {
					defaultValue: 'Minimum step is {{step}}',
					step,
				});
		}
	};

	handleToggleCheckbox = (name: keyof OrderEntryExecutionCheckboxes) => (value?: boolean) => {
		const { onToggleCheckbox, visibleValues } = this.props;

		onToggleCheckbox({
			...visibleValues,
			expireAtActive: name === 'executePriceOpened' && value === false,
			[name]: pipe(
				value,
				O.fromNullable,
				O.getOrElse(constFalse),
			),
		});
	};

	handlePriceInput = (value: number) => {
		const { onChangeExecutionPrice, formValues } = this.props;
		onChangeExecutionPrice({ ...formValues, priceValue: O.some(value) });
	};

	handleDateInput = (date: TDateInputValue) => {
		const { onChangeExecutionDates, formDate } = this.props;
		onChangeExecutionDates({ ...formDate, expireAt: date });
	};
}

export const OrderEntryExecutionComponent = withTranslation()(OrderEntryExecutionComponentUnwrapped);
