import { ResponseValidationError, TAPIClient } from '../client/client';
import { BracketGroupTemplateTO, BracketGroupTemplateTOIO } from '../definitions/BracketGroupTemplateTO';
import { HistoryOrderTO, HistoryOrderTOIO } from '../definitions/HistoryOrderTO';
import {
	IfThenBracketGroupTemplateTO,
	IfThenBracketGroupTemplateTOIO,
} from '../definitions/IfThenBracketGroupTemplateTO';
import { IfThenOCOGroupTemplateTO, IfThenOCOGroupTemplateTOIO } from '../definitions/IfThenOCOGroupTemplateTO';
import { OCOGroupTemplateTO, OCOGroupTemplateTOIO } from '../definitions/OCOGroupTemplateTO';
import { OSOGroupTemplateTO, OSOGroupTemplateTOIO } from '../definitions/OSOGroupTemplateTO';
import { OrderTO, OrderTOIO } from '../definitions/OrderTO';
import { OrderTemplateTO, OrderTemplateTOIO } from '../definitions/OrderTemplateTO';
import { ResponseResultTO, ResponseResultTOIO } from '../definitions/ResponseResultTO';
import { ValidationResultTO, ValidationResultTOIO } from '../definitions/ValidationResultTO';
import { fromEither } from '@devexperts/remote-data-ts';
import { LiveData } from '@devexperts/rx-utils/dist/rd/live-data.utils';
import { Option } from 'fp-ts/lib/Option';
import { asks } from 'fp-ts/lib/Reader';
import { array, string, type, partial, number, void as tvoid } from 'io-ts';
import { createOptionFromNullable } from 'io-ts-types';
import { map } from 'rxjs/operators';

export type OrdersController = {
	/**
	 * Get list of existing orders for specified accounts
	 * @param { object } parameters
	 */
	readonly getOrders: (parameters: { query: { accountId: Array<string> } }) => LiveData<Error, Array<OrderTO>>;

	/**
	 * Modify bracket order
	 * @param { object } [parameters]
	 */
	readonly modifyBracketOrder: (parameters: { body?: BracketGroupTemplateTO }) => LiveData<Error, ResponseResultTO>;

	/**
	 * Place bracket order
	 * @param { object } [parameters]
	 */
	readonly placeBracketOrder: (parameters: { body?: BracketGroupTemplateTO }) => LiveData<Error, ResponseResultTO>;

	/**
	 * Cancel order
	 * @param { object } parameters
	 */
	readonly cancelOrder: (parameters: {
		query: { accountId: string; orderChainId: string };
	}) => LiveData<Error, ResponseResultTO>;

	/**
	 * Get order history within date range
	 * @param { object } [parameters]
	 */
	readonly getOrderHistory: (parameters: {
		query?: { from: Option<number>; to: Option<number> };
	}) => LiveData<Error, Array<HistoryOrderTO>>;

	/**
	 * Request order history within date range
	 * @param { object } [parameters]
	 */
	readonly requestOrderHistory: (parameters: {
		query?: { from: Option<number>; to: Option<number> };
	}) => LiveData<Error, void>;

	/**
	 * Modify IF-THEN-BRACKET order
	 * @param { object } [parameters]
	 */
	readonly modifyIfThenBracketOrder: (parameters: {
		body?: IfThenBracketGroupTemplateTO;
	}) => LiveData<Error, ResponseResultTO>;

	/**
	 * Place IF-THEN-BRACKET order
	 * @param { object } [parameters]
	 */
	readonly placeIfThenBracketOrder: (parameters: {
		body?: IfThenBracketGroupTemplateTO;
	}) => LiveData<Error, ResponseResultTO>;

	/**
	 * Modify IF-THEN-OCO order
	 * @param { object } [parameters]
	 */
	readonly modifyIfThenOcoOrder: (parameters: {
		body?: IfThenOCOGroupTemplateTO;
	}) => LiveData<Error, ResponseResultTO>;

	/**
	 * Place IF-THEN-OCO order
	 * @param { object } [parameters]
	 */
	readonly placeIfThenOcoOrder: (parameters: {
		body?: IfThenOCOGroupTemplateTO;
	}) => LiveData<Error, ResponseResultTO>;

	/**
	 * Cancel all orders
	 */
	readonly cancelAllOrders: () => LiveData<Error, void>;

	/**
	 * Modify OCO order
	 * @param { object } [parameters]
	 */
	readonly modifyOCOOrder: (parameters: { body?: OCOGroupTemplateTO }) => LiveData<Error, ResponseResultTO>;

	/**
	 * Place OCO order
	 * @param { object } [parameters]
	 */
	readonly placeOCOOrder: (parameters: { body?: OCOGroupTemplateTO }) => LiveData<Error, ResponseResultTO>;

	/**
	 * Validate single order
	 * @param { object } [parameters]
	 */
	readonly validateOcoOrder: (parameters: {
		body?: OCOGroupTemplateTO;
	}) => LiveData<Error, Array<ValidationResultTO>>;

	/**
	 * Modify OSO order
	 * @param { object } [parameters]
	 */
	readonly modifyIfThenOrder: (parameters: { body?: OSOGroupTemplateTO }) => LiveData<Error, ResponseResultTO>;

	/**
	 * Place OSO order
	 * @param { object } [parameters]
	 */
	readonly placeIfThenOrder: (parameters: { body?: OSOGroupTemplateTO }) => LiveData<Error, ResponseResultTO>;

	/**
	 * Modify single order
	 * @param { object } [parameters]
	 */
	readonly modifySingleOrder: (parameters: { body?: OrderTemplateTO }) => LiveData<Error, ResponseResultTO>;

	/**
	 * Place single order
	 * @param { object } [parameters]
	 */
	readonly placeSingleOrder: (parameters: { body?: OrderTemplateTO }) => LiveData<Error, ResponseResultTO>;

	/**
	 * Validate single order
	 * @param { object } [parameters]
	 */
	readonly validateOrder: (parameters: { body?: OrderTemplateTO }) => LiveData<Error, Array<ValidationResultTO>>;

	/**
	 * Unsubscribe order history within date range
	 * @param { object } [parameters]
	 */
	readonly unsubscribeHistory: (parameters: {
		query?: { from: Option<number>; to: Option<number> };
	}) => LiveData<Error, void>;
};

export const ordersController = asks(
	(e: { apiClient: TAPIClient }): OrdersController => ({
		getOrders: parameters => {
			const encoded = partial({ query: type({ accountId: array(string) }) }).encode(parameters);

			return e.apiClient
				.request({
					url: `/orders`,
					method: 'GET',
					query: encoded.query,
				})
				.pipe(
					map(data =>
						data.chain(value =>
							fromEither(
								array(OrderTOIO)
									.decode(value)
									.mapLeft(ResponseValidationError.create),
							),
						),
					),
				);
		},

		modifyBracketOrder: parameters => {
			const encoded = partial({ body: BracketGroupTemplateTOIO }).encode(parameters);

			return e.apiClient
				.request({
					url: `/orders/bracket`,
					method: 'PUT',

					body: encoded.body,
				})
				.pipe(
					map(data =>
						data.chain(value =>
							fromEither(ResponseResultTOIO.decode(value).mapLeft(ResponseValidationError.create)),
						),
					),
				);
		},

		placeBracketOrder: parameters => {
			const encoded = partial({ body: BracketGroupTemplateTOIO }).encode(parameters);

			return e.apiClient
				.request({
					url: `/orders/bracket`,
					method: 'POST',

					body: encoded.body,
				})
				.pipe(
					map(data =>
						data.chain(value =>
							fromEither(ResponseResultTOIO.decode(value).mapLeft(ResponseValidationError.create)),
						),
					),
				);
		},

		cancelOrder: parameters => {
			const encoded = partial({ query: type({ accountId: string, orderChainId: string }) }).encode(parameters);

			return e.apiClient
				.request({
					url: `/orders/cancel`,
					method: 'DELETE',
					query: encoded.query,
				})
				.pipe(
					map(data =>
						data.chain(value =>
							fromEither(ResponseResultTOIO.decode(value).mapLeft(ResponseValidationError.create)),
						),
					),
				);
		},

		getOrderHistory: parameters => {
			const encoded = partial({
				query: type({ from: createOptionFromNullable(number), to: createOptionFromNullable(number) }),
			}).encode(parameters);

			return e.apiClient
				.request({
					url: `/orders/history`,
					method: 'GET',
					query: encoded.query,
				})
				.pipe(
					map(data =>
						data.chain(value =>
							fromEither(
								array(HistoryOrderTOIO)
									.decode(value)
									.mapLeft(ResponseValidationError.create),
							),
						),
					),
				);
		},

		requestOrderHistory: parameters => {
			const encoded = partial({
				query: type({ from: createOptionFromNullable(number), to: createOptionFromNullable(number) }),
			}).encode(parameters);

			return e.apiClient
				.request({
					url: `/orders/history`,
					method: 'POST',
					query: encoded.query,
				})
				.pipe(
					map(data =>
						data.chain(value => fromEither(tvoid.decode(value).mapLeft(ResponseValidationError.create))),
					),
				);
		},

		modifyIfThenBracketOrder: parameters => {
			const encoded = partial({ body: IfThenBracketGroupTemplateTOIO }).encode(parameters);

			return e.apiClient
				.request({
					url: `/orders/if-then-bracket`,
					method: 'PUT',

					body: encoded.body,
				})
				.pipe(
					map(data =>
						data.chain(value =>
							fromEither(ResponseResultTOIO.decode(value).mapLeft(ResponseValidationError.create)),
						),
					),
				);
		},

		placeIfThenBracketOrder: parameters => {
			const encoded = partial({ body: IfThenBracketGroupTemplateTOIO }).encode(parameters);

			return e.apiClient
				.request({
					url: `/orders/if-then-bracket`,
					method: 'POST',

					body: encoded.body,
				})
				.pipe(
					map(data =>
						data.chain(value =>
							fromEither(ResponseResultTOIO.decode(value).mapLeft(ResponseValidationError.create)),
						),
					),
				);
		},

		modifyIfThenOcoOrder: parameters => {
			const encoded = partial({ body: IfThenOCOGroupTemplateTOIO }).encode(parameters);

			return e.apiClient
				.request({
					url: `/orders/if-then-oco`,
					method: 'PUT',

					body: encoded.body,
				})
				.pipe(
					map(data =>
						data.chain(value =>
							fromEither(ResponseResultTOIO.decode(value).mapLeft(ResponseValidationError.create)),
						),
					),
				);
		},

		placeIfThenOcoOrder: parameters => {
			const encoded = partial({ body: IfThenOCOGroupTemplateTOIO }).encode(parameters);

			return e.apiClient
				.request({
					url: `/orders/if-then-oco`,
					method: 'POST',

					body: encoded.body,
				})
				.pipe(
					map(data =>
						data.chain(value =>
							fromEither(ResponseResultTOIO.decode(value).mapLeft(ResponseValidationError.create)),
						),
					),
				);
		},

		cancelAllOrders: () => {
			return e.apiClient
				.request({
					url: `/orders/liquidate`,
					method: 'POST',
				})
				.pipe(
					map(data =>
						data.chain(value => fromEither(tvoid.decode(value).mapLeft(ResponseValidationError.create))),
					),
				);
		},

		modifyOCOOrder: parameters => {
			const encoded = partial({ body: OCOGroupTemplateTOIO }).encode(parameters);

			return e.apiClient
				.request({
					url: `/orders/oco`,
					method: 'PUT',

					body: encoded.body,
				})
				.pipe(
					map(data =>
						data.chain(value =>
							fromEither(ResponseResultTOIO.decode(value).mapLeft(ResponseValidationError.create)),
						),
					),
				);
		},

		placeOCOOrder: parameters => {
			const encoded = partial({ body: OCOGroupTemplateTOIO }).encode(parameters);

			return e.apiClient
				.request({
					url: `/orders/oco`,
					method: 'POST',

					body: encoded.body,
				})
				.pipe(
					map(data =>
						data.chain(value =>
							fromEither(ResponseResultTOIO.decode(value).mapLeft(ResponseValidationError.create)),
						),
					),
				);
		},

		validateOcoOrder: parameters => {
			const encoded = partial({ body: OCOGroupTemplateTOIO }).encode(parameters);

			return e.apiClient
				.request({
					url: `/orders/oco/validate`,
					method: 'POST',

					body: encoded.body,
				})
				.pipe(
					map(data =>
						data.chain(value =>
							fromEither(
								array(ValidationResultTOIO)
									.decode(value)
									.mapLeft(ResponseValidationError.create),
							),
						),
					),
				);
		},

		modifyIfThenOrder: parameters => {
			const encoded = partial({ body: OSOGroupTemplateTOIO }).encode(parameters);

			return e.apiClient
				.request({
					url: `/orders/oso`,
					method: 'PUT',

					body: encoded.body,
				})
				.pipe(
					map(data =>
						data.chain(value =>
							fromEither(ResponseResultTOIO.decode(value).mapLeft(ResponseValidationError.create)),
						),
					),
				);
		},

		placeIfThenOrder: parameters => {
			const encoded = partial({ body: OSOGroupTemplateTOIO }).encode(parameters);

			return e.apiClient
				.request({
					url: `/orders/oso`,
					method: 'POST',

					body: encoded.body,
				})
				.pipe(
					map(data =>
						data.chain(value =>
							fromEither(ResponseResultTOIO.decode(value).mapLeft(ResponseValidationError.create)),
						),
					),
				);
		},

		modifySingleOrder: parameters => {
			const encoded = partial({ body: OrderTemplateTOIO }).encode(parameters);

			return e.apiClient
				.request({
					url: `/orders/single`,
					method: 'PUT',

					body: encoded.body,
				})
				.pipe(
					map(data =>
						data.chain(value =>
							fromEither(ResponseResultTOIO.decode(value).mapLeft(ResponseValidationError.create)),
						),
					),
				);
		},

		placeSingleOrder: parameters => {
			const encoded = partial({ body: OrderTemplateTOIO }).encode(parameters);

			return e.apiClient
				.request({
					url: `/orders/single`,
					method: 'POST',

					body: encoded.body,
				})
				.pipe(
					map(data =>
						data.chain(value =>
							fromEither(ResponseResultTOIO.decode(value).mapLeft(ResponseValidationError.create)),
						),
					),
				);
		},

		validateOrder: parameters => {
			const encoded = partial({ body: OrderTemplateTOIO }).encode(parameters);

			return e.apiClient
				.request({
					url: `/orders/single/validate`,
					method: 'POST',

					body: encoded.body,
				})
				.pipe(
					map(data =>
						data.chain(value =>
							fromEither(
								array(ValidationResultTOIO)
									.decode(value)
									.mapLeft(ResponseValidationError.create),
							),
						),
					),
				);
		},

		unsubscribeHistory: parameters => {
			const encoded = partial({
				query: type({ from: createOptionFromNullable(number), to: createOptionFromNullable(number) }),
			}).encode(parameters);

			return e.apiClient
				.request({
					url: `/orders/unsubscribeHistory`,
					method: 'POST',
					query: encoded.query,
				})
				.pipe(
					map(data =>
						data.chain(value => fromEither(tvoid.decode(value).mapLeft(ResponseValidationError.create))),
					),
				);
		},
	}),
);
