import { Observable } from 'rxjs/internal/Observable';
import { WithRXSelectorResult, withRX } from '@devexperts/react-kit/dist/utils/with-rx2';
import { ComponentType } from 'react';
import { asap } from 'rxjs/internal/scheduler/asap';

export const withRXWithDefaults = <P extends object>(Target: ComponentType<P>) => <D extends Partial<P>>(
	selector: (props$: Observable<Readonly<P>>) => WithRXSelectorResult<P, D>,
) =>
	withRX(Target)(selector, {
		scheduler: asap,
	});
