import * as React from 'react';
import { Component, ReactNode } from 'react';
import { withTranslation, WithTranslation } from 'react-i18next';
import { withTheme } from '@devexperts/react-kit/dist/utils/withTheme';
import { Button } from '../button/button.component';
import { PURE } from '@devexperts/react-kit/dist/utils/pure';
import { MakeTheme } from '../../utils/theme.utils';
import { tspan } from '../../utils/i18n.utils';
import * as css from './theme/cancel-confirmation.module.css';
import * as buttonCss from './theme/button.child.module.css';
import * as buttonSubmitCss from './theme/button-submit.child.module.css';

export type CancelConfirmationProps = {
	theme: MakeTheme<'container' | 'title'>;
	title: ReactNode;
	onSubmit: (e: React.MouseEvent<HTMLButtonElement>) => void;
	onCancel: () => void;
};

@PURE
class CancelConfirmationRaw extends Component<CancelConfirmationProps & WithTranslation> {
	render() {
		const { theme, title } = this.props;
		return (
			<div className={theme.container}>
				<div className={theme.title}>{title}</div>
				<Button onClick={this.handleClose} theme={buttonCss}>
					{tspan('confirmationDialogNo', 'No')}
				</Button>
				<Button onClick={this.handleSubmit} theme={buttonSubmitCss}>
					{tspan('confirmationDialogYes', 'Yes')}
				</Button>
			</div>
		);
	}

	handleClose = () => {
		this.props.onCancel();
	};

	handleSubmit = (e: React.MouseEvent<HTMLButtonElement>) => {
		this.props.onSubmit(e);
	};
}

export const CancelConfirmation = withTheme(Symbol(), css)(withTranslation()(CancelConfirmationRaw));
