import Emitter from '@devexperts/utils/dist/emitter/Emitter';

/**
 * CrossToolViewModel events
 * @enum {String}
 */
export const E_CROSS_TOOL_PLUGIN_MODEL = {
	CHANGE_IS_FIXED: 'CHANGE:IS_FIXED',
	CHANGE_IS_ENABLED: 'CHANGE:IS_ENABLED',
	CHANGE_DATETIME_FORMATTER: 'CHANGE:DATETIME_FORMATTER',
};

export type CrossToolConfig = {
	isFixed: boolean;
	isEnabled: boolean;
	dateTimeFormatter: (date: Date) => string;
};

/**
 * @typedef {Object} TCrossToolPluginModel
 * @property {boolean} [isFixed]
 * @property {boolean} [isEnabled]
 * @property {function(date:Date):String} [dateTimeFormatter]
 */

/**
 * @class CrossToolViewModel
 */
export default class CrossToolViewModel extends Emitter {
	private _isFixed = false;

	get isFixed() {
		return this._isFixed;
	}

	set isFixed(value: boolean) {
		if (this._isFixed !== value) {
			this._isFixed = value;
			this._emit(E_CROSS_TOOL_PLUGIN_MODEL.CHANGE_IS_FIXED, value);
		}
	}

	private _isEnabled = true;

	get isEnabled() {
		return this._isEnabled;
	}

	set isEnabled(value: boolean) {
		if (this._isEnabled !== value) {
			this._isEnabled = value;
			this._emit(E_CROSS_TOOL_PLUGIN_MODEL.CHANGE_IS_ENABLED, value);
		}
	}

	private _dateTimeFormatter: CrossToolConfig['dateTimeFormatter'] = value => value.toString();

	get dateTimeFormatter() {
		return this._dateTimeFormatter;
	}

	set dateTimeFormatter(formatter: (date: Date) => string) {
		if (this._dateTimeFormatter !== formatter) {
			this._dateTimeFormatter = formatter;
			this._emit(E_CROSS_TOOL_PLUGIN_MODEL.CHANGE_DATETIME_FORMATTER, formatter);
		}
	}

	/**
	 * @param {TCrossToolPluginModel} [config]
	 */
	constructor(config = CrossToolViewModel.config) {
		super();
		let merged = config;
		if (config !== CrossToolViewModel.config) {
			merged = Object.assign({}, CrossToolViewModel.config, config);
		}
		this.isFixed = merged.isFixed;
		this.isEnabled = merged.isEnabled;
		this.dateTimeFormatter = merged.dateTimeFormatter;
	}

	/**
	 * @type {TCrossToolPluginModel}
	 */
	static config: CrossToolConfig = {
		isFixed: true,
		isEnabled: false,
		dateTimeFormatter: value => value.toString(),
	};
}
