import { OrderEntryData, OrderValue } from '../../../../view-model/order-entry/order-entry.view-model';
import { OrderEntryPanelData, AmountValidationError } from '../../components/order-entry-panel/order-entry-panel.model';
import { definePipSettings } from '../../../../ui-common/pip-rate/pip-settings.utils';
import { Either, left, right } from 'fp-ts/lib/Either';
import { Option } from 'fp-ts/lib/Option';
import * as O from 'fp-ts/lib/Option';
import { pipe } from 'fp-ts/lib/pipeable';
import { constant } from 'fp-ts/lib/function';

export function toOrderEntryPanelData(
	data: OrderEntryData,
	minOrderSize: number,
	maxOrderSize: number,
	amountValidated: Either<AmountValidationError, string>,
	value: OrderValue,
	amountPrecision: number,
	unitName: Option<string>,
): OrderEntryPanelData {
	return {
		buy: {
			amount: data.ask,
			pipSettings: pipe(
				data.ask,
				O.fold(constant({ start: 0, length: 2 }), ask => definePipSettings(ask, data.pipsSize)),
			),
		},
		sell: {
			amount: data.bid,
			pipSettings: pipe(
				data.bid,
				O.fold(constant({ start: 0, length: 2 }), bid => definePipSettings(bid, data.pipsSize)),
			),
		},
		description: data.description,
		marginImpact: data.marginImpact,
		step: data.step,
		amountPrecision,
		precision: data.precision,
		minOrderSize,
		maxOrderSize,
		amountValidated,
		value,
		unitName,
	};
}

export const getEitherFromSideAndValue = (isLeft: boolean, value: string): Either<string, string> =>
	isLeft ? left(value) : right(value);
