import { literal, Tagged, type, voidType } from 'io-ts';

export const ACCOUNT_SWITCHED = 'ACCOUNT_SWITCHED';

export type AccountSwitchedMessageTO = {
	type: typeof ACCOUNT_SWITCHED;
	body: void;
};

export const accountSwitchedMessageTO: Tagged<'type', AccountSwitchedMessageTO, unknown> = type({
	type: literal(ACCOUNT_SWITCHED),
	body: voidType,
});
