import * as React from 'react';
import { PureComponent, Fragment } from 'react';
import { Option } from 'fp-ts/lib/Option';
import * as O from 'fp-ts/lib/Option';
import { combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { Route } from '../../../../ui-common/route/containers/route.container';
import { routes } from '../../../../ui-common/routes';
import { MatchType } from '../../../../utils/router.utils';
import { PositionContainer } from '../../containers/position-table/position-table.container';
import { RemoteDataState } from '../../../../ui-common/remote-data-state/remote-data-state.component';
import { PositionCloseContainer } from '../../../positions/containers/position-close/position-close.container';
import { EditingPositionContainer } from '../../../positions/containers/editing-position/editing-position.container';
import { PositionDashboardChartContainer } from '../../containers/position-dashboard-chart/position-dashboard-chart.container';
import { PositionDetailsContainer } from '../../../positions/containers/position-details/position-details.container';
import { RemoteData } from '@devexperts/remote-data-ts';
import { ScreenType } from '../../../../ui-common/empty-screen/components/empty-screen.component';
import { EmptyScreenContainer } from '../../../empty-screen/containers/empty-screen.container';
import { Workspace } from '../../../ui-kit/components/workspace/workspace.component';
import { WorkspaceChart } from '../../../ui-kit/components/workspace/workspace-chart.component';
import { Redirect } from '../../../../ui-common/redirect/containers/redirect.container';
import { pipe } from 'fp-ts/lib/pipeable';
import * as css from './theme/positions-dashboard.module.css';

export type PositionsDashboardProps = {
	firstPositionId: RemoteData<Error, Option<string>>;
};

export const PositionsDashboard = combineContext(
	Route,
	PositionContainer,
	PositionCloseContainer,
	EditingPositionContainer,
	PositionDetailsContainer,
	PositionDashboardChartContainer,
	EmptyScreenContainer,
	Redirect,
	(
		Route,
		PositionContainer,
		PositionCloseContainer,
		EditingPositionContainer,
		PositionDetailsContainer,
		PositionDashboardChartContainer,
		EmptyScreenContainer,
		Redirect,
	) => {
		return class PositionsDashboard extends PureComponent<PositionsDashboardProps> {
			render() {
				const { firstPositionId } = this.props;

				return <RemoteDataState data={firstPositionId} success={this.renderSuccess} />;
			}

			renderSuccess = (firstPositionId: Option<string>) => {
				return (
					<section className={css.container}>
						<div className={css.left}>
							<Workspace>
								<Route match={routes.positions.root} render={this.renderRoot(firstPositionId)} />
							</Workspace>
							<Route match={routes.positions.position.root} render={this.renderChart} />
						</div>
						<Route match={routes.positions.position.close.root} exact render={this.renderAside} />
						<Route
							match={routes.positions.position.edit.profitLossKind.root}
							exact
							render={this.renderEditingPositionAside}
						/>
						<Route
							match={routes.positions.position.info.root}
							exact
							render={this.renderPositionDetailsAside}
						/>
					</section>
				);
			};

			private renderRoot = (positionId: Option<string>) => () => {
				return pipe(
					positionId,
					O.fold(
						() => (
							<Fragment>
								<Route
									match={routes.positions.position.root}
									exact
									render={() => <Redirect to={routes.positions.root} value={{}} />}
								/>
								<EmptyScreenContainer screenType={ScreenType.Positions} />
							</Fragment>
						),
						positionId => (
							<Fragment>
								<Route match={routes.positions.position.root} render={this.renderPosition} />
								<Route
									match={routes.positions.root}
									exact
									render={() => (
										<Redirect to={routes.positions.position.root} value={{ positionId }} />
									)}
								/>
							</Fragment>
						),
					),
				);
			};

			private renderPosition = () => <PositionContainer />;

			private renderAside = (path: MatchType<typeof routes.positions.position.close.root>) => (
				<div className={css.aside}>
					<PositionCloseContainer uid={path.positionId} />
				</div>
			);

			private renderEditingPositionAside = (
				path: MatchType<typeof routes.positions.position.edit.profitLossKind.root>,
			) => (
				<div className={css.aside}>
					<EditingPositionContainer uid={path.positionId} plKind={path.profitLossKind} />
				</div>
			);

			private renderPositionDetailsAside = (path: MatchType<typeof routes.positions.position.info.root>) => (
				<div className={css.aside}>
					<PositionDetailsContainer uid={path.positionId} />
				</div>
			);

			private renderChart = (path: MatchType<typeof routes.positions.position.root>) => (
				<WorkspaceChart>
					<PositionDashboardChartContainer positionId={path.positionId} />
				</WorkspaceChart>
			);
		};
	},
);
