import * as React from 'react';
import { PureComponent } from 'react';
import { combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { ListChildComponentProps } from 'react-window';
import { AutosizedTable as Table } from '../../../../ui-common/autosized-table-ava/autosized-table.component';
import { ConfigItem, PositionsTableColumnNames, TableSize } from './position-table.model';
import { getColumnsConfig } from './position-table.utils';
import { PositionEntityContainer } from '../../containers/position-table/position-entity.container';
import { withTranslation, WithTranslation } from 'react-i18next';
import { tspan, TRANSLATION_PLACEHOLDER } from '../../../../utils/i18n.utils';

export type PositionTableProps = {
	positionsUid: string[];
	accountCurrency: string;
	config: ConfigItem[];
	tableSize: TableSize;
} & WithTranslation;

export const PositionTable = combineContext(PositionEntityContainer, PositionEntityContainer =>
	withTranslation()(
		class PositionTable extends PureComponent<PositionTableProps> {
			private getTranslatedColumnNames = (): PositionsTableColumnNames => {
				const { accountCurrency } = this.props;

				return {
					symbol: tspan('positionsTableColumnSymbol', 'Symbol'),
					side: tspan('positionsTableColumnSide', 'Side'),
					size: tspan('positionsTableColumnSize', 'Size'),
					value: tspan(
						'positionsTableColumnValue',
						'Value',
						`${TRANSLATION_PLACEHOLDER}, ${accountCurrency}`,
					),
					profitLoss: tspan(
						'positionsTableColumnProfitLoss',
						'Profit/Loss',
						`${TRANSLATION_PLACEHOLDER}, ${accountCurrency}`,
					),
					openPrice: tspan('positionsTableColumnOpenPrice', 'Open Price'),
					stopLoss: tspan('positionsTableColumnStopLoss', 'Stop Loss'),
					takeProfit: tspan('positionsTableColumnTakeProfit', 'Take Profit'),
				};
			};

			render() {
				const { positionsUid, config } = this.props;

				const columnNames = this.getTranslatedColumnNames();

				return (
					<Table
						itemCount={positionsUid.length}
						itemSize={60}
						itemData={positionsUid}
						itemKey={this.getItemKey}
						columns={getColumnsConfig(config, columnNames)}>
						{this.renderRow}
					</Table>
				);
			}

			private getItemKey = (rowIndex: number, data: string[]) => data[rowIndex];

			private renderRow = (props: ListChildComponentProps) => (
				<PositionEntityContainer
					config={this.props.config}
					tableSize={this.props.tableSize}
					rowData={props.data[props.index]}
					{...props}
				/>
			);
		},
	),
);
