import { map, distinctUntilChanged } from 'rxjs/internal/operators';
import { Observable, combineLatest } from 'rxjs';
import { ask, combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { AccountViewModel } from '../account/account.view-model';
import { isDemoMode } from './header.utils';
import * as O from 'fp-ts/lib/Option';
import { pipe } from 'fp-ts/lib/pipeable';
import { constant } from 'fp-ts/lib/function';

export type HeaderViewModel = {
	showDemoButton$: Observable<boolean>;
	showDepositButton$: Observable<boolean>;
};

export type HeaderViewModelContext = {
	accountViewModel: AccountViewModel;
};

export type CreateHeaderViewModel = () => HeaderViewModel;

export const createHeaderViewModel = combineContext(
	ask<HeaderViewModelContext>(),
	(e): CreateHeaderViewModel => {
		const { accountViewModel } = e;
		const { current$, availableAccounts$, equity$ } = accountViewModel;

		const showDemoButton$ = combineLatest(current$, availableAccounts$).pipe(
			map(([current, available]) => isDemoMode(current, available)),
		);

		const showDepositButton$ = combineLatest(current$, availableAccounts$, equity$).pipe(
			map(
				([current, available, equity]) =>
					!isDemoMode(current, available) &&
					pipe(
						equity,
						O.getOrElse(constant(0)),
					) === 0,
			),
			distinctUntilChanged(),
		);

		return () => ({
			showDemoButton$,
			showDepositButton$,
		});
	},
);
