import * as React from 'react';
import { Fragment, memo, useCallback } from 'react';
import { WithTranslation, withTranslation } from 'react-i18next';
import { formatNumber } from 'accounting';
import { Option } from 'fp-ts/lib/Option';
import { RemoteData } from '@devexperts/remote-data-ts';
import { PipRate } from '../pip-rate/pip-rate.component';
import { definePipSettings } from '../pip-rate/pip-settings.utils';
import { tspan, TRANSLATION_PLACEHOLDER } from '../../utils/i18n.utils';
import { Optional } from '../optional/optional.component';
import { RemoteDataState } from '../remote-data-state/remote-data-state.component';
import { DailyTradingHours, SymbolInfoData } from './symbol-info.model';
import { PriceDirection } from '../price-direction/price-direction.component';
import { SymbolInfoItem } from '../symbol-info-item/symbol-info-item.component';
import { empty } from 'fp-ts/lib/Array';
import { sequenceTOption } from '../../utils/option.utils';
import { MakeTheme } from '../../utils/theme.utils';
import { pipe } from 'fp-ts/lib/pipeable';
import { constant } from 'fp-ts/lib/function';
import * as O from 'fp-ts/lib/Option';
import * as css from './theme/symbol-info.module.css';
import * as directionPriceCss from './theme/price-direction.child.module.css';

export type SymbolInfoProps = {
	data: RemoteData<Error, SymbolInfoData>;
	symbol: string;
	categoryId: string;
	theme?: MakeTheme<'item' | 'item_secondary' | 'label' | 'value' | 'container' | 'title' | 'subtitle'>;
} & WithTranslation;

export const SymbolInfoCommon = withTranslation()(
	memo((props: SymbolInfoProps) => {
		const { data, t, theme } = props;

		const renderPrice = useCallback(
			(value: Option<number>, pipsSize: number, precision: number, direction: Option<number>) =>
				pipe(
					sequenceTOption(value, direction),
					O.map(([value, direction]) => (
						<Fragment>
							<PriceDirection direction={direction} theme={directionPriceCss} />
							<PipRate value={value} rise={definePipSettings(value, pipsSize)} digits={precision} />
						</Fragment>
					)),
				),
			[definePipSettings],
		);

		const renderChange = useCallback(
			(value: Option<number>) =>
				pipe(
					value,
					O.map(value => (
						<Fragment>
							{value < 0 && <span className={css.prefixChange}>–</span>}
							{formatNumber(Math.abs(value), 2)}
						</Fragment>
					)),
				),
			[formatNumber],
		);

		const renderTradingHours = useCallback(
			(dailyTradingHours: DailyTradingHours) =>
				pipe(
					dailyTradingHours,
					O.map(dailyTradingHours => (
						<Fragment>
							{dailyTradingHours.toArray().map((item, index) => (
								<span
									key={index}
									className={css.tradingHours}>{`${item.startTime} - ${item.endTime}`}</span>
							))}
						</Fragment>
					)),
				),
			[empty],
		);

		const renderSuccess = useCallback(
			(data: SymbolInfoData) => {
				const {
					pipSize,
					timeZoneOffset,
					tradingHours,
					currentBuyPrice,
					buyPriceDirection,
					currentSellPrice,
					sellPriceDirection,
					measurementUnit,
					precision,
					lotSize,
					dayHigh,
					dayLow,
					changeDay,
					changeWeek,
					changeMonth,
					marketSentimentLeft,
					marketSentimentRight,
				} = data;

				const marketSentimentWidth = pipe(
					marketSentimentLeft,
					O.fold(constant('0%'), value => `${value}%`),
				);

				return (
					<Fragment>
						<SymbolInfoItem theme={theme} label={tspan('symbolInfoMarketSentiment', 'Market sentiment')}>
							<div className={css.marketSentiment}>
								<span>
									<Optional value={marketSentimentLeft} /> %
								</span>
								<span>
									<Optional value={marketSentimentRight} /> %
								</span>
								<div className={css.marketSentimentBar}>
									<div
										className={css.marketSentimentBarLeft}
										style={{ width: marketSentimentWidth }}
									/>
								</div>
							</div>
						</SymbolInfoItem>
						<SymbolInfoItem theme={theme} label={tspan('symbolInfoCurrentBuyPrice', 'Current buy price')}>
							<Optional value={renderPrice(currentBuyPrice, pipSize, precision, buyPriceDirection)} />
						</SymbolInfoItem>
						<SymbolInfoItem theme={theme} label={tspan('symbolInfoCurrentSellPrice', 'Current sell price')}>
							<Optional value={renderPrice(currentSellPrice, pipSize, precision, sellPriceDirection)} />
						</SymbolInfoItem>
						<SymbolInfoItem theme={theme} label={tspan('symbolInfoMeasurementUnit', 'Measurement unit')}>
							<Optional value={measurementUnit} />
						</SymbolInfoItem>
						<SymbolInfoItem theme={theme} label={tspan('symbolInfoLotSize', 'Lot size')}>
							{formatNumber(lotSize, { thousand: ' ' })}
						</SymbolInfoItem>
						<SymbolInfoItem theme={theme} label={tspan('symbolInfoTradingHours', 'Trading hours')}>
							<Optional value={timeZoneOffset} />
						</SymbolInfoItem>
						<SymbolInfoItem theme={theme} label={tspan('symbolInfoSun', 'Sun')} isSecondary={true}>
							<Optional value={renderTradingHours(tradingHours.sunday)} />
						</SymbolInfoItem>
						<SymbolInfoItem theme={theme} label={tspan('symbolInfoMon', 'Mon')} isSecondary={true}>
							<Optional value={renderTradingHours(tradingHours.monday)} />
						</SymbolInfoItem>
						<SymbolInfoItem theme={theme} label={tspan('symbolInfoTue', 'Tue')} isSecondary={true}>
							<Optional value={renderTradingHours(tradingHours.tuesday)} />
						</SymbolInfoItem>
						<SymbolInfoItem theme={theme} label={tspan('symbolInfoWed', 'Wed')} isSecondary={true}>
							<Optional value={renderTradingHours(tradingHours.wednesday)} />
						</SymbolInfoItem>
						<SymbolInfoItem theme={theme} label={tspan('symbolInfoThu', 'Thu')} isSecondary={true}>
							<Optional value={renderTradingHours(tradingHours.thursday)} />
						</SymbolInfoItem>
						<SymbolInfoItem theme={theme} label={tspan('symbolInfoFri', 'Fri')} isSecondary={true}>
							<Optional value={renderTradingHours(tradingHours.friday)} />
						</SymbolInfoItem>
						<SymbolInfoItem theme={theme} label={tspan('symbolInfoSat', 'Sat')} isSecondary={true}>
							<Optional value={renderTradingHours(tradingHours.saturday)} />
						</SymbolInfoItem>
						{pipe(
							dayHigh,
							O.map(value => (
								<SymbolInfoItem theme={theme} label={tspan('symbolInfoDayHigh', 'Day high')}>
									<PipRate
										value={value}
										rise={definePipSettings(value, pipSize)}
										digits={precision}
									/>
								</SymbolInfoItem>
							)),
							O.toNullable,
						)}
						{pipe(
							dayLow,
							O.map(value => (
								<SymbolInfoItem theme={theme} label={tspan('symbolInfoDayLow', 'Day low')}>
									<PipRate
										value={value}
										rise={definePipSettings(value, pipSize)}
										digits={precision}
									/>
								</SymbolInfoItem>
							)),
							O.toNullable,
						)}
						<SymbolInfoItem
							theme={theme}
							label={tspan('symbolInfoDayChange', 'Day change', `${TRANSLATION_PLACEHOLDER}, %`)}>
							<Optional value={renderChange(changeDay)} />
						</SymbolInfoItem>
						<SymbolInfoItem
							theme={theme}
							label={tspan('symbolInfoWeekChange', 'Week change', `${TRANSLATION_PLACEHOLDER}, %`)}>
							<Optional value={renderChange(changeWeek)} />
						</SymbolInfoItem>
						<SymbolInfoItem
							theme={theme}
							label={tspan('symbolInfoMonthChange', 'Month change', `${TRANSLATION_PLACEHOLDER}, %`)}>
							<Optional value={renderChange(changeMonth)} />
						</SymbolInfoItem>
					</Fragment>
				);
			},
			[t, renderPrice, formatNumber, renderTradingHours, definePipSettings, renderChange],
		);

		return <RemoteDataState data={data} success={renderSuccess} />;
	}),
);
