import * as React from 'react';
import { PureComponent, Fragment } from 'react';
import { WithTranslation, withTranslation } from 'react-i18next';
import { ButtonCta } from '../../ui-common/button/button-cta.component';
import { Popup } from '../ui-kit/components/popup/popup.component';
import { Iframe } from '../../ui-common/iframe/iframe.component';
import { tspan, getLanguageForCRM } from '../../utils/i18n.utils';
import * as buttonCss from './theme/button.child.module.css';
import * as css from './theme/upgrade-account-button.module.css';

type UpgradeAccountButtonProps = {};

type UpgradeAccountButtonState = {
	isPopupOpened: boolean;
};

class UpgradeAccountButtonRaw extends PureComponent<
	UpgradeAccountButtonProps & WithTranslation,
	UpgradeAccountButtonState
> {
	readonly state: UpgradeAccountButtonState = {
		isPopupOpened: false,
	};

	render() {
		const { isPopupOpened } = this.state;
		const { i18n } = this.props;
		const language = getLanguageForCRM(i18n);
		return (
			<Fragment>
				<ButtonCta theme={buttonCss} onClick={this.handleClick}>
					{tspan('upgradeAccountToRealMoney', 'Upgrade to Real')}
				</ButtonCta>
				<Popup
					title={tspan('upgradeAccountToRealMoney', 'Upgrade to Real Money')}
					onClose={this.handleClose}
					isOpened={isPopupOpened}
					isModal={true}>
					<div className={css.iframe}>
						<Iframe src={`/api/avacrm/register?lang=${language}`} />
					</div>
				</Popup>
			</Fragment>
		);
	}

	private handleClick = () => {
		this.setState({ isPopupOpened: true });
	};

	private handleClose = () => {
		this.setState({ isPopupOpened: false });
	};
}

export const UpgradeAccountButton = withTranslation()(UpgradeAccountButtonRaw);
