import * as React from 'react';
import { PureComponent } from 'react';
import { FixedSizeList, FixedSizeListProps } from 'react-window';
import { raf } from '@devexperts/utils/dist/function/raf';
import { isNotNullable } from '@devexperts/utils/dist/object';
import { TableHeader } from './table-header.component';
import { CustomScrollbarsVirtualList } from './table-scrollbars.component';
import { Column } from './table.model';
import { getVerticalOffset } from './table.utils';
import { isMobileBuild } from '../../utils/node-env.utils';
import { pipe } from 'fp-ts/lib/pipeable';
import * as O from 'fp-ts/lib/Option';
import * as css from './theme/table.module.css';

export type TableProps = {
	columns: Array<Column>;
	tableHeadClassname?: string;
	scrollToItem?: number;
} & FixedSizeListProps;

export class Table extends PureComponent<TableProps> {
	outerRef!: HTMLDivElement | null;

	scheduleScroll = (scrollToItem: number, itemSize: number) =>
		raf(
			() =>
				this.outerRef &&
				pipe(
					getVerticalOffset(scrollToItem, itemSize, this.outerRef),
					O.map(value => (this.outerRef ? (this.outerRef.scrollTop = value) : null)),
					O.toNullable,
				),
		);

	componentDidMount() {
		const { scrollToItem, itemSize } = this.props;
		isNotNullable(scrollToItem) && this.scheduleScroll(scrollToItem, itemSize)();
	}

	componentDidUpdate() {
		const { scrollToItem, itemSize } = this.props;
		isNotNullable(scrollToItem) && this.scheduleScroll(scrollToItem, itemSize)();
	}

	render() {
		const { width, height, columns, tableHeadClassname } = this.props;
		const outerElementType = isMobileBuild ? undefined : CustomScrollbarsVirtualList;

		return (
			<div className={css.container} style={{ width, height }}>
				<TableHeader columns={columns} className={tableHeadClassname} />
				<FixedSizeList
					outerRef={ref => (this.outerRef = ref)}
					className={css.body}
					outerElementType={outerElementType}
					{...this.props}
				/>
			</div>
		);
	}
}
