import { eqString } from 'fp-ts/lib/Eq';
import * as O from 'fp-ts/lib/Option';
import { head } from 'fp-ts/lib/Array';
import { withRXWithDefaults } from '../../../../utils/withRXWithDefaults';
import { pending } from '@devexperts/remote-data-ts';
import { ask, combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { PositionsDashboard } from '../../components/positions-dashboard/positions-dashboard.component';
import { PositionsStore } from '../../../../store/positions/positions.store';
import { distinctUntilChangedStrict } from '../../../../utils/live-data.utils';
import { mapRD } from '@devexperts/rx-utils/dist/rd/operators/mapRD';
import { pipe } from 'fp-ts/lib/pipeable';

export type PositionDashboardContainerContext = {
	positionsStore: PositionsStore;
};

export const PositionDashboardContainer = combineContext(
	PositionsDashboard,
	ask<PositionDashboardContainerContext>(),
	(PositionsDashboard, e) =>
		withRXWithDefaults(PositionsDashboard)(() => {
			const { positionsStore } = e;

			const firstPositionId$ = positionsStore.all$.pipe(
				mapRD(list =>
					pipe(
						list,
						head,
						O.map(position => position.uid),
					),
				),
				distinctUntilChangedStrict(O.getEq(eqString)),
			);

			return {
				props: {
					firstPositionId: firstPositionId$,
				},
				defaultProps: {
					firstPositionId: pending,
				},
			};
		}),
);
