import { LiveData } from '@devexperts/rx-utils/dist/rd/live-data.utils';
import { HistoryOrderStore } from '../../store/history-order/history-order.store';
import { combineContext, ask } from '@devexperts/rx-utils/dist/context.utils';
import { mapRD } from '@devexperts/rx-utils/dist/rd/operators/mapRD';
import { Option } from 'fp-ts/lib/Option';
import { HistoryOrderSide } from '../../model/history/history.model';
import { toOrderSideEnum } from '../../ui-desktop/history/containers/history-table/history-table.utils';

export type HistoryTableData = {
	orderId: number;
	openDateTime: Option<Date>;
	instrument: string;
	profitLoss: Option<number>;
	side: HistoryOrderSide;
	amount: number;
	openPrice: Option<number>;
	closePrice: Option<number>;
	closeDateTime: Date;
	precision: number;
};

export type HistoryTableViewModel = {
	data$: LiveData<Error, HistoryTableData[]>;
};

type HistoryTableViewModelContext = {
	historyOrderStore: HistoryOrderStore;
};

type CreateHistoryTableViewModel = () => HistoryTableViewModel;

export const createHistoryTableViewModel = combineContext(
	ask<HistoryTableViewModelContext>(),
	(HistoryTableContext): CreateHistoryTableViewModel => () => {
		const {
			historyOrderStore: { requestedOrders$ },
		} = HistoryTableContext;

		const filteredOrders$: LiveData<Error, HistoryTableData[]> = requestedOrders$.pipe(
			mapRD(orders =>
				orders.map(to => ({
					orderId: to.orderId,
					openDateTime: to.openTime,
					instrument: to.instrument.symbol,
					profitLoss: to.profitSize,
					side: toOrderSideEnum(to.side),
					amount: Math.abs(to.quantity),
					openPrice: to.openPrice,
					closePrice: to.closePrice,
					closeDateTime: to.closeTime,
					precision: to.instrument.precision,
				})),
			),
		);

		return {
			data$: filteredOrders$,
		};
	},
);
