import { LiveData } from '@devexperts/rx-utils/dist/rd/live-data.utils';
import { QuoteTO } from '../../api/generated/swagger/definitions/QuoteTO';
import { SocketClient } from '../../api/socket-client';
import { combineLatest, Observable } from 'rxjs';
import { distinctUntilChanged, filter, map, mergeMap, scan, startWith } from 'rxjs/operators';
import {
	isQuoteSubscriptionInitializedTO,
	isQuoteTO,
	isSummaryTO,
	isAvaWatchListInstrumentInfoTO,
} from '../../api/socket/socket-message.to';
import { pending, success } from '@devexperts/remote-data-ts';
import { marketDataController } from '../../api/generated/swagger/controllers/MarketDataController';
import { EntityStore } from '@devexperts/rx-utils/dist/rd/entity-store.utils';
import { getEq, none, Option, some } from 'fp-ts/lib/Option';
import { findFirst } from 'fp-ts/lib/Array';
import { mapRD } from '@devexperts/rx-utils/dist/rd/operators/mapRD';
import { switchMapRD } from '@devexperts/rx-utils/dist/rd/operators/switchMapRD';
import { constUndefined } from 'fp-ts/lib/function';
import { createHandler } from '@devexperts/rx-utils/dist/create-handler.utils';
import { SummaryTO } from '../../api/generated/swagger/definitions/SummaryTO';
import { filterMap } from '../../utils/rxjs/filtermap';
import { getStructEq, Eq, eqString } from 'fp-ts/lib/Eq';
import { setoidStrict } from '../../utils/setoid.utils';
import { Sink } from '@devexperts/rx-utils/dist/sink.utils';
import { ask, combineContext, fromReader } from '@devexperts/rx-utils/dist/context.utils';
import { tapOnLastUnsubscribe } from '../../utils/rxjs/tap-on-last-unsubscribe';
import { shareReplayWithDefaults } from '../../utils/rxjs/shareReplayWithDefaults';

export type MarketDataEntity = {
	symbol: string;
	lastQuote: Option<QuoteTO>;
	lastSummary: Option<SummaryTO>;
	dayChange: Option<number>;
};
const setoidMarketDataEntity: Eq<MarketDataEntity> = getStructEq({
	symbol: eqString,
	lastQuote: getEq(setoidStrict),
	lastSummary: getEq(setoidStrict),
	dayChange: getEq(setoidStrict),
});

export type MarketDataStore = {
	readonly getDataBySymbol: (symbol: string) => LiveData<Error, MarketDataEntity>;
	readonly isInitialized$: LiveData<Error, boolean>;
};

export type MarketDataStoreContext = {
	socketClient: SocketClient;
};

export type CreateMarketDataStore = Sink<() => Sink<MarketDataStore>>;

export const createMarketDataStore = combineContext(
	fromReader(marketDataController),
	ask<MarketDataStoreContext>(),
	(marketDataController, e): CreateMarketDataStore => {
		const { socketClient } = e;

		const isInitialized$: LiveData<Error, boolean> = e.socketClient.message$.pipe(
			filter(isQuoteSubscriptionInitializedTO),
			filter(message => message.body.success),
			map(message => success<Error, boolean>(message.body.success)),
			startWith(pending),
			distinctUntilChanged(),
			shareReplayWithDefaults(),
		);

		const quotes$ = socketClient.message$.pipe(filter(isQuoteTO));
		const summaries$ = socketClient.message$.pipe(filter(isSummaryTO));
		const avaWatchlistInstrumentInfo$ = socketClient.message$.pipe(filter(isAvaWatchListInstrumentInfoTO));
		const pk = (entity: MarketDataEntity) => entity.symbol;

		return new Sink(() => {
			const store = new EntityStore<Error, MarketDataEntity>();

			const onSymbolRemove = createHandler<string>();
			const removeSymbolEffect$ = onSymbolRemove.value$.pipe(
				//request on each call
				mergeMap(symbol =>
					store
						.remove(
							symbol,
							pk,
							() =>
								marketDataController
									.removeTemporaryInstrumentSubscription({
										body: {
											symbols: some([symbol]),
										},
									})
									.pipe(mapRD(() => [])),
							true,
						)
						.pipe(mapRD(constUndefined)),
				),
			);

			const getDataBySymbol = (symbol: string): LiveData<Error, MarketDataEntity> =>
				store.get(symbol, () => {
					const request$ = isInitialized$.pipe(
						switchMapRD(() =>
							marketDataController.subscribeInstrumentTemporary({
								body: { symbols: some([symbol]) },
							}),
						),
					);

					const quote$: Observable<Option<QuoteTO>> = quotes$.pipe(
						filterMap(quotes => findFirst(quotes.body.toArray(), quote => quote.symbol === symbol)),
						map(quote => some(quote)),
						startWith(none),
					);
					const summary$: Observable<Option<SummaryTO>> = summaries$.pipe(
						filterMap(summaries =>
							findFirst(summaries.body.toArray(), summary => summary.symbol === symbol),
						),
						map(summary => some(summary)),
						startWith(none),
					);
					const dayChange$ = avaWatchlistInstrumentInfo$.pipe(
						filterMap(changeInfo => findFirst(changeInfo.body, changeInfo => changeInfo.symbol === symbol)),
						map(changeInfo => changeInfo.dayChange),
						startWith(none),
					);

					const entity$: Observable<MarketDataEntity> = combineLatest(quote$, summary$, dayChange$).pipe(
						scan<[Option<QuoteTO>, Option<SummaryTO>, Option<number>], MarketDataEntity>(
							(acc, entity) => {
								const [lastQuote, lastSummary, dayChange] = entity;
								return {
									...acc,
									lastQuote,
									lastSummary,
									dayChange,
								};
							},
							{
								symbol,
								lastQuote: none,
								lastSummary: none,
								dayChange: none,
							},
						),
						distinctUntilChanged(setoidMarketDataEntity.equals),
					);

					const result$ = combineLatest(request$, entity$).pipe(
						//make request and immediately switch to entity$ observable
						//messages in the socket are send _before_ the end of the request
						map(([request, entity]) => request.map(() => entity)),
					);

					return result$.pipe(tapOnLastUnsubscribe(() => onSymbolRemove.handle(symbol)));
				});

			return new Sink(
				{
					getDataBySymbol,
					isInitialized$,
				},
				removeSymbolEffect$,
			);
		}, isInitialized$);
	},
);
