import { some } from 'fp-ts/lib/Option';
import { HistoryOrderTO } from '../../../../api/generated/swagger/definitions/HistoryOrderTO';
import { HistoryOrderSide } from '../../../../model/history/history.model';
import { HistoryTableItem } from '../../components/history-table/history-table.model';
import { HistoryTableData } from '../../../../view-model/history/history-table.view-model';

export const toOrderSideEnum = (rawSide: HistoryOrderTO['side']): HistoryOrderSide => {
	switch (rawSide) {
		case 'BUY':
			return HistoryOrderSide.Buy;
		case 'SELL':
			return HistoryOrderSide.Sell;
	}
};

export const toHistoryTableData = (data: HistoryTableData): HistoryTableItem => ({
	orderId: data.orderId,
	openDateTime: data.openDateTime,
	instrument: some(data.instrument),
	profitLoss: data.profitLoss,
	side: some(data.side),
	amount: some(Math.abs(data.amount)),
	openPrice: data.openPrice,
	closePrice: data.closePrice,
	closeDateTime: some(data.closeDateTime),
	precision: data.precision,
});
