import { withRXWithDefaults } from '../../../../utils/withRXWithDefaults';
import { pending } from '@devexperts/remote-data-ts';
import { ask, combineContext, deferContext } from '@devexperts/rx-utils/dist/context.utils';
import {
	createPositionEntityViewModel,
	PositionEntityViewModel,
} from '../../../../view-model/positions/position-entity.view-model';
import { PositionEntity } from '../../components/position-table/position-entity.component';
import { runOnMount } from '../../../../utils/react.utils';
import { createSelector } from 'reselect';

export type PositionEntityContainerContext = {
	positionEntityViewModel: PositionEntityViewModel;
};

const Container = combineContext(PositionEntity, ask<PositionEntityContainerContext>(), (PositionEntity, e) =>
	withRXWithDefaults(PositionEntity)(() => {
		const {
			positionEntityViewModel: { data$, profitLoss$, symbol$, value$ },
		} = e;

		return {
			props: {
				remoteData: data$,
				symbol: symbol$,
				profitLoss: profitLoss$,
				value: value$,
			},
			defaultProps: {
				remoteData: pending,
				symbol: pending,
				profitLoss: pending,
				value: pending,
			},
		};
	}),
);

export const PositionEntityContainer = combineContext(
	deferContext(Container, 'positionEntityViewModel'),
	createPositionEntityViewModel,
	(Container, createPositionEntityViewModel) =>
		runOnMount(Container, props => {
			const rowData = createSelector(
				props,
				props => props.rowData,
			);
			return createSelector(
				rowData,
				rowData => ({
					positionEntityViewModel: createPositionEntityViewModel(rowData),
				}),
			);
		}),
);
