import { ReactText } from 'react';
import { Period, PeriodNames } from './history-filter-form.model';

export const getMenuItemName = (period: Period, translatedPeriodNames: PeriodNames): string => {
	switch (period) {
		case Period.last7days:
			return translatedPeriodNames.last7days;
		case Period.last30Days:
			return translatedPeriodNames.last30Days;
		case Period.last365days:
			return translatedPeriodNames.last365days;
		case Period.thisMonth:
			return translatedPeriodNames.thisMonth;
		case Period.thisYear:
			return translatedPeriodNames.thisYear;
		case Period.all:
			return translatedPeriodNames.all;
		case Period.custom:
			return translatedPeriodNames.custom;
	}
};

export const isPeriod = (value: ReactText): value is Period => {
	return Object.values(Period)
		.map(p => String(p))
		.includes(String(value));
};
