import { href } from './router.utils';
import { routes } from '../ui-common/routes';
import { isProductionBuild } from './node-env.utils';
import { empty } from 'fp-ts/lib/Record';

export const redirectToRelogin = () => {
	const reloginHref = isProductionBuild ? href(routes.api.auth.relogin, empty) : '/login.html';
	window.location.href = reloginHref;
};
