import * as React from 'react';
import { PureComponent } from 'react';
import cn from 'classnames';
import { Option } from 'fp-ts/lib/Option';
import * as O from 'fp-ts/lib/Option';
import { empty } from 'fp-ts/lib/Record';
import { withTranslation, WithTranslation } from 'react-i18next';
import { combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { routes } from '../../../../../ui-common/routes';
import { LogoIcon } from '../../../../ui-kit/components/icons/logo.icon';
import { HeaderAccountMetricsContainer } from '../../../containers/header-account-metrics/header-account-metrics.container';
import { RouteLink } from '../../../../../ui-common/route-link/containers/route-link.container';
import { Link } from '../../../../../ui-common/link/link.component';
import { RouteLinkRenderProps } from '../../../../../ui-common/route-link/components/route-link.component';
import { UpgradeAccountButton } from '../../../../upgrade-account-button/upgrade-account-button';
import { ButtonCta } from '../../../../../ui-common/button/button-cta.component';
import { tspan } from '../../../../../utils/i18n.utils';
import { pipe } from 'fp-ts/lib/pipeable';
import { constFalse } from 'fp-ts/lib/function';
import { RegistrationButton } from '../../../../ui-kit/components/preview-mode/components/registration-button.component';

import * as css from './theme/header.module.css';
import * as linkCss from './theme/link.child.module.css';
import * as registrationButtonCss from './theme/registration-button.child.module.css';

export type HeaderProps = {
	showDemoButton: boolean;
	showDepositButton: boolean;
	isRealAccount: Option<boolean>;
	previewMode: Option<boolean>;
};

export const Header = combineContext(
	HeaderAccountMetricsContainer,
	RouteLink,
	(HeaderAccountMetricsContainer, RouteLink) =>
		withTranslation()(
			class Header extends PureComponent<HeaderProps & WithTranslation> {
				public render() {
					const { showDemoButton, showDepositButton, isRealAccount, previewMode } = this.props;
					const accountLabelClassName = cn(css.accountLabel, {
						[css.accountLabel_real]: pipe(
							isRealAccount,
							O.getOrElse(constFalse),
						),
					});
					return (
						<header className={css.container}>
							<RouteLink render={this.renderLink} to={routes.root} value={empty} />
							{pipe(
								isRealAccount,
								O.map(isReal => (
									<div className={accountLabelClassName}>
										{isReal ? tspan('headerLabelReal', 'real') : tspan('headerLabelDemo', 'demo')}
									</div>
								)),
								O.toNullable,
							)}
							{showDemoButton && <UpgradeAccountButton />}
							{showDepositButton && (
								<RouteLink
									render={({ onNavigate }) => (
										<ButtonCta onClick={onNavigate}>{tspan('depositButton', 'Deposit')}</ButtonCta>
									)}
									to={routes.crm.deposit.root}
									value={empty}
								/>
							)}
							{pipe(
								previewMode,
								O.map(
									previewMode => previewMode && <RegistrationButton theme={registrationButtonCss} />,
								),
								O.toNullable,
							)}
							<HeaderAccountMetricsContainer previewMode={previewMode} />
						</header>
					);
				}

				private renderLink = (props: RouteLinkRenderProps) => (
					<Link href={props.href} onClick={props.onNavigate} theme={linkCss}>
						<LogoIcon />
					</Link>
				);
			},
		),
);
