import { createBrowserHistory } from 'history';
import { combineContext, deferContext, fromReader, sequenceTContext } from '@devexperts/rx-utils/dist/context.utils';
import { Authorized } from '../../components/authorized/authorized.component';
import { runOnMount } from '../../../../utils/react.utils';
import { createMarketDataStore } from '../../../../store/market-data/market-data.store';
import { createInstrumentStore } from '../../../../store/instrument/instrument.store';
import { createInstrumentInfoStore } from '../../../../store/instrument-info/instrument-info.store';
import { createPositionsStore } from '../../../../store/positions/positions.store';
import { sequenceTSink } from '@devexperts/rx-utils/dist/sink.utils';
import { createOrderStore } from '../../../../store/order/order.store';
import { createRouter } from '../../../../utils/router.utils';
import { createWatchlistStore } from '../../../../store/watchlist/watchlist.store';
import { createChartSettingsStore } from '../../../../store/chart-settings/chart-settings.store';
import { createAccountViewModel } from '../../../../view-model/account/account.view-model';
import { createHistoryOrderStore } from '../../../../store/history-order/history-order.store';
import { symbolSuggestController } from '../../../../api/generated/swagger/controllers/SymbolSuggestController';
import { instrumentinfoController } from '../../../../api/generated/swagger/controllers/InstrumentinfoController';
import { createConversionRateStore } from '../../../../store/conversion-rate/conversion-rate.store';
import { createMetricsStore } from '../../../../store/metrics/metrics.store';
import { createLimitsStore } from '../../../../store/limits/limits.store';
import { tradinghoursendpointController } from '../../../../api/generated/swagger/controllers/TradinghoursendpointController';
import { createMarginImpactStore } from '../../../../store/margin-impact/margin-impact.store';
import { createSystemSettingsViewModel } from '../../../../view-model/system-settings/system-settings.view-model';

const ContainerWithStores = combineContext(
	deferContext(
		Authorized,
		'instrumentStore',
		'marketDataStore',
		'marginImpactStore',
		'instrumentInfoStore',
		'orderStore',
		'watchlistStore',
		'chartSettingsStore',
		'historyOrderStore',
		'positionsStore',
		'metricsStore',
		'conversionRateStore',
		'limitsStore',
	),
	createInstrumentInfoStore,
	createHistoryOrderStore,
	createPositionsStore,
	createConversionRateStore,
	sequenceTContext(createMarketDataStore, createMarginImpactStore, createOrderStore, createWatchlistStore),
	sequenceTContext(createInstrumentStore, createChartSettingsStore, createMetricsStore, createLimitsStore),
	(
		Container,
		createInstrumentInfoStore,
		createHistoryOrderStore,
		createPositionsStore,
		createConversionRateStore,
		[createMarketDataStore, createMarginImpactStore, createOrderStore, createWatchlistStore],
		[createInstrumentStore, createChartSettingsStore, createMetricsStore, createLimitsStore],
	) =>
		sequenceTSink(
			createMarketDataStore(),
			createMarginImpactStore(),
			createConversionRateStore(),
			createInstrumentInfoStore(),
			createLimitsStore(),
		).chain(([marketDataStore, marginImpactStore, conversionRateStore, instrumentInfoStore, limitsStore]) =>
			Container.run({
				marketDataStore,
				marginImpactStore,
				orderStore: createOrderStore(),
				instrumentStore: createInstrumentStore(),
				instrumentInfoStore,
				watchlistStore: createWatchlistStore(),
				chartSettingsStore: createChartSettingsStore(),
				historyOrderStore: createHistoryOrderStore(),
				positionsStore: createPositionsStore(),
				metricsStore: createMetricsStore(),
				conversionRateStore,
				limitsStore,
			}),
		),
);

const ContainerWithStoresAndRouter = combineContext(
	deferContext(ContainerWithStores, 'router'),
	deferContext(createRouter, 'history'),
	(Container, createRouter) =>
		createRouter
			.run({ history: createBrowserHistory() })
			.chain(createRouter => createRouter())
			.chain(router =>
				Container.run({
					router,
				}),
			),
);

const ContainerWithGlobalViewModels = combineContext(
	deferContext(ContainerWithStoresAndRouter, 'accountViewModel', 'systemSettingsViewModel'),
	createAccountViewModel,
	createSystemSettingsViewModel,
	(Container, createAccountViewModel, createSystemSettingsViewModel) =>
		sequenceTSink(createAccountViewModel(), createSystemSettingsViewModel()).chain(
			([accountViewModel, systemSettingsViewModel]) =>
				Container.run({
					accountViewModel,
					systemSettingsViewModel,
				}),
		),
);

export const AuthorizedContainer = combineContext(
	deferContext(
		ContainerWithGlobalViewModels,
		'symbolSuggestController',
		'instrumentInfoController',
		'tradingHoursController',
	),
	fromReader(symbolSuggestController),
	fromReader(instrumentinfoController),
	fromReader(tradinghoursendpointController),
	(Container, symbolSuggestController, instrumentinfoController, tradinghoursendpointController) =>
		runOnMount(Container, () => () => ({
			symbolSuggestController,
			instrumentInfoController: instrumentinfoController,
			tradingHoursController: tradinghoursendpointController,
		})),
);
