import { LiveData, LiveDataOption } from '@devexperts/rx-utils/dist/rd/live-data.utils';
import { OrderTO } from '../../api/generated/swagger/definitions/OrderTO';
import { ask, combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { OrderStore } from '../../store/order/order.store';
import { InstrumentStore } from '../../store/instrument/instrument.store';
import { combineLatestRD } from '@devexperts/rx-utils/dist/rd/operators/combineLatestRD';
import { mapRD } from '@devexperts/rx-utils/dist/rd/operators/mapRD';
import { switchMapRD } from '@devexperts/rx-utils/dist/rd/operators/switchMapRD';
import * as A from 'fp-ts/lib/Array';
import { empty } from 'fp-ts/lib/Record';
import { Sink } from '@devexperts/rx-utils/dist/sink.utils';
import { map, switchMap, first, mapTo, startWith } from 'rxjs/operators';
import { fromOption, success } from '@devexperts/remote-data-ts';
import { Option } from 'fp-ts/lib/Option';
import * as O from 'fp-ts/lib/Option';
import { createHandler } from '@devexperts/rx-utils/dist/create-handler.utils';
import { merge, of, Observable, fromEvent, combineLatest } from 'rxjs';
import { getTriggerPrice, getOrderSideByQuantity, getAmount, getCurrentPrice } from './orders.utils';
import { MarketDataStore } from '../../store/market-data/market-data.store';
import { distinctUntilChangedStrict } from '../../utils/live-data.utils';
import { eqNumber } from 'fp-ts/lib/Eq';
import { tapRD } from '@devexperts/rx-utils/dist/rd/operators/tapRD';
import { Router, href } from '../../utils/router.utils';
import { shareReplayWithDefaults } from '../../utils/rxjs/shareReplayWithDefaults';
import { routes } from '../../ui-common/routes';
import { TableSize, ConfigItem } from '../../ui-desktop/orders/components/orders-table/orders-table.model';
import {
	OrdersLargeTableConfig,
	OrdersSmallTableConfig,
} from '../../ui-desktop/orders/components/orders-table/orders-table.utils';
import { pipe } from 'fp-ts/lib/pipeable';

export type OrderTOExtended = OrderTO & {
	symbol: string;
	precision: number;
};

export type OrderTOFull = OrderTOExtended & {
	triggerPrice: Option<number>;
	currentPrice: Option<number>;
};

type OrdersViewModelContext = {
	orderStore: OrderStore;
	instrumentStore: InstrumentStore;
	marketDataStore: MarketDataStore;
	router: Router;
};
export type OrdersViewModel = {
	orders$: LiveDataOption<Error, OrderTOFull[]>;
	tableSize$: Observable<TableSize>;
	tableSizeConfig$: Observable<ConfigItem[]>;
	getById: (orderId: number) => LiveData<Error, OrderTOFull>;
	onClose: (orderId: number) => void;
};
type CreateOrdersViewModel = Sink<() => OrdersViewModel>;

export const createOrdersViewModel = combineContext(
	ask<OrdersViewModelContext>(),
	(context): CreateOrdersViewModel => {
		const { orderStore, instrumentStore, marketDataStore, router } = context;
		const { push } = router;

		const close = createHandler<number>();

		const getBidAskFromMarketData = (symbol: string) => {
			const lastQuote$ = marketDataStore.getDataBySymbol(symbol).pipe(mapRD(({ lastQuote }) => lastQuote));

			const bid$ = lastQuote$.pipe(
				mapRD(quote =>
					pipe(
						quote,
						O.chain(({ bid }) => bid),
					),
				),
				distinctUntilChangedStrict(O.getEq(eqNumber)),
			);

			const ask$ = lastQuote$.pipe(
				mapRD(quote =>
					pipe(
						quote,
						O.chain(({ ask }) => ask),
					),
				),
				distinctUntilChangedStrict(O.getEq(eqNumber)),
			);

			return combineLatestRD(bid$, ask$);
		};

		const mapSingleOrderToOrderItem = (order: OrderTO): LiveData<Error, OrderTOFull> =>
			instrumentStore.getById(order.instrumentId).pipe(
				first(),
				switchMapRD(({ symbol, precision }) =>
					getBidAskFromMarketData(symbol).pipe(
						mapRD(([bid, ask]) => {
							const { quantity, type, limitPrice, stopPrice } = order;

							const side = getOrderSideByQuantity(quantity);
							const amount = getAmount(side, bid, ask);
							const triggerPrice = getTriggerPrice(type, amount, limitPrice, stopPrice);
							const currentPrice = getCurrentPrice(bid, ask, quantity);

							return {
								...order,
								symbol,
								precision,
								triggerPrice,
								currentPrice,
							};
						}),
					),
				),
			);

		const mapNonEmptyOrders = (orders: OrderTO[]): LiveData<Error, OrderTOFull[]> =>
			(combineLatestRD as any)(...orders.map(order => mapSingleOrderToOrderItem(order)));

		const orders$: LiveDataOption<Error, OrderTOFull[]> = orderStore.all$.pipe(
			switchMapRD(orders =>
				A.isEmpty(orders)
					? of(success<Error, Option<OrderTOFull[]>>(O.none))
					: mapNonEmptyOrders(orders).pipe(mapRD(O.some)),
			),
		);

		const getById = (id: number): LiveData<Error, OrderTOFull> =>
			orders$.pipe(
				map(ordersData =>
					ordersData.chain(orderData =>
						fromOption(
							pipe(
								orderData,
								O.chain(orderData => A.findFirst(orderData, data => data.orderId === id)),
							),
							() => new Error(`Order data with id ${id} not found in orders.store`),
						),
					),
				),
			);

		const onClose = (orderId: number) => close.handle(orderId);

		const closeEffect$ = close.value$.pipe(switchMap(orderId => orderStore.remove(orderId)));

		const getFilteredOrdersList = (removedOrderId: number) =>
			orderStore.all$.pipe(mapRD(orders => A.filter(orders, ({ orderId }) => orderId !== removedOrderId)));

		const jumpToFirstOrderEffect$ = close.value$.pipe(
			switchMap(getFilteredOrdersList),
			mapRD(A.head),
			tapRD(firstOrder =>
				pipe(
					firstOrder,
					O.fold(
						() => push(href(routes.orders.root, empty)),
						({ orderId }) => push(href(routes.orders.order.root, { orderId })),
					),
				),
			),
		);

		const routeState$ = merge(of(router.state), router.state$);
		const windowResize$ = fromEvent<Event>(window, 'resize').pipe(
			mapTo(undefined),
			startWith(undefined),
		);

		const tableSize$: Observable<TableSize> = combineLatest(routeState$, windowResize$).pipe(
			map(([routerState]) => routerState),
			map(({ location: { pathname } }) => {
				const isContainsEdit = pathname.includes('edit');
				const isContainsInfo = pathname.includes('info');
				const isSmallWindow = window.innerWidth <= 1280;
				return isSmallWindow && (isContainsEdit || isContainsInfo) ? TableSize.Small : TableSize.Large;
			}),
			shareReplayWithDefaults(),
		);

		const tableSizeConfig$: Observable<ConfigItem[]> = tableSize$.pipe(
			switchMap(tableSize => of(tableSize === TableSize.Large ? OrdersLargeTableConfig : OrdersSmallTableConfig)),
		);

		return new Sink(
			() => ({
				orders$,
				onClose,
				getById,
				tableSize$,
				tableSizeConfig$,
			}),
			merge(closeEffect$, jumpToFirstOrderEffect$),
		);
	},
);
