import { AggrPeriodRangePairTO, AggrPeriodRangePairTOIO } from '../definitions/AggrPeriodRangePairTO';
import { AggregationPeriodTO, AggregationPeriodTOIO } from '../definitions/AggregationPeriodTO';
import { RangeTO, RangeTOIO } from '../definitions/RangeTO';
import { StudyTO, StudyTOIO } from '../definitions/StudyTO';
import { Option } from 'fp-ts/lib/Option';
import { array, type } from 'io-ts';
import { createOptionFromNullable } from 'io-ts-types';

export type GlobalChartSettings = {
	ranges: Option<Array<RangeTO>>;
	studies: Option<Array<StudyTO>>;
	pairs: Option<Array<AggrPeriodRangePairTO>>;
	periods: Option<Array<AggregationPeriodTO>>;
};
export const GlobalChartSettingsIO = type({
	ranges: createOptionFromNullable(array(RangeTOIO)),
	studies: createOptionFromNullable(array(StudyTOIO)),
	pairs: createOptionFromNullable(array(AggrPeriodRangePairTOIO)),
	periods: createOptionFromNullable(array(AggregationPeriodTOIO)),
});
