import { tap } from 'rxjs/operators';
import { lookup } from 'fp-ts/lib/Record';
import { Option } from 'fp-ts/lib/Option';
import * as O from 'fp-ts/lib/Option';
import { AvasystemsettingsinfoController } from '../../api/generated/swagger/controllers/AvasystemsettingsinfoController';
import { mapRD } from '@devexperts/rx-utils/dist/rd/operators/mapRD';
import { LiveData } from '@devexperts/rx-utils/dist/rd/live-data.utils';
import { asks } from '@devexperts/rx-utils/dist/context.utils';
import { combineLatestRD } from '@devexperts/rx-utils/dist/rd/operators/combineLatestRD';
import { switchMapRD } from '@devexperts/rx-utils/dist/rd/operators/switchMapRD';
import { loadScript } from '../../utils/load-script.utils';
import { ServerError } from '../../utils/server-error.utils';
import { redirectToRelogin } from '../../utils/route.utils';
import { pipe } from 'fp-ts/lib/pipeable';
import { shareReplayWithDefaults } from '../../utils/rxjs/shareReplayWithDefaults';

export type EnvContext = {
	avasystemsettingsinfoController: AvasystemsettingsinfoController;
};

export type EnvViewModel = {
	initialize$: LiveData<ServerError, Option<string>>;
	loadedScripts$: LiveData<Error, Event[]>;
};

type InitializeData = LiveData<ServerError, { system: string }>;

export type CreateEnvViewModel = () => EnvViewModel;

export const createEnvViewModel = asks(
	(e: EnvContext): CreateEnvViewModel => {
		const { avasystemsettingsinfoController } = e;
		const settings$ = avasystemsettingsinfoController.getAppSettings() as InitializeData;

		const initialize$ = settings$.pipe(
			mapRD(data => lookup('system', data)),
			tap(dataRD => {
				if (dataRD.isFailure()) {
					const error = dataRD.error;
					if (ServerError.is(error) && error.status === 401) {
						redirectToRelogin();
					}
				}
			}),
			shareReplayWithDefaults(),
		);

		const loadedScripts$: LiveData<Error, Event[]> = initialize$.pipe(
			switchMapRD(system => {
				const id = pipe(
					system,
					O.exists(system => ['live', 'demo'].includes(system)),
				)
					? process.env.REACT_APP_DY_ID_PROD
					: process.env.REACT_APP_DY_ID_QA;
				return combineLatestRD(
					loadScript(`//cdn.dynamicyield.com/api/${id}/api_dynamic.js`),
					loadScript(`//cdn.dynamicyield.com/api/${id}/api_static.js`),
				);
			}),
		);

		return () => ({ initialize$, loadedScripts$ });
	},
);
