import { StudyLineTO, StudyLineTOIO } from '../definitions/StudyLineTO';
import { StudyParamTO, StudyParamTOIO } from '../definitions/StudyParamTO';
import { Option } from 'fp-ts/lib/Option';
import { string, array, boolean, type } from 'io-ts';
import { createOptionFromNullable } from 'io-ts-types';

export type StudyTO = {
	id: Option<string>;
	title: Option<string>;
	parameters: Array<StudyParamTO>;
	lines: Array<StudyLineTO>;
	overlaying: Option<boolean>;
	categories: Option<string>;
};
export const StudyTOIO = type({
	id: createOptionFromNullable(string),
	title: createOptionFromNullable(string),
	parameters: array(StudyParamTOIO),
	lines: array(StudyLineTOIO),
	overlaying: createOptionFromNullable(boolean),
	categories: createOptionFromNullable(string),
});
