import EventBus from '@dx-private/dxchart5/dist/js/chart/EventBus';
import CrossToolView from './views/cross-tool.view';
import CrossToolViewModel, { E_CROSS_TOOL_PLUGIN_MODEL } from './models/cross-tool.view-model';
import ChartInfoView from './views/chart-info.view';
import { Plugin, PluginDependencyResolver } from '../plugin.model';
import { StudySeries } from '../../chart.model';
import Hover from '@dx-private/dxchart5/dist/js/chart/model/Hover';
import ChartBootstrap from '@dx-private/dxchart5/dist/js/chart/bootstrap';
import { ChartComponents } from '@dx-private/dxchart5/dist/js/chart/ChartConfig';

const MAIN_CHART_INFO_VIEW_ID = 0;

const getAscendantByFilter = (element: Element | Document, filterFn: (el: Element | Document) => boolean) => {
	let el = element;

	while (el && el !== document && !filterFn(el)) {
		el = (el.parentElement || el.parentNode) as Element;
	}

	return element === document ? null : element;
};

const getAscendantByClassName = (element: Element | Document, className: string) => {
	return getAscendantByFilter(element, function(currentElement: Element | Document) {
		return currentElement['classList'] && currentElement['classList'].contains(className);
	});
};

export default class CrossTool extends Plugin {
	private _bus?: EventBus;

	private _clearer?: Function;

	private _views: { key: string; value: CrossToolView } | {} = {};

	private _model: CrossToolViewModel;
	private _isMobile: boolean;
	private _chartConfig?: ChartComponents;

	private _createChartInfoView: (
		container: Element,
		studySeries: StudySeries | null,
		model: CrossToolViewModel,
		isMobile: boolean,
	) => ChartInfoView;

	private _subscriptions: Function[] = [];

	constructor(
		model: CrossToolViewModel,
		createChartInfoView: (
			container: Element,
			studySeries: StudySeries | null,
			model: CrossToolViewModel,
			isMobile: boolean,
		) => ChartInfoView,
		isMobile = false,
	) {
		super();
		this._model = model;
		this._isMobile = isMobile;
		this._model.on(E_CROSS_TOOL_PLUGIN_MODEL.CHANGE_IS_ENABLED, this._onModelIsEnabledChange);
		this._createChartInfoView = createChartInfoView;
	}

	register(resolver: PluginDependencyResolver) {
		resolver(
			['eventBus', 'clearer', 'elements.mainCanvas', 'chartBootstrap'],
			(bus: EventBus, clearer: () => void, canvas: HTMLCanvasElement, chartBootstrap: ChartBootstrap, events) => {
				this._bus = bus;
				this._clearer = clearer;
				this._chartConfig = chartBootstrap.config.components;
				const mainChartGraphElement = getAscendantByClassName(canvas, 'chart--graph');
				const chartElement = getAscendantByClassName(canvas, 'chart');
				if (mainChartGraphElement && canvas.parentElement && chartElement) {
					this._views[MAIN_CHART_INFO_VIEW_ID] = new CrossToolView(
						canvas.parentElement,
						this._model,
						chartElement.querySelector('[data-element=underlaySibling] [data-element=xAxis]'),
						mainChartGraphElement.querySelector('[data-element=yAxis]'),
						null,
						this._isMobile,
						this._createChartInfoView,
					);
				}

				if (this._model.isEnabled) {
					this._enable();
				} else {
					this._iterateViews((view: CrossToolView) => view.disable());
				}

				//return clearer
				return this._clearer([
					this._bus.on(this._bus.types.STUDY_ADDED, this._onStudySeriesAdd),
					this._bus.on(this._bus.types.STUDY_REMOVED, this._onStudySeriesRemove),
					() => {
						this._disable();
						delete this['_model'];
						delete this['_ChartInfoViewClass'];
						this._iterateViews((view: CrossToolView) => {
							view.close();
							// '' - backward compatibility with chart
							const uiid = view.uiid === MAIN_CHART_INFO_VIEW_ID ? '' : view.uiid;
							if (this._bus) {
								this._bus.fire(`${this._bus.types.DEC_MOVE_HANDLER}${uiid}`);
							}
							delete this._views[uiid];
						});
					},
				]);
			},
		);
	}

	/////////////
	// PRIVATE //
	/////////////

	private _iterateViews(iterator: (view: CrossToolView) => void) {
		Object.keys(this._views).forEach(uiid => {
			iterator(this._views[uiid]);
		});
	}

	private _enable() {
		if (this._bus) {
			this._bus.fire(this._bus.types.INC_MOVE_HANDLER);
			if (this._chartConfig && this._chartConfig.chart) {
				this._chartConfig.chart.allowCandleAsLine = false;
				this._chartConfig.chart.highlightActiveCandle = true;
				this._chartConfig.chart.showActiveCandlesBorder = true;
			}
			this._subscriptions.push(
				this._bus.on(this._bus.types.HOVER, this._onChartHover),
				this._bus.on(this._bus.types.RESIZED, this._onChartResized),
				this._bus.on(this._bus.types.CLOSE_HOVER, this._onChartCloseHover),
			);
		}

		this._iterateViews(view => {
			view.enable();
			if (this._bus) {
				this._bus.fire(`${this._bus.types.INC_MOVE_HANDLER}${view.uiid}`);
			}
		});
	}

	private _disable() {
		if (this._bus) {
			this._bus.fire(this._bus.types.DEC_MOVE_HANDLER);
			if (this._chartConfig && this._chartConfig.chart) {
				this._chartConfig.chart.allowCandleAsLine = false;
				this._chartConfig.chart.highlightActiveCandle = true;
				this._chartConfig.chart.showActiveCandlesBorder = false;
			}
		}

		this._subscriptions.forEach(unsubscribe => unsubscribe());
		this._subscriptions = [];

		this._iterateViews(view => {
			if (this._bus) {
				this._bus.fire(`${this._bus.types.DEC_MOVE_HANDLER}${view.uiid}`);
			}
			view.disable();
		});
	}

	//////////////////////////
	// CHART EVENT HANDLERS //
	//////////////////////////

	private _onChartHover = (hoverModel: Hover) => {
		this._iterateViews(view => {
			view.show();
			view.update(hoverModel);
		});
	};

	private _onChartResized = () => {
		this._iterateViews(view => view.notifyResized());
	};

	private _onChartCloseHover = () => {
		this._iterateViews(view => {
			view.hide();
		});
	};

	private _onStudySeriesAdd = (studySeries: StudySeries) => {
		const uiid = studySeries.uiid;
		if (studySeries.overlaying) {
			this._views[MAIN_CHART_INFO_VIEW_ID].addStudy(studySeries);
		} else {
			const studyGraphElement = getAscendantByClassName(studySeries.canvasModel.parent, 'chart--graph');
			const container = studySeries.canvasModel.parent.querySelector('canvas').parentElement;
			if (studyGraphElement) {
				this._views[uiid] = new CrossToolView(
					container,
					this._model,
					null,
					studyGraphElement.querySelector('.dxc-yAxis'),
					studySeries,
					this._isMobile,
					this._createChartInfoView,
				);
			}
			if (this._bus) {
				this._bus.fire(`${this._bus.types.INC_MOVE_HANDLER}${uiid}`);
			}
		}
	};

	private _onStudySeriesRemove = (studySeries: StudySeries) => {
		if (studySeries.overlaying) {
			this._views[MAIN_CHART_INFO_VIEW_ID].removeStudy(studySeries);
		} else {
			const uiid = studySeries.uiid;
			if (this._bus) {
				this._bus.fire(`${this._bus.types.DEC_MOVE_HANDLER}${uiid}`);
			}
			const view = this._views[uiid];
			if (view) {
				view.close();
				delete this._views[uiid];
			}
		}
	};

	//////////////////////////
	// MODEL EVENT HANDLERS //
	//////////////////////////

	private _onModelIsEnabledChange = (isEnabled: boolean) => {
		if (isEnabled) {
			this._enable();
		} else {
			this._disable();
		}
	};
}
