import { ExecutionMessageTO, ExecutionMessageTOIO } from '../definitions/ExecutionMessageTO';
import { ProtectionOrderTO, ProtectionOrderTOIO } from '../definitions/ProtectionOrderTO';
import { Option } from 'fp-ts/lib/Option';
import { number, string, union, literal, boolean, array, dictionary, type } from 'io-ts';
import { createOptionFromNullable, DateFromISOString } from 'io-ts-types';

export type OrderTO = {
	orderId: number;
	accountId: string;
	orderChainId: string;
	ocoGroupCode: Option<string>;
	bracketGroupCode: Option<string>;
	instrumentId: number;
	status:
		| 'SENDING'
		| 'PENDING'
		| 'PLACED'
		| 'WAITING_CONDITION'
		| 'WAITING_TRIGGER'
		| 'WAITING_LIMIT'
		| 'WAITING_STOP'
		| 'WORKING'
		| 'CANCELING'
		| 'REPLACING'
		| 'CANCELED'
		| 'COMPLETED'
		| 'REJECTED'
		| 'EXPIRED'
		| 'EXECUTING'
		| 'UNKNOWN';
	statusDescription: string;
	createdTime: Date;
	modifiedTime: Date;
	quantity: number;
	remainingQuantity: number;
	filledQuantity: number;
	type:
		| 'CANCEL'
		| 'MARKET'
		| 'AUTO'
		| 'LIMIT'
		| 'STOP'
		| 'TRIGGERED_STOP'
		| 'STOP_LIMIT'
		| 'TRIGGERED_LIMIT'
		| 'TRAIL_STOP'
		| 'TRAIL_STOP_LIMIT'
		| 'OCO';
	limitPrice: Option<number>;
	averagePrice: Option<number>;
	stopPrice: Option<number>;
	stopLimitPrice: Option<number>;
	triggerPrice: Option<number>;
	liquidation: Option<boolean>;
	trailPrice: Option<number>;
	attached: boolean;
	stopPriceOffset: Option<number>;
	stopPriceOffsetPercent: Option<number>;
	requestId: Option<string>;
	timeInForce: Option<'DAY' | 'GTC' | 'IOC' | 'GTD' | 'FOK'>;
	fillPrice: Option<number>;
	expireAt: Option<Date>;
	closedPL: Option<number>;
	parentOrderId: Option<string>;
	thenOrdersIds: Option<Array<string>>;
	orderRole: Option<
		'SIMPLE' | 'OCO_ORDER' | 'IF_ORDER' | 'THEN_ORDER' | 'THEN_OCO_ORDER' | 'BRACKET_ORDER' | 'THEN_BRACKET_ORDER'
	>;
	orderGroupCode: Option<number>;
	takeProfit: Option<ProtectionOrderTO>;
	stopLoss: Option<ProtectionOrderTO>;
	positionEffect: Option<'OPENING' | 'CLOSING'>;
	exchange: Option<string>;
	lastFillTime: Option<Date>;
	stopPriceTriggerTime: Option<Date>;
	hasTriggeredStop: boolean;
	commissionFee: Option<number>;
	alertExpression: Option<string>;
	reason: Option<ExecutionMessageTO>;
	additionalParameters: { [key: string]: string };
};
export const OrderTOIO = type({
	orderId: number,
	accountId: string,
	orderChainId: string,
	ocoGroupCode: createOptionFromNullable(string),
	bracketGroupCode: createOptionFromNullable(string),
	instrumentId: number,
	status: union([
		literal('SENDING'),
		literal('PENDING'),
		literal('PLACED'),
		literal('WAITING_CONDITION'),
		literal('WAITING_TRIGGER'),
		literal('WAITING_LIMIT'),
		literal('WAITING_STOP'),
		literal('WORKING'),
		literal('CANCELING'),
		literal('REPLACING'),
		literal('CANCELED'),
		literal('COMPLETED'),
		literal('REJECTED'),
		literal('EXPIRED'),
		literal('EXECUTING'),
		literal('UNKNOWN'),
	]),
	statusDescription: string,
	createdTime: DateFromISOString,
	modifiedTime: DateFromISOString,
	quantity: number,
	remainingQuantity: number,
	filledQuantity: number,
	type: union([
		literal('CANCEL'),
		literal('MARKET'),
		literal('AUTO'),
		literal('LIMIT'),
		literal('STOP'),
		literal('TRIGGERED_STOP'),
		literal('STOP_LIMIT'),
		literal('TRIGGERED_LIMIT'),
		literal('TRAIL_STOP'),
		literal('TRAIL_STOP_LIMIT'),
		literal('OCO'),
	]),
	limitPrice: createOptionFromNullable(number),
	averagePrice: createOptionFromNullable(number),
	stopPrice: createOptionFromNullable(number),
	stopLimitPrice: createOptionFromNullable(number),
	triggerPrice: createOptionFromNullable(number),
	liquidation: createOptionFromNullable(boolean),
	trailPrice: createOptionFromNullable(number),
	attached: boolean,
	stopPriceOffset: createOptionFromNullable(number),
	stopPriceOffsetPercent: createOptionFromNullable(number),
	requestId: createOptionFromNullable(string),
	timeInForce: createOptionFromNullable(
		union([literal('DAY'), literal('GTC'), literal('IOC'), literal('GTD'), literal('FOK')]),
	),
	fillPrice: createOptionFromNullable(number),
	expireAt: createOptionFromNullable(DateFromISOString),
	closedPL: createOptionFromNullable(number),
	parentOrderId: createOptionFromNullable(string),
	thenOrdersIds: createOptionFromNullable(array(string)),
	orderRole: createOptionFromNullable(
		union([
			literal('SIMPLE'),
			literal('OCO_ORDER'),
			literal('IF_ORDER'),
			literal('THEN_ORDER'),
			literal('THEN_OCO_ORDER'),
			literal('BRACKET_ORDER'),
			literal('THEN_BRACKET_ORDER'),
		]),
	),
	orderGroupCode: createOptionFromNullable(number),
	takeProfit: createOptionFromNullable(ProtectionOrderTOIO),
	stopLoss: createOptionFromNullable(ProtectionOrderTOIO),
	positionEffect: createOptionFromNullable(union([literal('OPENING'), literal('CLOSING')])),
	exchange: createOptionFromNullable(string),
	lastFillTime: createOptionFromNullable(DateFromISOString),
	stopPriceTriggerTime: createOptionFromNullable(DateFromISOString),
	hasTriggeredStop: boolean,
	commissionFee: createOptionFromNullable(number),
	alertExpression: createOptionFromNullable(string),
	reason: createOptionFromNullable(ExecutionMessageTOIO),
	additionalParameters: dictionary(string, string),
});
