import { LiveData } from '@devexperts/rx-utils/dist/rd/live-data.utils';
import { Big } from 'big.js';
import { HistoryDetailsData, HistoryOrderSide } from '../../model/history/history.model';
import { ProfitLossKind } from '../../model/takeprofit-stoploss/takeprofit-stoploss.model';
import { HistoryOrderStore } from '../../store/history-order/history-order.store';
import { combineContext, ask } from '@devexperts/rx-utils/dist/context.utils';
import { mapRD } from '@devexperts/rx-utils/dist/rd/operators/mapRD';
import { first } from 'rxjs/internal/operators';
import * as O from 'fp-ts/lib/Option';
import { pipe } from 'fp-ts/lib/pipeable';

export type HistoryDetailsViewModel = {
	data$: LiveData<Error, HistoryDetailsData>;
};

type HistoryDetailsViewModelContext = {
	historyOrderStore: HistoryOrderStore;
};

type CreateHistoryDetailsViewModel = (orderId: number) => HistoryDetailsViewModel;

export const createHistoryDetailsViewModel = combineContext(
	ask<HistoryDetailsViewModelContext>(),
	(historyDetailsContext): CreateHistoryDetailsViewModel => orderId => {
		const {
			historyOrderStore: { getOrderHistoryById },
		} = historyDetailsContext;

		const order$ = getOrderHistoryById(orderId);

		const data$: LiveData<Error, HistoryDetailsData> = order$.pipe(
			mapRD(historyOrder => ({
				symbol: historyOrder.instrument.symbol,
				orderChainId: historyOrder.orderChainId,
				side: historyOrder.quantity > 0 ? HistoryOrderSide.Buy : HistoryOrderSide.Sell,
				amount: Math.abs(historyOrder.quantity),
				profitLossKind: pipe(
					historyOrder.profitSize,
					O.map(profitSize => (profitSize >= 0 ? ProfitLossKind.Profit : ProfitLossKind.Loss)),
				),
				openDateTime: historyOrder.openTime,
				closeDateTime: historyOrder.closeTime,
				sizeInLots: Number(Big(Math.abs(historyOrder.quantity)).div(historyOrder.instrument.lotSize)),
				takeProfitValue: pipe(
					historyOrder.takeProfit,
					O.chain(tp => tp.fixedPrice),
				),
				stopLossValue: pipe(
					historyOrder.stopLoss,
					O.chain(sl => sl.fixedPrice),
				),
				swaps: historyOrder.swaps,
				commissions: historyOrder.commissions,
				pipsSize: historyOrder.instrument.pipsSize,
				precision: historyOrder.instrument.precision,
			})),
			first(),
		);

		return {
			data$,
		};
	},
);
