import * as React from 'react';
import i18n from 'i18next';
import LanguageDetector from 'i18next-browser-languagedetector';
import translations from '../localizations/i18n.json';
import { isProductionBuild } from './node-env.utils';

const detectionOptions = {
	order: ['querystring', 'localStorage'],
	lookupQuerystring: 'lang',
	lookupLocalStorage: 'i18nextlng',
	lookupFromPathIndex: 0,
	lookupFromSubdomainIndex: 0,

	caches: ['localStorage'],
};

i18n.use(LanguageDetector).init({
	detection: detectionOptions,

	resources: translations,
	fallbackLng: {
		'zh-Hans': ['zh_CN'],
		default: ['en'],
	},
	debug: !isProductionBuild,
	nsSeparator: false,
	keySeparator: false,

	interpolation: {
		escapeValue: false,
		formatSeparator: ',',
	},

	react: {
		wait: true,
	},

	returnEmptyString: false,
});

export const i18next = i18n;

export const TRANSLATION_PLACEHOLDER = 'TRANSLATION_PLACEHOLDER';

const translate = (
	key: string,
	defaultTranslation: string | i18n.TOptions<i18n.StringMap>,
	node: keyof React.ReactHTML,
	transTemplate?: string,
) => {
	const translatedText = i18next.t(key, defaultTranslation);
	const textNode = transTemplate ? transTemplate.replace(TRANSLATION_PLACEHOLDER, translatedText) : translatedText;

	return React.createElement(
		node,
		{
			'data-lokalise': true,
			'data-key': key,
		},
		textNode,
	);
};

export const tspan = (
	key: string,
	defaultTranslation: string | i18n.TOptions<i18n.StringMap>,
	transTemplate?: string,
) => {
	return translate(key, defaultTranslation, 'span', transTemplate);
};

export const getLanguageForCRM = (i18n: i18n.i18n) => {
	switch (i18n.language) {
		case 'zh_CN':
			return 'zh-hans';
		default:
			return i18n.language;
	}
};
