import { map } from 'rxjs/operators';
import { none } from 'fp-ts/lib/Option';
import { withRXWithDefaults } from '../../../utils/withRXWithDefaults';
import { ask, combineContext, deferContext } from '@devexperts/rx-utils/dist/context.utils';
import { AccountViewModel } from '../../../view-model/account/account.view-model';
import { createChat } from '../chat.utils';
import { Chat } from '../components/chat.component';
import { runOnMount } from '../../../utils/react.utils';

export type ChatContainerContext = {
	accountViewModel: AccountViewModel;
};

export const Container = combineContext(ask<ChatContainerContext>(), Chat, ({ accountViewModel }, Chat) => {
	const chatConfig$ = accountViewModel.accountInfo$.pipe(
		map(info => {
			return {
				name: info.fullName,
				email: info.email,
				accountId: info.accountId,
			};
		}),
	);

	return withRXWithDefaults(Chat)(() => ({
		props: {
			chatConfig: chatConfig$,
		},
		defaultProps: {
			chatConfig: {
				name: none,
				email: none,
				accountId: none,
			},
		},
	}));
});

export const ChatContainer = combineContext(deferContext(Container, 'chat'), Container =>
	runOnMount(Container, () => () => ({
		chat: createChat(),
	})),
);
