import { Option } from 'fp-ts/lib/Option';
import * as O from 'fp-ts/lib/Option';
import { empty, findFirst, findIndex, head, index, last } from 'fp-ts/lib/Array';
import { pipe } from 'fp-ts/lib/pipeable';

export type Suggest = {
	description: Option<string>;
	id: string;
	symbol: string;
	category: string;
};
export type SuggestGroup = {
	title: string;
	values: Suggest[];
};

export const getSuggests = (suggestGroups: SuggestGroup[]): Suggest[] =>
	suggestGroups.reduce<Suggest[]>((acc, group) => acc.concat(group.values), empty);
export const getSuggest = (suggestId: string) => (suggests: Suggest[]): Option<Suggest> =>
	findFirst(suggests, suggest => suggest.id === suggestId);
export const nextSuggest = (selectedId: Option<string>) => (suggests: Suggest[]): Option<Suggest> =>
	pipe(
		selectedId,
		O.chain(selectedId => findIndex(suggests, item => item.id === selectedId)),
		O.map(i => i + 1),
		O.chain(i => index(i, suggests)),
		O.alt(() => head(suggests)),
	);
export const prevSuggest = (selectedId: Option<string>) => (suggests: Suggest[]): Option<Suggest> =>
	pipe(
		selectedId,
		O.chain(selectedId => findIndex(suggests, item => item.id === selectedId)),
		O.map(i => i - 1),
		O.chain(i => index(i, suggests)),
		O.alt(() => last(suggests)),
	);
