import { WatchlistEntity } from '../../../../store/watchlist/watchlist.store';

export const trendsSort = (first: WatchlistEntity, second: WatchlistEntity) => {
	const firstId = Number(first.id);
	const secondId = Number(second.id);

	if (Number.isNaN(firstId) || Number.isNaN(secondId)) {
		return 0;
	}
	if (firstId > secondId) {
		return -1;
	} else {
		return 1;
	}
};
