import { Component } from 'react';
import { withTranslation, WithTranslation } from 'react-i18next';
import { Option } from 'fp-ts/lib/Option';
import * as O from 'fp-ts/lib/Option';
import { isNotNullable } from '@devexperts/utils/dist/object';
import { ChatModel } from '../chat.model';
import { combineContext, ask } from '@devexperts/rx-utils/dist/context.utils';

export type ChatProps = {
	chatConfig: {
		name: Option<string>;
		email: Option<string>;
		accountId: Option<string>;
	};
};

export type ChatContext = {
	chat: ChatModel;
};

export const Chat = combineContext(ask<ChatContext>(), ({ chat }) =>
	withTranslation()(
		class Chat extends Component<WithTranslation & ChatProps> {
			private readonly changeChatLocale = (locale: string) => chat.setLocale(locale);

			componentDidMount() {
				// The i18n.language field is mutated on `changeLanguage`
				// and previous value doesn't store anywhere, so in shouldComponentUpdate
				// `this.props.i18n.language` and `nextProps.i18n.language` values are equal.
				this.props.i18n.on('languageChanged', this.changeChatLocale);
			}

			shouldComponentUpdate(nextProps: WithTranslation & ChatProps) {
				const {
					chatConfig,
					i18n: { language: locale },
				} = nextProps;
				const { accountId, name, email } = chatConfig;
				const {
					chatConfig: { accountId: currentAccountId, name: currentName, email: currentEmail },
				} = this.props;

				if (O.isSome(accountId)) {
					if (O.isSome(currentAccountId)) {
						const hasNameChanged =
							O.isSome(name) &&
							(O.isNone(currentName) || (O.isSome(currentName) && name.value !== currentName.value));
						const hasEmailChanged =
							O.isSome(email) &&
							(O.isNone(currentEmail) || (O.isSome(currentEmail) && email.value !== currentEmail.value));
						const hasAccountIdChanged = accountId.value !== currentAccountId.value;

						if (hasNameChanged || hasEmailChanged) {
							// In case this ever change in SPA mode
							// we need to update this fields in chat
							chat.prefill(name, email);
						}

						if (hasAccountIdChanged) {
							chat.setAccountId(accountId);
						}
					} else if (isNotNullable(locale)) {
						// Execute only once when the first data has arrived
						chat.init({ ...chatConfig, locale });
						chat.hide();
					}
				}

				return false;
			}

			componentWillUnmount() {
				this.props.i18n.off('languageChanged', this.changeChatLocale);
			}

			render() {
				return null;
			}
		},
	),
);
