import { ResponseValidationError, TAPIClient } from '../client/client';
import { ChartRequestResultTO, ChartRequestResultTOIO } from '../definitions/ChartRequestResultTO';
import { ChartRequestTO, ChartRequestTOIO } from '../definitions/ChartRequestTO';
import { ChartResponseTO, ChartResponseTOIO } from '../definitions/ChartResponseTO';
import { StudyTO, StudyTOIO } from '../definitions/StudyTO';
import {
	UpdateChartDataSubscriptionTO,
	UpdateChartDataSubscriptionTOIO,
} from '../definitions/UpdateChartDataSubscriptionTO';
import { fromEither } from '@devexperts/remote-data-ts';
import { LiveData } from '@devexperts/rx-utils/dist/rd/live-data.utils';
import { asks } from 'fp-ts/lib/Reader';
import { partial, void as tvoid, number, type } from 'io-ts';
import { map } from 'rxjs/operators';

export type ChartsController = {
	/**
	 * Get chart
	 * @param { object } [parameters]
	 */
	readonly getChart: (parameters: { body?: ChartRequestTO }) => LiveData<Error, ChartResponseTO>;

	/**
	 * Update charts data subscription
	 * @param { object } [parameters]
	 */
	readonly updateChartsSubscription: (parameters: { body?: UpdateChartDataSubscriptionTO }) => LiveData<Error, void>;

	/**
	 * Subscribe on chart data for the specified request
	 * @param { object } [parameters]
	 */
	readonly requestChart: (parameters: { body?: ChartRequestTO }) => LiveData<Error, ChartRequestResultTO>;

	/**
	 * Get study
	 * @param { object } parameters
	 */
	readonly getStudy: (parameters: { query: { id: number } }) => LiveData<Error, StudyTO>;
};

export const chartsController = asks(
	(e: { apiClient: TAPIClient }): ChartsController => ({
		getChart: parameters => {
			const encoded = partial({ body: ChartRequestTOIO }).encode(parameters);

			return e.apiClient
				.request({
					url: `/charts`,
					method: 'GET',

					body: encoded.body,
				})
				.pipe(
					map(data =>
						data.chain(value =>
							fromEither(ChartResponseTOIO.decode(value).mapLeft(ResponseValidationError.create)),
						),
					),
				);
		},

		updateChartsSubscription: parameters => {
			const encoded = partial({ body: UpdateChartDataSubscriptionTOIO }).encode(parameters);

			return e.apiClient
				.request({
					url: `/charts`,
					method: 'PUT',

					body: encoded.body,
				})
				.pipe(
					map(data =>
						data.chain(value => fromEither(tvoid.decode(value).mapLeft(ResponseValidationError.create))),
					),
				);
		},

		requestChart: parameters => {
			const encoded = partial({ body: ChartRequestTOIO }).encode(parameters);

			return e.apiClient
				.request({
					url: `/charts`,
					method: 'POST',

					body: encoded.body,
				})
				.pipe(
					map(data =>
						data.chain(value =>
							fromEither(ChartRequestResultTOIO.decode(value).mapLeft(ResponseValidationError.create)),
						),
					),
				);
		},

		getStudy: parameters => {
			const encoded = partial({ query: type({ id: number }) }).encode(parameters);

			return e.apiClient
				.request({
					url: `/charts/study`,
					method: 'GET',
					query: encoded.query,
				})
				.pipe(
					map(data =>
						data.chain(value =>
							fromEither(StudyTOIO.decode(value).mapLeft(ResponseValidationError.create)),
						),
					),
				);
		},
	}),
);
