import * as React from 'react';
import { Component, SyntheticEvent, CSSProperties, ReactNode, forwardRef, Ref } from 'react';
import Scrollbars from 'react-custom-scrollbars';
import throttle from '@devexperts/utils/dist/function/throttle';
import * as cn from 'classnames';
import * as scrollbarsCss from './theme/scrollbars.module.css';

// need to adjust table's viewport
const TABLE_HEADER_HEIGHT = 43;
const THROTTLE_DELAY = 100;

type ForwardedTableProps = {
	className: string;
	style: CSSProperties;
	children: ReactNode;
	forwardedRef: (ref: Ref<HTMLDivElement>) => void;
	onScroll: (event: SyntheticEvent<HTMLDivElement>) => void;
};

type CustomScrollbarsState = {
	isEndOfScroll: boolean;
};

class CustomScrollbars extends Component<ForwardedTableProps, CustomScrollbarsState> {
	readonly state = {
		isEndOfScroll: false,
	};

	private refSetter = (scrollbarsRef: any) => {
		const { forwardedRef } = this.props;

		if (scrollbarsRef) {
			forwardedRef(scrollbarsRef.view);
		} else {
			forwardedRef(null);
		}
	};

	private checkEndOfScroll = throttle((target: HTMLDivElement) => {
		const { isEndOfScroll: stateIsEndOfScroll } = this.state;

		if (target) {
			const { scrollTop, clientHeight, scrollHeight } = target;
			const isEndOfScroll: boolean = clientHeight + scrollTop >= scrollHeight;

			if (stateIsEndOfScroll !== isEndOfScroll) {
				this.setState({ isEndOfScroll });
			}
		}
	}, THROTTLE_DELAY);

	private renderScrollbarElements = (className: string) => (props: any) => <div {...props} className={className} />;

	private getStyles = () => {
		const { style } = this.props;

		return {
			...style,
			height: Number(style.height) - TABLE_HEADER_HEIGHT,
			overflow: 'hidden',
		};
	};

	private handleScroll = (e: SyntheticEvent<HTMLDivElement>): void => {
		this.checkEndOfScroll(e.target as HTMLDivElement);

		e.currentTarget && this.props.onScroll(e);
	};

	render() {
		return (
			<Scrollbars
				ref={this.refSetter}
				className={cn(scrollbarsCss.container, {
					[scrollbarsCss.container_hasShadow]: !this.state.isEndOfScroll,
				})}
				style={this.getStyles()}
				onScroll={this.handleScroll}
				renderThumbVertical={this.renderScrollbarElements(scrollbarsCss.scrollbar)}
				renderTrackVertical={this.renderScrollbarElements(scrollbarsCss.track)}>
				{this.props.children}
			</Scrollbars>
		);
	}
}

export const CustomScrollbarsVirtualList = forwardRef((props: ForwardedTableProps, ref) => (
	<CustomScrollbars {...props} forwardedRef={ref as (ref: Ref<HTMLDivElement>) => void} />
));
