import * as React from 'react';
import { MouseEventHandler, PureComponent } from 'react';
export * from '@devexperts/react-kit/dist/components/Link/Link';
import { Link as DXLink, TLinkProps } from '@devexperts/react-kit/dist/components/Link/Link';

export class Link extends PureComponent<TLinkProps> {
	render() {
		return <DXLink {...this.props} onClick={this.handleClick} />;
	}

	private handleClick: MouseEventHandler<HTMLAnchorElement> = e => {
		if (this.props.onClick) {
			e.preventDefault();
			this.props.onClick(e);
		}
	};
}
