import {
	differenceInDays,
	isSameDay,
	startOfYear,
	startOfMonth,
	startOfDay,
	subDays,
	endOfDay,
	getDate,
	getMonth,
	getYear,
} from 'date-fns';
import * as O from 'fp-ts/lib/Option';
import { TDateInputValue } from '@devexperts/react-kit/dist/components/DateInput/DateInput.model';
import {
	Period,
	HistoryFilterData,
	HistoryValueHandlerData,
	UnwrappedFilterFormData,
} from '../../model/history/history.model';
import { Either } from 'fp-ts/lib/Either';
import * as E from 'fp-ts/lib/Either';
import { pipe } from 'fp-ts/lib/pipeable';
import { constant } from 'fp-ts/lib/function';

const getTimestampFromDateInputValue = (inputDate: TDateInputValue): number =>
	new Date(
		pipe(
			inputDate.year,
			O.getOrElse(constant(0)),
		),
		pipe(
			inputDate.month,
			O.getOrElse(constant(0)),
		),
		pipe(
			inputDate.day,
			O.getOrElse(constant(0)),
		),
	).getTime();

export const mapDatesToPeriod = (value: HistoryValueHandlerData): HistoryFilterData => {
	const { startDate, endDate, isDateInputChanged } = value;

	if (!isDateInputChanged) {
		return value;
	}

	const getDifferenceInDays = (endDate: number): number => differenceInDays(Date.now(), endDate);

	const daysToThisMonth = getDifferenceInDays(startOfMonth(Date.now()).getTime());
	const daysToThisYear = getDifferenceInDays(startOfYear(Date.now()).getTime());
	const daysToAllPeriod = getDifferenceInDays(new Date(0).getTime());

	const differenceInDaysBetweenDates = differenceInDays(
		getTimestampFromDateInputValue(endDate),
		getTimestampFromDateInputValue(startDate),
	);

	const isEndDateCustomized = !isSameDay(new Date(getTimestampFromDateInputValue(endDate)), new Date());
	const isPeriodMatched = [7, 30, 365, daysToThisMonth, daysToThisYear, daysToAllPeriod].includes(
		differenceInDaysBetweenDates,
	);

	if (isEndDateCustomized || !isPeriodMatched) {
		return { ...value, selectedPeriod: Period.custom };
	}

	switch (differenceInDaysBetweenDates) {
		case 7:
			return { ...value, selectedPeriod: Period.last7days };

		case 30:
			return { ...value, selectedPeriod: Period.last30Days };

		case 365:
			return { ...value, selectedPeriod: Period.last365days };

		case daysToThisMonth:
			return { ...value, selectedPeriod: Period.thisMonth };

		case daysToThisYear:
			return { ...value, selectedPeriod: Period.thisYear };

		case daysToAllPeriod:
			return { ...value, selectedPeriod: Period.all };

		default:
			return { ...value, selectedPeriod: Period.custom };
	}
};

export const unwrapDatesFromValue = (value: HistoryFilterData): UnwrappedFilterFormData => {
	const endOfThisDay = endOfDay(Date.now()).getTime();
	const getStartOfDay = (substactDays: number): number => startOfDay(subDays(endOfThisDay, substactDays)).getTime();

	switch (value.selectedPeriod) {
		case Period.last7days:
			return [value.selectedPeriod, getStartOfDay(7), endOfThisDay];

		case Period.last30Days:
			return [value.selectedPeriod, getStartOfDay(30), endOfThisDay];

		case Period.last365days:
			return [value.selectedPeriod, getStartOfDay(365), endOfThisDay];

		case Period.thisMonth:
			return [value.selectedPeriod, startOfMonth(endOfThisDay).getTime(), endOfThisDay];

		case Period.thisYear:
			return [value.selectedPeriod, startOfYear(endOfThisDay).getTime(), endOfThisDay];

		case Period.all:
			return [value.selectedPeriod, new Date(0).getTime(), endOfThisDay];

		case Period.custom:
			return [
				value.selectedPeriod,
				startOfDay(getTimestampFromDateInputValue(value.startDate)).getTime(),
				endOfDay(getTimestampFromDateInputValue(value.endDate)).getTime(),
			];

		default:
			return [value.selectedPeriod, getStartOfDay(7), endOfThisDay];
	}
};

export const wrapDatesToValue = (unwrappedValues: UnwrappedFilterFormData): HistoryFilterData => {
	const [period, startDate, endDate] = unwrappedValues;

	return {
		selectedPeriod: period,
		startDate: {
			day: O.some(getDate(startDate)),
			month: O.some(getMonth(startDate)),
			year: O.some(getYear(startDate)),
		},
		endDate: {
			day: O.some(getDate(endDate)),
			month: O.some(getMonth(endDate)),
			year: O.some(getYear(endDate)),
		},
	};
};

export const getDefaultHistoryValue = (): HistoryFilterData => {
	const today = Date.now();
	const weekAgo = subDays(today, 7).getTime();

	return {
		selectedPeriod: Period.last7days,
		startDate: {
			day: O.some(getDate(weekAgo)),
			month: O.some(getMonth(weekAgo)),
			year: O.some(getYear(weekAgo)),
		},
		endDate: {
			day: O.some(getDate(today)),
			month: O.some(getMonth(today)),
			year: O.some(getYear(today)),
		},
	};
};

export const dedupeValues = (prevValue: UnwrappedFilterFormData, currValue: UnwrappedFilterFormData): boolean =>
	prevValue.every((prevHistoryDataValue, i) => currValue[i] === prevHistoryDataValue);

export const validateDateInputs = (unwrappedValues: UnwrappedFilterFormData): Either<void, UnwrappedFilterFormData> => {
	const [startTimestamp, endTimestamp] = unwrappedValues.slice(1);
	return startTimestamp > endTimestamp ? E.left(undefined) : E.right(unwrappedValues);
};
