import { RouteLink } from '../../../../ui-common/route-link/containers/route-link.container';
import * as React from 'react';
import * as cn from 'classnames';
import { withTranslation, WithTranslation } from 'react-i18next';
import { PureComponent } from 'react';
import { routes } from '../../../../ui-common/routes';
import { combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { findFirst } from 'fp-ts/lib/Array';
import { ListChildComponentProps } from 'react-window';
import {
	ConfigItem,
	EditOrdersPreset,
	OrdersSide,
	OrdersTableItem,
	TableSize,
} from '../orders-table/orders-table.model';
import { ReactNode } from 'react';
import { Optional } from '../../../../ui-common/optional/optional.component';
import { format } from 'date-fns';
import { formatNumber } from 'accounting';
import { InfoIcon } from '../../../../ui-common/icons/info.icon';
import { EditIcon } from '../../../../ui-common/icons/edit.icon';
import { DeleteIcon } from '../../../../ui-common/icons/delete.icon';
import { CancelConfirmation } from '../../../../ui-common/cancel-confirmation/cancel-confirmation.component';
import { RemoteData } from '@devexperts/remote-data-ts';
import { RemoteDataState } from '../../../../ui-common/remote-data-state/remote-data-state.component';
import { AddIcon } from '../../../ui-kit/components/icons/add.icon';
import { tspan } from '../../../../utils/i18n.utils';
import { Button } from '../../../../ui-common/button/button.component';
import { pipe } from 'fp-ts/lib/pipeable';
import { constant } from 'fp-ts/lib/function';
import * as O from 'fp-ts/lib/Option';
import * as css from './theme/order-entity.module.css';
import * as buttonCss from './theme/button.child.module.css';
import * as iconButtonCss from './theme/icon-button.child.module.css';

export type OrderEntityProps = ListChildComponentProps & {
	rowData: number;
	remoteData: RemoteData<Error, OrdersTableItem>;
	config: ConfigItem[];
	tableSize: TableSize;
	onClose: (orderId: number) => void;
};

type OrderEntityState = {
	isConfirmPopupOpened: boolean;
};
const defaultColumnWidth = 150;

export const OrderEntity = combineContext(RouteLink, RouteLink =>
	withTranslation()(
		class OrderEntity extends PureComponent<OrderEntityProps & WithTranslation, OrderEntityState> {
			readonly state = {
				isConfirmPopupOpened: false,
			};

			render() {
				const { remoteData } = this.props;

				return (
					<div>
						<RemoteDataState data={remoteData} success={this.renderSuccess} />
					</div>
				);
			}

			renderSuccess = (data: OrdersTableItem) => {
				const { index, style, tableSize, config } = this.props;

				const rowColumn = cn(css.rowColumn, {
					[css.rowColumn_small]: tableSize === TableSize.Small,
				});
				const rowColumnRight = cn(rowColumn, css.rowColumn_right);
				// const rowColumnLeft = cn(rowColumn, css.rowColumn_left);
				const rowColumnCenter = cn(rowColumn, css.rowColumn_center);
				const triggerPrice = pipe(
					data.triggerPrice,
					O.map(value => formatNumber(value, data.precision)),
				);
				return (
					<RouteLink
						to={routes.orders.order.root}
						value={{ orderId: data.orderId }}
						render={({ onNavigate, isActive }) => (
							<div
								onClick={this.handleEditClick(onNavigate)}
								className={cn(css.row, { [css.rowSelected]: isActive })}
								key={index}
								role="row"
								style={style}>
								<div
									className={rowColumn}
									style={{ flexBasis: this.getWidthFromConfig(config, 'symbol') }}>
									{data.symbol}
								</div>
								<div
									className={rowColumnCenter}
									style={{ flexBasis: this.getWidthFromConfig(config, 'side') }}>
									{this.getSide(data.side)}
								</div>
								<div
									className={rowColumnRight}
									style={{ flexBasis: this.getWidthFromConfig(config, 'quantity') }}>
									<Optional value={data.quantity} />
								</div>
								<div
									className={rowColumnRight}
									style={{
										flexBasis: this.getWidthFromConfig(config, 'triggerPrice'),
									}}>
									<Optional value={triggerPrice} />
								</div>
								<div
									className={rowColumnRight}
									style={{ flexBasis: this.getWidthFromConfig(config, 'distance') }}>
									<Optional
										value={pipe(
											data.distance,
											O.map(distance => formatNumber(distance, data.precision)),
										)}
									/>
								</div>
								<div
									className={rowColumnCenter}
									style={{ flexBasis: this.getWidthFromConfig(config, 'stopLoss') }}>
									{this.renderStopLossCell(data)}
								</div>
								<div
									className={rowColumnCenter}
									style={{
										flexBasis: this.getWidthFromConfig(config, 'takeProfit'),
									}}>
									{this.renderTakeProfitCell(data)}
								</div>
								<div
									className={rowColumnCenter}
									style={{
										flexBasis: this.getWidthFromConfig(config, 'validUntil'),
									}}>
									<Optional
										value={pipe(
											data.validUntil,
											O.map(date => format(date, 'mm.dd hh:mm:ss')),
										)}
									/>
								</div>
								<div
									className={rowColumnRight}
									style={{ flexBasis: this.getWidthFromConfig(config, 'controls') }}>
									<ul className={css.controls}>
										<li className={css.control}>
											<RouteLink
												to={routes.orders.order.info.root}
												value={{ orderId: data.orderId }}
												render={({ onNavigate }) => (
													<div className={css.infoCell}>
														<i
															className={css.icon}
															onClick={this.handleClickWithStopPropagation(onNavigate)}>
															<InfoIcon />
														</i>
													</div>
												)}
											/>
										</li>
										<li className={css.control}>
											<RouteLink
												to={routes.orders.order.edit.profitLossKind.root}
												value={{ orderId: data.orderId, profitLossKind: 'add-protection' }}
												render={({ onNavigate }) => (
													<Button theme={iconButtonCss} onClick={this.handleEdit(onNavigate)}>
														<EditIcon />
													</Button>
												)}
											/>
										</li>
										<li className={css.control}>
											<Button theme={iconButtonCss} onClick={this.openConfirmPopup}>
												<DeleteIcon />
											</Button>
										</li>
									</ul>
								</div>
								{this.state.isConfirmPopupOpened && (
									<CancelConfirmation
										title={tspan('orderEntityCancelOrder', 'Cancel this Order?')}
										onCancel={this.closeConfirmPopup}
										onSubmit={this.handleClose(data.orderId)}
									/>
								)}
							</div>
						)}
					/>
				);
			};

			handleEditClick = (onNavigate: () => void) => (e: React.MouseEvent<HTMLDivElement>) => {
				e.stopPropagation();
				onNavigate();
			};

			handleAddProtectionClick = (onNavigate: () => void) => (e: React.MouseEvent<HTMLButtonElement>) => {
				e.stopPropagation();
				onNavigate();
			};

			openConfirmPopup = () => {
				this.setState({ isConfirmPopupOpened: true });
			};

			closeConfirmPopup = () => {
				this.setState({ isConfirmPopupOpened: false });
			};

			handleClose = (id: number) => (e: React.MouseEvent<HTMLButtonElement>) => {
				e.stopPropagation();
				const { onClose } = this.props;

				onClose(id);
			};

			private renderStopLossCell = (item: OrdersTableItem) => {
				const { tableSize } = this.props;
				const value = pipe(
					item.stopLoss,
					O.fold<number, ReactNode>(
						() => this.renderAddProtection(item, EditOrdersPreset.StopLoss),
						data => (
							<span className={tableSize === TableSize.Small ? css.TPSLCell_small : ''}>
								{formatNumber(data, item.precision)}
							</span>
						),
					),
				);

				return value;
			};

			private renderTakeProfitCell = (item: OrdersTableItem) => {
				const { tableSize } = this.props;
				const value = pipe(
					item.takeProfit,
					O.fold<number, ReactNode>(
						() => this.renderAddProtection(item, EditOrdersPreset.TakeProfit),
						data => (
							<span className={tableSize === TableSize.Small ? css.TPSLCell_small : ''}>
								{formatNumber(data, item.precision)}
							</span>
						),
					),
				);

				return value;
			};

			private getEditingOrderRouteSuffix = (preset: EditOrdersPreset) => {
				switch (preset) {
					case EditOrdersPreset.StopLoss:
						return 'loss';
					case EditOrdersPreset.TakeProfit:
						return 'profit';
					case EditOrdersPreset.None:
						return '';
				}
			};

			renderAddProtection = (data: OrdersTableItem, preset: EditOrdersPreset) => {
				const { tableSize } = this.props;
				const { orderId } = data;

				const routeSuffix = this.getEditingOrderRouteSuffix(preset);

				return (
					<RouteLink
						to={routes.orders.order.edit.profitLossKind.root}
						value={{ orderId, profitLossKind: routeSuffix }}
						render={({ onNavigate }) => (
							<Button theme={buttonCss} onClick={this.handleAddProtectionClick(onNavigate)}>
								{tableSize === TableSize.Large ? (
									tspan('orderEntityAddProtection', '+ Add Protection')
								) : (
									<i className={css.icon_plus}>
										<AddIcon />
									</i>
								)}
							</Button>
						)}
					/>
				);
			};

			private handleEdit = (onNavigate: () => void) => (e: React.MouseEvent<HTMLButtonElement>) => {
				e.stopPropagation();
				onNavigate();
			};

			private handleClickWithStopPropagation = (onNavigate: () => void) => (
				e: React.MouseEvent<HTMLDivElement>,
			) => {
				e.stopPropagation();
				onNavigate();
			};

			private getSide = (side: OrdersSide): ReactNode => {
				switch (side) {
					case OrdersSide.Buy:
						return <div className={css.green}>{tspan('orderEntityBuy', 'Buy')}</div>;
					case OrdersSide.Sell:
						return <div className={css.red}>{tspan('orderEntitySell', 'Sell')}</div>;
				}
			};

			private getWidthFromConfig = (config: ConfigItem[], dataKey: string): number => {
				const currentColumn = findFirst(config, item => {
					return item['dataKey'] === dataKey;
				});

				return pipe(
					currentColumn,
					O.fold(constant(defaultColumnWidth), value => value.width),
				);
			};
		},
	),
);
