import { Option } from 'fp-ts/lib/Option';
import { ReactNode } from 'react';

export enum PositionSide {
	Bought = '* BOUGHT *',
	Sold = '* SOLD *',
}

export enum EditPositionPreset {
	TakeProfit = '* TAKE_PROFIT *',
	StopLoss = '* STOP_LOSS *',
	None = '* NONE *',
}

export enum TableSize {
	Small = '* SMALL *',
	Large = '* LARGE *',
}

export type PositionsTableItem = {
	uid: string;
	quantity: number;
	side: PositionSide;
	size: Option<number>;
	openPrice: Option<number>;
	stopLoss: Option<number>;
	takeProfit: Option<number>;
	precision: number;
};

export type ConfigItem = {
	width: number;
	dataKey: keyof PositionsTableItem | 'symbol' | 'profitLoss' | 'value' | 'controls';
};

export type PositionsTableColumnNames = {
	symbol: ReactNode;
	side: ReactNode;
	size: ReactNode;
	value: ReactNode;
	profitLoss: ReactNode;
	openPrice: ReactNode;
	stopLoss: ReactNode;
	takeProfit: ReactNode;
};
