import { createWatchlistViewModel, WatchlistViewModel } from '../../../../view-model/watchlist/watchlist.view-model';
import { withRXWithDefaults } from '../../../../utils/withRXWithDefaults';
import { runOnMount } from '../../../../utils/react.utils';
import { WatchlistTable } from '../../components/watchlist-table/watchlist-table.component';
import { WatchlistLargeTableConfig } from '../../components/watchlist-table/watchlist-table.utils';
import { pending } from '@devexperts/remote-data-ts';
import { ask, combineContext, deferContext } from '@devexperts/rx-utils/dist/context.utils';
import { Router } from '../../../../utils/router.utils';
import { TableSize } from '../../components/watchlist-table/watchlist-table.model';
import { createSelector } from 'reselect';

export type WatchlistContainerContext = {
	watchlistViewModel: WatchlistViewModel;
	router: Router;
};

const Container = combineContext(WatchlistTable, ask<WatchlistContainerContext>(), (WatchlistTable, e) =>
	withRXWithDefaults(WatchlistTable)(() => {
		const { watchlistViewModel } = e;
		const { data$, tableSize$, tableSizeConfig$ } = watchlistViewModel;

		return {
			props: {
				data: data$,
				tableSize: tableSize$,
				config: tableSizeConfig$,
			},
			defaultProps: {
				data: pending,
				config: WatchlistLargeTableConfig,
				tableSize: TableSize.Large,
			},
		};
	}),
);

export const WatchlistContainer = combineContext(
	deferContext(Container, 'watchlistViewModel'),
	createWatchlistViewModel,
	(Container, createWatchlistViewModel) =>
		runOnMount(Container, props => {
			const categoryId = createSelector(
				props,
				props => props.categoryId,
			);
			return createSelector(
				categoryId,
				categoryId =>
					createWatchlistViewModel(categoryId).map(watchlistViewModel => ({
						watchlistViewModel,
					})),
			);
		}),
);
