import { LiveData } from '@devexperts/rx-utils/dist/rd/live-data.utils';
import { ask, combineContext, fromReader } from '@devexperts/rx-utils/dist/context.utils';
import { InstrumentStore } from '../../store/instrument/instrument.store';
import { MarketDataStore } from '../../store/market-data/market-data.store';
import { InstrumentInfoStore } from '../../store/instrument-info/instrument-info.store';
import {
	TradinghoursendpointController,
	tradinghoursendpointController,
} from '../../api/generated/swagger/controllers/TradinghoursendpointController';
import { AvaTradingWeekTO } from '../../api/generated/swagger/definitions/AvaTradingWeekTO';
import { mapRD } from '@devexperts/rx-utils/dist/rd/operators/mapRD';
import { distinctRDOptionNumber, distinctRDOptionString, distinctRDNumber } from '../../utils/setoid.utils';
import { Option } from 'fp-ts/lib/Option';
import * as O from 'fp-ts/lib/Option';
import { first } from 'rxjs/operators';
import { combineLatestRD } from '@devexperts/rx-utils/dist/rd/operators/combineLatestRD';
import { map, startWith } from 'rxjs/operators';
import { success } from '@devexperts/remote-data-ts';
import { SymbolInfoData } from '../../ui-common/symbol-info/symbol-info.model';
import { toTradingHours } from './symbol-info.utils';
import { pipe } from 'fp-ts/lib/pipeable';

export type SymbolInfoViewModel = {
	symbolInfoData$: LiveData<Error, SymbolInfoData>;
};

export type SymbolInfoViewModelContext = {
	instrumentStore: InstrumentStore;
	marketDataStore: MarketDataStore;
	instrumentInfoStore: InstrumentInfoStore;
	tradingHoursController: TradinghoursendpointController;
};

export type CreateSymbolInfoViewModel = (symbol: string) => SymbolInfoViewModel;

export const createSymbolInfoViewModel = combineContext(
	fromReader(tradinghoursendpointController),
	ask<SymbolInfoViewModelContext>(),
	(tradinghoursendpointController, e): CreateSymbolInfoViewModel => (symbol: string) => {
		const { instrumentStore, marketDataStore, instrumentInfoStore, tradingHoursController } = e;

		// marketData sources
		const marketData$ = marketDataStore.getDataBySymbol(symbol);
		const bid$ = marketData$.pipe(
			mapRD(data =>
				pipe(
					data.lastQuote,
					O.chain(quote => quote.bid),
				),
			),
			distinctRDOptionNumber,
		);
		const ask$ = marketData$.pipe(
			mapRD(data =>
				pipe(
					data.lastQuote,
					O.chain(quote => quote.ask),
				),
			),
			distinctRDOptionNumber,
		);
		const askDirection$ = marketData$.pipe(
			mapRD(data =>
				pipe(
					data.lastQuote,
					O.map(quote => quote.askDirection),
				),
			),
			distinctRDOptionNumber,
		);
		const bidDirection$ = marketData$.pipe(
			mapRD(data =>
				pipe(
					data.lastQuote,
					O.map(quote => quote.bidDirection),
				),
			),
			distinctRDOptionNumber,
		);
		const dayHigh$ = marketData$.pipe(
			mapRD(data =>
				pipe(
					data.lastSummary,
					O.chain(summary => summary.dayHigh),
				),
			),
			distinctRDOptionNumber,
		);
		const dayLow$ = marketData$.pipe(
			mapRD(data =>
				pipe(
					data.lastSummary,
					O.chain(summary => summary.dayLow),
				),
			),
			distinctRDOptionNumber,
		);

		// basicInstrumentInfo sources
		const basicInstrumentInfo$ = instrumentStore.getBySymbol(symbol);
		const description$ = basicInstrumentInfo$.pipe(
			mapRD(data => data.description),
			distinctRDOptionString,
		);
		const pipsSize$ = basicInstrumentInfo$.pipe(
			mapRD(instrument => instrument.pipsSize),
			distinctRDNumber,
		);
		const unitName$ = basicInstrumentInfo$.pipe(
			mapRD(instrument => instrument.unitName),
			first(),
		);
		const precision$ = basicInstrumentInfo$.pipe(
			mapRD(instrument => instrument.precision),
			first(),
		);
		const lotSize$ = basicInstrumentInfo$.pipe(
			mapRD(instrument => instrument.lotSize),
			first(),
		);
		// extendedInstrumentInfo sources
		const extendedInstrumentInfo$ = instrumentInfoStore.getBySymbol(symbol);
		const dayChange$ = extendedInstrumentInfo$.pipe(
			mapRD(info => info.dayChange),
			distinctRDOptionNumber,
		);
		const weekChange$ = extendedInstrumentInfo$.pipe(
			mapRD(info => info.weekChange),
			distinctRDOptionNumber,
		);
		const monthChange$ = extendedInstrumentInfo$.pipe(
			mapRD(info => info.monthChange),
			distinctRDOptionNumber,
		);
		const sentimentSell$ = extendedInstrumentInfo$.pipe(
			mapRD(info => info.sentimentSell),
			distinctRDOptionNumber,
		);
		const sentimentBuy$ = extendedInstrumentInfo$.pipe(
			mapRD(info => info.sentimentBuy),
			distinctRDOptionNumber,
		);

		const tradingWeek$ = tradingHoursController
			.getWeekTradingHours({
				query: { symbol, timezoneOffset: new Date().getTimezoneOffset() },
			})
			.pipe(
				map(data => success<Error, Option<AvaTradingWeekTO>>(data.toOption())), // treat any problems with trading week as none
				startWith(success<Error, Option<AvaTradingWeekTO>>(O.none)),
			);
		const combinedData1$ = combineLatestRD(description$, pipsSize$, unitName$, precision$, lotSize$);
		const combinedData2$ = combineLatestRD(bid$, ask$, askDirection$, bidDirection$, dayHigh$, dayLow$);
		const combinedData3$ = combineLatestRD(dayChange$, weekChange$, monthChange$, sentimentSell$, sentimentBuy$);

		const symbolInfoData$: LiveData<Error, SymbolInfoData> = combineLatestRD(
			combinedData1$,
			combinedData2$,
			combinedData3$,
			tradingWeek$,
		).pipe(
			mapRD(
				([
					[description, pipsSize, unitName, precision, lotSize],
					[bid, ask, askDirection, bidDirection, dayHigh, dayLow],
					[dayChange, weekChange, monthChange, sentimentSell, sentimentBuy],
					tradingWeek,
				]) => ({
					description,
					currentBuyPrice: ask,
					buyPriceDirection: askDirection,
					currentSellPrice: bid,
					pipSize: pipsSize,
					sellPriceDirection: bidDirection,
					measurementUnit: unitName,
					tradingHours: {
						monday: pipe(
							tradingWeek,
							O.chain(tradingWeek => toTradingHours(tradingWeek.monday)),
						),
						tuesday: pipe(
							tradingWeek,
							O.chain(tradingWeek => toTradingHours(tradingWeek.tuesday)),
						),
						wednesday: pipe(
							tradingWeek,
							O.chain(tradingWeek => toTradingHours(tradingWeek.wednesday)),
						),
						thursday: pipe(
							tradingWeek,
							O.chain(tradingWeek => toTradingHours(tradingWeek.thursday)),
						),
						friday: pipe(
							tradingWeek,
							O.chain(tradingWeek => toTradingHours(tradingWeek.friday)),
						),
						saturday: pipe(
							tradingWeek,
							O.chain(tradingWeek => toTradingHours(tradingWeek.saturday)),
						),
						sunday: pipe(
							tradingWeek,
							O.chain(tradingWeek => toTradingHours(tradingWeek.sunday)),
						),
					},
					timeZoneOffset: pipe(
						tradingWeek,
						O.map(tradingWeek => tradingWeek.timeZone),
					),
					lotSize,
					precision,
					dayHigh,
					dayLow,
					changeDay: dayChange,
					changeWeek: weekChange,
					changeMonth: monthChange,
					marketSentimentLeft: sentimentSell,
					marketSentimentRight: sentimentBuy,
				}),
			),
		);

		return {
			symbolInfoData$,
		};
	},
);
