import { combineLatest } from 'rxjs';
import { Observable } from 'rxjs/internal/Observable';
import { filter, first, map, switchMap, tap, withLatestFrom, distinctUntilChanged } from 'rxjs/operators';
import { ask, combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { Sink } from '@devexperts/rx-utils/dist/sink.utils';
import { LiveData, LiveDataOption } from '@devexperts/rx-utils/dist/rd/live-data.utils';
import { PositionsStore } from '../../store/positions/positions.store';
import { createHandler } from '@devexperts/rx-utils/dist/create-handler.utils';
import { merge } from 'rxjs/internal/observable/merge';
import { isSuccess, success, RemoteData } from '@devexperts/remote-data-ts';
import { PositionCloseInstrumentData, PositionCloseValue } from '../../model/positions/position-close.model';
import { AccountViewModel } from '../account/account.view-model';
import { InstrumentStore } from '../../store/instrument/instrument.store';
import { OrderStore } from '../../store/order/order.store';
import { mapRD } from '@devexperts/rx-utils/dist/rd/operators/mapRD';
import { switchMapRD } from '@devexperts/rx-utils/dist/rd/operators/switchMapRD';
import { combineLatestRD } from '@devexperts/rx-utils/dist/rd/operators/combineLatestRD';
import { MarketDataStore } from '../../store/market-data/market-data.store';
import { href, Router } from '../../utils/router.utils';
import { MetricsStore } from '../../store/metrics/metrics.store';
import { LimitsStore } from '../../store/limits/limits.store';
import { distinctRDOptionNumber, distinctRDNumber } from '../../utils/setoid.utils';
import { pipe } from 'fp-ts/lib/pipeable';
import { Option } from 'fp-ts/lib/Option';
import * as O from 'fp-ts/lib/Option';
import { empty } from 'fp-ts/lib/Record';
import { PositionTO } from '../../api/generated/swagger/definitions/PositionTO';
import { findFirst } from 'fp-ts/lib/Array';
import { sequenceTOption } from '../../utils/option.utils';
import { routes } from '../../ui-common/routes';

export type PositionCloseViewModel = {
	value$: LiveData<Error, PositionCloseValue>;
	instrumentData$: LiveData<Error, PositionCloseInstrumentData>;
	profit$: LiveDataOption<Error, number>;
	isReadyToClose$: Observable<boolean>;
	onAmountChange: (amount: number) => void;
	onSend: () => void;
	error$: Observable<Option<Error>>;
};

type PositionCloseViewModelContext = {
	positionsStore: PositionsStore;
	metricsStore: MetricsStore;
	accountViewModel: AccountViewModel;
	instrumentStore: InstrumentStore;
	limitsStore: LimitsStore;
	orderStore: OrderStore;
	marketDataStore: MarketDataStore;
	router: Router;
};

type CreatePositionCloseViewModel = (uid: string) => Sink<PositionCloseViewModel>;

export const createPositionCloseViewModel = combineContext(
	ask<PositionCloseViewModelContext>(),
	(e): CreatePositionCloseViewModel => uid => {
		const {
			positionsStore: { getPositionByUid, sendCloseRequest, error$, closedPositions$ },
			metricsStore: { getMetricsByUid },
			accountViewModel,
			instrumentStore: { getById },
			limitsStore: { getLimitsByInstrumentId },
			marketDataStore: { getDataBySymbol },
			router: { push },
		} = e;

		const value = createHandler<number>();
		const send = createHandler<void>();

		const positionEntityData$ = getPositionByUid(uid);

		const plOpen$ = getMetricsByUid(uid).pipe(
			mapRD(metrics => metrics.plOpen),
			distinctRDOptionNumber,
		);

		const instrumentData$ = positionEntityData$.pipe(
			switchMapRD(({ positionKey: { instrumentId } }) => getById(instrumentId)),
			mapRD(instrument => ({
				symbol: instrument.symbol,
				symbolDescription: instrument.description,
				lotSize: instrument.lotSize,
				quantityIncrement: instrument.quantityIncrement,
				precision: instrument.precision,
			})),
			first(),
		);

		const limitsMinOrderIncrement$ = positionEntityData$.pipe(
			switchMapRD(({ positionKey: { instrumentId } }) => getLimitsByInstrumentId(instrumentId)),
			mapRD(limits => limits.minOrderIncrement),
			first(),
		);

		const symbolData$ = instrumentData$.pipe(switchMapRD(({ symbol }) => getDataBySymbol(symbol)));

		const ask$ = symbolData$.pipe(
			mapRD(({ lastQuote }) =>
				pipe(
					lastQuote,
					O.chain(({ ask }) => ask),
				),
			),
			distinctRDOptionNumber,
		);

		const bid$ = symbolData$.pipe(
			mapRD(({ lastQuote }) =>
				pipe(
					lastQuote,
					O.chain(({ bid }) => bid),
				),
			),
			distinctRDOptionNumber,
		);

		const quote$ = combineLatestRD(ask$, bid$).pipe(
			mapRD(([ask, bid]) => ({
				ask,
				bid,
			})),
			filter(isSuccess),
			map(quote => quote.value),
		);

		const positionQuantityRD$ = positionEntityData$.pipe(
			mapRD(({ quantity }) => quantity),
			// position amount may vary, but decided to left "as-is"
			first(),
		);

		const positionQuantity$ = positionQuantityRD$.pipe(
			filter(isSuccess),
			map(data => data.value),
		);

		const amount$ = merge(positionQuantity$, value.value$);

		const amountData$ = amount$.pipe(
			map(value => success<Error, number>(Math.abs(value))),
			distinctRDNumber,
		);

		const step$ = combineLatestRD(instrumentData$, limitsMinOrderIncrement$).pipe(
			mapRD(([instrument, minOrderIncrement]) => minOrderIncrement * instrument.lotSize),
		);

		const amountPrecision$ = step$.pipe(
			mapRD(step => {
				const stepString = step.toString();
				return step < 1 ? stepString.slice(stepString.indexOf('.') + 1).length : 0;
			}),
		);

		const lotsPrecision$ = limitsMinOrderIncrement$.pipe(
			mapRD(limitsMinOrderIncrement => {
				const limitsMinOrderIncrementString = String(limitsMinOrderIncrement);
				return limitsMinOrderIncrementString.slice(limitsMinOrderIncrementString.indexOf('.') + 1).length;
			}),
		);

		const value$ = combineLatestRD(
			positionQuantityRD$,
			instrumentData$,
			amountData$,
			step$,
			amountPrecision$,
			lotsPrecision$,
		).pipe(
			mapRD(([quantity, { lotSize }, amount, step, precision, lotsPrecision]) => ({
				initAmount: Math.abs(quantity),
				amount,
				lot: amount / lotSize,
				step,
				precision,
				lotsPrecision,
			})),
		);

		const accountId$ = accountViewModel.current$.pipe(map(current => current.id));
		const instrumentId$ = positionEntityData$.pipe(
			mapRD(({ positionKey: { instrumentId } }) => instrumentId),
			filter(isSuccess),
			map(instrument => instrument.value),
		);

		const positionKey$ = positionEntityData$.pipe(
			mapRD(({ positionKey }) => positionKey),
			filter(isSuccess),
			map(instrument => instrument.value),
		);

		const sendPrerequisites$ = combineLatest(
			instrumentId$,
			amount$,
			accountId$,
			positionKey$,
			quote$,
			positionQuantity$,
		);

		const sendEffect$ = send.value$.pipe(
			withLatestFrom(sendPrerequisites$),
			switchMap(([_, [instrumentId, amount, accountId, positionKey, quote, positionQuantity]]) =>
				merge(
					sendCloseRequest(accountId, instrumentId, amount, positionKey, quote, positionQuantity),
					closedPositions$.pipe(
						tap((positions: RemoteData<Error, PositionTO[]>) => {
							if (positions.isSuccess()) {
								const position = findFirst(positions.value, item =>
									pipe(
										sequenceTOption(item.positionKey.positionCode, positionKey.positionCode),
										O.exists(
											([itemPositionCode, positionCode]) => itemPositionCode === positionCode,
										),
									),
								);
								if (O.isSome(position)) {
									push(href(routes.positions.root, empty));
								}
							}
						}),
					),
				),
			),
		);

		const isReadyToClose$ = combineLatest(sendPrerequisites$, value$, plOpen$, instrumentData$).pipe(
			// allow closing position only if all data is fetched and resolved
			map(
				([_, value, plOpen, instrumentData]) =>
					value.isSuccess() && plOpen.isSuccess() && instrumentData.isSuccess(),
			),
			distinctUntilChanged(),
		);

		return new Sink<PositionCloseViewModel>(
			{
				value$,
				profit$: plOpen$,
				instrumentData$: instrumentData$.pipe(
					mapRD(({ symbol, symbolDescription }) => ({ symbol, symbolDescription })),
				),
				isReadyToClose$,
				onAmountChange: value.handle,
				onSend: () => send.handle(undefined),
				error$,
			},
			sendEffect$,
		);
	},
);
