import * as t from 'io-ts';
import { stringOption } from '../../../utils/io-ts.utils';
import { Option } from 'fp-ts/lib/Option';

export const QUOTE_SUBSCRIPTION_INITIALIZED = 'QUOTE_SUBSCRIPTION_INITIALIZED';

export type QuoteSubscriptionInitializedMessageTO = {
	type: typeof QUOTE_SUBSCRIPTION_INITIALIZED;
	accountId: Option<string>;
	body: {
		success: boolean;
	};
};
export const quoteSubscriptionInitializedMessageTO: t.Tagged<
	'type',
	QuoteSubscriptionInitializedMessageTO,
	unknown
> = t.type({
	type: t.literal(QUOTE_SUBSCRIPTION_INITIALIZED),
	accountId: stringOption,
	body: t.type({
		success: t.boolean,
	}),
});
