import * as React from 'react';
import { memo } from 'react';
import { Popup } from '../../../ui-kit/components/popup/popup.component';
import { Button } from '../../../../ui-common/button/button.component';
import { WithTranslation, withTranslation } from 'react-i18next';
import { tspan } from '../../../../utils/i18n.utils';
import { redirectToRelogin } from '../../../../utils/route.utils';
import * as css from './theme/connection-lost-popup.module.css';
import * as buttonCss from './theme/button.child.module.css';
import * as popupCss from './theme/popup.child.module.css';

type ConnectionLostPopupProps = {
	isPopupOpened: boolean;
};

const ConnectionLostPopupRaw = memo<ConnectionLostPopupProps & WithTranslation>(props => {
	const { isPopupOpened } = props;

	return (
		<Popup
			isModal
			onClose={redirectToRelogin}
			isOpened={isPopupOpened}
			theme={popupCss}
			title={tspan('connectionLostPopupConnectionLost', 'Connection Lost')}>
			<div className={css.textBody}>
				<div>
					{tspan('connectionLostPopupConnectionToPlatformLost', 'Connection to trading platform lost.')}
				</div>
				<div>
					{tspan(
						'connectionLostPopupCheckConnection',
						'Please check your internet connection and login again',
					)}
				</div>
			</div>
			<div className={css.buttonWrapper}>
				<Button isPrimary theme={buttonCss} onClick={redirectToRelogin}>
					{tspan('connectionLostPopupGoToLogin', 'Go to Login Page')}
				</Button>
			</div>
		</Popup>
	);
});

export const ConnectionLostPopup = withTranslation()(ConnectionLostPopupRaw);
