import { PureComponent, ReactNode } from 'react';
import { Action } from 'history';

export type RouteLinkNavigateOptions = {
	action: Action;
	href: string;
};

export type RouteLinkRenderProps = {
	readonly onNavigate: () => void;
	readonly isActive: boolean;
	readonly href: string;
};

export type RouteLinkProps = {
	readonly href: string;
	readonly action: Action;
	readonly isActive: boolean;
	readonly isExact: boolean;
	readonly onNavigate: (options: RouteLinkNavigateOptions) => void;
	readonly render: (props: RouteLinkRenderProps) => ReactNode;
};

export class RouteLinkComponent extends PureComponent<RouteLinkProps> {
	render() {
		const { href, render, isActive } = this.props;
		return render({
			onNavigate: this.handleNavigate,
			isActive,
			href,
		});
	}

	private handleNavigate = () => {
		const { href, action, isExact, onNavigate } = this.props;
		if (!isExact) {
			onNavigate({
				href,
				action,
			});
		}
	};
}
