export function applyT<A, B, C, D, R>(f: (a: A, b: B, c: C, d: D) => R): (t: [A, B, C, D]) => R;
export function applyT<A, B, C, R>(f: (a: A, b: B, c: C) => R): (t: [A, B, C]) => R;
export function applyT<A, B, R>(f: (a: A, b: B) => R): (t: [A, B]) => R;
export function applyT<A, R>(f: (a: A) => R): (t: [A]) => R;
export function applyT<A, R>(f: (...a: A[]) => R): (t: A[]) => R {
	return t => f(...t);
}

export const firstT = <A, B>(t: [A, B]): A => t[0];
export const secondT = <A, B>(t: [A, B]): B => t[1];
