import { Option } from 'fp-ts/lib/Option';
import { string, number, type } from 'io-ts';
import { createOptionFromNullable } from 'io-ts-types';

export type SummaryTO = {
	symbol: string;
	quantity: Option<number>;
	dayOpen: Option<number>;
	dayHigh: Option<number>;
	dayLow: Option<number>;
	prevDayClose: Option<number>;
	dayClosePrice: Option<number>;
};
export const SummaryTOIO = type({
	symbol: string,
	quantity: createOptionFromNullable(number),
	dayOpen: createOptionFromNullable(number),
	dayHigh: createOptionFromNullable(number),
	dayLow: createOptionFromNullable(number),
	prevDayClose: createOptionFromNullable(number),
	dayClosePrice: createOptionFromNullable(number),
});
