import * as React from 'react';
import { Fragment, PureComponent, ReactNode } from 'react';
import { findFirst } from 'fp-ts/lib/Array';
import { empty } from 'fp-ts/lib/Record';
import * as O from 'fp-ts/lib/Option';
import { WithTranslation, withTranslation } from 'react-i18next';
import { AVATRADE_CALC_URL, routes } from '../../../../../../ui-common/routes';
import { RouteLink } from '../../../../../../ui-common/route-link/containers/route-link.container';
import { combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { href } from '../../../../../../utils/router.utils';
import { makeAccountUrl } from '../../../../../../view-model/menu/menu.utils';
import { AccountsIcon } from '../../../../../../ui-mobile/ui-kit/components/icons/accounts.icon';
import { UploadDocsIcon } from '../../../../../ui-kit/components/icons/upload-docs.icon';
import { PersonalDetailsIcon } from '../../../../../ui-kit/components/icons/personal-details.icon';
import { CalculatorIcon } from '../../../../../ui-kit/components/icons/calculator.icon';
import { TraderIcon } from '../../../../../ui-kit/components/icons/trader.icon';
import { WithdrawIcon } from '../../../../../ui-kit/components/icons/withdraw.icon';
import { SwitchIcon } from '../../../../../ui-kit/components/icons/switch.icon';
import { MenuDivider } from '../../../../../../ui-common/layout/components/menu-divider/menu-divider.component';
import { MenuListItem } from '../../../../../../ui-common/layout/components/menu-list-item/menu-list-item.component';
import { Accounts, Mode } from './menu.model';
import { MenuAccounts } from '../../../../../../ui-common/layout/components/menu-accounts/menu-accounts.component';
import { ArrowDownFlatIcon } from '../../../../../../ui-common/icons/arrow-down-flat.icon';
import { tspan } from '../../../../../../utils/i18n.utils';
import * as css from './theme/menu-live.module.css';
import { pipe } from 'fp-ts/lib/pipeable';

export type MenuLiveProps = {
	onClose: () => void;
	onPreloaderChange: (state: boolean) => void;
	accounts: Accounts;
	mode: Mode;
	proTraderAvaliable: boolean;
};

export const MenuLive = combineContext(RouteLink, RouteLink =>
	withTranslation()(
		class MenuLive extends PureComponent<MenuLiveProps & WithTranslation> {
			render() {
				const {
					mode,
					accounts: { currentAccount },
					proTraderAvaliable,
				} = this.props;

				const isDemo = currentAccount.status === Mode.Demo;

				return (
					<div className={css.container}>
						<RouteLink
							to={routes.crm.accounts.root}
							value={empty}
							render={({ href, onNavigate }) => (
								<MenuListItem
									icon={<AccountsIcon />}
									text={tspan('menuMyAccounts', 'My Accounts')}
									href={href}
									preventDefault={true}
									onClick={this.handleRoute(onNavigate)}
								/>
							)}
						/>
						<RouteLink
							to={routes.crm.uploaddocs.root}
							value={empty}
							render={({ href, onNavigate }) => (
								<MenuListItem
									icon={<UploadDocsIcon />}
									text={tspan('menuUploadDocs', 'Upload Docs')}
									href={href}
									isDisabled={isDemo}
									preventDefault={true}
									onClick={this.handleRoute(onNavigate)}
								/>
							)}
						/>
						<RouteLink
							to={routes.crm.personal.root}
							value={empty}
							render={({ href, onNavigate }) => (
								<MenuListItem
									icon={<PersonalDetailsIcon />}
									text={tspan('menuPersonalDetails', 'Personal Details')}
									href={href}
									preventDefault={true}
									onClick={this.handleRoute(onNavigate)}
								/>
							)}
						/>
						<RouteLink
							to={routes.crm.withdraw.root}
							value={empty}
							render={({ href, onNavigate }) => (
								<MenuListItem
									icon={<WithdrawIcon />}
									text={tspan('menuWithdraw', 'Withdraw')}
									href={href}
									isDisabled={isDemo}
									preventDefault={true}
									onClick={this.handleRoute(onNavigate)}
								/>
							)}
						/>

						<MenuDivider />

						{mode !== Mode.Demo && proTraderAvaliable && (
							<RouteLink
								to={routes.crm.becomeTrader.root}
								value={empty}
								render={({ href, onNavigate }) => (
									<MenuListItem
										icon={<TraderIcon />}
										text={tspan('menuProfessionalTrader', 'Professional Trader')}
										href={href}
										isDisabled={isDemo}
										onClick={this.handleRoute(onNavigate)}
									/>
								)}
							/>
						)}

						<MenuListItem
							icon={<CalculatorIcon />}
							text={tspan('menuTradingCalculator', 'Trading Calculator')}
							href={AVATRADE_CALC_URL}
							target="_blank"
							rel="noopener"
						/>

						{this.renderAccounts(mode)}
					</div>
				);
			}

			private getSwitchModeText = (status: Mode) => {
				switch (status) {
					case Mode.Regular:
					case Mode.Real: {
						return tspan('menuSwitchToDemo', 'Switch to Demo');
					}
					case Mode.DemoLive:
					case Mode.Demo: {
						return tspan('menuSwitchToReal', 'Switch to Real');
					}
				}
			};

			handleRoute = (onNavigate: () => void) => () => {
				onNavigate();
				this.props.onClose();
			};

			handleSwitch = () => {
				this.props.onClose();
				this.props.onPreloaderChange(true);
			};

			renderAccounts = (mode: Mode): ReactNode => {
				const { accounts, i18n } = this.props;
				const { language } = i18n;
				switch (mode) {
					case Mode.Demo:
					case Mode.Real: {
						return (
							<Fragment>
								<MenuDivider label={tspan('menuAccounts', 'Accounts')} />
								<MenuListItem
									icon={<SwitchIcon />}
									text={tspan('menuSwitchToDemo', 'Switch to Demo')}
									href={href(routes.api.avarcm.create_demo_account, empty)}
									onClick={this.handleSwitch}
								/>
							</Fragment>
						);
					}
					case Mode.DemoLive: {
						const secondAccount = findFirst(
							accounts.availableAccounts,
							account => account.accountId !== accounts.currentAccount.accountId,
						);
						const switchModeText = this.getSwitchModeText(accounts.currentAccount.status);
						const accountId = pipe(
							secondAccount,
							O.map(account => account.accountId),
						);

						return pipe(
							accountId,
							O.map(id => (
								<Fragment>
									<MenuDivider label={tspan('menuAccounts', 'Accounts')} />
									<MenuListItem
										icon={<SwitchIcon />}
										text={switchModeText}
										href={makeAccountUrl(id, language)}
										onClick={this.handleSwitch}
									/>
								</Fragment>
							)),
							O.toNullable,
						);
					}
					case Mode.Regular: {
						return (
							<Fragment>
								<MenuDivider label={tspan('menuAccounts', 'Accounts')} />
								<MenuAccounts
									accounts={accounts}
									arrowIcon={<ArrowDownFlatIcon />}
									handleSwitch={this.handleSwitch}
								/>
							</Fragment>
						);
					}
				}
			};
		},
	),
);
