import { fromEvent, race } from 'rxjs';
import { startWith, map } from 'rxjs/operators';
import { pending, success, failure } from '@devexperts/remote-data-ts';
import { LiveData } from '@devexperts/rx-utils/dist/rd/live-data.utils';

export const loadScript = (src: string): LiveData<Error, Event> => {
	const script = document.createElement('script');
	script.src = src;
	document.body.appendChild(script);
	const load$ = fromEvent(script, 'load').pipe(map(e => success<Error, Event>(e)));
	const error$ = fromEvent(script, 'error').pipe(
		map(() => failure<Error, Event>(new Error(`Error loaded ${script.src}`))),
	);
	return race(error$, load$).pipe(startWith(pending));
};
