import { Option } from 'fp-ts/lib/Option';
import { ReactNode } from 'react';

export type TableData = {
	symbols: string[];
	positionId: Option<number>;
};

export type TableItem = {
	symbol: string;
	positions: Option<number>;
	pipsSize: Option<number>;
	precision: Option<number>;
	sell: Option<number>;
	buy: Option<number>;
	change: Option<number>;
	isFavorite: boolean;
};

export type ConfigItem = {
	width: number;
	dataKey: keyof TableItem | 'buy-sell-btns' | 'info-fav-icons';
	label: ReactNode;
};

export enum TableSize {
	Small = '* SMALL *',
	Large = '* LARGE *',
}

export type WatchlistTableColumnNames = {
	symbol: ReactNode;
	sell: ReactNode;
	buy: ReactNode;
	change: ReactNode;
};
