import * as React from 'react';
import { PureComponent } from 'react';
import { left } from 'fp-ts/lib/Either';
import { RemoteData } from '@devexperts/remote-data-ts';
import { combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { RemoteDataState } from '../../../../ui-common/remote-data-state/remote-data-state.component';
import { OrderEntryPanelContainer } from '../../../order-entry/containers/order-entry-panel/order-entry-panel.container';
import { OrderEntrySide } from '../../../order-entry/components/order-entry-panel/order-entry-panel.model';

type OrderEditData = {
	symbol: string;
	side: OrderEntrySide;
};

type OrderEditProps = {
	data: RemoteData<Error, OrderEditData>;
	orderId: number;
	profitLossKind: string;
};

export const OrderEdit = combineContext(
	OrderEntryPanelContainer,
	OrderEntryPanelContainer =>
		class OrderEdit extends PureComponent<OrderEditProps> {
			render() {
				const { data } = this.props;

				return <RemoteDataState data={data} success={this.renderSuccess} />;
			}

			private renderSuccess = (data: OrderEditData): JSX.Element => (
				<OrderEntryPanelContainer
					elementId={left(String(this.props.orderId))}
					plKind={this.props.profitLossKind}
					symbol={data.symbol}
					action={data.side}
					isEdit
				/>
			);
		},
);
