import * as React from 'react';
import { Component, createRef, Fragment, KeyboardEvent, MouseEvent, PureComponent } from 'react';
import { withTranslation, WithTranslation } from 'react-i18next';
import * as classNames from 'classnames';
import { Option } from 'fp-ts/lib/Option';
import * as O from 'fp-ts/lib/Option';
import { Lazy, constant } from 'fp-ts/lib/function';
import { head } from 'fp-ts/lib/Array';
import { Input, TInputProps } from '@devexperts/react-kit/dist/components/input/Input';
import { RemoteData } from '@devexperts/remote-data-ts';
import { Highlight } from '@devexperts/react-kit/dist/components/Highlight/Highlight';
import { PopoverAlign, PopoverPlacement } from '@devexperts/react-kit/dist/components/Popover/Popover';
import { Popover } from '../../../ui-kit/components/popover/popover.component';
import { Scrollable } from '../../../../ui-common/scrollable/scrollable.component';
import { RemoteDataState } from '../../../../ui-common/remote-data-state/remote-data-state.component';
import { ClearIcon } from '../../../ui-kit/components/icons/clear.icon';
import { SearchIcon } from '../../../ui-kit/components/icons/search.icon';
import { getSuggest, getSuggests, nextSuggest, prevSuggest, Suggest, SuggestGroup } from './symbol-search.model';
import { pipe } from 'fp-ts/lib/pipeable';
import * as inputTheme from './theme/input.child.module.css';
import * as css from './theme/symbol-search.module.css';
import * as popoverTheme from './theme/popover.child.module.css';
import * as highlightTheme from './theme/highlight.child.module.css';

const symbolClassName = {
	container: classNames(highlightTheme.container, highlightTheme.container_symbol),
	mark: highlightTheme.mark,
};

export type SymbolSearchProps = {
	data: RemoteData<Error, SuggestGroup[]>;
	isOpened: boolean;
	onCloseRequest: Lazy<void>;
	onEnter: (suggest: Option<Suggest>) => void;
	onInputBlur?: Lazy<void>;
	onSelect: (suggest: Option<Suggest>) => void;
	onValueChange: (value: string, keepOpened?: boolean) => void;
	selectedId: Option<string>;
	value: string;
};

class SymbolSearchUnwrapped extends PureComponent<SymbolSearchProps & WithTranslation> {
	private input = createRef<Component<TInputProps>>();
	private isInputFocused: boolean = false;

	public render() {
		const { data, value, isOpened, t } = this.props;

		return (
			<div className={css.container}>
				<Input
					onFocus={this.inputHandleFocus}
					onBlur={this.inputHandleBlur}
					value={value}
					onValueChange={this.handleValueChange}
					ref={this.input}
					onKeyDown={this.handleKeyDown}
					theme={inputTheme}
					placeholder={t('symbolSearchSearchSymbol', 'Search Symbol')}>
					<div className={css.iconWrapper} onClick={this.handleIconClick}>
						{value ? <ClearIcon /> : <SearchIcon />}
					</div>
				</Input>
				<Popover
					onRequestClose={this.popoverHandleBlur}
					closeOnClickAway={true}
					align={PopoverAlign.Left}
					placement={PopoverPlacement.Bottom}
					isOpened={isOpened}
					anchor={this.input.current}
					theme={popoverTheme}>
					<RemoteDataState data={data} success={this.renderSuccess} />
				</Popover>
			</div>
		);
	}

	private renderSuccess = (data: SuggestGroup[]) => {
		const { value, selectedId } = this.props;

		return (
			<Scrollable>
				<ul className={css.suggestList}>
					{data.map((group, groupIndex) => (
						<Fragment key={groupIndex}>
							<div className={css.groupTitle}>
								<span className={css.groupTitleText}>{group.title}</span>
							</div>
							{group.values.map((item, itemIndex) => {
								const listItemClassName = classNames(css.suggest, {
									[css.suggest_selected]: pipe(
										selectedId,
										O.exists(selectedId => selectedId === item.id),
									),
								});

								const symbolCN = {
									...symbolClassName,
									mark: pipe(
										selectedId,
										O.fold(constant(symbolClassName.mark), x =>
											x === item.id ? css.selectedMark : symbolClassName.mark,
										),
									),
								};

								return (
									<li
										className={listItemClassName}
										key={itemIndex}
										onMouseEnter={this.handleHover(item, selectedId)}
										onMouseMove={this.handleHover(item, selectedId)}
										onClick={this.handleClick(item)}>
										<Highlight theme={symbolCN} search={value}>
											{item.symbol}
										</Highlight>
									</li>
								);
							})}
						</Fragment>
					))}
				</ul>
			</Scrollable>
		);
	};

	// Inputs onValueChange passes string or undefined but it is, actually, impossible, because input always has a value
	private handleValueChange = (value: string | undefined) => this.props.onValueChange(value || '', !!value);

	private handleHover = (suggest: Suggest, selectedId: Option<string>) => () => {
		const { onSelect } = this.props;
		if (
			pipe(
				selectedId,
				O.exists(selectedId => selectedId === suggest.id),
			)
		) {
			return;
		}
		onSelect(O.some(suggest));
	};

	private handleKeyDown = (e: KeyboardEvent<HTMLInputElement>) => {
		const {
			isOpened,
			onEnter,
			value: inputValue,
			onSelect,
			data,
			selectedId,
			onValueChange,
			onCloseRequest,
		} = this.props;
		const suggestGroups = data.toOption();

		if (e.key === 'Escape' || e.key === 'Tab') {
			onCloseRequest();
			return;
		}

		const suggests = pipe(
			suggestGroups,
			O.map(getSuggests),
		);
		switch (e.key) {
			case 'ArrowDown':
				e.preventDefault();
				const next = pipe(
					suggests,
					O.chain(nextSuggest(selectedId)),
				);
				onSelect(next);
				break;
			case 'ArrowUp':
				if (!isOpened) {
					return;
				}
				e.preventDefault();
				const prev = pipe(
					suggests,
					O.chain(prevSuggest(selectedId)),
				);

				onSelect(prev);

				break;
			case 'Enter':
				if (!isOpened) {
					return;
				}
				const selectedSuggest = pipe(
					selectedId,
					O.map(getSuggest),
					O.chain(f =>
						pipe(
							suggests,
							O.chain(s => f(s)),
						),
					),
					O.alt(() =>
						pipe(
							suggests,
							O.chain(head),
						),
					),
				);
				onValueChange(
					pipe(
						selectedSuggest,
						O.map(selectedSuggest => selectedSuggest.symbol),
						O.getOrElse(() => inputValue),
					),
					false,
				);
				onEnter(selectedSuggest);
				break;
		}
	};

	private inputHandleBlur = () => {
		this.isInputFocused = false;
	};

	private inputHandleFocus = () => {
		this.isInputFocused = true;
	};

	private handleClick = (suggest: Suggest) => (e: MouseEvent<HTMLElement>) => {
		const { onValueChange, onEnter } = this.props;
		e.preventDefault();
		onValueChange(suggest.symbol);
		onEnter(O.some(suggest));
	};

	private popoverHandleBlur = () => {
		if (this.isInputFocused) {
			return;
		}

		this.props.onInputBlur && this.props.onInputBlur();
	};

	private handleIconClick = (e: MouseEvent<HTMLElement>) => {
		e.stopPropagation();

		const { value } = this.props;

		if (value) {
			this.props.onValueChange('');
		}
	};
}

export const SymbolSearch = withTranslation()(SymbolSearchUnwrapped);
