import { ask, combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { OrderStore } from '../../store/order/order.store';
import { PositionsStore } from '../../store/positions/positions.store';
import { map } from 'rxjs/operators';
import { Observable } from 'rxjs';

export type AsideViewModel = {
	orderCount$: Observable<number>;
	positionCount$: Observable<number>;
};

export type AsideViewModelContext = {
	orderStore: OrderStore;
	positionsStore: PositionsStore;
};
export type CreateAsideViewModel = () => AsideViewModel;

export const createAsideViewModel = combineContext(
	ask<AsideViewModelContext>(),
	(e): CreateAsideViewModel => () => {
		const { orderStore, positionsStore } = e;
		const orderCount$ = orderStore.keys$.pipe(map(keys => keys.length));
		const positionCount$ = positionsStore.keys$.pipe(map(keys => keys.length));

		return {
			orderCount$,
			positionCount$,
		};
	},
);
