import { Big } from 'big.js';
import { combineContext, ask } from '@devexperts/rx-utils/dist/context.utils';
import { OrderStore } from '../../store/order/order.store';
import { mapRD } from '@devexperts/rx-utils/dist/rd/operators/mapRD';
import { InstrumentStore } from '../../store/instrument/instrument.store';
import { switchMapRD } from '@devexperts/rx-utils/dist/rd/operators/switchMapRD';
import { combineLatestRD } from '@devexperts/rx-utils/dist/rd/operators/combineLatestRD';
import { LiveData } from '@devexperts/rx-utils/dist/rd/live-data.utils';
import { Option } from 'fp-ts/lib/Option';
import * as O from 'fp-ts/lib/Option';
import { MarketDataStore } from '../../store/market-data/market-data.store';
import { getTriggerPrice, getCurrentPrice, getOrderSideByQuantity, getAmount, getDistance } from './orders.utils';
import { OrderEntrySide } from '../../model/order-entry/order-entry.model';
import { first } from 'rxjs/operators';
import { distinctRDOptionNumber, distinctRDOptionDate } from '../../utils/setoid.utils';
import { pipe } from 'fp-ts/lib/pipeable';

type OrderDetailsData = {
	orderId: number;
	symbol: string;
	side: OrderEntrySide;
	triggerPrice: Option<number>;
	changeDirection: Option<number>;
	currentPrice: Option<number>;
	distance: Option<number>;
	quantity: number;
	validUntil: Option<Date>;
	openDateTime: Date;
	sizeInLots: number;
	takeProfit: Option<number>;
	stopLoss: Option<number>;
	amount: Option<number>;
	precision: number;
	pipSize: number;
};

export type OrderDetailsViewModel = {
	orderDetails$: LiveData<Error, OrderDetailsData>;
};

type CreateOrderDetailsViewModel = (orderId: number) => OrderDetailsViewModel;

type OrderDetailsViewModelContext = {
	orderStore: OrderStore;
	instrumentStore: InstrumentStore;
	marketDataStore: MarketDataStore;
};

export const createOrderDetailsViewModel = combineContext(
	ask<OrderDetailsViewModelContext>(),
	(context): CreateOrderDetailsViewModel => orderId => {
		const {
			orderStore: { getById },
			instrumentStore,
			marketDataStore: { getDataBySymbol },
		} = context;

		const order$ = getById(orderId);

		const instrument$ = order$.pipe(
			switchMapRD(order => instrumentStore.getById(order.instrumentId)),
			first(),
		);

		const marketData$ = instrument$.pipe(switchMapRD(instrument => getDataBySymbol(instrument.symbol)));

		const orderId$ = order$.pipe(
			mapRD(({ orderId }) => orderId),
			first(),
		);

		const quantity$ = order$.pipe(
			mapRD(({ quantity }) => quantity),
			first(),
		);

		const validUntil$ = order$.pipe(
			mapRD(({ expireAt }) => expireAt),
			distinctRDOptionDate,
		);

		const createdTime$ = order$.pipe(
			mapRD(({ createdTime }) => createdTime),
			first(),
		);

		const takeProfit$ = order$.pipe(
			mapRD(({ takeProfit }) =>
				pipe(
					takeProfit,
					O.chain(v => v.fixedPrice),
				),
			),
			distinctRDOptionNumber,
		);

		const stopLoss$ = order$.pipe(
			mapRD(({ stopLoss }) =>
				pipe(
					stopLoss,
					O.chain(v => v.fixedPrice),
				),
			),
			distinctRDOptionNumber,
		);

		const orderType$ = order$.pipe(
			mapRD(({ type }) => type),
			first(),
		);

		const stopPrice$ = order$.pipe(
			mapRD(({ stopPrice }) => stopPrice),
			distinctRDOptionNumber,
		);

		const limitPrice$ = order$.pipe(
			mapRD(({ limitPrice }) => limitPrice),
			distinctRDOptionNumber,
		);

		const quote$ = marketData$.pipe(mapRD(({ lastQuote }) => lastQuote));

		const bid$ = quote$.pipe(
			mapRD(quote =>
				pipe(
					quote,
					O.chain(quote => quote.bid),
				),
			),
			distinctRDOptionNumber,
		);

		const ask$ = quote$.pipe(
			mapRD(quote =>
				pipe(
					quote,
					O.chain(quote => quote.ask),
				),
			),
			distinctRDOptionNumber,
		);

		const changeDirection$ = combineLatestRD(quote$, order$).pipe(
			mapRD(([quote, order]) =>
				pipe(
					quote,
					O.map(quote => (order.quantity < 0 ? quote.bidDirection : quote.askDirection)),
				),
			),
			distinctRDOptionNumber,
		);

		const combinedData1$ = combineLatestRD(quantity$, validUntil$, createdTime$, takeProfit$, stopLoss$);
		const combinedData2$ = combineLatestRD(instrument$, orderId$, bid$, ask$, changeDirection$);
		const combinedData3$ = combineLatestRD(orderType$, limitPrice$, stopPrice$);

		const orderDetails$ = combineLatestRD(combinedData1$, combinedData2$, combinedData3$).pipe(
			mapRD(
				([
					[quantity, expireAt, createdTime, takeProfit, stopLoss],
					[instrument, orderId, bid, ask, changeDirection],
					[orderType, limitPrice, stopPrice],
				]) => {
					const currentPrice = getCurrentPrice(bid, ask, quantity);
					const quantityAbs = Math.abs(quantity);
					const side = getOrderSideByQuantity(quantity);
					const amount = getAmount(side, bid, ask);
					const triggerPrice = getTriggerPrice(orderType, amount, limitPrice, stopPrice);

					const orderDetails: OrderDetailsData = {
						orderId,
						symbol: instrument.symbol,
						side,
						triggerPrice,
						currentPrice,
						changeDirection,
						distance: pipe(
							currentPrice,
							O.chain(currentPrice =>
								pipe(
									triggerPrice,
									O.map(triggerPrice => getDistance(triggerPrice, currentPrice)),
								),
							),
						),
						quantity: quantityAbs,
						validUntil: expireAt,
						openDateTime: createdTime,
						sizeInLots: Number(Big(quantityAbs).div(instrument.lotSize)),
						takeProfit,
						stopLoss,
						amount,
						precision: instrument.precision,
						pipSize: instrument.pipsSize,
					};

					return orderDetails;
				},
			),
		);

		return { orderDetails$ };
	},
);
