import { Option } from 'fp-ts/lib/Option';
import { string, union, literal, number, array, type } from 'io-ts';
import { createOptionFromNullable } from 'io-ts-types';

export type StudyLineTO = {
	title: Option<string>;
	studyLineType: Option<
		| 'UNDEFINED_TYPE'
		| 'LINEAR_TYPE'
		| 'HISTOGRAM_TYPE'
		| 'POINTS_TYPE'
		| 'COMPARATIVE_TYPE'
		| 'TREND_HISTOGRAM_TYPE'
		| 'ARROW_UP_TYPE'
		| 'ARROW_DOWN_TYPE'
		| 'PIECEWISE_TYPE'
		| 'PIECEWISE_NO_MARKER_TYPE'
	>;
	thickness: Option<number>;
	colors: Option<Array<string>>;
};
export const StudyLineTOIO = type({
	title: createOptionFromNullable(string),
	studyLineType: createOptionFromNullable(
		union([
			literal('UNDEFINED_TYPE'),
			literal('LINEAR_TYPE'),
			literal('HISTOGRAM_TYPE'),
			literal('POINTS_TYPE'),
			literal('COMPARATIVE_TYPE'),
			literal('TREND_HISTOGRAM_TYPE'),
			literal('ARROW_UP_TYPE'),
			literal('ARROW_DOWN_TYPE'),
			literal('PIECEWISE_TYPE'),
			literal('PIECEWISE_NO_MARKER_TYPE'),
		]),
	),
	thickness: createOptionFromNullable(number),
	colors: createOptionFromNullable(array(string)),
});
