import { ask, combineContext, deferContext } from '@devexperts/rx-utils/dist/context.utils';
import { withRXWithDefaults } from '../../../../utils/withRXWithDefaults';
import { OrdersTable } from '../../components/orders-table/orders-table.component';
import { success } from '@devexperts/remote-data-ts';
import { OrdersViewModel, createOrdersViewModel, OrderTOFull } from '../../../../view-model/orders/orders.view-model';
import { mapRD } from '@devexperts/rx-utils/dist/rd/operators/mapRD';
import { runOnMount } from '../../../../utils/react.utils';
import * as O from 'fp-ts/lib/Option';
import { empty } from 'fp-ts/lib/Array';
import { distinctRDNumberArray } from '../../../../utils/setoid.utils';
import { TableSize } from '../../components/orders-table/orders-table.model';
import { OrdersLargeTableConfig } from '../../components/orders-table/orders-table.utils';
import { pipe } from 'fp-ts/lib/pipeable';
import { constant } from 'fp-ts/lib/function';

export type OrdersTableContainerContext = {
	ordersViewModel: OrdersViewModel;
};

const Container = combineContext(OrdersTable, ask<OrdersTableContainerContext>(), (OrdersTable, context) =>
	withRXWithDefaults(OrdersTable)(() => {
		const { ordersViewModel } = context;
		const { tableSize$, tableSizeConfig$, orders$ } = ordersViewModel;

		const data$ = orders$.pipe(
			mapRD(orders =>
				pipe(
					orders,
					O.getOrElse<OrderTOFull[]>(constant(empty)),
				),
			),
			mapRD(tableItems => tableItems.map(item => item.orderId)),
			distinctRDNumberArray,
		);

		return {
			defaultProps: {
				data: success(empty),
				config: OrdersLargeTableConfig,
				tableSize: TableSize.Large,
			},
			props: {
				data: data$,
				config: tableSizeConfig$,
				tableSize: tableSize$,
			},
		};
	}),
);

export const OrdersTableContainer = combineContext(
	deferContext(Container, 'ordersViewModel'),
	createOrdersViewModel,
	(Container, createOrdersViewModel) =>
		runOnMount(Container, () => () => ({ ordersViewModel: createOrdersViewModel() })),
);
