import CrossToolViewModel, { E_CROSS_TOOL_PLUGIN_MODEL } from '../models/cross-tool.view-model';
import css from './cross-tool.module.css';
import * as classnames from 'classnames';
import ChartInfoView from './chart-info.view';
import { StudySeries } from '../../../chart.model';
import Hover from '@dx-private/dxchart5/dist/js/chart/model/Hover';

function createElementWithClass(tagName: string, className: string) {
	const element = document.createElement(tagName);

	element.className = className;

	return element;
}

/**
 * This is used for main chart info box (Candles)
 * @type {Number}
 */

const MAIN_CHART_INFO_ID = 0;

/**
 * CrossToolPluginView
 */
export default class CrossToolPluginView {
	/**
	 * @type {CrossToolViewModel}
	 * @private
	 */
	_model: CrossToolViewModel;

	/**
	 * Main element
	 * @type {HTMLElement|Element}
	 * @private
	 */
	_element: Element;

	/**
	 * Parent container
	 * @type {HTMLElement|Element|EventTarget}
	 */
	_container: Element;

	/**
	 * @type {boolean}
	 * @private
	 */
	_isVisible = false;

	/**
	 * @type {boolean}
	 * @private
	 */
	_isDisabled = false;

	/**
	 * @type {boolean}
	 * @private
	 */
	_isMobile = false;

	/**
	 * @type {ChartInfoView}
	 * @private
	 */
	_chartInfoView: ChartInfoView;

	/**
	 * @type {HTMLElement}
	 * @private
	 */
	_chartInfoContainer: Element;

	/**
	 * @type {HTMLElement}
	 * @private
	 */
	_xAxisLine: Element;

	/**
	 * @type {HTMLElement}
	 * @private
	 */
	_yAxisLine: Element;

	/**
	 * @type {HTMLElement|Element}
	 * @private
	 */
	_xChartAxisLabel?: Element;

	/**
	 * @type {HTMLElement|Element}
	 * @private
	 */
	_yChartAxisLabel?: Element;

	/**
	 * @type {HTMLElement|Element}
	 * @private
	 */
	_xChartAxisLabelValue?: Element;

	/**
	 * @type {HTMLElement|Element}
	 * @private
	 */
	_yChartAxisLabelValue?: Element;

	/**
	 * @type {boolean}
	 * @private
	 */
	_isXAxisLineVisible = false;

	/**
	 * @type {Number}
	 */
	_uiid = MAIN_CHART_INFO_ID;

	/**
	 * @type {function(event:Event):void}
	 * @private
	 */
	_onMouseOver?: (e: Event) => void;

	/**
	 * @type {function(event:Event):void}
	 * @private
	 */
	_onMouseOut?: (e: Event) => void;

	/**
	 * @type {boolean}
	 * @private
	 */
	_isAlignedRight = false;

	constructor(
		container: Element,
		model: CrossToolViewModel,
		chartXAxis: Element | null,
		chartYAxis: Element | null,
		studySeries: StudySeries | null,
		isMobile: boolean,
		createChartInfoView: (
			container: Element,
			studySeries: StudySeries | null,
			model: CrossToolViewModel,
			isMobile: boolean,
		) => ChartInfoView,
	) {
		if (studySeries) {
			//study, not candles
			this._uiid = studySeries.uiid;
		}
		this._container = container;
		this._model = model;

		this._isDisabled = !model.isEnabled;
		this._isMobile = isMobile;

		//create element
		this._element = createElementWithClass('div', css.container);
		this._container.appendChild(this._element);

		//create axis-lines
		this._xAxisLine = createElementWithClass('div', classnames(css.axis, css.axis_x));
		this._yAxisLine = createElementWithClass('div', classnames(css.axis, css.axis_y));
		this._element.appendChild(this._xAxisLine);
		this._element.appendChild(this._yAxisLine);

		//create axis-labels
		if (chartXAxis) {
			this._xChartAxisLabel = createElementWithClass('div', classnames(css.axisLabel, css.axisLabel_x));
			this._xChartAxisLabelValue = createElementWithClass('div', classnames(css.axisLabelValue));
			const xChartAxisLabelLine = createElementWithClass('div', classnames(css.axis, css.axis_y));
			this._xChartAxisLabel.appendChild(xChartAxisLabelLine);
			this._xChartAxisLabel.appendChild(this._xChartAxisLabelValue);
			if (chartXAxis.parentNode) {
				chartXAxis.parentNode.appendChild(this._xChartAxisLabel);
			}
		}
		if (chartYAxis) {
			this._yChartAxisLabel = createElementWithClass('div', classnames(css.axisLabel, css.axisLabel_y));
			this._yChartAxisLabelValue = createElementWithClass('div', classnames(css.axisLabelValue));
			const yChartAxisLabelLine = createElementWithClass('div', classnames(css.axis, css.axis_x));
			this._yChartAxisLabel.appendChild(yChartAxisLabelLine);
			this._yChartAxisLabel.appendChild(this._yChartAxisLabelValue);
			if (chartYAxis.parentNode) {
				chartYAxis.parentNode.appendChild(this._yChartAxisLabel);
			}
		}

		//create ChartInfo view
		this._chartInfoContainer = createElementWithClass(
			'div',
			classnames(css.chartInfo, {
				[css.chartInfo_fixed]: this._model.isFixed,
				[css.chartInfo_mobile]: this._isMobile,
			}),
		);
		this._chartInfoView = createChartInfoView(this._chartInfoContainer, studySeries, this._model, this._isMobile);
		this._element.appendChild(this._chartInfoContainer);

		//subscribe to model
		this._model.on(E_CROSS_TOOL_PLUGIN_MODEL.CHANGE_IS_FIXED, this._onChangeIsFixed);

		if (this._isDisabled) {
			this._disable();
		} else {
			this._enable();
		}
	}

	/**
	 * Update
	 */
	update(hoverModel: Hover) {
		this._chartInfoView.update(hoverModel);
		this._moveChartInfoView(hoverModel.x);
		this._moveAxises(hoverModel.x, hoverModel.y, hoverModel.id);
		this._updateLabels(
			hoverModel.x,
			hoverModel.y,
			hoverModel.id,
			hoverModel.time,
			hoverModel.model.priceFormatter(hoverModel.yValue),
		);
	}

	/**
	 * Enables view
	 */
	enable() {
		if (this._isDisabled) {
			this._isDisabled = false;
			this._enable();
		}
	}

	/**
	 * @private
	 */
	_enable() {
		this._element.classList.remove(css.disabled);
	}

	/**
	 * Disables view and hides subview
	 */
	disable() {
		if (!this._isDisabled) {
			this._isDisabled = true;
			this._disable();
		}
	}

	/**
	 * @private
	 */
	_disable() {
		this._element.classList.add(css.disabled);
		this.hide();
	}

	/**
	 * Shows view
	 */
	show() {
		if (!this._isVisible) {
			this._isVisible = true;
			this._show();
		}
	}

	/**
	 * @private
	 */
	_show() {
		this._element.classList.add(css.visible);
		if (this._xChartAxisLabel) {
			this._xChartAxisLabel.classList.add(css.axisLabel_visible);
		}
	}

	/**
	 * Hides view
	 */
	hide() {
		if (this._isVisible) {
			this._isVisible = false;
			this._hide();
		}
	}

	/**
	 * @private
	 */
	_hide() {
		this._element.classList.remove(css.visible);
		if (this._xChartAxisLabel) {
			this._xChartAxisLabel.classList.remove(css.axisLabel_visible);
		}
		if (this._yChartAxisLabel) {
			this._yChartAxisLabel.classList.remove(css.axisLabel_visible);
		}
	}

	/**
	 * Notify this view that the chart have been resized.
	 */
	notifyResized() {
		this._chartInfoView.notifyResized();
	}

	/**
	 * Study uiid, empty is candles
	 */
	get uiid() {
		return this._uiid;
	}

	/**
	 * Adds study
	 * @param {StudySeries} studySeries
	 */
	addStudy(studySeries: StudySeries) {
		this._chartInfoView.addStudy(studySeries);
	}

	/**
	 * Removes study
	 * @param {StudySeries} studySeries
	 */
	removeStudy(studySeries: StudySeries) {
		this._chartInfoView.removeStudy(studySeries);
	}

	/**
	 * Teardown
	 */
	close() {
		//unsubscribe from model
		this._model.off(E_CROSS_TOOL_PLUGIN_MODEL.CHANGE_IS_FIXED, this._onChangeIsFixed);

		//close ChartInfo view
		this._chartInfoView.close();

		//clean chart axises
		if (this._xChartAxisLabel && this._xChartAxisLabel.parentNode) {
			this._xChartAxisLabel.parentNode.removeChild(this._xChartAxisLabel);
		}
		if (this._yChartAxisLabel && this._yChartAxisLabel.parentNode) {
			this._yChartAxisLabel.parentNode.removeChild(this._yChartAxisLabel);
		}

		//clean container
		this._container.removeChild(this._element);

		//clean passed references
		delete this['_container'];
		delete this['_model'];
	}

	/////////////////////
	// PRIVATE MEMBERS //
	/////////////////////

	/**
	 * Moves crosstool axis-lines
	 * @param {number} x
	 * @param {number} y
	 * @param {number} uiid
	 * @private
	 */
	_moveAxises(x: number, y: number, uiid: number) {
		this._yAxisLine['style'].transform = `translateX(${x}px)`;
		const notStudyView = !uiid && !this._uiid;
		if (notStudyView || this._uiid === uiid) {
			this._xAxisLine['style'].transform = `translateY(${y}px)`;
		}
		if (notStudyView || this._uiid === uiid) {
			this._xAxisLine.classList.add(css.axis_visible);
		} else {
			this._xAxisLine.classList.remove(css.axis_visible);
		}
	}

	/**
	 * Moves ChartInfoView
	 * @param {Number} x
	 * @private
	 */
	_moveChartInfoView(x: number) {
		if (!this._model.isFixed) {
			this._chartInfoContainer['style'].transform = `translateX(${x}px)`;
			if (x + this._chartInfoContainer.clientWidth >= this._container.clientWidth) {
				if (!this._isAlignedRight) {
					this._isAlignedRight = true;
					this._chartInfoContainer.classList.add(css.chartInfo_alignedRight);
				}
			} else {
				if (this._isAlignedRight) {
					this._isAlignedRight = false;
					this._chartInfoContainer.classList.remove(css.chartInfo_alignedRight);
				}
			}
		}
	}

	/**
	 * Move and update labels
	 * @param {number} x
	 * @param {number} y
	 * @param {number} uiid
	 * @param {string} xValue
	 * @param {string} yValue
	 * @private
	 */
	_updateLabels(x: number, y: number, uiid: number, xValue: string, yValue: string) {
		if (this._xChartAxisLabel) {
			//update x label
			this._xChartAxisLabel['style'].transform = `translateX(${x}px)`;
		}

		if (this._xChartAxisLabelValue) {
			this._xChartAxisLabelValue['textContent'] = xValue;
		}

		const notStudyView = !uiid && !this._uiid;
		if ((notStudyView || this._uiid === uiid) && this._yChartAxisLabel && this._yChartAxisLabelValue) {
			//update y label
			this._yChartAxisLabel.classList.add(css.axisLabel_visible);
			this._yChartAxisLabel['style'].transform = `translateY(${y}px)`;
			this._yChartAxisLabelValue['textContent'] = yValue;
		} else {
			if (this._yChartAxisLabel) {
				this._yChartAxisLabel.classList.remove(css.axisLabel_visible);
			}
		}
	}

	//////////////////////////
	// MODEL EVENT HANDLERS //
	//////////////////////////

	_onChangeIsFixed = (isFixed: boolean) => {
		if (isFixed) {
			this._chartInfoContainer.classList.add(css.chartInfo_fixed);
			this._chartInfoContainer['style'].transform = 'none';
		} else {
			this._chartInfoContainer.classList.remove(css.chartInfo_fixed);
		}
	};
}
