import { Option } from 'fp-ts/lib/Option';
import { number, string, type } from 'io-ts';
import { createOptionFromNullable } from 'io-ts-types';

export type TradeTO = {
	quantity: Option<number>;
	lastDirection: number;
	symbol: string;
	last: Option<number>;
	volume: Option<number>;
	time: number;
};
export const TradeTOIO = type({
	quantity: createOptionFromNullable(number),
	lastDirection: number,
	symbol: string,
	last: createOptionFromNullable(number),
	volume: createOptionFromNullable(number),
	time: number,
});
