import { Sink } from '@devexperts/rx-utils/dist/sink.utils';
import { ask, combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { HistoryOrderStore } from '../../store/history-order/history-order.store';
import { LiveData } from '@devexperts/rx-utils/dist/rd/live-data.utils';
import { mapRD } from '@devexperts/rx-utils/dist/rd/operators/mapRD';
import { tap } from 'rxjs/operators';
import { endOfDay } from 'date-fns';
import { of } from 'rxjs';

export type HistoryDashboardViewModel = {
	historyOrdersCount$: LiveData<Error, number>;
};

type HistoryDashboardViewModelContext = {
	historyOrderStore: HistoryOrderStore;
};

type CreateHistoryDashboardViewModel = () => Sink<HistoryDashboardViewModel>;

export const createHistoryDashboardViewModel = combineContext(
	ask<HistoryDashboardViewModelContext>(),
	(ctx: HistoryDashboardViewModelContext): CreateHistoryDashboardViewModel => {
		const { historyOrderStore } = ctx;

		// TODO: change 1000 to 0, when api will be ready
		const allHistoryRequest$ = of({ startDate: 1000, endDate: endOfDay(Date.now()).getTime() }).pipe(
			tap(({ startDate, endDate }) => historyOrderStore.onDateChange(startDate, endDate)),
		);

		const historyOrdersCount$ = historyOrderStore.allOrders$.pipe(mapRD(items => items.length));

		return () => new Sink({ historyOrdersCount$ }, allHistoryRequest$);
	},
);
