import Hover from '@dx-private/dxchart5/dist/js/chart/model/Hover';
import CrossToolViewModel, { CrossToolConfig, E_CROSS_TOOL_PLUGIN_MODEL } from '../models/cross-tool.view-model';
import View from '../../utils/view';
import { StudySeries } from '../../../chart.model';
export const ERROR_INCORRECT_TEMPLATE = 'ChartInfoView template should return only one top-level child';

export default class ChartInfoView extends View {
	private _container: Element;

	protected _element: Element;

	protected _studySeries: StudySeries | null;

	/**
	 * Used only for initial rendering
	 */
	private _initializedStudySeries: StudySeries[] = [];

	private _title: Element | null = null;

	private _open: Element | null = null;

	private _high: Element | null = null;

	private _low: Element | null = null;

	private _close: Element | null = null;

	private _studies: Element | null = null;

	_model: CrossToolViewModel;

	_lastHoverModel?: Hover;

	constructor(container: Element, studySeries: StudySeries | null, model: CrossToolViewModel) {
		super();
		this._model = model;
		this._container = container;
		this._studySeries = studySeries;
		this._element = this._createElement(this._studySeries);
		this._container.appendChild(this._element);
		this.parse(this._container);
		this._subscribe([
			this._model.on(
				E_CROSS_TOOL_PLUGIN_MODEL.CHANGE_DATETIME_FORMATTER,
				this._onCrossToolPluginModelDateTimeFormatterChange,
			),
		]);
	}

	/**
	 * Teardown
	 */
	close() {
		super.close();
		this._container.removeChild(this._element);
		this._initializedStudySeries = [];
		delete this['_container'];
		delete this['_studySeries'];
		delete this['_model'];
		delete this['_lastHoverModel'];
	}

	/**
	 * Updates view according to hoverModel
	 */
	update(hoverModel: Hover) {
		this._lastHoverModel = hoverModel;
		if (this._studySeries) {
			const currentStudySeries = hoverModel.studiesMap[this._studySeries.uiid];
			if (this._studies) {
				this._studies.innerHTML = this.studiesTemplate(currentStudySeries);
			}

			if (this._title) {
				this._title.textContent = this._getTitle(currentStudySeries);
			}
		} else {
			if (this._title) {
				this._title.textContent = this._model.dateTimeFormatter(hoverModel.time);
			}

			if (this._open) {
				this._open.textContent = hoverModel.open;
			}

			if (this._low) {
				this._low.textContent = hoverModel.low;
			}

			if (this._high) {
				this._high.textContent = hoverModel.high;
			}

			if (this._close) {
				this._close.textContent = hoverModel.close;
			}

			if (this._studies) {
				this._studies.innerHTML = hoverModel.overlays
					.map((overlay: StudySeries) => this.studiesTemplate(overlay))
					.join('');
			}
		}
	}

	/**
	 * Adds study
	 */
	addStudy(studySeries: StudySeries) {
		if (!this._studySeries && studySeries.overlaying) {
			this._initializedStudySeries.push(studySeries);
			if (this._studies) {
				this._studies.innerHTML = this._initializedStudySeries
					.map(study => {
						return this.studiesTemplate(study.conf);
					})
					.join('');
			}
		}
	}

	/**
	 * Removes study
	 */
	removeStudy(studySeries: StudySeries) {
		if (!this._studySeries && studySeries.overlaying) {
			let index = -1;
			this._initializedStudySeries.find((study, i) => {
				const found = study.uiid === studySeries.uiid;
				if (found) {
					index = i;
				}
				return found;
			});
			if (index !== -1) {
				this._initializedStudySeries.splice(index, 1);
			}
			if (this._studies) {
				this._studies.innerHTML = this._initializedStudySeries
					.map(study => {
						return this.studiesTemplate(study.conf);
					})
					.join('');
			}
		}
	}

	/**
	 * Notify this view that the chart have been resized
	 */
	notifyResized() {
		this.onChartResized();
	}

	///////////////
	// PROTECTED //
	///////////////

	/**
	 * Parses initial markup
	 */
	protected parse(element: Element) {
		this._title = element.querySelector('[data-element=chartInfo--title]');
		this._open = element.querySelector('[data-element=chartInfo--open]');
		this._high = element.querySelector('[data-element=chartInfo--high]');
		this._low = element.querySelector('[data-element=chartInfo--low]');
		this._close = element.querySelector('[data-element=chartInfo--close]');
		this._studies = element.querySelector('[data-element=chartInfo--studies]');
	}

	/**
	 * Generates initial markup
	 */
	protected template(studySeries: StudySeries | null) {
		return `
			<div data-element="chartInfo">
				<div data-element="chartInfo--title">
					${studySeries ? this._getTitle(studySeries.conf) : ''}
				</div>
				<div data-element="chartInfo--content">
					${studySeries ? '' : this.candleTemplate()}
					<ul data-element="chartInfo--studies">
						${studySeries ? this.studiesTemplate(studySeries.conf) : ''}
					</ul>
				</div>
			</div>
		`;
	}

	/**
	 * Generates template for study from lines
	 */
	protected studiesTemplate(studySeries: StudySeries) {
		return studySeries.lines
			.map((line, i) => {
				return `
				<li>
					<span>${line.title}</span>
					<span style="color: ${line.colors[line.colorIdx || 0]}">${line.value || ''}</span>
				</li>
			`;
			})
			.join('');
	}

	/**
	 * Generates candle template
	 */
	protected candleTemplate() {
		return `
			<div>
				<span>Open</span>
				<span data-element="chartInfo--open"></span>
			</div>
			<div>
				<span>Close</span>
				<span data-element="chartInfo--close"></span>
			</div>
			<div>
				<span>High</span>
				<span data-element="chartInfo--high"></span>
			</div>
			<div>
				<span>Low</span>
				<span data-element="chartInfo--low"></span>
			</div>
		`;
	}

	/**
	 * This method is invoked when chart was resized.
	 * It doesn't provide implementation and should be overriden in the derived class.
	 * @abstract
	 */
	protected onChartResized() {}

	//////////////////////////
	// MODEL EVENT HANDLERS //
	/////////////////////////

	/**
	 * @param {function(date:Date):String} formatter
	 */
	protected _onCrossToolPluginModelDateTimeFormatterChange = (formatter: CrossToolConfig['dateTimeFormatter']) => {
		if (!this._studySeries && this._lastHoverModel && this._title) {
			this._title.textContent = formatter(this._lastHoverModel.time);
		}
	};

	/////////////
	// PRIVATE //
	/////////////

	/**
	 * Gets title string
	 */
	protected _getTitle(study: StudySeries | null) {
		if (study) {
			const parameters = study.parameters.map(parameter => parameter.value);
			return `${study.title} (${parameters.join(',')})`;
		}
		return '';
	}

	private _createElement(studySeries: StudySeries | null) {
		const dummy = document.createElement('div');
		dummy.innerHTML = this.template(studySeries);
		if (dummy.children.length > 1) {
			throw new Error(ERROR_INCORRECT_TEMPLATE);
		}
		const element = dummy.children[0];
		dummy.removeChild(element);
		return element;
	}
}

export const createChartInfoView = (
	container: Element,
	studySeries: StudySeries | null,
	model: CrossToolViewModel,
	isMobile: boolean,
): ChartInfoView => new ChartInfoView(container, studySeries, model);
