import { InstrumentSuggestTO } from '../../../../api/generated/swagger/definitions/InstrumentSuggestTO';
import { InstrumentSuggestTreeNodeTO } from '../../../../api/generated/swagger/definitions/InstrumentSuggestTreeNodeTO';
import { Suggest, SuggestGroup } from '../../components/symbol-search/symbol-search.model';

export const toSuggest = (suggest: InstrumentSuggestTO, parent: InstrumentSuggestTreeNodeTO): Suggest => ({
	description: suggest.description,
	id: suggest.id.toString(),
	symbol: suggest.name,
	category: parent.name,
});

export const toSuggestGroup = (instrumentGroup: InstrumentSuggestTreeNodeTO): SuggestGroup => ({
	title: instrumentGroup.name,
	values: instrumentGroup.suggests.map(suggests => toSuggest(suggests, instrumentGroup)),
});
