import { withRXWithDefaults } from '../../../../utils/withRXWithDefaults';
import { pending } from '@devexperts/remote-data-ts';
import { WatchlistEntity } from '../../components/watchlist-entity/watchlist-entity.component';
import {
	createWatchlistEntityViewModel,
	WatchlistEntityViewModel,
} from '../../../../view-model/watchlist-entity/watchlist-entity.view-model';
import { runOnMount } from '../../../../utils/react.utils';
import { ask, combineContext, deferContext } from '@devexperts/rx-utils/dist/context.utils';
import { createSelector } from 'reselect';

export type WatchlistEntityContainerContext = {
	watchlistEntityViewModel: WatchlistEntityViewModel;
};

const Container = combineContext(WatchlistEntity, ask<WatchlistEntityContainerContext>(), (WatchlistEntity, e) =>
	withRXWithDefaults(WatchlistEntity)(() => {
		const { watchlistEntityViewModel } = e;

		return {
			props: {
				remoteData: watchlistEntityViewModel.data$,
			},
			defaultProps: {
				remoteData: pending,
				toggleFavorite: watchlistEntityViewModel.toggleFavorite,
			},
		};
	}),
);

export const WatchlistEntityContainer = combineContext(
	deferContext(Container, 'watchlistEntityViewModel'),
	createWatchlistEntityViewModel,
	(Container, createWatchlistEntityViewModel) =>
		runOnMount(Container, props => {
			const data = createSelector(
				props,
				props => props.rowData,
			);
			const vm = createSelector(
				data,
				createWatchlistEntityViewModel,
			);
			return createSelector(
				vm,
				createVM => createVM.map(watchlistEntityViewModel => ({ watchlistEntityViewModel })),
			);
		}),
);
