import * as React from 'react';
import * as cn from 'classnames';
import { memo, useMemo } from 'react';
import { withTranslation, WithTranslation } from 'react-i18next';
import { Option } from 'fp-ts/lib/Option';
import * as O from 'fp-ts/lib/Option';
import { withTheme } from '@devexperts/react-kit/dist/utils/withTheme';
import { TRANSLATION_PLACEHOLDER, tspan } from '../../utils/i18n.utils';
import { SymbolComponent, SymbolProps } from '../symbol/symbol';
import { EditingPositionData, EditingPositionTakeProfitStopLossData } from './editing-position.model';
import { Optional } from '../optional/optional.component';
import { MakeTheme } from '../../utils/theme.utils';
import { formatNumber, formatMoney } from 'accounting';
import {
	TakeProfitStopLossComponent,
	TakeProfitStopLossComponentProps,
} from '../takeprofit-stoploss-panel/takeprofit-stoploss-panel.component';
import {
	TakeProfitStopLossComponentData,
	ProfitLossKind,
} from '../takeprofit-stoploss-panel/takeprofit-stoploss-panel.model';
import { pipe } from 'fp-ts/lib/pipeable';
import { constant } from 'fp-ts/lib/function';
import * as css from './theme/editing-position.module.css';

export type EditingPositionProps = {
	data: Option<EditingPositionData>;
	unitName: Option<string>;
	amount: Option<number>;
	value: Option<number>;
	amountPrecision: number;
	currencySymbol: string;
	profitLoss: Option<number>;
	isShownValidErrors: boolean;
	onValueChange: (data: EditingPositionTakeProfitStopLossData) => void;
	theme: MakeTheme<
		| 'symbol_container'
		| 'symbol_title'
		| 'symbol_subtitle'
		| 'profitAmount'
		| 'profitAmount_positive'
		| 'profitAmount_negative'
		| 'info'
		| 'element'
		| 'element_label'
		| 'element_text'
		| 'profitWrapper'
		| 'profitLabel'
		| 'takeProfitStopLossWrapper'
		| 'divider'
		| 'verticalSpacer'
	> & {
		SymbolComponent?: SymbolProps['theme'];
		TakeProfitStopLoss?: TakeProfitStopLossComponentProps['theme'];
	};
};

const EditingPositionRaw = memo((props: EditingPositionProps & WithTranslation) => {
	const {
		data,
		unitName,
		amount,
		amountPrecision,
		value,
		currencySymbol,
		profitLoss,
		isShownValidErrors,
		theme,
	} = props;

	const { symbol, description } = useMemo(
		() =>
			pipe(
				data,
				O.fold(constant({ symbol: '', description: O.none }), ({ symbol, description }) => ({
					symbol,
					description,
				})),
			),
		[data],
	);

	const profitAmountClassName = useMemo(
		() =>
			cn(theme.profitAmount, {
				[theme.profitAmount_positive as string]: pipe(
					profitLoss,
					O.exists(pl => pl > 0),
				),
				[theme.profitAmount_negative as string]: pipe(
					profitLoss,
					O.exists(pl => pl < 0),
				),
			}),
		[profitLoss],
	);

	const handleValueChange = React.useCallback(
		(data: EditingPositionTakeProfitStopLossData) => (changedValue: TakeProfitStopLossComponentData) => {
			const { takeProfitData, stopLossData } = data;
			const { onValueChange } = props;

			switch (changedValue.kind) {
				case ProfitLossKind.Profit:
					onValueChange({
						takeProfitData: changedValue,
						stopLossData,
					});
					break;
				case ProfitLossKind.Loss:
					onValueChange({
						takeProfitData,
						stopLossData: changedValue,
					});
					break;
			}
		},
		[props.onValueChange],
	);

	return (
		<div>
			<SymbolComponent
				title={symbol}
				subtitle={pipe(
					description,
					O.getOrElse(constant('—')),
				)}
				theme={theme.SymbolComponent}
			/>
			<div className={theme.info}>
				<div className={theme.element}>
					<div className={theme.element_label}>
						{pipe(
							unitName,
							O.map(name =>
								tspan('editingPositionAmount', 'Amount', `${TRANSLATION_PLACEHOLDER}, ${name}`),
							),
							O.toNullable,
						)}
					</div>
					<div className={theme.element_text}>
						<Optional
							value={pipe(
								amount,
								O.map(amount => formatNumber(amount, amountPrecision)),
							)}
						/>
					</div>
				</div>
				<div className={theme.element}>
					<div className={theme.element_label}>{tspan('editingPositionLot', 'Lot')}</div>
					<div className={theme.element_text}>
						<Optional
							value={pipe(
								data,
								O.map(data => formatNumber(data.lot, data.lotsPrecision)),
							)}
						/>
					</div>
				</div>
				<div className={theme.element}>
					<div className={theme.element_label}>
						{tspan(
							'editingPositionMarginImpact',
							'Margin Impact',
							`${TRANSLATION_PLACEHOLDER}, ${currencySymbol}`,
						)}
					</div>
					<div className={theme.element_text}>
						<Optional
							value={pipe(
								data,
								O.map(data =>
									formatMoney(data.marginImpact, {
										format: '%v',
									}),
								),
							)}
						/>
					</div>
				</div>
				<div className={theme.element}>
					<div className={theme.element_label}>
						{tspan('editingPositionValue', 'Value', `${TRANSLATION_PLACEHOLDER}, ${currencySymbol}`)}
					</div>
					<div className={theme.element_text}>
						<Optional
							value={pipe(
								value,
								O.map(value => formatMoney(value, { format: '%v' })),
							)}
						/>
					</div>
				</div>
				<div className={theme.profitWrapper}>
					<span className={theme.profitLabel}>{tspan('editingPositionProfit', 'Profit')}</span>
					<span className={profitAmountClassName}>
						<Optional
							value={pipe(
								profitLoss,
								O.map(profit =>
									formatMoney(profit, {
										symbol: currencySymbol,
										format: '%v%s',
									}),
								),
							)}
						/>
					</span>
				</div>
			</div>
			{pipe(
				data,
				O.map(data => {
					const { takeProfitData, stopLossData } = data;
					return (
						<React.Fragment>
							<div className={theme.divider} />
							<div className={theme.takeProfitStopLossWrapper}>
								<TakeProfitStopLossComponent
									onValueChange={handleValueChange({
										takeProfitData,
										stopLossData,
									})}
									data={takeProfitData}
									isShownValidErrors={isShownValidErrors}
									currencySymbol={currencySymbol}
									theme={theme.TakeProfitStopLoss}
								/>
							</div>
							<div className={theme.divider} />
							<div className={theme.takeProfitStopLossWrapper}>
								<TakeProfitStopLossComponent
									onValueChange={handleValueChange({
										takeProfitData,
										stopLossData,
									})}
									data={stopLossData}
									isShownValidErrors={isShownValidErrors}
									currencySymbol={currencySymbol}
									theme={theme.TakeProfitStopLoss}
								/>
							</div>
							<div className={theme.divider} />
						</React.Fragment>
					);
				}),
				O.toNullable,
			)}
			<div className={theme.verticalSpacer} />
		</div>
	);
});

const wTheme = {
	...css,
};

export const EditingPosition = withTranslation()(withTheme(Symbol(), wTheme)(EditingPositionRaw));
