import * as React from 'react';
import {
	Button as DXCButton,
	BUTTON,
	TFullButtonProps as TDXCFullButtonProps,
	TButtonProps as TDXCButtonProps,
} from '@devexperts/react-kit/dist/components/Button/Button';
import { Component } from 'react';
import { Omit } from 'typelevel-ts';
import { PartialKeys } from '@devexperts/utils/dist/object/object';
import { withTheme } from '@devexperts/react-kit/dist/utils/withTheme';

export type TFullButtonProps = Omit<TDXCButtonProps, 'theme'> & {
	isExtra?: boolean;
	theme: {
		inner?: string;
		container_cta?: string;
	} & TDXCFullButtonProps['theme'];
};

class RawButton extends Component<TFullButtonProps> {
	render() {
		const { theme, children, ...restProps } = this.props;

		return (
			<DXCButton {...restProps} theme={theme}>
				<span className={theme.inner}>{children}</span>
			</DXCButton>
		);
	}
}

export type TButtonProps = PartialKeys<TFullButtonProps, 'theme'>;
export const Button = withTheme(BUTTON)(RawButton);
