import { combineContext, deferContext, fromReader, ask, ContextEnvType } from '@devexperts/rx-utils/dist/context.utils';
import { ask as askReader } from 'fp-ts/lib/Reader';
import * as Reader from 'fp-ts/lib/Reader';
import { createEnvViewModel, EnvViewModel } from '../../../view-model/env/env.view-model';
import { EnvComponent } from '../components/env.component';
import { pending } from '@devexperts/remote-data-ts';
import { runOnMount } from '../../../utils/react.utils';
import { avasystemsettingsinfoController } from '../../../api/generated/swagger/controllers/AvasystemsettingsinfoController';
import { ConnectionContext } from '../../app/containers/app/app.container';
import { createApiClient } from '../../../api/api-client';
import { pipe } from 'fp-ts/lib/pipeable';
import { withRXWithDefaults } from '../../../utils/withRXWithDefaults';

type EnvContainerContext = {
	envViewModel: EnvViewModel;
};

const Container = combineContext(EnvComponent, ask<EnvContainerContext>(), (EnvComponent, e) =>
	withRXWithDefaults(EnvComponent)(() => ({
		defaultProps: { initialize: pending },
		props: { initialize: e.envViewModel.initialize$ },
	})),
);

const EnvContainerInstanceVM = combineContext(
	deferContext(Container, 'envViewModel'),
	createEnvViewModel,
	(Container, createEnvViewModel) => runOnMount(Container, () => () => ({ envViewModel: createEnvViewModel() })),
);

const EnvContainerWithApiClient = combineContext(
	deferContext(EnvContainerInstanceVM, 'avasystemsettingsinfoController'),
	deferContext(fromReader(avasystemsettingsinfoController), 'apiClient'),
	ask<ConnectionContext>(),
	(EnvContainerInstanceVM, avasystemsettingsinfoController, e) => {
		const apiClient = createApiClient(e.restURL);
		return runOnMount(EnvContainerInstanceVM, () => () =>
			avasystemsettingsinfoController
				.map(avasystemsettingsinfoController => ({ avasystemsettingsinfoController }))
				.run({ apiClient }),
		);
	},
);

//asks for the environment and runs all effects inside App subtree on its mount
export const EnvContainer = pipe(
	askReader<ContextEnvType<typeof EnvContainerWithApiClient>>(),
	Reader.map(e => runOnMount(EnvContainerWithApiClient, () => () => e)),
);
