import * as t from 'io-ts';
import {
	QUOTE_SUBSCRIPTION_INITIALIZED,
	QuoteSubscriptionInitializedMessageTO,
	quoteSubscriptionInitializedMessageTO,
} from './to/quote-subscription-initialized-message.to';
import { QUOTE, quoteMessageTO, QuoteMessageTO } from './to/quote-message.to';
import { SUMMARY, summaryMessageTO, SummaryMessageTO } from './to/summary-message.to';
import { ACCOUNT_METRICS, accountMetricsMessageTO, AccountMetricsMessageTO } from './to/account-metrics-message.to';
import { CHART, ChartMessageTO, chartMessageTO } from './to/chart-message.to';
import { instrumentMessageTO, InstrumentMessageTO, INSTRUMENTS } from './to/instrument-message.to';
import { ACCOUNTS, accountsMessageTO, AccountsMessageTO } from './to/accounts-message.to';
import { POSITIONS, PositionMessageTO, positionMessageTOIO } from './to/position-message.to';
import {
	POSITION_METRICS,
	FxPositionMetricsMessageTo,
	positionMetricsMessageTOIO,
} from './to/fx-position-metrics-message.to';
import { AVAILABLE_WATCHLISTS, WatchlistMessageTO, watchlistMessageTOIO } from './to/watchlist-message.to';
import {
	WATCHLIST_SUBSCRIPTION_INITIALIZED,
	watchlistSubscriptionInitializedMessageTO,
	WatchlistSubscriptionInitializedMessageTO,
} from './to/watchlist-subbscription-initialized-message.to';
import {
	watchlistContentMessageTO,
	WatchlistContentMessageTO,
	WATCHLISTS_CONTENT,
} from './to/watchlist-content-message.to';
import {
	GLOBAL_CHART_SETTINGS,
	globalChartSettingsMessageTO,
	GlobalChartSettingsMessageTO,
} from './to/global-chart-settings-message.to';
import { CONVERSION_RATE, ConversionRateMessageTO, conversionRateMessageTO } from './to/conversion-rate-message.to';
import { MARGIN_IMPACT, MarginImpactMessageTO, marginImpactsMessageTOIO } from './to/margin-impacts-message.to';
import { UserInfoMessageTO, userInfoMessageTO, USER_LOGIN_INFO } from './to/user-message.to';
import { ORDERS, OrdersMessageTO, ordersMessageTOIO } from './to/orders-message.to';
import { HISTORY_ORDER, HistoryOrderMessageTO, historyOrderMessageTOIO } from './to/history-order-message.to';
import { ACCOUNT_SWITCHED, AccountSwitchedMessageTO, accountSwitchedMessageTO } from './to/account-switched-message.to';
import { AVA_TOKEN, AvaTokenMessageTO, avaTokenMessageTO } from './to/ava-token-message.to';
import {
	avaSystemSettingsMessageTOIO,
	AVA_SYSTEM_SETTINGS,
	AvaSystemSettingsMessageTO,
} from './to/ava-system-settings-message.to';
import {
	AccountSettingsMessageTO,
	ACCOUNT_SETTINGS,
	accountSettingsMessageTOIO,
} from './to/account-settings-message.to';
import { LIMITS, LimitsDomainMessageTO, limitsDomainMessageTOIO } from './to/limits-domain-message.to';
import {
	AVA_INSTRUMENT_INFO,
	AvaInstrumentInfoMessageTO,
	avaInstrumentInfoMessageTOIO,
} from './to/ava-instrument-info-message.to';
import {
	AVA_WATCH_LIST_INSTRUMENT_INFO,
	AvaWatchListInstrumentInfoMessageTO,
	AvaWatchListInstrumentInfoMessageTOIO,
} from './to/ava-watchlist-instrument-info-message.to';
import { LOGOUT_EVENT, LogoutEventMessageTO, logoutEvenyMessageTO } from './to/logout-event-message.to';

export type SocketMessageTO =
	| QuoteSubscriptionInitializedMessageTO
	| QuoteMessageTO
	| SummaryMessageTO
	| ConversionRateMessageTO
	| AvaTokenMessageTO
	| AccountMetricsMessageTO
	| ChartMessageTO
	| InstrumentMessageTO
	| AccountsMessageTO
	| AccountSwitchedMessageTO
	| OrdersMessageTO
	| MarginImpactMessageTO
	| PositionMessageTO
	| FxPositionMetricsMessageTo
	| WatchlistMessageTO
	| WatchlistSubscriptionInitializedMessageTO
	| WatchlistContentMessageTO
	| GlobalChartSettingsMessageTO
	| UserInfoMessageTO
	| HistoryOrderMessageTO
	| LimitsDomainMessageTO
	| AvaSystemSettingsMessageTO
	| AccountSettingsMessageTO
	| AvaInstrumentInfoMessageTO
	| AvaWatchListInstrumentInfoMessageTO
	| LogoutEventMessageTO;

export const socketMessageTO: t.Tagged<'type', SocketMessageTO, unknown> = t.taggedUnion('type', [
	quoteSubscriptionInitializedMessageTO,
	quoteMessageTO,
	summaryMessageTO,
	conversionRateMessageTO,
	chartMessageTO,
	avaTokenMessageTO,
	accountMetricsMessageTO,
	instrumentMessageTO,
	accountsMessageTO,
	accountSwitchedMessageTO,
	ordersMessageTOIO,
	positionMessageTOIO,
	positionMetricsMessageTOIO,
	marginImpactsMessageTOIO,
	watchlistMessageTOIO,
	watchlistSubscriptionInitializedMessageTO,
	watchlistContentMessageTO,
	globalChartSettingsMessageTO,
	userInfoMessageTO,
	historyOrderMessageTOIO,
	limitsDomainMessageTOIO,
	avaSystemSettingsMessageTOIO,
	accountSettingsMessageTOIO,
	avaInstrumentInfoMessageTOIO,
	AvaWatchListInstrumentInfoMessageTOIO,
	logoutEvenyMessageTO,
]);

export const isQuoteSubscriptionInitializedTO = (to: SocketMessageTO): to is QuoteSubscriptionInitializedMessageTO =>
	to.type === QUOTE_SUBSCRIPTION_INITIALIZED;

export const isQuoteTO = (to: SocketMessageTO): to is QuoteMessageTO => to.type === QUOTE;

export const isSummaryTO = (to: SocketMessageTO): to is SummaryMessageTO => to.type === SUMMARY;

export const isConversionRateTO = (to: SocketMessageTO): to is ConversionRateMessageTO => to.type === CONVERSION_RATE;

export const isChartTO = (to: SocketMessageTO): to is ChartMessageTO => to.type === CHART;

export const isAvaTokenTO = (to: SocketMessageTO): to is AvaTokenMessageTO => to.type === AVA_TOKEN;

export const isAccountMetricsTO = (to: SocketMessageTO): to is AccountMetricsMessageTO => to.type === ACCOUNT_METRICS;

export const isInstrumentTO = (to: SocketMessageTO): to is InstrumentMessageTO => to.type === INSTRUMENTS;

export const isAccountsTO = (to: SocketMessageTO): to is AccountsMessageTO => to.type === ACCOUNTS;

export const isAccountSwitchedTO = (to: SocketMessageTO): to is AccountSwitchedMessageTO =>
	to.type === ACCOUNT_SWITCHED;

export const isOrdersTO = (to: SocketMessageTO): to is OrdersMessageTO => to.type === ORDERS;

export const isLimitsTO = (to: SocketMessageTO): to is LimitsDomainMessageTO => to.type === LIMITS;

export const isHistoryOrderTO = (to: SocketMessageTO): to is HistoryOrderMessageTO => to.type === HISTORY_ORDER;

export const isPositionTO = (to: SocketMessageTO): to is PositionMessageTO => to.type === POSITIONS;

export const isPositionMetricsTO = (to: SocketMessageTO): to is FxPositionMetricsMessageTo =>
	to.type === POSITION_METRICS;

export const isWatchlistTO = (to: SocketMessageTO): to is WatchlistMessageTO => to.type === AVAILABLE_WATCHLISTS;

export const isMarginImpactTO = (to: SocketMessageTO): to is MarginImpactMessageTO => to.type === MARGIN_IMPACT;

export const isWatchlistSubscriptionInitializedTO = (
	to: SocketMessageTO,
): to is WatchlistSubscriptionInitializedMessageTO => to.type === WATCHLIST_SUBSCRIPTION_INITIALIZED;

export const isWatchlistContentTO = (to: SocketMessageTO): to is WatchlistContentMessageTO =>
	to.type === WATCHLISTS_CONTENT;

export const isGlobalChartSettingsTO = (to: SocketMessageTO): to is GlobalChartSettingsMessageTO =>
	to.type === GLOBAL_CHART_SETTINGS;

export const isUserInfoTO = (to: SocketMessageTO): to is UserInfoMessageTO => to.type === USER_LOGIN_INFO;

export const isAvaSystemSettingsTO = (to: SocketMessageTO): to is AvaSystemSettingsMessageTO =>
	to.type === AVA_SYSTEM_SETTINGS;

export const isAccountSettingsTO = (to: SocketMessageTO): to is AccountSettingsMessageTO =>
	to.type === ACCOUNT_SETTINGS;

export const isAvaInstrumentInfoTO = (to: SocketMessageTO): to is AvaInstrumentInfoMessageTO =>
	to.type === AVA_INSTRUMENT_INFO;

export const isAvaWatchListInstrumentInfoTO = (to: SocketMessageTO): to is AvaWatchListInstrumentInfoMessageTO =>
	to.type === AVA_WATCH_LIST_INSTRUMENT_INFO;

export const isLogoutEventTO = (to: SocketMessageTO): to is LogoutEventMessageTO => to.type === LOGOUT_EVENT;
