import * as React from 'react';
import { Component } from 'react';
import { RegistrationButton } from './registration-button.component';
import { withTranslation, WithTranslation } from 'react-i18next';
import { PURE } from '@devexperts/react-kit/dist/utils/pure';
import { withTheme } from '@devexperts/react-kit/dist/utils/withTheme';
import { InfoBox, InfoBoxProps } from '../../../../../ui-common/info-box/info-box.component';

import * as registrationButtonCss from './theme/registration-button.module.css';
import * as css from './theme/info-box-registration.module.css';

export type RegistrationInfoBoxProps = WithTranslation & Pick<InfoBoxProps, 'theme' | 'text'>;

@PURE
class RegistrationInfoBoxRaw extends Component<RegistrationInfoBoxProps> {
	render() {
		const { theme, text } = this.props;
		return (
			<InfoBox
				text={text}
				button={<RegistrationButton theme={registrationButtonCss} />}
				theme={theme}
				hides={false}
			/>
		);
	}
}

export const RegistrationInfoBox = withTheme(Symbol(), css)(withTranslation()(RegistrationInfoBoxRaw));
