import { ResponseValidationError, TAPIClient } from '../client/client';
import { FxPositionMetricsTO, FxPositionMetricsTOIO } from '../definitions/FxPositionMetricsTO';
import { OrderTemplateTO, OrderTemplateTOIO } from '../definitions/OrderTemplateTO';
import { PositionAttachProtectionsTO, PositionAttachProtectionsTOIO } from '../definitions/PositionAttachProtectionsTO';
import { PositionCloseByTO, PositionCloseByTOIO } from '../definitions/PositionCloseByTO';
import { PositionTO, PositionTOIO } from '../definitions/PositionTO';
import { fromEither } from '@devexperts/remote-data-ts';
import { LiveData } from '@devexperts/rx-utils/dist/rd/live-data.utils';
import { asks } from 'fp-ts/lib/Reader';
import { array, string, type, partial, void as tvoid, number } from 'io-ts';
import { map } from 'rxjs/operators';

export type PositionsController = {
	/**
	 * Get list of existing positions for specified accounts
	 * @param { object } parameters
	 */
	readonly getPositions: (parameters: { query: { accountId: Array<string> } }) => LiveData<Error, Array<PositionTO>>;

	/**
	 * Close position with closing order
	 * @param { object } [parameters]
	 */
	readonly closePosition: (parameters: { body?: OrderTemplateTO }) => LiveData<Error, void>;

	/**
	 * Close one position using another one
	 * @param { object } [parameters]
	 */
	readonly closeBy: (parameters: { body?: PositionCloseByTO }) => LiveData<Error, void>;

	/**
	 * Close all positions
	 */
	readonly closeAllPositions: () => LiveData<Error, void>;

	/**
	 * Get FX position metrics
	 * @param { object } parameters
	 */
	readonly getPositionMetrics: (parameters: {
		query: { accountId: string };
	}) => LiveData<Error, Array<FxPositionMetricsTO>>;

	/**
	 * Modify protection orders for position
	 * @param { object } [parameters]
	 */
	readonly modifyProtectionOrders: (parameters: { body?: PositionAttachProtectionsTO }) => LiveData<Error, void>;

	/**
	 * Place protection orders for position
	 * @param { object } [parameters]
	 */
	readonly placeProtectionOrders: (parameters: { body?: PositionAttachProtectionsTO }) => LiveData<Error, void>;

	/**
	 * Close all positions existing for the specified instrument
	 * @param { number } instrumentId - Instrument id
	 */
	readonly closeAllPositionsForInstrument: (instrumentId: number) => LiveData<Error, void>;
};

export const positionsController = asks(
	(e: { apiClient: TAPIClient }): PositionsController => ({
		getPositions: parameters => {
			const encoded = partial({ query: type({ accountId: array(string) }) }).encode(parameters);

			return e.apiClient
				.request({
					url: `/positions`,
					method: 'GET',
					query: encoded.query,
				})
				.pipe(
					map(data =>
						data.chain(value =>
							fromEither(
								array(PositionTOIO)
									.decode(value)
									.mapLeft(ResponseValidationError.create),
							),
						),
					),
				);
		},

		closePosition: parameters => {
			const encoded = partial({ body: OrderTemplateTOIO }).encode(parameters);

			return e.apiClient
				.request({
					url: `/positions/close`,
					method: 'POST',

					body: encoded.body,
				})
				.pipe(
					map(data =>
						data.chain(value => fromEither(tvoid.decode(value).mapLeft(ResponseValidationError.create))),
					),
				);
		},

		closeBy: parameters => {
			const encoded = partial({ body: PositionCloseByTOIO }).encode(parameters);

			return e.apiClient
				.request({
					url: `/positions/closeby`,
					method: 'POST',

					body: encoded.body,
				})
				.pipe(
					map(data =>
						data.chain(value => fromEither(tvoid.decode(value).mapLeft(ResponseValidationError.create))),
					),
				);
		},

		closeAllPositions: () => {
			return e.apiClient
				.request({
					url: `/positions/liquidate`,
					method: 'POST',
				})
				.pipe(
					map(data =>
						data.chain(value => fromEither(tvoid.decode(value).mapLeft(ResponseValidationError.create))),
					),
				);
		},

		getPositionMetrics: parameters => {
			const encoded = partial({ query: type({ accountId: string }) }).encode(parameters);

			return e.apiClient
				.request({
					url: `/positions/metrics`,
					method: 'GET',
					query: encoded.query,
				})
				.pipe(
					map(data =>
						data.chain(value =>
							fromEither(
								array(FxPositionMetricsTOIO)
									.decode(value)
									.mapLeft(ResponseValidationError.create),
							),
						),
					),
				);
		},

		modifyProtectionOrders: parameters => {
			const encoded = partial({ body: PositionAttachProtectionsTOIO }).encode(parameters);

			return e.apiClient
				.request({
					url: `/positions/protections`,
					method: 'PUT',

					body: encoded.body,
				})
				.pipe(
					map(data =>
						data.chain(value => fromEither(tvoid.decode(value).mapLeft(ResponseValidationError.create))),
					),
				);
		},

		placeProtectionOrders: parameters => {
			const encoded = partial({ body: PositionAttachProtectionsTOIO }).encode(parameters);

			return e.apiClient
				.request({
					url: `/positions/protections`,
					method: 'POST',

					body: encoded.body,
				})
				.pipe(
					map(data =>
						data.chain(value => fromEither(tvoid.decode(value).mapLeft(ResponseValidationError.create))),
					),
				);
		},

		closeAllPositionsForInstrument: instrumentId => {
			return e.apiClient
				.request({
					url: `/positions/${encodeURIComponent(number.encode(instrumentId).toString())}/liquidate`,
					method: 'POST',
				})
				.pipe(
					map(data =>
						data.chain(value => fromEither(tvoid.decode(value).mapLeft(ResponseValidationError.create))),
					),
				);
		},
	}),
);
