import * as React from 'react';
import { ReactNode } from 'react';
import { withTheme } from '@devexperts/react-kit/dist/utils/withTheme';
import { PURE } from '@devexperts/react-kit/dist/utils/pure';
import { MakeTheme } from '../../utils/theme.utils';
import * as css from './theme/symbol.module.css';

export type SymbolProps = {
	title: ReactNode;
	subtitle?: ReactNode;
	theme: MakeTheme<'container' | 'title' | 'subtitle'>;
};

@PURE
class RawSymbolComponent extends React.Component<SymbolProps> {
	render() {
		const { title, subtitle, theme } = this.props;
		return (
			<div className={theme.container}>
				<div className={theme.title}>{title}</div>
				{subtitle && <div className={theme.subtitle}>{subtitle}</div>}
			</div>
		);
	}
}

const wTheme = { ...css };

export const SymbolComponent = withTheme(Symbol(), wTheme)(RawSymbolComponent);
