import * as React from 'react';
import { PureComponent, Fragment } from 'react';
import { withTranslation, WithTranslation } from 'react-i18next';
import { head } from 'fp-ts/lib/Array';
import * as O from 'fp-ts/lib/Option';
import { RemoteData } from '@devexperts/remote-data-ts';
import { ListChildComponentProps } from 'react-window';
import { AutosizedTable as Table } from '../../../../ui-common/autosized-table-ava/autosized-table.component';
import { ConfigItem, TableSize, TableData, WatchlistTableColumnNames } from './watchlist-table.model';
import { RemoteDataState } from '../../../../ui-common/remote-data-state/remote-data-state.component';
import { combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { WatchlistEntityContainer } from '../../containers/watchlist-entity/watchlist-entity.container';
import { routes } from '../../../../ui-common/routes';
import { Redirect } from '../../../../ui-common/redirect/containers/redirect.container';
import { Route } from '../../../../ui-common/route/containers/route.container';
import { getColumnsConfig } from './watchlist-table.utils';
import { tspan } from '../../../../utils/i18n.utils';
import { pipe } from 'fp-ts/lib/pipeable';

export type TWatchlistTableProps = {
	data: RemoteData<Error, TableData>;
	config: ConfigItem[];
	categoryId: string;
	tableSize: TableSize;
};

export const WatchlistTable = combineContext(
	Route,
	Redirect,
	WatchlistEntityContainer,
	(Route, Redirect, WatchlistEntityContainer) =>
		withTranslation()(
			class WatchlistTable extends PureComponent<TWatchlistTableProps & WithTranslation> {
				private getTranslatedColumnNames = (): WatchlistTableColumnNames => ({
					symbol: tspan('watchlistTableSymbol', 'Symbol'),
					sell: tspan('watchlistTableSell', 'Sell'),
					buy: tspan('watchlistTableBuy', 'Buy'),
					change: tspan('watchlistTableChange', 'Change, %'),
				});

				render() {
					const { data } = this.props;

					return <RemoteDataState data={data} success={this.renderSuccess} />;
				}

				private renderSuccess = (data: TableData): JSX.Element => {
					return (
						<Fragment>
							<Route match={routes.trade.category.symbol.root} render={this.renderTable(data)} />
							<Route exact match={routes.trade.category.root} render={this.redirectToInstrument(data)} />
						</Fragment>
					);
				};

				private getItemKey = (rowIndex: number, data: string[]) => data[rowIndex];

				private renderTable = ({ symbols, positionId }: TableData) => () => {
					const { config } = this.props;

					const columnNames = this.getTranslatedColumnNames();

					// TODO: Use constant for itemSize
					return (
						<Table
							itemCount={symbols.length}
							itemSize={60}
							itemData={symbols}
							itemKey={this.getItemKey}
							scrollToItem={O.toUndefined(positionId)}
							columns={getColumnsConfig(config, columnNames)}>
							{this.renderRow}
						</Table>
					);
				};

				private renderRow = (props: ListChildComponentProps) => {
					return (
						<WatchlistEntityContainer
							config={this.props.config}
							categoryId={this.props.categoryId}
							tableSize={this.props.tableSize}
							rowData={props.data[props.index]}
							{...props}
						/>
					);
				};

				private redirectToInstrument = (data: TableData) => () => {
					const { categoryId } = this.props;
					const firstSymbol = head(data.symbols);
					return pipe(
						firstSymbol,
						O.fold(
							() => this.renderTable(data),
							symbol => () => (
								<Redirect to={routes.trade.category.symbol.root} value={{ categoryId, symbol }} />
							),
						),
					)();
				};
			},
		),
);
