import * as React from 'react';
import { TableCellAlign, TableCellProps, TableConfigItem } from './table.model';
import { TableCell } from './table-cell.component';
import { findFirst } from 'fp-ts/lib/Array';
import * as O from 'fp-ts/lib/Option';
import { pipe } from 'fp-ts/lib/pipeable';
import { constant } from 'fp-ts/lib/function';

const defaultColumnWidth = 150;
const TABLE_HEADER_HEIGHT = 24;

export function renderCell(align: TableCellAlign) {
	return function renderCell<A>(props: TableCellProps<A>) {
		const row = props.rowData[props.dataKey];
		return <TableCell align={align}>{row}</TableCell>;
	};
}

export const getWidthFromConfig = (config: TableConfigItem[], dataKey: string): number =>
	pipe(
		config,
		findFirst(item => item.dataKey === dataKey),
		O.fold(constant(defaultColumnWidth), value => value.width),
	);

export const getVerticalOffset = (itemIndex: number, itemSize: number, parentElem: HTMLDivElement) => {
	const verticalOffset = itemIndex * itemSize;
	const parentOffset = parentElem.scrollTop + parentElem.offsetHeight;
	const offsetToLastWholeItem = parentOffset - (parentOffset % itemSize);
	// if the user selects a row that does not completely fit into the table
	if (verticalOffset >= offsetToLastWholeItem && verticalOffset <= offsetToLastWholeItem + itemSize) {
		const elementsOnScreen = Math.trunc(parentElem.offsetHeight / itemSize);
		const elementsOnScreenHeight = elementsOnScreen * itemSize;
		const gap = parentElem.offsetHeight - elementsOnScreenHeight;
		return O.some(parentElem.scrollTop + gap + TABLE_HEADER_HEIGHT);
	}
	// works for search
	if (verticalOffset > parentOffset || verticalOffset < parentElem.scrollTop) {
		return O.some(verticalOffset);
	}
	// if the user selects a row that fits entirely into the table
	return O.none;
};
