import { createElement } from 'react';
import { render } from 'react-dom';
import { IO } from 'fp-ts/lib/IO';
import { EnvContainer } from './ui-desktop/env/containers/env.container';

const env = {
	restURL: '/api',
	socketURL: '/client/connector',
};

export const app = new IO<void>(() =>
	render(createElement(EnvContainer.run(env)), document.getElementById('root')),
).run();
