import * as React from 'react';
import { Component, ComponentClass, ReactNode } from 'react';
import * as classnames from 'classnames';
import { PURE } from '@devexperts/react-kit/dist/utils/pure';
import { Button } from '../../../../../ui-common/button/button.component';
import { withTheme } from '@devexperts/react-kit/dist/utils/withTheme';
import {
	TFullSelectboxAnchorProps,
	TSelectboxAnchorProps,
} from '@devexperts/react-kit/dist/components/Selectbox/SelectboxAnchor';
import { CrossToolMode } from '../toolbar.model';
import { NoCrosshairIcon } from '../../../../ui-kit/components/icons/no-crosshair.icon';
import { AimIcon } from '../../../../ui-kit/components/icons/aim.icon';
import { toMode } from '../toolbar.utils';

@PURE
class RawCrossToolAnchor extends Component<TFullSelectboxAnchorProps> {
	render() {
		const { theme, children, valueText, caretIcon, onClick, isOpened } = this.props;

		const buttonTheme = {
			container: classnames(theme.container, {
				[theme.container_isOpened as string]: isOpened,
			}),
		};

		return (
			<Button onClick={onClick} theme={buttonTheme}>
				<div className={theme.content}>
					<div className={theme.text}>{this.renderCrossToolIcon(valueText)}</div>
					<div className={theme.caret}>{caretIcon}</div>
				</div>
				{children}
			</Button>
		);
	}

	private renderCrossToolIcon = (valueText: ReactNode) => {
		if (valueText !== undefined && valueText !== null) {
			switch (toMode(valueText[1])) {
				case CrossToolMode.Disabled:
					return <NoCrosshairIcon />;
				case CrossToolMode.Crosshair:
					return <AimIcon />;
			}
		}
		return null;
	};
}

export const CrossToolAnchor: ComponentClass<TSelectboxAnchorProps> = withTheme(Symbol())(RawCrossToolAnchor);
