import { EditingPositionData as EditingPositionInputData } from '../../../../view-model/positions/editing-position.view-model';
import { EditingPositionData as EditingPositionOutputData } from '../../../../ui-common/editing-position/editing-position.model';
import { pipe } from 'fp-ts/lib/pipeable';
import { constant } from 'fp-ts/lib/function';
import * as E from 'fp-ts/lib/Either';

export const toEditingPositionData = (value: EditingPositionInputData): EditingPositionOutputData => ({
	symbol: value.symbol,
	description: value.description,
	lot: value.lot,
	marginImpact: pipe(
		value.marginImpact,
		E.getOrElse(constant(0)),
	),
	takeProfitData: value.takeProfitData,
	stopLossData: value.stopLossData,
	lotsPrecision: value.lotsPrecision,
});
