import { AccountTO } from '../../api/socket/to/accounts-message.to';
import { Mode } from './menu.view-model';
import { fromArray, NonEmptyArray } from 'fp-ts/lib/NonEmptyArray';
import * as O from 'fp-ts/lib/Option';
import { pipe } from 'fp-ts/lib/pipeable';

export const makeAccountUrl = (accountId: string, lang: string) =>
	`/api/accounts/switch/redirect?accountid=${accountId}&lang=${lang}`;

const isTwoAccountsWithDifferentStatus = (current: AccountTO, accounts: NonEmptyArray<AccountTO>): boolean =>
	accounts.tail.length === 0 && current.paperMoney !== accounts.head.paperMoney;

const getCurrentAccountMode = (account: AccountTO): Mode => (account.paperMoney ? Mode.Demo : Mode.Real);

export const chooseMode = (current: AccountTO, available: AccountTO[]): Mode => {
	const accountsExcludingCurrent = fromArray(available.filter(available => available.id !== current.id));

	return pipe(
		accountsExcludingCurrent,
		O.fold(
			() => getCurrentAccountMode(current),
			accountsExcludingCurrent => {
				if (isTwoAccountsWithDifferentStatus(current, accountsExcludingCurrent)) {
					return Mode.DemoLive;
				}
				return Mode.Regular;
			},
		),
	);
};
