import * as React from 'react';
import { memo } from 'react';
import { withTranslation, WithTranslation } from 'react-i18next';
import { fold, Option } from 'fp-ts/lib/Option';
import { combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { EmptyScreenContainer } from '../../../ui-desktop/empty-screen/containers/empty-screen.container';
import { Preloader } from '../../../ui-desktop/ui-kit/components/preloader/preloader.component';
import { ScreenType } from '../../empty-screen/components/empty-screen.component';
import { Iframe } from '../../iframe/iframe.component';
import { pipe } from 'fp-ts/lib/pipeable';

export type DepositPageProps = WithTranslation & {
	previewMode: Option<boolean>;
};

export const DepositPage = combineContext(EmptyScreenContainer, EmptyScreenContainer =>
	withTranslation()(
		memo<DepositPageProps>(props =>
			pipe(
				props.previewMode,
				fold(
					() => <Preloader />,
					previewMode =>
						previewMode ? (
							<EmptyScreenContainer screenType={ScreenType.Deposit} />
						) : (
							<Iframe src={`/api/avacrm/withdrawal?lang=${props.i18n.language}`} />
						),
				),
			),
		),
	),
);
