import { withRXWithDefaults } from '../../../../utils/withRXWithDefaults';
import { ask, combineContext, deferContext } from '@devexperts/rx-utils/dist/context.utils';
import { MarginPopup } from '../../components/margin-popup/margin-popup.component';
import { MarginViewModel, createMarginViewModel } from '../../../../view-model/margin/margin.view-model';
import { runOnMount } from '../../../../utils/react.utils';

type MarginPopupContainerContext = {
	marginViewModel: MarginViewModel;
};

const Container = combineContext(MarginPopup, ask<MarginPopupContainerContext>(), (MarginPopup, e) =>
	withRXWithDefaults(MarginPopup)(() => {
		const { shouldOpenPopup$, onClose } = e.marginViewModel;

		return {
			props: {
				isPopupOpened: shouldOpenPopup$,
			},
			defaultProps: {
				isPopupOpened: false,
				onClose,
			},
		};
	}),
);

export const MarginPopupContainer = combineContext(
	deferContext(Container, 'marginViewModel'),
	createMarginViewModel,
	(Container, createMarginViewModel) =>
		runOnMount(Container, () => () => ({
			marginViewModel: createMarginViewModel(),
		})),
);
