import { combineLatest } from 'rxjs';
import { first, map } from 'rxjs/internal/operators';
import * as O from 'fp-ts/lib/Option';
import { withRXWithDefaults } from '../../../../../utils/withRXWithDefaults';
import { AccountTO } from '../../../../../api/socket/to/accounts-message.to';
import { ask, combineContext, deferContext } from '@devexperts/rx-utils/dist/context.utils';
import { createMenuViewModel, MenuViewModel } from '../../../../../view-model/menu/menu.view-model';
import { AccountViewModel } from '../../../../../view-model/account/account.view-model';
import { runOnMount } from '../../../../../utils/react.utils';
import { Mode, MenuAccountData } from '../components/menu/menu.model';
import { Menu } from '../components/menu/menu.component';
import { currencyStringToSymbol } from '../../../../../utils/currency.utils';

const fromModelToData = (data: AccountTO): MenuAccountData => {
	return {
		currency: currencyStringToSymbol(data.baseCurrency),
		accountId: data.id,
		status: data.paperMoney ? Mode.Demo : Mode.Real,
	};
};

export type MenuContainerContext = {
	menuViewModel: MenuViewModel;
	accountViewModel: AccountViewModel;
};

export const Container = combineContext(Menu, ask<MenuContainerContext>(), (Menu, e) =>
	withRXWithDefaults(Menu)(() => {
		const { menuViewModel, accountViewModel } = e;
		const { menuMode$, currentAccount$, availableAccounts$, proTraderAvaliable$ } = menuViewModel;
		const { accountInfo$, onPreloaderChange } = accountViewModel;
		const email$ = accountInfo$.pipe(map(info => info.email));
		const accounts$ = combineLatest(currentAccount$, availableAccounts$).pipe(
			map(([current, available]) => ({
				currentAccount: fromModelToData(current),
				availableAccounts: available.map(fromModelToData),
			})),
		);

		return {
			props: {
				mode: menuMode$,
				accounts: accounts$,
				name: accountInfo$.pipe(map(account => account.fullName)),
				isRealAccount: currentAccount$.pipe(
					first(),
					map(account => O.some(!account.paperMoney)),
				),
				email: email$,
				proTraderAvaliable: proTraderAvaliable$,
			},
			defaultProps: {
				mode: Mode.Demo,
				email: O.none,
				name: O.none,
				isRealAccount: O.none,
				accounts: {
					currentAccount: {
						currency: '',
						accountId: '',
						status: Mode.Demo,
					},
					availableAccounts: [
						{
							currency: '',
							accountId: '',
							status: Mode.Demo,
						},
					],
				},
				onPreloaderChange,
				proTraderAvaliable: false,
				previewMode: O.some(false),
			},
		};
	}),
);

export const MenuContainer = combineContext(
	deferContext(Container, 'menuViewModel'),
	createMenuViewModel,
	(Container, createMenuViewModel) =>
		runOnMount(Container, () => () => ({
			menuViewModel: createMenuViewModel(),
		})),
);
