import { withRXWithDefaults } from '../../../../utils/withRXWithDefaults';
import { ask, combineContext, deferContext } from '@devexperts/rx-utils/dist/context.utils';
import { right } from 'fp-ts/lib/Either';
import { UnwrappedFilterFormData } from '../../../../model/history/history.model';
import { runOnMount } from '../../../../utils/react.utils';
import { HistoryFilterForm } from '../../components/history-filter-form/history-filter-form.component';
import {
	createHistoryFilterFormViewModel,
	HistoryFilterFormViewModel,
} from '../../../../view-model/history/history-filter-form.view-model';

type HistoryFilterFormContainerContext = {
	historyFilterFormViewModel: HistoryFilterFormViewModel;
};

export const Container = combineContext(ask<HistoryFilterFormContainerContext>(), ctx => {
	return withRXWithDefaults(HistoryFilterForm)(() => {
		const {
			historyFilterFormViewModel: {
				isPeriodDropdownOpened$,
				value$,
				onPeriodDropdownToggle,
				initValue,
				onValueChange,
				validatedValue$,
			},
		} = ctx;

		const { selectedPeriod: initPeriod } = initValue;

		return {
			props: {
				isPeriodDropdownOpened: isPeriodDropdownOpened$,
				value: value$,
				validatedValue: validatedValue$,
			},
			defaultProps: {
				isPeriodDropdownOpened: false,
				onPeriodDropdownToggle,
				value: initValue,
				validatedValue: right<void, UnwrappedFilterFormData>([initPeriod, 0, 0]),
				onValueChange,
			},
		};
	});
});

export const HistoryFilterFormContainer = combineContext(
	createHistoryFilterFormViewModel,
	deferContext(Container, 'historyFilterFormViewModel'),
	(createHistoryFilterFormViewModel, Container) =>
		runOnMount(Container, () => () =>
			createHistoryFilterFormViewModel().map(historyFilterFormViewModel => ({ historyFilterFormViewModel })),
		),
);
