import * as React from 'react';
import { PureComponent } from 'react';
import { WithTranslation, withTranslation } from 'react-i18next';
import { Option } from 'fp-ts/lib/Option';
import {
	TakeProfitStopLossComponentData,
	ProfitLossKind,
} from '../../../../ui-common/takeprofit-stoploss-panel/takeprofit-stoploss-panel.model';
import { SidePanel } from '../../../../ui-common/side-panel/side-panel.component';
import { Button } from '../../../../ui-common/button/button.component';
import { CloseButton } from '../../../../ui-common/close-button/close-button.component';
import { combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { EditingPosition as EditingPositionCommon } from '../../../../ui-common/editing-position/editing-position.component';
import { RouteLink } from '../../../../ui-common/route-link/containers/route-link.container';
import { routes } from '../../../../ui-common/routes';
import { tspan } from '../../../../utils/i18n.utils';
import * as css from './theme/editing-position.module.css';
import * as buttonCss from './theme/button.child.module.css';
import * as sidePanelCss from './theme/side-panel.child.module.css';
import {
	EditingPositionData,
	EditingPositionTakeProfitStopLossData,
} from '../../../../ui-common/editing-position/editing-position.model';

const sidePanelTheme = { SidePanel: sidePanelCss };

export type EditingPositionProps = {
	uid: string;
	plKind: string;
	data: Option<EditingPositionData>;
	amount: Option<number>;
	value: Option<number>;
	amountPrecision: number;
	profitLoss: Option<number>;
	onValueChange: (data: EditingPositionTakeProfitStopLossData) => void;
	onPositionUpdate: (isValid: boolean) => () => void;
	isValid: boolean;
	isShownValidErrors: boolean;
	currencySymbol: string;
	unitName: Option<string>;
	isEditingPositionAllowed: boolean;
};

export const EditingPosition = combineContext(RouteLink, RouteLink =>
	withTranslation()(
		class EditingPosition extends PureComponent<EditingPositionProps & WithTranslation> {
			render() {
				const {
					data,
					isShownValidErrors,
					profitLoss,
					amount,
					value,
					currencySymbol,
					unitName,
					amountPrecision,
					onValueChange,
				} = this.props;

				return (
					<SidePanel
						header={this.renderHeader()}
						footer={this.renderFooter()}
						theme={sidePanelTheme}
						scrollable>
						<div className={css.content}>
							<EditingPositionCommon
								data={data}
								unitName={unitName}
								amount={amount}
								value={value}
								amountPrecision={amountPrecision}
								currencySymbol={currencySymbol}
								profitLoss={profitLoss}
								isShownValidErrors={isShownValidErrors}
								onValueChange={onValueChange}
							/>
						</div>
					</SidePanel>
				);
			}

			handleValueChange = (data: EditingPositionTakeProfitStopLossData) => (
				changedValue: TakeProfitStopLossComponentData,
			) => {
				const { takeProfitData, stopLossData } = data;
				const { onValueChange } = this.props;

				switch (changedValue.kind) {
					case ProfitLossKind.Profit:
						onValueChange({
							takeProfitData: changedValue,
							stopLossData,
						});
						break;
					case ProfitLossKind.Loss:
						onValueChange({
							takeProfitData,
							stopLossData: changedValue,
						});
						break;
				}
			};

			private renderHeader = () => (
				<div className={css.header}>
					<span className={css.widgetLabel}>
						{tspan('editingPositionEditingPosition', 'Editing Position')}
					</span>
					<RouteLink
						to={routes.positions.position.root}
						value={{ positionId: this.props.uid }}
						render={({ onNavigate }) => (
							<div className={css.closeBtnWrapper}>
								<CloseButton onClick={onNavigate} />
							</div>
						)}
					/>
				</div>
			);

			private renderFooter = () => {
				const { onPositionUpdate, isValid, isEditingPositionAllowed } = this.props;

				return (
					<Button
						isDisabled={!isEditingPositionAllowed}
						onClick={onPositionUpdate(isValid)}
						isPrimary={true}
						theme={buttonCss}>
						{tspan('editingPositionUpdate', 'Update')}
					</Button>
				);
			};
		},
	),
);
