import { combineContext, ask } from '@devexperts/rx-utils/dist/context.utils';
import { withRXWithDefaults } from '../../../../utils/withRXWithDefaults';
import { OrderEntryExecutionComponent } from '../../components/order-entry-execution/order-entry-execution.component';
import { none } from 'fp-ts/lib/Option';
import { OrderEntryViewModel } from '../../../../view-model/order-entry/order-entry.view-model';
import { left } from 'fp-ts/lib/Either';
import { pending } from '@devexperts/remote-data-ts';
import { combineLatestRD } from '@devexperts/rx-utils/dist/rd/operators/combineLatestRD';
import { mapRD } from '@devexperts/rx-utils/dist/rd/operators/mapRD';
import { ExecutionPriceValidationErrorMessages } from '../../../../model/order-entry/order-entry.model';

const empty = '';

export type OrderEntryExecutionContext = {
	orderEntryViewModel: OrderEntryViewModel;
};

export const OrderEntryExecutionContainer = combineContext(ask<OrderEntryExecutionContext>(), e => {
	const {
		onToggleExecutionCheckboxes,
		onChangeExecutionPrice,
		onChangeExecutionDates,
		executionCheckboxes$,
		executionPrice$,
		executionDates$,
		initExecutionDates,
		priceIncrement$,
		precision$,
	} = e.orderEntryViewModel;

	return withRXWithDefaults(OrderEntryExecutionComponent)(() => ({
		defaultProps: {
			onToggleCheckbox: onToggleExecutionCheckboxes,
			onChangeExecutionPrice,
			onChangeExecutionDates,
			visibleValues: {
				executePriceOpened: false,
				expireAtActive: false,
			},
			formValues: {
				priceValue: none,
				executionPriceValidated: left({
					message: ExecutionPriceValidationErrorMessages.General,
					hiRange: empty,
					loRange: empty,
					step: empty,
				}),
			},
			formDate: initExecutionDates,
			stepperData: pending,
		},
		props: {
			visibleValues: executionCheckboxes$,
			formValues: executionPrice$,
			formDate: executionDates$,
			stepperData: combineLatestRD(priceIncrement$, precision$).pipe(
				mapRD(([priceIncrement, precision]) => ({ priceIncrement, precision })),
			),
		},
	}));
});
