import { Plugin, PluginDependencyResolver } from '../../../../ui-common/chart/plugin/plugin.model';
import { Clearer, ChartElementsCache, CandleDirection } from '../../components/chart/chart.model';
import { fromNullable, isSome, none, Option, some, option } from 'fp-ts/lib/Option';
import * as React from 'react';
import * as ReactDOM from 'react-dom';
import { ThemedLines } from '../lines/lines.component';
import { liftA3 } from 'fp-ts/lib/Apply';
import { ChartEvents } from '@dx-private/dxchart5/dist/js/chart/ChartEvents';
import { LastCandleOnAxisUpdateRequest } from '@dx-private/dxchart5/dist/js/chart/axisLabels';
import EventBus from '@dx-private/dxchart5/dist/js/chart/EventBus';
import ChartData from '@dx-private/dxchart5/dist/js/chart/model/ChartData';
import * as css from '../lines/theme/lines-plugin.module.css';
import * as lineTheme from './theme/current-price-line.module.css';

export class CurrentPriceLinePlugin extends Plugin {
	private container: Option<HTMLDivElement> = none;
	private chartModel: Option<ChartData> = none;
	private currentPrice: Option<number> = none;
	private isDownDirection: boolean = false;

	register(resolver: PluginDependencyResolver) {
		resolver(
			['eventBus', 'clearer', 'elements', 'events'],
			(bus: EventBus, clearer: Clearer, elements: ChartElementsCache, events: ChartEvents) => {
				const { mainCanvas: canvas } = elements;
				const div = document.createElement('div');
				div.classList.add(css.container);
				this.container = some(div);
				const parent = fromNullable(canvas.parentNode);
				if (isSome(parent)) {
					parent.value.appendChild(div);
				}
				return clearer([
					events.addRedrawListener(this.drawMainHandler),
					bus.on(bus.types.LAST_CANDLE, this.lastCandleTickHandler),
					() => {
						ReactDOM.unmountComponentAtNode(div);
						if (isSome(parent)) {
							parent.value.removeChild(div);
						}
					},
				]);
			},
		);
	}

	private render = (container: HTMLDivElement) => (chartModel: ChartData) => (currentPrice: number) => {
		const toY = (price: number) => Math.floor(chartModel.toY(price));
		const format = (price: number) => chartModel.priceFormatter(price);
		const theme = { price: this.isDownDirection ? lineTheme.price_down : lineTheme.price };
		ReactDOM.render(
			React.createElement(ThemedLines, {
				lines: [
					{
						label: none,
						theme,
						formattedPrice: format(currentPrice),
						price: currentPrice,
						yPosition: toY(currentPrice),
					},
				],
			}),
			container,
		);
	};

	private renderLiftA3 = () => {
		liftA3(option)(this.render)(this.container)(this.chartModel)(this.currentPrice);
	};

	private lastCandleTickHandler = (event: LastCandleOnAxisUpdateRequest) => {
		const currentPrice = fromNullable(event.value);
		this.isDownDirection = event.candleDirection === CandleDirection.ASK;
		this.currentPrice = currentPrice;
		this.renderLiftA3();
	};

	private drawMainHandler = (chartModel: ChartData) => {
		this.chartModel = fromNullable(chartModel);
		this.renderLiftA3();
	};
}
