import * as css from './ava-chart-Info.module.css';
import ChartInfoView from './chart-info.view';
import CrossToolViewModel, { E_CROSS_TOOL_PLUGIN_MODEL } from '../models/cross-tool.view-model';
import * as classnames from 'classnames';
import Hover from '@dx-private/dxchart5/dist/js/chart/model/Hover';
import { StudySeries } from '../../../chart.model';

let wheelEvent: string;
wheelEvent = document.documentElement && 'onmousewheel' in document.documentElement ? 'mousewheel' : 'DOMMouseScroll';
try {
	//noinspection JSPotentiallyInvalidConstructorUsage
	/*eslint-disable babel/new-cap*/
	const e = new WheelEvent('wheel');
	if (e) {
		wheelEvent = 'wheel';
	}
} catch (e) {}

const CANDLE_DIRECTION = {
	BULL: 'bull',
	BEAR: 'bear',
};

export default class AVAChartInfoView extends ChartInfoView {
	private _candleGroup: Element | null = null;

	protected _content: Element | null = null;

	private _toggle: Element | null = null;

	private _isCollapsed = false;

	private _overlaysAmount = 0;

	constructor(container: Element, studySeries: StudySeries | null, model: CrossToolViewModel, isMobile: boolean) {
		super(container, studySeries, model);
		this._model.on(E_CROSS_TOOL_PLUGIN_MODEL.CHANGE_IS_FIXED, this._onModelIsFixedChange);
		this._element.classList.add(isMobile ? css.containerMobile : css.containerDesktop);
		if (!studySeries) {
			this._element.classList.add(css.overlay);
		}
	}

	close() {
		this._model.off(E_CROSS_TOOL_PLUGIN_MODEL.CHANGE_IS_FIXED, this._onModelIsFixedChange);
		if (this._toggle) {
			this._toggle.removeEventListener('click', this._onToggleClick);
		}
		//noinspection JSUnresolvedFunction
		this._element.removeEventListener(wheelEvent, this._onWheelScroll);

		super.close();
	}

	update(hoverModel: Hover) {
		if (this._candleGroup) {
			this._candleGroup.classList.remove(css.group_up);
			this._candleGroup.classList.remove(css.group_down);
			switch (hoverModel.candle.direction) {
				case CANDLE_DIRECTION.BEAR:
					this._candleGroup.classList.add(css.group_down);
					break;
				case CANDLE_DIRECTION.BULL:
					this._candleGroup.classList.add(css.group_up);
					break;
			}
		}
		super.update(hoverModel);

		if (!this._studySeries) {
			//main ChartInfoView
			//update only for overlaying
			const overlaysAmount = hoverModel.overlays.length;
			if (this._overlaysAmount !== overlaysAmount) {
				//update only if number of overlays has changed
				//TODO: it's better to rely on react's diff
				this._overlaysAmount = overlaysAmount;
			}
		}
	}

	addStudy(studySeries: StudySeries) {
		this._overlaysAmount = -1;
		super.addStudy(studySeries);
	}

	removeStudy(studySeries: StudySeries) {
		this._overlaysAmount = -1;
		super.removeStudy(studySeries);
	}

	///////////////////////
	// PROTECTED MEMBERS //
	///////////////////////

	parse(element: Element) {
		super.parse(element);

		this._candleGroup = element.querySelector(`.${css.group_candle}`);
		this._content = element.querySelector('[data-element=chartInfo--content]');
		this._toggle = element.querySelector('[data-element=chartInfo--toggle]');

		setTimeout(() => {
			this._candleGroup = element.querySelector(`.${css.group_candle}`);
			this._content = element.querySelector('[data-element=chartInfo--content]');
			this._toggle = element.querySelector('[data-element=chartInfo--toggle]');

			if (this._toggle) {
				this._toggle.addEventListener('click', this._onToggleClick);
			}
			this._element.addEventListener(wheelEvent, this._onWheelScroll);
		});
		//use setTimeout because BaseChartInfoView is composed and rendered in memory
	}

	template(studySeries: StudySeries) {
		return `
			<div class="${css.container}" data-element="chartInfo">
				<div class="${css.header}">
					<span class="${css.title}" data-element="chartInfo--title">
						${studySeries ? this._getTitle(studySeries.conf) : ''}
					</span><button class="${css.toggle}" data-element="chartInfo--toggle">
						<svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" width="24" height="24" viewBox="0 0 24 24">
							<defs>
								<path id="a" d="M10.657 15h8v2h-10V7h2v8z"/>
							</defs>
							<g fill="none" fill-rule="evenodd">
								<mask id="b" fill="#fff">
									<use xlink:href="#a"/>
								</mask>
								<use fill="currentcolor" transform="rotate(45 13.657 12)" xlink:href="#a"/>
							</g>
						</svg>
					</button>
				</div>
				<div class="${css.content}" data-element="chartInfo--content">
					${studySeries ? '' : this.candleTemplate()}
				</div>
			</div>
		`;
	}

	candleTemplate() {
		return `
			<div class="${classnames(css.group, css.group_candle)}">
				<div class="${css.parameter}">
					<div class="${css.label}">
						Open
					</div>
					<div class="${css.value}" data-element="chartInfo--open"></div>
				</div>
				<div class="${css.parameter}">
					<div class="${css.label}">
						High
					</div>
					<div class="${css.value}" data-element="chartInfo--high"></div>
				</div>
				<div class="${css.parameter}">
					<div class="${css.label}">
						Low
					</div>
					<div class="${css.value}" data-element="chartInfo--low"></div>
				</div>
				<div class="${css.parameter}">
					<div class="${css.label}">
						Close
					</div>
					<div class="${css.value}" data-element="chartInfo--close"></div>
				</div>
			</div>
		`;
	}

	private _collapse() {
		if (!this._isCollapsed) {
			this._isCollapsed = true;
			this._element.classList.add(css.collapsed);
		}
	}

	private _expand() {
		if (this._isCollapsed) {
			this._isCollapsed = false;
			this._element.classList.remove(css.collapsed);
		}
	}

	//////////////////////////
	// MODEL EVENT HANDLERS //
	//////////////////////////

	/**
	 * @param {boolean} isFixed
	 * @private
	 */
	private _onModelIsFixedChange = (isFixed: boolean) => {
		if (!isFixed && this._model.isEnabled) {
			this._expand();
		}
	};

	////////////////////////
	// DOM EVENT HANDLERS //
	////////////////////////

	_onToggleClick = () => {
		if (this._isCollapsed) {
			this._expand();
		} else {
			this._collapse();
		}
	};

	private _onWheelScroll = (e: Event) => {
		e.stopPropagation();
	};
}

export const createAVAChartInfoView = (
	container: Element,
	studySeries: StudySeries | null,
	model: CrossToolViewModel,
	isMobile: boolean,
): AVAChartInfoView => new AVAChartInfoView(container, studySeries, model, isMobile);
