import { Option } from 'fp-ts/lib/Option';
import { string, number, type } from 'io-ts';
import { createOptionFromNullable } from 'io-ts-types';

export type InstrumentInfoTO = {
	symbol: string;
	dayChange: Option<number>;
	weekChange: Option<number>;
	monthChange: Option<number>;
	sentimentBuy: Option<number>;
	sentimentSell: Option<number>;
};
export const InstrumentInfoTOIO = type({
	symbol: string,
	dayChange: createOptionFromNullable(number),
	weekChange: createOptionFromNullable(number),
	monthChange: createOptionFromNullable(number),
	sentimentBuy: createOptionFromNullable(number),
	sentimentSell: createOptionFromNullable(number),
});
