import { findFirst, getEq, filter as filterArray } from 'fp-ts/lib/Array';
import { Option } from 'fp-ts/lib/Option';
import * as O from 'fp-ts/lib/Option';
import { filter, map, distinctUntilChanged, switchMap, mapTo, tap } from 'rxjs/operators';
import { ask, combineContext, fromReader } from '@devexperts/rx-utils/dist/context.utils';
import { LiveData } from '@devexperts/rx-utils/dist/rd/live-data.utils';
import { failure, fromOption, success } from '@devexperts/remote-data-ts';
import { EntityStore } from '@devexperts/rx-utils/dist/rd/entity-store.utils';
import { PositionTO } from '../../api/generated/swagger/definitions/PositionTO';
import { SocketClient } from '../../api/socket-client';
import { isOrdersTO, isPositionTO } from '../../api/socket/socket-message.to';
import { eqString } from 'fp-ts/lib/Eq';
import { ProtectionOrderTO } from '../../api/generated/swagger/definitions/ProtectionOrderTO';
import { positionsController } from '../../api/generated/swagger/controllers/PositionsController';
import { uuid } from '@devexperts/utils/dist/string';
import { OrderTemplateTO } from '../../api/generated/swagger/definitions/OrderTemplateTO';
import { PositionKeyTO } from '../../api/generated/swagger/definitions/PositionKeyTO';
import { Observable, merge } from 'rxjs';
import { mapRD } from '@devexperts/rx-utils/dist/rd/operators/mapRD';
import { tapRD } from '@devexperts/rx-utils/dist/rd/operators/tapRD';
import { Predicate } from 'fp-ts/lib/function';
import { Sink } from '@devexperts/rx-utils/dist/sink.utils';
import { OrdersMessageTO } from '../../api/socket/to/orders-message.to';
import { OrderTO } from '../../api/generated/swagger/definitions/OrderTO';
import { createHandler } from '@devexperts/rx-utils/dist/create-handler.utils';
import { pipe } from 'fp-ts/lib/pipeable';

type PositionQuote = { bid: Option<number>; ask: Option<number> };

export type PositionsStore = {
	all$: LiveData<Error, PositionTO[]>;
	keys$: Observable<string[]>;
	getPositionByUid: (uid: string) => LiveData<Error, PositionTO>;
	getAllByInstrumentId: (instrumentId: number) => LiveData<Error, PositionTO[]>;
	sendCloseRequest: (
		accountId: string,
		instrumentId: number,
		amount: number,
		positionKey: PositionKeyTO,
		quote: PositionQuote,
		quantity: number,
	) => LiveData<Error, void>;
	sendUpdateProtectionOrdersRequest: (
		positionKey: PositionKeyTO,
		protectionOrders: Array<ProtectionOrderTO>,
	) => LiveData<Error, void>;
	error$: Observable<Option<Error>>;
	closedPositions$: LiveData<Error, PositionTO[]>;
};

type PositionsStoreContext = {
	socketClient: SocketClient;
};

export type CreatePositionsStore = Sink<() => PositionsStore>;

export const createPositionsStore = combineContext(
	fromReader(positionsController),
	ask<PositionsStoreContext>(),
	(positionsController, context): CreatePositionsStore => {
		const { socketClient } = context;

		const pk = (positionTO: PositionTO): string => positionTO.uid;
		const store = new EntityStore<Error, PositionTO>();

		const error = createHandler<Option<Error>>();
		const error$ = error.value$.pipe(tap(r => console.log(r)));

		const positions$: LiveData<Error, PositionTO[]> = socketClient.message$.pipe(
			filter(isPositionTO),
			map(positions => success<Error, PositionTO[]>(positions.body)),
		);

		const closedPositions$: LiveData<Error, PositionTO[]> = positions$.pipe(
			mapRD(positions => positions.filter(pos => !filterZeroQtyPositionsPredicate(pos))),
		);

		const closeOrders$ = socketClient.message$.pipe(filter(isOrdersTO));

		const all$ = store
			.getAll(pk, () => positions$)
			.pipe(mapRD(positions => filterArray(positions, filterZeroQtyPositionsPredicate)));

		// TODO: delete distinctUntilChanged after #111 (dx-platform)
		const keys$ = store.keys$.pipe(distinctUntilChanged(getEq(eqString).equals));

		const getPositionByUid = (positionUid: string) =>
			store.get(positionUid, () =>
				positions$.pipe(
					map(positionsData =>
						positionsData.chain(positionData =>
							fromOption(
								findFirst(positionData, data => data.uid === positionUid),
								() => new Error(`Position data with uid ${positionUid} not found in positions.store`),
							),
						),
					),
				),
			);

		const getAllByInstrumentId = (instrumentId: number) =>
			all$.pipe(mapRD(data => data.filter(getPredicateByInstrumentId(instrumentId))));

		const sendCloseRequest = (
			accountId: string,
			instrumentId: number,
			amount: number,
			positionKey: PositionKeyTO,
			quote: PositionQuote,
			quantity: number,
		): LiveData<Error, void> => {
			const requestId = uuid();
			return positionsController
				.closePosition({
					body: buildCloseOrderTemplateTO(
						accountId,
						instrumentId,
						amount,
						positionKey,
						quote,
						requestId,
						quantity,
					),
				})
				.pipe(
					// we need to catch position close rejection for showing the error
					switchMap(() =>
						closeOrders$.pipe(
							map((orders: OrdersMessageTO) =>
								pipe(
									orders.body,
									findFirst(item =>
										pipe(
											item.requestId,
											O.exists(id => id === requestId),
										),
									),
									O.fold(
										() => failure<Error, OrderTO>(new Error()),
										order => {
											if (order.status === 'REJECTED') {
												const err = new Error(order.statusDescription);
												error.handle(O.some(err));
												return failure<Error, OrderTO>(err);
											}
											error.handle(O.none);
											return success(order);
										},
									),
								),
							),
						),
					),
					mapTo(success(undefined)),
				);
		};

		const sendUpdateProtectionOrdersRequest = (
			positionKey: PositionKeyTO,
			protectionOrders: Array<ProtectionOrderTO>,
		) => {
			return positionsController.modifyProtectionOrders({
				body: { positionKey, protectionOrders },
			});
		};

		const removePositionFromStore = (uid: string) => () => all$.pipe(mapRD(filterClosedPositions(uid)));

		const removeEffect$ = positions$.pipe(
			tapRD(positions =>
				positions.forEach(
					({ uid, quantity }) => quantity === 0 && store.remove(uid, pk, removePositionFromStore(uid)),
				),
			),
		);

		const effects$ = merge(all$, removeEffect$);

		return new Sink(
			() => ({
				all$,
				keys$,
				error$,
				getPositionByUid,
				getAllByInstrumentId,
				sendCloseRequest,
				sendUpdateProtectionOrdersRequest,
				closedPositions$,
			}),
			effects$,
		);
	},
);

const getPredicateByInstrumentId = (instrumentId: number): Predicate<PositionTO> => position =>
	position.positionKey.instrumentId === instrumentId;

function buildCloseOrderTemplateTO(
	accountId: string,
	instrumentId: number,
	amount: number,
	positionKey: PositionKeyTO,
	quote: PositionQuote,
	requestId: string,
	positionQuantity: number,
): OrderTemplateTO {
	//if quantity > 0 - position was opened by buying
	const isQuantityPositive = positionQuantity > 0;
	const limitPrice = isQuantityPositive ? quote.bid : quote.ask;
	const sign = positionQuantity > 0 ? -1 : 1;
	return {
		accountId,
		instrumentId,
		// ensure that we apply multiplier to an absolute value
		quantity: Math.abs(amount) * sign,
		orderType: 'MARKET',
		timeInForce: 'GTC',
		limitPrice,
		stopPrice: O.none,
		stopPriceOffset: O.none,
		ocoGroupCode: O.none,
		refOrderChainId: O.none,
		positionKey: O.some(positionKey),
		effect: 'CLOSING',
		orderSide: O.none,
		expireAt: O.none,
		alertExpression: O.none,
		directExchange: O.none,
		additionalParameters: O.none,
		takeProfit: O.none,
		stopLoss: O.none,
		requestId: O.some(requestId),
	};
}

function filterClosedPositions(uid: string) {
	return (positions: PositionTO[]) => filterArray(positions, position => position.uid === uid);
}

function filterZeroQtyPositionsPredicate(position: PositionTO) {
	return position.quantity !== 0;
}
