import { array, boolean, literal, string, Tagged, type } from 'io-ts';

export const ACCOUNTS = 'ACCOUNTS';

export type AccountTO = {
	id: string;
	baseCurrency: string;
	paperMoney: boolean;
};

export type AccountsTO = {
	currentAccount: AccountTO;
	availableAccounts: AccountTO[];
};

export type AccountsMessageTO = {
	type: typeof ACCOUNTS;
	body: AccountsTO;
};

export const accountsMessageTO: Tagged<'type', AccountsMessageTO, unknown> = type({
	type: literal(ACCOUNTS),
	body: type({
		currentAccount: type({
			id: string,
			baseCurrency: string,
			paperMoney: boolean,
		}),
		availableAccounts: array(
			type({
				id: string,
				baseCurrency: string,
				paperMoney: boolean,
			}),
		),
	}),
});
