import * as React from 'react';
import { Component, ReactNode } from 'react';
import * as classNames from 'classnames';
import { Omit } from 'typelevel-ts';
import { PartialKeys } from '@devexperts/utils/dist/object/object';
import { withTheme } from '@devexperts/react-kit/dist/utils/withTheme';
export * from '@devexperts/react-kit/dist/components/Checkbox/Checkbox';
import { TControlProps } from '@devexperts/react-kit/dist/components/Control/Control';
import {
	Checkbox as DXCCheckbox,
	CHECKBOX,
	TFullCheckboxProps as TDXCFullCheckboxProps,
} from '@devexperts/react-kit/dist/components/Checkbox/Checkbox';

import { CheckboxTickIcon } from '../../ui-desktop/ui-kit/components/icons/checkbox-tick.icon';

import * as css from './checkbox.module.css';

const iconCheckboxTick = <CheckboxTickIcon />;

export type TFullCheckboxProps = Omit<TDXCFullCheckboxProps, 'checkMark'> & {
	children?: ReactNode;
	checkMark?: ReactNode;
	theme: {
		view_isChecked?: string;
		checkboxContainer?: string;
		view_isCheckedDisabled?: string;
		text?: string;
	} & TDXCFullCheckboxProps['theme'];
} & TControlProps<boolean>;

class RawCheckbox extends Component<TFullCheckboxProps> {
	static defaultProps = {
		checkMark: iconCheckboxTick,
	};

	render() {
		const { value, isDisabled, children, checkMark, ...props } = this.props;
		let theme = props.theme;

		theme = {
			...theme,
			view: classNames(theme.view, {
				[theme.view_isChecked as string]: value,
				[theme.view_isCheckedDisabled as string]: value && isDisabled,
			}),
		};

		const containerTheme = classNames(theme.container, {
			[css.container_isDisabled as string]: isDisabled,
			[css.container_isCheckedDisabled as string]: value && isDisabled,
		});

		return (
			<label className={containerTheme}>
				<div className={theme.checkboxContainer}>
					<DXCCheckbox {...this.props} theme={theme} checkMark={checkMark} />
				</div>
				{children && <span className={theme.text}>{children}</span>}
			</label>
		);
	}
}

export type TCheckboxProps = PartialKeys<TFullCheckboxProps, 'theme'>;
export const Checkbox = withTheme(CHECKBOX, css)(RawCheckbox);
