import { filter, map } from 'rxjs/operators';
import { LiveData } from '@devexperts/rx-utils/dist/rd/live-data.utils';
import { Sink } from '@devexperts/rx-utils/dist/sink.utils';
import { EntityStore } from '@devexperts/rx-utils/dist/rd/entity-store.utils';
import { FxPositionMetricsTO } from '../../api/generated/swagger/definitions/FxPositionMetricsTO';
import { SocketClient } from '../../api/socket-client';
import { combineContext, ask } from '@devexperts/rx-utils/dist/context.utils';
import { isPositionMetricsTO } from '../../api/socket/socket-message.to';
import { success, fromOption } from '@devexperts/remote-data-ts';
import { findFirst } from 'fp-ts/lib/Array';

export type MetricsStore = {
	all$: LiveData<Error, FxPositionMetricsTO[]>;
	getMetricsByUid: (uid: string) => LiveData<Error, FxPositionMetricsTO>;
};

type MetricsStoreContext = {
	socketClient: SocketClient;
};

type CreateMetricsStore = Sink<() => MetricsStore>;

export const createMetricsStore = combineContext(
	ask<MetricsStoreContext>(),
	(context): CreateMetricsStore => {
		const { socketClient } = context;

		const pk = (metricsTO: FxPositionMetricsTO): string => metricsTO.uid;
		const store = new EntityStore<Error, FxPositionMetricsTO>();

		const metrics$ = socketClient.message$.pipe(
			filter(isPositionMetricsTO),
			map(metrics => success<Error, FxPositionMetricsTO[]>(metrics.body)),
		);

		const all$ = store.getAll(pk, () => metrics$);

		const getMetricsByUid = (uid: string) =>
			store.get(uid, () =>
				metrics$.pipe(
					map(metrics =>
						metrics.chain(metrics =>
							fromOption(
								findFirst(metrics, metric => metric.uid === uid),
								() => new Error(`Position metric with uid ${uid} not found in metrics.store`),
							),
						),
					),
				),
			);

		return new Sink(() => ({ all$, getMetricsByUid }), all$);
	},
);
