import { TPipSettings } from '../../../../ui-common/pip-rate/pip-rate.component';
import { Option } from 'fp-ts/lib/Option';
import { Either } from 'fp-ts/lib/Either';

export enum OrderEntrySide {
	Buy = '* BUY *',
	Sell = '* SELL *',
}

export type OrderEntryValue = {
	amount: string;
	side: OrderEntrySide;
};

export type OrderEntrySideValue = {
	amount: Option<number>;
	pipSettings: TPipSettings;
};

export enum AmountValidationErrorMessages {
	Max = '* MAX *',
	Min = '* MIN *',
	Multiple = '* MULTIPLE *',
}

export type AmountValidationError = {
	message: AmountValidationErrorMessages;
	maxOrderSize: number;
	minOrderSize: number;
	minOrderIncrement: number;
};

export type OrderEntryPanelData = {
	buy: OrderEntrySideValue;
	sell: OrderEntrySideValue;
	precision: number;
	amountPrecision: number;
	description: Option<string>;
	marginImpact: Either<Error, number>;
	step: number;
	amountValidated: Either<AmountValidationError, string>;
	minOrderSize: number;
	maxOrderSize: number;
	value: OrderEntryValue;
	unitName: Option<string>;
};
