import { ask, combineContext, deferContext } from '@devexperts/rx-utils/dist/context.utils';
import { withRXWithDefaults } from '../../../../utils/withRXWithDefaults';
import { pending } from '@devexperts/remote-data-ts';
import { PositionDetails } from '../../components/position-details/position-details.component';
import { runOnMount } from '../../../../utils/react.utils';
import {
	PositionDetailsViewModel,
	createPositionDetailsViewModel,
} from '../../../../view-model/positions/position-details.view-model';
import { createSelector } from 'reselect';
import { combineLatestRD } from '@devexperts/rx-utils/dist/rd/operators/combineLatestRD';
import { mapRD } from '@devexperts/rx-utils/dist/rd/operators/mapRD';
import { map } from 'rxjs/operators';
import { AccountViewModel } from '../../../../view-model/account/account.view-model';
import { currencyStringToSymbol } from '../../../../utils/currency.utils';

export type PositionDetailsContainerContext = {
	positionDetailsViewModel: PositionDetailsViewModel;
	accountViewModel: AccountViewModel;
};

const FALLBACK_PIPSSIZE_VALUE = 0.001;
const FALLBACK_PRECISION_VALUE = 5;

export const Container = combineContext(PositionDetails, ask<PositionDetailsContainerContext>(), (PositionDetails, e) =>
	withRXWithDefaults(PositionDetails)(() => {
		const {
			positionCode$,
			symbol$,
			side$,
			precision$,
			pipsSize$,
			openPrice$,
			currentPriceAndDirection$,
			quantity$,
			sizeInLots$,
			currentValue$,
			openDateTime$,
			takeProfitValue$,
			stopLossValue$,
			swap$,
			commission$,
		} = e.positionDetailsViewModel;

		return {
			props: {
				accountCurrency: e.accountViewModel.current$.pipe(
					map(data => currencyStringToSymbol(data.baseCurrency)),
				),
				pipsSize: pipsSize$.pipe(map(pipsSize => pipsSize.getOrElse(FALLBACK_PIPSSIZE_VALUE))),
				precision: precision$.pipe(map(precision => precision.getOrElse(FALLBACK_PRECISION_VALUE))),
				headerData: combineLatestRD(symbol$, positionCode$).pipe(
					mapRD(([symbol, positionCode]) => ({
						symbol,
						positionCode,
					})),
				),
				side: side$,
				openPrice: openPrice$,
				currentPriceAndDirection: currentPriceAndDirection$,
				quantity: quantity$,
				sizeInLots: sizeInLots$,
				currentValue: currentValue$,
				openDateTime: openDateTime$,
				takeProfitValue: takeProfitValue$,
				stopLossValue: stopLossValue$,
				swap: swap$,
				commission: commission$,
			},
			defaultProps: {
				accountCurrency: '',
				pipsSize: FALLBACK_PIPSSIZE_VALUE,
				precision: FALLBACK_PRECISION_VALUE,
				headerData: pending,
				side: pending,
				openPrice: pending,
				currentPriceAndDirection: pending,
				quantity: pending,
				sizeInLots: pending,
				currentValue: pending,
				openDateTime: pending,
				takeProfitValue: pending,
				stopLossValue: pending,
				swap: pending,
				commission: pending,
			},
		};
	}),
);

export const PositionDetailsContainer = combineContext(
	deferContext(Container, 'positionDetailsViewModel'),
	createPositionDetailsViewModel,
	(Container, createPositionDetailsViewModel) =>
		runOnMount(Container, props => {
			const uid = createSelector(
				props,
				props => props.uid,
			);
			return createSelector(
				uid,
				uid => ({
					positionDetailsViewModel: createPositionDetailsViewModel(uid),
				}),
			);
		}),
);
