import { pending } from '@devexperts/remote-data-ts';
import { withRXWithDefaults } from '../../../../utils/withRXWithDefaults';
import { HistoryTable } from '../../components/history-table/history-table.component';
import { HistoryTableConfig } from '../../components/history-table/history-table.utils';
import { ask, combineContext, deferContext } from '@devexperts/rx-utils/dist/context.utils';
import { AccountViewModel } from '../../../../view-model/account/account.view-model';
import { currencyStringToSymbol } from '../../../../utils/currency.utils';
import { map } from 'rxjs/operators';
import {
	createHistoryTableViewModel,
	HistoryTableViewModel,
} from '../../../../view-model/history/history-table.view-model';
import { toHistoryTableData } from './history-table.utils';
import { runOnMount } from '../../../../utils/react.utils';
import { mapRD } from '@devexperts/rx-utils/dist/rd/operators/mapRD';

type HistoryPageContainerContext = {
	historyTableViewModel: HistoryTableViewModel;
	accountViewModel: AccountViewModel;
};

export const Container = combineContext(
	ask<HistoryPageContainerContext>(),
	HistoryTable,
	(ctx, HistoryTableComponent) =>
		withRXWithDefaults(HistoryTableComponent)(() => {
			const { historyTableViewModel, accountViewModel } = ctx;
			const { current$ } = accountViewModel;
			const currencySymbol$ = current$.pipe(
				map(currentAccount => currencyStringToSymbol(currentAccount.baseCurrency)),
			);

			return {
				props: {
					data: historyTableViewModel.data$.pipe(mapRD(data => data.map(toHistoryTableData))),
					currencySymbol: currencySymbol$,
				},
				defaultProps: {
					data: pending,
					currencySymbol: '',
					config: HistoryTableConfig,
				},
			};
		}),
);

export const HistoryTableContainer = combineContext(
	deferContext(Container, 'historyTableViewModel'),
	createHistoryTableViewModel,
	(Container, createHistoryTableViewModel) =>
		runOnMount(Container, () => () => ({ historyTableViewModel: createHistoryTableViewModel() })),
);
