import * as React from 'react';
import { Button, TButtonProps as DefaultButtonProps } from './button.component';
import { BUTTON } from '@devexperts/react-kit/dist/components/Button/Button';
import { Component } from 'react';
import { Omit } from 'typelevel-ts';
import { PartialKeys } from '@devexperts/utils/dist/object/object';
import { withTheme } from '@devexperts/react-kit/dist/utils/withTheme';
import * as buttonCTATheme from './theme/button-cta.module.css';

export type FullButtonProps = Omit<DefaultButtonProps, 'theme'> & {
	theme: {
		container?: string;
	} & DefaultButtonProps['theme'];
};

class RawButtonCta extends Component<FullButtonProps> {
	render() {
		const { theme, children, ...restProps } = this.props;

		return (
			<Button {...restProps} theme={theme}>
				{children}
			</Button>
		);
	}
}

const wTheme = {
	...buttonCTATheme,
};

export type ButtonProps = PartialKeys<FullButtonProps, 'theme'>;
export const ButtonCta = withTheme(BUTTON, wTheme)(RawButtonCta);
