import * as React from 'react';
import { PureComponent } from 'react';
import * as cn from 'classnames';
import { withTheme } from '@devexperts/react-kit/dist/utils/withTheme';
import { ChartYAxisPosition } from './chart.model';
import * as css from './theme/chart.module.css';

type TChartGraphProps = {
	theme: {
		graph?: string;
		graph_resizable?: string;
		axisWrapper_y?: string;
		canvasArea?: string;
		canvasWrapper?: string;
		studyOverlay?: string;
		studyMarker?: string;
		axis?: string;
		axis_y?: string;
		extraCanvas?: string;
	};
	yAxisPosition?: ChartYAxisPosition;
};

class RawChartGraph extends PureComponent<TChartGraphProps> {
	render() {
		const { theme, yAxisPosition } = this.props;

		return (
			<div className={cn(theme.graph, theme.graph_resizable)} data-chart-container="true">
				{yAxisPosition === ChartYAxisPosition.Left && this.renderYAxis()}
				<div data-element="canvasArea" className={theme.canvasArea}>
					<div className={theme.canvasWrapper}>
						<canvas data-element="mainCanvas" />
						<canvas data-element="staticDrawingCanvas" className={theme.studyOverlay} />
						<canvas data-element="dynamicDrawingCanvas" className={theme.studyOverlay} />
						<canvas data-element="hitTestCanvas" className={theme.studyOverlay} />
						<canvas data-element="extraCanvas1" className={theme.extraCanvas} />
					</div>
				</div>
				{yAxisPosition === ChartYAxisPosition.Right && this.renderYAxis()}
			</div>
		);
	}

	renderYAxis = () => {
		const { theme } = this.props;
		const axisYClassName = cn(theme.axis, theme.axis_y);
		return (
			<div className={theme.axisWrapper_y}>
				<div data-element="yAxis" className={axisYClassName} />
			</div>
		);
	};
}

const wTheme = {
	...css,
};

export const ChartGraph = withTheme(Symbol('ChartGraph'), wTheme)(RawChartGraph);
