import * as React from 'react';
import { memo, ReactNode } from 'react';
import { Option } from 'fp-ts/lib/Option';
import * as O from 'fp-ts/lib/Option';
import { ExclamationIcon } from '../../icons/exclamation.icon';
import { pipe } from 'fp-ts/lib/pipeable';
import * as css from './theme/error-panel.module.css';

export type ErrorPanelProps = {
	error: Option<string>;
	title: ReactNode;
};

export const ErrorPanel = memo<ErrorPanelProps>(props =>
	pipe(
		props.error,
		O.map(error => (
			<div className={css.error}>
				<div className={css.error__icon}>
					<ExclamationIcon />
				</div>
				<div className={css.error__text}>
					<div className={css.error__title}>{props.title}</div>
					<div className={css.error__message}>{error}</div>
				</div>
			</div>
		)),
		O.toNullable,
	),
);
