import * as React from 'react';
import { Component, ReactNode } from 'react';
import { withTheme } from '@devexperts/react-kit/dist/utils/withTheme';
import { MakeTheme } from '../../utils/theme.utils';
import { Scrollable as ScrollableComponent } from '../scrollable/scrollable.component';
import * as css from './theme/side-panel.module.css';
import * as scrollableCss from './theme/scrollable.child.module.css';

export type TSidePanelProps = {
	header?: ReactNode;
	footer?: ReactNode;
	children: ReactNode;
	theme: {
		SidePanel: MakeTheme<'container' | 'content' | 'header' | 'footer' | 'body'>;
		Scrollable?: {
			scrollable?: string;
			withHorizontalScrollbar?: string;
			withVerticalScrollbar?: string;
			scrollbar?: string;
			container: string;
			wrapper?: string;
			content?: string;
			resizeDetector?: string;
			horizontal_scrollbar__bar?: string;
			vertical_scrollbar__bar?: string;
			overlayContent?: string;
		};
	};
	scrollable?: boolean;
};

class RawSidePanel extends Component<TSidePanelProps> {
	render() {
		const {
			theme: { SidePanel },
			header,
			footer,
		} = this.props;
		return (
			<div className={SidePanel.container}>
				{header && <div className={SidePanel.header}>{header}</div>}
				<div className={SidePanel.content}>{this.renderBody()}</div>
				{footer && <div className={SidePanel.footer}>{footer}</div>}
			</div>
		);
	}

	private renderBody() {
		const {
			theme: { SidePanel, Scrollable },
			scrollable,
			children,
		} = this.props;
		if (scrollable) {
			return (
				<ScrollableComponent theme={Scrollable}>
					<div className={SidePanel.body}>{children}</div>
				</ScrollableComponent>
			);
		}
		return <div className={SidePanel.body}>{children}</div>;
	}
}

const wTheme = { SidePanel: css, Scrollable: scrollableCss };

export const SidePanel = withTheme(Symbol(), wTheme)(RawSidePanel);
