import { unformat } from 'accounting';

export const parseFloatWithLeadingZero = (numericString: string): number =>
	numericString.charAt(0) === '0' && unformat(numericString) >= 1
		? // extract original value and replace first occurence of fractional sign
		  unformat(`0.${numericString.slice(1).replace('.', '')}`)
		: unformat(numericString);
