import * as React from 'react';
import { Fragment, memo, useCallback } from 'react';
import { WithTranslation, withTranslation } from 'react-i18next';
import { Option } from 'fp-ts/lib/Option';
import * as O from 'fp-ts/lib/Option';
import { RemoteData } from '@devexperts/remote-data-ts';
import { combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { routes } from '../../../../ui-common/routes';
import { SidePanel } from '../../../../ui-common/side-panel/side-panel.component';
import { RemoteDataState } from '../../../../ui-common/remote-data-state/remote-data-state.component';
import { RouteLink } from '../../../../ui-common/route-link/containers/route-link.container';
import { SymbolComponent } from '../../../../ui-common/symbol/symbol';
import { SymbolInfoData } from '../../../../ui-common/symbol-info/symbol-info.model';
import { SymbolInfoCommon } from '../../../../ui-common/symbol-info/symbol-info.component';
import { CloseButton } from '../../../../ui-common/close-button/close-button.component';
import { empty } from 'fp-ts/lib/Array';
import { pipe } from 'fp-ts/lib/pipeable';
import { constant } from 'fp-ts/lib/function';

export type SymbolInfoProps = {
	data: RemoteData<Error, SymbolInfoData>;
	symbol: string;
	categoryId: string;
} & WithTranslation;

export const SymbolInfo = combineContext(RouteLink, RouteLink =>
	withTranslation()(
		memo((props: SymbolInfoProps) => {
			const { data, symbol, categoryId } = props;

			const renderHeader = useCallback(
				(description: Option<string>) => (
					<Fragment>
						<SymbolComponent
							title={symbol}
							subtitle={pipe(
								description,
								O.getOrElse(constant('—')),
							)}
						/>
						<RouteLink
							to={routes.trade.category.symbol.root}
							value={{ symbol, categoryId }}
							render={({ onNavigate }) => <CloseButton onClick={onNavigate} />}
						/>
					</Fragment>
				),
				empty,
			);

			const renderSuccess = useCallback(
				({ description }: SymbolInfoData) => (
					<SidePanel header={renderHeader(description)} scrollable>
						<SymbolInfoCommon {...props} />
					</SidePanel>
				),
				[renderHeader, props],
			);

			return <RemoteDataState data={data} success={renderSuccess} />;
		}),
	),
);
