import * as React from 'react';
import { Component, ReactNode, Fragment } from 'react';
import { combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { AsideContainer } from '../../containers/aside/aside.container';
import { HeaderContainer } from '../../containers/header/header.container';
import { MenuContainer } from '../menu/containers/menu.container';
import { WelcomePopupContainer } from '../../../popup/containers/welcome-popup/welcome-popup.container';
import { ConnectionLostPopupContainer } from '../../../popup/containers/connection-lost-popup/connection-lost-popup.container';
import { MarginPopupContainer } from '../../../popup/containers/margin-popup/margin-popup.container';
import { ReloginPopupContainer } from '../../../popup/containers/relogin-popup/relogin-popup.container';
import { Preloader } from '../../../ui-kit/components/preloader/preloader.component';
import { ChatContainer } from '../../../chat/containers/chat.container';
import { Option } from 'fp-ts/lib/Option';
import * as O from 'fp-ts/lib/Option';
import { pipe } from 'fp-ts/lib/pipeable';
import { RegistrationContinueButton } from '../../../ui-kit/components/preview-mode/components/registration-open-button.component';

import * as css from './theme/layout.module.css';

type LayoutState = {
	menuIsOpened: boolean;
};

type LayoutProps = {
	children: ReactNode;
	loader: boolean;
	previewMode: Option<boolean>;
};

const combinedPopupContexts = combineContext(
	WelcomePopupContainer,
	MarginPopupContainer,
	ConnectionLostPopupContainer,
	ReloginPopupContainer,
	(WelcomePopupContainer, MarginPopupContainer, ConnectionLostPopupContainer, ReloginPopupContainer) => [
		WelcomePopupContainer,
		MarginPopupContainer,
		ConnectionLostPopupContainer,
		ReloginPopupContainer,
	],
);

export const Layout = combineContext(
	HeaderContainer,
	AsideContainer,
	MenuContainer,
	ChatContainer,
	combinedPopupContexts,
	(
		HeaderContainer,
		AsideContainer,
		MenuContainer,
		ChatContainer,
		[WelcomePopupContainer, MarginPopupContainer, ConnectionLostPopupContainer, ReloginPopupContainer],
	) =>
		class Layout extends Component<LayoutProps, LayoutState> {
			readonly state = {
				menuIsOpened: false,
			};

			public render() {
				const { children, previewMode } = this.props;

				return (
					<div className={css.container}>
						{this.props.loader ? (
							<Preloader />
						) : (
							<Fragment>
								<AsideContainer
									menuHandler={this.toggleMenu}
									menuCloser={this.closeMenu}
									menuIsOpened={this.state.menuIsOpened}
								/>
								<div className={css.content}>
									<div className={css.header}>
										<HeaderContainer />
									</div>
									<main className={css.main}>{children}</main>
								</div>
								<WelcomePopupContainer />
								<ConnectionLostPopupContainer />
								<MarginPopupContainer />
								<ReloginPopupContainer />
								<ChatContainer />
								{this.state.menuIsOpened && <MenuContainer onClose={this.toggleMenu} />}
								{pipe(
									previewMode,
									O.map(previewMode => previewMode && <RegistrationContinueButton />),
									O.toNullable,
								)}
							</Fragment>
						)}
					</div>
				);
			}

			toggleMenu = () => {
				this.setState({ menuIsOpened: !this.state.menuIsOpened });
			};

			closeMenu = () => {
				this.setState({ menuIsOpened: false });
			};
		},
);
