import { OrderTO } from '../../api/generated/swagger/definitions/OrderTO';
import { Option } from 'fp-ts/lib/Option';
import * as O from 'fp-ts/lib/Option';
import { OrderEntrySide } from '../../model/order-entry/order-entry.model';
import { some } from 'fp-ts/lib/Option';
import { OrdersSide, OrdersTableItem } from '../../model/orders/orders.model';
import { OrderTOFull } from './orders.view-model';
import { sequenceTOption } from '../../utils/option.utils';
import { pipe } from 'fp-ts/lib/pipeable';

export function getTriggerPrice(
	type: OrderTO['type'],
	amount: Option<number>,
	limitPrice: Option<number>,
	stopPrice: Option<number>,
): Option<number> {
	if (type === 'LIMIT') {
		return limitPrice;
	}
	if (type === 'STOP') {
		return stopPrice;
	}
	if (type === 'MARKET') {
		return amount;
	}
	return O.none;
}

export function getCurrentPrice(bid: Option<number>, ask: Option<number>, quantity: number): Option<number> {
	return pipe(
		sequenceTOption(bid, ask),
		O.map(([bid, ask]) => (quantity < 0 ? bid : ask)),
	);
}

export function getOrderSideByQuantity(quantity: number): OrderEntrySide {
	return quantity > 0 ? OrderEntrySide.Buy : OrderEntrySide.Sell;
}

export function getAmount(side: OrderEntrySide, bid: Option<number>, ask: Option<number>): Option<number> {
	switch (side) {
		case OrderEntrySide.Buy: {
			return bid;
		}
		case OrderEntrySide.Sell: {
			return ask;
		}
	}
}

export function getSignBySide(side: OrderEntrySide): -1 | 1 {
	switch (side) {
		case OrderEntrySide.Buy: {
			return 1;
		}
		case OrderEntrySide.Sell: {
			return -1;
		}
	}
}

export function getDistance(triggerPrice: number, currentPrice: number): number {
	return Math.abs(triggerPrice - currentPrice);
}

export const toOrderItem = (order: OrderTOFull): OrdersTableItem => {
	const { expireAt, orderId, quantity, stopLoss, symbol, takeProfit, triggerPrice, precision, currentPrice } = order;

	return {
		orderId,
		quantity: some(Math.abs(quantity)),
		distance: pipe(
			currentPrice,
			O.chain(currentPrice =>
				pipe(
					order.triggerPrice,
					O.map(triggerPrice => getDistance(triggerPrice, currentPrice)),
				),
			),
		),
		side: quantity > 0 ? OrdersSide.Buy : OrdersSide.Sell,
		stopLoss: pipe(
			stopLoss,
			O.chain(stopLoss => stopLoss.fixedPrice),
		),
		symbol,
		precision,
		takeProfit: pipe(
			takeProfit,
			O.chain(takeProfit => takeProfit.fixedPrice),
		),
		triggerPrice,
		validUntil: expireAt,
	};
};
