import * as React from 'react';
import { LineProp } from './line.model';
import { withTheme } from '@devexperts/react-kit/dist/utils/withTheme';
import { pipe } from 'fp-ts/lib/pipeable';
import * as O from 'fp-ts/lib/Option';
import * as css from './theme/line.component.module.css';

class RawLine extends React.Component<LineProp> {
	render() {
		const { yPosition, formattedPrice, label, theme } = this.props;
		const style = {
			top: `${yPosition}px`,
		};

		return (
			<div className={theme.container} style={style}>
				{pipe(
					label,
					O.map(label => <div className={theme.label}>{label}</div>),
					O.toNullable,
				)}
				<div className={theme.price}>{formattedPrice}</div>
			</div>
		);
	}
}

const wTheme = {
	...css,
};

export const Line = withTheme(Symbol(), wTheme)(RawLine);
