import { ask, combineContext, deferContext } from '@devexperts/rx-utils/dist/context.utils';
import { HistoryDashboard } from '../../components/history-dashboard/history-dashboard.component';
import { runOnMount } from '../../../../utils/react.utils';
import { withRXWithDefaults } from '../../../../utils/withRXWithDefaults';
import { pending } from '@devexperts/remote-data-ts';
import {
	createHistoryDashboardViewModel,
	HistoryDashboardViewModel,
} from '../../../../view-model/history/history-dashboard.view-model';

export type HistoryDashboardContainerContext = {
	historyDashboardViewModel: HistoryDashboardViewModel;
};

export const Container = combineContext(
	ask<HistoryDashboardContainerContext>(),
	HistoryDashboard,
	(ctx, HistoryDashboard) => {
		return withRXWithDefaults(HistoryDashboard)(() => {
			const {
				historyDashboardViewModel: { historyOrdersCount$ },
			} = ctx;

			return {
				props: {
					historyOrdersCount: historyOrdersCount$,
				},
				defaultProps: {
					historyOrdersCount: pending,
				},
			};
		});
	},
);

export const HistoryDashboardContainer = combineContext(
	createHistoryDashboardViewModel,
	deferContext(Container, 'historyDashboardViewModel'),
	(createHistoryDashboardViewModel, Container) =>
		runOnMount(Container, () => () =>
			createHistoryDashboardViewModel().map(historyDashboardViewModel => ({ historyDashboardViewModel })),
		),
);
