import { LiveData } from '@devexperts/rx-utils/dist/rd/live-data.utils';
import { ordersController } from '../../api/generated/swagger/controllers/OrdersController';
import { SocketClient } from '../../api/socket-client';
import { OrderTO } from '../../api/generated/swagger/definitions/OrderTO';
import { ProtectionOrderTO } from '../../api/generated/swagger/definitions/ProtectionOrderTO';
import { EntityStore } from '@devexperts/rx-utils/dist/rd/entity-store.utils';
import { isOrdersTO } from '../../api/socket/socket-message.to';
import { filter, map, mapTo, switchMap, tap, distinctUntilChanged } from 'rxjs/operators';
import { findFirst, getEq } from 'fp-ts/lib/Array';
import { failure, fromOption, isSuccess, success } from '@devexperts/remote-data-ts';
import { Observable } from 'rxjs';
import { sequenceTOption } from '../../utils/option.utils';
import { Option } from 'fp-ts/lib/Option';
import * as O from 'fp-ts/lib/Option';
import { uuid } from '@devexperts/utils/dist/string';
import { OrderTemplateTO } from '../../api/generated/swagger/definitions/OrderTemplateTO';
import { combineLatestRD } from '@devexperts/rx-utils/dist/rd/operators/combineLatestRD';
import { mapRD } from '@devexperts/rx-utils/dist/rd/operators/mapRD';
import { secondT } from '../../utils/tuple.utils';
import { constUndefined, constant } from 'fp-ts/lib/function';
import { Sink } from '@devexperts/rx-utils/dist/sink.utils';
import { ask, combineContext, fromReader } from '@devexperts/rx-utils/dist/context.utils';
import { createHandler } from '@devexperts/rx-utils/dist/create-handler.utils';
import { switchMapRD } from '@devexperts/rx-utils/dist/rd/operators/switchMapRD';
import { merge } from 'rxjs/internal/observable/merge';
import { eqString } from 'fp-ts/lib/Eq';
import { pipe } from 'fp-ts/lib/pipeable';

export type UpdateOrderData = {
	orderId: string;
	data: OrderData;
};

export type OrderData = {
	requestId: Option<string>;
	accountId: string;
	instrumentId: number;
	bid: number;
	ask: number;
	quantity: number;
	side: OrderTemplateTO['orderSide'];
	expireAt: Option<Date>;
	executionPrice: Option<number>;
	takeProfit: Option<ProtectionOrderTO>;
	stopLoss: Option<ProtectionOrderTO>;
	orderChainId: Option<string>;
	distance: number;
};

export type OrderStore = Readonly<{
	place: (
		accountId: string,
		instrumentId: number,
		bid: number,
		ask: number,
		quantity: number,
		side: OrderTemplateTO['orderSide'],
		expireAt: Option<Date>,
		executionPrice: Option<number>,
		takeProfit: Option<ProtectionOrderTO>,
		stopLoss: Option<ProtectionOrderTO>,
		distance: number,
	) => LiveData<Error, void>;
	updateOrder: (data: UpdateOrderData) => void;
	getById: (id: number) => LiveData<Error, OrderTO>;
	all$: LiveData<Error, OrderTO[]>;
	error$: Observable<Option<Error>>;
	remove: (id: number) => LiveData<Error, void>;
	keys$: Observable<string[]>;
}>;

export type OrderStoreContext = {
	socketClient: SocketClient;
};

export type CreateOrderStore = Sink<() => OrderStore>;

export const createOrderStore = combineContext(
	fromReader(ordersController),
	ask<OrderStoreContext>(),
	(ordersController, e): CreateOrderStore => {
		const { socketClient } = e;
		const pk = (order: OrderTO) => order.orderId.toString();

		const store = new EntityStore<Error, OrderTO>();

		const error = createHandler<Option<Error>>();
		const error$ = error.value$;

		const orderMessages$ = socketClient.message$.pipe(filter(isOrdersTO));

		const orders$: Observable<OrderTO[]> = orderMessages$.pipe(
			map(message => message.body),
			map(orders => orders.filter(order => filterWorkingOrderPredicate(order))),
		);

		const all$ = store.getAll(pk, () => orders$.pipe(map(to => success(to))));
		// TODO: delete distinctUntilChanged after #111 (dx-platform)
		const keys$ = store.keys$.pipe(distinctUntilChanged(getEq(eqString).equals));

		const removeFromStore = (id: number) => (): LiveData<Error, Array<OrderTO>> =>
			all$.pipe(mapRD(orders => orders.filter(order => order.orderId !== id)));

		const removeEffect$ = orderMessages$.pipe(
			map(message => message.body),
			map(orders => orders.filter(order => filterCompletedAndCancelledOrdersPredicate(order))),
			tap(ordersToRemove =>
				ordersToRemove.forEach(({ orderId }) => store.remove(String(orderId), pk, removeFromStore(orderId))),
			),
		);

		const place = (
			accountId: string,
			instrumentId: number,
			bid: number,
			ask: number,
			quantity: number,
			side: OrderTemplateTO['orderSide'],
			expireAt: Option<Date>,
			executionPrice: Option<number>,
			takeProfit: Option<ProtectionOrderTO>,
			stopLoss: Option<ProtectionOrderTO>,
			distance: number,
		): LiveData<Error, void> =>
			store
				.create(pk, () => {
					const requestId = uuid();

					const request$ = ordersController.placeSingleOrder({
						body: buildOrderTemplateTO({
							accountId,
							instrumentId,
							bid,
							ask,
							quantity,
							side,
							takeProfit,
							stopLoss,
							requestId: O.some(requestId),
							expireAt,
							executionPrice,
							orderChainId: O.none,
							distance,
						}),
					});

					const order$ = orderMessages$.pipe(
						map(orders =>
							pipe(
								findFirst(orders.body, item =>
									pipe(
										item.requestId,
										O.exists(id => id === requestId),
									),
								),
								O.fold(
									() =>
										failure<Error, OrderTO>(
											new Error(`Order with id ${requestId} not found in orders.store`),
										),
									order => success<Error, OrderTO>(order),
								),
							),
						),
						map(order =>
							order.chain(order => {
								if (order.status === 'REJECTED') {
									const err = new Error(order.statusDescription);
									error.handle(O.some(err));
									return failure<Error, OrderTO>(err);
								}
								error.handle(O.none);
								return success(order);
							}),
						),
						filter(order => order.exists(filterWorkingOrderPredicate)),
					);

					return combineLatestRD(request$, order$).pipe(mapRD(secondT));
				})
				.pipe(mapRD(constUndefined));

		const getById = (id: number): LiveData<Error, OrderTO> =>
			all$.pipe(
				map(orders =>
					orders.chain(order =>
						fromOption(
							findFirst(order, data => data.orderId === id),
							() => new Error(`Order with id ${id} not found in orders.store`),
						),
					),
				),
			);

		const updateOrderInStore = (id: number, data: OrderTO) => (): LiveData<Error, OrderTO> =>
			all$.pipe(
				map(orders =>
					orders.chain(order =>
						pipe(
							findFirst(order, data => data.orderId === id),
							O.fold(
								() =>
									failure<Error, OrderTO>(new Error(`Order with id ${id} not found in orders.store`)),
								() => success(data),
							),
						),
					),
				),
			);

		const updateOrder = createHandler<UpdateOrderData>();

		const sendRequestToUpdateOrder = (data: UpdateOrderData) => {
			const requestId = uuid();
			const dataTemplateTO = { ...buildOrderTemplateTO(data.data), requestId: O.some(requestId) };
			return ordersController.modifySingleOrder({ body: dataTemplateTO }).pipe(
				filter(isSuccess),
				mapTo(data),
			);
		};

		const subscribeToOrdersAndUpdateStore = (data: UpdateOrderData) =>
			orderMessages$.pipe(
				map(orders =>
					pipe(
						findFirst(orders.body, item => item.orderId.toString() === data.orderId),
						O.fold(
							() =>
								failure<Error, OrderTO>(
									new Error(`Order with id ${data.orderId} not found in orders.store`),
								),
							order => success<Error, OrderTO>(order),
						),
					),
				),
				map(order =>
					order.chain(order => {
						if (order.status === 'REJECTED') {
							const err = new Error(order.statusDescription);
							error.handle(O.some(err));
							return failure<Error, OrderTO>(err);
						}
						error.handle(O.none);
						return success(order);
					}),
				),
				filter(isSuccess),
				mapRD(order => store.update(data.orderId, updateOrderInStore(Number(data.orderId), order))),
			);

		const updateEffects$ = updateOrder.value$.pipe(
			switchMap(data => sendRequestToUpdateOrder(data)),
			switchMap(data => subscribeToOrdersAndUpdateStore(data)),
		);

		const remove = (id: number): LiveData<Error, void> =>
			all$.pipe(
				map(orders =>
					orders.chain(orders =>
						pipe(
							findFirst(orders, order => order.orderId === id),
							O.fold(
								() =>
									failure<Error, OrderTO>(new Error(`Order with id ${id} not found in orders.store`)),
								order => success<Error, OrderTO>(order),
							),
						),
					),
				),
				switchMapRD(order =>
					ordersController.cancelOrder({
						query: {
							accountId: order.accountId,
							orderChainId: order.orderChainId,
						},
					}),
				),
				mapTo(success(undefined)),
			);

		return new Sink(
			() => ({
				place,
				all$,
				getById,
				error$,
				remove,
				updateOrder: updateOrder.handle,
				keys$,
			}),
			merge(all$, updateEffects$, removeEffect$),
		);
	},
);

function buildOrderTemplateTO(data: OrderData): OrderTemplateTO {
	const {
		accountId,
		instrumentId,
		bid,
		ask,
		quantity,
		side,
		takeProfit,
		stopLoss,
		requestId,
		expireAt,
		executionPrice,
		orderChainId,
		distance,
	} = data;
	const currentPrice = getCurrentPrice(side, bid, ask);
	const orderType = getOrderType(executionPrice, currentPrice, distance, side);
	const stopPrice = orderType === 'STOP' ? executionPrice : O.none;
	const limitPrice = orderType === 'LIMIT' ? executionPrice : O.none;
	const timeInForce = O.isSome(expireAt) ? 'GTD' : 'GTC';
	const currentQuantity = getCurrentQuantity(side, quantity);

	return {
		accountId,
		instrumentId,
		quantity: currentQuantity,
		orderType,
		timeInForce,
		limitPrice,
		stopPrice,
		stopPriceOffset: O.none,
		ocoGroupCode: O.none,
		refOrderChainId: orderChainId,
		positionKey: O.none,
		effect: 'OPENING',
		orderSide: side,
		expireAt,
		alertExpression: O.none,
		directExchange: O.none,
		additionalParameters: O.none,
		takeProfit,
		stopLoss,
		requestId,
	};
}

function getCurrentPrice(side: OrderTemplateTO['orderSide'], bid: number, ask: number): Option<number> {
	return pipe(
		side,
		O.map(sideVal => {
			switch (sideVal) {
				case 'BUY': {
					return ask;
				}
				case 'SELL': {
					return bid;
				}
			}
		}),
	);
}

export function getOrderType(
	executionPrice: Option<number>,
	currentPrice: Option<number>,
	distance: number,
	side: OrderTemplateTO['orderSide'],
): OrderTemplateTO['orderType'] {
	return pipe(
		sequenceTOption(executionPrice, currentPrice, side),
		O.map(([price, currentPrice, side]) => {
			// please refer to https://jira.in.devexperts.com/browse/DXREQ-10550?focusedCommentId=1956701&page=com.atlassian.jira.plugin.system.issuetabpanels:comment-tabpanel#comment-1956701
			switch (side) {
				case 'BUY': {
					if (price >= currentPrice + distance) {
						return 'STOP';
					}

					if (price <= currentPrice - distance) {
						return 'LIMIT';
					}
				}
				case 'SELL': {
					if (price >= currentPrice + distance) {
						return 'LIMIT';
					}

					if (price <= currentPrice - distance) {
						return 'STOP';
					}
				}
				default:
					return 'MARKET';
			}
		}),
		O.getOrElse<OrderTemplateTO['orderType']>(constant('MARKET')),
	);
}

function getCurrentQuantity(side: OrderTemplateTO['orderSide'], quantity: number): number {
	return pipe(
		side,
		O.map(sideVal => {
			switch (sideVal) {
				case 'BUY':
					return quantity;
				case 'SELL':
					return -quantity;
			}
		}),
		O.getOrElse(() => quantity),
	);
}

function filterWorkingOrderPredicate(order: OrderTO) {
	const statusWorking: Array<OrderTO['status']> = [
		'WORKING',
		'WAITING_LIMIT',
		'WAITING_STOP',
		'WAITING_TRIGGER',
		'EXECUTING',
	];
	const foundStatusWorking = findFirst(statusWorking, status => status === order.status);
	return O.isSome(foundStatusWorking);
}

function filterCompletedAndCancelledOrdersPredicate(order: OrderTO) {
	const statusWorking: Array<OrderTO['status']> = ['CANCELED', 'COMPLETED'];
	const foundStatusWorking = findFirst(statusWorking, status => status === order.status);
	return O.isSome(foundStatusWorking);
}
