import { combineContext, ask, deferContext } from '@devexperts/rx-utils/dist/context.utils';
import { withRXWithDefaults } from '../../../../utils/withRXWithDefaults';
import { createOrderEditViewModel, OrderEditViewModel } from '../../../../view-model/orders/order-edit.view-model';
import { runOnMount } from '../../../../utils/react.utils';
import { OrderEdit } from '../../components/order-edit/order-edit.component';
import { pending } from '@devexperts/remote-data-ts';
import { createSelector } from 'reselect';

type EditContainerContext = {
	orderEditViewModel: OrderEditViewModel;
};

const Container = combineContext(OrderEdit, ask<EditContainerContext>(), (OrderEdit, context) =>
	withRXWithDefaults(OrderEdit)(() => {
		const {
			orderEditViewModel: { orderEditData$ },
		} = context;

		return {
			defaultProps: {
				data: pending,
			},
			props: {
				data: orderEditData$,
			},
		};
	}),
);

export const OrderEditContainer = combineContext(
	deferContext(Container, 'orderEditViewModel'),
	createOrderEditViewModel,
	(Container, createOrderEditViewModel) =>
		runOnMount(Container, props => {
			const orderId = createSelector(
				props,
				props => props.orderId,
			);
			return createSelector(
				orderId,
				orderId => ({
					orderEditViewModel: createOrderEditViewModel(orderId),
				}),
			);
		}),
);
