import * as React from 'react';
import { Component, createRef } from 'react';
import '@dx-private/dxchart5/dist/css/chart-base.css';
import '@dx-private/dxchart5/dist/chart-combined.js';
import ChartBootstrap from '@dx-private/dxchart5/dist/js/chart/bootstrap';
import EventBus from '@dx-private/dxchart5/dist/js/chart/EventBus';
import Candle from '@dx-private/dxchart5/dist/js/chart/model/Candle';
import { withTheme } from '@devexperts/react-kit/dist/utils/withTheme';
import { PartialKeys } from '@devexperts/utils/dist/object/object';
import { withDefaults } from '@devexperts/react-kit/dist/utils/with-defaults';
import { setoidStrict } from '@devexperts/utils/dist/typeclasses/setoid/setoid.utils';
import CrossTool from '../../../../ui-common/chart/plugin/cross-tool/cross-tool';
import CrossToolViewModel from '../../../../ui-common/chart/plugin/cross-tool/models/cross-tool.view-model';
import { createAVAChartInfoView } from '../../../../ui-common/chart/plugin/cross-tool/views/ava-chart-info-view';
import { MakeTheme } from '../../../../utils/theme.utils';
import { ChartType } from '../toolbar/toolbar.model';
import { ChartYAxisPosition, TInstrument, Candle as CandleType, Plugin, PeriodRangePair, BarType } from './chart.model';
import { mainConfig } from './chart.config';
import { ChartGraph } from './chart-graph.component';
import { ChartMap } from './chart-map.component';
import * as cn from 'classnames';
import * as css from './theme/chart.module.css';

export type RawChartProps = {
	symbol: string;
	precision: number;
	historical: CandleType[];
	periodRangePair: PeriodRangePair;
	plugins: Plugin[];
	last: CandleType[];
	selectedType: ChartType;
	isCrossToolEnabled: boolean;
	theme: MakeTheme<'wrapper' | 'container' | 'axis' | 'axis_x' | 'axisWrapper_x'>;
};

const CANDLES_ON_CHART = 45;

class RawChart extends Component<RawChartProps> {
	private ref = createRef<HTMLDivElement>();
	private chart?: ChartBootstrap;
	private crosstoolViewModel = new CrossToolViewModel();
	private requestAnimationFrameId: number | undefined;

	componentDidMount() {
		const {
			plugins,
			isCrossToolEnabled,
			historical,
			selectedType,
			symbol,
			precision,
			periodRangePair: { period, range },
		} = this.props;
		this.crosstoolViewModel.isEnabled = isCrossToolEnabled;

		if (this.ref.current) {
			const chartConfig = {
				...mainConfig,
				components: {
					...mainConfig.components,
					chart: {
						...((mainConfig.components && mainConfig.components.chart) || {}),
						type: this.typeToString(selectedType),
					},
				},
			};
			const defaultPlugins: Plugin[] = [new CrossTool(this.crosstoolViewModel, createAVAChartInfoView)];

			const chartInstance = new window.chart.ChartBootstrap(this.ref.current, chartConfig);
			this.chart = chartInstance;
			const instrument = createInstrument(symbol, precision);
			this.chart.setInstrument(instrument);
			this.updateHistorical(instrument, historical, period, range, this.chart.bus);
			// FIXME: until chart plugin will not correctly scale on initialize
			this.updateChartScale();
			[...defaultPlugins, ...plugins].forEach(plugin => plugin.register(chartInstance.registerPlugin));
		}
	}

	shouldComponentUpdate(nextProps: Readonly<RawChartProps>): boolean {
		if (this.crosstoolViewModel && nextProps.isCrossToolEnabled !== this.props.isCrossToolEnabled) {
			this.crosstoolViewModel.isEnabled = nextProps.isCrossToolEnabled;
		}
		if (this.chart) {
			const isPeriodAndRangeChanged =
				this.props.periodRangePair.period !== nextProps.periodRangePair.period ||
				this.props.periodRangePair.range !== nextProps.periodRangePair.range;

			if (
				nextProps.symbol !== this.props.symbol ||
				nextProps.historical !== this.props.historical ||
				isPeriodAndRangeChanged
			) {
				const { period, range } = nextProps.periodRangePair;

				const instrument = createInstrument(nextProps.symbol, this.props.precision);
				this.chart.setInstrument(instrument);
				this.updateHistorical(instrument, nextProps.historical, period, range, this.chart.bus);
			}

			if (nextProps.last && !setoidStrict.equals(this.props.last, nextProps.last)) {
				this.updateLastCandles(nextProps.last, this.chart.bus);
			}

			if (nextProps.selectedType !== this.props.selectedType) {
				this.chart.setChartType(this.typeToString(nextProps.selectedType));
				this.updateLastCandles(nextProps.last, this.chart.bus);
			}
		}

		return false;
	}

	componentWillUnmount() {
		this.requestAnimationFrameId && cancelAnimationFrame(this.requestAnimationFrameId);
	}

	render() {
		const { theme } = this.props;

		const chartAxisClassName = cn(theme.axis, theme.axis_x);

		return (
			<div data-chart-container className={theme.wrapper}>
				<div className={theme.container} ref={this.ref}>
					<ChartGraph yAxisPosition={ChartYAxisPosition.Right} />

					<div data-element="underlaySibling" className={theme.axisWrapper_x}>
						<div data-element="xAxis" className={chartAxisClassName} />
					</div>
					<ChartMap />
				</div>
			</div>
		);
	}

	private updateChartScale = () => {
		const { historical } = this.props;
		if (this.chart && historical.length >= CANDLES_ON_CHART) {
			this.chart.setXScale(historical.length - CANDLES_ON_CHART, historical.length);
		}
		this.requestAnimationFrameId = requestAnimationFrame(() => this.updateChartScale());
	};

	private updateHistorical(
		instrument: TInstrument,
		historical: CandleType[],
		period: number,
		range: number,
		bus: EventBus,
	): void {
		if (this.chart) {
			if (historical.length > 0) {
				bus.fire(bus.types.LOADING, {
					instrument,
					period,
					range,
				});
			}
			this.chart.setCandles(historical as Candle[]);

			if (historical.length >= CANDLES_ON_CHART) {
				this.chart.setXScale(historical.length - CANDLES_ON_CHART, historical.length);
			}
		}
	}

	private updateLastCandles(candles: CandleType[], bus: EventBus) {
		bus.fire(bus.types.CANDLE_CAME, candles);
		/* todo remove next after chart version update */
		bus.fire(bus.types.RESIZED);
	}

	private typeToString = (type: ChartType): BarType => {
		switch (type) {
			case ChartType.Candle:
				return BarType.CANDLE;
			case ChartType.Bar:
				return BarType.BAR;
			case ChartType.Line:
				return BarType.LINE;
		}
	};
}

export type ChartProps = PartialKeys<RawChartProps, 'plugins'>;
export const Chart = withTheme(Symbol(), css)(
	withDefaults<RawChartProps, 'plugins'>({
		plugins: [],
	})(RawChart),
);

function createInstrument(code: string, precision: number): TInstrument {
	return {
		code,
		pricePrecisions: [precision, 1.0000000001, precision],
		type: 'FOREX',
	};
}
