import * as React from 'react';
import { PureComponent } from 'react';
import { WithTranslation, withTranslation } from 'react-i18next';
import { combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { RemoteData } from '@devexperts/remote-data-ts';
import { some } from 'fp-ts/lib/Option';
import { RemoteDataState } from '../../../../ui-common/remote-data-state/remote-data-state.component';
import { ChartLoaderContainer } from '../../../chart/containers/chart-loader/chart-loader.container';
import { CurrentPriceLinePlugin } from '../../../chart/plugin/current-price-line/current-price-line-plugin';
import { LinesPlugin } from '../../../chart/plugin/lines/lines-plugin';
import { Plugin } from '../../../../ui-common/chart/plugin/plugin.model';
import { tspan } from '../../../../utils/i18n.utils';
import * as css from './theme/positions-dashboard.module.css';

type ChartData = {
	costBasis: number;
	symbol: string;
};

type PositionDashboardChartProps = {
	data: RemoteData<Error, ChartData>;
};

export const PositionDashboardChart = combineContext(ChartLoaderContainer, ChartLoaderContainer =>
	withTranslation()(
		class PositionDashboardChart extends PureComponent<PositionDashboardChartProps & WithTranslation> {
			render() {
				const { data } = this.props;

				return (
					<div>
						<RemoteDataState data={data} success={this.renderSuccess} />
					</div>
				);
			}

			private renderSuccess(data: ChartData): JSX.Element {
				const { symbol, costBasis } = data;

				const plugins: Plugin[] = [
					new CurrentPriceLinePlugin(),
					new LinesPlugin([
						{
							price: costBasis,
							label: some(tspan('positionDashboardChartLineOpenPrice', 'Open Price')),
						},
					]),
				];

				return (
					<div className={css.chart}>
						<ChartLoaderContainer symbol={symbol} plugins={plugins} />
					</div>
				);
			}
		},
	),
);
