import { unknownType } from '../utils/utils';
import { Option } from 'fp-ts/lib/Option';
import { string, number, dictionary, type } from 'io-ts';
import { createOptionFromNullable } from 'io-ts-types';

export type AccountMetricsTO = {
	accountId: string;
	availableFunds: Option<number>;
	balance: Option<number>;
	equity: Option<number>;
	margin: Option<number>;
	riskLevel: Option<number>;
	profitLoss: Option<number>;
	allMetrics: { [key: string]: unknown };
};
export const AccountMetricsTOIO = type({
	accountId: string,
	availableFunds: createOptionFromNullable(number),
	balance: createOptionFromNullable(number),
	equity: createOptionFromNullable(number),
	margin: createOptionFromNullable(number),
	riskLevel: createOptionFromNullable(number),
	profitLoss: createOptionFromNullable(number),
	allMetrics: dictionary(string, unknownType),
});
