import { StudyParamValidationTO, StudyParamValidationTOIO } from '../definitions/StudyParamValidationTO';
import { unknownType } from '../utils/utils';
import { Option } from 'fp-ts/lib/Option';
import { string, union, literal, type } from 'io-ts';
import { createOptionFromNullable } from 'io-ts-types';

export type StudyParamTO = {
	id: Option<string>;
	studyParamType: Option<
		| 'INTEGER_RANGE'
		| 'DOUBLE_RANGE'
		| 'PRICE_FIELD'
		| 'STRING'
		| 'AGGREGATION'
		| 'BOOLEAN'
		| 'AVERAGE'
		| 'UNDEFINED'
	>;
	value: Option<unknown>;
	validationTO: Option<StudyParamValidationTO>;
};
export const StudyParamTOIO = type({
	id: createOptionFromNullable(string),
	studyParamType: createOptionFromNullable(
		union([
			literal('INTEGER_RANGE'),
			literal('DOUBLE_RANGE'),
			literal('PRICE_FIELD'),
			literal('STRING'),
			literal('AGGREGATION'),
			literal('BOOLEAN'),
			literal('AVERAGE'),
			literal('UNDEFINED'),
		]),
	),
	value: createOptionFromNullable(unknownType),
	validationTO: createOptionFromNullable(StudyParamValidationTOIO),
});
