import * as React from 'react';
import { PureComponent, Fragment, ReactNode } from 'react';
import {
	Popup as DXPopup,
	POPUP,
	TPopupProps as DXPopupProps,
} from '@devexperts/react-kit/dist/components/Popup/Popup';
import { withTheme } from '@devexperts/react-kit/dist/utils/withTheme';
import { Omit } from 'typelevel-ts';
import { CloseButton } from '../../../../ui-common/close-button/close-button.component';
import * as css from './theme/popup.module.css';
import * as closeButtonCss from './theme/close-button.child.module.css';

export type PopupProps = Omit<DXPopupProps, 'header' | 'theme'> & {
	theme: {
		title?: string;
		close?: string;
	} & DXPopupProps['theme'];
	title?: ReactNode;
	onClose: () => void;
};

class RawPopup extends PureComponent<PopupProps> {
	render() {
		return <DXPopup {...this.props} header={this.renderHeader()} />;
	}

	private renderHeader = () => {
		const { theme, title, onClose } = this.props;

		return (
			<Fragment>
				<div className={theme.title}>{title}</div>
				<CloseButton onClick={onClose} theme={closeButtonCss} />
			</Fragment>
		);
	};
}

export const Popup = withTheme(POPUP, css)(RawPopup);
