import { combineContext, ask } from '@devexperts/rx-utils/dist/context.utils';
import { OrderStore } from '../../store/order/order.store';
import { mapRD } from '@devexperts/rx-utils/dist/rd/operators/mapRD';
import { InstrumentStore } from '../../store/instrument/instrument.store';
import { switchMapRD } from '@devexperts/rx-utils/dist/rd/operators/switchMapRD';
import { LiveData, LiveDataOption } from '@devexperts/rx-utils/dist/rd/live-data.utils';
import { distinctRDOptionNumber } from '../../utils/setoid.utils';
import { first } from 'rxjs/internal/operators';
import { pipe } from 'fp-ts/lib/pipeable';
import * as O from 'fp-ts/lib/Option';

export type OrderDashboardChartViewModel = {
	symbol$: LiveData<Error, string>;
	costBasis$: LiveDataOption<Error, number>;
};

type CreateOrderDashboardChart = (orderId: number) => OrderDashboardChartViewModel;

type OrderDashboardChartContext = {
	orderStore: OrderStore;
	instrumentStore: InstrumentStore;
};

export const createOrderDashboardChartViewModel = combineContext(
	ask<OrderDashboardChartContext>(),
	(context): CreateOrderDashboardChart => orderId => {
		const { orderStore, instrumentStore } = context;
		const order$ = orderStore.getById(orderId);

		const costBasis$ = order$.pipe(
			mapRD(order =>
				pipe(
					order.limitPrice,
					O.alt(() => order.stopPrice),
				),
			),
			distinctRDOptionNumber,
		);
		const symbol$ = order$.pipe(
			switchMapRD(order =>
				instrumentStore.getById(order.instrumentId).pipe(mapRD(instrument => instrument.symbol)),
			),
			first(),
		);

		return {
			symbol$,
			costBasis$,
		};
	},
);
