import { withRXWithDefaults } from '../../../../utils/withRXWithDefaults';
import { pending } from '@devexperts/remote-data-ts';
import { ask, combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { WatchlistStore } from '../../../../store/watchlist/watchlist.store';
import { LiveData } from '@devexperts/rx-utils/dist/rd/live-data.utils';
import { TradeDashboard } from '../../components/trade-dashboard/trade-dashboard.component';
import { map } from 'rxjs/operators';
import { distinctRDString } from '../../../../utils/setoid.utils';
import { toCategoryId } from './trade-dashboard.utils';

type TradeDashboardContainerContext = {
	watchlistStore: WatchlistStore;
};

export const TradeDashboardContainer = combineContext(
	TradeDashboard,
	ask<TradeDashboardContainerContext>(),
	(TradeDashboard, e) =>
		withRXWithDefaults(TradeDashboard)(() => {
			const { watchlistStore } = e;
			const firstCategoryId$: LiveData<Error, string> = watchlistStore.private$.pipe(
				map(toCategoryId),
				distinctRDString,
			);

			return {
				props: {
					firstCategoryId: firstCategoryId$,
				},
				defaultProps: {
					firstCategoryId: pending,
				},
			};
		}),
);
