import * as React from 'react';
import { Fragment, ReactNode, memo, useCallback } from 'react';
import { WithTranslation, withTranslation } from 'react-i18next';
import { format } from 'date-fns';
import { Option } from 'fp-ts/lib/Option';
import { combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { RemoteData } from '@devexperts/remote-data-ts';
import { OrderDetailsData } from './order-details.model';
import { RouteLink } from '../route-link/containers/route-link.container';
import { tspan } from '../../utils/i18n.utils';
import { OrdersSide } from '../../model/orders/orders.model';
import { SymbolInfoItem } from '../symbol-info-item/symbol-info-item.component';
import { SymbolComponent } from '../symbol/symbol';
import { Optional } from '../optional/optional.component';
import { PriceDirection } from '../price-direction/price-direction.component';
import { RemoteDataState } from '../remote-data-state/remote-data-state.component';
import { TPipSettings, PipRate } from '../pip-rate/pip-rate.component';
import { MakeTheme } from '../../utils/theme.utils';
import * as O from 'fp-ts/lib/Option';
import { pipe } from 'fp-ts/lib/pipeable';
import { constant } from 'fp-ts/lib/function';
import * as css from './theme/order-details.module.css';
import * as symbolCss from './theme/symbol.child.module.css';

const DEFAULT_PIP_SETTINGS = { start: 0, length: 2 };

const directionTheme = { icon: css.icon };
const labelTheme = { label: css.label };

export type OrderDetailsProps = {
	data: RemoteData<Error, OrderDetailsData>;
	theme?: MakeTheme<'item' | 'item_secondary' | 'label' | 'value' | 'container' | 'title' | 'subtitle'>;
} & WithTranslation;

export const OrderDetailsCommon = combineContext(RouteLink, RouteLink =>
	withTranslation()(
		memo((props: OrderDetailsProps) => {
			const { data, t, theme } = props;

			const localizeOrderDetailsSide = useCallback(
				(ordersSide: OrdersSide) => {
					switch (ordersSide) {
						case OrdersSide.Buy: {
							return tspan('orderDetailsSideBuy', 'Buy');
						}
						case OrdersSide.Sell: {
							return tspan('orderDetailsSideSell', 'Sell');
						}
					}
				},
				[t],
			);

			const renderSuccess = useCallback(
				(orderDetails: OrderDetailsData): JSX.Element => {
					const {
						orderId,
						symbol,
						side,
						triggerPrice,
						currentPrice,
						changeDirection,
						distance,
						quantity,
						validUntil,
						openDateTime,
						sizeInLots,
						takeProfit,
						stopLoss,
						precision,
						pipSettings,
					} = orderDetails;
					const renderPipRate = getPipRateRenderer(pipSettings, precision);

					return (
						<Fragment>
							<SymbolComponent title={`ID ${orderId}`} subtitle={symbol} theme={theme || symbolCss} />
							<SymbolInfoItem theme={theme || labelTheme} label={tspan('orderDetailsSide', 'Side')}>
								<div className={getSideClassName(side)}>{localizeOrderDetailsSide(side)}</div>
							</SymbolInfoItem>
							<SymbolInfoItem
								theme={theme || labelTheme}
								label={tspan('orderDetailsTriggerPrice', 'Trigger price')}>
								<Optional
									value={pipe(
										triggerPrice,
										O.map(renderPipRate),
									)}
								/>
							</SymbolInfoItem>
							<SymbolInfoItem
								theme={theme || labelTheme}
								label={tspan('orderDetailsCurrentPrice', 'Current price')}>
								<Optional
									value={pipe(
										currentPrice,
										O.map(currentPrice => (
											<Fragment>
												{pipe(
													changeDirection,
													O.map(direction => (
														<PriceDirection direction={direction} theme={directionTheme} />
													)),
													O.toNullable,
												)}
												{renderPipRate(currentPrice)}
											</Fragment>
										)),
									)}
								/>
							</SymbolInfoItem>
							<SymbolInfoItem
								theme={theme || labelTheme}
								label={tspan('orderDetailsDistance', 'Distance')}>
								<Optional
									value={pipe(
										distance,
										O.map(renderPipRate),
									)}
								/>
							</SymbolInfoItem>
							<SymbolInfoItem
								theme={theme || labelTheme}
								label={tspan('orderDetailsQuantity', 'Quantity')}>
								{quantity}
							</SymbolInfoItem>
							<SymbolInfoItem
								theme={theme || labelTheme}
								label={tspan('orderDetailsValidUntil', 'Valid until')}>
								<Optional
									value={pipe(
										validUntil,
										O.map(renderFormattedDate),
									)}
								/>
							</SymbolInfoItem>
							<SymbolInfoItem
								theme={theme || labelTheme}
								label={tspan('orderDetailsOpenDateTime', 'Open date time')}>
								{renderFormattedDate(openDateTime)}
							</SymbolInfoItem>
							<SymbolInfoItem
								theme={theme || labelTheme}
								label={tspan('orderDetailsSizeInLots', 'Size in lots')}>
								{sizeInLots}
							</SymbolInfoItem>
							<SymbolInfoItem
								theme={theme || labelTheme}
								label={tspan('orderDetailsTakeProfit', 'Take profit')}>
								<Optional
									value={pipe(
										takeProfit,
										O.map(renderPipRate),
									)}
								/>
							</SymbolInfoItem>
							<SymbolInfoItem
								theme={theme || labelTheme}
								label={tspan('orderDetailsStopLoss', 'Stop loss')}>
								<Optional
									value={pipe(
										stopLoss,
										O.map(renderPipRate),
									)}
								/>
							</SymbolInfoItem>
						</Fragment>
					);
				},
				[t, getSideClassName, localizeOrderDetailsSide, renderFormattedDate],
			);

			return <RemoteDataState data={data} success={renderSuccess} />;
		}),
	),
);

function getSideClassName(side: OrdersSide): string {
	switch (side) {
		case OrdersSide.Buy: {
			return css.green;
		}
		case OrdersSide.Sell: {
			return css.red;
		}
	}
}

const getPipRateRenderer = (settings: Option<TPipSettings>, precision: number) => (value: number): ReactNode => (
	<PipRate
		value={value}
		rise={pipe(
			settings,
			O.getOrElse(constant(DEFAULT_PIP_SETTINGS)),
		)}
		digits={precision}
	/>
);

function renderFormattedDate(date: Date): ReactNode {
	const seconds = format(date, ':ss');
	const mainPart = format(date, 'dd.mm.yyyy hh:mm');

	return (
		<Fragment>
			{mainPart}
			<span className={css.seconds}>{seconds}</span>
		</Fragment>
	);
}
