import * as O from 'fp-ts/lib/Option';
import { pipe } from 'fp-ts/lib/pipeable';
import { constant, constFalse } from 'fp-ts/lib/function';
import { CrossToolMode } from './chart-loader.model';
import { CandleTO } from '../../../../api/generated/swagger/definitions/CandleTO';
import Candle from '@dx-private/dxchart5/dist/js/chart/model/Candle';

export function booleanToMode(isEnabled: boolean): CrossToolMode {
	return isEnabled ? CrossToolMode.Crosshair : CrossToolMode.Disabled;
}

export function isCrosshair(mode: CrossToolMode) {
	return mode === CrossToolMode.Crosshair;
}

export function toChartCandle(to: CandleTO): Candle {
	return new window.chart.Candle(
		pipe(
			to.high,
			O.getOrElse(constant(0)),
		),
		pipe(
			to.low,
			O.getOrElse(constant(0)),
		),
		pipe(
			to.open,
			O.getOrElse(constant(0)),
		),
		pipe(
			to.close,
			O.getOrElse(constant(0)),
		),
		to.timestamp.getTime(),
		pipe(
			to.volume,
			O.getOrElse(constant(0)),
		),
		pipe(
			to.expansion,
			O.getOrElse(constFalse),
		),
		0,
	);
}
