import { Option } from 'fp-ts/lib/Option';
import { number, array, boolean, type } from 'io-ts';
import { createOptionFromNullable } from 'io-ts-types';

export type PriceIncrementsTO = {
	priceIncrements: Option<Array<number>>;
	pricePrecisions: Option<Array<number>>;
	bondFraction: Option<boolean>;
};
export const PriceIncrementsTOIO = type({
	priceIncrements: createOptionFromNullable(array(number)),
	pricePrecisions: createOptionFromNullable(array(number)),
	bondFraction: createOptionFromNullable(boolean),
});
