import { ask, combineContext, deferContext } from '@devexperts/rx-utils/dist/context.utils';
import { withRXWithDefaults } from '../../../../utils/withRXWithDefaults';
import { pending } from '@devexperts/remote-data-ts';
import { HistoryDetails } from '../../components/history-details/history-details.component';
import { runOnMount } from '../../../../utils/react.utils';
import {
	HistoryDetailsViewModel,
	createHistoryDetailsViewModel,
} from '../../../../view-model/history/history-details.view-model';
import { createSelector } from 'reselect';

type HistoryDetailsContainerContext = {
	historyDetailsViewModel: HistoryDetailsViewModel;
};

export const Container = combineContext(ask<HistoryDetailsContainerContext>(), HistoryDetails, (e, HistoryDetails) =>
	withRXWithDefaults(HistoryDetails)(() => {
		const { data$ } = e.historyDetailsViewModel;

		return {
			props: { data: data$ },
			defaultProps: {
				data: pending,
			},
		};
	}),
);

export const HistoryDetailsContainer = combineContext(
	deferContext(Container, 'historyDetailsViewModel'),
	createHistoryDetailsViewModel,
	(Container, createHistoryDetailsViewModel) =>
		runOnMount(Container, props => {
			const orderId = createSelector(
				props,
				props => props.orderId,
			);
			return createSelector(
				orderId,
				orderId => ({
					historyDetailsViewModel: createHistoryDetailsViewModel(orderId),
				}),
			);
		}),
);
