import * as React from 'react';
import { Component, ComponentClass } from 'react';
import * as classnames from 'classnames';
import { PURE } from '@devexperts/react-kit/dist/utils/pure';
import { Button } from '../../../../../ui-common/button/button.component';
import { withTheme } from '@devexperts/react-kit/dist/utils/withTheme';
import {
	TFullSelectboxAnchorProps,
	TSelectboxAnchorProps,
} from '@devexperts/react-kit/dist/components/Selectbox/SelectboxAnchor';

@PURE
class RawPeriodsAnchor extends Component<TFullSelectboxAnchorProps> {
	render() {
		const { theme, children, valueText, caretIcon, onClick, isOpened } = this.props;

		const buttonTheme = {
			container: classnames(theme.container, {
				[theme.container_isOpened as string]: isOpened,
			}),
		};

		return (
			<Button onClick={onClick} theme={buttonTheme}>
				<div className={theme.content}>
					<div className={theme.text}>{valueText}</div>
					<div className={theme.caret}>{caretIcon}</div>
				</div>
				{children}
			</Button>
		);
	}
}

export const PeriodsAnchor: ComponentClass<TSelectboxAnchorProps> = withTheme(Symbol())(RawPeriodsAnchor);
