import { end, int, lit, str, Match } from 'fp-ts-routing';
import {
	REDIRECT_TO_TRADE,
	REDIRECT_TO_POSITIONS,
	REDIRECT_TO_ORDERS,
	REDIRECT_TO_HISTORY,
	REDIRECT_TO_CRM_DEPOSIT,
	REDIRECT_TO_CRM_WITHDRAW,
	REDIRECT_TO_CRM_ACCOUNTS,
	REDIRECT_TO_CRM_UPLOADDOCS,
	REDIRECT_TO_CRM_PERSONAL,
	LocalRedirectPostMessageTO,
} from '../api/post-message/post-message.to';

const root = end;

//api
const api = lit('api');
const auth = api.then(lit('auth'));
const relogin = auth.then(lit('relogin'));
const logout = auth.then(lit('logout'));
const redirect = logout.then(lit('redirect'));
const avacrm = api.then(lit('avacrm'));
const createDemoAccount = avacrm.then(lit('create_demo_account'));
//trade
const trade = lit('trade');
const category = trade.then(str('categoryId'));
const tradeSymbol = trade.then(lit('symbol'));
const tradeSymbolName = tradeSymbol.then(str('symbol'));
const tradeSymbolAction = tradeSymbolName.then(str('action'));

const categorySymbol = category.then(str('symbol'));
const categorySymbolAction = categorySymbol.then(str('action'));
const info = categorySymbol.then(lit('info'));

// menu links
const crm = lit('crm');
const crm_deposit = crm.then(lit('deposit'));
const crm_withdraw = crm.then(lit('withdraw'));
const crm_accounts = crm.then(lit('accounts'));
const crm_uploaddocs = crm.then(lit('uploaddocs'));
const crm_personal = crm.then(lit('personal'));
const crm_calculator = crm.then(lit('calculator'));
const crm_become_trader = crm.then(lit('become_trader'));

//positions
const positions = lit('positions');
const position = positions.then(str('positionId'));
const positionEdit = position.then(lit('edit'));
const positionClose = position.then(lit('close'));
const positionInfo = position.then(lit('info'));
const editingPositionTakeProfitStopLossKind = positionEdit.then(str('profitLossKind'));

//orders
const orders = lit('orders');
const order = orders.then(int('orderId'));
const orderInfo = order.then(lit('info'));
const orderEdit = order.then(lit('edit'));
const editingOrderTakeProfitStopLossKind = orderEdit.then(str('profitLossKind'));

//history
const history = lit('history');
const historyOrder = history.then(int('historyOrderId'));
const historyInfo = historyOrder.then(lit('info'));

//chat
const chat = lit('chat');

export const AVATRADE_CALC_URL = '//www.avatrade.com/trading-info/trading-calculator';
export const POPUP_CONTENT_URL = '/api/iframe/welcome';

export const routes = {
	root,
	api: {
		root: api,
		avarcm: {
			root: avacrm,
			create_demo_account: createDemoAccount,
		},
		auth: {
			root: auth,
			logout: {
				root: logout,
				redirect,
			},
			relogin,
		},
	},
	trade: {
		root: trade,
		category: {
			root: category,
			symbol: {
				root: categorySymbol,
				action: {
					root: categorySymbolAction,
				},
				info: {
					root: info,
				},
			},
		},
		symbol: {
			root: tradeSymbolName,
			actionId: {
				root: tradeSymbolAction,
			},
		},
	},
	positions: {
		root: positions,
		position: {
			root: position,
			edit: {
				root: positionEdit,
				profitLossKind: {
					root: editingPositionTakeProfitStopLossKind,
				},
			},
			close: {
				root: positionClose,
			},
			info: {
				root: positionInfo,
			},
		},
	},
	orders: {
		root: orders,
		order: {
			root: order,
			info: {
				root: orderInfo,
			},
			edit: {
				profitLossKind: {
					root: editingOrderTakeProfitStopLossKind,
				},
				root: orderEdit,
			},
		},
	},
	history: {
		root: history,
		order: {
			root: historyOrder,
			info: {
				root: historyInfo,
			},
		},
	},
	chat: {
		root: chat,
	},
	crm: {
		root: crm,
		accounts: {
			root: crm_accounts,
		},
		deposit: {
			root: crm_deposit,
		},
		withdraw: {
			root: crm_withdraw,
		},
		uploaddocs: {
			root: crm_uploaddocs,
		},
		personal: {
			root: crm_personal,
		},
		calculator: {
			root: crm_calculator,
		},
		becomeTrader: {
			root: crm_become_trader,
		},
	},
};

export const getPath = (message: LocalRedirectPostMessageTO): Match<{}> => {
	switch (message.type) {
		case REDIRECT_TO_TRADE: {
			return routes.trade.root;
		}
		case REDIRECT_TO_POSITIONS: {
			return routes.positions.root;
		}
		case REDIRECT_TO_ORDERS: {
			return routes.orders.root;
		}
		case REDIRECT_TO_HISTORY: {
			return routes.history.root;
		}
		case REDIRECT_TO_CRM_DEPOSIT: {
			return routes.crm.deposit.root;
		}
		case REDIRECT_TO_CRM_WITHDRAW: {
			return routes.crm.withdraw.root;
		}
		case REDIRECT_TO_CRM_ACCOUNTS: {
			return routes.crm.accounts.root;
		}
		case REDIRECT_TO_CRM_UPLOADDOCS: {
			return routes.crm.uploaddocs.root;
		}
		case REDIRECT_TO_CRM_PERSONAL: {
			return routes.crm.personal.root;
		}
	}
};
