import { Option } from 'fp-ts/lib/Option';
import { string, number, union, literal, type } from 'io-ts';
import { createOptionFromNullable } from 'io-ts-types';

export type ProfileTO = {
	symbol: string;
	quantity: Option<number>;
	tradingStatus: 'UNDEFINED' | 'HALTED' | 'ACTIVE';
	statusReason: string;
	haltStartTime: number;
	haltEndTime: number;
	highLimitPrice: Option<number>;
	lowLimitPrice: Option<number>;
};
export const ProfileTOIO = type({
	symbol: string,
	quantity: createOptionFromNullable(number),
	tradingStatus: union([literal('UNDEFINED'), literal('HALTED'), literal('ACTIVE')]),
	statusReason: string,
	haltStartTime: number,
	haltEndTime: number,
	highLimitPrice: createOptionFromNullable(number),
	lowLimitPrice: createOptionFromNullable(number),
});
