import { ExecutionMessageTO, ExecutionMessageTOIO } from '../definitions/ExecutionMessageTO';
import { Option } from 'fp-ts/lib/Option';
import { number, union, literal, array, type } from 'io-ts';
import { createOptionFromNullable } from 'io-ts-types';

export type ValidationResultTO = {
	riskyOrdersTradeCost: Option<number>;
	restriction: Option<
		| 'NOT_RESTRICTED'
		| 'CONFIRMATION_NEEDED'
		| 'DO_NOT_ACTIVATE'
		| 'RESTRICTED_REJECT'
		| 'RESTRICTED_MANUAL_EXECUTION'
	>;
	executionMessages: Option<Array<ExecutionMessageTO>>;
};
export const ValidationResultTOIO = type({
	riskyOrdersTradeCost: createOptionFromNullable(number),
	restriction: createOptionFromNullable(
		union([
			literal('NOT_RESTRICTED'),
			literal('CONFIRMATION_NEEDED'),
			literal('DO_NOT_ACTIVATE'),
			literal('RESTRICTED_REJECT'),
			literal('RESTRICTED_MANUAL_EXECUTION'),
		]),
	),
	executionMessages: createOptionFromNullable(array(ExecutionMessageTOIO)),
});
