import { ResponseValidationError, TAPIClient } from '../client/client';
import { AvaSystemSettingsTO, AvaSystemSettingsTOIO } from '../definitions/AvaSystemSettingsTO';
import { unknownType } from '../utils/utils';
import { fromEither } from '@devexperts/remote-data-ts';
import { LiveData } from '@devexperts/rx-utils/dist/rd/live-data.utils';
import { asks } from 'fp-ts/lib/Reader';
import { string, dictionary } from 'io-ts';
import { map } from 'rxjs/operators';

export type AvasystemsettingsinfoController = {
	/**
	 * Get app settings if user is authenticated
	 */
	readonly getAppSettings: () => LiveData<Error, { [key: string]: unknown }>;

	/**
	 * Get ava system settings info
	 */
	readonly getSystemInfo: () => LiveData<Error, AvaSystemSettingsTO>;
};

export const avasystemsettingsinfoController = asks(
	(e: { apiClient: TAPIClient }): AvasystemsettingsinfoController => ({
		getAppSettings: () => {
			return e.apiClient
				.request({
					url: `/settings/app`,
					method: 'GET',
				})
				.pipe(
					map(data =>
						data.chain(value =>
							fromEither(
								dictionary(string, unknownType)
									.decode(value)
									.mapLeft(ResponseValidationError.create),
							),
						),
					),
				);
		},

		getSystemInfo: () => {
			return e.apiClient
				.request({
					url: `/settings/info`,
					method: 'GET',
				})
				.pipe(
					map(data =>
						data.chain(value =>
							fromEither(AvaSystemSettingsTOIO.decode(value).mapLeft(ResponseValidationError.create)),
						),
					),
				);
		},
	}),
);
