import { TDateInputValue } from '@devexperts/react-kit/dist/components/DateInput/DateInput.model';
import { RemoteData, getSetoid as getSetoidRD } from '@devexperts/remote-data-ts';
import { Option } from 'fp-ts/lib/Option';
import * as O from 'fp-ts/lib/Option';
import { Either } from 'fp-ts/lib/Either';
import * as E from 'fp-ts/lib/Either';
import { Eq, getStructEq, eqString, eqNumber, eqBoolean, eqDate, strictEqual } from 'fp-ts/lib/Eq';
import { getDate, getMonth, getYear, endOfDay } from 'date-fns';
import { formatNumber } from 'accounting';
import { Big } from 'big.js';
import { OrderEntryValues, OrderEntryData } from './order-entry.view-model';
import { sequenceTOption, OptionType } from '../../utils/option.utils';
import { setoidStrict, setoidOptionNumber } from '../../utils/setoid.utils';
import {
	OrderEntrySide,
	OrderEntryExecutionDate,
	OrderEntryExecutionStatusFlags,
	OrderEntryExecutionPrice,
	AmountValidationError,
	AmountValidationErrorMessages,
	ExecutionPriceValidationErrorMessages,
	ExecutionPriceValidationError,
} from '../../model/order-entry/order-entry.model';
import { OrderTO } from '../../api/generated/swagger/definitions/OrderTO';
import { OrderTemplateTO } from '../../api/generated/swagger/definitions/OrderTemplateTO';
import { pipe } from 'fp-ts/lib/pipeable';
import { constant } from 'fp-ts/lib/function';

export const getExpireAt = (): TDateInputValue => {
	const today = new Date();
	return {
		day: O.some(getDate(today)),
		month: O.some(getMonth(today)),
		year: O.some(getYear(today)),
	};
};

export const validateExecutionPrice = (
	executionPrice: Option<number>,
	bidOrAskPrice: number,
	allowedDistance: number,
	precision: number,
	priceIncrement: number,
): Either<ExecutionPriceValidationError, number> => {
	const commonErrorFields = {
		hiRange: formatNumber(bidOrAskPrice + allowedDistance, precision),
		loRange: formatNumber(bidOrAskPrice - allowedDistance, precision),
		step: String(priceIncrement),
	};

	if (O.isNone(executionPrice)) {
		return E.left({ ...commonErrorFields, message: ExecutionPriceValidationErrorMessages.General });
	}

	const price = executionPrice.value;
	if (price > bidOrAskPrice - allowedDistance && price < bidOrAskPrice + allowedDistance) {
		return E.left({ ...commonErrorFields, message: ExecutionPriceValidationErrorMessages.NotInRange });
	}
	if (isMultiple(price, priceIncrement)) {
		return E.left({ ...commonErrorFields, message: ExecutionPriceValidationErrorMessages.WrongStepping });
	}

	return E.right(price);
};

export const validateOrderEditPrice = (
	executionPrice: Option<number>,
	orderType: Option<OrderTO['type']>,
	side: OrderEntrySide,
	bidOrAskPrice: number,
	allowedDistance: number,
	precision: number,
): Either<ExecutionPriceValidationError, number> => {
	const priceAndType = pipe(
		sequenceTOption(executionPrice, orderType),
		O.map(([price, type]) => ({
			price,
			type,
		})),
	);

	const commonErrorFields = {
		hiRange: formatNumber(bidOrAskPrice + allowedDistance, precision),
		loRange: formatNumber(bidOrAskPrice - allowedDistance, precision),
		step: '',
	};

	if (O.isNone(priceAndType)) {
		return E.left({ ...commonErrorFields, message: ExecutionPriceValidationErrorMessages.General });
	}

	const { price, type } = priceAndType.value;

	if (side === OrderEntrySide.Sell) {
		if (type === 'LIMIT' && price < bidOrAskPrice + allowedDistance) {
			return E.left({ ...commonErrorFields, message: ExecutionPriceValidationErrorMessages.OverRange });
		}

		if (type === 'STOP' && price > bidOrAskPrice - allowedDistance) {
			return E.left({ ...commonErrorFields, message: ExecutionPriceValidationErrorMessages.BelowRange });
		}
	}

	if (side === OrderEntrySide.Buy) {
		if (type === 'LIMIT' && price > bidOrAskPrice - allowedDistance) {
			return E.left({ ...commonErrorFields, message: ExecutionPriceValidationErrorMessages.BelowRange });
		}

		if (type === 'STOP' && price < bidOrAskPrice + allowedDistance) {
			return E.left({ ...commonErrorFields, message: ExecutionPriceValidationErrorMessages.OverRange });
		}
	}

	return E.right(price);
};

const isMultiple = (dividend: number, divisor: number): boolean =>
	!Big(dividend)
		.mod(divisor)
		.eq(0);

export const validateAmount = (
	inputValue: string,
	minOrderSize: number,
	maxOrderSize: number,
	minOrderIncrement: number,
): Either<AmountValidationError, string> => {
	const value = Number(inputValue);

	const commonFailureFields = {
		maxOrderSize,
		minOrderSize,
		minOrderIncrement,
	};

	if (value > maxOrderSize) {
		return E.left({
			...commonFailureFields,
			message: AmountValidationErrorMessages.Max,
		});
	}

	if (value < minOrderSize) {
		return E.left({ ...commonFailureFields, message: AmountValidationErrorMessages.Min });
	}

	if (isMultiple(value, minOrderIncrement)) {
		return E.left({ ...commonFailureFields, message: AmountValidationErrorMessages.Multiple });
	}

	return E.right(inputValue);
};

export const convertDateInputValueToDate = ({ year, month, day }: TDateInputValue): Option<Date> =>
	pipe(
		sequenceTOption(year, month, day),
		O.map(([year, month, day]) => new Date(year, month, day)),
	);

export const setoidOEValues: Eq<OrderEntryValues> = getStructEq({
	value: eqString,
	execFormValue: O.getEq(eqNumber),
	isExecEnabled: eqBoolean,
	isExpireChecked: eqBoolean,
	expireAtDate: O.getEq(eqDate),
	isTPEnabled: eqBoolean,
	takeProfitValue: eqNumber,
	isSLEnabled: eqBoolean,
	stopLossValue: eqNumber,
});

export const setoidBooleanRD: Eq<RemoteData<Error, boolean>> = getSetoidRD(setoidStrict, eqBoolean);

const executionPriceValidationSetoid = getStructEq<ExecutionPriceValidationError>({
	message: setoidStrict,
	hiRange: eqString,
	loRange: eqString,
	step: eqString,
});

export const setoidOrderEntryExecutionPrice = getStructEq<OrderEntryExecutionPrice>({
	priceValue: setoidOptionNumber,
	executionPriceValidated: E.getEq<ExecutionPriceValidationError, number>(executionPriceValidationSetoid, eqNumber),
});

export const setoidOrderType = O.getEq<OrderTO['type']>({ equals: strictEqual });

export const toOrderTOSide = (operation: OrderEntrySide): OptionType<OrderTemplateTO['orderSide']> => {
	switch (operation) {
		case OrderEntrySide.Buy: {
			return 'BUY';
		}
		case OrderEntrySide.Sell: {
			return 'SELL';
		}
	}
};

export const toOrderEntryData = (
	bid: Option<number>,
	ask: Option<number>,
	pipsSize: number,
	precision: number,
	description: Option<string>,
	step: number,
	marginImpact: Either<Error, number>,
): OrderEntryData => ({
	bid,
	ask,
	pipsSize,
	precision,
	description,
	step,
	marginImpact,
});

export const toExpireAt = (
	values: OrderEntryExecutionDate,
	checkboxValues: OrderEntryExecutionStatusFlags,
): Option<Date> => {
	const { expireAt } = values;
	const date = new Date(
		pipe(
			expireAt.year,
			O.getOrElse(constant(0)),
		),
		pipe(
			expireAt.month,
			O.getOrElse(constant(0)),
		),
		pipe(
			expireAt.day,
			O.getOrElse(constant(0)),
		),
	);
	return checkboxValues.expireAtActive ? O.some(endOfDay(date)) : O.none;
};

export const getAmount = (side: OrderEntrySide, bid: number, ask: number): number => {
	switch (side) {
		case OrderEntrySide.Buy: {
			return ask;
		}
		case OrderEntrySide.Sell: {
			return bid;
		}
	}
};

export function getOrderType(
	executionPrice: Option<number>,
	currentPrice: Option<number>,
	distance: number,
	side: OrderEntrySide,
): OrderTemplateTO['orderType'] {
	return pipe(
		sequenceTOption(executionPrice, currentPrice),
		O.map(([price, currentPrice]) => {
			// please refer to https://jira.in.devexperts.com/browse/DXREQ-10550?focusedCommentId=1956701&page=com.atlassian.jira.plugin.system.issuetabpanels:comment-tabpanel#comment-1956701
			switch (side) {
				case OrderEntrySide.Buy: {
					if (price >= currentPrice + distance) {
						return 'STOP';
					}

					if (price <= currentPrice - distance) {
						return 'LIMIT';
					}
				}
				case OrderEntrySide.Sell: {
					if (price >= currentPrice + distance) {
						return 'LIMIT';
					}

					if (price <= currentPrice - distance) {
						return 'STOP';
					}
				}
				default:
					return 'MARKET';
			}
		}),
		O.getOrElse<OrderTemplateTO['orderType']>(constant('MARKET')),
	);
}
