import { ResponseValidationError, TAPIClient } from '../client/client';
import { MarginImpactTO, MarginImpactTOIO } from '../definitions/MarginImpactTO';
import {
	UpdateMarketSymbolsSubscriptionTO,
	UpdateMarketSymbolsSubscriptionTOIO,
} from '../definitions/UpdateMarketSymbolsSubscriptionTO';
import {
	UpdatePositionKeysSubscriptionTO,
	UpdatePositionKeysSubscriptionTOIO,
} from '../definitions/UpdatePositionKeysSubscriptionTO';
import { fromEither } from '@devexperts/remote-data-ts';
import { LiveData } from '@devexperts/rx-utils/dist/rd/live-data.utils';
import { asks } from 'fp-ts/lib/Reader';
import { array, string, type, partial, void as tvoid } from 'io-ts';
import { map } from 'rxjs/operators';

export type MarginImpactsController = {
	/**
	 * Get list of margin impacts for specified accounts by positions
	 * @param { object } parameters
	 */
	readonly getMarginImpactsByPositions: (parameters: {
		query: { accountId: Array<string>; positionCodes: Array<string> };
	}) => LiveData<Error, Array<MarginImpactTO>>;

	/**
	 * Subscribe on margin impacts by positions
	 * @param { object } [parameters]
	 */
	readonly changeSubscriptionByPositions: (parameters: {
		body?: UpdatePositionKeysSubscriptionTO;
	}) => LiveData<Error, void>;

	/**
	 * Get list of margin impacts for specified accounts by symbols
	 * @param { object } parameters
	 */
	readonly getMarginImpactsBySymbols: (parameters: {
		query: { accountId: Array<string>; symbols: Array<string> };
	}) => LiveData<Error, Array<MarginImpactTO>>;

	/**
	 * Subscribe on margin impacts by symbols
	 * @param { object } [parameters]
	 */
	readonly changeSubscriptionByInstruments: (parameters: {
		body?: UpdateMarketSymbolsSubscriptionTO;
	}) => LiveData<Error, void>;
};

export const marginImpactsController = asks(
	(e: { apiClient: TAPIClient }): MarginImpactsController => ({
		getMarginImpactsByPositions: parameters => {
			const encoded = partial({ query: type({ accountId: array(string), positionCodes: array(string) }) }).encode(
				parameters,
			);

			return e.apiClient
				.request({
					url: `/marginImpacts/positions`,
					method: 'GET',
					query: encoded.query,
				})
				.pipe(
					map(data =>
						data.chain(value =>
							fromEither(
								array(MarginImpactTOIO)
									.decode(value)
									.mapLeft(ResponseValidationError.create),
							),
						),
					),
				);
		},

		changeSubscriptionByPositions: parameters => {
			const encoded = partial({ body: UpdatePositionKeysSubscriptionTOIO }).encode(parameters);

			return e.apiClient
				.request({
					url: `/marginImpacts/positions`,
					method: 'POST',

					body: encoded.body,
				})
				.pipe(
					map(data =>
						data.chain(value => fromEither(tvoid.decode(value).mapLeft(ResponseValidationError.create))),
					),
				);
		},

		getMarginImpactsBySymbols: parameters => {
			const encoded = partial({ query: type({ accountId: array(string), symbols: array(string) }) }).encode(
				parameters,
			);

			return e.apiClient
				.request({
					url: `/marginImpacts/symbols`,
					method: 'GET',
					query: encoded.query,
				})
				.pipe(
					map(data =>
						data.chain(value =>
							fromEither(
								array(MarginImpactTOIO)
									.decode(value)
									.mapLeft(ResponseValidationError.create),
							),
						),
					),
				);
		},

		changeSubscriptionByInstruments: parameters => {
			const encoded = partial({ body: UpdateMarketSymbolsSubscriptionTOIO }).encode(parameters);

			return e.apiClient
				.request({
					url: `/marginImpacts/symbols`,
					method: 'POST',

					body: encoded.body,
				})
				.pipe(
					map(data =>
						data.chain(value => fromEither(tvoid.decode(value).mapLeft(ResponseValidationError.create))),
					),
				);
		},
	}),
);
