import * as React from 'react';
import { memo } from 'react';
import { I18nextProvider } from 'react-i18next';
import { i18next } from '../../../../utils/i18n.utils';
import { Theme } from '../../../ui-kit/components/theme/theme.component';
import { LayoutContainer } from '../../../layout/containers/layout/layout.container';
import { combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { Route } from '../../../../ui-common/route/containers/route.container';
import { routes } from '../../../../ui-common/routes';
import { TradeDashboardContainer } from '../../../trade/containers/trade-dashboard/trade-dashboard.container';
import { Redirect } from '../../../../ui-common/redirect/containers/redirect.container';
import { PositionDashboardContainer } from '../../../positions/containers/positions-dashboard/positions-dashboard.container';
import { RedirectListener } from '../../../../ui-common/redirect/containers/redirect-listener.container';
import { OrdersDashboardContainer } from '../../../orders/containers/orders-dashboard/orders-dashboard.container';
import { HistoryDashboardContainer } from '../../../history/containers/history-dashboard/history-dashboard.container';
import { CrmPages } from '../../../../ui-common/crm-pages/components/crm-pages.component';
import { empty } from 'fp-ts/lib/Record';

export const Authorized = combineContext(
	LayoutContainer,
	HistoryDashboardContainer,
	PositionDashboardContainer,
	TradeDashboardContainer,
	Route,
	Redirect,
	OrdersDashboardContainer,
	RedirectListener,
	CrmPages,
	(
		LayoutContainer,
		HistoryDashboardContainer,
		PositionDashboardContainer,
		TradeDashboardContainer,
		Route,
		Redirect,
		OrdersDashboardContainer,
		RedirectListener,
		CrmPages,
	) =>
		memo(() => {
			const redirectToTrade = () => <Redirect to={routes.trade.root} value={empty} />;
			const renderTrade = () => <TradeDashboardContainer />;
			const renderPositions = () => <PositionDashboardContainer />;
			const renderOrders = () => <OrdersDashboardContainer />;
			const renderHistory = () => <HistoryDashboardContainer />;
			return (
				<I18nextProvider i18n={i18next}>
					<Theme>
						<LayoutContainer>
							<Route match={routes.root} render={redirectToTrade} />
							<Route match={routes.trade.root} render={renderTrade} />
							<Route match={routes.positions.root} render={renderPositions} />
							<Route match={routes.orders.root} render={renderOrders} />
							<Route match={routes.history.root} render={renderHistory} />
							<CrmPages />
							<RedirectListener />
						</LayoutContainer>
					</Theme>
				</I18nextProvider>
			);
		}),
);
