import * as React from 'react';
import { Fragment, PureComponent } from 'react';
import { formatNumber } from 'accounting';
import { withTranslation, WithTranslation } from 'react-i18next';
import { ConfigItem, TableItem, TableSize } from '../watchlist-table/watchlist-table.model';
import * as cn from 'classnames';
import { findFirst } from 'fp-ts/lib/Array';
import { RemoteData } from '@devexperts/remote-data-ts';
import { RemoteDataState } from '../../../../ui-common/remote-data-state/remote-data-state.component';
import { Optional } from '../../../../ui-common/optional/optional.component';
import { PipRate } from '../../../../ui-common/pip-rate/pip-rate.component';
import * as O from 'fp-ts/lib/Option';
import { RouteLink } from '../../../../ui-common/route-link/containers/route-link.container';
import { routes } from '../../../../ui-common/routes';
import { InfoIcon } from '../../../../ui-common/icons/info.icon';
import { StarOnIcon } from '../../../ui-kit/components/icons/star-on.icon';
import { StarOffIcon } from '../../../ui-kit/components/icons/star-off.icon';
import { combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { definePipSettings } from '../../../../ui-common/pip-rate/pip-settings.utils';
import { PriceDirection } from '../../../../ui-common/price-direction/price-direction.component';
import { ListChildComponentProps } from 'react-window';
import { tspan, TRANSLATION_PLACEHOLDER } from '../../../../utils/i18n.utils';
import { pipe } from 'fp-ts/lib/pipeable';
import { constant } from 'fp-ts/lib/function';
import { sequenceTOption } from '../../../../utils/option.utils';
import { empty } from 'fp-ts/lib/Record';
import * as css from './theme/watchlist-entity.component.module.css';
import * as buttonSellCss from './theme/button-sell.child.module.css';
import * as buttonBuyCss from './theme/button-buy.child.module.css';
import * as priceDirectionCss from './theme/price-direction.child.module.css';
import * as pipRateCss from './theme/pip-rate.child.module.css';
import * as optionalCss from './theme/optional.module.css';

export type WatchlistEntityProps = ListChildComponentProps & {
	rowData: string;
	remoteData: RemoteData<Error, TableItem>;
	config: Array<ConfigItem>;
	categoryId: string;
	toggleFavorite: (data: TableItem) => void;
	tableSize: TableSize;
};

const defaultColumnWidth = 180;

const accentIconClassName = cn(css.icon, css.icon_accent);

export const WatchlistEntity = combineContext(RouteLink, RouteLink =>
	withTranslation()(
		class WatchlistEntity extends PureComponent<WatchlistEntityProps & WithTranslation> {
			render() {
				const { remoteData } = this.props;

				return (
					<div>
						<RemoteDataState data={remoteData} success={this.renderSuccess} />
					</div>
				);
			}

			private renderSuccess = (data: TableItem): JSX.Element => {
				const { index, style, config, categoryId } = this.props;

				return (
					<RouteLink
						to={routes.trade.category.symbol.root}
						value={{ categoryId, symbol: data.symbol }}
						render={({ onNavigate, isActive }) => (
							<div
								onClick={onNavigate}
								className={cn(css.row, { [css.rowSelected]: isActive })}
								key={index}
								style={style}>
								<div
									className={css.rowColumn}
									style={{ flexBasis: this.getWidthFromConfig(config, 'symbol') }}>
									{this.renderSymbolCell(data)}
								</div>
								<div
									className={css.rowColumn}
									style={{ flexBasis: this.getWidthFromConfig(config, 'sell') }}>
									{this.renderSellCell(data)}
								</div>
								<div
									className={css.rowColumn}
									style={{
										flexBasis: this.getWidthFromConfig(config, 'buy-sell-btns'),
									}}>
									{this.renderSellButton(data)}
									{this.renderBuyButton(data)}
								</div>
								<div
									className={css.rowColumn}
									style={{ flexBasis: this.getWidthFromConfig(config, 'buy') }}>
									{this.renderBuyCell(data)}
								</div>
								<div
									className={css.rowColumn}
									style={{ flexBasis: this.getWidthFromConfig(config, 'change') }}>
									{this.renderChangeCell(data)}
								</div>
								<div
									className={css.rowColumn}
									style={{
										flexBasis: this.getWidthFromConfig(config, 'info-fav-icons'),
									}}>
									{this.renderInfoIcon(data)}
									{this.renderFavIcon(data)}
								</div>
							</div>
						)}
					/>
				);
			};

			private renderSymbolCell = (item: TableItem) => {
				const { symbol, positions } = item;
				const { tableSize } = this.props;

				return (
					<span className={css.symbolCell}>
						<span>{symbol}</span>
						{pipe(
							positions,
							O.fold(constant(<span />), data => (
								<RouteLink
									to={routes.positions.root}
									value={empty}
									render={({ onNavigate }) => (
										<span
											className={css.positions}
											onClick={this.handleClickWithStopPropagation(onNavigate)}>
											{data}
											{tableSize === TableSize.Large &&
												(data === 1
													? tspan(
															'tradeSymbolOnePositionIndicator',
															'position',
															` ${TRANSLATION_PLACEHOLDER}`,
													  )
													: tspan(
															'tradeSymbolPositionsIndicator',
															'positions',
															` ${TRANSLATION_PLACEHOLDER}`,
													  ))}
										</span>
									)}
								/>
							)),
						)}
					</span>
				);
			};

			private renderSellCell = (item: TableItem) => {
				const { sell, pipsSize, precision } = item;

				const value = sequenceTOption(sell, pipsSize, precision);
				const pipRendered = pipe(
					value,
					O.map(([sell, pipsSize, precision]) => (
						<PipRate
							value={sell}
							rise={definePipSettings(sell, pipsSize)}
							digits={precision}
							theme={pipRateCss}
						/>
					)),
				);

				return <Optional className={optionalCss.noValue} value={pipRendered} />;
			};

			private renderSellButton = (item: TableItem) => {
				const { categoryId } = this.props;
				const { symbol } = item;

				return (
					<RouteLink
						to={routes.trade.category.symbol.action.root}
						value={{ categoryId, symbol, action: 'sell' }}
						render={({ onNavigate }) => (
							<div
								className={buttonSellCss.container}
								onClick={this.handleClickWithStopPropagation(onNavigate)}>
								{tspan('watchlistEntitySell', 'Sell')}
							</div>
						)}
					/>
				);
			};

			private renderBuyButton = (item: TableItem) => {
				const { categoryId } = this.props;
				const { symbol } = item;

				return (
					<RouteLink
						to={routes.trade.category.symbol.action.root}
						value={{ categoryId, symbol, action: 'buy' }}
						render={({ onNavigate }) => (
							<div
								className={buttonBuyCss.container}
								onClick={this.handleClickWithStopPropagation(onNavigate)}>
								{tspan('watchlistEntityBuy', 'Buy')}
							</div>
						)}
					/>
				);
			};

			private handleClickWithStopPropagation = (onNavigate: () => void) => (
				e: React.MouseEvent<HTMLDivElement>,
			) => {
				e.stopPropagation();
				onNavigate();
			};

			private renderBuyCell = (item: TableItem) => {
				const { buy, pipsSize, precision } = item;

				const value = sequenceTOption(buy, pipsSize, precision);
				const pipRendered = pipe(
					value,
					O.map(([buy, pipsSize, precision]) => (
						<PipRate
							value={buy}
							rise={definePipSettings(buy, pipsSize)}
							digits={precision}
							theme={pipRateCss}
						/>
					)),
				);

				return <Optional className={optionalCss.noValue} value={pipRendered} />;
			};

			private renderChangeCell = (item: TableItem) => {
				const { change } = item;
				const cellTheme = pipe(
					change,
					O.fold(constant(css.changeCell_empty), constant(css.changeCell)),
				);

				return (
					<span className={cellTheme}>
						<Optional
							value={pipe(
								change,
								O.map(value => (
									<Fragment>
										<PriceDirection direction={value} theme={priceDirectionCss} />
										{formatNumber(value, 2)}
									</Fragment>
								)),
							)}
						/>
					</span>
				);
			};

			private renderInfoIcon = (item: TableItem) => {
				const { categoryId } = this.props;
				const { symbol } = item;

				return (
					<RouteLink
						to={routes.trade.category.symbol.info.root}
						value={{ categoryId, symbol }}
						render={({ onNavigate }) => (
							<div className={css.infoCell}>
								<i className={css.icon} onClick={this.handleClickWithStopPropagation(onNavigate)}>
									<InfoIcon />
								</i>
							</div>
						)}
					/>
				);
			};

			private renderFavIcon = (item: TableItem) =>
				item.isFavorite ? (
					<div className={css.favCell}>
						<i className={accentIconClassName} onClick={this.handleFavoriteIcon(item)}>
							<StarOnIcon />
						</i>
					</div>
				) : (
					<div className={css.favCell}>
						<i className={css.icon} onClick={this.handleFavoriteIcon(item)}>
							<StarOffIcon />
						</i>
					</div>
				);

			private handleFavoriteIcon = (props: TableItem) => () => this.props.toggleFavorite(props);

			private getWidthFromConfig = (config: ConfigItem[], dataKey: string): number => {
				const currentColumn = findFirst(config, item => {
					return item['dataKey'] === dataKey;
				});

				return pipe(
					currentColumn,
					O.fold(constant(defaultColumnWidth), value => value.width),
				);
			};
		},
	),
);
