import { withRXWithDefaults } from '../../../../utils/withRXWithDefaults';
import { ask, combineContext, deferContext } from '@devexperts/rx-utils/dist/context.utils';
import { runOnMount } from '../../../../utils/react.utils';
import { AsideViewModel, createAsideViewModel } from '../../../../view-model/aside/aside.view-model';
import { Aside } from '../../components/aside/aside.component';
import { AccountViewModel } from '../../../../view-model/account/account.view-model';
import { first, map } from 'rxjs/operators';
import { none, some } from 'fp-ts/lib/Option';
import { createChat } from '../../../chat/chat.utils';

export type AsideContainerContext = {
	asideViewModel: AsideViewModel;
	accountViewModel: AccountViewModel;
};

export const Container = combineContext(Aside, ask<AsideContainerContext>(), (Aside, e) =>
	withRXWithDefaults(Aside)(() => {
		const { asideViewModel, accountViewModel } = e;
		const { orderCount$, positionCount$ } = asideViewModel;
		const { current$ } = accountViewModel;

		return {
			props: {
				orderCount: orderCount$,
				positionCount: positionCount$,
				isRealAccount: current$.pipe(
					first(),
					map(account => some(!account.paperMoney)),
				),
			},
			defaultProps: {
				orderCount: 0,
				positionCount: 0,
				isRealAccount: none,
			},
		};
	}),
);

export const AsideContainer = combineContext(
	deferContext(Container, 'asideViewModel', 'chat'),
	createAsideViewModel,
	(Container, createAsideViewModel) =>
		runOnMount(Container, () => () => ({
			asideViewModel: createAsideViewModel(),
			chat: createChat(),
		})),
);
