import { withRXWithDefaults } from '../../../../utils/withRXWithDefaults';
import { pending } from '@devexperts/remote-data-ts';
import { ask, combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { LiveData } from '@devexperts/rx-utils/dist/rd/live-data.utils';
import { head } from 'fp-ts/lib/Array';
import { eqNumber } from 'fp-ts/lib/Eq';
import { distinctUntilChangedStrict } from '../../../../utils/live-data.utils';
import { OrdersDashboard } from '../../components/orders-dashboard/orders-dashboard.component';
import { OrderStore } from '../../../../store/order/order.store';
import { mapRD } from '@devexperts/rx-utils/dist/rd/operators/mapRD';
import { Option } from 'fp-ts/lib/Option';
import * as O from 'fp-ts/lib/Option';
import { pipe } from 'fp-ts/lib/pipeable';

export type OrderDashboardContainerContext = {
	orderStore: OrderStore;
};
export const OrdersDashboardContainer = combineContext(
	OrdersDashboard,
	ask<OrderDashboardContainerContext>(),
	(OrdersDashboard, e) =>
		withRXWithDefaults(OrdersDashboard)(() => {
			const { orderStore } = e;

			const firstOrderId$: LiveData<Error, Option<number>> = orderStore.all$.pipe(
				mapRD(list =>
					pipe(
						list,
						head,
						O.map(order => order.orderId),
					),
				),
				distinctUntilChangedStrict(O.getEq(eqNumber)),
			);

			return {
				props: {
					firstOrderId: firstOrderId$,
				},
				defaultProps: {
					firstOrderId: pending,
				},
			};
		}),
);
