import * as React from 'react';
import { Component } from 'react';
import { ArrowUpIcon } from '../icons/arrow-up.icon';
import { ArrowDownIcon } from '../icons/arrow-down.icon';
import { withTheme } from '@devexperts/react-kit/dist/utils/withTheme';
import { MakeTheme } from '../../utils/theme.utils';
import * as css from './theme/price-direction.module.css';

type RawPriceDirectionProps = {
	theme: MakeTheme<'icon' | 'arrowUp' | 'arrowDown'>;
	direction: number;
};

class RawPriceDirection extends Component<RawPriceDirectionProps> {
	shouldComponentUpdate(nextProps: Readonly<RawPriceDirectionProps>): boolean {
		return nextProps.direction !== 0;
	}

	render() {
		const { theme, direction } = this.props;
		if (direction > 0) {
			return (
				<div className={theme.icon}>
					<i className={theme.arrowUp}>
						<ArrowUpIcon />
					</i>
				</div>
			);
		}
		if (direction < 0) {
			return (
				<div className={theme.icon}>
					<i className={theme.arrowDown}>
						<ArrowDownIcon />
					</i>
				</div>
			);
		}
		return <div className={theme.icon} />;
	}
}

const wTheme = {
	...css,
};

export const PriceDirection = withTheme(Symbol(), wTheme)(RawPriceDirection);
