import { Observable, combineLatest } from 'rxjs';
import { distinctUntilChanged, filter, map, startWith } from 'rxjs/operators';
import { createHandler } from '@devexperts/rx-utils/dist/create-handler.utils';
import { Option } from 'fp-ts/lib/Option';
import * as O from 'fp-ts/lib/Option';
import { chartsController } from '../../api/generated/swagger/controllers/ChartsController';
import { SocketClient } from '../../api/socket-client';
import { Sink } from '@devexperts/rx-utils/dist/sink.utils';
import { isChartTO } from '../../api/socket/socket-message.to';
import { LiveData } from '@devexperts/rx-utils/dist/rd/live-data.utils';
import { CandleTO } from '../../api/generated/swagger/definitions/CandleTO';
import { mapRD } from '@devexperts/rx-utils/dist/rd/operators/mapRD';
import { ChartRequestTO } from '../../api/generated/swagger/definitions/ChartRequestTO';
import { ChartSettingsStore } from '../../store/chart-settings/chart-settings.store';
import { ask, combineContext, fromReader } from '@devexperts/rx-utils/dist/context.utils';
import { MarketDataStore } from '../../store/market-data/market-data.store';
import { head, mapOption, findFirst } from 'fp-ts/lib/Array';
import { switchMapRD } from '@devexperts/rx-utils/dist/rd/operators/switchMapRD';
import { fromOption, pending, success } from '@devexperts/remote-data-ts';
import { combineLatestRD } from '@devexperts/rx-utils/dist/rd/operators/combineLatestRD';
import { ChartResponseTO } from '../../api/generated/swagger/definitions/ChartResponseTO';
import { InstrumentStore } from '../../store/instrument/instrument.store';
import { sequenceTOption } from '../../utils/option.utils';
import { Predicate } from 'fp-ts/lib/function';
import { AggrPeriodRangePairTO } from '../../api/generated/swagger/definitions/AggrPeriodRangePairTO';
import { tapRD } from '@devexperts/rx-utils/dist/rd/operators/tapRD';
import { pipe } from 'fp-ts/lib/pipeable';
import { shareReplayWithDefaults } from '../../utils/rxjs/shareReplayWithDefaults';

export enum ChartType {
	Bar = '* BAR *',
	Candle = '* CANDLE *',
	Line = '* LINE *',
}

export type ChartPeriod = {
	name: string;
	seconds: number;
};

type AggregationPeriodPair = {
	range: number;
	period: number;
};

export type ChartViewModel = {
	aggregationPeriodPair$: Observable<AggregationPeriodPair>;
	precision$: LiveData<Error, number>;
	candles$: LiveData<Error, CandleTO[]>;
	lastCandles$: LiveData<Error, CandleTO[]>;
	periods$: LiveData<Error, ChartPeriod[]>;
	description$: LiveData<Error, Option<string>>;
	selectedPeriod$: Observable<ChartPeriod>;
	selectedChartType$: Observable<ChartType>;
	isCrossToolEnabled$: Observable<boolean>;
	onChangePeriod: (period: ChartPeriod) => void;
	onChangeChartType: (type: ChartType) => void;
	onSwitchCrossTool: (isEnabled: boolean) => void;
};

type ChartContext = {
	socketClient: SocketClient;
	chartSettingsStore: ChartSettingsStore;
	marketDataStore: MarketDataStore;
	instrumentStore: InstrumentStore;
};

type CreateChartViewModel = Sink<(symbol: string, isMobile: boolean) => Sink<ChartViewModel>>;

const SUBTOPIC = 'chart';
let chartId = 0;

export const createChartViewModel = combineContext(
	fromReader(chartsController),
	ask<ChartContext>(),
	(chartsController, ctx): CreateChartViewModel => {
		const { socketClient, chartSettingsStore, marketDataStore, instrumentStore } = ctx;

		const charts$ = socketClient.message$.pipe(
			filter(isChartTO),
			map(data => data.body),
			shareReplayWithDefaults(),
		);

		const effects$ = charts$;

		const create = (symbol: string, isMobile: boolean): Sink<ChartViewModel> => {
			const id = chartId++;

			const selectedPeriod = createHandler<ChartPeriod>();
			const selectedChartType = createHandler<ChartType>();
			const isCrossToolEnabled = createHandler<boolean>();
			const aggregationPeriodPair = createHandler<AggregationPeriodPair>();

			const periods$: LiveData<Error, ChartPeriod[]> = chartSettingsStore.periods$.pipe(
				mapRD(data =>
					mapOption(data, el =>
						pipe(
							el.name,
							O.chain(name =>
								pipe(
									el.seconds,
									O.map(seconds => ({ name, seconds })),
								),
							),
						),
					),
				),
			);

			const chartPeriodName$ = selectedPeriod.value$.pipe(
				map(({ name }) => name),
				distinctUntilChanged(),
			);

			const chartPeriodSeconds$ = selectedPeriod.value$.pipe(
				map(({ seconds }) => seconds),
				distinctUntilChanged(),
			);

			const selectedPeriod$: Observable<ChartPeriod> = combineLatest(chartPeriodName$, chartPeriodSeconds$).pipe(
				map(([name, seconds]) => ({ name, seconds })),
			);

			const selectedChartType$ = selectedChartType.value$;
			const isCrossToolEnabled$ = isCrossToolEnabled.value$;

			const chart$ = charts$.pipe(
				filter(to => to.requestID === id),
				map(data => success<Error, ChartResponseTO>(data)),
				startWith(pending),
				shareReplayWithDefaults(),
			);

			const requestChartData = (period: ChartPeriod) =>
				chartSettingsStore.pairs$.pipe(
					mapRD(pairs => findFirst(pairs, getAggregationPairMatchingPredicate(period))),
					mapRD(aggregationPeriodPair =>
						pipe(
							sequenceTOption(
								pipe(
									aggregationPeriodPair,
									O.chain(({ aggregationPeriod }) => aggregationPeriod),
								),
								pipe(
									aggregationPeriodPair,
									O.chain(({ range }) => range),
								),
							),
							O.chain(([{ seconds }, { duration }]) => sequenceTOption(seconds, duration)),
						),
					),
					map(aggregationPeriodPair =>
						aggregationPeriodPair.chain(value =>
							fromOption(
								value,
								() => new Error(`Could not find selected period/range pair in Chart settings pairs`),
							),
						),
					),
					tapRD(([period, range]) => aggregationPeriodPair.handle({ period, range })),
					switchMapRD(([seconds, duration]) => {
						return chartsController.updateChartsSubscription({
							body: {
								chartIds: O.some([]),
								requests: O.some([getChartRequestTO(id, symbol, seconds, duration)]),
							},
						});
					}),
				);

			const initialPeriod$ = periods$.pipe(
				map(periods =>
					periods.chain(periods =>
						fromOption(
							isMobile ? findFirst(periods, period => period.seconds === 1800) : head(periods),
							() => new Error('There are no any periods'),
						),
					),
				),
			);

			const requestHistoricalData$ = combineLatestRD(initialPeriod$, marketDataStore.isInitialized$).pipe(
				switchMapRD(([period]) => requestChartData(period)),
				shareReplayWithDefaults(),
			);

			const requestChangeChartParameters$ = selectedPeriod$.pipe(
				map(value => success<Error, ChartPeriod>(value)),
				switchMapRD(period => requestChartData(period)),
				shareReplayWithDefaults(),
			);

			const allChartData$ = combineLatestRD(requestHistoricalData$, chart$).pipe(
				mapRD(([_, chart]) => chart),
				distinctUntilChanged(),
				shareReplayWithDefaults(),
			);

			const candles$ = allChartData$.pipe(
				filter(data => !data.isSuccess() || data.value.historyData),
				mapRD(data => data.data),
			);
			const lastCandles$ = allChartData$.pipe(
				filter(data => !data.isSuccess() || !data.value.historyData),
				mapRD(data => data.data),
			);
			const instrument$ = instrumentStore.getBySymbol(symbol);
			const description$ = instrument$.pipe(mapRD(instrument => instrument.description));
			const precision$ = instrument$.pipe(mapRD(instrument => instrument.precision));

			return new Sink(
				{
					aggregationPeriodPair$: aggregationPeriodPair.value$,
					precision$,
					candles$,
					lastCandles$,
					periods$,
					description$,
					selectedPeriod$,
					isCrossToolEnabled$,
					selectedChartType$,
					onChangePeriod: selectedPeriod.handle,
					onChangeChartType: selectedChartType.handle,
					onSwitchCrossTool: isCrossToolEnabled.handle,
				},
				requestChangeChartParameters$,
			);
		};

		return new Sink(create, effects$);
	},
);

const getAggregationPairMatchingPredicate = (period: ChartPeriod): Predicate<AggrPeriodRangePairTO> => ({
	aggregationPeriod,
}) =>
	pipe(
		aggregationPeriod,
		O.exists(({ name }) =>
			pipe(
				name,
				O.exists(name => name === period.name),
			),
		),
	);

const getChartRequestTO = (id: number, symbol: string, seconds: number, duration: number): ChartRequestTO => ({
	symbol,
	aggregationPeriodSeconds: seconds,
	range: duration,
	forexPriceField: O.some('bid'),
	studySubscription: O.some([]),
	subtopic: SUBTOPIC,
	maxBarsCount: 3500,
	extendedSession: true,
	id,
});
