import { Option } from 'fp-ts/lib/Option';
import { number, boolean, string, union, literal, array, type } from 'io-ts';
import { createOptionFromNullable } from 'io-ts-types';

export type ChartInstrumentTO = {
	id: Option<number>;
	precision: Option<number>;
	hasVolume: Option<boolean>;
	hasDescription: Option<boolean>;
	code: Option<string>;
	description: Option<string>;
	type: Option<
		| 'CURRENCY'
		| 'STOCK'
		| 'FOREX'
		| 'FUTURES'
		| 'PRODUCT'
		| 'INDEX'
		| 'CFD'
		| 'OPTION'
		| 'SPREAD_BET'
		| 'CFD_FUTURES'
		| 'SB_FUTURES'
		| 'FX_OPTION'
		| 'FX_FORWARD'
		| 'CFD_OPTION'
		| 'SB_OPTION'
		| 'CFD_STOCK'
		| 'SB_STOCK'
		| 'CFD_FOREX'
		| 'SB_FOREX'
		| 'SPREAD'
		| 'WARRANT'
		| 'ETF'
		| 'OTHER'
	>;
	pricePrecisions: Option<Array<number>>;
	priceIncrements: Option<Array<number>>;
	bondFraction: Option<boolean>;
};
export const ChartInstrumentTOIO = type({
	id: createOptionFromNullable(number),
	precision: createOptionFromNullable(number),
	hasVolume: createOptionFromNullable(boolean),
	hasDescription: createOptionFromNullable(boolean),
	code: createOptionFromNullable(string),
	description: createOptionFromNullable(string),
	type: createOptionFromNullable(
		union([
			literal('CURRENCY'),
			literal('STOCK'),
			literal('FOREX'),
			literal('FUTURES'),
			literal('PRODUCT'),
			literal('INDEX'),
			literal('CFD'),
			literal('OPTION'),
			literal('SPREAD_BET'),
			literal('CFD_FUTURES'),
			literal('SB_FUTURES'),
			literal('FX_OPTION'),
			literal('FX_FORWARD'),
			literal('CFD_OPTION'),
			literal('SB_OPTION'),
			literal('CFD_STOCK'),
			literal('SB_STOCK'),
			literal('CFD_FOREX'),
			literal('SB_FOREX'),
			literal('SPREAD'),
			literal('WARRANT'),
			literal('ETF'),
			literal('OTHER'),
		]),
	),
	pricePrecisions: createOptionFromNullable(array(number)),
	priceIncrements: createOptionFromNullable(array(number)),
	bondFraction: createOptionFromNullable(boolean),
});
