import * as React from 'react';
import { Omit } from 'typelevel-ts';
import { Button, TButtonProps as DefaultButtonProps } from '../../../../../ui-common/button/button.component';
import { Component } from 'react';
import { withTheme } from '@devexperts/react-kit/dist/utils/withTheme';
import { BUTTON } from '@devexperts/react-kit/dist/components/Button/Button';
import { PURE } from '@devexperts/react-kit/dist/utils/pure';
import { constUndefined } from 'fp-ts/lib/function';
import { withTranslation, WithTranslation } from 'react-i18next';
import { tspan } from '../../../../../utils/i18n.utils';

import * as css from './theme/registration-button.module.css';

export type FullButtonProps = Omit<DefaultButtonProps, 'theme'> &
	WithTranslation & {
		theme: {
			container?: string;
		} & DefaultButtonProps['theme'];
	};

@PURE
class RawRegistrationButton extends Component<FullButtonProps> {
	render() {
		const { theme, ...restProps } = this.props;

		return (
			<Button {...restProps} theme={theme} onClick={constUndefined}>
				{tspan('continueRegistrationBtn', 'Continue Registration')}
			</Button>
		);
	}
}

const wTheme = {
	...css,
};

export const RegistrationButton = withTranslation()(withTheme(BUTTON, wTheme)(RawRegistrationButton));
