import * as React from 'react';
import { Fragment, PureComponent, ReactNode } from 'react';
import { WithTranslation, withTranslation } from 'react-i18next';
import * as cn from 'classnames';
import { Accounts } from '../../../../ui-desktop/layout/components/menu/components/menu/menu.model';
import { makeAccountUrl } from '../../../../view-model/menu/menu.utils';
import { Link } from '@devexperts/react-kit/dist/components/Link/Link';
import { MenuAccount } from '../menu-account/menu-account.component';
import { PartialKeys } from '@devexperts/utils/dist/object';
import { RawMenuListItemProps } from '../menu-list-item/menu-list-item.component';
import { withTheme } from '@devexperts/react-kit/dist/utils/withTheme';
import { MakeTheme } from '../../../../utils/theme.utils';
import * as css from './theme/menu-accounts.module.css';

export type RawMenuAccountsProps = {
	theme: MakeTheme<
		| 'current'
		| 'current_opened'
		| 'icon'
		| 'list'
		| 'accountContainers'
		| 'accountContainers_last'
		| 'accountCurrentContainer'
		| 'accountCurrentContainer_isOpened'
		| 'accountStatus'
		| 'accountId'
		| 'accountCurrency'
		| 'accountIcon'
	>;
	accounts: Accounts;
	arrowIcon: ReactNode;
	handleSwitch: () => void;
};

type MenuAccountsState = {
	isOpened: boolean;
};

class RawMenuAccounts extends PureComponent<RawMenuAccountsProps & WithTranslation, MenuAccountsState> {
	readonly state: MenuAccountsState = {
		isOpened: false,
	};

	render() {
		const { accounts, theme, arrowIcon, i18n } = this.props;
		const { language } = i18n;
		const { isOpened } = this.state;
		const { currentAccount, availableAccounts } = accounts;

		return (
			<Fragment>
				<div
					className={cn(theme.current, { [theme.current_opened as string]: isOpened })}
					onClick={this.toggleList}>
					<i className={theme.icon}>{arrowIcon}</i>
					<MenuAccount
						theme={{
							container: cn(theme.accountCurrentContainer, {
								[theme.accountCurrentContainer_isOpened as string]: isOpened,
							}),
							status: theme.accountStatus,
							id: theme.accountId,
							icon: theme.accountIcon,
							currency: theme.accountCurrency,
						}}
						data={{
							currency: currentAccount.currency,
							accountId: currentAccount.accountId,
							status: currentAccount.status,
						}}
						isSelected={false}
					/>
				</div>

				{isOpened && (
					<div className={theme.list}>
						{availableAccounts.map((account, index) => {
							const isLast = availableAccounts.length - 1 === index;
							return (
								<Link
									href={makeAccountUrl(account.accountId, language)}
									key={account.accountId}
									onClick={this.props.handleSwitch}>
									<MenuAccount
										theme={{
											container: cn(theme.accountContainers, {
												[theme.accountContainers_last as string]: isLast,
											}),
											status: theme.accountStatus,
											id: theme.accountId,
											icon: theme.accountIcon,
											currency: theme.accountCurrency,
										}}
										data={{
											currency: account.currency,
											accountId: account.accountId,
											status: account.status,
										}}
										isSelected={account.accountId === currentAccount.accountId}
									/>
								</Link>
							);
						})}
					</div>
				)}
			</Fragment>
		);
	}

	toggleList = () => {
		this.setState({ isOpened: !this.state.isOpened });
	};
}

const wTheme = {
	...css,
};

export type MenuAccountsProps = PartialKeys<RawMenuListItemProps, 'theme'>;
export const MenuAccounts = withTranslation()(withTheme(Symbol(), wTheme)(RawMenuAccounts));
