import * as React from 'react';
import { PureComponent, ReactNode, MouseEvent } from 'react';
import * as cn from 'classnames';
import { PartialKeys } from '@devexperts/utils/dist/object';
import { Link } from '@devexperts/react-kit/dist/components/Link/Link';
import { withTheme } from '@devexperts/react-kit/dist/utils/withTheme';
import { MakeTheme } from '../../../../utils/theme.utils';
import * as css from './theme/menu-list-item.module.css';

export type RawMenuListItemProps = {
	theme: MakeTheme<'container' | 'icon' | 'container_disabled'>;
	text: ReactNode;
	href?: string;
	target?: string;
	rel?: string;
	icon: ReactNode;
	onClick?: () => void;
	preventDefault?: boolean;
	isDisabled?: boolean;
};

export class RawMenuListItem extends PureComponent<RawMenuListItemProps> {
	render() {
		const { text, icon, isDisabled, href, target, rel, theme } = this.props;

		return (
			<Link
				theme={{ container: cn(theme.container, { [theme.container_disabled as string]: isDisabled }) }}
				href={href}
				target={target}
				rel={rel}
				onClick={this.handleClick}>
				<i className={theme.icon}>{icon}</i>
				<span>{text}</span>
			</Link>
		);
	}
	handleClick = (e: MouseEvent<HTMLElement>) => {
		const { onClick, isDisabled, preventDefault } = this.props;

		if (isDisabled || preventDefault) {
			e.preventDefault();
		}
		if (isDisabled || !onClick) {
			return;
		}
		onClick();
	};
}

const wTheme = {
	...css,
};

export type MenuListItemProps = PartialKeys<RawMenuListItemProps, 'theme'>;
export const MenuListItem = withTheme(Symbol(), wTheme)(RawMenuListItem);
