import { ReactNode } from 'react';

export enum TableCellAlign {
	Left = '* LEFT *',
	Center = '* CENTER *',
	Right = '* RIGHT *',
}

export type Column = {
	align: TableCellAlign;
	width: number;
	label: ReactNode;
	key: string;
};

export type TableCellProps<A> = {
	dataKey: string;
	rowData: A;
	rowIndex: number;
};

export type TableConfigItem = {
	width: number;
	dataKey: string;
};
