import { withRXWithDefaults } from '../../../../utils/withRXWithDefaults';
import { combineContext, deferContext, asks } from '@devexperts/rx-utils/dist/context.utils';
import { ConnectionLostPopup } from '../../components/connection-lost-popup/connection-lost-popup.component';
import { runOnMount } from '../../../../utils/react.utils';
import {
	createConnectionLostViewModel,
	ConnectionStatusViewModel,
} from '../../../../view-model/connection-status/connection-status.view-model';
import { constant } from 'fp-ts/lib/function';

const connectionStatusViewModel = 'connectionStatusViewModel';

type ConectionLostPopupContainerContext = {
	connectionStatusViewModel: ConnectionStatusViewModel;
};

const Container = asks((ctx: ConectionLostPopupContainerContext) =>
	withRXWithDefaults(ConnectionLostPopup)(() => {
		const {
			connectionStatusViewModel: { isConnectionError$ },
		} = ctx;

		return {
			props: {
				isPopupOpened: isConnectionError$,
			},
			defaultProps: {
				isPopupOpened: false,
			},
		};
	}),
);

export const ConnectionLostPopupContainer = combineContext(
	deferContext(Container, connectionStatusViewModel),
	createConnectionLostViewModel,
	(Container, createConnectionLostViewModel) =>
		runOnMount(Container, () =>
			constant({
				[connectionStatusViewModel]: createConnectionLostViewModel(),
			}),
		),
);
