import { MonoTypeOperatorFunction } from 'rxjs';
import { getSetoid, RemoteData } from '@devexperts/remote-data-ts';
import { distinctUntilChanged } from 'rxjs/operators';
import { Eq } from 'fp-ts/lib/Eq';
import { setoidStrict } from '@devexperts/utils/dist/typeclasses/setoid/setoid.utils';

export function distinctUntilChangedStrict<L, A>(setoid: Eq<A>): MonoTypeOperatorFunction<RemoteData<L, A>> {
	return distinctUntilChanged(getSetoid(setoidStrict, setoid).equals) as any;
}
