import { DailyTradingHours } from '../../ui-common/symbol-info/symbol-info.model';
import { fromArray } from 'fp-ts/lib/NonEmptyArray';
import { AvaTradingSessionTO } from '../../api/generated/swagger/definitions/AvaTradingSessionTO';

export const toTradingHours = (tradingSessions: Array<AvaTradingSessionTO>): DailyTradingHours =>
	fromArray(
		tradingSessions.map(session => ({
			startTime: session.startTimeFormatted,
			endTime: session.endTimeFormatted,
		})),
	);
