import { withRXWithDefaults } from '../../../../utils/withRXWithDefaults';
import { ask, combineContext, deferContext } from '@devexperts/rx-utils/dist/context.utils';
import { WelcomePopup } from '../../components/welcome-popup/welcome-popup.component';
import { WelcomeViewModel, createWelcomeViewModel } from '../../../../view-model/welcome/welcome.view-model';
import { runOnMount } from '../../../../utils/react.utils';

type WelcomePopupContainerContext = {
	welcomeViewModel: WelcomeViewModel;
};

const Container = combineContext(ask<WelcomePopupContainerContext>(), ctx =>
	withRXWithDefaults(WelcomePopup)(() => {
		const { shouldOpenPopup$, onClose } = ctx.welcomeViewModel;

		return {
			props: {
				isPopupOpened: shouldOpenPopup$,
			},
			defaultProps: {
				isPopupOpened: false,
				onClose,
			},
		};
	}),
);

export const WelcomePopupContainer = combineContext(
	deferContext(Container, 'welcomeViewModel'),
	createWelcomeViewModel,
	(Container, createWelcomeViewModel) =>
		runOnMount(Container, () => () => ({
			welcomeViewModel: createWelcomeViewModel(),
		})),
);
