import { withRXWithDefaults } from '../../../../utils/withRXWithDefaults';
import { pending } from '@devexperts/remote-data-ts';
import { ask, combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { WatchlistStore } from '../../../../store/watchlist/watchlist.store';
import { LiveData } from '@devexperts/rx-utils/dist/rd/live-data.utils';
import { CategoriesList } from '../../components/categories/categories.component';
import { mapRD } from '@devexperts/rx-utils/dist/rd/operators/mapRD';
import { none, some } from 'fp-ts/lib/Option';
import { combineLatestRD } from '@devexperts/rx-utils/dist/rd/operators/combineLatestRD';
import { fromArray } from 'fp-ts/lib/NonEmptyArray';
import { CategoryList } from '../../components/categories/categories.model';
import { trendsSort } from './categories.utils';

type CategoryContainerContext = {
	watchlistStore: WatchlistStore;
};

export const CategoriesContainer = combineContext(
	CategoriesList,
	ask<CategoryContainerContext>(),
	(CategoriesList, e) =>
		withRXWithDefaults(CategoriesList)(() => {
			const { watchlistStore } = e;
			//#region component streams
			const categories$: LiveData<Error, CategoryList[]> = combineLatestRD(
				watchlistStore.private$,
				watchlistStore.public$,
				watchlistStore.categories$,
			).pipe(
				mapRD(([privateList, publicList, categoryList]) => [
					{
						title: none,
						categories: fromArray(privateList),
					},
					{
						title: some('Trends'),
						categories: fromArray(publicList.sort(trendsSort)),
					},
					{
						title: some('Categories'),
						categories: fromArray(categoryList),
					},
				]),
			);
			//#endregion

			return {
				defaultProps: {
					data: pending,
				},
				props: {
					data: categories$,
				},
			};
		}),
);
