import * as React from 'react';
import { PureComponent } from 'react';
import { PeriodRangePair } from '../chart/chart.model';
import { Toolbar } from '../toolbar/toolbar/toolbar.component';
import { CrossToolMode, Period, ChartType } from '../toolbar/toolbar.model';
import { isCrosshair } from '../toolbar/toolbar.utils';
import { Chart } from '../chart/chart.component';
import { withDefaults } from '@devexperts/react-kit/dist/utils/with-defaults';
import { Plugin } from '../../../../ui-common/chart/plugin/plugin.model';
import { Option } from 'fp-ts/lib/Option';
import Candle from '@dx-private/dxchart5/dist/js/chart/model/Candle';
import * as css from './theme/chart-wrapper.module.css';

export type ChartWrapperProps = {
	symbol: string;
	precision: number;
	title: Option<string>;
	plugins: Plugin[];
	historical: Candle[];
	periodRangePair: PeriodRangePair;
	lastCandles: Candle[];
	periods: Period[];
	selectedPeriod: Period;
	selectedType: ChartType;
	selectedCrossToolMode: CrossToolMode;
	onPeriodSelect: (period: Period) => void;
	onTypeSelect: (type: ChartType) => void;
	onCrossToolModeSelect: (type: CrossToolMode) => void;
};

class RawChartWrapper extends PureComponent<ChartWrapperProps> {
	render() {
		const {
			title,
			symbol,
			precision,
			periods,
			selectedPeriod,
			onPeriodSelect,
			plugins,
			historical,
			lastCandles,
			selectedType,
			onTypeSelect,
			selectedCrossToolMode,
			onCrossToolModeSelect,
			periodRangePair,
		} = this.props;

		return (
			<div className={css.container}>
				<Toolbar
					title={title}
					periods={periods}
					selectedPeriod={selectedPeriod}
					onPeriodSelect={onPeriodSelect}
					selectedType={selectedType}
					onTypeSelect={onTypeSelect}
					selectedCrossToolMode={selectedCrossToolMode}
					onCrossToolModeSelect={onCrossToolModeSelect}
				/>
				<Chart
					isCrossToolEnabled={isCrosshair(selectedCrossToolMode)}
					symbol={symbol}
					precision={precision}
					historical={historical}
					periodRangePair={periodRangePair}
					plugins={plugins}
					last={lastCandles}
					selectedType={selectedType}
				/>
			</div>
		);
	}
}

export const ChartWrapper = withDefaults<ChartWrapperProps, 'plugins'>({
	plugins: [],
})(RawChartWrapper);
