import { Observable } from 'rxjs';
import { asks } from '@devexperts/rx-utils/dist/context.utils';
import { map } from 'rxjs/operators';
import { SocketClient, ConnectionStatus } from '../../api/socket-client';
import { constant } from 'fp-ts/lib/function';

export type ConnectionStatusViewModel = {
	isConnectionError$: Observable<boolean>;
};

type ConnectionStatusViewModelContext = {
	socketClient: SocketClient;
};

type CreateConnectionLostViewModel = () => ConnectionStatusViewModel;

const ErrorStatuses: ConnectionStatus[] = ['ERROR', 'TIMEDOUT'];

export const createConnectionLostViewModel = asks(
	(ctx: ConnectionStatusViewModelContext): CreateConnectionLostViewModel => {
		const {
			socketClient: { connectionStatus$ },
		} = ctx;

		const isConnectionError$ = connectionStatus$.pipe(map(status => ErrorStatuses.includes(status)));

		return constant({
			isConnectionError$,
		});
	},
);
