import { ask, combineContext, deferContext } from '@devexperts/rx-utils/dist/context.utils';
import {
	OrderDashboardChartViewModel,
	createOrderDashboardChartViewModel,
} from '../../../../view-model/orders/order-dashboard-chart.view-model';
import { withRXWithDefaults } from '../../../../utils/withRXWithDefaults';
import { OrderDashboardChart } from '../../components/orders-dashboard/order-dashboard-chart.component';
import { pending } from '@devexperts/remote-data-ts';
import { runOnMount } from '../../../../utils/react.utils';
import { withProps } from '../../../../utils/with-props.utils';
import { mapRD } from '@devexperts/rx-utils/dist/rd/operators/mapRD';
import { combineLatestRD } from '@devexperts/rx-utils/dist/rd/operators/combineLatestRD';

type OrderDashboardChartContainerContext = {
	orderDashboardChartViewModel: OrderDashboardChartViewModel;
};

const Container = combineContext(
	OrderDashboardChart,
	ask<OrderDashboardChartContainerContext>(),
	(OrderDashboardChart, context) =>
		withRXWithDefaults(OrderDashboardChart)(() => {
			const {
				orderDashboardChartViewModel: { symbol$, costBasis$ },
			} = context;

			const data$ = combineLatestRD(symbol$, costBasis$).pipe(
				mapRD(([symbol, costBasis]) => ({ symbol, costBasis })),
			);
			return {
				defaultProps: {
					data: pending,
				},
				props: {
					data: data$,
				},
			};
		}),
);

const withOrderId = withProps<{ orderId: number }>();

export const OrderDashboardChartContainer = combineContext(
	deferContext(Container.map(withOrderId), 'orderDashboardChartViewModel'),
	createOrderDashboardChartViewModel,
	(Container, createOrderDashboardChartViewModel) =>
		runOnMount(Container, () => props => ({
			orderDashboardChartViewModel: createOrderDashboardChartViewModel(props.orderId),
		})),
);
