export default class View {
	private _subscriptions: Function[] = [];

	protected _subscribe(clearers: Function[]) {
		clearers.forEach(clearer => this._subscriptions.push(clearer));
	}

	/**
	 * Closes view, unsbuscribes from subscriptions
	 */
	close() {
		while (this._subscriptions.length !== 0) {
			const close = this._subscriptions.pop();
			if (close) {
				close();
			}
		}
	}
}
