import { LiveData } from '@devexperts/rx-utils/dist/rd/live-data.utils';
import { SocketClient } from '../../api/socket-client';
import { filter, map, switchMap } from 'rxjs/operators';
import { isMarginImpactTO } from '../../api/socket/socket-message.to';
import { EntityStore } from '@devexperts/rx-utils/dist/rd/entity-store.utils';
import { findFirst } from 'fp-ts/lib/Array';
import { mapRD } from '@devexperts/rx-utils/dist/rd/operators/mapRD';
import { createHandler } from '@devexperts/rx-utils/dist/create-handler.utils';
import { filterMap } from '../../utils/rxjs/filtermap';
import { marginImpactsController } from '../../api/generated/swagger/controllers/MarginImpactsController';
import { MarginImpactTO } from '../../api/generated/swagger/definitions/MarginImpactTO';
import { Sink } from '@devexperts/rx-utils/dist/sink.utils';
import { ask, combineContext, fromReader } from '@devexperts/rx-utils/dist/context.utils';
import { tapOnLastUnsubscribe } from '../../utils/rxjs/tap-on-last-unsubscribe';
import { combineLatest } from 'rxjs/internal/observable/combineLatest';
import { Observable } from 'rxjs/internal/Observable';
import { pipe } from 'fp-ts/lib/pipeable';
import { constant } from 'fp-ts/lib/function';
import * as O from 'fp-ts/lib/Option';
import { shareReplayWithDefaults } from '../../utils/rxjs/shareReplayWithDefaults';

export type MarginImpactStore = {
	readonly getMarginImpactBySymbol: (symbol: string) => LiveData<Error, MarginImpactTO>;
};

export type MarginImpactStoreContext = {
	socketClient: SocketClient;
};

export type CreateMarginImpactStore = () => Sink<MarginImpactStore>;

export const createMarginImpactStore = combineContext(
	fromReader(marginImpactsController),
	ask<MarginImpactStoreContext>(),
	(marginImpactsController, e): CreateMarginImpactStore => {
		const { socketClient } = e;
		const pk = (data: MarginImpactTO) =>
			pipe(
				data.instrumentSymbol,
				O.getOrElse(constant('')),
			);

		const marginImpacts$ = socketClient.message$.pipe(filter(isMarginImpactTO));

		return () => {
			const store = new EntityStore<Error, MarginImpactTO>();

			const onSymbolRemove = createHandler<string>();
			const removeSymbolEffect$ = onSymbolRemove.value$.pipe(
				//request on each call
				switchMap(symbol =>
					store.remove(symbol, pk, () =>
						marginImpactsController
							.changeSubscriptionByInstruments({
								body: {
									symbolsToAdd: [],
									symbolsToRemove: [symbol],
								},
							})
							.pipe(mapRD(() => [])),
					),
				),
				shareReplayWithDefaults(),
			);

			const getMarginImpactBySymbol = (symbol: string): LiveData<Error, MarginImpactTO> => {
				const request$ = marginImpactsController.changeSubscriptionByInstruments({
					body: {
						symbolsToAdd: [symbol],
						symbolsToRemove: [],
					},
				});

				const marginImpact$: Observable<MarginImpactTO> = marginImpacts$.pipe(
					filterMap(marginImpacts =>
						findFirst(
							marginImpacts.body,
							marginImpact =>
								pipe(
									marginImpact.instrumentSymbol,
									O.getOrElse(constant('')),
								) === symbol,
						),
					),
					shareReplayWithDefaults(),
				);

				const result$ = combineLatest(request$, marginImpact$).pipe(
					map(([request, entity]) => request.map(() => entity)),
				);

				return store.get(symbol, () => result$.pipe(tapOnLastUnsubscribe(() => onSymbolRemove.handle(symbol))));
			};

			return new Sink(
				{
					getMarginImpactBySymbol,
				},
				removeSymbolEffect$,
			);
		};
	},
);
