import * as React from 'react';
import { PureComponent, ReactNode, Fragment } from 'react';
import { withTranslation, WithTranslation } from 'react-i18next';
import cn from 'classnames';
import { empty } from 'fp-ts/lib/Record';
import { Option } from 'fp-ts/lib/Option';
import * as O from 'fp-ts/lib/Option';
import { Scrollable } from '@devexperts/react-kit/dist/components/Scrollable/Scrollable';
import { Link } from '@devexperts/react-kit/dist/components/Link/Link';
import { LogoutIcon } from '../../../../../ui-kit/components/icons/logout.icon';
import { LocaleSelector } from '../../../../../../ui-common/locale-selector/locale-selector.component';
import { MenuDemo } from './menu-demo.component';
import { MenuLive } from './menu-live.component';
import { combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { Accounts, Mode } from './menu.model';
import { tspan } from '../../../../../../utils/i18n.utils';
import { href } from '../../../../../../utils/router.utils';
import { routes } from '../../../../../../ui-common/routes';
import { pipe } from 'fp-ts/lib/pipeable';
import { constant } from 'fp-ts/lib/function';
import { DisableOverlay } from '../../../../../ui-kit/components/preview-mode/components/disable-overlay.component';
import { RegistrationInfoBox } from '../../../../../ui-kit/components/preview-mode/components/registration-info-box.component';

import * as css from './theme/menu.module.css';

export type MenuProps = {
	email: Option<string>;
	mode: Mode;
	accounts: Accounts;
	name: Option<string>;
	isRealAccount: Option<boolean>;
	onClose: () => void;
	onPreloaderChange: (state: boolean) => void;
	proTraderAvaliable: boolean;
	previewMode: Option<boolean>;
};

const logoutLinkTheme = { container: css.logout };

export const Menu = combineContext(MenuDemo, MenuLive, (MenuDemo, MenuLive) =>
	withTranslation()(
		class Menu extends PureComponent<MenuProps & WithTranslation> {
			render() {
				const { name, isRealAccount, email, mode, previewMode } = this.props;
				return (
					<div className={css.container} onClick={this.onContainerClick}>
						<div className={css.panel}>
							<div className={css.wrapper}>
								<div>
									<div className={css.header}>
										<h3 className={css.account}>
											{pipe(
												name,
												O.getOrElse(constant('')),
											)}
										</h3>
										{pipe(
											previewMode,
											O.fold(
												() =>
													pipe(
														isRealAccount,
														O.map(isReal => (
															<div
																className={cn(css.accountLabel, {
																	[css.accountLabel_real]: isReal,
																})}>
																{isReal
																	? tspan('asideMenuReal', 'Real Account')
																	: tspan('asideMenuDemo', 'Demo Account')}
															</div>
														)),
														O.toNullable,
													),
												previewMode =>
													previewMode ? (
														<div className={cn(css.accountLabel, css.accountLabel_preview)}>
															{tspan('asideMenuPreview', 'Preview Account')}
														</div>
													) : null,
											),
										)}
									</div>
								</div>
								<Scrollable>
									<Fragment>
										{pipe(
											previewMode,
											O.map(
												previewMode =>
													previewMode && (
														<RegistrationInfoBox
															text={tspan(
																'menuRegistrationDescription',
																'Continue registration in order to view full menu',
															)}
														/>
													),
											),
											O.toNullable,
										)}
										<div className={css.menuContainer}>
											{this.renderContent(mode)}
											{pipe(
												previewMode,
												O.map(previewMode => previewMode && <DisableOverlay />),
												O.toNullable,
											)}
										</div>
									</Fragment>
								</Scrollable>
							</div>
							<div className={css.footer}>
								<Link href={href(routes.api.auth.logout.redirect, empty)} theme={logoutLinkTheme}>
									<div className={css.logoutLinkEmailWrapper}>
										<div className={css.logoutIconWrapper}>
											<i className={css.logoutIcon}>
												<LogoutIcon />
											</i>
											{tspan('menuLogout', 'Log Out')}
										</div>
										{pipe(
											email,
											O.map(email => <div className={css.email}>{email}</div>),
											O.toNullable,
										)}
									</div>
								</Link>
								<div className={css.localeSelectorWrapper}>
									<LocaleSelector />
								</div>
							</div>
						</div>
					</div>
				);
			}

			private onContainerClick = (e: React.MouseEvent<HTMLDivElement>) => {
				if (e.target === e.currentTarget) {
					this.props.onClose();
				}
			};

			renderContent = (mode: Mode): ReactNode => {
				const { onClose, onPreloaderChange, accounts, proTraderAvaliable } = this.props;
				switch (mode) {
					case Mode.Demo: {
						return <MenuDemo onClose={onClose} />;
					}
					case Mode.Real:
					case Mode.DemoLive:
					case Mode.Regular: {
						return (
							<MenuLive
								onClose={onClose}
								onPreloaderChange={onPreloaderChange}
								accounts={accounts}
								mode={mode}
								proTraderAvaliable={proTraderAvaliable}
							/>
						);
					}
				}
			};
		},
	),
);
