import { href, Router } from '../../../utils/router.utils';
import { RouteLinkComponent, RouteLinkProps } from '../components/route-link.component';
import { createElement, PureComponent } from 'react';
import { Omit } from 'typelevel-ts';
import { PartialKeys } from '@devexperts/utils/dist/object';
import { distinctUntilChanged, map } from 'rxjs/operators';
import { combineLatest } from 'rxjs';
import { Match } from 'fp-ts-routing';
import { ask } from '@devexperts/rx-utils/dist/context.utils';
import { withRXWithDefaults } from '../../../utils/withRXWithDefaults';

export type RouteLinkContainerContext = {
	router: Router;
};

const rx = withRXWithDefaults(RouteLinkComponent);

export type RouteLinkContainerProps<A extends object> = PartialKeys<
	Omit<RouteLinkProps, 'onNavigate' | 'isActive' | 'isExact' | 'href'>,
	'action'
> & {
	to: Match<A>;
	value: A;
};

export const RouteLink = ask<RouteLinkContainerContext>().map(e => {
	const Intermediate = rx(props$ => {
		const href$ = props$.pipe(
			map(props => props.href),
			distinctUntilChanged(),
		);
		const pathname$ = e.router.state$.pipe(
			map(state => state.location.pathname),
			distinctUntilChanged(),
		);
		const isActive$ = combineLatest(href$, pathname$).pipe(
			map(([href, pathname]) => {
				const pattern = `^${decodeURIComponent(href)}(\/|$)`;
				const regexp = new RegExp(pattern);

				return regexp.test(decodeURIComponent(pathname));
			}),
			distinctUntilChanged(),
		);
		const isExact$ = combineLatest(href$, pathname$).pipe(
			map(([href, pathname]) => pathname === href),
			distinctUntilChanged(),
		);
		return {
			props: {
				isActive: isActive$,
				isExact: isExact$,
			},
			defaultProps: {
				onNavigate: options => {
					const { href, action } = options;
					switch (action) {
						case 'PUSH': {
							return e.router.push(href);
						}
						case 'REPLACE': {
							return e.router.replace(href);
						}
					}
				},
			},
		};
	});

	// dedicated class for type inference of type argument A
	return class RouteLinkContainer<A extends object> extends PureComponent<RouteLinkContainerProps<A>> {
		static displayName = `RouteLinkContainer(${Intermediate.displayName})`;

		render() {
			const { children, to, value, action, ...props } = this.props;
			const hrefValue = href(to, value);
			const pattern = `^${decodeURIComponent(hrefValue)}(\/|$)`;
			const regexp = new RegExp(pattern);

			const isActive = regexp.test(decodeURIComponent(e.router.state.location.pathname));
			const isExact = decodeURIComponent(e.router.state.location.pathname) === decodeURIComponent(hrefValue);
			return createElement(Intermediate, {
				...props,
				isActive,
				isExact,
				action: action || 'PUSH',
				href: hrefValue,
			});
		}
	};
});
