import * as React from 'react';
import { PureComponent, Fragment } from 'react';
import { ListChildComponentProps } from 'react-window';
import { withTranslation, WithTranslation } from 'react-i18next';
import * as cn from 'classnames';
import { formatMoney, formatNumber } from 'accounting';
import { format } from 'date-fns';
import { findFirst } from 'fp-ts/lib/Array';
import { empty } from 'fp-ts/lib/Record';
import * as O from 'fp-ts/lib/Option';
import { combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { RemoteData } from '@devexperts/remote-data-ts';
import { RouteLink } from '../../../../ui-common/route-link/containers/route-link.container';
import { routes } from '../../../../ui-common/routes';
import { Link } from '../../../../ui-common/link/link.component';
import { AutosizedTable as Table } from '../../../../ui-common/autosized-table-ava/autosized-table.component';
import { TableCellAlign } from '../../../../ui-common/table-ava/table.model';
import { TableCell } from '../../../../ui-common/table-ava/table-cell.component';
import { InfoIcon } from '../../../../ui-common/icons/info.icon';
import { Optional } from '../../../../ui-common/optional/optional.component';
import {
	HistoryTableItem,
	HistoryTableConfigItem,
	HistoryTableColumnNames,
	HistoryOrderSide,
} from './history-table.model';
import { stringifyOrderSideEnum, getItemKey, getColumnsConfig } from './history-table.utils';
import { RenderRemoteData } from '@devexperts/react-kit/dist/components/render-remote-data/render-remote-data.component';
import { EmptyScreenContainer } from '../../../empty-screen/containers/empty-screen.container';
import { ScreenType } from '../../../../ui-common/empty-screen/components/empty-screen.component';
import { Preloader } from '../../../ui-kit/components/preloader/preloader.component';
import { tspan, TRANSLATION_PLACEHOLDER } from '../../../../utils/i18n.utils';
import { pipe } from 'fp-ts/lib/pipeable';
import { constant } from 'fp-ts/lib/function';
import * as css from './theme/history-table.module.css';

export type HistoryTableProps = {
	data: RemoteData<Error, HistoryTableItem[]>;
	config: HistoryTableConfigItem[];
	currencySymbol: string;
};

const defaultColumnWidth = 150;

export const HistoryTable = combineContext(RouteLink, EmptyScreenContainer, (RouteLink, EmptyScreenContainer) =>
	withTranslation()(
		class HistoryTable extends PureComponent<HistoryTableProps & WithTranslation> {
			private getTranslatedColumnNames = (): HistoryTableColumnNames => {
				const { currencySymbol } = this.props;

				return {
					openDateTime: tspan('historyTableOpenDateTime', 'Open Date-time'),
					instrument: tspan('historyTableInstrument', 'Instrument'),
					profitLoss: tspan(
						'historyTableProfitLoss',
						'Profit/Loss',
						`${TRANSLATION_PLACEHOLDER}, ${currencySymbol}`,
					),
					side: tspan('historyTableSide', 'Side'),
					amount: tspan('historyTableAmount', 'Amount'),
					openPrice: tspan('historyTableOpenPrice', 'Open Price'),
					closePrice: tspan('historyTableClosePrice', 'Close Price'),
					closeDateTime: tspan('historyTableCloseDateTime', 'Close Date-time'),
				};
			};

			render() {
				const { data } = this.props;

				return (
					<RenderRemoteData
						data={data}
						success={this.renderSuccess}
						noData={data => data.length === 0}
						DataStateNoData={() => <EmptyScreenContainer screenType={ScreenType.HistorySearch} />}
						DataStatePending={() => <Preloader />}
						DataStateFailure={() => <div>Error loading data</div>}
					/>
				);
			}

			private renderSuccess = (data: HistoryTableItem[]) => {
				const { config } = this.props;

				const columnNames = this.getTranslatedColumnNames();

				return (
					<Table
						itemCount={data.length}
						itemSize={60}
						itemData={data}
						itemKey={getItemKey}
						columns={getColumnsConfig(config, columnNames)}>
						{this.renderRow}
					</Table>
				);
			};
			private renderRow = (props: ListChildComponentProps) => {
				const { style } = props;
				const rowData = props.data[props.index];

				return (
					<RouteLink
						to={routes.history.root}
						value={empty}
						render={({ onNavigate }) => (
							<div className={css.row} onClick={onNavigate} style={style}>
								<div className={css.rowColumn} style={this.getFlexBasis('openDateTime')}>
									{this.renderOpenDateTime(rowData)}
								</div>
								<div className={css.rowColumn} style={this.getFlexBasis('instrument')}>
									{this.renderInstrument(rowData)}
								</div>
								<div className={css.rowColumn} style={this.getFlexBasis('profitLoss')}>
									{this.renderProfitLoss(rowData)}
								</div>
								<div className={css.rowColumn} style={this.getFlexBasis('side')}>
									{this.renderSide(rowData)}
								</div>
								<div className={css.rowColumn} style={this.getFlexBasis('amount')}>
									{this.renderAmount(rowData)}
								</div>
								<div className={css.rowColumn} style={this.getFlexBasis('openPrice')}>
									{this.renderOpenPrice(rowData)}
								</div>
								<div className={css.rowColumn} style={this.getFlexBasis('closePrice')}>
									{this.renderClosePrice(rowData)}
								</div>
								<div className={css.rowColumn} style={this.getFlexBasis('closeDateTime')}>
									{this.renderCloseDateTime(rowData)}
								</div>
								<div className={css.rowColumn} style={this.getFlexBasis('info-icon')}>
									{this.renderInfoIcon(rowData)}
								</div>
							</div>
						)}
					/>
				);
			};

			private renderOpenDateTime = (props: HistoryTableItem) => {
				const value = props.openDateTime;
				const cnCell = pipe(
					value,
					O.fold(constant(cn(css.cell, css.cell_empty)), constant(css.cell)),
				);
				const openDateTime = (
					<Optional
						value={pipe(
							value,
							O.map(date => this.toStringDate(date)),
						)}
					/>
				);

				return <div className={cnCell}>{openDateTime}</div>;
			};

			private renderInstrument = (props: HistoryTableItem) => {
				const value = props.instrument;
				const cnCell = pipe(
					value,
					O.fold(constant(cn(css.cell, css.cell_empty)), constant(css.cell)),
				);
				const instrument = <Optional value={value} />;

				return <div className={cnCell}>{instrument}</div>;
			};

			private renderProfitLoss = (props: HistoryTableItem) => {
				const value = props.profitLoss;
				const cnCell = pipe(
					value,
					O.fold(constant(cn(css.cell, css.cell_empty)), profitLoss =>
						cn(css.cell, css.cell_numeric, {
							[css.cell_positive]: profitLoss > 0,
							[css.cell_negative]: profitLoss < 0,
						}),
					),
				);
				const profitLoss = (
					<Optional
						value={pipe(
							value,
							O.map(this.toStringProfitLoss),
						)}
					/>
				);

				return <div className={cnCell}>{profitLoss}</div>;
			};

			private renderSide = (props: HistoryTableItem) => {
				const value = props.side;
				const cnCell = pipe(
					value,
					O.fold(constant(cn(css.cell, css.cell_empty)), side => {
						const stringifiedSide = stringifyOrderSideEnum(side);
						return cn(css.cell, {
							[css.cell_buy]: stringifiedSide === 'Buy',
							[css.cell_sell]: stringifiedSide === 'Sell',
						});
					}),
				);
				const side = (
					<Optional
						value={pipe(
							value,
							O.map(this.localizeOrderSideEnum),
						)}
					/>
				);

				return <div className={cnCell}>{side}</div>;
			};

			private renderAmount = (props: HistoryTableItem) => {
				const value = props.amount;
				const cnCell = pipe(
					value,
					O.fold(
						constant(cn(css.cell, [css.cell_numeric, css.cell_empty])),
						constant(cn(css.cell, css.cell_numeric)),
					),
				);
				const amount = <Optional value={value} />;

				return <div className={cnCell}>{amount}</div>;
			};

			private renderOpenPrice = (props: HistoryTableItem) => {
				const value = props.openPrice;
				const cnCell = pipe(
					value,
					O.fold(
						constant(cn(css.cell, [css.cell_numeric, css.cell_empty])),
						constant(cn(css.cell, css.cell_numeric)),
					),
				);
				const openPrice = (
					<Optional
						value={pipe(
							value,
							O.map(amount => formatNumber(amount, props.precision)),
						)}
					/>
				);

				return <div className={cnCell}>{openPrice}</div>;
			};

			private renderClosePrice = (props: HistoryTableItem) => {
				const value = props.closePrice;
				const cnCell = pipe(
					value,
					O.fold(
						constant(cn(css.cell, [css.cell_numeric, css.cell_empty])),
						constant(cn(css.cell, css.cell_numeric)),
					),
				);
				const closePrice = (
					<Optional
						value={pipe(
							value,
							O.map(price => formatNumber(price, props.precision)),
						)}
					/>
				);

				return <div className={cnCell}>{closePrice}</div>;
			};

			private renderCloseDateTime = (props: HistoryTableItem) => {
				const value = props.closeDateTime;
				const cnCell = pipe(
					value,
					O.fold(constant(cn(css.cell, css.cell_empty)), constant(css.cell)),
				);
				const closeDateTime = (
					<Optional
						value={pipe(
							value,
							O.map(date => this.toStringDate(date)),
						)}
					/>
				);

				return <div className={cnCell}>{closeDateTime}</div>;
			};

			private toStringProfitLoss = (value: number) => {
				const prefix = value > 0 ? '+' : '';
				const result = formatMoney(value, {
					format: '%v',
				});
				return `${prefix}${result}`;
			};

			private toStringDate = (value: Date) => {
				return (
					<Fragment>
						{format(value, 'mm.dd.yyyy hh:mm')}
						<span className={css.seconds}>{format(value, ':ss')}</span>
					</Fragment>
				);
			};

			private renderInfoIcon = (props: HistoryTableItem) => (
				<TableCell align={TableCellAlign.Right}>
					<RouteLink
						to={routes.history.order.info.root}
						value={{ historyOrderId: props.orderId }}
						render={({ href, onNavigate }) => (
							<Link href={href} onClick={onNavigate}>
								<i className={css.icon}>
									<InfoIcon />
								</i>
							</Link>
						)}
					/>
				</TableCell>
			);

			private getFlexBasis = (dataKey: string): { flexBasis: number } => {
				const { config } = this.props;
				const currentColumn = findFirst(config, item => {
					return item['dataKey'] === dataKey;
				});

				const width = pipe(
					currentColumn,
					O.fold(constant(defaultColumnWidth), value => value.width),
				);
				return { flexBasis: width };
			};

			private localizeOrderSideEnum = (side: HistoryOrderSide) => {
				const stringifiedSide = stringifyOrderSideEnum(side);

				switch (stringifiedSide) {
					case 'Buy':
						return tspan('historyTableBuySide', 'Buy');
					case 'Sell':
						return tspan('historyTableSellSide', 'Sell');
				}
			};
		},
	),
);
