import * as React from 'react';
import { PureComponent } from 'react';
import { WithTranslation, withTranslation } from 'react-i18next';
import { findFirst } from 'fp-ts/lib/Array';
import { Option } from 'fp-ts/lib/Option';
import * as O from 'fp-ts/lib/Option';
import { MenuItem } from '@devexperts/react-kit/dist/components/Menu/Menu';
import { CrossToolMode, Period, ChartType } from '../toolbar.model';
import { typeToString, modeToString, toMode, toType } from '../toolbar.utils';
import { eqNumber, eqString } from 'fp-ts/lib/Eq';
import { ChartBarIcon } from '../../../../ui-kit/components/icons/chart-bar.icon';
import { ChartCandlesIcon } from '../../../../ui-kit/components/icons/chart-candles.icon';
import { ChartLineIcon } from '../../../../ui-kit/components/icons/chart-line.icon';
import { Selectbox } from '../../../../ui-kit/components/selectbox/selectbox.component';
import { Menu, TMenuProps } from '../../../../ui-kit/components/menu/menu.component';
import { Scrollable } from '../../../../../ui-common/scrollable/scrollable.component';
import { PeriodsAnchor } from '../periods-anchor/periods-anchor.component';
import { TypeAnchor } from '../type-anchor/type-anchor.component';
import { NoCrosshairIcon } from '../../../../ui-kit/components/icons/no-crosshair.icon';
import { AimIcon } from '../../../../ui-kit/components/icons/aim.icon';
import { CrossToolAnchor } from '../cross-tool-anchor/cross-tool-anchor.component';
import { tspan } from '../../../../../utils/i18n.utils';
import { pipe } from 'fp-ts/lib/pipeable';
import { constant } from 'fp-ts/lib/function';
import * as css from './theme/toolbar.module.css';
import * as periodsSelectboxCss from './theme/periods-selectbox.child.module.css';
import * as typeSelectboxCss from './theme/type-selectbox.child.module.css';
import * as crossToolSelectboxCss from './theme/cross-tool-selectbox.child.module.css';

export type ToolbarProps = {
	periods: Period[];
	selectedPeriod: Period;
	title: Option<string>;
	selectedType: ChartType;
	selectedCrossToolMode: CrossToolMode;
	onPeriodSelect: (period: Period) => void;
	onTypeSelect: (type: ChartType) => void;
	onCrossToolModeSelect: (type: CrossToolMode) => void;
};

type ToolbarState = {
	isPeriodsSelectboxOpened: boolean;
	isTypeSelectboxOpened: boolean;
	isCrossToolModeSelectboxOpened: boolean;
};

class ToolbarRaw extends PureComponent<ToolbarProps & WithTranslation, ToolbarState> {
	readonly state: ToolbarState = {
		isPeriodsSelectboxOpened: false,
		isTypeSelectboxOpened: false,
		isCrossToolModeSelectboxOpened: false,
	};

	render() {
		const { periods, title, selectedPeriod, selectedType, selectedCrossToolMode } = this.props;
		const { isPeriodsSelectboxOpened, isTypeSelectboxOpened, isCrossToolModeSelectboxOpened } = this.state;

		return (
			<div className={css.container}>
				<div className={css.controls}>
					<Selectbox
						isOpened={isPeriodsSelectboxOpened}
						onToggle={this.handlePeriodsToggle}
						value={selectedPeriod.seconds}
						onValueChange={this.handlePeriodSelect}
						Anchor={PeriodsAnchor}
						Menu={this.renderScrollableMenu()}
						theme={periodsSelectboxCss}>
						{periods.map((period, index) => (
							<MenuItem
								key={index}
								value={period.seconds}
								isActive={eqNumber.equals(selectedPeriod.seconds, period.seconds)}>
								{period.name}
							</MenuItem>
						))}
					</Selectbox>
					<Selectbox
						isOpened={isTypeSelectboxOpened}
						onToggle={this.handleTypeToggle}
						value={typeToString(selectedType)}
						onValueChange={this.handleTypeSelect}
						Anchor={TypeAnchor}
						theme={typeSelectboxCss}>
						{Object.values(ChartType).map(type => (
							<MenuItem
								key={type}
								value={typeToString(type)}
								isActive={eqString.equals(typeToString(selectedType), typeToString(type))}>
								<div className={css.menuTypeIcon}>{this.renderTypeIcon(type)}</div>
								{this.localizeChartType(type)}
							</MenuItem>
						))}
					</Selectbox>
					<Selectbox
						isOpened={isCrossToolModeSelectboxOpened}
						onToggle={this.handleCrossToolModeToggle}
						value={modeToString(selectedCrossToolMode)}
						onValueChange={this.handleCrossToolModeSelect}
						Anchor={CrossToolAnchor}
						theme={crossToolSelectboxCss}>
						{Object.values(CrossToolMode).map(mode => (
							<MenuItem
								key={mode}
								value={modeToString(mode)}
								isActive={eqString.equals(modeToString(selectedCrossToolMode), modeToString(mode))}>
								<div className={css.menuTypeIcon}>{this.renderCrossToolIcon(mode)}</div>
								{this.localizeCrossMode(mode)}
							</MenuItem>
						))}
					</Selectbox>
				</div>
				<div className={css.title}>
					{pipe(
						title,
						O.getOrElse(constant('')),
					)}
				</div>
			</div>
		);
	}

	private renderScrollableMenu = () => (props: TMenuProps) => (
		<Scrollable>
			<Menu {...props} />
		</Scrollable>
	);

	private handlePeriodsToggle = (isOpened: boolean | undefined) => {
		if (isOpened !== undefined) {
			this.setState({ isPeriodsSelectboxOpened: isOpened });
		}
	};

	private handlePeriodSelect = (seconds: number | undefined | string) => {
		const selectedPeriod = pipe(
			O.fromNullable(seconds),
			O.chain(seconds => findFirst(this.props.periods, item => item.seconds === seconds)),
		);
		O.isSome(selectedPeriod) && this.props.onPeriodSelect(selectedPeriod.value);
	};

	private renderTypeIcon = (type: ChartType): JSX.Element => {
		switch (type) {
			case ChartType.Bar:
				return <ChartBarIcon />;
			case ChartType.Candle:
				return <ChartCandlesIcon />;
			case ChartType.Line:
				return <ChartLineIcon />;
		}
	};

	private renderCrossToolIcon = (mode: CrossToolMode): JSX.Element => {
		switch (mode) {
			case CrossToolMode.Disabled:
				return <NoCrosshairIcon />;
			case CrossToolMode.Crosshair:
				return <AimIcon />;
		}
	};

	private handleTypeToggle = (isOpened: boolean | undefined) => {
		if (isOpened !== undefined) {
			this.setState({ isTypeSelectboxOpened: isOpened });
		}
	};

	private handleTypeSelect = (type: string | number | undefined) => {
		if (type !== undefined) {
			this.props.onTypeSelect(toType(type.toString()));
		}
	};

	private handleCrossToolModeToggle = (isOpened: boolean | undefined) => {
		if (isOpened !== undefined) {
			this.setState({ isCrossToolModeSelectboxOpened: isOpened });
		}
	};

	private handleCrossToolModeSelect = (type: string | number | undefined) => {
		if (type !== undefined) {
			this.props.onCrossToolModeSelect(toMode(type.toString()));
		}
	};

	private localizeChartType = (type: ChartType) => {
		switch (type) {
			case ChartType.Bar:
				return tspan('chartToolbarTypeBar', 'Bar');
			case ChartType.Candle:
				return tspan('chartToolbarTypeCandle', 'Candle');
			case ChartType.Line:
				return tspan('chartToolbarTypeLine', 'Line');
		}
	};

	private localizeCrossMode = (mode: CrossToolMode) => {
		switch (mode) {
			case CrossToolMode.Disabled:
				return tspan('chartToolbarCrossModeDisabled', 'No crosshair');
			case CrossToolMode.Crosshair:
				return tspan('chartToolbarCrossModeCrosshair', 'Crosshair');
		}
	};
}

export const Toolbar = withTranslation()(ToolbarRaw);
