import { Option } from 'fp-ts/lib/Option';
import { number, string, type } from 'io-ts';
import { createOptionFromNullable } from 'io-ts-types';

export type UserTO = {
	id: Option<number>;
	login: Option<string>;
	fullName: Option<string>;
	email: Option<string>;
	status: Option<string>;
	accountId: Option<string>;
	delimiterUserNameDomain: Option<string>;
	domain: Option<string>;
};
export const UserTOIO = type({
	id: createOptionFromNullable(number),
	login: createOptionFromNullable(string),
	fullName: createOptionFromNullable(string),
	email: createOptionFromNullable(string),
	status: createOptionFromNullable(string),
	accountId: createOptionFromNullable(string),
	delimiterUserNameDomain: createOptionFromNullable(string),
	domain: createOptionFromNullable(string),
});
