type PluginInitializer = (...args: any) => void;
type Dependencies =
	| 'eventBus'
	| 'clearer'
	| 'options'
	| 'elements'
	| 'elements.mainCanvas'
	| 'chartBootstrap'
	| 'events';
export type PluginDependencyResolver = (dependencies: Dependencies[], initializer: PluginInitializer) => void;

export abstract class Plugin {
	abstract register(resolver: PluginDependencyResolver): void;
}
