import { href, Router } from '../../../utils/router.utils';
import { Component } from 'react';
import { Match } from 'fp-ts-routing';
import { asks } from '@devexperts/rx-utils/dist/context.utils';

export type RedirectContainerContext = {
	router: Router;
};

export type RedirectContainerProps<A extends object> = {
	to: Match<A>;
	value: A;
};

export const Redirect = asks(
	(e: RedirectContainerContext) =>
		class Redirect<A extends object> extends Component<RedirectContainerProps<A>> {
			componentDidMount() {
				e.router.replace(href(this.props.to, this.props.value));
			}

			render() {
				return null;
			}
		},
);
