import * as React from 'react';
import { PureComponent, Fragment } from 'react';
import { withTranslation, WithTranslation } from 'react-i18next';
import { formatNumber } from 'accounting';
import * as cn from 'classnames';
import { Option } from 'fp-ts/lib/Option';
import * as O from 'fp-ts/lib/Option';
import { RemoteData } from '@devexperts/remote-data-ts';
import { combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { routes } from '../../../../ui-common/routes';
import { SidePanel } from '../../../../ui-common/side-panel/side-panel.component';
import { Optional } from '../../../../ui-common/optional/optional.component';
import { RouteLink } from '../../../../ui-common/route-link/containers/route-link.container';
import { RemoteDataState } from '../../../../ui-common/remote-data-state/remote-data-state.component';
import { NumericStepper } from '../../../../ui-common/numeric-stepper-ava/numeric-stepper.component';
import { Button } from '../../../../ui-common/button/button.component';
import { CloseButton } from '../../../../ui-common/close-button/close-button.component';
import { SymbolComponent } from '../../../../ui-common/symbol/symbol';
import { PositionCloseValue, PositionCloseInstrumentData } from './position-close.model';
import { tspan } from '../../../../utils/i18n.utils';
import { pipe } from 'fp-ts/lib/pipeable';
import { constFalse, constant } from 'fp-ts/lib/function';
import { ErrorPanel } from '../../../../ui-common/error-panel/components/error-panel.component';

import * as buttonCss from './theme/button.child.module.css';
import * as symbolCss from './theme/symbol.child.module.css';
import * as stepperCss from './theme/numeric-stepper.child.module.css';
import * as stepperButtonCss from './theme/numeric-stepper.button.module.css';
import * as css from './theme/position-close.module.css';

const steppableTheme = {
	Input: stepperCss,
	SteppableInput: {
		ButtonIcon: stepperButtonCss,
	},
};

export type PositionCloseProps = {
	uid: string;
	value: RemoteData<Error, PositionCloseValue>;
	accountCurrency: string;
	instrumentData: RemoteData<Error, PositionCloseInstrumentData>;
	profitLossValue: RemoteData<Error, Option<number>>;
	isClosingPositionAllowed: boolean;
	onAmountChange: (amount: number) => void;
	onPositionClose: () => void;
	error: Option<string>;
};

export const PositionClose = combineContext(RouteLink, RouteLink =>
	withTranslation()(
		class PositionClose extends PureComponent<PositionCloseProps & WithTranslation> {
			render() {
				const { profitLossValue, value, instrumentData } = this.props;

				return (
					<SidePanel header={this.renderHeader()} footer={this.renderFooter()} scrollable>
						<RemoteDataState data={instrumentData} success={this.renderSuccessfulInstrumentData} />
						<div className={css.profit}>
							<h2 className={css.profitLabel}>{tspan('positionCloseProfit', 'Profit')}</h2>
							<RemoteDataState data={profitLossValue} success={this.renderSuccessfulProfitLoss} />
						</div>
						<RemoteDataState data={value} success={this.renderControls} />
					</SidePanel>
				);
			}

			private renderHeader = () => (
				<div className={css.header}>
					<h1 className={css.headerLabel}>{tspan('positionCloseClosingPosition', 'Closing Position')}</h1>
					<RouteLink
						to={routes.positions.position.root}
						value={{ positionId: this.props.uid }}
						render={({ onNavigate }) => <CloseButton onClick={onNavigate} />}
					/>
				</div>
			);

			private renderFooter = () => {
				const { isClosingPositionAllowed, error } = this.props;

				return (
					<Fragment>
						<ErrorPanel
							error={error}
							title={tspan('positionCloseErrorClosePosition', 'Error Closing Position')}
						/>
						<Button
							isDisabled={!isClosingPositionAllowed}
							isPrimary={true}
							theme={buttonCss}
							onClick={this.handlePositionClose}>
							{tspan('positionCloseClosePosition', 'Close Position')}
						</Button>
					</Fragment>
				);
			};

			private renderSuccessfulInstrumentData = (instrumentData: PositionCloseInstrumentData) => {
				const { symbol, symbolDescription } = instrumentData;

				return (
					<SymbolComponent
						title={symbol}
						subtitle={pipe(
							symbolDescription,
							O.getOrElse(constant('—')),
						)}
						theme={symbolCss}
					/>
				);
			};

			private renderSuccessfulProfitLoss = (profit: Option<number>) => {
				const profitValueClassName = cn(css.profitValue, {
					[css.profitValue_positive]: pipe(
						profit,
						O.fold(constFalse, profit => profit > 0),
					),
					[css.profitValue_negative]: pipe(
						profit,
						O.fold(constFalse, profit => profit < 0),
					),
				});

				return (
					<span className={profitValueClassName}>
						<Optional
							value={pipe(
								profit,
								O.map(profit => `${formatNumber(profit, 2)} ${this.props.accountCurrency}`),
							)}
						/>
					</span>
				);
			};

			private renderControls = (value: PositionCloseValue) => {
				const { initAmount, amount, lot, step, precision, lotsPrecision } = value;

				return (
					<div className={css.columns}>
						<div className={css.amount}>
							<div className={css.amountHeader}>
								<h2 className={css.amountLabel}>{tspan('positionCloseAmount', 'Amount')}</h2>
								<div className={css.amountQuantity}>
									<span>{`${value.amount} of ${value.initAmount}`}</span>
								</div>
							</div>
							<NumericStepper
								value={amount}
								min={lot}
								step={step}
								max={initAmount}
								formatter={(inputValue: number) => formatNumber(inputValue, precision)}
								onIncrement={this.handleValueChange}
								onDecrement={this.handleValueChange}
								onValueChange={this.handleValueChange}
								theme={steppableTheme}
							/>
						</div>
						<div className={css.lot}>
							<h2 className={css.lotLabel}>{tspan('positionCloseLot', 'Lot')}</h2>
							<div className={css.lotValue}>{formatNumber(lot, lotsPrecision)}</div>
						</div>
					</div>
				);
			};

			private handleValueChange = (amount: number) => {
				this.props.onAmountChange(amount);
			};

			private handlePositionClose = () => {
				this.props.onPositionClose();
			};
		},
	),
);
