import * as React from 'react';
import { Component } from 'react';
import { withTheme } from '@devexperts/react-kit/dist/utils/withTheme';
import { PartialKeys } from '@devexperts/utils/dist/object';
import { putDot } from './pip-rate.model';
import { formatNumber } from 'accounting';
import * as css from './theme/pip-rate.module.css';

export type TPipSettings = {
	start: number;
	length: number;
};

export type TRawPipRateProps = {
	theme: {
		container?: string;
		pip?: string;
	};
	value: number;
	rise: TPipSettings;
	digits: number;
};

class RawPipRate extends Component<TRawPipRateProps> {
	render() {
		const { value, rise, theme, digits } = this.props;

		const stringValue = formatNumber(value, digits);
		const dotIndex = stringValue.indexOf('.');
		const commaIndex = stringValue.indexOf(',');
		const start = rise.start >= commaIndex && commaIndex > -1 ? rise.start + 1 : rise.start;
		const length = rise.start + 1 === commaIndex && commaIndex > -1 ? rise.length + 1 : rise.length;

		if (dotIndex === -1) {
			return (
				<span className={theme.container}>
					{stringValue.slice(0, start)}
					<span className={theme.pip}>{stringValue.slice(start, start + length)}</span>
					{stringValue.slice(start + length, stringValue.length)}
				</span>
			);
		}

		const valueNoDot = stringValue.slice(0, dotIndex) + stringValue.slice(dotIndex + 1, stringValue.length);

		return (
			<span className={theme.container}>
				{putDot(dotIndex, 0, valueNoDot.slice(0, start))}
				<span className={theme.pip}>{putDot(dotIndex, start, valueNoDot.slice(start, start + length))}</span>
				{putDot(dotIndex, start + length, valueNoDot.slice(start + length, valueNoDot.length))}
			</span>
		);
	}
}

const wTheme = {
	...css,
};

export type TPipRateProps = PartialKeys<TRawPipRateProps, 'theme'>;
export const PipRate = withTheme(Symbol(), wTheme)(RawPipRate);
