import * as React from 'react';
import { PureComponent } from 'react';
import * as cn from 'classnames';
import { TableCell } from './table-cell.component';
import { Column } from './table.model';
import * as css from './theme/table.module.css';

type TableHeaderProps = {
	columns: Array<Column>;
	className?: string;
};

export class TableHeader extends PureComponent<TableHeaderProps> {
	private rendeHeaderCell = ({ align, label, width, key }: Column) => (
		<div key={key} className={css.headCell} style={{ flexBasis: width }}>
			<TableCell align={align}>{label}</TableCell>
		</div>
	);

	private renderHeaderRow = () => {
		const { columns } = this.props;

		return columns.map(this.rendeHeaderCell);
	};

	render() {
		return <div className={cn(css.row, css.head, this.props.className)}>{this.renderHeaderRow()}</div>;
	}
}
