import { combineContext, ask, deferContext } from '@devexperts/rx-utils/dist/context.utils';
import { withRXWithDefaults } from '../../../../utils/withRXWithDefaults';
import { OrderDetails } from '../../components/order-details/order-details.component';
import { pending } from '@devexperts/remote-data-ts';
import { runOnMount } from '../../../../utils/react.utils';
import {
	OrderDetailsViewModel,
	createOrderDetailsViewModel,
} from '../../../../view-model/orders/order-details.view-model';
import { withProps } from '../../../../utils/with-props.utils';
import { mapRD } from '@devexperts/rx-utils/dist/rd/operators/mapRD';
import { definePipSettings } from '../../../../ui-common/pip-rate/pip-settings.utils';
import { getOrdersSide } from '../../orders.model';
import { pipe } from 'fp-ts/lib/pipeable';
import * as O from 'fp-ts/lib/Option';

type OrderDetailsContainerContext = {
	orderInfoViewModel: OrderDetailsViewModel;
};

const Container = combineContext(OrderDetails, ask<OrderDetailsContainerContext>(), (OrderDetails, context) =>
	withRXWithDefaults(OrderDetails)(() => {
		const {
			orderInfoViewModel: { orderDetails$ },
		} = context;

		return {
			props: {
				data: orderDetails$.pipe(
					mapRD(orderDetails => ({
						...orderDetails,
						pipSettings: pipe(
							orderDetails.amount,
							O.map(amount => definePipSettings(amount, orderDetails.pipSize)),
						),
						side: getOrdersSide(orderDetails.side),
					})),
				),
			},
			defaultProps: { data: pending },
		};
	}),
);

const withOrderId = withProps<{ orderId: number }>();

export const OrderInfoContainer = combineContext(
	deferContext(Container.map(withOrderId), 'orderInfoViewModel'),
	createOrderDetailsViewModel,
	(Container, createOrderInfoViewModel) =>
		runOnMount(Container, () => props => ({ orderInfoViewModel: createOrderInfoViewModel(props.orderId) })),
);
