import { PriceIncrementsTO, PriceIncrementsTOIO } from '../definitions/PriceIncrementsTO';
import { Option } from 'fp-ts/lib/Option';
import { number, string, union, literal, boolean, type, recursion } from 'io-ts';
import { createOptionFromNullable, DateFromISOString } from 'io-ts-types';

export type InstrumentTO = {
	id: number;
	symbol: string;
	description: Option<string>;
	type:
		| 'CURRENCY'
		| 'STOCK'
		| 'FOREX'
		| 'FUTURES'
		| 'PRODUCT'
		| 'INDEX'
		| 'CFD'
		| 'OPTION'
		| 'SPREAD_BET'
		| 'CFD_FUTURES'
		| 'SB_FUTURES'
		| 'FX_OPTION'
		| 'FX_FORWARD'
		| 'CFD_OPTION'
		| 'SB_OPTION'
		| 'CFD_STOCK'
		| 'SB_STOCK'
		| 'CFD_FOREX'
		| 'SB_FOREX'
		| 'SPREAD'
		| 'WARRANT'
		| 'ETF';
	currency: Option<string>;
	currencyPrecision: number;
	precision: number;
	pipsSize: number;
	quantityIncrement: number;
	quantityPrecision: Option<number>;
	priceIncrement: number;
	version: number;
	priceIncrementsTO: PriceIncrementsTO;
	lotSize: number;
	baseCurrency: Option<string>;
	lotName: Option<string>;
	multiplier: number;
	open: boolean;
	expiration: Option<Date>;
	firstNoticeDate: Option<Date>;
	initialMargin: Option<number>;
	maintenanceMargin: Option<number>;
	lastTradeDate: Option<Date>;
	underlying: Option<InstrumentTO>;
	flex: Option<boolean>;
	mmy: Option<string>;
	unitName: Option<string>;
};
export const InstrumentTOIO = recursion<InstrumentTO, unknown>('InstrumentTOIO', InstrumentTOIO =>
	type({
		id: number,
		symbol: string,
		description: createOptionFromNullable(string),
		type: union([
			literal('CURRENCY'),
			literal('STOCK'),
			literal('FOREX'),
			literal('FUTURES'),
			literal('PRODUCT'),
			literal('INDEX'),
			literal('CFD'),
			literal('OPTION'),
			literal('SPREAD_BET'),
			literal('CFD_FUTURES'),
			literal('SB_FUTURES'),
			literal('FX_OPTION'),
			literal('FX_FORWARD'),
			literal('CFD_OPTION'),
			literal('SB_OPTION'),
			literal('CFD_STOCK'),
			literal('SB_STOCK'),
			literal('CFD_FOREX'),
			literal('SB_FOREX'),
			literal('SPREAD'),
			literal('WARRANT'),
			literal('ETF'),
		]),
		currency: createOptionFromNullable(string),
		currencyPrecision: number,
		precision: number,
		pipsSize: number,
		quantityIncrement: number,
		quantityPrecision: createOptionFromNullable(number),
		priceIncrement: number,
		version: number,
		priceIncrementsTO: PriceIncrementsTOIO,
		lotSize: number,
		baseCurrency: createOptionFromNullable(string),
		lotName: createOptionFromNullable(string),
		multiplier: number,
		open: boolean,
		expiration: createOptionFromNullable(DateFromISOString),
		firstNoticeDate: createOptionFromNullable(DateFromISOString),
		initialMargin: createOptionFromNullable(number),
		maintenanceMargin: createOptionFromNullable(number),
		lastTradeDate: createOptionFromNullable(DateFromISOString),
		underlying: createOptionFromNullable(InstrumentTOIO),
		flex: createOptionFromNullable(boolean),
		mmy: createOptionFromNullable(string),
		unitName: createOptionFromNullable(string),
	}),
);
