import { RouteComponent } from '../../../ui-desktop/ui-kit/components/route/route.component';
import { createElement, PureComponent, ReactNode } from 'react';
import { Match } from 'fp-ts-routing';
import { Router } from '../../../utils/router.utils';
import { distinctUntilChanged, map } from 'rxjs/operators';
import { ask } from '@devexperts/rx-utils/dist/context.utils';
import { withRXWithDefaults } from '../../../utils/withRXWithDefaults';

export type RouteContainerContext = {
	router: Router;
};

export type RouteProps<A extends object> = {
	match: Match<A>;
	render: (a: A) => ReactNode;
	exact?: true;
	children?: never;
};

export const Route = ask<RouteContainerContext>().map(e => {
	const Intermediate = withRXWithDefaults(RouteComponent)(props => ({
		props: {
			path: e.router.state$.pipe(
				map(state => state.location.pathname),
				distinctUntilChanged(),
			),
		},
		defaultProps: {
			path: e.router.state.location.pathname,
		},
	}));

	// dedicated class for type inference of type argument A
	return class RouteContainer<A extends object> extends PureComponent<RouteProps<A>> {
		static displayName = `RouteContainer(${Intermediate.displayName})`;

		render() {
			return createElement(Intermediate, this.props as any);
		}
	};
});
