import * as React from 'react';
import { PureComponent, ReactNode } from 'react';
import { withTranslation, WithTranslation } from 'react-i18next';
import cn from 'classnames';
import { Match } from 'fp-ts-routing';
import { combineContext, ask } from '@devexperts/rx-utils/dist/context.utils';
import { routes } from '../../../../ui-common/routes';
import { RouteLink } from '../../../../ui-common/route-link/containers/route-link.container';
import { Link } from '../../../../ui-common/link/link.component';
import { TradeIcon } from '../../../ui-kit/components/icons/trade.icon';
import { PositionsIcon } from '../../../ui-kit/components/icons/positions.icon';
import { OrdersIcon } from '../../../ui-kit/components/icons/orders.icon';
import { HistoryIcon } from '../../../ui-kit/components/icons/history.icon';
import { DepositIcon } from '../../../ui-kit/components/icons/deposit.icon';
import { ChatIcon } from '../../../ui-kit/components/icons/chat.icon';
import { BurgerIcon } from '../../../ui-kit/components/icons/burger.icon';
import { BurgerCloseIcon } from '../../../ui-kit/components/icons/burger-close.icon';
import { Option } from 'fp-ts/lib/Option';
import * as O from 'fp-ts/lib/Option';
import { empty } from 'fp-ts/lib/Record';
import { ChatModel } from '../../../chat/chat.model';
import { tspan } from '../../../../utils/i18n.utils';
import { pipe } from 'fp-ts/lib/pipeable';
import { constant } from 'fp-ts/lib/function';
import * as css from './theme/aside.module.css';

type AsideProps = {
	orderCount: number;
	positionCount: number;
	menuIsOpened: boolean;
	isRealAccount: Option<boolean>;
	menuHandler: () => void;
	menuCloser: () => void;
};

type AsideComponentContext = {
	chat: ChatModel;
};

type DyData = {
	'data-lng': string;
	'data-type'?: 'real' | 'demo';
};

const activeLinkTheme = {
	container: cn(css.link, css.link_active),
};
const linkTheme = {
	container: css.link,
};

export const Aside = combineContext(ask<AsideComponentContext>(), RouteLink, ({ chat }, RouteLink) =>
	withTranslation()(
		class AsideComponent extends PureComponent<AsideProps & WithTranslation> {
			render() {
				const { menuIsOpened, menuHandler, orderCount, positionCount, isRealAccount, i18n } = this.props;
				const { language } = i18n;
				const dyData = pipe(
					isRealAccount,
					O.fold<boolean, DyData>(constant({ 'data-lng': language }), isReal => ({
						'data-lng': language,
						'data-type': isReal ? 'real' : 'demo',
					})),
				);
				return (
					<aside className={css.container}>
						<ul className={css.list}>
							<li className={css.item}>
								<i className={css.menuIcon} onClick={menuHandler}>
									{menuIsOpened ? <BurgerCloseIcon /> : <BurgerIcon />}
								</i>
							</li>
							{this.renderItem(routes.trade.root, empty, tspan('asideMenuTrade', 'Trade'), <TradeIcon />)}
							{this.renderItem(
								routes.positions.root,
								empty,
								tspan('asideMenuPositions', 'Positions'),
								<PositionsIcon />,
								positionCount,
							)}
							{this.renderItem(
								routes.orders.root,
								empty,
								tspan('asideMenuOrders', 'Orders'),
								<OrdersIcon />,
								orderCount,
							)}
							{this.renderItem(
								routes.history.root,
								empty,
								tspan('asideMenuHistory', 'History'),
								<HistoryIcon />,
								0,
							)}
							{pipe(
								isRealAccount,
								O.map(isReal =>
									isReal
										? this.renderItem(
												routes.crm.deposit.root,
												empty,
												tspan('asideMenuDeposit', 'Deposit'),
												<DepositIcon />,
												0,
										  )
										: null,
								),
								O.toNullable,
							)}
							<li className={cn(css.item, css.item_break)}>
								<div id="dy-injected" {...dyData} />
							</li>
							<li className={css.item} onClick={this.toggleChat}>
								<div className={css.link}>
									<div className={css.icon}>
										<ChatIcon />
									</div>
									<h3 className={css.title}>{tspan('asideMenuLiveChat', 'LiveChat')}</h3>
								</div>
							</li>
						</ul>
					</aside>
				);
			}

			renderItem = <A extends object>(
				match: Match<A>,
				value: A,
				name: ReactNode,
				icon: ReactNode,
				label?: number,
			) => (
				<li className={css.item}>
					<RouteLink
						to={match}
						value={value}
						render={({ href, onNavigate, isActive }) => {
							return (
								<Link
									theme={isActive ? activeLinkTheme : linkTheme}
									href={href}
									onClick={this.handleClick(isActive, onNavigate)}>
									{label ? <div className={css.label}>{label}</div> : null}
									<div className={css.icon}>{icon}</div>
									<h3 className={css.title}>{name}</h3>
								</Link>
							);
						}}
					/>
				</li>
			);

			handleClick = (isActive: boolean, onNavigate: () => void) => (e: React.MouseEvent<HTMLAnchorElement>) => {
				e.preventDefault();
				!isActive && onNavigate();
				this.props.menuCloser();
				chat.close();
			};

			private toggleChat = (e: React.MouseEvent<HTMLLIElement>) => {
				e.preventDefault();

				this.props.menuCloser();
				chat.toggle();
			};
		},
	),
);
