import { withRXWithDefaults } from '../../../../utils/withRXWithDefaults';
import { ask, combineContext, deferContext } from '@devexperts/rx-utils/dist/context.utils';
import { empty } from 'fp-ts/lib/Array';
import { runOnMount } from '../../../../utils/react.utils';
import { InstrumentStore } from '../../../../store/instrument/instrument.store';
import { PositionLargeTableConfig } from '../../components/position-table/position-table.utils';
import { PositionTable } from '../../components/position-table/position-table.component';
import { createPositionViewModel, PositionViewModel } from '../../../../view-model/positions/positions.view-model';
import { AccountViewModel } from '../../../../view-model/account/account.view-model';
import { first, map } from 'rxjs/operators';
import { currencyStringToSymbol } from '../../../../utils/currency.utils';
import { TableSize } from '../../components/position-table/position-table.model';

export type PositionContainerContext = {
	positionViewModel: PositionViewModel;
	instrumentStore: InstrumentStore;
	accountViewModel: AccountViewModel;
};

const Container = combineContext(PositionTable, ask<PositionContainerContext>(), (PositionTable, e) =>
	withRXWithDefaults(PositionTable)(() => {
		const {
			positionViewModel: { positionsUid$, tableSize$, tableSizeConfig$ },
			accountViewModel: { current$ },
		} = e;

		return {
			props: {
				positionsUid: positionsUid$,
				accountCurrency: current$.pipe(
					first(),
					map(value => currencyStringToSymbol(value.baseCurrency)),
				),
				config: tableSizeConfig$,
				tableSize: tableSize$,
			},
			defaultProps: {
				positionsUid: empty,
				accountCurrency: '',
				config: PositionLargeTableConfig,
				tableSize: TableSize.Large,
			},
		};
	}),
);

export const PositionContainer = combineContext(
	deferContext(Container, 'positionViewModel'),
	createPositionViewModel,
	(Container, createPositionViewModel) =>
		runOnMount(Container, () => () => ({
			positionViewModel: createPositionViewModel(),
		})),
);
