import { unknownType } from '../utils/utils';
import { Option } from 'fp-ts/lib/Option';
import { number, type } from 'io-ts';
import { createOptionFromNullable } from 'io-ts-types';

export type StudyParamValidationTO = { min: Option<unknown>; max: Option<unknown>; precision: Option<number> };
export const StudyParamValidationTOIO = type({
	min: createOptionFromNullable(unknownType),
	max: createOptionFromNullable(unknownType),
	precision: createOptionFromNullable(number),
});
