import { merge, Observable } from 'rxjs';
import { getTime, compareDesc, compareAsc } from 'date-fns';
import { filter, map, switchMap, share } from 'rxjs/operators';
import { some } from 'fp-ts/lib/Option';
import { findFirst, filter as filterArray, takeEnd } from 'fp-ts/lib/Array';
import { ordersController } from '../../api/generated/swagger/controllers/OrdersController';
import { SocketClient } from '../../api/socket-client';
import { EntityStore } from '@devexperts/rx-utils/dist/rd/entity-store.utils';
import { Sink } from '@devexperts/rx-utils/dist/sink.utils';
import { ask, combineContext, fromReader } from '@devexperts/rx-utils/dist/context.utils';
import { isHistoryOrderTO } from '../../api/socket/socket-message.to';
import { LiveData } from '@devexperts/rx-utils/dist/rd/live-data.utils';
import { createHandler } from '@devexperts/rx-utils/dist/create-handler.utils';
import { success, fromOption } from '@devexperts/remote-data-ts';
import { HistoryOrderTO } from '../../api/generated/swagger/definitions/HistoryOrderTO';
import { mapRD } from '@devexperts/rx-utils/dist/rd/operators/mapRD';
import { combineLatestRD } from '@devexperts/rx-utils/dist/rd/operators/combineLatestRD';
import { switchMapRD } from '@devexperts/rx-utils/dist/rd/operators/switchMapRD';
import { shareReplayWithDefaults } from '../../utils/rxjs/shareReplayWithDefaults';

const ORDERS_LIMIT = 100;

export type HistoryOrderStore = Readonly<{
	requestedOrders$: LiveData<Error, HistoryOrderTO[]>;
	allOrders$: LiveData<Error, HistoryOrderTO[]>;
	getOrderHistoryById: (orderId: number) => LiveData<Error, HistoryOrderTO>;
	onDateChange: (from: number, to: number) => void;
}>;

type HistoryContext = {
	socketClient: SocketClient;
};

type CreateHistoryOrderStore = Sink<() => HistoryOrderStore>;

type RequestData = {
	from: number;
	to: number;
};

export const createHistoryOrderStore = combineContext(
	fromReader(ordersController),
	ask<HistoryContext>(),
	(ordersController, ctx): CreateHistoryOrderStore => {
		const { socketClient } = ctx;

		const pk = (order: HistoryOrderTO) => String(order.orderId);

		const store = new EntityStore<Error, HistoryOrderTO>();

		const orders$: Observable<HistoryOrderTO[]> = socketClient.message$.pipe(
			filter(isHistoryOrderTO),
			map(data => data.body),
		);

		const allOrders$: LiveData<Error, HistoryOrderTO[]> = store.getAll(pk, () =>
			orders$.pipe(map(to => success(to.sort((a, b) => compareAsc(a.closeTime, b.closeTime))))),
		);

		const lastOrders$ = allOrders$.pipe(
			mapRD(orders => takeEnd(ORDERS_LIMIT, orders).sort((a, b) => compareDesc(a.closeTime, b.closeTime))),
		);

		const getOrderHistoryById = (orderId: number): LiveData<Error, HistoryOrderTO> =>
			store.get(String(orderId), () =>
				lastOrders$.pipe(
					map(orders =>
						orders.chain(orders =>
							fromOption(
								findFirst(orders, order => order.orderId === orderId),
								() => new Error(`History orders with id ${orderId} not found in history-order.store`),
							),
						),
					),
				),
			);

		const sendOrdersHistoryRequest = createHandler<RequestData>();

		const sharedHistoryRequestData$ = sendOrdersHistoryRequest.value$.pipe(shareReplayWithDefaults());

		const sendEffect$ = sharedHistoryRequestData$.pipe(
			switchMap(requestData =>
				ordersController.requestOrderHistory({
					query: { from: some(requestData.from), to: some(requestData.to) },
				}),
			),
			share(),
		);

		const allOrdersWithinGivenPeriod$: LiveData<Error, HistoryOrderTO[]> = combineLatestRD(
			lastOrders$,
			sharedHistoryRequestData$.pipe(map(data => success(data))),
		).pipe(mapRD(([orders, requestData]) => filterArray(orders, isOrderWithinRangePredicate(requestData))));

		const requestedOrders$: LiveData<Error, HistoryOrderTO[]> = sendEffect$.pipe(
			switchMapRD(sendEffect => allOrdersWithinGivenPeriod$),
			share(),
		);

		const effect$ = merge(orders$, requestedOrders$);

		return new Sink(
			(): HistoryOrderStore => ({
				requestedOrders$,
				allOrders$,
				getOrderHistoryById,
				onDateChange: (from: number, to: number) => sendOrdersHistoryRequest.handle({ from, to }),
			}),
			effect$,
		);
	},
);

function isOrderWithinRangePredicate(requestData: RequestData) {
	return (order: HistoryOrderTO) =>
		requestData.from <= getTime(order.closeTime) && requestData.to >= getTime(order.closeTime);
}
