import * as React from 'react';
import { Fragment, ReactNode, memo, useCallback } from 'react';
import { Option } from 'fp-ts/lib/Option';
import { formatMoney } from 'accounting';
import { format } from 'date-fns';
import { RemoteData } from '@devexperts/remote-data-ts';
import { HeaderData, PositionSide } from './position-details.model';
import { withTranslation, WithTranslation } from 'react-i18next';
import { RemoteDataState } from '../remote-data-state/remote-data-state.component';
import { SymbolComponent } from '../symbol/symbol';
import { TRANSLATION_PLACEHOLDER, tspan } from '../../utils/i18n.utils';
import { SymbolInfoItem } from '../symbol-info-item/symbol-info-item.component';
import { Optional } from '../optional/optional.component';
import { definePipSettings } from '../pip-rate/pip-settings.utils';
import { PipRate } from '../pip-rate/pip-rate.component';
import { PriceDirection } from '../price-direction/price-direction.component';
import { empty } from 'fp-ts/lib/Array';
import { MakeTheme } from '../../utils/theme.utils';
import * as O from 'fp-ts/lib/Option';
import { pipe } from 'fp-ts/lib/pipeable';
import { constant } from 'fp-ts/lib/function';
import * as css from './theme/position-details.module.css';
import * as infoItemCss from './theme/symbol-info-item.child.module.css';
import * as directionPriceCss from './theme/price-direction.child.module.css';
import * as symbolCss from './theme/symbol.child.module.css';

export type PositionDetailsProps = {
	uid: string;
	pipsSize: number;
	accountCurrency: string;
	precision: number;
	headerData: RemoteData<Error, HeaderData>;
	side: RemoteData<Error, PositionSide>;
	openPrice: RemoteData<Error, number>;
	currentPriceAndDirection: RemoteData<Error, Option<number>[]>;
	quantity: RemoteData<Error, number>;
	sizeInLots: RemoteData<Error, number>;
	currentValue: RemoteData<Error, Option<number>>;
	openDateTime: RemoteData<Error, Date>;
	takeProfitValue: RemoteData<Error, Option<number>>;
	stopLossValue: RemoteData<Error, Option<number>>;
	swap: RemoteData<Error, Option<number>>;
	commission: RemoteData<Error, Option<number>>;
	theme?: MakeTheme<'item' | 'item_secondary' | 'label' | 'value' | 'container' | 'title' | 'subtitle'>;
} & WithTranslation;

export const PositionDetailsCommomn = withTranslation()(
	memo((props: PositionDetailsProps) => {
		const {
			headerData,
			pipsSize,
			precision,
			side,
			openPrice,
			currentPriceAndDirection,
			quantity,
			currentValue,
			sizeInLots,
			openDateTime,
			takeProfitValue,
			stopLossValue,
			swap,
			commission,
			accountCurrency,
			t,
			theme,
		} = props;

		const renderSide = useCallback(
			(side: PositionSide) => {
				switch (side) {
					case PositionSide.Buy:
						return <span className={css.positiveSide}>{tspan('positionDetailsBuy', 'Buy')}</span>;
					case PositionSide.Sell:
						return <span className={css.negativeSide}>{tspan('positionDetailsSell', 'Sell')}</span>;
				}
			},
			[t],
		);

		const renderPipsValue = useCallback(
			(value: number, pipsSize: number, precision: number) => {
				const pipSettings = definePipSettings(value, pipsSize);

				return <PipRate value={value} rise={pipSettings} digits={precision} />;
			},
			[definePipSettings],
		);

		const renderOptionalPipsValue = useCallback(
			(maybeValue: Option<number>, pipsSize: number, precision: number) =>
				pipe(
					maybeValue,
					O.map(value => renderPipsValue(value, pipsSize, precision)),
				),
			[renderPipsValue],
		);

		const renderOptionalPipsDirectionValue = useCallback(
			(maybeValue: Option<number>, maybeDirection: Option<number>, pipsSize: number, precision: number) => {
				if (O.isSome(maybeValue)) {
					const piprateNode = renderPipsValue(maybeValue.value, pipsSize, precision);
					const directionNode = pipe(
						maybeDirection,
						O.map(direction => <PriceDirection direction={direction} theme={directionPriceCss} />),
						O.toNullable,
					);

					const nodeToRender = (
						<Fragment>
							{directionNode}
							{piprateNode}
						</Fragment>
					);

					return O.some(nodeToRender);
				}

				return O.none;
			},
			[renderPipsValue],
		);

		const renderDateString = useCallback((value: Date) => {
			return (
				<Fragment>
					{format(value, 'MM.dd.yyyy HH:mm')}
					<span className={css.seconds}>{format(value, ':ss')}</span>
				</Fragment>
			);
		}, empty);

		return (
			<Fragment>
				<RemoteDataState
					data={headerData}
					success={({ symbol, positionCode }) => (
						<SymbolComponent
							theme={theme || symbolCss}
							title={pipe(
								positionCode,
								O.fold<string, ReactNode>(constant('—'), positionId =>
									tspan('positionDetailsId', 'ID', `${TRANSLATION_PLACEHOLDER} ${positionId}`),
								),
							)}
							subtitle={symbol}
						/>
					)}
				/>
				<SymbolInfoItem theme={theme || infoItemCss} label={tspan('positionDetailsSide', 'Side')}>
					<RemoteDataState data={side} success={renderSide} />
				</SymbolInfoItem>
				<SymbolInfoItem theme={theme || infoItemCss} label={tspan('positionDetailsOpenPrice', 'Open price')}>
					<RemoteDataState
						data={openPrice}
						success={openPrice => renderPipsValue(openPrice, pipsSize, precision)}
					/>
				</SymbolInfoItem>
				<SymbolInfoItem
					theme={theme || infoItemCss}
					label={tspan('positionDetailsCurrentPrice', 'Current price')}>
					<RemoteDataState
						data={currentPriceAndDirection}
						success={([currentPrice, direction]) => (
							<Optional
								value={renderOptionalPipsDirectionValue(currentPrice, direction, pipsSize, precision)}
							/>
						)}
					/>
				</SymbolInfoItem>
				<SymbolInfoItem theme={theme || infoItemCss} label={tspan('positionDetailsQuantity', 'Quantity')}>
					<RemoteDataState data={quantity} success={quantity => <Fragment>{quantity}</Fragment>} />
				</SymbolInfoItem>
				<SymbolInfoItem theme={theme || infoItemCss} label={tspan('positionDetailsSizeInLots', 'Size in lots')}>
					<RemoteDataState data={sizeInLots} success={sizeInLots => <Fragment>{sizeInLots}</Fragment>} />
				</SymbolInfoItem>
				<SymbolInfoItem
					theme={theme || infoItemCss}
					label={tspan(
						'positionDetailsCurrentValue',
						'Current value',
						`${TRANSLATION_PLACEHOLDER}, ${accountCurrency}`,
					)}>
					<RemoteDataState
						data={currentValue}
						success={currentValue => (
							<Optional
								value={pipe(
									currentValue,
									O.map(value => formatMoney(value, { format: '%v' })),
								)}
							/>
						)}
					/>
				</SymbolInfoItem>
				<SymbolInfoItem
					theme={theme || infoItemCss}
					label={tspan('positionDetailsOpenDateTime', 'Open date time')}>
					<RemoteDataState data={openDateTime} success={renderDateString} />
				</SymbolInfoItem>
				<SymbolInfoItem theme={theme || infoItemCss} label={tspan('positionDetailsTakeProfit', 'Take profit')}>
					<RemoteDataState
						data={takeProfitValue}
						success={takeProfitValue => (
							<Optional value={renderOptionalPipsValue(takeProfitValue, pipsSize, precision)} />
						)}
					/>
				</SymbolInfoItem>
				<SymbolInfoItem theme={theme || infoItemCss} label={tspan('positionDetailsStopLoss', 'Stop loss')}>
					<RemoteDataState
						data={stopLossValue}
						success={stopLossValue => (
							<Optional value={renderOptionalPipsValue(stopLossValue, pipsSize, precision)} />
						)}
					/>
				</SymbolInfoItem>
				<SymbolInfoItem theme={theme || infoItemCss} label={tspan('positionDetailsSwap', 'Swap')}>
					<RemoteDataState data={swap} success={swap => <Optional value={swap} />} />
				</SymbolInfoItem>
				<SymbolInfoItem theme={theme || infoItemCss} label={tspan('positionDetailsCommission', 'Commission')}>
					<RemoteDataState data={commission} success={commission => <Optional value={commission} />} />
				</SymbolInfoItem>
			</Fragment>
		);
	}),
);
