import * as React from 'react';
import { memo } from 'react';
import { WithTranslation, withTranslation } from 'react-i18next';
import * as cn from 'classnames';
import { combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { Popup } from '../../../ui-kit/components/popup/popup.component';
import { Link } from '../../../../ui-common/link/link.component';
import { routes } from '../../../../ui-common/routes';
import { RouteLink } from '../../../../ui-common/route-link/containers/route-link.container';
import { tspan } from '../../../../utils/i18n.utils';
import { ButtonCta } from '../../../../ui-common/button/button-cta.component';
import { Button } from '../../../../ui-common/button/button.component';
import { empty } from 'fp-ts/lib/Record';
import * as css from './theme/margin-popup.module.css';
import * as popupTheme from './theme/popup.child.module.css';
import * as buttonTheme from './theme/button.child.module.css';

type MarginPopupProps = {
	isPopupOpened: boolean;
	onClose: () => void;
};

const linkCN = {
	container: cn(css.text, css.link),
};

export const MarginPopup = combineContext(RouteLink, RouteLink =>
	withTranslation()(
		memo((props: MarginPopupProps & WithTranslation) => {
			const { isPopupOpened, onClose, t } = props;
			const marginCallHelpLink = t(
				'marginCallLink',
				`https://support.avatrade.com/hc/en-us/articles/360001782791-What-is-a-Margin-Call-`,
			);

			return (
				<Popup
					onClose={onClose}
					isOpened={isPopupOpened}
					isModal
					title={tspan('marginCallTitle', 'Margin Call Warning')}
					theme={popupTheme}>
					<div className={css.content}>
						<p className={css.text}>
							{tspan(
								'marginAlertText',
								`Your account balance is insufficient to hold your open positions. To avoid automatic
								position liquidation, please manually close some of your positions or fund your account.`,
							)}
						</p>
						<Link href={marginCallHelpLink} theme={linkCN}>
							{tspan('learnMoreMarginCall', 'Learn more about Margin Call')}
						</Link>
						<div className={css.buttons}>
							<RouteLink
								to={routes.trade.root}
								value={empty}
								render={() => {
									return (
										<Button onClick={onClose} isPrimary theme={buttonTheme}>
											{tspan('toTrade', 'Back to Trade')}
										</Button>
									);
								}}
							/>
							<RouteLink
								to={routes.crm.deposit.root}
								value={empty}
								render={({ onNavigate }) => {
									return (
										<ButtonCta
											theme={buttonTheme}
											onClick={() => {
												onClose();
												onNavigate();
											}}>
											{tspan('depositButton', 'Deposit')}
										</ButtonCta>
									);
								}}
							/>
						</div>
					</div>
				</Popup>
			);
		}),
	),
);
