import { Observable } from 'rxjs';
import { tap, map, startWith, distinctUntilChanged, filter } from 'rxjs/operators';
import { HistoryOrderStore } from '../../store/history-order/history-order.store';
import { createHandler } from '@devexperts/rx-utils/dist/create-handler.utils';
import { HistoryFilterData, HistoryValueHandlerData, UnwrappedFilterFormData } from '../../model/history/history.model';
import {
	mapDatesToPeriod,
	unwrapDatesFromValue,
	wrapDatesToValue,
	getDefaultHistoryValue,
	dedupeValues,
	validateDateInputs,
} from './history-filter-form.utils';
import { ask, combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { Sink } from '@devexperts/rx-utils/dist/sink.utils';
import { Either } from 'fp-ts/lib/Either';
import * as E from 'fp-ts/lib/Either';

export type HistoryFilterFormViewModel = {
	initValue: HistoryFilterData;
	isPeriodDropdownOpened$: Observable<boolean>;
	value$: Observable<HistoryFilterData>;
	validatedValue$: Observable<Either<void, UnwrappedFilterFormData>>;
	onPeriodDropdownToggle: (isOpened: boolean) => void;
	onValueChange: (value: HistoryValueHandlerData) => void;
};

type HistoryFilterFormViewModelContext = {
	historyOrderStore: HistoryOrderStore;
};

type CreateHistoryHistoryFilterFormViewModel = () => Sink<HistoryFilterFormViewModel>;

export const createHistoryFilterFormViewModel = combineContext(
	ask<HistoryFilterFormViewModelContext>(),
	(ctx: HistoryFilterFormViewModelContext): CreateHistoryHistoryFilterFormViewModel => {
		const { historyOrderStore } = ctx;

		const dropdownHandler = createHandler<boolean>();
		const filterFormHandler = createHandler<HistoryValueHandlerData>();

		const initValue = getDefaultHistoryValue();

		const initHandlerValue: HistoryValueHandlerData = {
			...initValue,
			isDateInputChanged: false,
		};

		const unwrappedValue$ = filterFormHandler.value$.pipe(
			startWith(initHandlerValue),
			map(mapDatesToPeriod),
			map(unwrapDatesFromValue),
			distinctUntilChanged(dedupeValues),
		);

		const validatedValue$ = unwrappedValue$.pipe(map(validateDateInputs));

		const validValue$ = validatedValue$.pipe(filter(E.isRight));

		const effect$ = validValue$.pipe(
			tap(({ value: [_, startTimestamp, endTimestamp] }) =>
				historyOrderStore.onDateChange(startTimestamp, endTimestamp),
			),
		);

		const value$ = unwrappedValue$.pipe(map(wrapDatesToValue));

		return () =>
			new Sink(
				{
					initValue,
					isPeriodDropdownOpened$: dropdownHandler.value$,
					value$,
					validatedValue$,
					onPeriodDropdownToggle: dropdownHandler.handle,
					onValueChange: filterFormHandler.handle,
				},
				effect$,
			);
	},
);
