import { PositionKeyTO, PositionKeyTOIO } from '../definitions/PositionKeyTO';
import { ProtectionOrderTO, ProtectionOrderTOIO } from '../definitions/ProtectionOrderTO';
import { Option } from 'fp-ts/lib/Option';
import { string, number, union, literal, boolean, dictionary, type } from 'io-ts';
import { createOptionFromNullable, DateFromISOString } from 'io-ts-types';

export type OrderTemplateTO = {
	accountId: string;
	instrumentId: number;
	quantity: number;
	orderType:
		| 'CANCEL'
		| 'MARKET'
		| 'AUTO'
		| 'LIMIT'
		| 'STOP'
		| 'TRIGGERED_STOP'
		| 'STOP_LIMIT'
		| 'TRIGGERED_LIMIT'
		| 'TRAIL_STOP'
		| 'TRAIL_STOP_LIMIT'
		| 'OCO';
	timeInForce: 'DAY' | 'GTC' | 'IOC' | 'GTD' | 'FOK';
	limitPrice: Option<number>;
	stopPrice: Option<number>;
	stopPriceOffset: Option<number>;
	ocoGroupCode: Option<string>;
	refOrderChainId: Option<string>;
	positionKey: Option<PositionKeyTO>;
	effect: 'OPENING' | 'CLOSING';
	orderSide: Option<'BUY' | 'SELL'>;
	expireAt: Option<Date>;
	alertExpression: Option<string>;
	directExchange: Option<boolean>;
	additionalParameters: Option<{ [key: string]: string }>;
	takeProfit: Option<ProtectionOrderTO>;
	stopLoss: Option<ProtectionOrderTO>;
	requestId: Option<string>;
};
export const OrderTemplateTOIO = type({
	accountId: string,
	instrumentId: number,
	quantity: number,
	orderType: union([
		literal('CANCEL'),
		literal('MARKET'),
		literal('AUTO'),
		literal('LIMIT'),
		literal('STOP'),
		literal('TRIGGERED_STOP'),
		literal('STOP_LIMIT'),
		literal('TRIGGERED_LIMIT'),
		literal('TRAIL_STOP'),
		literal('TRAIL_STOP_LIMIT'),
		literal('OCO'),
	]),
	timeInForce: union([literal('DAY'), literal('GTC'), literal('IOC'), literal('GTD'), literal('FOK')]),
	limitPrice: createOptionFromNullable(number),
	stopPrice: createOptionFromNullable(number),
	stopPriceOffset: createOptionFromNullable(number),
	ocoGroupCode: createOptionFromNullable(string),
	refOrderChainId: createOptionFromNullable(string),
	positionKey: createOptionFromNullable(PositionKeyTOIO),
	effect: union([literal('OPENING'), literal('CLOSING')]),
	orderSide: createOptionFromNullable(union([literal('BUY'), literal('SELL')])),
	expireAt: createOptionFromNullable(DateFromISOString),
	alertExpression: createOptionFromNullable(string),
	directExchange: createOptionFromNullable(boolean),
	additionalParameters: createOptionFromNullable(dictionary(string, string)),
	takeProfit: createOptionFromNullable(ProtectionOrderTOIO),
	stopLoss: createOptionFromNullable(ProtectionOrderTOIO),
	requestId: createOptionFromNullable(string),
});
