import { InstrumentSuggestTO, InstrumentSuggestTOIO } from '../definitions/InstrumentSuggestTO';
import { string, array, type, recursion } from 'io-ts';

export type InstrumentSuggestTreeNodeTO = {
	name: string;
	childNodes: Array<InstrumentSuggestTreeNodeTO>;
	suggests: Array<InstrumentSuggestTO>;
};
export const InstrumentSuggestTreeNodeTOIO = recursion<InstrumentSuggestTreeNodeTO, unknown>(
	'InstrumentSuggestTreeNodeTOIO',
	InstrumentSuggestTreeNodeTOIO =>
		type({
			name: string,
			childNodes: array(InstrumentSuggestTreeNodeTOIO),
			suggests: array(InstrumentSuggestTOIO),
		}),
);
