import { Option } from 'fp-ts/lib/Option';
import { ReactNode } from 'react';

export enum OrdersSide {
	Buy = '* BUY *',
	Sell = '* SELL *',
}

export enum EditOrdersPreset {
	TakeProfit = '* TAKE_PROFIT *',
	StopLoss = '* STOP_LOSS *',
	None = '* NONE *',
}

export enum TableSize {
	Small = '* SMALL *',
	Large = '* LARGE *',
}

export type OrdersTableItem = {
	orderId: number;
	symbol: string;
	precision: number;
	side: OrdersSide;
	quantity: Option<number>;
	triggerPrice: Option<number>;
	distance: Option<number>;
	stopLoss: Option<number>;
	takeProfit: Option<number>;
	validUntil: Option<Date>;
};

export type ConfigItem = {
	width: number;
	dataKey: keyof OrdersTableItem | 'controls';
};

export type OrdersTableColumnNames = {
	symbol: ReactNode;
	side: ReactNode;
	quantity: ReactNode;
	triggerPrice: ReactNode;
	distance: ReactNode;
	stopLoss: ReactNode;
	takeProfit: ReactNode;
	validUntil: ReactNode;
};
