import { Option } from 'fp-ts/lib/Option';
import * as O from 'fp-ts/lib/Option';
import { format } from 'date-fns';
import { isProductionBuild } from '../../utils/node-env.utils';
import { ChatModel, ChatTags, ChatConfig } from './chat.model';
import { pipe } from 'fp-ts/lib/pipeable';
import { constant } from 'fp-ts/lib/function';

// TODO: switch to zE instead of $zopim when the Widget is switched to new version
// All `this` calls should be left in the upper scope of each function
// All other `$zopim` calls should be replaced with the nearest `zE` calls
// All wrapping functions, even `zE(() => {})` should be removed

const DEFAULT_LOCALE = 'en'; // TODO: use common const with locale-selector component

let currentTags: Array<string> = [];
const tagsObject: ChatTags = {
	accountId: O.none,
	locale: O.some(DEFAULT_LOCALE),
};

const convertTags = () => {
	return Object.keys(tagsObject).reduce((t: Array<string>, key) => {
		t.push(`${key}_${tagsObject[key].getOrElse('')}`);
		return t;
	}, []);
};

const convertRegionalLocale = (locale: string) => {
	return locale.replace(/^([a-z]{2})[-_]([a-z]{2})$/, (l, r) => `${l}-${r}`);
};

const updateTags = () => {
	// The following construction is required to properly call $zopim.
	// No wonder Zendesk wanted to migrate to new API
	zE(() => {
		$zopim(() => {
			$zopim.livechat.removeTags(currentTags);
			currentTags = convertTags();
			$zopim.livechat.addTags(currentTags);
		});
	});
	// zE('webWidget', 'updateSettings', {
	// 	webWidget: {
	// 		chat: {
	// 			tags: this.convertTags(),
	// 		},
	// 	},
	// });
};

const prefill = (name: Option<string>, email: Option<string>) => {
	if (!isProductionBuild) {
		return;
	}

	// Should be uncommented during complete migration from $zopim
	// const prefill: PrefillData = {};

	// if (name.isSome()) {
	// 	prefill.name = {
	// 		value: name.value,
	// 	};
	// }

	// if (email.isSome())) {
	// 	prefill.email = {
	// 		value: email.value,
	// 	};
	// }

	zE(() => {
		$zopim(() => {
			if (O.isSome(name)) {
				$zopim.livechat.setName(name.value);
			}
			if (O.isSome(email)) {
				$zopim.livechat.setEmail(email.value);
			}
		});
	});
	// zE('webWidget', 'prefill', prefill);
};

const hide = () => {
	if (!isProductionBuild) {
		return;
	}

	zE(() => {
		$zopim(() => {
			$zopim.livechat.window.hide();
		});
	});
	// zE('webWidget', 'hide');
};

const open = () => {
	if (!isProductionBuild) {
		return;
	}

	zE(() => {
		$zopim(() => {
			$zopim.livechat.window.show();
		});
	});
	// zE('webWidget', 'show');
	// zE('webWidget', 'open');
};

const close = () => {
	if (!isProductionBuild) {
		return;
	}

	zE(() => {
		$zopim(() => {
			$zopim.livechat.window.hide();
		});
	});
	// zE('webWidget', 'hide');
	// zE('webWidget', 'close');
};

const toggle = () => {
	console.log('toggle', !isProductionBuild);

	// if (!isProductionBuild) {
	// 	return;
	// }

	// const chatStatus: string = zE('webWidget:get', 'display');
	zE(() => {
		$zopim(() => {
			$zopim.livechat.window.toggle();
		});
	});
	// zE('webWidget', chatStatus === 'hidden' ? 'show' : 'hide');
	// zE('webWidget', 'toggle');
};

const setLocale = (locale: string) => {
	if (!isProductionBuild) {
		return;
	}

	tagsObject.locale = O.some(locale);
	zE(() => {
		$zopim(() => {
			$zopim.livechat.setLanguage(convertRegionalLocale(locale));
			// zE('webWidget', 'setLocale', convertRegionalLocale(locale));
			updateTags();
		});
	});
};

const setAccountId = (accountId: Option<string>) => {
	if (!isProductionBuild) {
		return;
	}

	tagsObject.accountId = accountId;
	zE(() => {
		$zopim(() => {
			updateTags();
		});
	});
};

const sendStartingMessage = ({ name, email, accountId, locale }: ChatConfig) => {
	if (!isProductionBuild) {
		return;
	}

	const date: string = format(new Date(), 'yyyy-mm-dd hh:mm A UTC Z');
	const msg: string = `
		Message sent: ${date}

		Name: ${pipe(
			name,
			O.getOrElse(constant('—')),
		)}
		Email: ${pipe(
			email,
			O.getOrElse(constant('—')),
		)}
		Account ID: ${pipe(
			accountId,
			O.getOrElse(constant('—')),
		)}
		Application locale: ${locale}
	`; // TODO: change AccountID to TP-number when server number is available

	zE(() => {
		$zopim(() => {
			$zopim.livechat.say(msg);
			// zE('webWidget', 'chat:send', msg);
		});
	});
};

const init = (config: ChatConfig) => {
	if (!isProductionBuild) {
		return;
	}

	const { name, email, accountId, locale } = config;

	zE(() => {
		$zopim(() => {
			$zopim.livechat.window.onHide(() => hide());
			$zopim.livechat.setOnChatStart(() => sendStartingMessage(config));
			// zE('webWidget:on', 'close', () => hide());
			// zE('webWidget:on', 'chat:start', () => sendStartingMessage(config));

			prefill(name, email);
			setAccountId(accountId);
			setLocale(locale);
		});
	});
};

export const createChat: () => ChatModel = () => ({
	init,
	hide,
	open,
	close,
	toggle,
	prefill,
	setAccountId,
	setLocale,
});
