import * as React from 'react';
import { PureComponent, Fragment } from 'react';
import { combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { OrdersTableContainer } from '../../containers/orders-table/orders-table.container';
import { Route } from '../../../../ui-common/route/containers/route.container';
import { MatchType } from '../../../../utils/router.utils';
import { routes } from '../../../../ui-common/routes';
import { OrderInfoContainer } from '../../containers/order-details/order-details.container';
import { OrderDashboardChartContainer } from '../../containers/order-dashboard-chart/order-dashboard-chart.container';
import { OrderEditContainer } from '../../containers/order-edit/order-edit.container';
import { RemoteData } from '@devexperts/remote-data-ts';
import { RemoteDataState } from '../../../../ui-common/remote-data-state/remote-data-state.component';
import { Redirect } from '../../../../ui-common/redirect/containers/redirect.container';
import { Option } from 'fp-ts/lib/Option';
import * as O from 'fp-ts/lib/Option';
import { constant } from 'fp-ts/lib/function';
import { pipe } from 'fp-ts/lib/pipeable';
import { EmptyScreenContainer } from '../../../empty-screen/containers/empty-screen.container';
import { ScreenType } from '../../../../ui-common/empty-screen/components/empty-screen.component';
import { Workspace } from '../../../ui-kit/components/workspace/workspace.component';
import * as css from './theme/orders-dashboard.module.css';

export type OrdersDashboardProps = {
	firstOrderId: RemoteData<Error, Option<number>>;
};
export const OrdersDashboard = combineContext(
	OrdersTableContainer,
	Route,
	OrderInfoContainer,
	OrderEditContainer,
	OrderDashboardChartContainer,
	EmptyScreenContainer,
	Redirect,
	(
		OrdersTableContainer,
		Route,
		OrderInfoContainer,
		OrderEditContainer,
		OrderDashboardChartContainer,
		EmptyScreenContainer,
		Redirect,
	) =>
		class OrdersDashboard extends PureComponent<OrdersDashboardProps> {
			render() {
				const { firstOrderId } = this.props;

				return <RemoteDataState data={firstOrderId} success={this.renderSuccess} />;
			}

			private renderSuccess = (firstOrderId: Option<number>) => (
				<section className={css.container}>
					<div className={css.left}>
						<Workspace>
							<Route match={routes.orders.root} render={this.renderRoot(firstOrderId)} />
						</Workspace>
						<Route match={routes.orders.order.root} render={this.renderChart} />
					</div>
					<Route exact match={routes.orders.order.info.root} render={this.renderOrderInfo} />
					<Route exact match={routes.orders.order.edit.profitLossKind.root} render={this.renderEdit} />
				</section>
			);

			private renderRoot = (orderId: Option<number>) => () =>
				pipe(
					orderId,
					O.fold(constant(<EmptyScreenContainer screenType={ScreenType.Orders} />), orderId => (
						<Fragment>
							<Route
								match={routes.orders.root}
								exact
								render={() => <Redirect to={routes.orders.order.root} value={{ orderId }} />}
							/>
							<Route match={routes.orders.order.root} render={() => <OrdersTableContainer />} />
						</Fragment>
					)),
				);

			private renderChart = (path: MatchType<typeof routes.orders.order.root>) => (
				<OrderDashboardChartContainer orderId={path.orderId} />
			);

			private renderOrderInfo = (path: MatchType<typeof routes.orders.order.info.root>) => (
				<div className={css.aside}>
					<OrderInfoContainer orderId={path.orderId} />
				</div>
			);

			private renderEdit = (path: MatchType<typeof routes.orders.order.edit.profitLossKind.root>) => (
				<div className={css.aside}>
					<OrderEditContainer orderId={path.orderId} profitLossKind={path.profitLossKind} />
				</div>
			);
		},
);
