import { ResponseValidationError, TAPIClient } from '../client/client';
import { ConversionRateTO, ConversionRateTOIO } from '../definitions/ConversionRateTO';
import { MarketSymbolsTO, MarketSymbolsTOIO } from '../definitions/MarketSymbolsTO';
import { ProfileTO, ProfileTOIO } from '../definitions/ProfileTO';
import { QuoteTO, QuoteTOIO } from '../definitions/QuoteTO';
import { SummaryTO, SummaryTOIO } from '../definitions/SummaryTO';
import { TradeTO, TradeTOIO } from '../definitions/TradeTO';
import {
	UpdateMarketSymbolsSubscriptionTO,
	UpdateMarketSymbolsSubscriptionTOIO,
} from '../definitions/UpdateMarketSymbolsSubscriptionTO';
import { fromEither } from '@devexperts/remote-data-ts';
import { LiveData } from '@devexperts/rx-utils/dist/rd/live-data.utils';
import { asks } from 'fp-ts/lib/Reader';
import { array, string, type, partial, void as tvoid } from 'io-ts';
import { map } from 'rxjs/operators';

export type MarketDataController = {
	/**
	 * Get list of conversion rates by symbols
	 * @param { object } parameters
	 */
	readonly getConversionRates: (parameters: {
		query: { symbols: Array<string> };
	}) => LiveData<Error, Array<ConversionRateTO>>;

	/**
	 * Change conversion rates subscription
	 * @param { object } [parameters]
	 */
	readonly changeConversionRateSubscription: (parameters: {
		body?: UpdateMarketSymbolsSubscriptionTO;
	}) => LiveData<Error, void>;

	/**
	 * Change market symbols subscription (quotes, trades, summary)
	 * @param { object } [parameters]
	 */
	readonly changeSubscription: (parameters: { body?: UpdateMarketSymbolsSubscriptionTO }) => LiveData<Error, void>;

	/**
	 * Subscribe on additional market symbols (quotes, trades, summary)
	 * @param { object } [parameters]
	 */
	readonly subscribeInstrumentTemporary: (parameters: { body?: MarketSymbolsTO }) => LiveData<Error, void>;

	/**
	 * Remove subscription on market symbols (quotes, trades, summary)
	 * @param { object } [parameters]
	 */
	readonly removeTemporaryInstrumentSubscription: (parameters: { body?: MarketSymbolsTO }) => LiveData<Error, void>;

	/**
	 * Get the latest profiles for symbols
	 * @param { object } parameters
	 */
	readonly getLatestProfiles: (parameters: {
		query: { symbols: Array<string> };
	}) => LiveData<Error, Array<ProfileTO>>;

	/**
	 * Get the latest quotes for symbols
	 * @param { object } parameters
	 */
	readonly getLatestQuotes: (parameters: { query: { symbols: Array<string> } }) => LiveData<Error, Array<QuoteTO>>;

	/**
	 * Get the latest summaries for symbols
	 * @param { object } parameters
	 */
	readonly getLatestSummaries: (parameters: {
		query: { symbols: Array<string> };
	}) => LiveData<Error, Array<SummaryTO>>;

	/**
	 * Get the latest trades for symbols
	 * @param { object } parameters
	 */
	readonly getLatestTrades: (parameters: { query: { symbols: Array<string> } }) => LiveData<Error, Array<TradeTO>>;
};

export const marketDataController = asks(
	(e: { apiClient: TAPIClient }): MarketDataController => ({
		getConversionRates: parameters => {
			const encoded = partial({ query: type({ symbols: array(string) }) }).encode(parameters);

			return e.apiClient
				.request({
					url: `/conversionrates`,
					method: 'GET',
					query: encoded.query,
				})
				.pipe(
					map(data =>
						data.chain(value =>
							fromEither(
								array(ConversionRateTOIO)
									.decode(value)
									.mapLeft(ResponseValidationError.create),
							),
						),
					),
				);
		},

		changeConversionRateSubscription: parameters => {
			const encoded = partial({ body: UpdateMarketSymbolsSubscriptionTOIO }).encode(parameters);

			return e.apiClient
				.request({
					url: `/conversionrates`,
					method: 'PUT',

					body: encoded.body,
				})
				.pipe(
					map(data =>
						data.chain(value => fromEither(tvoid.decode(value).mapLeft(ResponseValidationError.create))),
					),
				);
		},

		changeSubscription: parameters => {
			const encoded = partial({ body: UpdateMarketSymbolsSubscriptionTOIO }).encode(parameters);

			return e.apiClient
				.request({
					url: `/marketdata`,
					method: 'PUT',

					body: encoded.body,
				})
				.pipe(
					map(data =>
						data.chain(value => fromEither(tvoid.decode(value).mapLeft(ResponseValidationError.create))),
					),
				);
		},

		subscribeInstrumentTemporary: parameters => {
			const encoded = partial({ body: MarketSymbolsTOIO }).encode(parameters);

			return e.apiClient
				.request({
					url: `/marketdata`,
					method: 'POST',

					body: encoded.body,
				})
				.pipe(
					map(data =>
						data.chain(value => fromEither(tvoid.decode(value).mapLeft(ResponseValidationError.create))),
					),
				);
		},

		removeTemporaryInstrumentSubscription: parameters => {
			const encoded = partial({ body: MarketSymbolsTOIO }).encode(parameters);

			return e.apiClient
				.request({
					url: `/marketdata`,
					method: 'DELETE',

					body: encoded.body,
				})
				.pipe(
					map(data =>
						data.chain(value => fromEither(tvoid.decode(value).mapLeft(ResponseValidationError.create))),
					),
				);
		},

		getLatestProfiles: parameters => {
			const encoded = partial({ query: type({ symbols: array(string) }) }).encode(parameters);

			return e.apiClient
				.request({
					url: `/marketdata/profile`,
					method: 'GET',
					query: encoded.query,
				})
				.pipe(
					map(data =>
						data.chain(value =>
							fromEither(
								array(ProfileTOIO)
									.decode(value)
									.mapLeft(ResponseValidationError.create),
							),
						),
					),
				);
		},

		getLatestQuotes: parameters => {
			const encoded = partial({ query: type({ symbols: array(string) }) }).encode(parameters);

			return e.apiClient
				.request({
					url: `/marketdata/quote`,
					method: 'GET',
					query: encoded.query,
				})
				.pipe(
					map(data =>
						data.chain(value =>
							fromEither(
								array(QuoteTOIO)
									.decode(value)
									.mapLeft(ResponseValidationError.create),
							),
						),
					),
				);
		},

		getLatestSummaries: parameters => {
			const encoded = partial({ query: type({ symbols: array(string) }) }).encode(parameters);

			return e.apiClient
				.request({
					url: `/marketdata/summary`,
					method: 'GET',
					query: encoded.query,
				})
				.pipe(
					map(data =>
						data.chain(value =>
							fromEither(
								array(SummaryTOIO)
									.decode(value)
									.mapLeft(ResponseValidationError.create),
							),
						),
					),
				);
		},

		getLatestTrades: parameters => {
			const encoded = partial({ query: type({ symbols: array(string) }) }).encode(parameters);

			return e.apiClient
				.request({
					url: `/marketdata/trade`,
					method: 'GET',
					query: encoded.query,
				})
				.pipe(
					map(data =>
						data.chain(value =>
							fromEither(
								array(TradeTOIO)
									.decode(value)
									.mapLeft(ResponseValidationError.create),
							),
						),
					),
				);
		},
	}),
);
