import { combineLatest, Observable, merge } from 'rxjs';
import { distinctUntilChanged, filter, map, startWith, switchMap, tap, withLatestFrom, first } from 'rxjs/operators';
import { Option } from 'fp-ts/lib/Option';
import * as O from 'fp-ts/lib/Option';
import { eqNumber } from 'fp-ts/lib/Eq';
import { ask, combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { Sink } from '@devexperts/rx-utils/dist/sink.utils';
import { LiveData, LiveDataOption } from '@devexperts/rx-utils/dist/rd/live-data.utils';
import { fromOption, isSuccess, success } from '@devexperts/remote-data-ts';
import { mapRD } from '@devexperts/rx-utils/dist/rd/operators/mapRD';
import { combineLatestRD } from '@devexperts/rx-utils/dist/rd/operators/combineLatestRD';
import { switchMapRD } from '@devexperts/rx-utils/dist/rd/operators/switchMapRD';
import { createHandler } from '@devexperts/rx-utils/dist/create-handler.utils';
import { AccountViewModel } from '../account/account.view-model';
import { InstrumentStore } from '../../store/instrument/instrument.store';
import { OrderStore } from '../../store/order/order.store';
import { PositionsStore } from '../../store/positions/positions.store';
import { MarketDataStore } from '../../store/market-data/market-data.store';
import { ConversionRateStore } from '../../store/conversion-rate/conversion-rate.store';
import { routes } from '../../ui-common/routes';
import { href, Router } from '../../utils/router.utils';
import { filterMap } from '../../utils/rxjs/filtermap';
import {
	OrderEntryTakeProfitStopLossData,
	TakeProfitStopLossData,
	TakeProfitStopLossOptions,
	OrderKind,
} from '../../model/takeprofit-stoploss/takeprofit-stoploss.model';
import { OrderEntrySide } from '../../model/order-entry/order-entry.model';
import {
	getCurrentPrice,
	getDefaultTakeProfitStopLossData,
	getUpdatedProtectionOrderOption,
	getTakeProfitStopLossValue,
} from '../../model/takeprofit-stoploss/takeprofit-stoploss.utils';
import { countDistance } from '../../model/limits/limits.utils';
import { Either } from 'fp-ts/lib/Either';
import { MetricsStore } from '../../store/metrics/metrics.store';
import { LimitsStore } from '../../store/limits/limits.store';
import { currencyStringToSymbol } from '../../utils/currency.utils';
import { MarginImpactStore } from '../../store/margin-impact/margin-impact.store';
import {
	distinctRDOptionNumber,
	distinctRDOptionString,
	distinctRDNumber,
	setoidOptionNumber,
} from '../../utils/setoid.utils';
import { pipe } from 'fp-ts/lib/pipeable';
import { constant, constFalse } from 'fp-ts/lib/function';
import * as E from 'fp-ts/lib/Either';
import { shareReplayWithDefaults } from '../../utils/rxjs/shareReplayWithDefaults';

// position it is order with type equals 'MARKET'
const ORDER_TYPE = 'MARKET';

export type EditingPositionData = {
	symbol: string;
	description: Option<string>;
	lot: number;
	marginImpact: Either<Error, number>;
	takeProfitData: TakeProfitStopLossData;
	stopLossData: TakeProfitStopLossData;
	lotsPrecision: number;
};

export type EditingPositionViewModel = {
	data$: LiveData<Error, EditingPositionData>;
	positionAmountAbs$: LiveData<Error, number>;
	value$: LiveDataOption<Error, number>;
	profitLoss$: LiveDataOption<Error, number>;
	onTakeProfitStopLossValueChange: (value: OrderEntryTakeProfitStopLossData) => void;
	onPositionUpdate: (isValid: boolean) => () => void;
	isValid$: Observable<boolean>;
	isShownValidErrors$: Observable<boolean>;
	amountPrecision$: Observable<number>;
	currencySymbol$: Observable<string>;
	unitName$: LiveDataOption<Error, string>;
	isEditingPositionAllowed$: Observable<boolean>;
};

type EditingPositionViewModelContext = {
	positionsStore: PositionsStore;
	metricsStore: MetricsStore;
	accountViewModel: AccountViewModel;
	instrumentStore: InstrumentStore;
	orderStore: OrderStore;
	marketDataStore: MarketDataStore;
	marginImpactStore: MarginImpactStore;
	conversionRateStore: ConversionRateStore;
	limitsStore: LimitsStore;
	router: Router;
};

type CreateEditingPositionViewModel = (uid: string, plKind: string) => Sink<EditingPositionViewModel>;

export const createEditingPositionViewModel = combineContext(
	ask<EditingPositionViewModelContext>(),
	(editingPositionContext): CreateEditingPositionViewModel => (uid, plKind) => {
		const {
			positionsStore: { getPositionByUid, sendUpdateProtectionOrdersRequest },
			metricsStore: { getMetricsByUid },
			instrumentStore: { getById, getBySymbol },
			marketDataStore: { getDataBySymbol },
			marginImpactStore,
			conversionRateStore: { getRateBySymbol },
			router: { push },
			limitsStore: { getLimitsByInstrumentId },
			accountViewModel: { current$, accountMetrics$ },
		} = editingPositionContext;

		const takeProfitStopLossValueHandler = createHandler<OrderEntryTakeProfitStopLossData>();
		const updateProtectionOrdersHandler = createHandler();
		const isValidHandler = createHandler<boolean>();
		const isShownValidErrorsHandler = createHandler<boolean>();

		const position$ = getPositionByUid(uid);

		const profitLoss$ = getMetricsByUid(uid).pipe(
			mapRD(data => data.plOpen),
			distinctRDOptionNumber,
		);

		const positionAmount$ = position$.pipe(
			mapRD(position => position.quantity),
			distinctRDNumber,
		);
		const positionAmountAbs$ = position$.pipe(
			mapRD(position => Math.abs(position.quantity)),
			distinctRDNumber,
		);
		const positionOpeningPrice$ = position$.pipe(
			mapRD(({ costBasis }) => costBasis),
			filter(isSuccess),
			map(data => data.value),
			distinctUntilChanged(),
		);

		const instrumentData$ = position$.pipe(
			switchMapRD(({ positionKey: { instrumentId } }) => getById(instrumentId)),
			mapRD(instrument => ({
				id: instrument.id,
				lotSize: instrument.lotSize,
				symbol: instrument.symbol,
				symbolDescription: instrument.description,
				precision: instrument.precision,
				quantityIncrement: instrument.quantityIncrement,
				priceIncrement: instrument.priceIncrement,
				unitName: instrument.unitName,
				pipsSize: instrument.pipsSize,
			})),
			first(),
		);

		const extractedTakeProfitStopLoss$ = position$.pipe(
			mapRD(position => ({
				takeProfitData: position.takeProfit,
				stopLossData: position.stopLoss,
			})),
		);

		const symbol$ = instrumentData$.pipe(mapRD(data => data.symbol));

		const symbolMarginImpact$ = symbol$.pipe(
			switchMapRD(symbol => marginImpactStore.getMarginImpactBySymbol(symbol)),
		);

		const availableFunds$ = accountMetrics$.pipe(
			map(({ availableFunds }) => availableFunds),
			distinctUntilChanged(O.getEq(eqNumber).equals),
		);

		const baseCurrency$ = current$.pipe(
			map(currentAccount => success<Error, string>(currentAccount.baseCurrency)),
			distinctUntilChanged(),
		);

		const currencySymbol$ = current$.pipe(
			map(currentAccount => currencyStringToSymbol(currentAccount.baseCurrency)),
			first(),
		);

		const unitName$ = instrumentData$.pipe(
			mapRD(instrument => instrument.unitName),
			distinctRDOptionString,
		);

		const conversionRate$ = symbol$.pipe(
			switchMapRD(symbol => {
				const currency$ = getBySymbol(symbol).pipe(
					map(instrumentRD =>
						instrumentRD.chain(instrument =>
							fromOption(instrument.currency, () => new Error('There is no currency in this instrument')),
						),
					),
				);
				return combineLatestRD(currency$, baseCurrency$).pipe(
					switchMapRD(([currency, baseCurrency]) => {
						return getRateBySymbol(currency, baseCurrency);
					}),
				);
			}),
		);

		const value$ = combineLatestRD(symbol$, positionAmount$).pipe(
			switchMapRD(([symbol, quantity]) =>
				combineLatestRD(getDataBySymbol(symbol), conversionRate$).pipe(
					mapRD(([marketData, conversionRateData]) => {
						const { conversionRate } = conversionRateData;
						const isQuantityPositive = quantity > 0;
						return pipe(
							conversionRate,
							O.chain(rate => {
								const currentPrice = getCurrentPrice(isQuantityPositive)(marketData);
								return pipe(
									currentPrice,
									O.chain(price =>
										pipe(
											rate.value,
											O.map(value => Math.abs(quantity) * price * value),
										),
									),
								);
							}),
						);
					}),
				),
			),
			distinctRDOptionNumber,
		);

		const lotSize$ = instrumentData$.pipe(
			mapRD(value => value.lotSize),
			filter(isSuccess),
			map(lotSize => lotSize.value),
		);

		const formulaMarginImpact$ = symbolMarginImpact$.pipe(
			mapRD(data => data.formula),
			distinctRDOptionString,
		);
		const argumentsMarginImpact$ = symbolMarginImpact$.pipe(
			mapRD(data => data.arguments),
			distinctRDOptionString,
		);

		const quote$ = instrumentData$.pipe(switchMapRD(({ symbol }) => getDataBySymbol(symbol)));

		const bid$ = quote$.pipe(
			mapRD(data =>
				pipe(
					data.lastQuote,
					O.chain(({ bid }) => bid),
				),
			),
			filter(isSuccess),
			filterMap(quote => quote.value),
			distinctUntilChanged(),
		);

		const ask$ = quote$.pipe(
			mapRD(data =>
				pipe(
					data.lastQuote,
					O.chain(({ ask }) => ask),
				),
			),
			filter(isSuccess),
			filterMap(quote => quote.value),
			distinctUntilChanged(),
		);

		const precision$ = instrumentData$.pipe(
			mapRD(value => value.precision),
			filter(isSuccess),
			map(precision => precision.value),
		);

		const amount$ = positionAmount$.pipe(
			filter(isSuccess),
			map(amount => amount.value),
		);

		const amountAbs$ = positionAmountAbs$.pipe(
			filter(isSuccess),
			map(amount => amount.value),
		);

		const quantityInc$ = instrumentData$.pipe(
			mapRD(value => value.quantityIncrement),
			filter(isSuccess),
			map(quantityIncrement => quantityIncrement.value),
		);

		const priceInc$ = instrumentData$.pipe(
			mapRD(value => value.priceIncrement),
			filter(isSuccess),
			map(priceIncrement => priceIncrement.value),
		);

		const pipsSize$ = instrumentData$.pipe(
			mapRD(value => value.pipsSize),
			filter(isSuccess),
			map(pipsSize => pipsSize.value),
		);

		const prevTPValue$ = extractedTakeProfitStopLoss$.pipe(
			mapRD(value =>
				pipe(
					value.takeProfitData,
					O.chain(takeProfitData => takeProfitData.fixedPrice),
				),
			),
			filter(isSuccess),
			map(takeProfitValue => takeProfitValue.value),
			distinctUntilChanged(setoidOptionNumber.equals),
		);

		const prevSLValue$ = extractedTakeProfitStopLoss$.pipe(
			mapRD(value =>
				pipe(
					value.stopLossData,
					O.chain(stopLossData => stopLossData.fixedPrice),
				),
			),
			filter(isSuccess),
			map(stopLossValue => stopLossValue.value),
			distinctUntilChanged(setoidOptionNumber.equals),
		);

		const orderSide$ = amount$.pipe(
			map(amount => (amount > 0 ? OrderEntrySide.Buy : OrderEntrySide.Sell)),
			first(),
		);

		const spot$ = combineLatest(bid$, ask$, orderSide$).pipe(
			map(([bid, ask, orderSide]) => {
				switch (orderSide) {
					case OrderEntrySide.Sell:
						return bid;

					case OrderEntrySide.Buy:
						return ask;
				}
			}),
			distinctUntilChanged(),
		);

		const marginImpactQuantity$ = combineLatest(lotSize$, quantityInc$, orderSide$).pipe(
			map(([lotSize, quantityInc, orderSide]) => {
				const sign = orderSide === OrderEntrySide.Sell ? -1 : 1;
				return success<Error, number>(quantityInc * sign * lotSize);
			}),
		);

		const marginImpact$ = combineLatestRD(marginImpactQuantity$, formulaMarginImpact$, argumentsMarginImpact$).pipe(
			mapRD(([quantity, formula, args]) =>
				E.tryCatch2v(
					() =>
						pipe(
							args,
							O.chain(value =>
								pipe(
									formula,
									O.map(data => new Function(value, data)),
								),
							),
							O.fold(constant(new Error('Something went wrong during margin impact counting')), data =>
								data(quantity),
							),
							Number,
						),
					constant(new Error('Something went wrong during margin impact counting')),
				),
			),
		);

		const spotAmountCostCombined$ = combineLatest(spot$, amountAbs$, positionOpeningPrice$);

		const initTakeProfitStopLossValue$ = combineLatest(prevTPValue$, prevSLValue$).pipe(
			switchMap(([prevTPValue, prevSLValue]) =>
				takeProfitStopLossValueHandler.value$.pipe(
					startWith(
						getDefaultTakeProfitStopLossData({
							hasTP: pipe(
								prevTPValue,
								O.fold(() => (plKind === 'profit' ? O.some(0) : O.none), O.some),
							),
							hasSL: pipe(
								prevSLValue,
								O.fold(() => (plKind === 'loss' ? O.some(0) : O.none), O.some),
							),
						}),
					),
				),
			),
		);

		const currentAndPreviousTakeProfitStopLoss$ = combineLatest(
			initTakeProfitStopLossValue$,
			prevTPValue$,
			prevSLValue$,
		);

		const isShownValidErrors$: Observable<boolean> = isShownValidErrorsHandler.value$.pipe(
			startWith(false),
			distinctUntilChanged(),
			shareReplayWithDefaults(),
		);

		const limitsData$ = instrumentData$.pipe(switchMapRD(({ id }) => getLimitsByInstrumentId(id)));

		const limits$ = limitsData$.pipe(
			filter(isSuccess),
			map(data => data.value),
		);

		const minOrderIncrement$ = limits$.pipe(
			map(limit => limit.minOrderIncrement),
			distinctUntilChanged(),
		);

		const amountPrecision$ = combineLatest(lotSize$, minOrderIncrement$).pipe(
			map(([lotSize, increment]) => {
				const step = increment * lotSize;
				const stepString = step.toString();
				return step < 1 ? stepString.slice(stepString.indexOf('.') + 1).length : 0;
			}),
		);

		const lotsPrecision$ = limitsData$.pipe(
			mapRD(({ minOrderIncrement }) => {
				const limitsMinOrderIncrementString = String(minOrderIncrement);
				return limitsMinOrderIncrementString.slice(limitsMinOrderIncrementString.indexOf('.') + 1).length;
			}),
			first(),
		);

		const distance$ = combineLatest(limits$, pipsSize$, bid$, ask$).pipe(
			map(([limits, pipsSize, bid, ask]) =>
				countDistance(
					limits.limitStopDistanceType,
					limits.limitStopDistance,
					limits.limitStopDistanceInPercentOfSpread,
					Math.abs(bid - ask),
					pipsSize,
				),
			),
			first(),
		);

		const rate$ = conversionRate$.pipe(
			map(rates =>
				pipe(
					rates.toOption(),
					O.chain(rate =>
						pipe(
							rate.conversionRate,
							O.chain(conversionRate => conversionRate.value),
						),
					),
				),
			),
			distinctUntilChanged(setoidOptionNumber.equals),
		);

		const takeProfitStopLossCalculatedData$ = combineLatest(
			rate$,
			bid$,
			ask$,
			combineLatest(
				currentAndPreviousTakeProfitStopLoss$,
				priceInc$,
				spotAmountCostCombined$,
				orderSide$,
				precision$,
				distance$,
			),
		).pipe(
			filter(
				([rate, bid, ask, [[{ takeProfitData, stopLossData }]]]) =>
					takeProfitData.isSelected || stopLossData.isSelected,
			),
			map(
				([
					rate,
					bid,
					ask,
					[
						[{ takeProfitData, stopLossData }, prevTakeProfitValue, prevStopLossValue],
						priceIncrement,
						[spotValue, amount, costBasis],
						side,
						precision,
						distance,
					],
				]): TakeProfitStopLossOptions => ({
					currentTakeProfitData: takeProfitData,
					currentStopLossData: stopLossData,
					prevTakeProfitFixedPrice: prevTakeProfitValue,
					prevStopLossFixedPrice: prevStopLossValue,
					parentOrderPrice: spotValue,
					bid,
					ask,
					orderType: ORDER_TYPE,
					amount: Number(amount),
					side,
					priceIncrement,
					precision,
					rate: pipe(
						rate,
						O.getOrElse(constant(1)),
					),
					orderKind: OrderKind.Position,
					distance,
					costBasis,
				}),
			),
			map(data => getTakeProfitStopLossValue(data)),
			tap(data => {
				const {
					takeProfitData: { validatedPrice: validatedPriceTP },
					stopLossData: { validatedPrice: validatedPriceSL },
				} = data;

				isValidHandler.handle(E.isRight(validatedPriceTP) && E.isRight(validatedPriceSL));
			}),
		);

		const takeProfitStopLossData$ = merge(initTakeProfitStopLossValue$, takeProfitStopLossCalculatedData$);

		const takeProfitStopLossLiveData$ = takeProfitStopLossData$.pipe(
			map(value => success<Error, OrderEntryTakeProfitStopLossData>(value)),
		);

		const data$ = combineLatestRD(
			positionAmountAbs$,
			instrumentData$,
			takeProfitStopLossLiveData$,
			marginImpact$,
			lotsPrecision$,
		).pipe(
			mapRD(([amount, instrumentData, takeProfitStopLossLiveData, marginImpact, lotsPrecision]) => ({
				symbol: instrumentData.symbol,
				description: instrumentData.symbolDescription,
				lot: amount / instrumentData.lotSize,
				marginImpact,
				takeProfitData: takeProfitStopLossLiveData.takeProfitData,
				stopLossData: takeProfitStopLossLiveData.stopLossData,
				lotsPrecision,
			})),
		);

		const positionKey$ = position$.pipe(
			mapRD(position => position.positionKey),
			filter(isSuccess),
			map(positionKey => positionKey.value),
		);

		const successfulPrecision$ = instrumentData$.pipe(
			mapRD(value => value.precision),
			filter(isSuccess),
		);

		const protectionOrders$ = combineLatestRD(position$, takeProfitStopLossLiveData$, successfulPrecision$).pipe(
			mapRD(([position, takeProfitStopLossLiveData, successfulPrecision]) => {
				const { takeProfit, stopLoss } = position;
				const { takeProfitData, stopLossData } = takeProfitStopLossLiveData;

				const tpProtection = getUpdatedProtectionOrderOption(takeProfit, takeProfitData, successfulPrecision);
				const slProtection = getUpdatedProtectionOrderOption(stopLoss, stopLossData, successfulPrecision);

				return [tpProtection, slProtection];
			}),
			filter(isSuccess),
			map(protectionOrders => protectionOrders.value),
		);

		const isValid$: Observable<boolean> = isValidHandler.value$.pipe(
			startWith(true),
			distinctUntilChanged(),
			shareReplayWithDefaults(),
		);

		const onPositionUpdate = (isValid: boolean) => () => {
			isShownValidErrorsHandler.handle(!isValid);
			isValid && updateProtectionOrdersHandler.handle(void 0);
		};

		const sendEffect$ = updateProtectionOrdersHandler.value$.pipe(
			withLatestFrom(positionKey$, protectionOrders$),
			switchMap(([_, positionKey, protectionOrders]) => {
				const [tpProtection, slProtection] = protectionOrders;

				const takeProfitProtection = O.isSome(tpProtection) ? [tpProtection.value] : [];
				const stopLossProtection = O.isSome(slProtection) ? [slProtection.value] : [];

				const maybeEmptyArrayWithProtections = [...takeProfitProtection, ...stopLossProtection];

				return sendUpdateProtectionOrdersRequest(positionKey, maybeEmptyArrayWithProtections);
			}),
			filter(isSuccess),
			tap(() => push(href(routes.positions.position.root, { positionId: uid }))),
		);

		const isFundsEnough$ = combineLatestRD(
			marginImpact$,
			availableFunds$.pipe(map(val => success<Error, Option<number>>(val))),
		).pipe(
			mapRD(
				([marginImpact, availableFunds]) =>
					E.isRight(marginImpact) &&
					pipe(
						availableFunds,
						O.fold(constFalse, availableFunds => availableFunds > marginImpact.value),
					),
			),
		);

		const isEditingPositionAllowed$ = isFundsEnough$.pipe(
			map(isFundsEnough => isFundsEnough.getOrElse(false)),
			distinctUntilChanged(),
		);

		return new Sink(
			{
				data$,
				positionAmountAbs$,
				value$,
				amountPrecision$,
				profitLoss$,
				isValid$,
				isShownValidErrors$,
				onPositionUpdate,
				onTakeProfitStopLossValueChange: takeProfitStopLossValueHandler.handle,
				currencySymbol$,
				unitName$,
				/* TODO: add full featured checking like in OE or closing position */
				isEditingPositionAllowed$,
			},
			sendEffect$,
		);
	},
);
