import * as React from 'react';
import { PureComponent } from 'react';
import { Table, TableProps } from '../table-ava/table-list.component';
import { Omit } from 'typelevel-ts';
import AutoSizer, { Size } from 'react-virtualized-auto-sizer';

export type TAutosizedTableProps = Omit<TableProps, 'width' | 'height'>;

export class AutosizedTable extends PureComponent<TAutosizedTableProps> {
	public render() {
		return (
			<AutoSizer>{(size: Size) => <Table height={size.height} width={size.width} {...this.props} />}</AutoSizer>
		);
	}
}
