import * as React from 'react';
import { Component, ReactNode } from 'react';
import { WithTranslation, withTranslation } from 'react-i18next';
import * as cn from 'classnames';
import { withTheme } from '@devexperts/react-kit/dist/utils/withTheme';
import { PURE } from '@devexperts/react-kit/dist/utils/pure';
import { Button } from '@devexperts/react-kit/dist/components/Button/Button';
import { tspan } from '../../utils/i18n.utils';
import * as notNowButtonCss from './theme/not-now-button.module.css';
import * as css from './theme/info-box.module.css';

export type InfoBoxProps = {
	text: ReactNode;
	button: ReactNode;
	theme: {
		container?: string;
		text?: string;
	};
	hides: boolean;
} & WithTranslation;

type InfoBoxState = {
	isHidden: boolean;
};

@PURE
class RawInfoBox extends Component<InfoBoxProps, InfoBoxState> {
	readonly state = {
		isHidden: false,
	};

	handleToggle = () => {
		this.setState({
			isHidden: !this.state.isHidden,
		});
	};

	render() {
		const { text, button, theme, hides } = this.props;
		const { isHidden } = this.state;

		return (
			<div className={isHidden ? cn(theme.container, css.container_isHidden) : theme.container}>
				<div className={theme.text}>{text}</div>
				{button}
				{hides && (
					<Button theme={notNowButtonCss} onClick={this.handleToggle}>
						{tspan('infoBoxCancelButton', 'Not Now')}
					</Button>
				)}
			</div>
		);
	}
}

export const InfoBox = withTheme(Symbol(), css)(withTranslation()(RawInfoBox));
