import { ResponseValidationError, TAPIClient } from '../client/client';
import { CreateWatchlistTO, CreateWatchlistTOIO } from '../definitions/CreateWatchlistTO';
import { ResponseResultTO, ResponseResultTOIO } from '../definitions/ResponseResultTO';
import {
	UpdateWatchlistSubscriptionTO,
	UpdateWatchlistSubscriptionTOIO,
} from '../definitions/UpdateWatchlistSubscriptionTO';
import { WatchlistContentTO, WatchlistContentTOIO } from '../definitions/WatchlistContentTO';
import { WatchlistCreationTO, WatchlistCreationTOIO } from '../definitions/WatchlistCreationTO';
import { WatchlistsTO, WatchlistsTOIO } from '../definitions/WatchlistsTO';
import { fromEither } from '@devexperts/remote-data-ts';
import { LiveData } from '@devexperts/rx-utils/dist/rd/live-data.utils';
import { asks } from 'fp-ts/lib/Reader';
import { partial, void as tvoid, string, type, array } from 'io-ts';
import { map } from 'rxjs/operators';

export type WatchlistsController = {
	/**
	 * Create watchlist
	 * @param { object } [parameters]
	 */
	readonly createWatchlist: (parameters: { body?: CreateWatchlistTO }) => LiveData<Error, WatchlistCreationTO>;

	/**
	 * Update watchlists content subscription. Subscribes on changes in watchlist's content
	 * @param { object } [parameters]
	 */
	readonly changeWatchlistContentSubscription: (parameters: {
		body?: UpdateWatchlistSubscriptionTO;
	}) => LiveData<Error, void>;

	/**
	 * Rename watchlist
	 * @param { string } watchlistId - Watchlist's id
	 * @param { object } parameters
	 */
	readonly renameWatchlist: (
		watchlistId: string,
		parameters: { query: { newName: string } },
	) => LiveData<Error, ResponseResultTO>;

	/**
	 * Delete watchlist
	 * @param { string } watchlistId - Watchlist's id
	 */
	readonly deleteWatchlist: (watchlistId: string) => LiveData<Error, ResponseResultTO>;

	/**
	 * Update watchlist
	 * @param { string } watchlistId - Watchlist's id
	 * @param { object } [parameters]
	 */
	readonly updateWatchlistCondition: (
		watchlistId: string,
		parameters: { body?: Array<string> },
	) => LiveData<Error, ResponseResultTO>;

	/**
	 * Duplicate watchlist
	 * @param { string } watchlistId - Watchlist's id
	 * @param { object } parameters
	 */
	readonly duplicateWatchlist: (
		watchlistId: string,
		parameters: { query: { name: string } },
	) => LiveData<Error, WatchlistCreationTO>;

	/**
	 * Get list of available watchtlists
	 */
	readonly getWatchlists: () => LiveData<Error, Array<WatchlistsTO>>;

	/**
	 * Get watchlist content
	 * @param { object } parameters
	 */
	readonly getWatchlistsContent: (parameters: {
		query: { watchlistIds: Array<string> };
	}) => LiveData<Error, Array<WatchlistContentTO>>;
};

export const watchlistsController = asks(
	(e: { apiClient: TAPIClient }): WatchlistsController => ({
		createWatchlist: parameters => {
			const encoded = partial({ body: CreateWatchlistTOIO }).encode(parameters);

			return e.apiClient
				.request({
					url: `/watchlist`,
					method: 'POST',

					body: encoded.body,
				})
				.pipe(
					map(data =>
						data.chain(value =>
							fromEither(WatchlistCreationTOIO.decode(value).mapLeft(ResponseValidationError.create)),
						),
					),
				);
		},

		changeWatchlistContentSubscription: parameters => {
			const encoded = partial({ body: UpdateWatchlistSubscriptionTOIO }).encode(parameters);

			return e.apiClient
				.request({
					url: `/watchlist/updateWatchlistContentSubscription`,
					method: 'POST',

					body: encoded.body,
				})
				.pipe(
					map(data =>
						data.chain(value => fromEither(tvoid.decode(value).mapLeft(ResponseValidationError.create))),
					),
				);
		},

		renameWatchlist: (watchlistId, parameters) => {
			const encoded = partial({ query: type({ newName: string }) }).encode(parameters);

			return e.apiClient
				.request({
					url: `/watchlist/${encodeURIComponent(string.encode(watchlistId).toString())}`,
					method: 'PUT',
					query: encoded.query,
				})
				.pipe(
					map(data =>
						data.chain(value =>
							fromEither(ResponseResultTOIO.decode(value).mapLeft(ResponseValidationError.create)),
						),
					),
				);
		},

		deleteWatchlist: watchlistId => {
			return e.apiClient
				.request({
					url: `/watchlist/${encodeURIComponent(string.encode(watchlistId).toString())}`,
					method: 'DELETE',
				})
				.pipe(
					map(data =>
						data.chain(value =>
							fromEither(ResponseResultTOIO.decode(value).mapLeft(ResponseValidationError.create)),
						),
					),
				);
		},

		updateWatchlistCondition: (watchlistId, parameters) => {
			const encoded = partial({ body: array(string) }).encode(parameters);

			return e.apiClient
				.request({
					url: `/watchlist/${encodeURIComponent(string.encode(watchlistId).toString())}/content`,
					method: 'PUT',

					body: encoded.body,
				})
				.pipe(
					map(data =>
						data.chain(value =>
							fromEither(ResponseResultTOIO.decode(value).mapLeft(ResponseValidationError.create)),
						),
					),
				);
		},

		duplicateWatchlist: (watchlistId, parameters) => {
			const encoded = partial({ query: type({ name: string }) }).encode(parameters);

			return e.apiClient
				.request({
					url: `/watchlist/${encodeURIComponent(string.encode(watchlistId).toString())}/duplicate`,
					method: 'POST',
					query: encoded.query,
				})
				.pipe(
					map(data =>
						data.chain(value =>
							fromEither(WatchlistCreationTOIO.decode(value).mapLeft(ResponseValidationError.create)),
						),
					),
				);
		},

		getWatchlists: () => {
			return e.apiClient
				.request({
					url: `/watchlists`,
					method: 'GET',
				})
				.pipe(
					map(data =>
						data.chain(value =>
							fromEither(
								array(WatchlistsTOIO)
									.decode(value)
									.mapLeft(ResponseValidationError.create),
							),
						),
					),
				);
		},

		getWatchlistsContent: parameters => {
			const encoded = partial({ query: type({ watchlistIds: array(string) }) }).encode(parameters);

			return e.apiClient
				.request({
					url: `/watchlists/content`,
					method: 'GET',
					query: encoded.query,
				})
				.pipe(
					map(data =>
						data.chain(value =>
							fromEither(
								array(WatchlistContentTOIO)
									.decode(value)
									.mapLeft(ResponseValidationError.create),
							),
						),
					),
				);
		},
	}),
);
