import * as React from 'react';
import { Fragment, memo, useCallback } from 'react';
import { Option } from 'fp-ts/lib/Option';
import { RemoteData } from '@devexperts/remote-data-ts';
import { SidePanel } from '../../../../ui-common/side-panel/side-panel.component';
import { CloseButton } from '../../../../ui-common/close-button/close-button.component';
import { PositionSide, HeaderData } from '../../../../ui-common/position-details/position-details.model';
import { combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { RouteLink } from '../../../../ui-common/route-link/containers/route-link.container';
import { routes } from '../../../../ui-common/routes';
import { withTranslation, WithTranslation } from 'react-i18next';
import { tspan } from '../../../../utils/i18n.utils';
import { PositionDetailsCommomn } from '../../../../ui-common/position-details/position-details.component';
import { Button } from '../../../../ui-common/button/button.component';
import * as buttonCss from './theme/button.child.module.css';
import * as css from './theme/position-details.module.css';

export type PositionDetailsProps = {
	uid: string;
	pipsSize: number;
	accountCurrency: string;
	precision: number;
	headerData: RemoteData<Error, HeaderData>;
	side: RemoteData<Error, PositionSide>;
	openPrice: RemoteData<Error, number>;
	currentPriceAndDirection: RemoteData<Error, Option<number>[]>;
	quantity: RemoteData<Error, number>;
	sizeInLots: RemoteData<Error, number>;
	currentValue: RemoteData<Error, Option<number>>;
	openDateTime: RemoteData<Error, Date>;
	takeProfitValue: RemoteData<Error, Option<number>>;
	stopLossValue: RemoteData<Error, Option<number>>;
	swap: RemoteData<Error, Option<number>>;
	commission: RemoteData<Error, Option<number>>;
};

export const PositionDetails = combineContext(RouteLink, RouteLink =>
	withTranslation()(
		memo((props: PositionDetailsProps & WithTranslation) => {
			const { uid, t } = props;

			const renderFooter = useCallback(
				() => (
					<div className={css.buttonWrapper}>
						<RouteLink
							to={routes.positions.position.close.root}
							value={{ positionId: uid }}
							render={({ onNavigate }) => (
								<Button onClick={onNavigate} isPrimary theme={buttonCss}>
									{tspan('positionDetailsClosePosition', 'Close Position')}
								</Button>
							)}
						/>
						<RouteLink
							to={routes.positions.position.edit.profitLossKind.root}
							value={{ positionId: uid, profitLossKind: 'add-protection' }}
							render={({ onNavigate }) => (
								<Button onClick={onNavigate} isPrimary theme={buttonCss}>
									{tspan('positionDetailsEditPosition', 'Edit Position')}
								</Button>
							)}
						/>
					</div>
				),
				[t],
			);

			const renderHeader = useCallback(
				() => (
					<Fragment>
						<span className={css.title}>{tspan('PositionInfoTitle', 'Position info')}</span>
						<RouteLink
							to={routes.positions.position.root}
							value={{ positionId: uid }}
							render={({ onNavigate }) => <CloseButton onClick={onNavigate} />}
						/>
					</Fragment>
				),
				[t],
			);

			return (
				<SidePanel header={renderHeader()} footer={renderFooter()} scrollable>
					<PositionDetailsCommomn {...props} />
				</SidePanel>
			);
		}),
	),
);
