import * as React from 'react';
import { Locales } from './locale-selector.model';
import { FlagUsaIcon } from '../icons/flag-usa.icon';
import { FlagRussiaIcon } from '../icons/flag-russia.icon';
import { FlagGermanyIcon } from '../icons/flag-germany.icon';
import { FlagFranceIcon } from '../icons/flag-france.icon';
import { FlagItalyIcon } from '../icons/flag-italy.icon';
import { FlagPortugeseIcon } from '../icons/flag-portugese.icon';
import { FlagSpainIcon } from '../icons/flag-spain.icon';
import { FlagIsraelIcon } from '../icons/flag-israel.icon';
import { FlagArabicIcon } from '../icons/flag-arabic.icon';
import { FlagChinaIcon } from '../icons/flag-china.icon';
import { IO } from 'fp-ts/lib/IO';

// It's static. No need to put this inside component
export const getFlagIcon = (locale: Locales) => {
	switch (locale) {
		case 'en':
			return <FlagUsaIcon />;
		case 'ru':
			return <FlagRussiaIcon />;
		case 'de':
			return <FlagGermanyIcon />;
		case 'fr':
			return <FlagFranceIcon />;
		case 'it':
			return <FlagItalyIcon />;
		case 'pt':
			return <FlagPortugeseIcon />;
		case 'es':
			return <FlagSpainIcon />;
		case 'he':
			return <FlagIsraelIcon />;
		case 'ar':
			return <FlagArabicIcon />;
		case 'zh_CN':
			return <FlagChinaIcon />;
		default:
			return <FlagUsaIcon />;
	}
};

export const dispatchLocaleChangeEvent = (locale: Locales) =>
	new IO<void>(() => document.dispatchEvent(new CustomEvent('lokalise-update-locale', { detail: locale })));

export const isLocale = (value: unknown): value is Locales =>
	typeof value === 'string' && ['en', 'ru', 'de', 'fr', 'it', 'pt', 'es', 'he', 'ar', 'zh_CN'].includes(value);

export const getFallbackLanguage = (lng: string) => {
	switch (lng) {
		case 'zh-Hans':
			return 'zh_CN';
		default:
			return lng;
	}
};
