import * as React from 'react';
import { PureComponent, ReactNode } from 'react';
import { formatNumber } from 'accounting';
import * as cn from 'classnames';
import { Option } from 'fp-ts/lib/Option';
import * as O from 'fp-ts/lib/Option';
import { findFirst } from 'fp-ts/lib/Array';
import { RemoteData } from '@devexperts/remote-data-ts';
import { combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { RouteLink } from '../../../../ui-common/route-link/containers/route-link.container';
import { routes } from '../../../../ui-common/routes';
import { ConfigItem, PositionsTableItem, EditPositionPreset, PositionSide, TableSize } from './position-table.model';
import { TableCellAlign } from '../../../../ui-common/table-ava/table.model';
import { Optional } from '../../../../ui-common/optional/optional.component';
import { RemoteDataState } from '../../../../ui-common/remote-data-state/remote-data-state.component';
import { InfoIcon } from '../../../../ui-common/icons/info.icon';
import { DeleteIcon } from '../../../../ui-common/icons/delete.icon';
import { EditIcon } from '../../../../ui-common/icons/edit.icon';
import { ListChildComponentProps } from 'react-window';
import { withTranslation, WithTranslation } from 'react-i18next';
import { AddIcon } from '../../../ui-kit/components/icons/add.icon';
import { tspan } from '../../../../utils/i18n.utils';
import { pipe } from 'fp-ts/lib/pipeable';
import { constant } from 'fp-ts/lib/function';
import * as css from './theme/position-table.module.css';

export type PositionEntityProps = ListChildComponentProps & {
	rowData: string;
	remoteData: RemoteData<Error, PositionsTableItem>;
	symbol: RemoteData<Error, string>;
	profitLoss: RemoteData<Error, Option<number>>;
	value: RemoteData<Error, Option<number>>;
	config: ConfigItem[];
	tableSize: TableSize;
} & WithTranslation;

const defaultColumnWidth = 150;

export const PositionEntity = combineContext(RouteLink, RouteLink =>
	withTranslation()(
		class PositionEntity extends PureComponent<PositionEntityProps> {
			render() {
				const { remoteData } = this.props;

				return (
					<div>
						<RemoteDataState data={remoteData} success={this.renderSuccess} />
					</div>
				);
			}

			private renderSuccess = (data: PositionsTableItem): JSX.Element => {
				const { index, style, symbol, profitLoss, value } = this.props;
				const openPrice = pipe(
					data.openPrice,
					O.map(value => formatNumber(value, data.precision)),
				);

				return (
					<RouteLink
						to={routes.positions.position.root}
						value={{ positionId: data.uid }}
						render={({ onNavigate, isActive }) => (
							<div
								className={cn(css.row, { [css.rowSelected]: isActive })}
								key={index}
								role="row"
								style={style}
								onClick={onNavigate}>
								{this.renderSymbolCell(symbol)}
								{this.renderSide(data)}
								{this.renderCell(<Optional value={data.size} />, 'size', TableCellAlign.Right)}
								{this.renderValueCell(value)}
								{this.renderProfitLossCell(profitLoss)}
								{this.renderCell(<Optional value={openPrice} />, 'openPrice', TableCellAlign.Right)}
								{this.renderStopLossCell(data)}
								{this.renderTakeProfitCell(data)}
								{this.renderControls(data)}
							</div>
						)}
					/>
				);
			};

			private renderCell = (val: ReactNode, type: string, align: TableCellAlign) => {
				const { config, tableSize } = this.props;
				const className = cn(css.rowColumn, {
					[css.rowColumn_left]: align === TableCellAlign.Left,
					[css.rowColumn_right]: align === TableCellAlign.Right,
					[css.rowColumn_center]: align === TableCellAlign.Center,
					[css.rowColumn_small]: tableSize === TableSize.Small,
				});
				return (
					<div className={className} style={{ flexBasis: this.getWidthFromConfig(config, type) }}>
						{val}
					</div>
				);
			};

			private renderSymbolCell = (symbol: RemoteData<Error, string>) => {
				return this.renderCell(
					<RemoteDataState
						data={symbol}
						success={data => {
							return <span>{data}</span>;
						}}
					/>,
					'symbol',
					TableCellAlign.Left,
				);
			};

			private renderValueCell = (value: RemoteData<Error, Option<number>>) => {
				return this.renderCell(
					<RemoteDataState
						data={value}
						success={val => {
							const value = pipe(
								val,
								O.map(val => formatNumber(val, 2)),
							);
							return <Optional value={value} />;
						}}
					/>,
					'value',
					TableCellAlign.Right,
				);
			};

			private renderProfitLossCell = (profitLoss: RemoteData<Error, Option<number>>): ReactNode =>
				this.renderCell(
					<RemoteDataState
						data={profitLoss}
						success={val => {
							const value = pipe(
								val,
								O.map(pl => <div className={pl > 0 ? css.green : css.red}>{formatNumber(pl, 2)}</div>),
							);
							return <Optional value={value} />;
						}}
					/>,
					'profitLoss',
					TableCellAlign.Right,
				);

			private getSide = (props: PositionsTableItem): ReactNode => {
				const { side } = props;
				switch (side) {
					case PositionSide.Bought:
						return <div className={css.green}>{tspan('positionsTableEntityBought', 'Bought')}</div>;
					case PositionSide.Sold:
						return <div className={css.red}>{tspan('positionsTableEntitySold', 'Sold')}</div>;
				}
			};
			private renderSide = (props: PositionsTableItem): ReactNode => {
				const side = this.getSide(props);
				return this.renderCell(side, 'side', TableCellAlign.Center);
			};

			private renderStopLossCell = (data: PositionsTableItem) => {
				const { stopLoss, precision } = data;
				const { tableSize } = this.props;
				const value = pipe(
					stopLoss,
					O.fold<number, ReactNode>(
						() => this.renderAddProtection(data, EditPositionPreset.StopLoss),
						value => (
							<span className={tableSize === TableSize.Small ? css.addProtection_small : ''}>
								{formatNumber(value, precision)}
							</span>
						),
					),
				);

				return this.renderCell(value, 'stopLoss', TableCellAlign.Center);
			};

			private renderTakeProfitCell = (data: PositionsTableItem) => {
				const { takeProfit, precision } = data;
				const { tableSize } = this.props;
				const value = pipe(
					takeProfit,
					O.fold<number, ReactNode>(
						() => this.renderAddProtection(data, EditPositionPreset.TakeProfit),
						value => (
							<span className={tableSize === TableSize.Small ? css.addProtection_small : ''}>
								{formatNumber(value, precision)}
							</span>
						),
					),
				);

				return this.renderCell(value, 'takeProfit', TableCellAlign.Center);
			};

			private renderControls = (data: PositionsTableItem) => {
				const { uid } = data;
				const editingPositionRootSuffix = 'add-protection';

				return this.renderCell(
					<ul className={css.controls}>
						<li className={css.control}>
							<RouteLink
								to={routes.positions.position.info.root}
								value={{ positionId: uid }}
								render={({ onNavigate }) => (
									<div className={css.icon} onClick={this.handleClickWithStopPropagation(onNavigate)}>
										<InfoIcon />
									</div>
								)}
							/>
						</li>
						<li className={css.control}>
							<RouteLink
								to={routes.positions.position.edit.profitLossKind.root}
								value={{ positionId: uid, profitLossKind: editingPositionRootSuffix }}
								render={({ onNavigate }) => (
									<div className={css.icon} onClick={this.handleClickWithStopPropagation(onNavigate)}>
										<EditIcon />
									</div>
								)}
							/>
						</li>
						<li className={css.control}>
							<RouteLink
								to={routes.positions.position.close.root}
								value={{ positionId: uid }}
								render={({ onNavigate }) => (
									<div className={css.icon} onClick={this.handleClickWithStopPropagation(onNavigate)}>
										<DeleteIcon />
									</div>
								)}
							/>
						</li>
					</ul>,
					'controls',
					TableCellAlign.Right,
				);
			};

			private handleClickWithStopPropagation = (onNavigate: () => void) => (
				e: React.MouseEvent<HTMLDivElement>,
			) => {
				e.stopPropagation();
				onNavigate();
			};

			private renderAddProtection = (data: PositionsTableItem, preset: EditPositionPreset) => {
				const { uid } = data;
				const { tableSize } = this.props;

				const routeSuffix = this.getEditingPositionRouteSuffix(preset);

				return (
					<RouteLink
						to={routes.positions.position.edit.profitLossKind.root}
						value={{ positionId: uid, profitLossKind: routeSuffix }}
						render={({ onNavigate }) => (
							<div
								className={css.addProtection}
								onClick={this.handleClickWithStopPropagation(onNavigate)}>
								{tableSize === TableSize.Large ? (
									tspan('positionsTableEntityAddProtection', '+ Add')
								) : (
									<i className={css.icon_plus}>
										<AddIcon />
									</i>
								)}
							</div>
						)}
					/>
				);
			};

			private getEditingPositionRouteSuffix = (preset: EditPositionPreset) => {
				switch (preset) {
					case EditPositionPreset.StopLoss:
						return 'loss';
					case EditPositionPreset.TakeProfit:
						return 'profit';
					case EditPositionPreset.None:
						return '';
				}
			};

			private getWidthFromConfig = (config: ConfigItem[], dataKey: string): number => {
				const currentColumn = findFirst(config, item => {
					return item['dataKey'] === dataKey;
				});

				return pipe(
					currentColumn,
					O.fold(constant(defaultColumnWidth), value => value.width),
				);
			};
		},
	),
);
