import { withRXWithDefaults } from '../../../../utils/withRXWithDefaults';
import { ChartViewModel, createChartViewModel } from '../../../../view-model/chart/chart.view-model';
import { ChartLoader } from '../../components/chart-loader/chart-loader.component';
import { runOnMount } from '../../../../utils/react.utils';
import { pending } from '@devexperts/remote-data-ts';
import { mapRD } from '@devexperts/rx-utils/dist/rd/operators/mapRD';
import Candle from '@dx-private/dxchart5/dist/js/chart/model/Candle';
import { map } from 'rxjs/operators';
import { CrossToolMode, Period, ChartType } from './chart-loader.model';
import { booleanToMode, isCrosshair, toChartCandle } from './chart-loader.utils';
import { ask, combineContext, deferContext } from '@devexperts/rx-utils/dist/context.utils';
import { createSelector } from 'reselect';

type ChartLoaderContainerContext = {
	chartViewModel: ChartViewModel;
};

const empty: Candle[] = [];

const Container = combineContext(ask<ChartLoaderContainerContext>(), ctx => {
	return withRXWithDefaults(ChartLoader)(() => {
		const { chartViewModel } = ctx;
		const onChangeType = (type: ChartType) => chartViewModel.onChangeChartType(type);
		const onChangePeriod = (period: Period) => chartViewModel.onChangePeriod(period);
		const onChangeCrossToolMode = (mode: CrossToolMode) => chartViewModel.onSwitchCrossTool(isCrosshair(mode));

		const periods$ = chartViewModel.periods$;
		const description$ = chartViewModel.description$;
		const precision$ = chartViewModel.precision$;
		const selectedChartType$ = chartViewModel.selectedChartType$;
		const selectedPeriod$ = chartViewModel.selectedPeriod$;
		const selectedCrossToolMode$ = chartViewModel.isCrossToolEnabled$.pipe(map(booleanToMode));

		const historical$ = chartViewModel.candles$.pipe(mapRD(candles => candles.map(toChartCandle)));

		const lastCandles$ = chartViewModel.lastCandles$.pipe(
			map(candles => candles.map(candles => candles.map(toChartCandle)).getOrElse(empty)),
		);

		return {
			props: {
				precision: precision$,
				title: description$,
				historical: historical$,
				periodRangePair: chartViewModel.aggregationPeriodPair$,
				lastCandles: lastCandles$,
				periods: periods$,
				selectedPeriod: selectedPeriod$,
				selectedType: selectedChartType$,
				selectedCrossToolMode: selectedCrossToolMode$,
			},
			defaultProps: {
				precision: pending,
				title: pending,
				historical: pending,
				periodRangePair: {
					period: 60,
					range: 432000,
				},
				lastCandles: [],
				periods: pending,
				selectedPeriod: {
					name: '1m',
					seconds: 60,
				},
				selectedType: ChartType.Candle,
				selectedCrossToolMode: CrossToolMode.Disabled,
				onPeriodSelect: onChangePeriod,
				onTypeSelect: onChangeType,
				onCrossToolModeSelect: onChangeCrossToolMode,
			},
		};
	});
});

export const ChartLoaderContainer = combineContext(
	deferContext(Container, 'chartViewModel'),
	createChartViewModel,
	(Container, createChartViewModel) =>
		runOnMount(Container, props => {
			const plugins = createSelector(
				props,
				props => props.plugins,
			);
			const symbol = createSelector(
				props,
				props => props.symbol,
			);
			return createSelector(
				plugins,
				symbol,
				(plugins, symbol) =>
					createChartViewModel(symbol, false).map(chartViewModel => ({
						plugins,
						chartViewModel,
					})),
			);
		}),
);
