import { PositionKeyTO, PositionKeyTOIO } from '../definitions/PositionKeyTO';
import { ProtectionOrderTO, ProtectionOrderTOIO } from '../definitions/ProtectionOrderTO';
import { Option } from 'fp-ts/lib/Option';
import { string, number, type } from 'io-ts';
import { DateFromISOString, createOptionFromNullable } from 'io-ts-types';

export type PositionTO = {
	uid: string;
	accountId: string;
	positionKey: PositionKeyTO;
	quantity: number;
	cost: number;
	costBasis: number;
	time: Date;
	modifiedTime: Option<Date>;
	userLogin: Option<string>;
	takeProfit: Option<ProtectionOrderTO>;
	stopLoss: Option<ProtectionOrderTO>;
};
export const PositionTOIO = type({
	uid: string,
	accountId: string,
	positionKey: PositionKeyTOIO,
	quantity: number,
	cost: number,
	costBasis: number,
	time: DateFromISOString,
	modifiedTime: createOptionFromNullable(DateFromISOString),
	userLogin: createOptionFromNullable(string),
	takeProfit: createOptionFromNullable(ProtectionOrderTOIO),
	stopLoss: createOptionFromNullable(ProtectionOrderTOIO),
});
