import { fromEvent, Observable } from 'rxjs';
import { PostMessageTO, postMessagesTO } from './post-message/post-message.to';
import { filter, map } from 'rxjs/operators';
import { isRight, tryCatch2v } from 'fp-ts/lib/Either';
import * as E from 'fp-ts/lib/Either';
import { pipe } from 'fp-ts/lib/pipeable';

export type PostMessageClient = {
	postMessage$: Observable<PostMessageTO>;
};

const postMessage$ = fromEvent<MessageEvent>(window, 'message');

export const createPostMessageClient = (): PostMessageClient => {
	const decodedMessages$ = postMessage$.pipe(
		filter(message => message.origin.indexOf('.avatrade') > 0),
		map(message =>
			pipe(
				tryCatch2v(
					() => JSON.parse(message.data),
					e => (e instanceof Error ? e : new Error('Cannot parse JSON')),
				),
				E.chain(data =>
					pipe(
						data,
						postMessagesTO.decode,
						E.mapLeft(() => new Error('Validation error')),
					),
				),
			),
		),
		filter(isRight),
		map(data => data.value),
	);

	return {
		postMessage$: decodedMessages$,
	};
};
