import { TDateInputValue } from '@devexperts/react-kit/dist/components/DateInput/DateInput.model';
import { ProfitLossKind } from '../takeprofit-stoploss/takeprofit-stoploss.model';
import { Option } from 'fp-ts/lib/Option';

export enum HistoryOrderSide {
	Buy = '* BUY *',
	Sell = '* SELL *',
}

export enum Period {
	last7days = '* LAST_7_DAYS *',
	last30Days = '* LAST_30_DAYS *',
	last365days = '* LAST_365_DAYS *',
	thisMonth = '* THIS_MONTH *',
	thisYear = '* THIS_YEAR *',
	all = '* ALL *',
	custom = '* CUSTOM *',
}

export type UnwrappedFilterFormData = [Period, number, number];

export type HistoryFilterData = {
	selectedPeriod: Period;
	startDate: TDateInputValue;
	endDate: TDateInputValue;
};

export type HistoryValueHandlerData = HistoryFilterData & {
	isDateInputChanged: boolean;
};

export type HistoryDetailsData = {
	orderChainId: string;
	symbol: string;
	side: HistoryOrderSide;
	amount: number;
	profitLossKind: Option<ProfitLossKind>;
	openDateTime: Option<Date>;
	closeDateTime: Date;
	sizeInLots: number;
	takeProfitValue: Option<number>;
	stopLossValue: Option<number>;
	swaps: Option<number>;
	commissions: Option<number>;
	pipsSize: number;
	precision: number;
};
