import * as React from 'react';
import { withTranslation, WithTranslation } from 'react-i18next';
import * as accounting from 'accounting';
import { Option } from 'fp-ts/lib/Option';
import * as O from 'fp-ts/lib/Option';
import { Optional } from '../../../../../ui-common/optional/optional.component';
import { tspan } from '../../../../../utils/i18n.utils';
import { PureComponent } from 'react';
import { pipe } from 'fp-ts/lib/pipeable';
import * as css from './theme/header-account-metrics.module.css';

type THeaderAccountMetricsProps = {
	available: Option<number>;
	totalAllocated: Option<number>;
	profit: Option<number>;
	equity: Option<number>;
	currencySymbol: string;
	previewMode: Option<boolean>;
};
class HeaderAccountMetricsRaw extends PureComponent<THeaderAccountMetricsProps & WithTranslation> {
	render() {
		const { available, totalAllocated, profit, equity } = this.props;

		return (
			<div className={css.container}>
				<div className={css.section}>
					<header className={css.section__header}>
						{tspan('headerAccountMetricsAvailable', 'Available')}
					</header>
					<div className={css.section__value}>{this.withPreview(available)}</div>
				</div>
				<div className={css.separator} />
				<div className={css.section}>
					<header className={css.section__header}>
						{tspan('headerAccountMetricsTotalAllocated', 'Total Allocated')}
					</header>
					<div className={css.section__value}>{this.withPreview(totalAllocated)}</div>
				</div>
				<div className={css.separator} />
				<div className={css.section}>
					<header className={css.section__header}>{tspan('headerAccountMetricsProfit', 'Profit')}</header>
					<div className={css.section__value}>{this.withPreview(profit)}</div>
				</div>
				<div className={css.separator} />
				<div className={css.section}>
					<header className={css.section__header}>{tspan('headerAccountMetricsEquity', 'Equity')}</header>
					<div className={css.section__value}>{this.withPreview(equity)}</div>
				</div>
			</div>
		);
	}

	private formatValue = (value: number) =>
		accounting.formatMoney(value, { symbol: this.props.currencySymbol, format: '%s %v' });

	private withPreview = (value: Option<number>): JSX.Element => {
		const { previewMode } = this.props;
		return (
			<Optional
				value={pipe(
					previewMode,
					O.map(previewMode =>
						previewMode ? (
							'N/A'
						) : (
							<Optional
								value={pipe(
									value,
									O.map(this.formatValue),
								)}
							/>
						),
					),
				)}
			/>
		);
	};
}

export const HeaderAccountMetrics = withTranslation()(HeaderAccountMetricsRaw);
