import { ResponseValidationError, TAPIClient } from '../client/client';
import { SuggestResultTO, SuggestResultTOIO } from '../definitions/SuggestResultTO';
import { TreeSuggestResultTO, TreeSuggestResultTOIO } from '../definitions/TreeSuggestResultTO';
import { fromEither } from '@devexperts/remote-data-ts';
import { LiveData } from '@devexperts/rx-utils/dist/rd/live-data.utils';
import { Option } from 'fp-ts/lib/Option';
import { asks } from 'fp-ts/lib/Reader';
import { string, array, type, partial } from 'io-ts';
import { createOptionFromNullable } from 'io-ts-types';
import { map } from 'rxjs/operators';

export type SymbolSuggestController = {
	/**
	 * Request suggest results using symbol text for suggestion
	 * @param { object } parameters
	 */
	readonly requestSuggestions: (parameters: {
		query: { text: string; instrumentTypes: Option<Array<string>> };
	}) => LiveData<Error, SuggestResultTO>;

	/**
	 * Request tree suggest results using symbol text
	 * @param { object } parameters
	 */
	readonly requestSuggestsTree: (parameters: {
		query: { text: string; instrumentTypes: Option<Array<string>> };
	}) => LiveData<Error, TreeSuggestResultTO>;
};

export const symbolSuggestController = asks(
	(e: { apiClient: TAPIClient }): SymbolSuggestController => ({
		requestSuggestions: parameters => {
			const encoded = partial({
				query: type({ text: string, instrumentTypes: createOptionFromNullable(array(string)) }),
			}).encode(parameters);

			return e.apiClient
				.request({
					url: `/suggest`,
					method: 'GET',
					query: encoded.query,
				})
				.pipe(
					map(data =>
						data.chain(value =>
							fromEither(SuggestResultTOIO.decode(value).mapLeft(ResponseValidationError.create)),
						),
					),
				);
		},

		requestSuggestsTree: parameters => {
			const encoded = partial({
				query: type({ text: string, instrumentTypes: createOptionFromNullable(array(string)) }),
			}).encode(parameters);

			return e.apiClient
				.request({
					url: `/suggest/tree`,
					method: 'GET',
					query: encoded.query,
				})
				.pipe(
					map(data =>
						data.chain(value =>
							fromEither(TreeSuggestResultTOIO.decode(value).mapLeft(ResponseValidationError.create)),
						),
					),
				);
		},
	}),
);
