import { map, pluck } from 'rxjs/internal/operators';
import { Observable, combineLatest, of } from 'rxjs';
import { ask, combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { AccountViewModel } from '../account/account.view-model';
import { AccountTO } from '../../api/socket/to/accounts-message.to';
import { chooseMode } from './menu.utils';
import { Route } from 'fp-ts-routing';
import { fromNullable } from 'fp-ts/lib/Option';
import { Router } from '../../utils/router.utils';
import * as O from 'fp-ts/lib/Option';
import { pipe } from 'fp-ts/lib/pipeable';

export enum Mode {
	Demo = '* DEMO *',
	Real = '* REAL *',
	DemoLive = '* DEMO_LIVE *',
	Regular = '* REGULAR *',
}

export type MenuViewModel = {
	menuMode$: Observable<Mode>;
	currentAccount$: Observable<AccountTO>;
	availableAccounts$: Observable<AccountTO[]>;
	proTraderAvaliable$: Observable<boolean>;
};

export type MenuViewModelContext = {
	accountViewModel: AccountViewModel;
	router: Router;
};

export type CreateMenuViewModel = () => MenuViewModel;

export const createMenuViewModel = combineContext(
	ask<MenuViewModelContext>(),
	(e): CreateMenuViewModel => {
		const { current$, availableAccounts$ } = e.accountViewModel;

		const menuMode$ = combineLatest(current$, availableAccounts$).pipe(
			map(([current, available]) => chooseMode(current, available)),
		);

		const proTraderAvaliable$ = of(e.router.state.location.search).pipe(
			map(search => Route.parse(search).query),
			pluck('is_protrader'),
			map(data =>
				pipe(
					fromNullable(data),
					O.exists(value => value === 'true'),
				),
			),
		);

		return () => ({
			menuMode$,
			currentAccount$: current$,
			availableAccounts$,
			proTraderAvaliable$,
		});
	},
);
