import { Option } from 'fp-ts/lib/Option';
import { string, boolean, type } from 'io-ts';
import { createOptionFromNullable } from 'io-ts-types';

export type WatchlistsTO = {
	id: string;
	name: string;
	description: Option<string>;
	futures: Option<boolean>;
	common: Option<boolean>;
};
export const WatchlistsTOIO = type({
	id: string,
	name: string,
	description: createOptionFromNullable(string),
	futures: createOptionFromNullable(boolean),
	common: createOptionFromNullable(boolean),
});
