import { ChartInstrumentTO, ChartInstrumentTOIO } from '../definitions/ChartInstrumentTO';
import { Option } from 'fp-ts/lib/Option';
import { string, union, literal, type } from 'io-ts';
import { createOptionFromNullable } from 'io-ts-types';

export type ChartRequestResultTO = {
	feedSubtopicId: Option<string>;
	instrument: Option<ChartInstrumentTO>;
	chartResultRequestError: Option<'ERROR_NONE' | 'ERROR_UNORDERED' | 'ERROR_NO_SYMBOL' | 'ERROR_INCORRECT_VENDOR'>;
	errorMessage: Option<string>;
};
export const ChartRequestResultTOIO = type({
	feedSubtopicId: createOptionFromNullable(string),
	instrument: createOptionFromNullable(ChartInstrumentTOIO),
	chartResultRequestError: createOptionFromNullable(
		union([
			literal('ERROR_NONE'),
			literal('ERROR_UNORDERED'),
			literal('ERROR_NO_SYMBOL'),
			literal('ERROR_INCORRECT_VENDOR'),
		]),
	),
	errorMessage: createOptionFromNullable(string),
});
