import { RemoteData, fromOption } from '@devexperts/remote-data-ts';
import { WatchlistEntity } from '../../../../store/watchlist/watchlist.store';
import * as O from 'fp-ts/lib/Option';
import { head } from 'fp-ts/lib/Array';
import { pipe } from 'fp-ts/lib/pipeable';

export const toCategoryId = (categoriesListRD: RemoteData<Error, WatchlistEntity[]>) =>
	categoriesListRD.chain(categoriesList =>
		fromOption(
			pipe(
				categoriesList,
				head,
				O.map(category => category.id),
			),
			() => new Error('There is no categories in list'),
		),
	);
