import { doOnMount } from '../../../../utils/react.utils';
import { ask, combineContext, deferContext } from '@devexperts/rx-utils/dist/context.utils';
import { createSocketClient } from '../../../../api/socket-client';
import { createApiClient } from '../../../../api/api-client';
import { AuthorizedContainer } from '../authorized/authorized.container';
import { createPostMessageClient } from '../../../../api/post-message.client';

export type ConnectionContext = {
	readonly restURL: string;
	readonly socketURL: string;
};

export const AppContainer = combineContext(
	deferContext(AuthorizedContainer, 'socketClient', 'apiClient', 'postMessageClient'),
	ask<ConnectionContext>(),
	(Connection, e) => {
		const socketClient = createSocketClient(e.socketURL);
		const apiClient = createApiClient(e.restURL, socketClient.uuid$);
		const postMessageClient = createPostMessageClient();
		return Connection.run({ socketClient, apiClient, postMessageClient }).map(Resolved =>
			doOnMount(Resolved, () => {
				socketClient.run();
			}),
		);
	},
);
