import { combineContext, deferContext, ask } from '@devexperts/rx-utils/dist/context.utils';
import { withRXWithDefaults } from '../../../../utils/withRXWithDefaults';
import { combineLatestRD } from '@devexperts/rx-utils/dist/rd/operators/combineLatestRD';
import { mapRD } from '@devexperts/rx-utils/dist/rd/operators/mapRD';
import { pending } from '@devexperts/remote-data-ts';
import { withProps } from '../../../../utils/with-props.utils';
import { runOnMount } from '../../../../utils/react.utils';
import { PositionDashboardChart } from '../../components/positions-dashboard/position-dashboard-chart.component';
import {
	PositionDashboardChartViewModel,
	createPositionDashboardChartViewModel,
} from '../../../../view-model/positions/position-dashboard-chart.view-model';
import { createSelector } from 'reselect';

type PositionDashboardChartContainerContext = {
	positionDashboardChartViewModel: PositionDashboardChartViewModel;
};

const Container = combineContext(
	PositionDashboardChart,
	ask<PositionDashboardChartContainerContext>(),
	(PositionDashboardChart, context) =>
		withProps<{ positionId: string }>()(
			withRXWithDefaults(PositionDashboardChart)(() => {
				const {
					positionDashboardChartViewModel: { symbol$, costBasis$ },
				} = context;

				const data$ = combineLatestRD(symbol$, costBasis$).pipe(
					mapRD(([symbol, costBasis]) => ({ symbol, costBasis })),
				);

				return {
					defaultProps: {
						data: pending,
					},
					props: {
						data: data$,
					},
				};
			}),
		),
);

export const PositionDashboardChartContainer = combineContext(
	deferContext(Container, 'positionDashboardChartViewModel'),
	createPositionDashboardChartViewModel,
	(Container, createPositionDashboardChartViewModel) =>
		runOnMount(Container, props => {
			const positionId = createSelector(
				props,
				props => props.positionId,
			);
			return createSelector(
				positionId,
				positionId => ({
					positionDashboardChartViewModel: createPositionDashboardChartViewModel(positionId),
				}),
			);
		}),
);
