import * as React from 'react';
import { PureComponent, ReactNode } from 'react';
import { withTheme } from '@devexperts/react-kit/dist/utils/withTheme';
import { PartialKeys } from '@devexperts/utils/dist/object';
import { MakeTheme } from '../../utils/theme.utils';
import * as css from './theme/info-panel.module.css';
import classnames from 'classnames';

type RawSymbolInfoItemProps = {
	theme: MakeTheme<'item' | 'item_secondary' | 'label' | 'value'>;
	label: ReactNode;
	isSecondary?: boolean;
};

class RawSymbolInfoItem extends PureComponent<RawSymbolInfoItemProps> {
	render() {
		const { theme, label, children, isSecondary = false } = this.props;

		return (
			<div
				className={classnames(theme.item, {
					[theme.item_secondary as string]: isSecondary,
				})}>
				<div className={theme.label}>{label}</div>
				{children && <div className={theme.value}>{children}</div>}
			</div>
		);
	}
}

const wTheme = {
	...css,
};

export type SymbolInfoItemProps = PartialKeys<RawSymbolInfoItemProps, 'theme'>;
export const SymbolInfoItem = withTheme(Symbol(), wTheme)(RawSymbolInfoItem);
