import * as React from 'react';
import { memo } from 'react';
import { WithTranslation, withTranslation } from 'react-i18next';
import { Popup } from '../../../ui-kit/components/popup/popup.component';
import { Button } from '../../../../ui-common/button/button.component';
import { tspan } from '../../../../utils/i18n.utils';
import { redirectToRelogin } from '../../../../utils/route.utils';
import * as css from './theme/relogin-popup.module.css';
import * as popupTheme from './theme/popup.child.module.css';
import * as buttonTheme from './theme/button.child.module.css';

type ReloginPopupProps = {
	isPopupOpened: boolean;
};

const ReloginPopupRaw = memo<ReloginPopupProps & WithTranslation>(props => {
	const { isPopupOpened } = props;

	return (
		<Popup
			onClose={redirectToRelogin}
			isOpened={isPopupOpened}
			isModal
			title={tspan('reloginPopupTitle', 'Session Expired')}
			theme={popupTheme}>
			<div className={css.content}>
				<p className={css.text}>{tspan('reloginPopupText', `Your session is expired. Please login again`)}</p>
				<div className={css.buttons}>
					<Button onClick={redirectToRelogin} isPrimary theme={buttonTheme}>
						{tspan('loginButtonText', 'Login')}
					</Button>
				</div>
			</div>
		</Popup>
	);
});

export const ReloginPopup = withTranslation()(ReloginPopupRaw);
