import { Option } from 'fp-ts/lib/Option';
import { string, number, type } from 'io-ts';
import { createOptionFromNullable } from 'io-ts-types';

export type FxPositionMetricsTO = {
	uid: string;
	accountId: string;
	margin: Option<number>;
	plOpen: Option<number>;
	swap: Option<number>;
	commission: Option<number>;
};
export const FxPositionMetricsTOIO = type({
	uid: string,
	accountId: string,
	margin: createOptionFromNullable(number),
	plOpen: createOptionFromNullable(number),
	swap: createOptionFromNullable(number),
	commission: createOptionFromNullable(number),
});
