export const stringifyOrderSideEnum = (side: HistoryOrderSide): StringifiedOrderSide => {
	switch (side) {
		case HistoryOrderSide.Buy:
			return 'Buy';
		case HistoryOrderSide.Sell:
			return 'Sell';
	}
};

