import * as React from 'react';
import { Component, ComponentClass, ReactNode } from 'react';
import * as classnames from 'classnames';
import { PURE } from '@devexperts/react-kit/dist/utils/pure';
import { Button } from '../../../../../ui-common/button/button.component';
import { withTheme } from '@devexperts/react-kit/dist/utils/withTheme';
import {
	TFullSelectboxAnchorProps,
	TSelectboxAnchorProps,
} from '@devexperts/react-kit/dist/components/Selectbox/SelectboxAnchor';
import { ChartType } from '../toolbar.model';
import { toType } from '../toolbar.utils';
import { ChartBarIcon } from '../../../../ui-kit/components/icons/chart-bar.icon';
import { ChartCandlesIcon } from '../../../../ui-kit/components/icons/chart-candles.icon';
import { ChartLineIcon } from '../../../../ui-kit/components/icons/chart-line.icon';

@PURE
class RawTypeAnchor extends Component<TFullSelectboxAnchorProps> {
	render() {
		const { theme, children, valueText, caretIcon, onClick, isOpened } = this.props;

		const buttonTheme = {
			container: classnames(theme.container, {
				[theme.container_isOpened as string]: isOpened,
			}),
		};

		return (
			<Button onClick={onClick} theme={buttonTheme}>
				<div className={theme.content}>
					<div className={theme.text}>{this.renderTypeIcon(valueText)}</div>
					<div className={theme.caret}>{caretIcon}</div>
				</div>
				{children}
			</Button>
		);
	}

	private renderTypeIcon = (valueText: ReactNode) => {
		if (valueText !== undefined && valueText !== null) {
			switch (toType(valueText[1])) {
				case ChartType.Bar:
					return <ChartBarIcon />;
				case ChartType.Candle:
					return <ChartCandlesIcon />;
				case ChartType.Line:
					return <ChartLineIcon />;
			}
		}
		return null;
	};
}

export const TypeAnchor: ComponentClass<TSelectboxAnchorProps> = withTheme(Symbol())(RawTypeAnchor);
