import { AvaAccountTO, AvaAccountTOIO } from '../definitions/AvaAccountTO';
import { Option } from 'fp-ts/lib/Option';
import { union, literal, string, array, boolean, type } from 'io-ts';
import { createOptionFromNullable } from 'io-ts-types';

export type AvaSystemSettingsTO = {
	platformType: 'DEV' | 'PROD';
	reloginPage: string;
	availableAccounts: Array<AvaAccountTO>;
	previewUser: Option<boolean>;
};
export const AvaSystemSettingsTOIO = type({
	platformType: union([literal('DEV'), literal('PROD')]),
	reloginPage: string,
	availableAccounts: array(AvaAccountTOIO),
	previewUser: createOptionFromNullable(boolean),
});
