import { StudyChartRequestTO, StudyChartRequestTOIO } from '../definitions/StudyChartRequestTO';
import { Option } from 'fp-ts/lib/Option';
import { string, number, array, boolean, type } from 'io-ts';
import { createOptionFromNullable } from 'io-ts-types';

export type ChartRequestTO = {
	symbol: string;
	aggregationPeriodSeconds: number;
	range: number;
	forexPriceField: Option<string>;
	studySubscription: Option<Array<StudyChartRequestTO>>;
	subtopic: string;
	maxBarsCount: number;
	extendedSession: boolean;
	id: number;
};
export const ChartRequestTOIO = type({
	symbol: string,
	aggregationPeriodSeconds: number,
	range: number,
	forexPriceField: createOptionFromNullable(string),
	studySubscription: createOptionFromNullable(array(StudyChartRequestTOIO)),
	subtopic: string,
	maxBarsCount: number,
	extendedSession: boolean,
	id: number,
});
