import { pending } from '@devexperts/remote-data-ts';
import { withRXWithDefaults } from '../../../../utils/withRXWithDefaults';
import { ask, combineContext, deferContext } from '@devexperts/rx-utils/dist/context.utils';
import { runOnMount } from '../../../../utils/react.utils';
import {
	PositionCloseViewModel,
	createPositionCloseViewModel,
} from '../../../../view-model/positions/position-close.view-model';
import { PositionClose } from '../../components/position-close/position-close.component';
import { createSelector } from 'reselect';
import { AccountViewModel } from '../../../../view-model/account/account.view-model';
import { first, map } from 'rxjs/operators';
import { currencyStringToSymbol } from '../../../../utils/currency.utils';
import * as O from 'fp-ts/lib/Option';
import { pipe } from 'fp-ts/lib/pipeable';

export type PositionCloseContainerContext = {
	positionCloseViewModel: PositionCloseViewModel;
	accountViewModel: AccountViewModel;
};

export const Container = combineContext(PositionClose, ask<PositionCloseContainerContext>(), (PositionClose, e) =>
	withRXWithDefaults(PositionClose)(() => {
		const {
			positionCloseViewModel: {
				value$,
				profit$,
				instrumentData$,
				isReadyToClose$,
				onAmountChange,
				onSend,
				error$,
			},
			accountViewModel: { current$ },
		} = e;
		return {
			props: {
				value: value$,
				accountCurrency: current$.pipe(
					first(),
					map(value => currencyStringToSymbol(value.baseCurrency)),
				),
				instrumentData: instrumentData$,
				profitLossValue: profit$,
				isClosingPositionAllowed: isReadyToClose$,
				error: error$.pipe(
					map(error =>
						pipe(
							error,
							O.map(error => error.message),
						),
					),
				),
			},
			defaultProps: {
				value: pending,
				accountCurrency: '',
				instrumentData: pending,
				profitLossValue: pending,
				isClosingPositionAllowed: false,
				onAmountChange: value => onAmountChange(value),
				onPositionClose: onSend,
				error: O.none,
			},
		};
	}),
);

export const PositionCloseContainer = combineContext(
	deferContext(Container, 'positionCloseViewModel'),
	createPositionCloseViewModel,
	(Container, createPositionCloseViewModel) =>
		runOnMount(Container, props => {
			const uid = createSelector(
				props,
				props => props.uid,
			);
			return createSelector(
				uid,
				uid =>
					createPositionCloseViewModel(uid).map(positionCloseViewModel => ({
						positionCloseViewModel,
					})),
			);
		}),
);
