import * as t from 'io-ts';
import * as O from 'fp-ts/lib/Option';

export const REDIRECT_TO_TRADE = 'REDIRECT_TO_TRADE';
export const REDIRECT_TO_POSITIONS = 'REDIRECT_TO_POSITIONS';
export const REDIRECT_TO_ORDERS = 'REDIRECT_TO_ORDERS';
export const REDIRECT_TO_HISTORY = 'REDIRECT_TO_HISTORY';
export const REDIRECT_TO_CRM_DEPOSIT = 'REDIRECT_TO_CRM_DEPOSIT';
export const REDIRECT_TO_CRM_WITHDRAW = 'REDIRECT_TO_CRM_WITHDRAW';
export const REDIRECT_TO_CRM_ACCOUNTS = 'REDIRECT_TO_CRM_ACCOUNTS';
export const REDIRECT_TO_CRM_UPLOADDOCS = 'REDIRECT_TO_CRM_UPLOADDOCS';
export const REDIRECT_TO_CRM_PERSONAL = 'REDIRECT_TO_CRM_PERSONAL';
export const UPGRADE_ACCOUNT = 'UPGRADE_ACCOUNT';
export const CLOSE_POPUP = 'CLOSE';

type RedirectToTrade = {
	type: typeof REDIRECT_TO_TRADE;
	payload: Record<string, unknown> | undefined;
};
type RedirectToTradeWithSymbol = {
	type: typeof REDIRECT_TO_TRADE;
	payload: {
		symbol: string;
		action?: string;
	};
};
type RedirectToPositions = {
	type: typeof REDIRECT_TO_POSITIONS;
};
type RedirectToOrders = {
	type: typeof REDIRECT_TO_ORDERS;
};
type RedirectToHistory = {
	type: typeof REDIRECT_TO_HISTORY;
};
type RedirectToCrmDeposit = {
	type: typeof REDIRECT_TO_CRM_DEPOSIT;
};
type RedirectToCrmWithdraw = {
	type: typeof REDIRECT_TO_CRM_WITHDRAW;
};
type RedirectToCrmAccounts = {
	type: typeof REDIRECT_TO_CRM_ACCOUNTS;
};
type RedirectToCrmUploaddocs = {
	type: typeof REDIRECT_TO_CRM_UPLOADDOCS;
};
type RedirectToCrmPersonal = {
	type: typeof REDIRECT_TO_CRM_PERSONAL;
};
export type RedirectToRealAccount = {
	type: typeof UPGRADE_ACCOUNT;
	payload: {
		url: string;
	};
};
export type ClosePopup = {
	type: typeof CLOSE_POPUP;
};

const redirectToTradePostMessageTO: t.Tagged<'type', RedirectToTrade, unknown> = t.type({
	type: t.literal(REDIRECT_TO_TRADE),
	payload: t.union([t.UnknownRecord, t.undefined]),
});
const redirectToTradeWithSymbolPostMessageTO: t.Tagged<'type', RedirectToTradeWithSymbol, unknown> = t.type({
	type: t.literal(REDIRECT_TO_TRADE),
	payload: t.union([
		t.type({
			symbol: t.string,
			action: t.string,
		}),
		t.type({
			symbol: t.string,
		}),
	]),
});
const redirectToRealAccountPostMessageTO: t.Tagged<'type', RedirectToRealAccount, unknown> = t.type({
	type: t.literal(UPGRADE_ACCOUNT),
	payload: t.type({
		url: t.string,
	}),
});
const redirectToPositionsPostMessageTO: t.Tagged<'type', RedirectToPositions, unknown> = t.type({
	type: t.literal(REDIRECT_TO_POSITIONS),
});
const redirectToOrdersPostMessageTO: t.Tagged<'type', RedirectToOrders, unknown> = t.type({
	type: t.literal(REDIRECT_TO_ORDERS),
});
const redirectToHistoryPostMessageTO: t.Tagged<'type', RedirectToHistory, unknown> = t.type({
	type: t.literal(REDIRECT_TO_HISTORY),
});
const redirectToCrmDepositPostMessageTO: t.Tagged<'type', RedirectToCrmDeposit, unknown> = t.type({
	type: t.literal(REDIRECT_TO_CRM_DEPOSIT),
});
const redirectToCrmWithdrawPostMessageTO: t.Tagged<'type', RedirectToCrmWithdraw, unknown> = t.type({
	type: t.literal(REDIRECT_TO_CRM_WITHDRAW),
});
const redirectToCrmAccountsPostMessageTO: t.Tagged<'type', RedirectToCrmAccounts, unknown> = t.type({
	type: t.literal(REDIRECT_TO_CRM_ACCOUNTS),
});
const redirectToCrmUploaddocsPostMessageTO: t.Tagged<'type', RedirectToCrmUploaddocs, unknown> = t.type({
	type: t.literal(REDIRECT_TO_CRM_UPLOADDOCS),
});
const redirectToCrmPersonalPostMessageTO: t.Tagged<'type', RedirectToCrmPersonal, unknown> = t.type({
	type: t.literal(REDIRECT_TO_CRM_PERSONAL),
});
const closePopupMessageTO: t.Tagged<'type', ClosePopup, unknown> = t.type({
	type: t.literal(CLOSE_POPUP),
});

export type PostMessageTO =
	| RedirectToTrade
	| RedirectToTradeWithSymbol
	| RedirectToPositions
	| RedirectToOrders
	| RedirectToHistory
	| RedirectToCrmDeposit
	| RedirectToCrmWithdraw
	| RedirectToCrmAccounts
	| RedirectToCrmUploaddocs
	| RedirectToCrmPersonal
	| RedirectToRealAccount
	| ClosePopup;

export type LocalRedirectPostMessageTO = Exclude<PostMessageTO, ClosePopup | RedirectToRealAccount>;

export const postMessagesTO: t.Tagged<'type', PostMessageTO, unknown> = t.taggedUnion('type', [
	t.union([redirectToTradePostMessageTO, redirectToTradeWithSymbolPostMessageTO]),
	redirectToPositionsPostMessageTO,
	redirectToOrdersPostMessageTO,
	redirectToHistoryPostMessageTO,
	redirectToCrmDepositPostMessageTO,
	redirectToCrmWithdrawPostMessageTO,
	redirectToCrmAccountsPostMessageTO,
	redirectToCrmUploaddocsPostMessageTO,
	redirectToCrmPersonalPostMessageTO,
	redirectToRealAccountPostMessageTO,
	closePopupMessageTO,
]);

export const isRedirectToTradeWithSymbol = (to: PostMessageTO): to is RedirectToTradeWithSymbol =>
	to.type === REDIRECT_TO_TRADE && O.isSome(O.fromNullable(to.payload && to.payload.symbol));

export const isRedirectToRealAccount = (to: PostMessageTO): to is RedirectToRealAccount => to.type === UPGRADE_ACCOUNT;

export const isClosePopup = (to: PostMessageTO): to is ClosePopup => to.type === CLOSE_POPUP;

export const isRedirectToCRMDeposit = (to: PostMessageTO): to is RedirectToCrmDeposit =>
	to.type === REDIRECT_TO_CRM_DEPOSIT;
