import { ask, combineContext, deferContext } from '@devexperts/rx-utils/dist/context.utils';
import { withRXWithDefaults } from '../../../../utils/withRXWithDefaults';
import { Header } from '../../components/header/header/header.component';
import { HeaderViewModel, createHeaderViewModel } from '../../../../view-model/header/header.view-model';
import { runOnMount } from '../../../../utils/react.utils';
import { AccountViewModel } from '../../../../view-model/account/account.view-model';
import { first, map } from 'rxjs/operators';
import * as O from 'fp-ts/lib/Option';
import { SystemSettingsViewModel } from '../../../../view-model/system-settings/system-settings.view-model';

export type HeaderContainerContext = {
	headerViewModel: HeaderViewModel;
	accountViewModel: AccountViewModel;
	systemSettingsViewModel: SystemSettingsViewModel;
};

const Container = combineContext(Header, ask<HeaderContainerContext>(), (Header, e) =>
	withRXWithDefaults(Header)(() => {
		const { headerViewModel, accountViewModel } = e;

		return {
			defaultProps: {
				showDemoButton: false,
				showDepositButton: false,
				isRealAccount: O.none,
				previewMode: O.some(false),
			},
			props: {
				showDemoButton: headerViewModel.showDemoButton$,
				showDepositButton: headerViewModel.showDepositButton$,
				isRealAccount: accountViewModel.current$.pipe(
					first(),
					map(account => O.some(!account.paperMoney)),
				),
			},
		};
	}),
);

export const HeaderContainer = combineContext(
	deferContext(Container, 'headerViewModel'),
	createHeaderViewModel,
	(Container, createHeaderViewModel) =>
		runOnMount(Container, () => () => ({
			headerViewModel: createHeaderViewModel(),
		})),
);
