import { Sink } from '@devexperts/rx-utils/dist/sink.utils';
import { LiveData } from '@devexperts/rx-utils/dist/rd/live-data.utils';
import { SocketClient } from '../../api/socket-client';
import { ask, combineContext, fromReader } from '@devexperts/rx-utils/dist/context.utils';
import { ConversionRateTO } from '../../api/generated/swagger/definitions/ConversionRateTO';
import { Option } from 'fp-ts/lib/Option';
import * as O from 'fp-ts/lib/Option';
import { combineLatest, merge, Observable, of, Subscriber } from 'rxjs';
import { EntityStore } from '@devexperts/rx-utils/dist/rd/entity-store.utils';
import { distinctUntilChanged, filter, map, mergeMap, scan } from 'rxjs/operators';
import { isConversionRateTO } from '../../api/socket/socket-message.to';
import { createHandler } from '@devexperts/rx-utils/dist/create-handler.utils';
import { mapRD } from '@devexperts/rx-utils/dist/rd/operators/mapRD';
import { constUndefined } from 'fp-ts/lib/function';
import { marketDataController } from '../../api/generated/swagger/controllers/MarketDataController';
import { filterMap } from '../../utils/rxjs/filtermap';
import { getStructEq, Eq, eqString } from 'fp-ts/lib/Eq';
import { findFirst } from 'fp-ts/lib/Array';
import { RemoteData, success } from '@devexperts/remote-data-ts';
import { setoidStrict } from '../../utils/setoid.utils';
import { pipe } from 'fp-ts/lib/pipeable';

export type ConversionRateEntity = {
	symbol: string;
	conversionRate: Option<ConversionRateTO>;
};
const setoidConversionRateEntity: Eq<ConversionRateEntity> = getStructEq({
	symbol: eqString,
	conversionRate: O.getEq(setoidStrict),
});
export type ConversionRateStore = {
	readonly getRateBySymbol: (from: string, to: string) => LiveData<Error, ConversionRateEntity>;
};

export type ConversionRateStoreContext = {
	socketClient: SocketClient;
};
export type CreateConversionRateStore = () => Sink<ConversionRateStore>;

export const createConversionRateStore = combineContext(
	fromReader(marketDataController),
	ask<ConversionRateStoreContext>(),
	(marketDataController, e): CreateConversionRateStore => {
		const { socketClient } = e;
		const pk = (entity: ConversionRateEntity) => entity.symbol;

		const conversionRates$ = socketClient.message$.pipe(filter(isConversionRateTO));

		return () => {
			const store = new EntityStore<Error, ConversionRateEntity>();

			const onSymbolRemove = createHandler<string>();
			const removeSymbolEffect$ = onSymbolRemove.value$.pipe(
				mergeMap(symbol =>
					store
						.remove(
							symbol,
							pk,
							() =>
								marketDataController
									.changeConversionRateSubscription({
										body: {
											symbolsToAdd: [],
											symbolsToRemove: [symbol],
										},
									})
									.pipe(mapRD(() => [])),
							true,
						)
						.pipe(mapRD(constUndefined)),
				),
			);

			const getRateBySymbol = (from: string, to: string): LiveData<Error, ConversionRateEntity> => {
				const symbol = `${from}/${to}`;
				if (from === to) {
					return of(
						success({
							symbol,
							conversionRate: O.some({
								quantity: O.some(1),
								from: O.some(from),
								to: O.some(to),
								value: O.some(1),
							}),
						}),
					);
				}
				return store.get(symbol, () => {
					/**
					 * Current subscriptions counter
					 * @mutable
					 */
					let count = 0;
					const request$ = marketDataController.changeConversionRateSubscription({
						body: {
							symbolsToAdd: [symbol],
							symbolsToRemove: [],
						},
					});

					const rate$: Observable<ConversionRateTO> = conversionRates$.pipe(
						filterMap(rates =>
							findFirst(rates.body, rate =>
								O.getEq(eqString).equals(
									pipe(
										rate.from,
										O.chain(from =>
											pipe(
												rate.to,
												O.map(to => `${from}/${to}`),
											),
										),
									),
									O.some(symbol),
								),
							),
						),
					);

					const entity$: Observable<ConversionRateEntity> = rate$.pipe(
						scan<ConversionRateTO, ConversionRateEntity>(
							(acc, rate) => {
								return {
									...acc,
									conversionRate: O.some(rate),
								};
							},
							{
								symbol,
								conversionRate: O.none,
							},
						),
						distinctUntilChanged(setoidConversionRateEntity.equals),
					);

					const result$ = combineLatest(request$, entity$).pipe(
						map(([request, entity]) => request.map(() => entity)),
					);
					return Observable.create((subscriber: Subscriber<RemoteData<Error, ConversionRateEntity>>) => {
						count++;
						const subscription = result$.subscribe(subscriber);
						return () => {
							count--;
							subscription.unsubscribe();
							if (count === 0) {
								onSymbolRemove.handle(symbol);
							}
						};
					});
				});
			};

			const effects$ = merge(removeSymbolEffect$);

			return new Sink(
				{
					getRateBySymbol,
				},
				effects$,
			);
		};
	},
);
