import * as React from 'react';
import { PureComponent } from 'react';
import { Popup } from '../../../ui-kit/components/popup/popup.component';
import { Iframe } from '../../../../ui-common/iframe/iframe.component';
import { POPUP_CONTENT_URL } from '../../../../ui-common/routes';
import * as css from './theme/welcome-popup.module.css';

type WelcomePopupProps = {
	isPopupOpened: boolean;
	onClose: () => void;
};

export class WelcomePopup extends PureComponent<WelcomePopupProps> {
	render() {
		const { isPopupOpened, onClose } = this.props;

		return (
			<Popup onClose={onClose} isOpened={isPopupOpened} isModal>
				<div className={css.iframe}>
					<Iframe src={POPUP_CONTENT_URL} scrolling={'no'} />
				</div>
			</Popup>
		);
	}
}
