import { pending } from '@devexperts/remote-data-ts';
import { SymbolSearch } from '../../components/symbol-search/symbol-search.component';
import { withRXWithDefaults } from '../../../../utils/withRXWithDefaults';
import { mapRD } from '@devexperts/rx-utils/dist/rd/operators/mapRD';
import { Option } from 'fp-ts/lib/Option';
import * as O from 'fp-ts/lib/Option';
import { ask, combineContext, deferContext } from '@devexperts/rx-utils/dist/context.utils';
import {
	createSymbolSearchViewModel,
	SymbolSearchViewModel,
} from '../../../../view-model/symbol-search/symbol-search.view-model';
import { runOnMount } from '../../../../utils/react.utils';
import { toSuggestGroup } from './symbol-search.model';
import { map } from 'rxjs/operators';
import { Suggest } from '../../components/symbol-search/symbol-search.model';
import { pipe } from 'fp-ts/lib/pipeable';

type TSymbolSearchContainerContext = {
	symbolSearchViewModel: SymbolSearchViewModel;
};

export const Container = combineContext(ask<TSymbolSearchContainerContext>(), context =>
	withRXWithDefaults(SymbolSearch)(() => {
		const { symbolSearchViewModel } = context;

		//#region props
		const suggest$ = symbolSearchViewModel.suggest$.pipe(mapRD(data => data.map(toSuggestGroup)));
		const selectedId$ = symbolSearchViewModel.selectedSuggestId$.pipe(
			map(id =>
				pipe(
					id,
					O.map(id => id.toString()),
				),
			),
		);
		//#endregion

		//#region handlers
		const onInputBlur = () => symbolSearchViewModel.onSelectSuggest(O.none);
		const onSelect = (selectedSuggest: Option<Suggest>) =>
			symbolSearchViewModel.onSelectSuggest(
				pipe(
					selectedSuggest,
					O.map(selectedSuggest => +selectedSuggest.id),
					O.filter(id => !isNaN(id)),
				),
			);
		const onEnter = (selectedSuggest: Option<Suggest>) =>
			O.isSome(selectedSuggest) &&
			symbolSearchViewModel.onSearch(selectedSuggest.value.symbol, selectedSuggest.value.category);
		//#endregion

		return {
			defaultProps: {
				data: pending,
				isOpened: false,
				onCloseRequest: symbolSearchViewModel.onClose,
				onEnter,
				onInputBlur,
				onSelect,
				onValueChange: symbolSearchViewModel.onInputValueChange,
				selectedId: O.none,
				value: '',
			},
			props: {
				data: suggest$,
				isOpened: symbolSearchViewModel.isOpened$,
				selectedId: selectedId$,
				value: symbolSearchViewModel.inputValue$,
			},
		};
	}),
);

export const SymbolSearchContainer = combineContext(
	deferContext(Container, 'symbolSearchViewModel'),
	createSymbolSearchViewModel,
	(Container, createSymbolSearchViewModel) =>
		runOnMount(Container, () => () => ({
			symbolSearchViewModel: createSymbolSearchViewModel(),
		})),
);
