import { ResponseValidationError, TAPIClient } from '../client/client';
import { AvaTradingWeekTO, AvaTradingWeekTOIO } from '../definitions/AvaTradingWeekTO';
import { fromEither } from '@devexperts/remote-data-ts';
import { LiveData } from '@devexperts/rx-utils/dist/rd/live-data.utils';
import { asks } from 'fp-ts/lib/Reader';
import { string, number, type, partial } from 'io-ts';
import { map } from 'rxjs/operators';

export type TradinghoursendpointController = {
	/**
	 * Get instrument's trading sessions hourw for whole week
	 * @param { object } parameters
	 */
	readonly getWeekTradingHours: (parameters: {
		query: { symbol: string; timezoneOffset: number };
	}) => LiveData<Error, AvaTradingWeekTO>;
};

export const tradinghoursendpointController = asks(
	(e: { apiClient: TAPIClient }): TradinghoursendpointController => ({
		getWeekTradingHours: parameters => {
			const encoded = partial({ query: type({ symbol: string, timezoneOffset: number }) }).encode(parameters);

			return e.apiClient
				.request({
					url: `/instruments/tradinghours`,
					method: 'GET',
					query: encoded.query,
				})
				.pipe(
					map(data =>
						data.chain(value =>
							fromEither(AvaTradingWeekTOIO.decode(value).mapLeft(ResponseValidationError.create)),
						),
					),
				);
		},
	}),
);
