import { Match } from 'fp-ts-routing';
import { PureComponent, ReactNode } from 'react';
import { exact, parse } from '../../../../utils/router.utils';
import { pipe } from 'fp-ts/lib/pipeable';
import * as O from 'fp-ts/lib/Option';

export type RouteComponentProps<A extends object> = {
	match: Match<A>;
	render: (a: A) => ReactNode;
	exact?: true;
	path: string;
};

export class RouteComponent<A extends object> extends PureComponent<RouteComponentProps<A>> {
	render() {
		const { render, path } = this.props;
		const match = Boolean(this.props.exact) ? exact(this.props.match) : this.props.match;
		return pipe(
			parse(match, path),
			O.map(([a, _]) => render(a)),
			O.toNullable,
		);
	}
}
