import { LiveData } from '@devexperts/rx-utils/dist/rd/live-data.utils';
import { BehaviorSubject, Observable } from 'rxjs';
import { PathReporter } from 'io-ts/lib/PathReporter';
import { socketMessageTO, SocketMessageTO } from './socket/socket-message.to';
import { fromOption, pending, RemoteData } from '@devexperts/remote-data-ts';
import * as atmosphere from 'atmosphere.js';
import * as O from 'fp-ts/lib/Option';
import * as E from 'fp-ts/lib/Either';
import { createHandler } from '@devexperts/rx-utils/dist/create-handler.utils';
import { pipe } from 'fp-ts/lib/pipeable';

export type ConnectionStatus = 'TIMEDOUT' | 'ERROR' | 'WORKING';

export type SocketClient = {
	readonly uuid$: LiveData<Error, string>;
	readonly message$: Observable<SocketMessageTO>;
	readonly connectionStatus$: Observable<ConnectionStatus>;
};

export type IdleSocketClient = SocketClient & {
	readonly run: () => void;
};

const MAX_STREAM_SIZE = 2000000;
const RECONNECT_INTERVAL = 300;
const SESSION_STATE_RECOVER = 'dx-recover';
const SESSION_STATE_NEW = 'dx-new';

const subscribe = atmosphere.subscribe as (request?: Atmosphere.Request) => Atmosphere.Request;

export const createSocketClient = (socketURL: string): IdleSocketClient => {
	const uuid$ = new BehaviorSubject<RemoteData<Error, string>>(pending);
	const onMessage = createHandler<SocketMessageTO>();
	const onConnectionStatusChange = createHandler<ConnectionStatus>();

	/**
	 * @mutable
	 */
	const request: Atmosphere.Request = {
		url: socketURL,
		transport: 'websocket',
		fallbackTransport: 'streaming',
		withCredentials: true,
		enableProtocol: true,
		enableXDR: true,
		rewriteURL: true,
		trackMessageLength: true,
		maxStreamingLength: MAX_STREAM_SIZE,
		reconnectInterval: RECONNECT_INTERVAL,
		attachHeadersAsQueryString: true,
		headers: {
			sessionState: SESSION_STATE_NEW,
		},
		onMessage(message) {
			if (message && message.responseBody) {
				try {
					const parsed = JSON.parse(message.responseBody);
					const decoded = socketMessageTO.decode(parsed);
					const errorReport = PathReporter.report(decoded);
					if (E.isRight(decoded)) {
						if (errorReport[0] !== 'No errors!') {
							console.error('VALIDATION TOIO HAS ERRORS', parsed, errorReport);
						}
						onMessage.handle(decoded.value);
					}
				} catch (e) {}
			}
			onConnectionStatusChange.handle('WORKING');
		},
		onOpen(response) {
			//overwrite
			request.headers.sessionState = SESSION_STATE_RECOVER;
			uuid$.next(
				fromOption(
					pipe(
						response,
						O.fromNullable,
						O.mapNullable(response => response.request),
						O.mapNullable(request => request.uuid),
					),
					() => new Error('Cannot read UUID from Atmosphere event'),
				),
			);
		},
		onClientTimeout() {
			onConnectionStatusChange.handle('TIMEDOUT');
		},
		onError() {
			onConnectionStatusChange.handle('ERROR');
		},
	};

	return {
		uuid$,
		message$: onMessage.value$,
		connectionStatus$: onConnectionStatusChange.value$,
		run() {
			subscribe(request);
		},
	};
};
