import { merge, Observable } from 'rxjs';
import { filter, map, distinctUntilChanged, startWith } from 'rxjs/operators';
import { Option } from 'fp-ts/lib/Option';
import { isAccountMetricsTO, isAccountsTO, isLogoutEventTO, isUserInfoTO } from '../../api/socket/socket-message.to';
import { SocketClient } from '../../api/socket-client';
import { AccountMetricsTO } from '../../api/generated/swagger/definitions/AccountMetricsTO';
import { AccountTO } from '../../api/socket/to/accounts-message.to';
import { UserTO } from '../../api/generated/swagger/definitions/UserTO';
import { LogoutEventTO } from '../../api/generated/swagger/definitions/LogoutEventTO';
import { Sink } from '@devexperts/rx-utils/dist/sink.utils';
import { ask, combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { createHandler } from '@devexperts/rx-utils/dist/create-handler.utils';
import { setoidOptionNumber } from '../../utils/setoid.utils';
import { shareReplayWithDefaults } from '../../utils/rxjs/shareReplayWithDefaults';

export type AccountViewModel = {
	accountMetrics$: Observable<AccountMetricsTO>;
	current$: Observable<AccountTO>;
	availableAccounts$: Observable<AccountTO[]>;
	accountInfo$: Observable<UserTO>;
	equity$: Observable<Option<number>>;
	preloader$: Observable<boolean>;
	onPreloaderChange: (state: boolean) => void;
	logoutEvent$: Observable<LogoutEventTO>;
};

export type AccountViewModelContext = {
	socketClient: SocketClient;
};

export type CreateAccountViewModel = () => Sink<AccountViewModel>;

export const createAccountViewModel = combineContext(
	ask<AccountViewModelContext>(),
	(e): CreateAccountViewModel => {
		const accountMetrics$ = e.socketClient.message$.pipe(
			filter(isAccountMetricsTO),
			map(accountMetrics => accountMetrics.body),
			shareReplayWithDefaults(),
		);
		const current$ = e.socketClient.message$.pipe(
			filter(isAccountsTO),
			map(accounts => accounts.body.currentAccount),
			shareReplayWithDefaults(),
		);
		const availableAccounts$ = e.socketClient.message$.pipe(
			filter(isAccountsTO),
			map(accounts => accounts.body.availableAccounts),
			shareReplayWithDefaults(),
		);
		const accountInfo$ = e.socketClient.message$.pipe(
			filter(isUserInfoTO),
			map(data => data.body.user),
			shareReplayWithDefaults(),
		);
		const equity$ = accountMetrics$.pipe(
			map(accountMetrics => accountMetrics.equity),
			distinctUntilChanged(setoidOptionNumber.equals),
			shareReplayWithDefaults(),
		);
		const preloader = createHandler<boolean>();
		const onPreloaderChange = (state: boolean) => preloader.handle(state);

		const preloader$ = preloader.value$.pipe(
			startWith(false),
			shareReplayWithDefaults(),
		);

		const logoutEvent$ = e.socketClient.message$.pipe(
			filter(isLogoutEventTO),
			map(data => data.body),
		);

		const effects$ = merge(current$, availableAccounts$, accountMetrics$, accountInfo$, preloader$);

		return () =>
			new Sink(
				{
					current$,
					availableAccounts$,
					accountMetrics$,
					accountInfo$,
					equity$,
					preloader$,
					onPreloaderChange,
					logoutEvent$,
				},
				effects$,
			);
	},
);
