import { combineContext, ask } from '@devexperts/rx-utils/dist/context.utils';
import { withRXWithDefaults } from '../../../../utils/withRXWithDefaults';
import { OrderEntryTakeProfitStopLoss } from '../../components/order-entry-takeprofit-stoploss/order-entry-takeprofit-stoploss.component';
import { pending } from '@devexperts/remote-data-ts';
import { OrderEntryViewModel } from '../../../../view-model/order-entry/order-entry.view-model';
import { mapRD } from '@devexperts/rx-utils/dist/rd/operators/mapRD';
import { toOrderEntryTakeProfitStopLossComponentData } from './order-entry-takeprofit-stoploss.utils';

export type OrderEntryTakeProfitStopLossContext = {
	orderEntryViewModel: OrderEntryViewModel;
};

export const OrderEntryTakeProfitStopLossContainer = combineContext(ask<OrderEntryTakeProfitStopLossContext>(), e => {
	const { takeProfitStopLossValue$, onTakeProfitStopLossValueChange, currencySymbol$ } = e.orderEntryViewModel;

	return withRXWithDefaults(OrderEntryTakeProfitStopLoss)(() => ({
		defaultProps: {
			data: pending,
			onValueChange: onTakeProfitStopLossValueChange,
			currencySymbol: '',
		},
		props: {
			data: takeProfitStopLossValue$.pipe(mapRD(value => toOrderEntryTakeProfitStopLossComponentData(value))),
			currencySymbol: currencySymbol$,
		},
	}));
});
