import * as React from 'react';
import { memo, useCallback } from 'react';
import { withTranslation, WithTranslation } from 'react-i18next';
import { Option } from 'fp-ts/lib/Option';
import * as O from 'fp-ts/lib/Option';
import { empty } from 'fp-ts/lib/Record';
import * as classNames from 'classnames';
import { ButtonCta } from '../../button/button-cta.component';
import { EmptyScreenIcon } from '../../icons/empty-screen.icon';
import { EmptyScreenFundIcon } from '../../icons/empty-screen-fund.icon';
import { EmptyScreenSearchIcon } from '../../icons/empty-screen-search.icon';
import { withTheme } from '@devexperts/react-kit/dist/utils/withTheme';
import { MakeTheme } from '../../../utils/theme.utils';
import { combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { RouteLink } from '../../route-link/containers/route-link.container';
import { routes } from '../../routes';
import { tspan } from '../../../utils/i18n.utils';
import { pipe } from 'fp-ts/lib/pipeable';
import { RegistrationButton } from '../../../ui-desktop/ui-kit/components/preview-mode/components/registration-button.component';

import * as css from './theme/empty-screen.module.css';
import * as buttonCss from './theme/button.child.module.css';

export enum ScreenType {
	Positions = '* POSITIONS *',
	Orders = '* ORDERS *',
	History = '* HISTORY *',
	HistorySearch = '* HISTORY_SEARCH *',
	Deposit = '* DEPOSIT *',
}

export type EmptyScreenProps = {
	theme: MakeTheme<
		'container' | 'content' | 'iconContainer' | 'textWrapper' | 'title' | 'subtitle' | 'title_preview'
	>;
	screenType: ScreenType;
	isZeroEquity: Option<boolean>;
	isCurrentDemo: boolean;
	previewMode: Option<boolean>;
} & WithTranslation;

const EmptyScreenRaw = combineContext(RouteLink, RouteLink =>
	memo<EmptyScreenProps>(props => {
		const { isCurrentDemo, screenType, isZeroEquity, t, theme, previewMode } = props;

		const renderButton = useCallback(
			(previewMode: boolean) => {
				const toTrade =
					!pipe(
						isZeroEquity,
						O.getOrElse(() => false),
					) || isCurrentDemo;
				const buttonTheme = {
					container: classNames(buttonCss.container, {
						[buttonCss.container_trade]: toTrade,
					}),
				};

				return previewMode ? (
					<RegistrationButton />
				) : (
					<RouteLink
						to={toTrade ? routes.trade.root : routes.crm.deposit.root}
						value={empty}
						render={({ onNavigate }) => {
							return (
								<ButtonCta theme={buttonTheme} onClick={onNavigate}>
									{toTrade
										? tspan('toTrade', 'Back to Trade')
										: tspan('fundAccount', 'Fund your Account')}
								</ButtonCta>
							);
						}}
					/>
				);
			},
			[isCurrentDemo, isZeroEquity, t],
		);

		const renderIconByScreenType = useCallback(
			(screenType: ScreenType) => {
				switch (screenType) {
					case ScreenType.Positions:
						return pipe(
							isZeroEquity,
							O.map(isZero => (isZero ? <EmptyScreenFundIcon /> : <EmptyScreenIcon />)),
							O.toNullable,
						);
					case ScreenType.Orders:
						return pipe(
							isZeroEquity,
							O.map(isZero => (isZero ? <EmptyScreenFundIcon /> : <EmptyScreenIcon />)),
							O.toNullable,
						);
					case ScreenType.History:
						return <EmptyScreenIcon />;
					case ScreenType.HistorySearch:
						return <EmptyScreenSearchIcon />;
					case ScreenType.Deposit:
						return <EmptyScreenFundIcon />;
				}
			},
			[isZeroEquity],
		);

		const getTitleByScreenType = useCallback(
			(screenType: ScreenType, previewMode: boolean) => {
				if (previewMode) {
					switch (screenType) {
						case ScreenType.Positions:
							return tspan(
								'continuerRegistrationPagePositionsText',
								'Continue registration in order to view positions',
							);
						case ScreenType.Orders:
							return tspan(
								'continuerRegistrationPageOrdersText',
								'Continue registration in order to view positions orders',
							);
						case ScreenType.History:
							return tspan(
								'continuerRegistrationPageHistoryText',
								'Continue registration in order to view positions history',
							);
						case ScreenType.Deposit:
							return tspan(
								'continuerRegistrationPageDepositText',
								'Continue registration in order to deposit your funds',
							);
					}
				}

				switch (screenType) {
					case ScreenType.Positions:
						return tspan('noPositions', 'You have no open positions');
					case ScreenType.Orders:
						return tspan('noOrders', 'You have no pending orders');
					case ScreenType.History:
						return tspan('noHistory', 'You did not open any trade yet');
					case ScreenType.HistorySearch:
						return tspan('noHistorySearch', 'No trades matching your request');
					default:
						return null;
				}
			},
			[t],
		);

		const getSubTitleByScreenType = useCallback(
			(screenType: ScreenType) => {
				switch (screenType) {
					case ScreenType.Orders:
					case ScreenType.History:
					case ScreenType.Positions:
						return pipe(
							isZeroEquity,
							O.exists(isZero => isZero === true),
						)
							? tspan('fundToStartTrade', 'Fund your account to start trading')
							: null;
					default:
						return null;
				}
			},
			[t, isZeroEquity],
		);

		return pipe(
			previewMode,
			O.map(previewMode => (
				<section className={theme.container}>
					<div className={theme.content}>
						<div className={theme.iconContainer}>{renderIconByScreenType(screenType)}</div>
						<div className={theme.textWrapper}>
							<h2 className={classNames(theme.title, { [theme.title_preview as string]: previewMode })}>
								{getTitleByScreenType(screenType, previewMode)}
							</h2>
							<div className={theme.subtitle}>{getSubTitleByScreenType(screenType)}</div>
						</div>
						{renderButton(previewMode)}
					</div>
				</section>
			)),
			O.toNullable,
		);
	}),
);

const wTheme = {
	...css,
};

export const EmptyScreen = combineContext(EmptyScreenRaw, EmptyScreenRaw =>
	withTranslation()(withTheme(Symbol(), wTheme)(EmptyScreenRaw)),
);
