import { Observable, of, Subject } from 'rxjs';
import { failure, pending, RemoteData, success } from '@devexperts/remote-data-ts';
import { LiveData } from '@devexperts/rx-utils/dist/rd/live-data.utils';
import { startWith, switchMap, catchError } from 'rxjs/operators';
import { stringify } from 'querystring';
import { TAPIClient } from './generated/swagger/client/client';
import { fromPromise } from 'rxjs/internal-compatibility';
import { distinctRDString } from '../utils/setoid.utils';
import * as O from 'fp-ts/lib/Option';
import { pipe } from 'fp-ts/lib/pipeable';
import { ServerError } from '../utils/server-error.utils';
import { shareReplayWithDefaults } from '../utils/rxjs/shareReplayWithDefaults';

export type ApiRequest = {
	url: string;
	query?: object;
	body?: unknown;
};
export type FullApiRequest = ApiRequest & {
	method: 'GET' | 'POST' | 'PUT' | 'DELETE' | 'PATCH' | 'HEAD' | 'OPTIONS';
};
type FetchHeaders =
	| {
			'x-atmosphere-tracking-id': string;
			'Content-Type': string;
	  }
	| { 'Content-Type': string };

export interface ApiClient extends TAPIClient {
	readonly error$: Observable<Error>;
	readonly request: <R>(request: FullApiRequest) => LiveData<Error, R>;
	readonly get: <R>(request: ApiRequest) => LiveData<Error, R>;
	readonly post: <R>(request: ApiRequest) => LiveData<Error, R>;
	readonly remove: <R>(request: ApiRequest) => LiveData<Error, R>;
	readonly put: <R>(request: ApiRequest) => LiveData<Error, R>;
}

export const createApiClient = (baseHref: string, uuid$?: LiveData<Error, string>): ApiClient => {
	const errorSubj$ = new Subject<Error>();
	const error$ = errorSubj$.asObservable();

	const sharedUuid$ = pipe(
		O.fromNullable(uuid$),
		O.map(uuid$ =>
			uuid$.pipe(
				distinctRDString,
				shareReplayWithDefaults(),
			),
		),
		O.getOrElse<LiveData<Error, string>>(() => of(pending)),
	);

	const request = <R = never>(request: FullApiRequest): LiveData<Error, R> => {
		const url = `${baseHref}${buildUrl(request.url, request.query)}`;
		return sharedUuid$.pipe(
			switchMap(uuid => {
				const headers = uuid
					.map<FetchHeaders>(uuid => ({
						'x-atmosphere-tracking-id': uuid,
						'Content-Type': 'application/json; charset=UTF-8',
					}))
					.getOrElse({ 'Content-Type': 'application/json; charset=UTF-8' });

				const init: RequestInit = {
					method: request.method,
					credentials: 'include',
					redirect: 'follow',
					headers,
					body: JSON.stringify(request.body),
				};

				return fromPromise<RemoteData<Error, R>>(
					fetch(url, init)
						.then(response => {
							if (!response.ok) {
								throw new ServerError(response.status, response.statusText);
							}
							return response;
						})
						.then(response => response.text())
						.then(text => (text === '' ? undefined : JSON.parse(text)))
						.then(response => success<Error, R>(response)),
				);
			}),
			catchError<RemoteData<Error, R>, LiveData<Error, R>>(response => {
				errorSubj$.next(response);
				return of(failure(response));
			}),
			startWith(pending),
		);
	};

	const get = <R = never>(apiRequest: ApiRequest): LiveData<Error, R> =>
		request({
			...apiRequest,
			method: 'GET',
		});

	const post = <Response = never>(apiRequest: ApiRequest): LiveData<Error, Response> => {
		return request({
			...apiRequest,
			method: 'POST',
		});
	};

	const remove = <Response = never>(apiRequest: ApiRequest): LiveData<Error, Response> => {
		return request({
			...apiRequest,
			method: 'DELETE',
		});
	};

	const put = <Response = never>(apiRequest: ApiRequest): LiveData<Error, Response> => {
		return request({
			...apiRequest,
			method: 'PUT',
		});
	};

	return {
		error$,
		request,
		get,
		put,
		post,
		remove,
	};
};

function buildUrl(url: string, query?: {}): string {
	return query ? `${url}?${stringify(query)}` : url;
}
