import * as React from 'react';
import { memo, ReactNode } from 'react';
import { PartialKeys } from '@devexperts/utils/dist/object';
import { withTheme } from '@devexperts/react-kit/dist/utils/withTheme';
import { RawMenuListItemProps } from '../menu-list-item/menu-list-item.component';
import { MakeTheme } from '../../../../utils/theme.utils';
import * as css from './theme/menu-divider.module.css';

type RawMenuDividerProps = {
	theme: MakeTheme<'container' | 'hr' | 'label'>;
	label?: ReactNode;
};

const RawMenuDivider = memo((props: RawMenuDividerProps) => {
	const { theme } = props;
	return (
		<div className={theme.container}>
			{props.label && <span className={theme.label}>{props.label}</span>}
			<div className={theme.hr} />
		</div>
	);
});

const wTheme = {
	...css,
};

export type MenuDividerProps = PartialKeys<RawMenuListItemProps, 'theme'>;
export const MenuDivider = withTheme(Symbol(), wTheme)(RawMenuDivider);
