import * as React from 'react';
import * as cn from 'classnames';
import { PureComponent } from 'react';
import { withTranslation, WithTranslation } from 'react-i18next';
import { formatMoney, formatNumber } from 'accounting';
import { withTheme } from '@devexperts/react-kit/dist/utils/withTheme';
import * as E from 'fp-ts/lib/Either';
import { constNull } from 'fp-ts/lib/function';
import {
	NumericStepper,
	TNumericStepperFullProps,
} from '../../ui-common/numeric-stepper-ava/numeric-stepper.component';
import { MakeTheme } from '../../utils/theme.utils';
import {
	ProfitLossKind,
	ProvidedLabelType,
	TakeProfitStopLossComponentData,
	PriceValidationError,
	PriceValidationErrorMessages,
} from './takeprofit-stoploss-panel.model';
import { tspan, TRANSLATION_PLACEHOLDER } from '../../utils/i18n.utils';
import { TCheckboxProps, Checkbox } from '../checkbox/checkbox.component';
import { ErrorIcon } from '../../ui-desktop/ui-kit/components/icons/error.icon';
import { pipe } from 'fp-ts/lib/pipeable';
import * as stepperCss from './theme/numeric-stepper.child.module.css';
import * as stepperButtonCss from './theme/numeric-stepper.button.module.css';
import * as css from './theme/takeprofit-stoploss-panel.module.css';

export type TakeProfitStopLossComponentProps = {
	data: TakeProfitStopLossComponentData;
	onValueChange: (data: TakeProfitStopLossComponentData) => void;
	isShownValidErrors: boolean;
	currencySymbol: string;
	theme: MakeTheme<
		| 'content'
		| 'controls'
		| 'element'
		| 'controlsWrapper'
		| 'profitControlsWrapper'
		| 'checkboxWrapper'
		| 'label'
		| 'priceWrapper'
		| 'priceLabel'
		| 'priceValue'
		| 'limitLabel'
		| 'plLabel'
		| 'error'
		| 'errorIcon'
	> & {
		Checkbox?: TCheckboxProps['theme'];
		NumericStepper?: TNumericStepperFullProps['theme'];
	};
};
// this function is needed to prevent negative input, but keep the input values less than the required
const handleNegativeValue = (newValue: number) => {
	if (newValue < 0) {
		return 0;
	}
	return newValue;
};

const steppableInputTheme = {
	Input: stepperCss,
	SteppableInput: {
		ButtonIcon: stepperButtonCss,
	},
};

class TakeProfitStopLossComponentUnwrapped extends PureComponent<TakeProfitStopLossComponentProps & WithTranslation> {
	render() {
		const {
			isShownValidErrors,
			currencySymbol,
			theme,
			data: {
				kind,
				rawPrice,
				isSelected,
				isDisabled,
				minLimit,
				maxLimit,
				value,
				step,
				possiblePL,
				providedLabel,
				providedLimit,
				instrumentPrecision,
			},
		} = this.props;

		const PLLabel = this.getPLLabel(kind);

		const controlsWrapperClassname = cn(theme.controlsWrapper, {
			[theme.profitControlsWrapper as string]: this.getPLLabel(kind) === 'Profit',
		});

		const min = isShownValidErrors ? minLimit : -Infinity;
		const max = isShownValidErrors ? maxLimit : Infinity;

		const label = this.stringifyProvidedLabel(providedLabel);
		const limit = formatNumber(providedLimit, instrumentPrecision);

		return (
			<div className={theme.content}>
				<div className={theme.checkboxWrapper}>
					<Checkbox
						onValueChange={this.handleCheckboxChange}
						value={isSelected}
						isDisabled={isDisabled}
						theme={theme.Checkbox}>
						{this.getCheckboxLabel(kind)}
					</Checkbox>
				</div>
				{isSelected && (
					<div className={controlsWrapperClassname}>
						<div className={theme.controls}>
							<div className={theme.priceWrapper}>
								<span className={theme.priceLabel}>
									{tspan('takeProfitStopLossPanelPrice', 'Price')}
								</span>
								<span className={theme.priceValue}>{formatNumber(rawPrice, instrumentPrecision)}%</span>
							</div>
							<NumericStepper
								value={value}
								formatter={this.formatNumericStepperValue}
								min={min}
								max={max}
								step={step}
								onValueChange={this.handleValueChange}
								theme={{ ...steppableInputTheme, ...theme.NumericStepper }}
							/>
							<div className={cn(theme.label, theme.limitLabel)}>{`${label}: ${limit}`}</div>
						</div>
						<div className={theme.element}>
							<div className={theme.label}>
								{tspan(
									'takeProfitStopLossPanelPossiblePL',
									'Possible',
									`${TRANSLATION_PLACEHOLDER} ${PLLabel}, ${currencySymbol}`,
								)}
							</div>
							<div className={cn(theme.label, theme.plLabel)}>
								{formatMoney(possiblePL, { format: '%v' })}
							</div>
						</div>
						{isShownValidErrors && this.renderError()}
					</div>
				)}
			</div>
		);
	}

	formatNumericStepperValue = (value: number) => {
		const { instrumentPrecision } = this.props.data;
		return formatNumber(value, instrumentPrecision);
	};

	private renderError = () => {
		const { theme, data } = this.props;

		return pipe(
			data.validatedPrice,
			E.fold(
				error => (
					<div className={theme.error}>
						<div className={theme.errorIcon}>
							<ErrorIcon />
						</div>
						{this.stringifyError(error)}
					</div>
				),
				constNull,
			),
		);
	};

	private stringifyError = ({ message, value }: PriceValidationError) => {
		switch (message) {
			case PriceValidationErrorMessages.MoreOrEqual:
				return tspan('takeProfitStopLossPanelPriceShallBeMoreOrEqual', {
					defaultValue: 'Price shall be more or equal to {{value}}',
					value,
				});
			case PriceValidationErrorMessages.LessOrEqual:
				return tspan('takeProfitStopLossPanelPriceShallBeLessOrEqual', {
					defaultValue: 'Price shall be less or equal to {{value}}',
					value,
				});
		}
	};

	handleCheckboxChange = () => {
		const { onValueChange, data } = this.props;

		const updatedData = {
			...data,
			isSelected: !data.isSelected,
			isValueManuallyChanged: false,
		};

		onValueChange(updatedData);
	};

	handleValueChange = (newValue: number) => {
		const { onValueChange, data } = this.props;

		const value = handleNegativeValue(newValue);

		const updatedData = {
			...data,
			value,
			isValueManuallyChanged: true,
		};

		onValueChange(updatedData);
	};

	private getCheckboxLabel = (PLKind: ProfitLossKind) => {
		switch (PLKind) {
			case ProfitLossKind.Profit:
				return tspan('takeProfitStopLossPanelTakeProfit', 'Take Profit');
			case ProfitLossKind.Loss:
				return tspan('takeProfitStopLossPanelStopLoss', 'Stop Loss');
		}
	};

	private getPLLabel = (PLKind: ProfitLossKind): string => {
		const { t } = this.props;

		switch (PLKind) {
			case ProfitLossKind.Profit:
				return t('takeProfitStopLossPanelProfit', 'Profit');
			case ProfitLossKind.Loss:
				return t('takeProfitStopLossPanelLoss', 'Loss');
		}
	};

	private stringifyProvidedLabel = (providedLabel: ProvidedLabelType): string => {
		const { t } = this.props;

		switch (providedLabel) {
			case ProvidedLabelType.Min:
				return t('takeProfitStopLossPanelMin', 'Min');
			case ProvidedLabelType.Max:
				return t('takeProfitStopLossPanelMax', 'Max');
		}
	};
}

const wTheme = {
	...css,
};

export const TakeProfitStopLossComponent = withTranslation()(
	withTheme(Symbol(), wTheme)(TakeProfitStopLossComponentUnwrapped),
);
