import { StudyCandleTO, StudyCandleTOIO } from '../definitions/StudyCandleTO';
import { Option } from 'fp-ts/lib/Option';
import { number, boolean, type } from 'io-ts';
import { DateFromISOString, createOptionFromNullable } from 'io-ts-types';

export type CandleTO = {
	timestamp: Date;
	open: Option<number>;
	high: Option<number>;
	low: Option<number>;
	close: Option<number>;
	volume: Option<number>;
	expansion: Option<boolean>;
	visible: boolean;
	studyValues: Option<StudyCandleTO>;
	time: Option<number>;
};
export const CandleTOIO = type({
	timestamp: DateFromISOString,
	open: createOptionFromNullable(number),
	high: createOptionFromNullable(number),
	low: createOptionFromNullable(number),
	close: createOptionFromNullable(number),
	volume: createOptionFromNullable(number),
	expansion: createOptionFromNullable(boolean),
	visible: boolean,
	studyValues: createOptionFromNullable(StudyCandleTOIO),
	time: createOptionFromNullable(number),
});
