import * as React from 'react';
import { Fragment, memo } from 'react';
import { Route } from '../../../ui-common/route/containers/route.container';
import { combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { routes } from '../../routes';
import { withTranslation, WithTranslation } from 'react-i18next';
import { getLanguageForCRM } from '../../../utils/i18n.utils';
import { Iframe } from '../../iframe/iframe.component';
import { DepositPage } from './deposit.component';
import * as O from 'fp-ts/lib/Option';

export const CrmPages = combineContext(Route, DepositPage, (Route, DepositPage) =>
	withTranslation()(
		memo((props: WithTranslation) => {
			const language = getLanguageForCRM(props.i18n);
			return (
				<Fragment>
					<Route
						match={routes.crm.accounts.root}
						render={() => <Iframe src={`/api/avacrm/my_account?lang=${language}`} />}
					/>
					<Route
						match={routes.crm.personal.root}
						render={() => <Iframe src={`/api/avacrm/personal_details?lang=${language}`} />}
					/>
					<Route
						match={routes.crm.uploaddocs.root}
						render={() => <Iframe src={`/api/avacrm/upload_document?lang=${language}`} />}
					/>
					<Route
						match={routes.crm.withdraw.root}
						render={() => <Iframe src={`/api/avacrm/withdrawal?lang=${language}`} />}
					/>
					<Route match={routes.crm.deposit.root} render={() => <DepositPage previewMode={O.some(false)} />} />
					<Route
						match={routes.crm.calculator.root}
						render={() => (
							<Iframe src={`//www.avatrade.com/trading-info/trading-calculator?lang=${language}`} />
						)}
					/>
					<Route
						match={routes.crm.becomeTrader.root}
						render={() => <Iframe src={`/api/avacrm/become_trader?lang=${language}`} />}
					/>
				</Fragment>
			);
		}),
	),
);
