import { LiveData } from '@devexperts/rx-utils/dist/rd/live-data.utils';
import { merge, Observable } from 'rxjs';
import { ask, combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { createHandler } from '@devexperts/rx-utils/dist/create-handler.utils';
import { distinctUntilChanged, filter, map, mapTo, startWith, switchMap } from 'rxjs/operators';
import { Option } from 'fp-ts/lib/Option';
import * as O from 'fp-ts/lib/Option';
import { pipe } from 'fp-ts/lib/pipeable';
import { mapRD } from '@devexperts/rx-utils/dist/rd/operators/mapRD';
import { SymbolSuggestController } from '../../api/generated/swagger/controllers/SymbolSuggestController';
import { InstrumentSuggestTreeNodeTO } from '../../api/generated/swagger/definitions/InstrumentSuggestTreeNodeTO';
import { Lazy } from 'fp-ts/lib/function';
import { href, Router } from '../../utils/router.utils';
import { routes } from '../../ui-common/routes';
import { Sink } from '@devexperts/rx-utils/dist/sink.utils';
import { shareReplayWithDefaults } from '../../utils/rxjs/shareReplayWithDefaults';

type Suggestion = {
	symbol: string;
	category: string;
};

export type SymbolSearchViewModel = {
	suggest$: LiveData<Error, InstrumentSuggestTreeNodeTO[]>;
	isOpened$: Observable<boolean>;
	inputValue$: Observable<string>;
	onInputValueChange: (value: string, keepOpened?: boolean) => void;
	selectedSuggestId$: Observable<Option<number>>;
	onSelectSuggest: (id: Option<number>) => void;
	onSearch: (symbol: string, category: string) => void;
	onClose: Lazy<void>;
};

export type SymbolSearchViewContext = {
	symbolSuggestController: SymbolSuggestController;
	router: Router;
};

export type CreateSymbolSearchViewModel = Sink<() => SymbolSearchViewModel>;

export const createSymbolSearchViewModel = combineContext(
	ask<SymbolSearchViewContext>(),
	(context): CreateSymbolSearchViewModel => {
		const { symbolSuggestController } = context;

		const isOpened = createHandler<boolean>();
		const inputValue = createHandler<string>();
		const selectedSuggestId = createHandler<Option<number>>();
		const searchValue = createHandler<Suggestion>();
		const closeRequest = createHandler<void>();

		//#region state
		const searchValue$ = searchValue.value$.pipe(
			distinctUntilChanged(),
			shareReplayWithDefaults(),
		);

		const inputValue$ = merge(searchValue$.pipe(mapTo('')), inputValue.value$).pipe(
			startWith(''),
			distinctUntilChanged(),
			shareReplayWithDefaults(),
		);

		const suggest$: LiveData<Error, InstrumentSuggestTreeNodeTO[]> = inputValue$.pipe(
			filter<string>(Boolean),
			switchMap(text =>
				symbolSuggestController.requestSuggestsTree({ query: { text, instrumentTypes: O.none } }),
			),
			mapRD(response => response.root.childNodes),
			shareReplayWithDefaults(),
		);

		const selectedSuggestId$ = merge(searchValue$.pipe(mapTo(O.none)), selectedSuggestId.value$).pipe(
			startWith(O.none),
		);

		const isOpened$ = merge(
			isOpened.value$,
			closeRequest.value$.pipe(mapTo(false)),
			selectedSuggestId$.pipe(
				map(selectedSymbol =>
					pipe(
						selectedSymbol,
						O.isSome,
					),
				),
			),
		).pipe(startWith(false));
		//#endregion

		//#region handlers
		const onInputValueChange = (value: string, keepOpened: boolean = false) => {
			inputValue.handle(value);
			isOpened.handle(keepOpened);
		};
		const onSelectSuggest = (id: Option<number>) => selectedSuggestId.handle(id);
		const onSearch = (symbol: string, category: string) => searchValue.handle({ symbol, category });
		const onClose = () => closeRequest.handle(undefined);
		//#endregion

		//#region handlers
		const searchEffect$ = searchValue$.pipe(
			// map(({ category, symbol }: Suggestion) => context.router.push(`/trade/${category.toLowerCase()}/${encodeURIComponent(symbol)}`)),
			map(({ category, symbol }: Suggestion) =>
				context.router.push(
					href(routes.trade.category.symbol.root, { categoryId: category.toLowerCase(), symbol }),
				),
			),
		);
		//#endregion

		return new Sink(
			() => ({
				inputValue$,
				isOpened$,
				onClose,
				onInputValueChange,
				onSearch,
				onSelectSuggest,
				selectedSuggestId$,
				suggest$,
			}),
			searchEffect$,
		);
	},
);
