import * as t from 'io-ts';
import { Option } from 'fp-ts/lib/Option';
import { stringOption } from '../../../utils/io-ts.utils';

export const WATCHLIST_SUBSCRIPTION_INITIALIZED = 'WATCHLIST_SUBSCRIPTION_INITIALIZED';

export type WatchlistSubscriptionInitializedMessageTO = {
	type: typeof WATCHLIST_SUBSCRIPTION_INITIALIZED;
	accountId: Option<string>;
	body: {
		success: boolean;
	};
};

export const watchlistSubscriptionInitializedMessageTO: t.Tagged<
	'type',
	WatchlistSubscriptionInitializedMessageTO,
	unknown
> = t.type({
	type: t.literal(WATCHLIST_SUBSCRIPTION_INITIALIZED),
	accountId: stringOption,
	body: t.type({
		success: t.boolean,
	}),
});
