import * as React from 'react';
import { remoteData, RemoteData } from '@devexperts/remote-data-ts';
import { ComponentClass, ComponentType, PureComponent } from 'react';
import { RemoteDataState } from '../remote-data-state/remote-data-state.component';
import { sequence } from 'fp-ts/lib/Traversable';
import { array } from 'fp-ts/lib/Array';

export type MakeRemoteDataLoaderProps<P extends object, Keys extends keyof P> = {
	[K in keyof P]: K extends Keys ? RemoteData<unknown, P[K]> : P[K];
};

const sequenceRDArray = sequence(remoteData, array);

export function makeRemoteDataLoader<P extends object, K extends keyof P>(
	Target: ComponentType<P>,
	...keys: K[]
): ComponentClass<MakeRemoteDataLoaderProps<P, K>> {
	return class RemoteDataLoader extends PureComponent<MakeRemoteDataLoaderProps<P, K>> {
		static displayName = `RemoteDataLoader(${Target.displayName || Target.name})`;

		render() {
			const values = keys.map(key => {
				const prop: RemoteData<Error, P[K]> = this.props[key] as any;
				return prop.map(value => ({
					key,
					value,
				}));
			});
			const data = sequenceRDArray(values);
			return <RemoteDataState data={data} success={this.handleSuccess} />;
		}

		private handleSuccess = (values: Array<{ key: K; value: P[K] }>) => {
			const props = values.reduce(
				(acc, value) => {
					acc[value.key] = value.value;
					return acc;
				},
				{} as P,
			);
			return <Target {...this.props} {...props} />;
		};
	};
}
