import { LimitsDomainTO } from '../../api/generated/swagger/definitions/LimitsDomainTO';

export function countDistance(
	limitStopDistanceType: LimitsDomainTO['limitStopDistanceType'],
	limitStopDistance: number,
	limitStopDistanceInPercentOfSpread: number,
	spread: number,
	pipsSize: number,
): number {
	switch (limitStopDistanceType) {
		case 'PIPS': {
			return limitStopDistance * pipsSize;
		}
		case 'PERCENT_OF_SPREAD': {
			return (spread * limitStopDistanceInPercentOfSpread) / 100 / pipsSize;
		}
	}
}
