import * as React from 'react';
import * as cn from 'classnames';
import { FunctionComponent, ReactNode } from 'react';
import * as css from './theme/workspace.module.css';

type WorkspaceProps = {
	children: ReactNode;
	innerShadow?: boolean;
};

export const Workspace: FunctionComponent<WorkspaceProps> = ({ children, innerShadow }) => {
	const workspaceClassName = cn(css.container, {
		[css.container_innerShadow]: innerShadow,
	});

	return <div className={workspaceClassName}>{children}</div>;
};
