import { InstrumentTO } from '../../api/generated/swagger/definitions/InstrumentTO';
import { SocketClient } from '../../api/socket-client';
import { EntityStore } from '@devexperts/rx-utils/dist/rd/entity-store.utils';
import { Observable } from 'rxjs';
import { filter, map } from 'rxjs/operators';
import { isInstrumentTO } from '../../api/socket/socket-message.to';
import { fromOption, success } from '@devexperts/remote-data-ts';
import { LiveData } from '@devexperts/rx-utils/dist/rd/live-data.utils';
import { findFirst } from 'fp-ts/lib/Array';
import { filterMap } from '../../utils/rxjs/filtermap';
import { Sink } from '@devexperts/rx-utils/dist/sink.utils';
import { combineContext, ask } from '@devexperts/rx-utils/dist/context.utils';
import { pipe } from 'fp-ts/lib/pipeable';
import * as O from 'fp-ts/lib/Option';

export type InstrumentStore = Readonly<{
	getBySymbol: (symbol: string) => LiveData<Error, InstrumentTO>;
	getById: (id: number) => LiveData<Error, InstrumentTO>;
	all$: LiveData<Error, InstrumentTO[]>;
}>;

export type CreateInstrumentStore = Sink<() => InstrumentStore>;

export type InstrumentStoreContext = {
	socketClient: SocketClient;
};

const getPKFromId = (id: number) => id.toString();

export const createInstrumentStore = combineContext(
	ask<InstrumentStoreContext>(),
	(e): CreateInstrumentStore => {
		const { socketClient } = e;
		const pk = (instrument: InstrumentTO) => getPKFromId(instrument.id);

		const store = new EntityStore<Error, InstrumentTO>();
		const instruments$: Observable<InstrumentTO[]> = socketClient.message$.pipe(
			filter(isInstrumentTO),
			map(message => message.body.toArray()),
		);

		const all$ = store.getAll(pk, () => instruments$.pipe(map(to => success(to))));

		const getById = (id: number): LiveData<Error, InstrumentTO> =>
			store.get(getPKFromId(id), () => {
				return instruments$.pipe(
					filterMap(instruments =>
						pipe(
							findFirst(instruments, instrument => instrument.id === id),
							O.map(to => success<Error, InstrumentTO>(to)),
						),
					),
				);
			});

		const getBySymbol = (symbol: string): LiveData<Error, InstrumentTO> =>
			all$.pipe(
				map(data =>
					data.chain(instruments =>
						fromOption(
							findFirst(instruments, instrument => instrument.symbol === symbol),
							() => new Error(`Instrument with symbol ${symbol} not found in instrument.store`),
						),
					),
				),
			);

		return new Sink<() => InstrumentStore>(
			() => ({
				getBySymbol,
				getById,
				all$,
			}),
			all$,
		);
	},
);
