import * as React from 'react';
import { memo, Fragment, useCallback } from 'react';
import { WithTranslation, withTranslation } from 'react-i18next';
import { format } from 'date-fns';
import { Option } from 'fp-ts/lib/Option';
import { RemoteData } from '@devexperts/remote-data-ts';
import { combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { RouteLink } from '../../ui-common/route-link/containers/route-link.container';
import { RemoteDataState } from '../../ui-common/remote-data-state/remote-data-state.component';
import { Optional } from '../../ui-common/optional/optional.component';
import { PipRate } from '../../ui-common/pip-rate/pip-rate.component';
import { SymbolInfoItem } from '../symbol-info-item/symbol-info-item.component';
import { definePipSettings } from '../../ui-common/pip-rate/pip-settings.utils';
import { tspan } from '../../utils/i18n.utils';
import { HistoryDetailsData, HistoryOrderSide } from '../../model/history/history.model';
import { ProfitLossKind } from '../../model/takeprofit-stoploss/takeprofit-stoploss.model';
import { MakeTheme } from '../../utils/theme.utils';
import { empty } from 'fp-ts/lib/Array';
import * as O from 'fp-ts/lib/Option';
import * as css from './theme/history-details.module.css';
import { pipe } from 'fp-ts/lib/pipeable';

export type HistoryDetailsProps = {
	orderId: number;
	data: RemoteData<Error, HistoryDetailsData>;
	theme?: MakeTheme<'item' | 'item_secondary' | 'label' | 'value' | 'container' | 'title' | 'subtitle'>;
} & WithTranslation;

export const HistoryDetailsCommon = combineContext(RouteLink, RouteLink =>
	withTranslation()(
		memo((props: HistoryDetailsProps) => {
			const { data, t, theme } = props;

			const renderSide = useCallback(
				(side: HistoryOrderSide) => {
					switch (side) {
						case HistoryOrderSide.Buy:
							return <span className={css.positiveSide}>{tspan('historyDetailsBuy', 'Buy')}</span>;
						case HistoryOrderSide.Sell:
							return <span className={css.negativeSide}>{tspan('historyDetailsSell', 'Sell')}</span>;
					}
				},
				[t],
			);

			const stringifyProfitLossKind = useCallback(
				(plKind: ProfitLossKind) => {
					switch (plKind) {
						case ProfitLossKind.Profit:
							return <span className={css.positiveSide}>{tspan('historyDetailsProfit', 'Profit')}</span>;
						case ProfitLossKind.Loss:
							return <span className={css.negativeSide}>{tspan('historyDetailsLoss', 'Loss')}</span>;
					}
				},
				[t],
			);

			const renderDateString = useCallback((value: Date) => {
				return (
					<Fragment>
						{format(value, 'MM.dd.yyyy HH:mm')}
						<span className={css.seconds}>{format(value, ':ss')}</span>
					</Fragment>
				);
			}, empty);

			const renderPipsValue = useCallback(
				(value: number, pipsSize: number, precision: number) => {
					const pipSettings = definePipSettings(value, pipsSize);

					return <PipRate value={value} rise={pipSettings} digits={precision} />;
				},
				[definePipSettings],
			);

			const renderOptionalPipsValue = useCallback(
				(maybeValue: Option<number>, pipsSize: number, precision: number) =>
					pipe(
						maybeValue,
						O.map(value => renderPipsValue(value, pipsSize, precision)),
					),
				[renderPipsValue],
			);

			const renderSuccess = useCallback(
				(data: HistoryDetailsData) => {
					const {
						side,
						amount,
						profitLossKind,
						openDateTime,
						closeDateTime,
						sizeInLots,
						takeProfitValue,
						stopLossValue,
						swaps,
						commissions,
						precision,
						pipsSize,
					} = data;

					return (
						<Fragment>
							<SymbolInfoItem theme={theme} label={tspan('historyDetailsSide', 'Side')}>
								{renderSide(side)}
							</SymbolInfoItem>
							<SymbolInfoItem theme={theme} label={tspan('historyDetailsAmount', 'Amount')}>
								{amount}
							</SymbolInfoItem>
							<SymbolInfoItem theme={theme} label={tspan('historyDetailsProfitLoss', 'Profit/Loss')}>
								<Optional
									value={pipe(
										profitLossKind,
										O.map(stringifyProfitLossKind),
									)}
								/>
							</SymbolInfoItem>
							<SymbolInfoItem theme={theme} label={tspan('historyDetailsOpenDateTime', 'Open date time')}>
								<Optional
									value={pipe(
										openDateTime,
										O.map(renderDateString),
									)}
								/>
							</SymbolInfoItem>
							<SymbolInfoItem
								theme={theme}
								label={tspan('historyDetailsCloseDateTime', 'Close date time')}>
								{renderDateString(closeDateTime)}
							</SymbolInfoItem>
							<SymbolInfoItem theme={theme} label={tspan('historyDetailsSizeInLots', 'Size in lots')}>
								{sizeInLots}
							</SymbolInfoItem>
							<SymbolInfoItem theme={theme} label={tspan('historyDetailsTakeProfit', 'Take profit')}>
								<Optional value={renderOptionalPipsValue(takeProfitValue, pipsSize, precision)} />
							</SymbolInfoItem>
							<SymbolInfoItem theme={theme} label={tspan('historyDetailsStopLoss', 'Stop loss')}>
								<Optional value={renderOptionalPipsValue(stopLossValue, pipsSize, precision)} />
							</SymbolInfoItem>
							<SymbolInfoItem theme={theme} label={tspan('historyDetailsSwaps', 'Swaps')}>
								<Optional value={swaps} />
							</SymbolInfoItem>
							<SymbolInfoItem theme={theme} label={tspan('historyDetailsCommissions', 'Commissions')}>
								<Optional value={commissions} />
							</SymbolInfoItem>
						</Fragment>
					);
				},
				[t, renderSide, stringifyProfitLossKind, renderDateString, renderOptionalPipsValue],
			);

			return <RemoteDataState data={data} success={renderSuccess} />;
		}),
	),
);
