import { ask, combineContext, fromReader } from '@devexperts/rx-utils/dist/context.utils';
import { SocketClient } from '../../api/socket-client';
import { combineLatest, merge } from 'rxjs';
import { distinctUntilChanged, filter, map, switchMap } from 'rxjs/operators';
import {
	isWatchlistContentTO,
	isWatchlistSubscriptionInitializedTO,
	isWatchlistTO,
} from '../../api/socket/socket-message.to';
import { EntityStore } from '@devexperts/rx-utils/dist/rd/entity-store.utils';
import { success, getSetoid as getSetoidRD, fromOption, RemoteData } from '@devexperts/remote-data-ts';
import { LiveData } from '@devexperts/rx-utils/dist/rd/live-data.utils';
import { Sink } from '@devexperts/rx-utils/dist/sink.utils';
import { switchMapRD } from '@devexperts/rx-utils/dist/rd/operators/switchMapRD';
import * as O from 'fp-ts/lib/Option';
import { symbolSuggestController } from '../../api/generated/swagger/controllers/SymbolSuggestController';
import { mapRD } from '@devexperts/rx-utils/dist/rd/operators/mapRD';
import { watchlistsController } from '../../api/generated/swagger/controllers/WatchlistsController';
import { findFirst, getEq as getSetoidArray, mapOption, isEmpty } from 'fp-ts/lib/Array';
import { getStructEq, Eq, eqString } from 'fp-ts/lib/Eq';
import { setoidStrict } from '@devexperts/utils/dist/typeclasses/setoid/setoid.utils';
import { combineLatestRD } from '@devexperts/rx-utils/dist/rd/operators/combineLatestRD';
import { identity } from 'fp-ts/lib/function';
import { ordString } from 'fp-ts/lib/Ord';
import { pipe } from 'fp-ts/lib/pipeable';
import { shareReplayWithDefaults } from '../../utils/rxjs/shareReplayWithDefaults';

export enum WatchlistType {
	PRIVATE = '* PRIVATE *',
	PUBLIC = '* PUBLIC *',
	CATEGORY = '* CATEGORY *',
}

export type WatchlistEntity = {
	id: string;
	name: string;
	symbols: string[];
	type: WatchlistType;
};

const setoidWatchlistEntity: Eq<WatchlistEntity> = getStructEq({
	id: eqString,
	name: eqString,
	symbols: getSetoidArray(eqString),
	type: setoidStrict,
});

const setoidWatchlistEntityRD: Eq<RemoteData<Error, WatchlistEntity>> = getSetoidRD(
	setoidStrict,
	setoidWatchlistEntity,
);

const setoidWatchlistEntityArrayRD: Eq<RemoteData<Error, WatchlistEntity[]>> = getSetoidRD(
	setoidStrict,
	getSetoidArray(setoidWatchlistEntity),
);

export type WatchlistStore = Readonly<{
	readonly all$: LiveData<Error, WatchlistEntity[]>;
	readonly private$: LiveData<Error, WatchlistEntity[]>;
	readonly public$: LiveData<Error, WatchlistEntity[]>;
	readonly categories$: LiveData<Error, WatchlistEntity[]>;
	readonly getById: (id: string) => LiveData<Error, WatchlistEntity>;
}>;

export type CreateWatchlistStore = Sink<() => WatchlistStore>;

export type WatchlistStoreContext = {
	socketClient: SocketClient;
};

export const createWatchlistStore = combineContext(
	fromReader(watchlistsController),
	fromReader(symbolSuggestController),
	ask<WatchlistStoreContext>(),
	(watchlistsController, symbolSuggestController, context: WatchlistStoreContext): CreateWatchlistStore => {
		const { socketClient } = context;
		const pk = (category: WatchlistEntity) => category.id;

		const isInitialized$: LiveData<Error, boolean> = socketClient.message$.pipe(
			filter(isWatchlistSubscriptionInitializedTO),
			filter(message => message.body.success),
			map(message => success(message.body.success)),
		);

		const store = new EntityStore<Error, WatchlistEntity>();
		const watchlists$ = socketClient.message$.pipe(
			filter(isWatchlistTO),
			map(watchlists => [...watchlists.body.commonWatchlists, ...watchlists.body.accountWatchlists]),
		);

		const watchlistData$ = socketClient.message$.pipe(
			filter(isWatchlistContentTO),
			map(wldata => wldata.body),
		);

		const watchlistsWithData$: LiveData<Error, WatchlistEntity[]> = combineLatest(watchlists$, watchlistData$).pipe(
			map(([watchlists, watchlistsData]) =>
				mapOption(watchlistsData, watchlistData => {
					return pipe(
						watchlists,
						findFirst(item => item.id.toString() === watchlistData.watchlistId),
						O.map(data => ({
							id: data.id,
							name: data.name,
							symbols: watchlistData.symbols,
							type: pipe(
								data.common,
								O.exists(identity),
							)
								? WatchlistType.PUBLIC
								: WatchlistType.PRIVATE,
						})),
					);
				}),
			),
			map(data => success(data)),
		);

		const watchlistSubscribeEffect$ = watchlists$.pipe(
			switchMap(watchlists =>
				watchlistsController.changeWatchlistContentSubscription({
					body: {
						watchlistIdsToAdd: watchlists.map(wl => wl.id),
						watchlistIdsToRemove: [],
					},
				}),
			),
		);

		const watchlistsFromInstrumentTree$: LiveData<
			Error,
			WatchlistEntity[]
		> = symbolSuggestController.requestSuggestsTree({ query: { text: '*', instrumentTypes: O.none } }).pipe(
			mapRD(data =>
				data.root.childNodes.map(suggestion => ({
					id: suggestion.name.toLowerCase(),
					name: suggestion.name,
					symbols: suggestion.suggests.map(item => item.name),
					type: WatchlistType.CATEGORY,
				})),
			),
		);

		const request$: LiveData<Error, WatchlistEntity[]> = isInitialized$.pipe(
			switchMapRD(() => merge(watchlistsWithData$, watchlistsFromInstrumentTree$)),
			shareReplayWithDefaults(),
		);
		const all$: LiveData<Error, WatchlistEntity[]> = store
			.getAll(pk, () => request$)
			.pipe(mapRD(data => data.sort((a, b) => ordString.compare(a.name, b.name))));
		const private$: LiveData<Error, WatchlistEntity[]> = all$.pipe(
			mapRD(data => data.filter(item => item.type === WatchlistType.PRIVATE)),
			filter(privateWlRD => privateWlRD.map(privateWl => !isEmpty(privateWl)).getOrElse(true)),
			distinctUntilChanged(setoidWatchlistEntityArrayRD.equals),
			shareReplayWithDefaults(),
		);

		const public$: LiveData<Error, WatchlistEntity[]> = all$.pipe(
			mapRD(data => data.filter(item => item.type === WatchlistType.PUBLIC)),
			distinctUntilChanged(setoidWatchlistEntityArrayRD.equals),
			shareReplayWithDefaults(),
		);

		const categories$: LiveData<Error, WatchlistEntity[]> = all$.pipe(
			mapRD(data => data.filter(item => item.type === WatchlistType.CATEGORY)),
			distinctUntilChanged(setoidWatchlistEntityArrayRD.equals),
			shareReplayWithDefaults(),
		);

		const getById = (id: string): LiveData<Error, WatchlistEntity> =>
			store
				.get(id, () =>
					combineLatestRD(watchlistsWithData$, watchlistsFromInstrumentTree$).pipe(
						switchMapRD(() =>
							all$.pipe(
								mapRD(data => findFirst(data, item => item.id === id)),
								map(data =>
									data.chain(value =>
										fromOption(value, () => new Error('Watchlist is not available')),
									),
								),
							),
						),
					),
				)
				.pipe(
					distinctUntilChanged(setoidWatchlistEntityRD.equals),
					shareReplayWithDefaults(),
				);

		return new Sink(
			() => ({ all$, public$, private$, categories$, getById }),
			merge(all$, watchlistSubscribeEffect$),
		);
	},
);
