import * as React from 'react';
import { ReactNode } from 'react';
import { PURE } from '@devexperts/react-kit/dist/utils/pure';
import * as classnames from 'classnames';
import { ButtonIcon, TButtonIconProps } from '@devexperts/react-kit/dist/components/ButtonIcon/ButtonIcon';
import { Button } from '@devexperts/react-kit/dist/components/Button/Button';
import {
	startOfMonth,
	startOfWeek,
	differenceInCalendarWeeks,
	addWeeks,
	addMonths,
	subMonths,
	isSameDay,
	isValid,
	isAfter,
	isBefore,
	addDays,
} from 'date-fns';
import { Holdable } from '@devexperts/react-kit/dist/components/Holdable/Holdable';
import { TCalendarProps as TBaseCalendarProps } from '@devexperts/react-kit/dist/components/DateInput/DateInput';
import { MoveLeftIcon } from '../icons/move-left.icon';
import { MoveRightIcon } from '../icons/move-right.icon';
import { withTheme } from '@devexperts/react-kit/dist/utils/withTheme';
import { MakeTheme } from '../../../../utils/theme.utils';
import { isUndefined } from 'util';
import { withTranslation, WithTranslation } from 'react-i18next';
import { tspan } from '../../../../utils/i18n.utils';
import * as css from './theme/calendar.module.css';
import * as buttonIconCss from './theme/button-icon.module.css';

export type TCalendarOwnProps = TBaseCalendarProps;

const iconMoveLeft = <MoveLeftIcon />;
const iconMoveRight = <MoveRightIcon />;

export type TCalendarInjectedProps = {
	theme: MakeTheme<
		| 'container'
		| 'calendar'
		| 'calendar__header'
		| 'calendar__body'
		| 'calendar__day'
		| 'calendar__day_isCurrent'
		| 'calendar__day_isSelected'
		| 'calendar__day_isHidden'
		| 'calendar__row'
		| 'calendar__monthName'
		| 'calendar__yearsText'
		| 'button_icon__wrapper'
	> & { ButtonIcon?: TButtonIconProps['theme'] };
	disabledDates?: Date[];
	selectedDate: Date;
};

export type TCalendarFullProps = TCalendarOwnProps & TCalendarInjectedProps & WithTranslation;

type TCalendarState = {
	selectedDate: Date;
};

@PURE
class RawCalendar extends React.Component<TCalendarFullProps, TCalendarState> {
	state: TCalendarState = {
		selectedDate: this.props.value || new Date(),
	};

	private getTranslatedMonthNames = () => {
		return [
			tspan('calendarJanuary', 'January'),
			tspan('calendarFebruary', 'February'),
			tspan('calendarMarch', 'March'),
			tspan('calendarApril', 'April'),
			tspan('calendarMay', 'May'),
			tspan('calendarJune', 'June'),
			tspan('calendarJuly', 'July'),
			tspan('calendarAugust', 'August'),
			tspan('calendarSeptember', 'September'),
			tspan('calendarOctober', 'October'),
			tspan('calendarNovember', 'November'),
			tspan('calendarDecember', 'December'),
		];
	};

	componentDidUpdate(prevProps: TCalendarFullProps) {
		const { props } = this;

		if (props.value !== prevProps.value) {
			if (props.value && isValid(props.value)) {
				this.setState({
					selectedDate: props.value,
				});
			} else {
				this.setState({
					selectedDate: new Date(),
				});
			}
		}
	}

	render() {
		const { theme, value, onMouseDown } = this.props;
		const { selectedDate } = this.state;

		const currentMonthIndex = selectedDate.getMonth();

		const nextMonth = addMonths(selectedDate, 1);

		const startOfCurrentMonth = startOfMonth(selectedDate);
		const startOfCurrentMonthWeek = startOfWeek(startOfCurrentMonth, {
			weekStartsOn: 1,
		});

		const startOfNextMonth = startOfMonth(nextMonth);
		const startOfNextMonthWeek = startOfWeek(startOfNextMonth, {
			weekStartsOn: 1,
		});
		let startOfNextFullMonthWeek = startOfNextMonthWeek;
		if (startOfNextMonthWeek.getMonth() === currentMonthIndex) {
			startOfNextFullMonthWeek = addWeeks(startOfNextMonthWeek, 1);
		}
		const diffInCalendarWeeks = differenceInCalendarWeeks(startOfNextFullMonthWeek, startOfCurrentMonthWeek, {
			weekStartsOn: 1,
		});

		const monthNames = this.getTranslatedMonthNames();

		return (
			<div className={theme.container} onMouseDown={onMouseDown}>
				<div className={theme.calendar}>
					<div className={theme.calendar__monthName}>
						<Holdable onHold={this.onPrevMonthClick}>
							<div className={theme.button_icon__wrapper}>
								<ButtonIcon
									icon={iconMoveLeft}
									theme={theme.ButtonIcon}
									tabIndex={-1}
									onClick={this.onPrevMonthClick}
									isFlat={true}
								/>
							</div>
						</Holdable>
						<div className={theme.calendar__yearsText}>
							{monthNames[currentMonthIndex]} {selectedDate.getFullYear()}
						</div>
						<Holdable onHold={this.onNextMonthClick}>
							<div className={theme.button_icon__wrapper}>
								<ButtonIcon
									icon={iconMoveRight}
									theme={theme.ButtonIcon}
									tabIndex={-1}
									onClick={this.onNextMonthClick}
									isFlat={true}
								/>
							</div>
						</Holdable>
					</div>
					<div className={theme.calendar__header}>
						<div className={theme.calendar__row}>
							<div className={theme.calendar__day}>{tspan('calendarMonday', 'MON')}</div>
							<div className={theme.calendar__day}>{tspan('calendarTuesday', 'TUE')}</div>
							<div className={theme.calendar__day}>{tspan('calendarWednesday', 'WED')}</div>
							<div className={theme.calendar__day}>{tspan('calendarThursday', 'THU')}</div>
							<div className={theme.calendar__day}>{tspan('calendarFriday', 'FRI')}</div>
							<div className={theme.calendar__day}>{tspan('calendarSaturday', 'SAT')}</div>
							<div className={theme.calendar__day}>{tspan('calendarSunday', 'SUN')}</div>
						</div>
					</div>
					<div className={theme.calendar__body}>
						{Array.from(new Array(diffInCalendarWeeks).keys()).map(i => {
							return this.renderWeek(
								addWeeks(startOfCurrentMonthWeek, i),
								currentMonthIndex,
								selectedDate,
								value,
								`current_${i}`,
							);
						})}
					</div>
				</div>
			</div>
		);
	}

	private renderWeek(
		from: Date,
		forMonth: number,
		currentDate: Date,
		selectedDate: TCalendarProps['value'],
		key?: number | string,
	): ReactNode {
		const { theme, min, max, disabledDates } = this.props;

		return (
			<div key={key} className={theme.calendar__row}>
				{Array.from(new Array(7).keys()).map(i => {
					const day = addDays(from, i);
					const dayMonthIndex = day.getMonth();
					const isHidden = dayMonthIndex !== forMonth;
					const className = classnames(theme.calendar__day, {
						[theme.calendar__day_isCurrent as string]: !isHidden && isSameDay(day, currentDate),
						[theme.calendar__day_isSelected as string]:
							!isHidden && selectedDate && isSameDay(day, selectedDate),
						[theme.calendar__day_isHidden as string]: isHidden,
					});
					const dayTheme = {
						container: className,
					};
					const isDayOff =
						disabledDates && !isUndefined(disabledDates.find(disabledDay => isSameDay(disabledDay, day)));

					const isDisabled =
						isHidden || isDayOff || (min && isBefore(day, min)) || (max && isAfter(day, max));

					return (
						<Button
							theme={dayTheme}
							isDisabled={isDisabled}
							isFlat={true}
							onClick={this.onDayClick(day)}
							key={i}>
							{day.getDate()}
						</Button>
					);
				})}
			</div>
		);
	}

	private onPrevMonthClick = () => {
		const { selectedDate } = this.state;

		this.setState({ selectedDate: subMonths(selectedDate, 1) });
	};

	private onNextMonthClick = () => {
		const { selectedDate } = this.state;

		this.setState({ selectedDate: addMonths(selectedDate, 1) });
	};

	private onDayClick = (day: Date) => () => {
		const { onValueChange } = this.props;
		onValueChange && onValueChange(day);
	};
}

const theme = {
	...css,
	ButtonIcon: buttonIconCss,
};

export type TCalendarProps = TCalendarOwnProps & Partial<TCalendarInjectedProps>;
export const Calendar = withTheme(Symbol(), theme)(withTranslation()(RawCalendar)) as React.ComponentClass<
	TCalendarProps
>;
