import { ask, combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { Sink } from '@devexperts/rx-utils/dist/sink.utils';
import { PositionsStore } from '../../store/positions/positions.store';
import { Observable, merge, of, fromEvent, combineLatest } from 'rxjs';
import { LiveData } from '@devexperts/rx-utils/dist/rd/live-data.utils';
import { mapRD } from '@devexperts/rx-utils/dist/rd/operators/mapRD';
import { InstrumentStore } from '../../store/instrument/instrument.store';
import { Router } from '../../utils/router.utils';
import { map, switchMap, mapTo, startWith } from 'rxjs/operators';
import { TableSize, ConfigItem } from '../../ui-desktop/positions/components/position-table/position-table.model';
import {
	PositionLargeTableConfig,
	PositionSmallTableConfig,
} from '../../ui-desktop/positions/components/position-table/position-table.utils';
import { shareReplayWithDefaults } from '../../utils/rxjs/shareReplayWithDefaults';

export type PositionViewModel = {
	positionsUid$: Observable<string[]>;
	tableSize$: Observable<TableSize>;
	tableSizeConfig$: Observable<ConfigItem[]>;
	getSymbolByInstrumentId: (instrumentId: number) => LiveData<Error, string>;
};

type PositionViewModelContext = {
	instrumentStore: InstrumentStore;
	positionsStore: PositionsStore;
	router: Router;
};

type CreatePositionViewModel = Sink<() => PositionViewModel>;

export const createPositionViewModel = combineContext(
	ask<PositionViewModelContext>(),
	(e): CreatePositionViewModel => {
		const { positionsStore, instrumentStore, router } = e;

		const getSymbolByInstrumentId = (instrumentId: number): LiveData<Error, string> =>
			instrumentStore.getById(instrumentId).pipe(mapRD(instrument => instrument.symbol));

		const positionsUid$ = positionsStore.keys$;

		const routeState$ = merge(of(router.state), router.state$);
		const windowResize$ = fromEvent<Event>(window, 'resize').pipe(
			mapTo(undefined),
			startWith(undefined),
		);
		const tableSize$: Observable<TableSize> = combineLatest(routeState$, windowResize$).pipe(
			map(([routerState]) => routerState),
			map(({ location: { pathname } }) => {
				const isContainsClose = pathname.includes('close');
				const isContainsEdit = pathname.includes('edit');
				const isContainsInfo = pathname.includes('info');
				const isSmallWindow = window.innerWidth <= 1280;

				return isSmallWindow && (isContainsClose || isContainsEdit || isContainsInfo)
					? TableSize.Small
					: TableSize.Large;
			}),
			shareReplayWithDefaults(),
		);

		const tableSizeConfig$: Observable<ConfigItem[]> = tableSize$.pipe(
			switchMap(tableSize =>
				of(tableSize === TableSize.Large ? PositionLargeTableConfig : PositionSmallTableConfig),
			),
		);

		return new Sink(() => ({
			positionsUid$,
			tableSize$,
			tableSizeConfig$,
			getSymbolByInstrumentId,
		}));
	},
);
