import * as React from 'react';
import { PureComponent, Fragment } from 'react';
import { RemoteData } from '@devexperts/remote-data-ts';
import { RemoteDataState } from '../../../../ui-common/remote-data-state/remote-data-state.component';
import { TakeProfitStopLossComponent } from '../../../../ui-common/takeprofit-stoploss-panel/takeprofit-stoploss-panel.component';
import {
	TakeProfitStopLossComponentData,
	ProfitLossKind,
} from '../../../../ui-common/takeprofit-stoploss-panel/takeprofit-stoploss-panel.model';
import { OrderEntryTakeProfitStopLossData } from './order-entry-takeprofit-stoploss.model';

import * as css from './theme/order-entry-takeprofit-stoploss.module.css';

export type OrderEntryTakeProfitStopLossProps = {
	data: RemoteData<Error, OrderEntryTakeProfitStopLossData>;
	onValueChange: (value: OrderEntryTakeProfitStopLossData) => void;
	isShownValidErrors: boolean;
	currencySymbol: string;
};

export class OrderEntryTakeProfitStopLoss extends PureComponent<OrderEntryTakeProfitStopLossProps> {
	render() {
		const { data } = this.props;

		return <RemoteDataState data={data} success={data => this.renderSuccess(data)} />;
	}

	private renderSuccess(data: OrderEntryTakeProfitStopLossData) {
		const { isShownValidErrors, currencySymbol } = this.props;
		const { takeProfitData, stopLossData } = data;

		return (
			<Fragment>
				<div className={css.wrapper}>
					<TakeProfitStopLossComponent
						onValueChange={this.handleValueChange(data)}
						data={takeProfitData}
						isShownValidErrors={isShownValidErrors}
						currencySymbol={currencySymbol}
					/>
				</div>
				<div className={css.wrapper}>
					<TakeProfitStopLossComponent
						onValueChange={this.handleValueChange(data)}
						data={stopLossData}
						isShownValidErrors={isShownValidErrors}
						currencySymbol={currencySymbol}
					/>
				</div>
			</Fragment>
		);
	}

	handleValueChange = (data: OrderEntryTakeProfitStopLossData) => (changedValue: TakeProfitStopLossComponentData) => {
		const { takeProfitData, stopLossData } = data;
		const { onValueChange } = this.props;

		switch (changedValue.kind) {
			case ProfitLossKind.Profit:
				onValueChange({
					takeProfitData: changedValue,
					stopLossData,
				});
				break;
			case ProfitLossKind.Loss:
				onValueChange({
					takeProfitData,
					stopLossData: changedValue,
				});
				break;
		}
	};
}
