import { Option } from 'fp-ts/lib/Option';
import { TDateInputValue } from '@devexperts/react-kit/dist/components/DateInput/DateInput.model';
import { Either } from 'fp-ts/lib/Either';

export type OrderEntryExecutionCheckboxes = {
	executePriceOpened: boolean;
	expireAtActive: boolean;
};

export enum ExecutionPriceValidationErrorMessages {
	General = '* GENERAL *',
	NotInRange = '* NOT_IN_RANGE *',
	OverRange = '* HI_RANGE *',
	BelowRange = '* BELOW_RANGE *',
	WrongStepping = '* WRONG_STEPPING *',
}

export type ExecutionPriceValidationError = {
	message: ExecutionPriceValidationErrorMessages;
	hiRange: string;
	loRange: string;
	step: string;
};

export type OrderEntryExecutionVisibleFormPrice = {
	priceValue: Option<number>;
	executionPriceValidated: Either<ExecutionPriceValidationError, number>;
};

export type OrderEntryExecutionVisibleFormDates = {
	expireAt: TDateInputValue;
	disabledDaysMin: Date;
};

export type StepperData = {
	priceIncrement: number;
	precision: number;
};
