import * as React from 'react';
import { MinusIcon } from '../../ui-desktop/ui-kit/components/icons/minus.icon';
import { PlusIcon } from '../../ui-desktop/ui-kit/components/icons/plus.icon';
import { withDefaults } from '@devexperts/react-kit/dist/utils/with-defaults';
export * from '@devexperts/react-kit/dist/components/NumericStepper/NumericStepper';
import {
	TSteppableInputProps,
	SteppableInput,
} from '../../ui-desktop/ui-kit/components/steppable-input/steppable-input';
import { KeyCode, TControlProps } from '@devexperts/react-kit/dist/components/Control/Control';
import { Input, TInputProps } from '@devexperts/react-kit/dist/components/input/Input';
import * as O from 'fp-ts/lib/Option';
import { PartialKeys } from '@devexperts/utils/dist/object';
import { withTheme } from '@devexperts/react-kit/dist/utils/withTheme';
import { ReactRef } from '@devexperts/react-kit/dist/utils/typings';
import { parseFloatWithLeadingZero } from './numeric-stepper.utils';
import { pipe } from 'fp-ts/lib/pipeable';
import { constant } from 'fp-ts/lib/function';

export const NUMERIC_STEPPER = Symbol('NumericStepper') as symbol;

export type TNumericStepperOwnProps = TSteppableInputProps &
	TControlProps<any> & {
		step: number;
		min: number;
		max: number;
		parser?: any;
		formatter?: any;
		manualEdit: boolean;
	};

export type TNumericStepperFullProps = TNumericStepperOwnProps & {
	SteppableInput: React.ComponentClass<TSteppableInputProps> | React.SFC<TSteppableInputProps>;
	theme: {
		SteppableInput?: TSteppableInputProps['theme'];
		Input?: TInputProps['theme'];
	};
};

type TNumericStepperState = {
	isFocused: boolean;
	displayedValue: any;
};

class RawNumericStepper extends React.Component<TNumericStepperFullProps, TNumericStepperState> {
	private inputRef!: ReactRef<HTMLElement>;
	private selectionStart!: number | null;
	private selectionEnd!: number | null;

	constructor(props: TNumericStepperFullProps) {
		super(props);

		const state: TNumericStepperState = {
			isFocused: false,
			displayedValue: props.value,
		};

		if (props.value) {
			state.displayedValue = RawNumericStepper.formatValue(props, props.value);
		}

		this.state = state;
	}

	static getDerivedStateFromProps(
		newProps: TNumericStepperFullProps,
		prevState: TNumericStepperState,
	): TNumericStepperState {
		return {
			...prevState,
			displayedValue: RawNumericStepper.formatValue(newProps, newProps.value),
		};
	}

	componentWillUpdate() {
		this.saveCaretPosition();
	}

	componentDidUpdate() {
		this.restoreCaretPosition();
	}

	render() {
		const { displayedValue } = this.state;

		const {
			theme,
			decrementIcon,
			incrementIcon,
			value,
			isDisabled,
			clearIcon,
			min,
			max,
			SteppableInput,
		} = this.props;

		const hasError = isNaN(value) || value < min || value > max;

		return (
			<SteppableInput
				isDisabled={isDisabled}
				error={hasError}
				theme={theme.SteppableInput}
				onDecrement={this.onDecrement}
				onIncrement={this.onIncrement}
				decrementIcon={decrementIcon}
				incrementIcon={incrementIcon}
				clearIcon={clearIcon}>
				<Input
					type="text"
					onKeyDown={this.onKeyDown}
					onFocus={this.onFocus}
					onWheel={this.onWheel}
					tabIndex={-1}
					error={hasError}
					theme={theme.Input}
					isDisabled={isDisabled}
					value={`${displayedValue}`}
					onBlur={this.onBlur}
					onValueChange={this.onInputChange}
					innerRef={ref => (this.inputRef = ref)}
				/>
			</SteppableInput>
		);
	}

	private getInnerInput = () => {
		// Input referred to div wrapper of actual input element,
		// so first child of the wrapper is our target
		const inputWrapper = this.inputRef as HTMLElement;
		return inputWrapper.firstChild as HTMLInputElement;
	};

	private saveCaretPosition = () => {
		const input = this.getInnerInput();

		this.selectionStart = input.selectionStart;
		this.selectionEnd = input.selectionEnd;
	};

	private restoreCaretPosition = () => {
		const input = this.getInnerInput();

		if (this.selectionStart || this.selectionStart === 0) {
			input.selectionStart = this.selectionStart;
		}

		if (this.selectionEnd || this.selectionEnd === 0) {
			input.selectionEnd = this.selectionEnd;
		}
	};

	private onKeyDown = (event: React.KeyboardEvent<HTMLElement>) => {
		const input = this.getInnerInput();
		const { displayedValue } = this.state;

		const value = String(displayedValue);
		const isSelectionsEqualAndHasValue = input.selectionStart === input.selectionEnd && value.length;

		switch (event.keyCode) {
			case KeyCode.Up:
				event.preventDefault();
				this.increase();
				break;
			case KeyCode.Down:
				event.preventDefault();
				this.decrease();
				break;
			case KeyCode.Backspace:
				if (
					isSelectionsEqualAndHasValue &&
					Number(input.selectionEnd) > 0 &&
					value.charAt(Number(input.selectionEnd) - 1) === '.'
				) {
					event.preventDefault();
					// jump over the dot
					input.selectionStart = input.selectionEnd = Number(input.selectionEnd) - 1;
				}
				break;
			case KeyCode.Delete:
				if (
					isSelectionsEqualAndHasValue &&
					Number(input.selectionEnd) < value.length - 1 &&
					value.charAt(Number(input.selectionEnd)) === '.'
				) {
					event.preventDefault();
					// jump over the dot
					input.selectionStart = input.selectionEnd = Number(input.selectionEnd) + 1;
				}
				break;
		}
	};

	private onInputChange = (value: string | undefined) => {
		if (!this.props.manualEdit) {
			return;
		}
		if (this.state.isFocused) {
			const newValue = this.parseValue(
				pipe(
					value,
					O.fromNullable,
					O.getOrElse(constant('')),
				),
			);
			this.onValueChange(newValue, true);
		}
	};

	private getPrecision(step: number): number {
		if (Number.isInteger(step)) {
			return 0;
		} else {
			return step.toString(10).split('.')[1].length;
		}
	}

	private step(n: number) {
		const { step } = this.props;
		const value = Number(this.props.value);

		const num = value + n * step;
		const precision = this.getPrecision(step);

		const frac = Math.pow(10, precision);
		const newValue = Math.round(num * frac) / frac;

		if (newValue !== value) {
			this.onValueChange(newValue, true);
		}
	}

	private onFocus = () => {
		this.setState({
			isFocused: true,
		});
	};

	private static formatValue(props: TNumericStepperFullProps, value: number | string): string {
		const { formatter } = props;
		return formatter ? formatter(value) : value;
	}

	/**
	 * Trying to parse the entered string to a number.
	 * Done here because `onChange` should return a valid numeric value if possible,
	 * e.g. for proper interop with `stateful`.
	 */
	private parseValue(value: string): string | number {
		const { parser } = this.props;

		if (parser) {
			return parser(value);
		} else {
			const num = Number(value);
			return isNaN(num) ? value : num;
		}
	}

	private onBlur = (event: React.FocusEvent<HTMLInputElement>) => {
		const { manualEdit } = this.props;
		if (!manualEdit) {
			return;
		}

		const { value } = event.target;
		const newValue = this.parseValue(value as string);

		this.onValueChange(newValue);
	};

	private onWheel = (event: React.WheelEvent<HTMLElement>) => {
		const { isDisabled } = this.props;
		const { isFocused } = this.state;

		if (!isDisabled && isFocused) {
			if (event.deltaY < 0) {
				this.increase();
			} else {
				this.decrease();
			}
		}
	};

	private decrease = () => {
		this.step(-1);
	};

	private increase = () => {
		this.step(1);
	};

	onIncrement = () => {
		this.increase();
	};

	onDecrement = () => {
		this.decrease();
	};

	onValueChange = (newValue: any, isFocused = false) => {
		const { onValueChange } = this.props;

		this.setState({
			isFocused,
			displayedValue: RawNumericStepper.formatValue(this.props, newValue),
		});

		onValueChange && onValueChange(newValue);
	};
}

export type TNumericStepperProps = PartialKeys<
	TNumericStepperFullProps,
	'theme' | 'SteppableInput' | 'step' | 'max' | 'min' | 'manualEdit'
>;

export const NumericStepper: React.ComponentClass<TNumericStepperProps> = withTheme(NUMERIC_STEPPER)(
	withDefaults<
		TNumericStepperFullProps,
		'SteppableInput' | 'step' | 'max' | 'min' | 'manualEdit' | 'decrementIcon' | 'incrementIcon' | 'parser'
	>({
		step: 1,
		manualEdit: true,
		min: Number.NEGATIVE_INFINITY,
		max: Number.POSITIVE_INFINITY,
		parser: parseFloatWithLeadingZero,
		decrementIcon: <MinusIcon />,
		incrementIcon: <PlusIcon />,
		SteppableInput,
	})(RawNumericStepper),
);
