import { LiveData } from '@devexperts/rx-utils/dist/rd/live-data.utils';
import { PositionsStore } from '../../store/positions/positions.store';
import { Option } from 'fp-ts/lib/Option';
import * as O from 'fp-ts/lib/Option';
import { pipe } from 'fp-ts/lib/pipeable';
import { combineContext, ask } from '@devexperts/rx-utils/dist/context.utils';
import { mapRD } from '@devexperts/rx-utils/dist/rd/operators/mapRD';
import { PositionTO } from '../../api/generated/swagger/definitions/PositionTO';
import { MetricsStore } from '../../store/metrics/metrics.store';
import { InstrumentStore } from '../../store/instrument/instrument.store';
import { switchMapRD } from '@devexperts/rx-utils/dist/rd/operators/switchMapRD';
import { fromOption, success } from '@devexperts/remote-data-ts';
import { PositionSide } from '../../ui-desktop/positions/components/position-table/position-table.model';
import { distinctUntilChanged, map, first } from 'rxjs/operators';
import { combineLatestRD } from '@devexperts/rx-utils/dist/rd/operators/combineLatestRD';
import { AccountViewModel } from '../account/account.view-model';
import { MarketDataStore } from '../../store/market-data/market-data.store';
import { ConversionRateStore } from '../../store/conversion-rate/conversion-rate.store';
import { getCurrentPrice } from './position-entity.utils';
import { distinctRDOptionNumber, distinctRDString, distinctRDNumber } from '../../utils/setoid.utils';

type PositionData = {
	uid: string;
	quantity: number;
	side: PositionSide;
	size: Option<number>;
	openPrice: Option<number>;
	stopLoss: Option<number>;
	takeProfit: Option<number>;
	precision: number;
};

export type PositionEntityViewModel = {
	position$: LiveData<Error, PositionTO>;
	profitLoss$: LiveData<Error, Option<number>>;
	symbol$: LiveData<Error, string>;
	value$: LiveData<Error, Option<number>>;
	data$: LiveData<Error, PositionData>;
};

type PositionEntityViewModelContext = {
	positionsStore: PositionsStore;
	metricsStore: MetricsStore;
	instrumentStore: InstrumentStore;
	marketDataStore: MarketDataStore;
	conversionRateStore: ConversionRateStore;
	accountViewModel: AccountViewModel;
};

type CreatePositionEntityViewModel = (uid: string) => PositionEntityViewModel;

export const createPositionEntityViewModel = combineContext(
	ask<PositionEntityViewModelContext>(),
	(context): CreatePositionEntityViewModel => (uid: string) => {
		const {
			positionsStore: { getPositionByUid },
			metricsStore: { getMetricsByUid },
			marketDataStore: { getDataBySymbol },
			instrumentStore: { getBySymbol, getById },
			conversionRateStore: { getRateBySymbol },
			accountViewModel,
		} = context;

		const getSymbolByInstrumentId = (instrumentId: number): LiveData<Error, string> =>
			getById(instrumentId).pipe(mapRD(({ symbol }) => symbol));

		const position$ = getPositionByUid(uid);

		const quantity$ = position$.pipe(
			mapRD(({ quantity }) => quantity),
			distinctRDNumber,
		);

		const costBasis$ = position$.pipe(
			mapRD(({ costBasis }) => costBasis),
			first(),
		);

		const stopLoss$ = position$.pipe(
			mapRD(({ stopLoss }) =>
				pipe(
					stopLoss,
					O.chain(({ fixedPrice }) => fixedPrice),
				),
			),
			distinctRDOptionNumber,
		);

		const takeProfit$ = position$.pipe(
			mapRD(({ takeProfit }) =>
				pipe(
					takeProfit,
					O.chain(({ fixedPrice }) => fixedPrice),
				),
			),
			distinctRDOptionNumber,
		);

		const uid$ = position$.pipe(
			mapRD(({ uid }) => uid),
			first(),
		);

		const profitLoss$ = getMetricsByUid(uid).pipe(
			mapRD(({ plOpen }) => plOpen),
			distinctRDOptionNumber,
		);

		const symbol$ = position$.pipe(
			switchMapRD(({ positionKey: { instrumentId } }) => getSymbolByInstrumentId(instrumentId)),
			first(),
		);

		const baseCurrency$ = accountViewModel.current$.pipe(
			distinctUntilChanged((a, b) => a.baseCurrency === b.baseCurrency),
			map(currentAccount => success<Error, string>(currentAccount.baseCurrency)),
		);

		const instrumentData$ = symbol$.pipe(switchMapRD(symbol => getBySymbol(symbol)));

		const currency$ = instrumentData$.pipe(
			map(instrumentRD =>
				instrumentRD.chain(instrument =>
					fromOption(instrument.currency, () => new Error('There is no currency in this instrument')),
				),
			),
			distinctRDString,
		);

		const precision$ = instrumentData$.pipe(
			mapRD(({ precision }) => precision),
			first(),
		);

		const conversionRate$ = combineLatestRD(currency$, baseCurrency$).pipe(
			switchMapRD(([currency, baseCurrency]) => getRateBySymbol(currency, baseCurrency)),
		);

		const value$ = combineLatestRD(symbol$, quantity$).pipe(
			switchMapRD(([symbol, quantity]) =>
				combineLatestRD(getDataBySymbol(symbol), conversionRate$).pipe(
					mapRD(([marketData, conversionRateData]) => {
						const { conversionRate } = conversionRateData;
						const isQuantityPositive = quantity > 0;
						return pipe(
							conversionRate,
							O.chain(rate => {
								const currentPrice = getCurrentPrice(isQuantityPositive)(marketData);
								return pipe(
									currentPrice,
									O.chain(price =>
										pipe(
											rate.value,
											O.map(value => Math.abs(quantity * price * value)),
										),
									),
								);
							}),
						);
					}),
					distinctRDOptionNumber,
				),
			),
		);

		const data$ = combineLatestRD(precision$, quantity$, costBasis$, stopLoss$, takeProfit$, uid$).pipe(
			mapRD(([precision, quantity, costBasis, stopLoss, takeProfit, uid]) => {
				const isQuantityPositive = quantity > 0;
				const side = isQuantityPositive ? PositionSide.Bought : PositionSide.Sold;
				return {
					uid,
					quantity,
					side,
					size: O.fromNullable(Math.abs(quantity)),
					openPrice: O.fromNullable(Math.abs(costBasis)),
					stopLoss,
					takeProfit,
					precision,
				};
			}),
		);

		return {
			position$,
			profitLoss$,
			symbol$,
			value$,
			data$,
		};
	},
);
