import * as React from 'react';
import { PureComponent } from 'react';
import { WithTranslation, withTranslation } from 'react-i18next';
import * as cn from 'classnames';
import { CheckIcon } from '../../../icons/check.icon';
import { Mode, MenuAccountData } from '../../../../ui-desktop/layout/components/menu/components/menu/menu.model';
import { PartialKeys } from '@devexperts/utils/dist/object';
import { RawMenuListItemProps } from '../menu-list-item/menu-list-item.component';
import { withTheme } from '@devexperts/react-kit/dist/utils/withTheme';
import { MakeTheme } from '../../../../utils/theme.utils';
import * as css from './theme/menu-account.module.css';

export type RawMenuAccountProps = {
	theme: MakeTheme<'container' | 'container_selected' | 'status' | 'status_demo' | 'currency' | 'id' | 'icon'>;
	data: MenuAccountData;
	isSelected: boolean;
};

class MenuAccountRaw extends PureComponent<RawMenuAccountProps & WithTranslation> {
	render() {
		const { isSelected, data, theme } = this.props;
		const { currency, accountId, status } = data;

		const statusTheme = cn(theme.status, { [css.status_demo]: status === Mode.Demo });
		const containerTheme = cn(theme.container, { [css.container_selected]: isSelected });

		return (
			<div className={containerTheme}>
				<div className={theme.currency}>{currency}</div>
				<div className={theme.id}>{accountId}</div>
				<div className={statusTheme}>{this.getText(status)}</div>
				{isSelected && (
					<i className={theme.icon}>
						<CheckIcon />
					</i>
				)}
			</div>
		);
	}

	getText = (status: Mode): string => {
		const { t } = this.props;
		switch (status) {
			case Mode.DemoLive:
			case Mode.Demo: {
				return t('menuAccountDemo', 'DEMO');
			}
			case Mode.Regular:
			case Mode.Real: {
				return t('menuAccountReal', 'REAL');
			}
		}
	};
}

const wTheme = {
	...css,
};

export type MenuAccountProps = PartialKeys<RawMenuListItemProps, 'theme'>;
export const MenuAccount = withTranslation()(withTheme(Symbol(), wTheme)(MenuAccountRaw));
