import * as React from 'react';
import { FunctionComponent, ReactNode } from 'react';
import { ThemeProvider, TTheme } from '@devexperts/react-kit/dist/utils/withTheme';

import '../preloader/theme/preloader-window.module.css';
import 'reset-css';
import './theme.module.css';

import { CONTEXT_THEME } from '../../../../config/theme.config';

export type TThemeProps = {
	children?: ReactNode;
	theme?: TTheme;
};

export const Theme: FunctionComponent<TThemeProps> = props => {
	const theme = props.theme || CONTEXT_THEME;
	return <ThemeProvider theme={theme}>{props.children}</ThemeProvider>;
};
