import { HeaderAccountMetrics } from '../../components/header/header-account-metrics/header-account-metrics.component';
import { withRXWithDefaults } from '../../../../utils/withRXWithDefaults';
import { none } from 'fp-ts/lib/Option';
import { ask, combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { map, distinctUntilChanged } from 'rxjs/operators';
import { AccountViewModel } from '../../../../view-model/account/account.view-model';
import { currencyStringToSymbol } from '../../../../utils/currency.utils';
import { setoidOptionNumber } from '../../../../utils/setoid.utils';

export type HeaderAccountMetricsContainerContext = {
	accountViewModel: AccountViewModel;
};

export const HeaderAccountMetricsContainer = combineContext(ask<HeaderAccountMetricsContainerContext>(), e =>
	withRXWithDefaults(HeaderAccountMetrics)(() => {
		const { accountMetrics$, current$, equity$ } = e.accountViewModel;
		const availableFunds$ = accountMetrics$.pipe(
			map(accountMetrics => accountMetrics.availableFunds),
			distinctUntilChanged(setoidOptionNumber.equals),
		);
		const margin$ = accountMetrics$.pipe(
			map(accountMetrics => accountMetrics.margin),
			distinctUntilChanged(setoidOptionNumber.equals),
		);
		const profitLoss$ = accountMetrics$.pipe(
			map(accountMetrics => accountMetrics.profitLoss),
			distinctUntilChanged(setoidOptionNumber.equals),
		);
		const currencySymbol$ = current$.pipe(
			map(currentAccount => currencyStringToSymbol(currentAccount.baseCurrency)),
			distinctUntilChanged(),
		);

		return {
			props: {
				available: availableFunds$,
				totalAllocated: margin$,
				profit: profitLoss$,
				equity: equity$,
				currencySymbol: currencySymbol$,
			},
			defaultProps: {
				available: none,
				totalAllocated: none,
				profit: none,
				equity: none,
				currencySymbol: '',
			},
		};
	}),
);
