import { withRXWithDefaults } from '../../../../utils/withRXWithDefaults';
import {
	createOrderEntryViewModel,
	OrderEntryViewModel,
} from '../../../../view-model/order-entry/order-entry.view-model';
import { runOnMount } from '../../../../utils/react.utils';
import { pending } from '@devexperts/remote-data-ts';
import { OrderEntryPanel } from '../../components/order-entry-panel/order-entry-panel.component';
import { mapRD } from '@devexperts/rx-utils/dist/rd/operators/mapRD';
import { toOrderEntryPanelData, getEitherFromSideAndValue } from './order-entry-panel.utils';
import * as O from 'fp-ts/lib/Option';
import * as E from 'fp-ts/lib/Either';
import { combineContext, ask, deferContext } from '@devexperts/rx-utils/dist/context.utils';
import { createSelector } from 'reselect';
import { map } from 'rxjs/operators';
import { combineLatestRD } from '@devexperts/rx-utils/dist/rd/operators/combineLatestRD';
import { merge } from 'rxjs';
import { pipe } from 'fp-ts/lib/pipeable';

export type OrderEntryContainerContext = {
	orderEntryViewModel: OrderEntryViewModel;
};

const Container = combineContext(OrderEntryPanel, ask<OrderEntryContainerContext>(), (OrderEntryPanel, e) =>
	withRXWithDefaults(OrderEntryPanel)(() => {
		const { orderEntryViewModel } = e;
		const limitsOrderSize$ = combineLatestRD(
			orderEntryViewModel.amountMinOrderSize$,
			orderEntryViewModel.amountMaxOrderSize$,
		);
		const fullData$ = combineLatestRD(
			limitsOrderSize$,
			orderEntryViewModel.amountValidated$,
			orderEntryViewModel.data$,
			orderEntryViewModel.value$,
			orderEntryViewModel.amountPrecision$,
			orderEntryViewModel.unitName$,
		).pipe(
			mapRD(([[minOrderSize, maxOrderSize], amountValidated, viewModelData, value, amountPrecision, unitName]) =>
				toOrderEntryPanelData(
					viewModelData,
					minOrderSize,
					maxOrderSize,
					amountValidated,
					value,
					amountPrecision,
					unitName,
				),
			),
		);

		return {
			defaultProps: {
				data: pending,
				lot: O.none,
				accountBalance: O.none,
				result: 0,
				onValueChange: value => orderEntryViewModel.onValueChange(value),
				onFooterButtonClick: orderEntryViewModel.onSend,
				moreOptionsOpened: false,
				onMoreOptionsClick: orderEntryViewModel.onToggleMoreOptions,
				error: O.none,
				isValid: true,
				isShownValidErrors: false,
				currencySymbol: '',
				isSubmitAllowed: pending,
				isEditAllowed: pending,
				previewMode: O.some(false),
			},
			props: {
				data: fullData$,
				lot: orderEntryViewModel.lot$,
				accountBalance: orderEntryViewModel.accountBalance$,
				result: orderEntryViewModel.valueSum$,
				moreOptionsOpened: orderEntryViewModel.moreOptionsOpened$,
				error: orderEntryViewModel.error$.pipe(
					map(error =>
						pipe(
							error,
							O.map(error => error.message),
						),
					),
				),
				isValid: orderEntryViewModel.isValid$,
				isShownValidErrors: orderEntryViewModel.isShownValidErrors$,
				currencySymbol: orderEntryViewModel.currencySymbol$,
				isSubmitAllowed: merge(orderEntryViewModel.isFundsEnough$, orderEntryViewModel.isEmitingOrdersAllowed$),
				isEditAllowed: orderEntryViewModel.isOrderEntryValuesChanged$,
			},
		};
	}),
);

export const OrderEntryPanelContainer = combineContext(
	createOrderEntryViewModel,
	deferContext(Container, 'orderEntryViewModel'),
	(createOrderEntryViewModel, Container) =>
		runOnMount(Container, props => {
			const symbol = createSelector(
				props,
				props => props.symbol,
			);
			const action = createSelector(
				props,
				props => props.action,
			);
			const elementId = createSelector(
				props,
				props => props.elementId.value,
			);
			const isElementIdLeft = createSelector(
				props,
				props => E.isLeft(props.elementId),
			);
			const plKind = createSelector(
				props,
				props => props.plKind,
			);
			return createSelector(
				symbol,
				action,
				elementId,
				isElementIdLeft,
				plKind,
				(symbol, action, elementIdValue, isElementIdLeft, plKind) => {
					const elementId = getEitherFromSideAndValue(isElementIdLeft, elementIdValue);

					return createOrderEntryViewModel(symbol, action, elementId, plKind).map(orderEntryViewModel => ({
						orderEntryViewModel,
					}));
				},
			);
		}),
);
