import { Option } from 'fp-ts/lib/Option';
import { number, string, type } from 'io-ts';
import { createOptionFromNullable } from 'io-ts-types';

export type ConversionRateTO = {
	quantity: Option<number>;
	from: Option<string>;
	to: Option<string>;
	value: Option<number>;
};
export const ConversionRateTOIO = type({
	quantity: createOptionFromNullable(number),
	from: createOptionFromNullable(string),
	to: createOptionFromNullable(string),
	value: createOptionFromNullable(number),
});
