import * as React from 'react';
import { WithTranslation, withTranslation } from 'react-i18next';
import { combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { RemoteData } from '@devexperts/remote-data-ts';
import { routes } from '../../../../ui-common/routes';
import { OrderDetailsData } from '../../../../ui-common/order-details/order-details.model';
import { SidePanel } from '../../../../ui-common/side-panel/side-panel.component';
import { RouteLink } from '../../../../ui-common/route-link/containers/route-link.container';
import { CloseButton } from '../../../../ui-common/close-button/close-button.component';
import { RemoteDataState } from '../../../../ui-common/remote-data-state/remote-data-state.component';
import { tspan } from '../../../../utils/i18n.utils';
import { OrderDetailsCommon } from '../../../../ui-common/order-details/order-details.component';
import { memo } from 'react';
import * as css from './theme/order-details.module.css';
import * as scrollableCss from './theme/scrollable.child.module.css';
import * as sidePanelCss from './theme/side-panel.child.module.css';

const sidePanelTheme = { SidePanel: sidePanelCss, Scrollable: scrollableCss };

type OrderDetailsProps = {
	data: RemoteData<Error, OrderDetailsData>;
};

export const OrderDetails = combineContext(RouteLink, OrderDetailsCommon, (RouteLink, OrderDetailsCommon) =>
	withTranslation()(
		memo((props: OrderDetailsProps & WithTranslation) => {
			const { data } = props;

			const renderHeader = (orderId: number) => (
				<div className={css.header}>
					<span className={css.widgetLabel}>{tspan('orderDetailsOrderInfo', 'Order Info')}</span>
					<RouteLink
						to={routes.orders.order.root}
						value={{ orderId }}
						render={({ onNavigate }) => (
							<div className={css.closeBtnWrapper}>
								<CloseButton onClick={onNavigate} />
							</div>
						)}
					/>
				</div>
			);

			const renderSuccess = (localData: OrderDetailsData) => {
				const { orderId } = localData;
				return (
					<SidePanel header={renderHeader(orderId)} scrollable theme={sidePanelTheme}>
						<OrderDetailsCommon data={data} />
					</SidePanel>
				);
			};

			return <RemoteDataState data={data} success={renderSuccess} />;
		}),
	),
);
