import { ask, combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { withRXWithDefaults } from '../../../../utils/withRXWithDefaults';
import { AvaSystemSettingsTO } from '../../../../api/generated/swagger/definitions/AvaSystemSettingsTO';
import { Layout } from '../../components/layout/layout.component';
import { AccountViewModel } from '../../../../view-model/account/account.view-model';
import { SystemSettingsViewModel } from '../../../../view-model/system-settings/system-settings.view-model';
import { Router } from '../../../../utils/router.utils';
import { combineLatest, merge, of } from 'rxjs';
import { distinctUntilChanged, filter, map, pluck, tap, mapTo, switchMap } from 'rxjs/operators';
import { Route } from 'fp-ts-routing';
import * as O from 'fp-ts/lib/Option';
import { Pushwoosh } from 'web-push-notifications';
import { Sink } from '@devexperts/rx-utils/dist/sink.utils';
import { string } from 'io-ts';
import i18next from 'i18next';
import { loadScript } from '../../../../utils/load-script.utils';

export type LayoutContainerContext = {
	accountViewModel: AccountViewModel;
	systemSettingsViewModel: SystemSettingsViewModel;
	router: Router;
};

const PushwooshInstance = new Pushwoosh();

export const LayoutContainer = combineContext(Layout, ask<LayoutContainerContext>(), (Layout, e) => {
	const availableAccounts$ = e.systemSettingsViewModel.availableAccounts$;
	const platformType$ = e.systemSettingsViewModel.platformType$;

	const query$ = of(e.router.state).pipe(
		map(state => Route.parse(state.location.search)),
		map(({ query }) => query),
	);

	const enableLokaliseTranslation$ = query$.pipe(
		pluck('translate'),
		filter(value => string.is(value) && value.toLowerCase() === 'true'),
		tap(() => {
			Object.assign(window, {
				LOKALISE_CONFIG: {
					projectId: String(process.env.REACT_APP_LOKALISE_PROJECT_ID),
					locale: i18next.language,
					disableAdvancedHtml: true,
				},
			});
		}),
		mapTo(`https://lokalise.co/live-js/script.min.js?${new Date().getTime()}`),
		switchMap(loadScript),
	);

	const customerId$ = query$.pipe(
		pluck('customer_id'),
		filter(string.is),
		distinctUntilChanged(),
	);

	const pushWooshInfo$ = combineLatest(availableAccounts$, customerId$, platformType$).pipe(
		tap(([availableAccounts, customerId, platformType]) => {
			const codes: { [k in AvaSystemSettingsTO['platformType']]: string | undefined } = {
				PROD: process.env.REACT_APP_PUSHWOOSH_APPLICATION_CODE_PROD,
				DEV: process.env.REACT_APP_PUSHWOOSH_APPLICATION_CODE_QA,
			};
			const applicationCode = O.fromNullable(codes[platformType]);

			if (O.isSome(applicationCode)) {
				PushwooshInstance.push([
					'init',
					{
						applicationCode: applicationCode.value,
						logLevel: 'error',
						safariWebsitePushID: 'web.com.example.domain', //TODO after settings pushwoosh crm add id
						defaultNotificationTitle: 'Ava-trade',
						autoSubscribe: true,
						userId: customerId,
						tags: {
							AccountID: availableAccounts.map(data => `${data.serverName}:${data.accountId}`),
						},
					},
				]);
			}
		}),
	);

	// this effects for different custom api libs for register data
	const effects$ = merge(pushWooshInfo$, enableLokaliseTranslation$);

	return new Sink(
		withRXWithDefaults(Layout)(() => {
			const { accountViewModel } = e;
			const { preloader$ } = accountViewModel;

			return {
				defaultProps: {
					loader: false,
					previewMode: O.some(false),
				},
				props: {
					loader: preloader$,
				},
			};
		}),
		effects$,
	);
});
