import * as React from 'react';
import { PureComponent } from 'react';
import { withTranslation, WithTranslation } from 'react-i18next';
import cn from 'classnames';
import { isNotNullable } from '@devexperts/utils/dist/object';
import { Selectbox } from '../../ui-desktop/ui-kit/components/selectbox/selectbox.component';
import { MenuItem } from '../../ui-desktop/ui-kit/components/menu/menu.component';
import { CheckIcon } from '../icons/check.icon';
import { LocaleSelectorAnchor } from './locale-selector-anchor.component';
import { Locales, localeTitles, localesOptions } from './locale-selector.model';
import { getFlagIcon, dispatchLocaleChangeEvent, isLocale, getFallbackLanguage } from './locale-selector.utils';
import * as css from './theme/locale-selector.module.css';
import * as selectboxCss from './theme/selectbox.child.module.css';
import * as menuItemCss from './theme/menu-item.child.module.css';

type LocaleSelectorState = Readonly<{
	isOpened: boolean;
}>;

class LocaleSelectorUnwrapped extends PureComponent<WithTranslation, LocaleSelectorState> {
	readonly state: LocaleSelectorState = {
		isOpened: false,
	};

	private toggleLocaleSelectorState = (isOpened: boolean | undefined) =>
		isNotNullable(isOpened) && this.setState({ isOpened });

	private handleLocaleSelect = (locale?: string | number) => {
		if (isLocale(locale)) {
			this.changeLocale(locale);
			dispatchLocaleChangeEvent(locale).run();
		}
	};

	private changeLocale(locale: Locales) {
		// do not destructure 'i18n' prop as it's relays on 'this' keyword under the hood
		const { i18n } = this.props;
		const currentLocale = i18n.language;

		if (!isNotNullable(locale)) {
			i18n.changeLanguage(currentLocale);
			return;
		}

		i18n.changeLanguage(locale).catch(() => i18n.changeLanguage(currentLocale));
	}

	render() {
		const { i18n } = this.props;
		const { isOpened } = this.state;
		const currentLocale = getFallbackLanguage(i18n.language);

		return (
			<Selectbox
				isOpened={isOpened}
				onToggle={this.toggleLocaleSelectorState}
				value={currentLocale}
				onValueChange={this.handleLocaleSelect}
				Anchor={LocaleSelectorAnchor}
				theme={selectboxCss}>
				{localesOptions.map(locale => {
					const isActive = locale === currentLocale;
					const itemClassName = cn(css.localeInSelector, { [css.isCurrentLocale]: isActive });

					return (
						<MenuItem key={locale} theme={menuItemCss} value={locale} isActive={isActive}>
							<div className={itemClassName}>
								<div className={css.flag}>{getFlagIcon(locale)}</div>
								{localeTitles[locale]}
							</div>
							{isActive && (
								<i className={css.icon}>
									<CheckIcon />
								</i>
							)}
						</MenuItem>
					);
				})}
			</Selectbox>
		);
	}
}

export const LocaleSelector = withTranslation()(LocaleSelectorUnwrapped);
