import { Observable, of } from 'rxjs';
import { asks } from '@devexperts/rx-utils/dist/context.utils';
import { Router } from '../../utils/router.utils';
import { merge } from 'rxjs';
import { map, mapTo, filter, first } from 'rxjs/operators';
import { Route } from 'fp-ts-routing';
import { createHandler } from '@devexperts/rx-utils/dist/create-handler.utils';
import { PostMessageClient } from '../../api/post-message.client';
import { isClosePopup, isRedirectToCRMDeposit } from '../../api/post-message/post-message.to';
import { or } from 'fp-ts/lib/function';

export type WelcomeViewModel = {
	shouldOpenPopup$: Observable<boolean>;
	onClose: () => void;
};

type WelcomeViewModelContext = {
	router: Router;
	postMessageClient: PostMessageClient;
};

type CreateWelcomeViewModel = () => WelcomeViewModel;

export const createWelcomeViewModel = asks(
	(ctx: WelcomeViewModelContext): CreateWelcomeViewModel => {
		const { router, postMessageClient } = ctx;

		const openCloseHandler = createHandler();

		const initState$ = of(router.state.location.search).pipe(
			map(locationSearch => Route.parse(locationSearch)),
			map(({ query }) => query.show_as_popup === 'true'),
		);

		const closePopupFromPostMessage$ = postMessageClient.postMessage$.pipe(
			filter(or(isClosePopup, isRedirectToCRMDeposit)),
		);

		const handledState$ = merge(openCloseHandler.value$, closePopupFromPostMessage$).pipe(
			mapTo(false),
			first(),
		);

		return () => ({
			shouldOpenPopup$: merge(initState$, handledState$),
			onClose: () => openCloseHandler.handle(undefined),
		});
	},
);
