import { CrossToolMode, ChartType } from './toolbar.model';

export const typeToString = (type: ChartType): string => {
	switch (type) {
		case ChartType.Line:
			return 'line';
		case ChartType.Bar:
			return 'bar';
		case ChartType.Candle:
			return 'candle';
	}
};

export const toType = (type: string): ChartType => {
	switch (type) {
		case 'line':
			return ChartType.Line;
		case 'bar':
			return ChartType.Bar;
		case 'candle':
			return ChartType.Candle;
		default:
			return ChartType.Candle;
	}
};

export const modeToString = (mode: CrossToolMode): string => {
	switch (mode) {
		case CrossToolMode.Disabled:
			return 'no crosshair';
		case CrossToolMode.Crosshair:
			return 'crosshair';
	}
};

export const booleanToMode = (isEnabled: boolean): CrossToolMode =>
	isEnabled ? CrossToolMode.Crosshair : CrossToolMode.Disabled;

export const isCrosshair = (mode: CrossToolMode) => mode === CrossToolMode.Crosshair;

export const toMode = (mode: string): CrossToolMode => {
	switch (mode) {
		case 'no crosshair':
			return CrossToolMode.Disabled;
		case 'crosshair':
			return CrossToolMode.Crosshair;
		default:
			return CrossToolMode.Disabled;
	}
};
