import { Option } from 'fp-ts/lib/Option';
import * as O from 'fp-ts/lib/Option';
import { ProtectionOrderTO } from '../../api/generated/swagger/definitions/ProtectionOrderTO';
import {
	OrderEntryTakeProfitStopLossData,
	ProfitLossKind,
	ProvidedLabelType,
	TakeProfitStopLossData,
	TakeProfitStopLossOptions,
	OrderKind,
	PriceValidationError,
	PriceValidationErrorMessages,
} from './takeprofit-stoploss.model';
import { OrderEntrySide } from '../order-entry/order-entry.model';
import { MarketDataEntity } from '../../store/market-data/market-data.store';
import { formatNumber } from 'accounting';
import { left, right, Either } from 'fp-ts/lib/Either';
import { OrderTO } from '../../api/generated/swagger/definitions/OrderTO';
import { pipe } from 'fp-ts/lib/pipeable';
import { constant } from 'fp-ts/lib/function';

const preseededTPSLData = {
	isSelected: false,
	isDisabled: false,
	rawPrice: 0,
	validatedPrice: right<PriceValidationError, number>(0),
	minLimit: -Infinity,
	maxLimit: Infinity,
	step: 0,
	value: 0,
	possiblePL: 0,
	isValueManuallyChanged: false,
	providedLabel: ProvidedLabelType.Max,
	providedLimit: Infinity,
	instrumentPrecision: 5,
};

export const getDefaultTakeProfitStopLossData = (data: {
	hasTP: Option<number>;
	hasSL: Option<number>;
}): OrderEntryTakeProfitStopLossData => ({
	takeProfitData: {
		...preseededTPSLData,
		kind: ProfitLossKind.Profit,
		isSelected: O.isSome(data.hasTP),
	},
	stopLossData: {
		...preseededTPSLData,
		kind: ProfitLossKind.Loss,
		isSelected: O.isSome(data.hasSL),
	},
});

export const calcTakeProfitStopLossPrice = (symbolPrice: number, enteredPrice: number): number => {
	if (symbolPrice === 0) {
		return 0;
	}

	return (enteredPrice / symbolPrice - 1) * 100;
};

export const calcPossiblePL = (symbolPrice: number, enteredPrice: number, amount: number, rate: number): number => {
	return Math.abs(enteredPrice - symbolPrice) * amount * rate;
};

export const roundPriceWithPrecision = (price: number, precision: number): number => Number(price.toFixed(precision));

const getOrderType = (plKind: ProfitLossKind): string => {
	switch (plKind) {
		case ProfitLossKind.Profit:
			return 'LIMIT';
		case ProfitLossKind.Loss:
			return 'STOP';
	}
};

export const mapTakeProfitStopLossPayload = (
	data: TakeProfitStopLossData,
	precision: number,
): Option<ProtectionOrderTO> => {
	if (!data.isSelected) {
		return O.none;
	}

	const priceRounded = roundPriceWithPrecision(data.value, precision);

	const preliminaryValue = {
		fixedPrice: O.some(priceRounded),
		fixedOffset: O.none,
		priceFixed: true,
		refOrderChainId: O.none,
		requestId: O.none,
	};

	const orderType = getOrderType(data.kind);

	return O.some({
		...preliminaryValue,
		orderType,
	});
};

const calcDefaultTakeProfitValue = (
	side: OrderEntrySide,
	price: number,
	distance: number,
	bid: number,
	ask: number,
	orderType: OrderTO['type'],
): number => {
	const multiplier = 4 * distance;

	switch (side) {
		case OrderEntrySide.Buy:
			if (orderType === 'MARKET') {
				return ask + multiplier;
			}
			return price + multiplier;

		case OrderEntrySide.Sell:
			if (orderType === 'MARKET') {
				return bid - multiplier;
			}
			return price - multiplier;
	}
};

const calcDefaultStopLossValue = (
	side: OrderEntrySide,
	price: number,
	distance: number,
	bid: number,
	ask: number,
	orderType: OrderTO['type'],
): number => {
	const multiplier = 4 * distance;

	switch (side) {
		case OrderEntrySide.Buy:
			if (orderType === 'MARKET') {
				return bid - multiplier;
			}
			return price - multiplier;

		case OrderEntrySide.Sell:
			if (orderType === 'MARKET') {
				return ask + multiplier;
			}
			return price + multiplier;
	}
};

export const getDefaultValueForTakeProfitStopLoss = (
	side: OrderEntrySide,
	PLKind: ProfitLossKind,
	price: number,
	distance: number,
	bid: number,
	ask: number,
	orderType: OrderTO['type'],
	step: number,
): number => {
	switch (PLKind) {
		case ProfitLossKind.Profit: {
			const calculated = calcDefaultTakeProfitValue(side, price, distance, bid, ask, orderType);
			return calculated < step ? step : calculated;
		}

		case ProfitLossKind.Loss: {
			const calculated = calcDefaultStopLossValue(side, price, distance, bid, ask, orderType);
			return calculated < step ? step : calculated;
		}
	}
};

const calcTakeProfitMinMaxValues = (
	side: OrderEntrySide,
	price: number,
	bid: number,
	ask: number,
	distance: number,
	orderType: OrderTO['type'],
): [ProvidedLabelType, number] => {
	switch (side) {
		case OrderEntrySide.Buy:
			if (orderType === 'MARKET') {
				return [ProvidedLabelType.Min, ask + distance];
			}

			return [ProvidedLabelType.Min, price + distance];

		case OrderEntrySide.Sell:
			if (orderType === 'MARKET') {
				return [ProvidedLabelType.Max, bid - distance];
			}

			return [ProvidedLabelType.Max, price - distance];
	}
};

const calcStopLossMinMaxValues = (
	side: OrderEntrySide,
	price: number,
	bid: number,
	ask: number,
	distance: number,
	orderType: OrderTO['type'],
): [ProvidedLabelType, number] => {
	switch (side) {
		case OrderEntrySide.Buy: {
			if (orderType === 'MARKET') {
				return [ProvidedLabelType.Max, bid - distance];
			}

			return [ProvidedLabelType.Max, price - distance];
		}

		case OrderEntrySide.Sell: {
			if (orderType === 'MARKET') {
				return [ProvidedLabelType.Min, ask + distance];
			}

			return [ProvidedLabelType.Min, price + distance];
		}
	}
};

export const getLabelAndMinMaxValuesForTakeProfitStopLoss = (
	side: OrderEntrySide,
	PLKind: ProfitLossKind,
	price: number,
	bid: number,
	ask: number,
	distance: number,
	orderType: OrderTO['type'],
): [ProvidedLabelType, number] => {
	switch (PLKind) {
		case ProfitLossKind.Profit:
			return calcTakeProfitMinMaxValues(side, price, bid, ask, distance, orderType);

		case ProfitLossKind.Loss:
			return calcStopLossMinMaxValues(side, price, bid, ask, distance, orderType);
	}
};

export const getCurrentPrice = (isQuantityPositive: boolean) => (marketData: MarketDataEntity): Option<number> => {
	return isQuantityPositive
		? pipe(
				marketData.lastQuote,
				O.chain(quote => quote.ask),
		  )
		: pipe(
				marketData.lastQuote,
				O.chain(quote => quote.bid),
		  );
};

export const getUpdatedProtectionOrderOption = (
	protection: Option<ProtectionOrderTO>,
	takeProfitStopLossData: TakeProfitStopLossData,
	precision: number,
): Option<ProtectionOrderTO> => {
	if (takeProfitStopLossData.isSelected) {
		const fixedPrice = O.some(roundPriceWithPrecision(takeProfitStopLossData.value, precision));
		const orderType = getOrderType(takeProfitStopLossData.kind);

		const defaultProtection = O.some({
			orderType,
			fixedPrice,
			fixedOffset: O.none,
			priceFixed: true,
			refOrderChainId: O.none,
			requestId: O.none,
		});

		return pipe(
			protection,
			O.fold(constant(defaultProtection), possibleProtection =>
				O.some({
					...possibleProtection,
					fixedPrice,
				}),
			),
		);
	}

	return O.none;
};

export const getMinMaxLimit = (
	providedLabel: ProvidedLabelType,
	providedLimit: number,
	step: number,
): [number, number] => {
	switch (providedLabel) {
		case ProvidedLabelType.Min:
			return [providedLimit, Infinity];
		case ProvidedLabelType.Max:
			return [step, providedLimit];
	}
};

const getValidatedPrice = (
	value: number,
	minLimit: number,
	maxLimit: number,
	instrumentPrecision: number,
	isEnabled: boolean = true,
): Either<PriceValidationError, number> => {
	// pass validation if no protection is selected, for example
	if (!isEnabled) {
		return right(value);
	}

	if (value < minLimit) {
		return left({
			message: PriceValidationErrorMessages.MoreOrEqual,
			value: formatNumber(minLimit, instrumentPrecision),
		});
	}
	if (value > maxLimit) {
		return left({
			message: PriceValidationErrorMessages.LessOrEqual,
			value: formatNumber(maxLimit, instrumentPrecision),
		});
	}
	return right(value);
};

const getRefPriceForPossiblePL = (
	orderKind: OrderKind,
	{ costBasis, parentPrice }: { costBasis: number; parentPrice: number },
): number => {
	switch (orderKind) {
		case OrderKind.Order:
			return parentPrice;
		case OrderKind.Position:
			return costBasis;
	}
};

export const getTakeProfitStopLossValue = ({
	currentTakeProfitData: takeProfitData,
	currentStopLossData: stopLossData,
	prevTakeProfitFixedPrice: prevTakeProfitValue,
	prevStopLossFixedPrice: prevStopLossValue,
	parentOrderPrice: parentPrice,
	bid,
	ask,
	orderType,
	side,
	priceIncrement: step,
	precision: instrumentPrecision,
	rate,
	distance,
	amount,
	orderKind,
	costBasis,
}: TakeProfitStopLossOptions): OrderEntryTakeProfitStopLossData => {
	// recalc preentered value
	const preenteredTPValue = pipe(
		prevTakeProfitValue,
		O.getOrElse(() =>
			getDefaultValueForTakeProfitStopLoss(
				side,
				takeProfitData.kind,
				parentPrice,
				distance,
				bid,
				ask,
				orderType,
				step,
			),
		),
	);

	const preenteredSLValue = pipe(
		prevStopLossValue,
		O.getOrElse(() =>
			getDefaultValueForTakeProfitStopLoss(
				side,
				stopLossData.kind,
				parentPrice,
				distance,
				bid,
				ask,
				orderType,
				step,
			),
		),
	);

	const propTPValue = takeProfitData.isValueManuallyChanged ? takeProfitData.value : preenteredTPValue;
	const propSLValue = stopLossData.isValueManuallyChanged ? stopLossData.value : preenteredSLValue;

	const recalculatedPriceTP = calcTakeProfitStopLossPrice(parentPrice, propTPValue);
	const recalculatedPriceSL = calcTakeProfitStopLossPrice(parentPrice, propSLValue);

	// recalc possible p/l
	const refPrice = getRefPriceForPossiblePL(orderKind, { costBasis: costBasis!, parentPrice });

	const possibleProfit = calcPossiblePL(refPrice, propTPValue, Number(amount), rate);
	const possibleLoss = calcPossiblePL(refPrice, propSLValue, Number(amount), rate);

	// recalc min/max values
	const [labelTakeProfit, minMaxTPValue] = getLabelAndMinMaxValuesForTakeProfitStopLoss(
		side,
		takeProfitData.kind,
		parentPrice,
		bid,
		ask,
		distance,
		orderType,
	);

	const [labelStopLoss, minMaxSLValue] = getLabelAndMinMaxValuesForTakeProfitStopLoss(
		side,
		stopLossData.kind,
		parentPrice,
		bid,
		ask,
		distance,
		orderType,
	);

	// round final value
	const tpPriceRounded = roundPriceWithPrecision(propTPValue, instrumentPrecision);
	const slPriceRounded = roundPriceWithPrecision(propSLValue, instrumentPrecision);

	const [minLimitTP, maxLimitTP] = getMinMaxLimit(labelTakeProfit, minMaxTPValue, step);
	const [minLimitSL, maxLimitSL] = getMinMaxLimit(labelStopLoss, minMaxSLValue, step);

	const validatedPriceTP = getValidatedPrice(
		tpPriceRounded,
		minLimitTP,
		maxLimitTP,
		instrumentPrecision,
		takeProfitData.isSelected,
	);
	const validatedPriceSL = getValidatedPrice(
		slPriceRounded,
		minLimitSL,
		maxLimitSL,
		instrumentPrecision,
		stopLossData.isSelected,
	);

	return {
		takeProfitData: {
			...takeProfitData,
			step,
			instrumentPrecision,
			value: tpPriceRounded,
			rawPrice: recalculatedPriceTP,
			possiblePL: possibleProfit,
			providedLabel: labelTakeProfit,
			providedLimit: minMaxTPValue,
			minLimit: minLimitTP,
			maxLimit: maxLimitTP,
			validatedPrice: validatedPriceTP,
		},
		stopLossData: {
			...stopLossData,
			step,
			instrumentPrecision,
			value: slPriceRounded,
			rawPrice: recalculatedPriceSL,
			possiblePL: possibleLoss,
			providedLabel: labelStopLoss,
			providedLimit: minMaxSLValue,
			minLimit: minLimitSL,
			maxLimit: maxLimitSL,
			validatedPrice: validatedPriceSL,
		},
	};
};
