import { Option } from 'fp-ts/lib/Option';
import { string, type } from 'io-ts';
import { createOptionFromNullable } from 'io-ts-types';

export type MarginImpactTO = {
	uid: Option<string>;
	instrumentSymbol: Option<string>;
	arguments: Option<string>;
	formula: Option<string>;
};
export const MarginImpactTOIO = type({
	uid: createOptionFromNullable(string),
	instrumentSymbol: createOptionFromNullable(string),
	arguments: createOptionFromNullable(string),
	formula: createOptionFromNullable(string),
});
