import { isEmpty } from 'fp-ts/lib/Array';
import { AccountTO } from '../../api/socket/to/accounts-message.to';

export const isDemoMode = (current: AccountTO, available: AccountTO[]): boolean => {
	const accountsExcludingCurrent = available.filter(available => available.id !== current.id);

	return isEmpty(accountsExcludingCurrent) ? current.paperMoney : false;
};
