import { Eq, strictEqual, eqString, eqNumber, eqDate } from 'fp-ts/lib/Eq';
import { getEq, Option, getOrd } from 'fp-ts/lib/Option';
import { getEq as getEqArray } from 'fp-ts/lib/Array';
import { ordNumber } from 'fp-ts/lib/Ord';
import { distinctUntilChangedStrict } from './live-data.utils';

export const setoidStrict: Eq<unknown> = {
	equals: strictEqual,
};

export const setoidOptionString = getEq(eqString);
export const setoidOptionNumber = getEq(eqNumber);
export const setoidOptionDate = getEq(eqDate);
export const distinctRDString = distinctUntilChangedStrict<Error, string>(eqString);
export const distinctRDOptionString = distinctUntilChangedStrict<Error, Option<string>>(getEq(eqString));
export const distinctRDNumber = distinctUntilChangedStrict<Error, number>(eqNumber);
export const distinctRDOptionNumber = distinctUntilChangedStrict<Error, Option<number>>(getEq(eqNumber));
export const distinctRDOptionDate = distinctUntilChangedStrict<Error, Option<Date>>(getEq(eqDate));
export const distinctRDNumberArray = distinctUntilChangedStrict<Error, number[]>(getEqArray(ordNumber));
export const distinctRDOptionNumberArray = distinctUntilChangedStrict<Error, Option<number>[]>(
	getEqArray(getOrd(ordNumber)),
);
