import { ask, combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { SocketClient } from '../../api/socket-client';
import { Sink } from '@devexperts/rx-utils/dist/sink.utils';
import { LiveData } from '@devexperts/rx-utils/dist/rd/live-data.utils';
import { WatchlistStore } from '../../store/watchlist/watchlist.store';
import { mapRD } from '@devexperts/rx-utils/dist/rd/operators/mapRD';
import { merge, Observable, of } from 'rxjs';
import { switchMapRD } from '@devexperts/rx-utils/dist/rd/operators/switchMapRD';
import { MarketDataStore } from '../../store/market-data/market-data.store';
import { map, switchMap, filter, startWith } from 'rxjs/operators';
import { Router, parse } from '../../utils/router.utils';
import { TableSize, ConfigItem } from '../../ui-desktop/watchlist/components/watchlist-table/watchlist-table.model';
import {
	WatchlistLargeTableConfig,
	WatchlistSmallTableConfig,
} from '../../ui-desktop/watchlist/components/watchlist-table/watchlist-table.utils';
import { isSome } from 'fp-ts/lib/Option';
import { success } from '@devexperts/remote-data-ts';
import { routes } from '../../ui-common/routes';
import { combineLatestRD } from '@devexperts/rx-utils/dist/rd/operators/combineLatestRD';
import { findIndex } from 'fp-ts/lib/Array';
import * as O from 'fp-ts/lib/Option';
import { Option } from 'fp-ts/lib/Option';
import { pipe } from 'fp-ts/lib/pipeable';
import { shareReplayWithDefaults } from '../../utils/rxjs/shareReplayWithDefaults';

const empty = '';

export type WatchlistViewModel = {
	readonly data$: LiveData<Error, TableData>;
	tableSize$: Observable<TableSize>;
	tableSizeConfig$: Observable<ConfigItem[]>;
};

export type WatchlistViewModelContext = {
	socketClient: SocketClient;
	watchlistStore: WatchlistStore;
	marketDataStore: MarketDataStore;
	router: Router;
};

export type TableData = {
	symbols: string[];
	positionId: Option<number>;
};

export type CreateWatchlistViewModel = (categoryId: string) => Sink<WatchlistViewModel>;

export const createWatchlistViewModel = combineContext(
	ask<WatchlistViewModelContext>(),
	(e): CreateWatchlistViewModel => categoryId => {
		const { watchlistStore, router } = e;

		const data$ = watchlistStore.getById(categoryId).pipe(
			mapRD(wl => wl.symbols),
			shareReplayWithDefaults(),
		);
		const subscribeAllWlInstrumentsEffect$ = data$.pipe(
			switchMapRD(data => merge(...data.map(symbol => e.marketDataStore.getDataBySymbol(symbol)))),
		);

		const routeState$ = merge(of(router.state), router.state$);

		const tableSize$: Observable<TableSize> = routeState$.pipe(
			map(({ location: { pathname } }) => {
				const isContainsBuy = pathname.includes('buy');
				const isContainsSell = pathname.includes('sell');
				const isContainsInfo = pathname.includes('info');

				return isContainsBuy || isContainsSell || isContainsInfo ? TableSize.Small : TableSize.Large;
			}),
		);

		const tableSizeConfig$: Observable<Array<ConfigItem>> = tableSize$.pipe(
			switchMap(tableSize =>
				of(tableSize === TableSize.Large ? WatchlistLargeTableConfig : WatchlistSmallTableConfig),
			),
		);

		const instrumentFromURL$ = routeState$.pipe(
			map(({ location: { pathname } }) => parse(routes.trade.category.symbol.root, pathname)),
			map(parsedURL =>
				pipe(
					parsedURL,
					O.map(([{ symbol }]) => symbol),
				),
			),
			filter(isSome),
			map(symbol => success<Error, string>(symbol.value)),
			startWith(success<Error, string>(empty)),
		);

		const dataAndInstrumentPositionId$ = combineLatestRD(data$, instrumentFromURL$).pipe(
			mapRD(([symbols, symbol]) => {
				const positionId = findIndex(symbols, s => s === symbol);
				return { symbols, positionId };
			}),
		);

		return new Sink(
			{
				data$: dataAndInstrumentPositionId$,
				tableSizeConfig$,
				tableSize$,
			},
			subscribeAllWlInstrumentsEffect$,
		);
	},
);
