import { AggregationPeriodTO } from '../../api/generated/swagger/definitions/AggregationPeriodTO';
import { AggrPeriodRangePairTO } from '../../api/generated/swagger/definitions/AggrPeriodRangePairTO';
import { SocketClient } from '../../api/socket-client';
import { isGlobalChartSettingsTO } from '../../api/socket/socket-message.to';
import { filter, map } from 'rxjs/operators';
import { Sink } from '@devexperts/rx-utils/dist/sink.utils';
import { combineContext, ask } from '@devexperts/rx-utils/dist/context.utils';
import { LiveData } from '@devexperts/rx-utils/dist/rd/live-data.utils';
import { fromOption } from '@devexperts/remote-data-ts';
import { pipe } from 'fp-ts/lib/pipeable';
import * as O from 'fp-ts/lib/Option';
import { shareReplayWithDefaults } from '../../utils/rxjs/shareReplayWithDefaults';

export type ChartSettingsStore = {
	periods$: LiveData<Error, AggregationPeriodTO[]>;
	pairs$: LiveData<Error, AggrPeriodRangePairTO[]>;
};

type ChartSettingsStoreContext = {
	socketClient: SocketClient;
};

export type CreateChartSettingsStore = Sink<() => ChartSettingsStore>;

export const createChartSettingsStore = combineContext(
	ask<ChartSettingsStoreContext>(),
	(context: ChartSettingsStoreContext): CreateChartSettingsStore => {
		const { socketClient } = context;

		const chartSettings$ = socketClient.message$.pipe(
			filter(isGlobalChartSettingsTO),
			map(res => res.body.chartSettings),
			shareReplayWithDefaults(),
		);

		const periods$ = chartSettings$.pipe(
			map(chartSettings =>
				fromOption(
					pipe(
						chartSettings,
						O.chain(settings => settings.periods),
					),
					() => new Error(`Can't loading chart period settings`),
				),
			),
			shareReplayWithDefaults(),
		);

		const pairs$ = chartSettings$.pipe(
			map(chartSettings =>
				fromOption(
					pipe(
						chartSettings,
						O.chain(settings => settings.pairs),
					),
					() => new Error(`Can't loading chart period settings`),
				),
			),
			shareReplayWithDefaults(),
		);

		return new Sink(() => ({ periods$, pairs$ }), chartSettings$);
	},
);
