import { Either } from 'fp-ts/lib/Either';
import { Option } from 'fp-ts/lib/Option';
import { OrderEntrySide } from '../order-entry/order-entry.model';
import { OrderTO } from '../../api/generated/swagger/definitions/OrderTO';

export type OrderType = OrderTO['type'];

export enum ProfitLossKind {
	Profit = '* PROFIT *',
	Loss = '* LOSS *',
}

export enum ProvidedLabelType {
	Min = '* MIN *',
	Max = '* MAX *',
}

export enum PriceValidationErrorMessages {
	MoreOrEqual = '* MORE_OR_EQUAL *',
	LessOrEqual = '* LESS_OR_EQUAL *',
}

export type PriceValidationError = {
	message: PriceValidationErrorMessages;
	value: string;
};

export type TakeProfitStopLossData = {
	kind: ProfitLossKind;
	isSelected: boolean;
	isDisabled: boolean;
	rawPrice: number;
	validatedPrice: Either<PriceValidationError, number>;
	minLimit: number;
	maxLimit: number;
	step: number;
	value: number;
	possiblePL: number;
	providedLabel: ProvidedLabelType;
	providedLimit: number;
	isValueManuallyChanged: boolean;
	instrumentPrecision: number;
};

export type OrderEntryTakeProfitStopLossData = {
	takeProfitData: TakeProfitStopLossData;
	stopLossData: TakeProfitStopLossData;
};

export enum OrderKind {
	Order = '* ORDER *',
	Position = '* POSITION *',
}

export type TakeProfitStopLossOptions = {
	currentTakeProfitData: TakeProfitStopLossData;
	currentStopLossData: TakeProfitStopLossData;
	prevTakeProfitFixedPrice: Option<number>;
	prevStopLossFixedPrice: Option<number>;
	parentOrderPrice: number;
	bid: number;
	ask: number;
	orderType: OrderType;
	amount: number;
	side: OrderEntrySide;
	priceIncrement: number;
	precision: number;
	rate: number;
	distance: number;
	orderKind: OrderKind;
	costBasis?: number;
};
