import * as React from 'react';
import { PureComponent } from 'react';
import { WithTranslation, withTranslation } from 'react-i18next';
import { PipRate, TPipSettings } from '../../../../ui-common/pip-rate/pip-rate.component';
import { Option } from 'fp-ts/lib/Option';
import * as O from 'fp-ts/lib/Option';
import { Optional } from '../../../../ui-common/optional/optional.component';
import { OrderEntrySide } from './order-entry-panel-side.model';
import { tspan } from '../../../../utils/i18n.utils';
import { pipe } from 'fp-ts/lib/pipeable';
import * as cn from 'classnames';
import * as css from './theme/order-entry-panel-side.module.css';
import * as pipCss from './theme/pip-rate.child.module.css';

export type OrderEntryPanelSideProps = {
	amount: Option<number>;
	pipSettings: TPipSettings;
	side: OrderEntrySide;
	isActive: boolean;
	isDisabled?: boolean;
	precision: number;
	onClick: (data: OrderEntrySide) => void;
};

class OrderEntryPanelSideUnwrapped extends PureComponent<OrderEntryPanelSideProps & WithTranslation> {
	render() {
		const { amount, side, isActive, isDisabled, pipSettings, precision } = this.props;

		const containerTheme = cn(css.container, {
			[css.container_sell]: isActive && side === OrderEntrySide.Sell,
			[css.container_buy]: isActive && side === OrderEntrySide.Buy,
			[css.container_disabled]: isDisabled,
		});

		return (
			<div className={containerTheme} onClick={this.handleClick}>
				<Optional
					value={pipe(
						amount,
						O.map(amount => (
							<PipRate value={amount} rise={pipSettings} digits={precision} theme={pipCss} />
						)),
					)}
				/>

				<div className={css.text}>{this.getText(side)}</div>
			</div>
		);
	}

	private handleClick = () => {
		const { onClick, side, isDisabled } = this.props;
		!isDisabled && onClick(side);
	};

	private getText = (value: OrderEntrySide) => {
		switch (value) {
			case OrderEntrySide.Sell: {
				return tspan('orderEntryPanelSideSell', 'Sell');
			}
			case OrderEntrySide.Buy: {
				return tspan('orderEntryPanelSideBuy', 'Buy');
			}
		}
	};
}

export const OrderEntryPanelSide = withTranslation()(OrderEntryPanelSideUnwrapped);
