import { createSelector } from 'reselect';
import { map, filter } from 'rxjs/operators';
import * as O from 'fp-ts/lib/Option';
import { isSuccess } from '@devexperts/remote-data-ts';
import { ask, combineContext, deferContext } from '@devexperts/rx-utils/dist/context.utils';
import { withRXWithDefaults } from '../../../../utils/withRXWithDefaults';
import { mapRD } from '@devexperts/rx-utils/dist/rd/operators/mapRD';
import { EditingPosition } from '../../components/editing-position/editing-position.component';
import { runOnMount } from '../../../../utils/react.utils';
import {
	EditingPositionViewModel,
	createEditingPositionViewModel,
} from '../../../../view-model/positions/editing-position.view-model';
import { toEditingPositionData } from './editing-position.utils';
import { identity } from 'io-ts';
import { pipe } from 'fp-ts/lib/pipeable';

export type EditingPositionContainerContext = {
	editingPositionViewModel: EditingPositionViewModel;
};

export const Container = combineContext(EditingPosition, ask<EditingPositionContainerContext>(), (EditingPosition, e) =>
	withRXWithDefaults(EditingPosition)(() => {
		const {
			data$,
			positionAmountAbs$,
			value$,
			amountPrecision$,
			profitLoss$,
			onTakeProfitStopLossValueChange,
			onPositionUpdate,
			isValid$,
			isShownValidErrors$,
			currencySymbol$,
			unitName$,
			isEditingPositionAllowed$,
		} = e.editingPositionViewModel;

		return {
			props: {
				data: data$.pipe(
					mapRD(toEditingPositionData),
					map(data => data.toOption()),
				),
				amount: positionAmountAbs$.pipe(map(data => data.toOption())),
				value: value$.pipe(
					map(data =>
						pipe(
							data.toOption(),
							O.chain(identity),
						),
					),
				),
				amountPrecision: amountPrecision$,
				profitLoss: profitLoss$.pipe(
					filter(isSuccess),
					map(profitLoss => profitLoss.value),
				),
				isValid: isValid$,
				isShownValidErrors: isShownValidErrors$,
				currencySymbol: currencySymbol$,
				unitName: unitName$.pipe(
					map(unitName =>
						pipe(
							unitName.toOption(),
							O.chain(identity),
						),
					),
				),
				isEditingPositionAllowed: isEditingPositionAllowed$,
			},
			defaultProps: {
				data: O.none,
				amount: O.none,
				value: O.none,
				amountPrecision: 0,
				profitLoss: O.none,
				onValueChange: onTakeProfitStopLossValueChange,
				onPositionUpdate,
				isValid: true,
				isShownValidErrors: false,
				currencySymbol: '',
				unitName: O.none,
				isEditingPositionAllowed: false,
			},
		};
	}),
);

export const EditingPositionContainer = combineContext(
	deferContext(Container, 'editingPositionViewModel'),
	createEditingPositionViewModel,
	(Container, createEditingPositionViewModel) =>
		runOnMount(Container, props => {
			const uid = createSelector(
				props,
				props => props.uid,
			);
			const plKind = createSelector(
				props,
				props => props.plKind,
			);
			return createSelector(
				uid,
				plKind,
				(uid, plKind) =>
					createEditingPositionViewModel(uid, plKind).map(editingPositionViewModel => ({
						editingPositionViewModel,
					})),
			);
		}),
);
