import * as React from 'react';
import { Component, ReactNode } from 'react';
import { TableCellAlign } from './table.model';
import * as cn from 'classnames';
import * as css from './theme/table-cell.module.css';

export type TTableCellProps = {
	align: TableCellAlign;
	value?: string | number;
	children?: ReactNode;
};

export class TableCell extends Component<TTableCellProps> {
	render() {
		const { align, value, children } = this.props;
		const className = cn(css.cell, {
			[css.cell_leftAligned]: align === TableCellAlign.Left,
			[css.cell_centerAligned]: align === TableCellAlign.Center,
			[css.cell_rightAligned]: align === TableCellAlign.Right,
		});

		return (
			<span className={className}>
				{value}
				{children}
			</span>
		);
	}
}
