import * as React from 'react';
import { PureComponent, ReactText } from 'react';
import { withTranslation, WithTranslation } from 'react-i18next';
import { Either } from 'fp-ts/lib/Either';
import * as E from 'fp-ts/lib/Either';
import { constNull } from 'fp-ts/lib/function';
import { Menu, MenuItem } from '@devexperts/react-kit/dist/components/Menu/Menu';
import { Dropdown, TAnchorProps } from '@devexperts/react-kit/dist/components/Dropdown/Dropdown';
import {
	Popover,
	PopoverAlign,
	PopoverPlacement,
	TPopoverProps,
} from '../../../ui-kit/components/popover/popover.component';
import { ArrowIcon } from '../../../ui-kit/components/icons/arrow.icon';
import { Button } from '../../../../ui-common/button/button.component';
import { DateInput } from '../../../ui-kit/components/date-input/date-input';
import { TDateInputValue } from '@devexperts/react-kit/dist/components/DateInput/DateInput.model';
import { Calendar } from '../../../ui-kit/components/calendar/calendar.component';
import { getMenuItemName, isPeriod } from './history-filter-form.utils';
import {
	UnwrappedFilterFormData,
	HistoryFilterData,
	HistoryValueHandlerData,
	Period,
	PeriodNames,
	DateType,
} from './history-filter-form.model';
import { tspan } from '../../../../utils/i18n.utils';
import { pipe } from 'fp-ts/lib/pipeable';
import * as css from './theme/history-filter-form.module.css';
import * as periodButtonCss from './theme/period-button.child.module.css';
import * as periodPopoverCss from './theme/period-popover.child.module.css';
import * as dateInputPopoverCss from './theme/date-input-popover.child.module.css';
import * as dateInputCss from './theme/date-input.child.module.css';
import * as dateInputButtonCss from './theme/date-input.button.module.css';
import * as menuItemCss from './theme/menu-item.child.module.css';

export type HistoryFilterProps = {
	validatedValue: Either<void, UnwrappedFilterFormData>;
	isPeriodDropdownOpened: boolean;
	value: HistoryFilterData;
	onPeriodDropdownToggle: (isOpened: boolean) => void;
	onValueChange: (value: HistoryValueHandlerData) => void;
};

const periods: Array<Period> = [
	Period.last7days,
	Period.last30Days,
	Period.last365days,
	Period.thisMonth,
	Period.thisYear,
	Period.all,
	Period.custom,
];

const dateInputTheme = {
	SteppableInput: {
		Input: dateInputCss,
		ButtonIcon: dateInputButtonCss,
	},
	Popover: dateInputPopoverCss,
};

class HistoryFilterFormUnwrapped extends PureComponent<HistoryFilterProps & WithTranslation> {
	private getTranslatedPeriodNames = (): PeriodNames => {
		const { t } = this.props;

		return {
			last7days: t('historyFilterFormLast7Days', 'Last 7 days'),
			last30Days: t('historyFilterFormLast30Days', 'Last 30 days'),
			last365days: t('historyFilterFormLast365days', 'Last 365 days'),
			thisMonth: t('historyFilterFormThisMonth', 'This month'),
			thisYear: t('historyFilterFormThisYear', 'This year'),
			all: t('historyFilterFormAll', 'All'),
			custom: t('historyFilterFormCustom', 'Custom'),
		};
	};

	render() {
		const { isPeriodDropdownOpened, validatedValue } = this.props;
		const { selectedPeriod, startDate, endDate } = this.props.value;

		const periodNames = this.getTranslatedPeriodNames();

		return (
			<div className={css.container}>
				<div className={css.dropdownWrapper}>
					<Dropdown
						Anchor={this.renderPeriodsAnchor}
						isOpened={isPeriodDropdownOpened}
						onToggle={this.handlePeriodsToggle}
						Popover={this.renderPeriodsPopover}>
						<Menu onItemSelect={this.handlePeriodSelect}>
							{periods.map(period => (
								<MenuItem
									key={getMenuItemName(period, periodNames)}
									value={period}
									isActive={selectedPeriod === period}
									theme={menuItemCss}>
									{getMenuItemName(period, periodNames)}
								</MenuItem>
							))}
						</Menu>
					</Dropdown>
				</div>
				<div className={css.datePickersWrapper}>
					<span className={css.label}>{tspan('historyFilterFormFrom', 'From')}</span>
					<div className={css.dateInputWrapper}>
						<DateInput
							isDisabled={false}
							onValueChange={this.handleDateChange(DateType.startDate)}
							Calendar={Calendar}
							value={startDate}
							error={E.isLeft(validatedValue)}
							theme={dateInputTheme}
						/>
						{this.renderValidationResult()}
					</div>
					<span className={css.label}>{tspan('historyFilterFormTo', 'to')}</span>
					<div className={css.dateInputWrapper}>
						<DateInput
							isDisabled={false}
							onValueChange={this.handleDateChange(DateType.endDate)}
							Calendar={Calendar}
							value={endDate}
							error={E.isLeft(validatedValue)}
							theme={dateInputTheme}
						/>
					</div>
				</div>
			</div>
		);
	}

	private renderPeriodsAnchor = (props: TAnchorProps) => {
		const { children, onClick, innerRef } = props;
		const { selectedPeriod } = this.props.value;

		const periodNames = this.getTranslatedPeriodNames();

		return (
			<Button isFlat onClick={onClick} ref={innerRef} theme={periodButtonCss}>
				<span className={css.text}>{getMenuItemName(selectedPeriod, periodNames)}</span>
				<div className={css.triangleWrapper}>
					<ArrowIcon />
				</div>
				{children}
			</Button>
		);
	};

	private renderPeriodsPopover = (props: TPopoverProps) => (
		<Popover {...props} placement={PopoverPlacement.Bottom} align={PopoverAlign.Left} theme={periodPopoverCss} />
	);

	private renderValidationResult = () => {
		return pipe(
			this.props.validatedValue,
			E.fold(
				() => (
					<div className={css.error}>
						{tspan(
							'historyFilterFormDateValidationError',
							'Start date must be equal or lower than end date',
						)}
					</div>
				),
				constNull,
			),
		);
	};

	private handlePeriodsToggle = (isOpened: boolean | undefined) => {
		const { onPeriodDropdownToggle } = this.props;

		if (isOpened !== undefined) {
			onPeriodDropdownToggle(isOpened);
		}
	};

	private handlePeriodSelect = (period: ReactText) => {
		const { startDate, endDate } = this.props.value;
		const { isPeriodDropdownOpened, onValueChange, onPeriodDropdownToggle } = this.props;

		if (!isPeriod(period)) {
			return;
		}

		onPeriodDropdownToggle(!isPeriodDropdownOpened);

		onValueChange({
			selectedPeriod: period,
			startDate,
			endDate,
			isDateInputChanged: false,
		});
	};

	private handleDateChange = (dateType: DateType) => (date: TDateInputValue) => {
		const { selectedPeriod, startDate, endDate } = this.props.value;
		const { onValueChange } = this.props;

		const changeDate = (from: TDateInputValue, to: TDateInputValue) =>
			onValueChange({
				selectedPeriod,
				startDate: from,
				endDate: to,
				isDateInputChanged: true,
			});

		switch (dateType) {
			case DateType.startDate:
				changeDate(date, endDate);
				break;

			case DateType.endDate:
				changeDate(startDate, date);
				break;
		}
	};
}

export const HistoryFilterForm = withTranslation()(HistoryFilterFormUnwrapped);
