import * as React from 'react';
import { PureComponent } from 'react';
import { combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { routes } from '../../../../ui-common/routes';
import { Route } from '../../../../ui-common/route/containers/route.container';
import { HistoryFilterFormContainer } from '../../containers/history-filter-form/history-filter-form.container';
import { HistoryTableContainer } from '../../containers/history-table/history-table.container';
import { HistoryDetailsContainer } from '../../containers/history-details/history-details.container';
import { MatchType } from '../../../../utils/router.utils';
import { EmptyScreenContainer } from '../../../empty-screen/containers/empty-screen.container';
import { RemoteData } from '@devexperts/remote-data-ts';
import { ScreenType } from '../../../../ui-common/empty-screen/components/empty-screen.component';
import { RenderRemoteData } from '@devexperts/react-kit/dist/components/render-remote-data/render-remote-data.component';
import { Workspace } from '../../../ui-kit/components/workspace/workspace.component';
import { WorkspacePanel } from '../../../ui-kit/components/workspace/workspace-panel.component';
import { Preloader } from '../../../ui-kit/components/preloader/preloader.component';
import * as css from './theme/history-dashboard.module.css';

export type HistoryDashboardProps = {
	historyOrdersCount: RemoteData<Error, number>;
};

export const HistoryDashboard = combineContext(
	Route,
	HistoryFilterFormContainer,
	HistoryTableContainer,
	HistoryDetailsContainer,
	EmptyScreenContainer,
	(Route, HistoryFilterFormContainer, HistoryTableContainer, HistoryDetailsContainer, EmptyScreenContainer) =>
		class HistoryDashboard extends PureComponent<HistoryDashboardProps> {
			render() {
				const { historyOrdersCount } = this.props;

				return (
					<RenderRemoteData
						data={historyOrdersCount}
						success={this.renderSuccess}
						noData={data => data === 0}
						DataStateNoData={() => (
							<div className={css.emptyScreen}>
								<EmptyScreenContainer screenType={ScreenType.History} />
							</div>
						)}
						DataStatePending={() => <Preloader />}
						DataStateFailure={() => <div>Error loading data</div>}
					/>
				);
			}

			private renderSuccess = () => (
				<section className={css.container}>
					<div className={css.left}>
						<WorkspacePanel>
							<div className={css.filterFormWrapper}>
								<HistoryFilterFormContainer />
							</div>
						</WorkspacePanel>
						<Workspace>
							<div className={css.historyTableWrapper}>
								<HistoryTableContainer />
							</div>
						</Workspace>
					</div>
					<Route match={routes.history.order.info.root} exact render={this.renderHistoryDetailsAside} />
				</section>
			);

			private renderHistoryDetailsAside = (path: MatchType<typeof routes.history.order.info.root>) => (
				<div className={css.aside}>
					<HistoryDetailsContainer orderId={path.historyOrderId} />
				</div>
			);
		},
);
