import { OrderEntrySide } from '../../model/order-entry/order-entry.model';
import { OrdersSide } from './components/orders-table/orders-table.model';

export function getOrdersSide(side: OrderEntrySide): OrdersSide {
	switch (side) {
		case OrderEntrySide.Buy: {
			return OrdersSide.Buy;
		}
		case OrderEntrySide.Sell: {
			return OrdersSide.Sell;
		}
	}
}
