import * as React from 'react';
import { PureComponent } from 'react';
import { RemoteData } from '@devexperts/remote-data-ts';
import { RemoteDataState } from '../../../ui-common/remote-data-state/remote-data-state.component';
import { combineContext } from '@devexperts/rx-utils/dist/context.utils';
import { AppContainer } from '../../app/containers/app/app.container';
import { Option } from 'fp-ts/lib/Option';

export type EnvProps = {
	initialize: RemoteData<Error, Option<string>>;
};

export const EnvComponent = combineContext(
	AppContainer,
	AppContainer =>
		class EnvComponent extends PureComponent<EnvProps> {
			render() {
				return <RemoteDataState data={this.props.initialize} success={this.renderSuccess} />;
			}

			renderSuccess() {
				return <AppContainer />;
			}
		},
);
