import { Option } from 'fp-ts/lib/Option';
import { boolean, number, string, type } from 'io-ts';
import { createOptionFromNullable, DateFromISOString } from 'io-ts-types';

export type QuoteTO = {
	stale: boolean;
	quantity: Option<number>;
	askDirection: number;
	bidDirection: number;
	markDirection: number;
	symbol: string;
	ask: Option<number>;
	bid: Option<number>;
	askSize: number;
	bidSize: number;
	markPrice: Option<number>;
	date: Date;
};
export const QuoteTOIO = type({
	stale: boolean,
	quantity: createOptionFromNullable(number),
	askDirection: number,
	bidDirection: number,
	markDirection: number,
	symbol: string,
	ask: createOptionFromNullable(number),
	bid: createOptionFromNullable(number),
	askSize: number,
	bidSize: number,
	markPrice: createOptionFromNullable(number),
	date: DateFromISOString,
});
