import * as React from 'react';
import { LineProp } from './line.model';
import { Line } from './line.component';
import { withTheme } from '@devexperts/react-kit/dist/utils/withTheme';
import { PartialKeys } from '@devexperts/utils/dist/object';
import { Theme } from '../../../ui-kit/components/theme/theme.component';
import { MakeTheme } from '../../../../utils/theme.utils';
import * as css from './theme/lines.component.module.css';

type LinesProps = {
	lines: LineProp[];
	theme: MakeTheme<'container'>;
};

export class RawLines extends React.Component<LinesProps> {
	render() {
		const { lines } = this.props;
		return (
			<div className={css.container}>
				{lines.map((line, index) => (
					<Line
						key={`line${index}`}
						theme={line.theme}
						formattedPrice={line.formattedPrice}
						yPosition={line.yPosition}
						label={line.label}
					/>
				))}
			</div>
		);
	}
}

export const Lines = withTheme(Symbol(), css)(RawLines);

export const ThemedLines = (props: PartialKeys<LinesProps, 'theme'>) => (
	<Theme>
		<Lines {...props} />
	</Theme>
);
